import { TsMap } from 'corengf/src/utils/TsMap';
import { Helper } from 'corengf/src/utils/Helper';
import { Actions } from 'corengf/src/utils/Actions';
import { ButtonId } from 'corengf/src/buttons/ButtonId';
import { Orientation } from 'corengf/src/display/Viewport';
import { ISprite } from 'corengf/src/elements/canvas/sprite/Sprite';
import { IButtonConfig } from 'corengf/src/elements/canvas/button/Button';
import { ICompViewConfig } from 'corengf/src/core/config/ICompViewConfig';
import { ICoreElementLayoutConfig } from 'corengf/src/elements/canvas/CoreElement';
import { ILoadingBarConfig } from 'corengf/src/elements/canvas/progressiveBar/LoadingBar';
import { ISpineLoadingBarConfig } from 'corengf/src/elements/canvas/progressiveBar/SpineLoadingBar';


export interface ISmartPreloaderConfig extends ICompViewConfig {
	loadingBar: ILoadingBarConfig | ISpineLoadingBarConfig;
	background: ISprite;
	preloaderButton: IButtonConfig;
	buttonStartYPositionPortrait: number;
	buttonStartYPositionLandscape: number;
}

export class SmartPreloaderConfig implements ISmartPreloaderConfig {
	public get loadingBarConfig (): ILoadingBarConfig {
		return this.loadingBar as ILoadingBarConfig;
	}

	public get spineLoadingBarConfig (): ISpineLoadingBarConfig {
		return this.loadingBar as ISpineLoadingBarConfig;
	}

	public name: string = 'preloader';

	public background: ISprite = {
		name: 'preloaderBackground',
		isOrientationSupported: true,
		assetName: 'preloaderBackground.jpg',
		position: { x: 0, y: 0 }
	};

	public loadingBar: ILoadingBarConfig | ISpineLoadingBarConfig = {
		name: 'preloadBar',
		isOrientationSupported: true,
		position: { x: 360, y: 1150.5 },
		fullAssetName: 'loadingBar.png',
		emptyAssetName: Helper.EMPTY,
		animationAssets: null,
		completeAnimationAssets: null,
		anchor: { x: 0.5, y: 0.5 }
	};

	public buttonStartYPositionPortrait: number = 1120;
	public buttonStartYPositionLandscape: number = 585;

	public preloaderButton: IButtonConfig = {
		id: ButtonId.BUTTON_CLOSE_PRELOADER,
		name: ButtonId.BUTTON_CLOSE_PRELOADER,
		isOrientationSupported: true,
		action: Actions.ON_GAME_START,
		productLine: 'NGF',
		assetName: 'playNow',
		isLocal: false,
		alpha: 0,
		position: { x: 360, y: 1060 },
		anchor: { x: 0.5, y: 0.5 },
		rectangleHitArea: { width: 0, height: 0 }
	};

	public coreElementOrientationConfig: TsMap<Orientation, TsMap<string, ICoreElementLayoutConfig>> = new TsMap( [
		[
			Orientation.PORTRAIT, new TsMap( [
				[
					this.preloaderButton.name, {
						name: this.preloaderButton.name,
						position: this.preloaderButton.position
					}
				],
				[
					this.loadingBar.name, {
						name: this.loadingBar.name,
						position: this.loadingBar.position,
						fullAssetName: this.loadingBarConfig.fullAssetName
					}
				],
				[
					this.background.name, {
						name: this.background.name,
						assetName: this.background.assetName
					}
				]
			] )
		],
		[
			Orientation.LANDSCAPE, new TsMap( [
				[
					this.preloaderButton.name, {
						name: this.preloaderButton.name,
						position: { x: 640, y: 555 }
					}
				],
				[
					this.loadingBar.name, {
						name: this.loadingBar.name,
						position: { x: 640, y: 600 },
						fullAssetName: Orientation.LANDSCAPE + '_' + this.loadingBarConfig.fullAssetName
					}
				],
				[
					this.background.name, {
						name: this.background.name,
						assetName: Orientation.LANDSCAPE + '_' + this.background.assetName
					}
				]
			] )
		]
	] );
}


