import { ICompViewConfig } from './../../core/config/ICompViewConfig';
import { ICoreUISpineAnimationElementConfig } from './../../factory/CoreUIElementFactory';


export class AnimationViewConfig implements IAnimationViewConfig {
	public name: string = '';
	public animations: Array<ICoreUISpineAnimationElementConfig> = [];
}

export interface IAnimationViewConfig extends ICompViewConfig {
	animations: Array<ICoreUISpineAnimationElementConfig>;
	mask?: AnimationMask;
}

export interface AnimationMask {
	x: number;
	y: number;
	width: number;
	height: number;
}


