import { Sprite } from 'corengf/src/elements/canvas/sprite/Sprite';
import { CoreUIElementFactory } from 'corengf/src/factory/CoreUIElementFactory';
import { AnimationView } from 'corengf/src/components/animationView/AnimationView';
import { IBackgroundConfig } from 'corengf/src/components/background/BackgroundConfig';


export class BackgroundView extends AnimationView {

	protected config: IBackgroundConfig;
	protected staticImages: Array<Sprite> = new Array<Sprite>();

	protected createElements ( config?: IBackgroundConfig ): void {
		super.createElements( config );
		this.setupStaticImages();
	}

	protected setupStaticImages (): void {
		if ( this.config.staticImages == undefined ) {
			return;
		}

		this.config.staticImages.forEach( ( spriteElement ) => {
			let image: Sprite = CoreUIElementFactory.createInstance( spriteElement );

			this.addChild( image );
			this.staticImages.push( image );
		} );
	}
}


