import { spine } from 'pixi.js';
import { Inject } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { SoundManager, AudioTrackName } from 'corengf/src/audio/SoundManager';
import { SpineAnimation, ISpineAnimationConfig, SoundAttribute } from './SpineAnimation';


export class SpineSoundAnimation extends SpineAnimation {

	public static elementType: string = 'SpineSoundAnimation';

	@Inject
	protected soundManager: SoundManager;

	private stopSoundOnStateChanged: boolean;

	protected currentStateName: string;
	protected prevStateName: string;

	protected config: ISpineSoundAnimationConfig;

	constructor ( config: ISpineSoundAnimationConfig ) {
		super( config );
		this.stopSoundOnStateChanged = ( config.stopSoundOnStateChanged ) ? config.stopSoundOnStateChanged : false;
	}

	protected onStart ( trackEntry: spine.core.TrackEntry ): void {
		super.onStart( trackEntry );

		this.playSound( trackEntry.animation.name );
	}

	protected playSound ( name: string ): void {
		if ( this.config.soundMap ) {
			const soundAttributes = this.getSoundAttributes( name );
			if ( soundAttributes ) {
				soundAttributes.forEach( ( soundAttribute ) => {
					if ( soundAttribute.loop && this.soundManager.isPlaying( soundAttribute.key ) ) {
						return;
					} else {
						this.soundManager.play( soundAttribute.key, AudioTrackName.EFFECT, soundAttribute.loop );
					}
				} );
			}
		}
	}

	public playByStateName ( stateName: string, context?: any, skipOnDuplicate: boolean = true ): void {
		this.prevStateName = this.currentStateName;
		this.currentStateName = stateName;
		if ( this.currentStateName !== this.prevStateName && this.stopSoundOnStateChanged ) {
			this.stopAllSound();
		}
		super.playByStateName( stateName, context, skipOnDuplicate );
	}

	public stop (): void {
		super.stop();
		this.stopAllSound();
	}

	public hide (): void {
		super.hide();
		this.stopAllSound();
	}

	protected stopAllSound (): void {
		if ( this.config.soundMap ) {
			this.config.soundMap.forEach( ( soundAttributes ) => {
				soundAttributes.forEach( ( soundAttribute ) => {
					this.soundManager.stop( soundAttribute.key );
				} );
			} );
		}
	}

	protected getSoundAttributes ( name: string ): Array<SoundAttribute> {
		if ( !this.config.soundMap ) {
			return null;
		}
		return this.config.soundMap.get( name );
	}
}

export interface ISpineSoundAnimationConfig extends ISpineAnimationConfig {
	soundMap: TsMap<string, Array<SoundAttribute>>;
	stopSoundOnStateChanged?: boolean;
}


