import { AbstractFormatter } from 'corengf/src/server/abstract/formatter/AbstractFormatter';
import { Scenario } from 'corengf/src/states/Scenario';
import { ITransport } from 'corengf/src/server/abstract/transport/ITransport';
import { BlankTransport } from 'corengf/src/server/abstract/transport/BlankTransport';

export abstract class AbstractApi {

	protected _rawData: Object;

	public get rawData () {
		return this._rawData;
	}

	protected request: Object;

	protected response: Object | Scenario;

	protected requestFormatter: AbstractFormatter;

	protected responseFormatter: AbstractFormatter;

	protected transport: BlankTransport;

	protected config: any;

	protected requestFormat ( data?: any ) {
		if ( !this.requestFormatter ) {
			return;
		}
		this.request = this.requestFormatter.codeMessage( data );
	}

	protected responseFormat ( data?: any ) {
		if ( !this.responseFormatter ) {
			this.response = data;
			return;
		}
		this.response = this.responseFormatter.processIncomingMessage( data.payload );
	}

	protected abstract encapsulation ( data?: any );

	public send ( messageType: string, data?: any ): Promise<any> {
		this.requestFormat( data );
		const apiPackage = this.encapsulation( messageType );
		return this.transport.psend( apiPackage ).then( ( response ) => {
			this._rawData = response.payload;
			this.responseFormat( response );
			return this.onResponse();
		} );
	}


	protected onResponse () {
		return new Promise( ( resolve ) => {
			resolve( this.response );
		} );
	}

	public abstract init ( transport: ITransport, config?: any );

	public getScenario (): Scenario {
		return null;
	}

	public setScenario ( data?: Object ) {
		this.responseFormat( data );
	}
}


