import { Inject } from 'typescript-ioc/es5';
import { MLUISpinPanel } from './MLUISpinPanel';
import { IMLUISpinConfig, MLUISpinConfig } from './MLUISpinConfig';
import { UIPanelController } from 'corengf/src/components/ui/components/UIPanelController';


export class MLUISpinController extends UIPanelController {

	protected view: MLUISpinPanel;

	constructor ( @Inject view: MLUISpinPanel ) {
		super( view );
	}

	public init ( data: any ): void {
		let config = this.gameConfig.getConfig<IMLUISpinConfig>( MLUISpinConfig );
		this.view.initialize( config );
		this.finishStateAction();
	}

	public onAutoSpinEvent (): void {
		this.view.onPressEvent();
	}

	protected startSpin ( data: any ): void {
		this.view.showStopSpinButton();
		this.view.onSpinStart();
		this.finishStateAction();
	}

	protected spinStop ( data: any ): void {
		this.view.showStopReelButton();
		this.view.onSpinStop();
		this.finishStateAction();
	}

	protected showWinning (): void {
		this.view.showSpinButton();
		this.finishStateAction();
	}

	protected allLineWin (): void {
		this.view.showSpinButton();
		this.finishStateAction();
	}

	protected baseGameIdle ( data: any ): void {
		this.view.checkAndStopAutoSpins();
		this.view.showSpinButton();
		this.finishStateAction();
	}

	protected autoSpinStartSpin ( data: any ): void {
		this.startSpin( data );
		this.finishStateAction();
	}

	protected autoSpinDashboardIdle ( data: any ): void {
		this.baseGameIdle( data );
		this.finishStateAction();
	}

	protected autoSpinShowWinning (): void {
		this.view.showSpinButton();
		this.finishStateAction();
	}

	protected freeSpinsIdle ( data: any ): void {
		this.view.showSpinButton();
		this.finishStateAction();
	}

	protected showRetrigger ( data: any ): void {
		this.view.updateClickData( data.currentCount, data.totalFreeSpinCount );
		this.finishStateAction();
	}

	protected freeSpinsStart ( data: any ): void {
		this.startSpin( data );
		this.finishStateAction();
	}

	protected freeSpinsStop ( data: any ): void {
		this.view.onSpinStop();
		this.view.showStopReelButton();
		this.finishStateAction();
	}

	protected skip_spinning ( data: any ): void {
		this.view.showStopReelButton();
		this.finishStateAction();
	}

	protected skip_freeSpinning ( data: any ): void {
		this.view.showStopReelButton();
		this.finishStateAction();
	}

	protected transitionToAutoSpin ( data: any ): void {
		const spins = ( data.spins === 'infinite' ) ? -1 : +data.spins;
		this.view.setSpinButtonAutoSpins( spins, data.currentCount );
		this.finishStateAction();
	}

	protected transitionToFreeSpins ( data: any ): void {
		this.view.transitionToFreeSpins( data.currentFreeSpinCount, data.totalFreeSpinCount );
		this.view.setInteractive( false );
		this.finishStateAction();
	}

	protected transitionToBaseGame (): void {
		this.view.setInteractive( true );
		this.view.transitionToBaseGame();
		this.finishStateAction();
	}

	public setSpinButtonFreeSpins ( currentCount: number, totalCount: number ): void {
		this.view.setSpinButtonFreeSpins( currentCount, totalCount );
	}

	public stopAutoSpins (): void {
		this.view.stopAutoSpins();
	}

	public autoSpinSpinStop (): void {
		this.view.onSpinStop();
		this.view.showStopReelButton();
		this.finishStateAction();
	}

	protected dashboardDisable (): void {
		this.view.showDisabledButton();
		this.finishStateAction();
	}
}



