import { Container, Inject } from 'typescript-ioc/es5';
import { Application } from 'corengf/src/core/Application';
import { Component } from 'corengf/src/mvc/CompController';
import { UrlParameters } from 'corengf/src/utils/UrlParameters';
import { GameController } from 'corengf/src/core/GameController';
import { ScenarioNames } from 'corengf/src/states/ScenarioNames';
import { SoundConfig } from 'corengf/src/core/config/SoundConfig';
import { MoneyFormatter } from 'corengf/src/utils/MoneyFormatter';
import { ScenarioManager } from 'corengf/src/core/ScenarioManager';
import { SeleniumPortal } from 'corengf/src/selenium/SeleniumPortal';
import { DialogView } from 'corengf/src/components/dialog/DialogView';
import { ButtonsStateSet } from 'corengf/src/buttons/ButtonsStateSet';
import { MarqueeView } from 'corengf/src/components/marquee/MarqueeView';
import { ButtonsController } from 'corengf/src/buttons/ButtonsController';
import { AbstractServer } from 'corengf/src/server/abstract/AbstractServer';
import { MarqueeConfig } from 'corengf/src/components/marquee/MarqueeConfig';
import { NGServerConfig } from 'corengf/src/server/ng/config/NGServerConfig';
import { NGBNGController } from 'corengf/src/server/ng/sdk/bng/NGBNGController';
import { DialogController } from 'corengf/src/components/dialog/DialogController';
import { TooltipController } from 'corengf/src/components/tooltip/TooltipController';
import { BackgroundConfig } from 'corengf/src/components/background/BackgroundConfig';
import { UILocalButtonsStateSet } from 'corengf/src/components/ui/buttons/UILocalButtonsStateSet';
import { SmartPreloaderController } from 'corengf/src/components/preloader/SmartPreloaderController';
import { UILocalButtonsController } from 'corengf/src/components/ui/buttons/UILocalButtonsController';

import { SlotScenarioNames } from 'slotngf/src/states/SlotScenarioNames';
import { SlotGameController } from 'slotngf/src/core/SlotGameController';
import { SlotSoundConfig } from 'slotngf/src/core/config/SlotSoundConfig';
import { SlotScenarioManager } from 'slotngf/src/core/SlotScenarioManager';
import { SlotSeleniumPortal } from 'slotngf/src/selenium/SlotSeleniumPortal';
import { ReelController } from 'slotngf/src/components/reelV2/ReelController';
import { SlotUIController } from 'slotngf/src/components/ui/SlotUIController';
import { SlotDialogView } from 'slotngf/src/components/dialog/SlotDialogView';
import { SlotMarqueeView } from 'slotngf/src/components/marquee/SlotMarqueeView';
import { BigWinController } from 'slotngf/src/components/bigwin/BigWinController';
import { SlotMarqueeConfig } from 'slotngf/src/components/marquee/SlotMarqueeConfig';
import { SlotNGServerConfig } from 'slotngf/src/server/ng/config/SlotNGServerConfig';
import { SlotNGBNGController } from 'slotngf/src/server/ng/sdk/bng/SlotBNGController';
import { SlotButtonsStateSet } from 'slotngf/src/components/buttons/SlotButtonsStateSet';
import { SlotDialogController } from 'slotngf/src/components/dialog/SlotDialogController';
import { SlotButtonController } from 'slotngf/src/components/buttons/SlotButtonController';
import { BaseWinLinesController } from 'slotngf/src/components/lines/BaseWinLinesController';
import { SlotBackgroundConfig } from 'slotngf/src/components/background/SlotBackgroundConfig';
import { ForcingSpinController } from 'slotngf/src/components/demoPanel/ForcingSpinController';
import { PaylinesController } from 'slotngf/src/components/winDisplay/paylines/PaylinesController';
import { SlotFreeSpinsController } from 'slotngf/src/components/freeSpins/SlotFreeSpinsController';
import { SlotBackgroundController } from 'slotngf/src/components/background/SlotBackgroundController';
import { SlotCoinShowerController } from 'slotngf/src/components/coinshower/SlotCoinShowerController';
import { SlotUILocalButtonsStateSet } from 'slotngf/src/components/ui/buttons/SlotUILocalButtonsStateSet';
import { SlotUILocalButtonsController } from 'slotngf/src/components/ui/buttons/SlotUILocalButtonsController';


export class SlotApplication extends Application {

	@Inject
	protected moneyFormatter: MoneyFormatter;

	protected preloader: SmartPreloaderController;
	protected initCallBack: Function;

	protected setupBindings (): void {
		//do basic bindings first!
		super.setupBindings();

		Container.bind( GameController ).to( SlotGameController );
		// server
		Container.bind( NGServerConfig ).to( SlotNGServerConfig );
		// sdk
		Container.bind( NGBNGController ).to( SlotNGBNGController );
		// button
		Container.bind( ButtonsStateSet ).to( SlotButtonsStateSet );
		Container.bind( ButtonsController ).to( SlotButtonController );
		// selenium
		Container.bind( SeleniumPortal ).to( SlotSeleniumPortal );
		// ui
		Container.bind( UILocalButtonsStateSet ).to( SlotUILocalButtonsStateSet );
		Container.bind( UILocalButtonsController ).to( SlotUILocalButtonsController );
		// scenario
		Container.bind( ScenarioNames ).to( SlotScenarioNames );
		Container.bind( ScenarioManager ).to( SlotScenarioManager );
		// components
		Container.bind( DialogView ).to( SlotDialogView );
		Container.bind( SoundConfig ).to( SlotSoundConfig );
		Container.bind( MarqueeView ).to( SlotMarqueeView );
		Container.bind( MarqueeConfig ).to( SlotMarqueeConfig );
		Container.bind( BackgroundConfig ).to( SlotBackgroundConfig );
		Container.bind( DialogController ).to( SlotDialogController );
	}

	protected createComponentList ( components: Array<Component> ): void {
		components.push( { id: 'background', class: SlotBackgroundController } );
		components.push( { id: 'reel', class: ReelController } );
		components.push( { id: 'lines', class: BaseWinLinesController } );
		components.push( { id: 'paylines', class: PaylinesController } );
		components.push( { id: 'coinShower', class: SlotCoinShowerController } );
		components.push( { id: 'bigWin', class: BigWinController } );
		components.push( { id: 'freeSpins', class: SlotFreeSpinsController } );
		components.push( { id: 'ui', class: SlotUIController } );
		components.push( { id: 'tooltip', class: TooltipController } );

		if ( !UrlParameters.isRealMode ) {
			components.push( { id: 'forceOutcomeComponent', class: ForcingSpinController } );
		}
	}

	protected createPreloader (): void {
		super.createPreloader();
		this.preloader.gameStartSignal.add( this.onShowGame, this );
	}

	protected onPreloadingDone ( data: { callback: Function, thisArg: any } ): void {
		this.initCallBack = data.callback.bind( data.thisArg );
		super.onPreloadingDone();
	}

	protected createServer (): void {
		this.server = Container.get( AbstractServer );
		this.gameController.setServer( this.server );
		this.gameController.gameReadySignal.once( this.onGameReady, this );
	}

	protected onGameReady (): void {
		this.setPageTitle();
		this.initCallBack();
	}

	protected setup (): void {
		super.setup();
		this.moneyFormatter.coinSize = 100;
	}
}



