import * as MiniSignal from 'mini-signals';
import { CoreState } from 'corengf/src/states/CoreState';
import { ComponentManager } from 'corengf/src/components/ComponentManager';

/**
 * Local State Machine.
 * Handles states to component manager.
 */
export class LocalStateMachine {

	public stateMachineName: string = 'localStateMachine';

	/**
	 * Fires when new signal is taken from stack processed
	 */
	public stateChangedSignal: MiniSignal;

	/**
	 * Current state of local state machine.
	 */
	protected _currentState: CoreState;
	public get currentState (): CoreState {
		return this._currentState;
	}

	protected componentManager: ComponentManager;

	public initialize ( componentManager: ComponentManager ): void {
		this.componentManager = componentManager;
		this.stateChangedSignal = new MiniSignal();
	}

	/**
	 * Set state for all components.
	 * @param state
	 */
	public setState ( state: CoreState ): void {
		console.log( this.stateMachineName + ' current state - ' + state.stateName );
		if ( this._currentState != state ) {
			this._currentState = state;
			this.componentManager.setState( state );
			this.stateChangedSignal.dispatch( state.stateName );
		} else {
			console.warn( 'LocalStateMachine setState, try to set same state again, state = ' + state );
		}
	}
}


