import * as MiniSignal from 'mini-signals';
import { Inject, Singleton } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { NAudio } from 'corengf/src/audio/NAudio';
import { Browser } from 'corengf/src/utils/Browser';
import { Viewport } from 'corengf/src/display/Viewport';
import { Translator } from 'corengf/src/utils/Translator';
import { PartnerHelper } from 'corengf/src/utils/PartnerHelper';
import { ResourceConfig } from 'corengf/src/core/config/ResourceConfig';
import { GlobalDispatcher, EventParams, IListener } from 'corengf/src/events/GlobalDispatcher';
import { ErrorHandleService } from 'corengf/src/core/ErrorHandleService';
import { ErrorDialogType } from 'corengf/src/components/errorDialog/ErrorDialogConfig';
import { AbstractFormatter } from 'corengf/src/server/abstract/formatter/AbstractFormatter';

@Singleton
export class AssetLoader implements IListener {

	public static readonly MONITOR_GROUP: string = 'MONITOR_GROUP';

	@Inject
	protected translator: Translator;

	@Inject
	protected partnerHelper: PartnerHelper;

	@Inject
	protected viewport: Viewport;

	@Inject
	private _errorHandleService: ErrorHandleService;

	public get errorHandleService (): ErrorHandleService {
		return this._errorHandleService;
	}

	/**
	 * Dispatched on each resource group completed.
	 *
	 * @type {MiniSignal}
	 * @memberof AssetLoader
	 */
	public onGroupCompleted: MiniSignal = new MiniSignal();

	public onGroupProgress: MiniSignal = new MiniSignal();

	protected loader: PIXI.loaders.Loader = PIXI.loader;

	/**
	 * Determine the image's size according to the device.
	 *
	 * @private
	 * @type {string}
	 * @memberof AssetLoader
	 */
	private postfix: string = '';

	/**
	 * External callback triggers when the loader finished.
	 *
	 * @private
	 * @type {Function}
	 * @memberof AssetLoader
	 */
	private _onComplete: Function;

	private isLoading = false;

	/**
	 * An url that contains {locale} will be replaced to user's language.
	 *
	 * @private
	 * @memberof AssetLoader
	 */
	private regex = /({locale})/g;
	private partnerRegex = /({partner})/g;

	/**
	 * key: groupIndex
	 *
	 * @private
	 * @type {TsMap<number, ResourceGroupStatus>}
	 * @memberof AssetLoader
	 */
	private resourceGroup: TsMap<number, ResourceGroupStatus> = new TsMap<number, ResourceGroupStatus>();

	/**
	 * key: groupIndex
	 *
	 * @private
	 * @type {TsMap<number, Array<Asset>>}
	 * @memberof AssetLoader
	 */
	private resourceURLMap: TsMap<number, Array<Asset>> = new TsMap<number, Array<Asset>>();

	/**
	 * {key: resource_file_name, value: groupIndex}
	 *
	 * @private
	 * @type {TsMap<string, number >}
	 * @memberof AssetLoader
	 */
	private resourceMap: TsMap<string, number> = new TsMap<string, number>();

	/**
	 * The load sequence.
	 *
	 * @private
	 * @type {Array<number>}
	 * @memberof AssetLoader
	 */
	private order: Array<number>;

	private currentGroup: number = -1;

	private lazyGroupFileList: Array<string> = new Array<string>();

	private loadingState: AssetLoaderState = AssetLoaderState.LOADING;

	public init (): void {
		this.postfix = Browser.isMobile() ? '@1x.' : '@2x';

		// original resource loader doesn't reconize the .mp3 extension, so add the following code:
		PIXI.loaders.Resource.setExtensionLoadType( 'mp3', PIXI.loaders.Resource.LOAD_TYPE.XHR );
		PIXI.loaders.Resource.setExtensionXhrType( 'mp3', PIXI.loaders.Resource.XHR_RESPONSE_TYPE.BLOB );

		PIXI.loaders.Loader.addPixiMiddleware( this.beforeFetchResource.bind( this ) );
		PIXI.loaders.Loader.addPixiMiddleware( this.beforeFetchAudio.bind( this ) );
		PIXI.loader.pre( this.beforeFetchAudio.bind( this ) );
		PIXI.loader.pre( this.beforeFetchResource.bind( this ) );

		this.setLoadSequence( [
			ResourceConfig.PRELOAD,
			ResourceConfig.MAIN_GAME
		] );
		this.addEventListeners();
	}

	public addEventListeners (): void {
		GlobalDispatcher.addListener( this, AssetLoader.MONITOR_GROUP, this.updateLoadPriority );
	}

	/**
	 * Trigger loader to load assets.
	 *
	 * @param {Function} [onComplete]
	 * @param {number} [groupIdx] - used for on-demand load
	 * @returns {void}
	 * @memberof AssetLoader
	 */
	public start ( onComplete?: Function, groupIdx?: number ): void {
		if ( this.isLoading ) {
			// onDemand load, add it to head.
			if ( groupIdx !== undefined && !this.resourceGroup.get( groupIdx ).isReady
				&& this.order.indexOf( groupIdx ) === -1 && groupIdx !== this.currentGroup ) {
				this.order.unshift( groupIdx );
			}
			return;
		} else if ( groupIdx !== undefined ) {
			const index = this.order.indexOf( groupIdx );
			if ( this.resourceGroup.get( groupIdx ).isReady ) {
				console.warn( `The resource group[${ groupIdx }] is already loaded.` );
				return;
			} else if ( index !== -1 ) {
				this.order.splice( index, 1 );
			}
		}

		this.reset();
		this.currentGroup = ( groupIdx === undefined ) ? this.order.shift() : groupIdx;

		console.log( '[AssetLoader] START LOAD', this.currentGroup );

		this.resourceURLMap.get( this.currentGroup ).forEach( ( asset ) => {
			this.loader.add( asset.assetKey, this.toTargetPath( asset.assetURL ) );
		} );

		this._onComplete = onComplete;
		this.loader.onError.add( this.onError.bind( this ) );
		this.loader.onComplete.add( this.onComplete.bind( this ) );
		this.loader.onProgress.add( this.onProgress.bind( this ) );
		this.loader.load();
		this.isLoading = true;
	}

	public add ( target: Array<Asset> ): void {
		target.forEach( asset => {
			const filenameExt = this.filename( asset.assetURL );

			let groupIdx = ( asset.group === undefined ) ? ResourceConfig.MAIN_GAME : asset.group;

			if ( Browser.isMobile() ) {
				if ( this.viewport.isPortraitMode && asset.group === ResourceConfig.PORTRAIT_VIEW ||
					!this.viewport.isPortraitMode && asset.group === ResourceConfig.LANDSCAPE_VIEW ) {
					groupIdx = ResourceConfig.MAIN_GAME;
				}
			} else if ( asset.group === ResourceConfig.PORTRAIT_VIEW || asset.group === ResourceConfig.LANDSCAPE_VIEW ) {
				groupIdx = ResourceConfig.MAIN_GAME;
			}

			if ( !this.resourceURLMap.has( groupIdx ) ) {
				this.resourceURLMap.set( groupIdx, [] );
			}

			if ( !this.resourceGroup.has( groupIdx ) ) {
				this.resourceGroup.set( groupIdx, {
					isReady: false,
					onDemandLoad: asset.onDemandLoad || false,
					files: [],
					totalNum: 0,
					loadedNum: 0
				} );
			}


			if ( !this.resourceGroup.get( groupIdx ).onDemandLoad && this.order.indexOf( groupIdx ) === -1 ) {
				this.order.push( groupIdx );
				this.order.sort( ( a, b ) => { // sort order in asc.
					return a - b;
				} );
			}

			this.resourceURLMap.get( groupIdx ).push( asset );
			this.resourceGroup.get( groupIdx ).files.push( filenameExt );
			this.resourceGroup.get( groupIdx ).totalNum += 1;
			this.resourceMap.set( this.toTargetPath( asset.assetURL ).split( '.' )[ 0 ], groupIdx );

			// create a load file list in group, when error occurred, try to find the file name from list
			if ( groupIdx !== ResourceConfig.PRELOAD && groupIdx !== ResourceConfig.MAIN_GAME ) {
				this.lazyGroupFileList.push( filenameExt );
				/* this is a temporarily solution, since based on current config setting
				 * we don’t know what kind of resource it is, so there will be few situations
				 *  1. spine file will contains three files
				 *      a. file_name.json
				 *      b. file_name.atlas
				 *      c. file_name.png
				 *  2. texture sprite will contains two files
				 *      a. file_name.json
				 *      b. file_name.png
				 *  3. particle file
				 *      a. file_name.json
				 *  4. language file
				 *      a. file_name.json
				 *
				 * so since we don’t know the correct resource type,
				 * here is assumed all the json resource referring to a spine file
				 */
				if ( filenameExt.split( '.' )[ 1 ] === 'json' ) {
					let name = filenameExt.split( '.' )[ 0 ];
					this.lazyGroupFileList.push( name.concat( '.atlas' ) );
					this.lazyGroupFileList.push( name.concat( '.png' ) );
				}
			}
		} );
		// console.log( '[AssetLoader] resourceGroup', this.resourceGroup );
		// console.log( '[AssetLoader] resourceURLMap', this.resourceURLMap );
		// console.log( '[AssetLoader] resourceMap', this.resourceMap );
		// console.log( '[AssetLoader] order', this.order );
	}

	public reset ( clean: boolean = false ): void {
		if ( this.isLoading ) {
			return;
		}
		this.loader.onComplete.detachAll();
		this.loader.onProgress.detachAll();
		this.loader.onError.detachAll();
		if ( clean ) {
			this.loader.reset();
		}
	}

	public detachAll (): void {
		if ( this.isLoading ) {
			return;
		}
		this.loader.onComplete.detachAll();
		this.loader.onProgress.detachAll();
		this.loader.onError.detachAll();
	}

	public updateLoadPriority ( message: EventParams ): void {
		const group = message.data.group;

		const pendingGroups = new Array<number>();

		if ( !Array.isArray( group ) ) {
			return;
		}

		group.forEach( ( name ) => {
			if ( this.isResourceReady( name ) || this.currentGroup === name ) {
				return;
			}
			pendingGroups.push( name );
			const index = this.order.indexOf( name );
			if ( index !== -1 ) {
				this.order.splice( index, 1 );
			}

		} );
		this.order = pendingGroups.concat( this.order );
	}

	protected onProgress ( _data, resource ): void {
		const groupIdx = this.resourceMap.get( resource.url.split( '.' )[ 0 ] );
		if ( groupIdx >= 0 ) {
			const group = this.resourceGroup.get( groupIdx );
			group.loadedNum += 1;
			this.onGroupProgress.dispatch( +groupIdx, group.loadedNum / group.totalNum );
		}

		this.resourceGroup.forEach( ( group: ResourceGroupStatus, groupIdx: number ) => {
			if ( group.loadedNum === group.totalNum && !group.isReady ) {
				group.isReady = true;
				this.onGroupCompleted.dispatch( groupIdx );
			}
		} );
	}

	protected onError ( err, loader, resource ): void {
		console.error( '[AssetLoader] onError:', err );

		let filename = resource.url.replace( /^.*(\\|\/|\:)/, '' );
		if ( this.isResourceInLazyGroup( filename ) ) {
			this.loadingState = AssetLoaderState.ERROR;
		} else {
			this.loader.reset();
			this.errorHandleService.onReceiveError( { code: '-2' } );
		}
	}

	// if Preload and Main game array without this file, it means in Lazy group.
	private isResourceInLazyGroup ( filename: string ): boolean {
		return ( this.lazyGroupFileList.indexOf( filename ) !== -1 );
	}
	/**
	 * Fires on each time the loader completed.
	 *
	 * @protected
	 * @memberof AssetLoader
	 */
	protected onComplete (): void {
		this.isLoading = false;
		console.log( '[AssetLoader] FINISISH LOAD', this.currentGroup );

		if ( this._onComplete ) {
			this._onComplete( this.currentGroup );
		}

		if ( this.currentGroup === ResourceConfig.PRELOAD ) {
			this.reset( true );
		}

		if ( this.order.length > 0 ) {
			this.start();
		} else {
			this.loadingState = ( this.loadingState !== AssetLoaderState.ERROR ) ? AssetLoaderState.DONE : this.loadingState;
		}
	}

	/**
	 * @example 'assets/{locale}/sprite.png' will transfer to 'assets/cn/sprite.png' when locale === cn
	 */
	public toTargetPath ( url: string ): string {
		url = this.toTargetLanguagePath( url );

		url = this.toTargetPartnerPath( url );

		url = this.toTargetMD5HashPath( url );

		return url;
	}

	public toTargetLanguagePath ( url: string ): string {
		if ( this.regex.test( url ) ) {
			url = url.replace( this.regex, this.translator.language );
		}

		return url;
	}

	public toTargetPartnerPath ( url: string ): string {
		if ( this.partnerRegex.test( url ) ) {
			url = url.replace( this.partnerRegex, this.partnerHelper.partner );
		}

		return url;
	}

	public toTargetMD5HashPath ( url: string ): string {
		try {
			url = MD5HASH + '_' + url;
		} catch {
			console.warn( 'please add MD5HASH setting into your project' );
		}

		return url;
	}

	get resources (): PIXI.loaders.ResourceDictionary {
		return this.loader.resources;
	}

	public setLoadSequence ( array: Array<number> ) {
		this.order = array;
	}

	/**
	 * Check if the given resourceGroup id is fully loaded.
	 *
	 * @param {(number | Array<number>)} groupIndex
	 * @returns {boolean}
	 * @memberof AssetLoader
	 */
	public isResourceReady ( groupIndex: number | Array<number> ): boolean {
		if ( !Array.isArray( groupIndex ) ) {

			if ( !this.resourceGroup.has( groupIndex ) ) {
				return true;
			} else {
				return this.resourceGroup.get( groupIndex ).isReady;
			}
		} else {
			return groupIndex.reduce( ( accumulator, currentValue ) => {
				return accumulator && this.resourceGroup.get( currentValue ).isReady;
			}, true );
		}
	}

	public isLazyResourceReady (): AssetLoaderState {
		return this.loadingState;
	}

	public hasGroup ( groupIndex: number ): boolean {
		return ( this.resourceGroup.get( groupIndex ) !== undefined );
	}

	/**
	 *  Get completion rate of the group
	 *
	 * @param {number} groupIndex
	 * @returns {number}
	 * @memberof AssetLoader
	 */
	public getResourceRatio ( groupIndex: number ): number {
		const ratio = this.resourceGroup.get( groupIndex ).loadedNum / this.resourceGroup.get( groupIndex ).totalNum;
		return ratio;
	}

	private beforeFetchResource ( resource: PIXI.loaders.Resource, next ) {
		const filenameExt = this.filename( resource.url );
		const filename = resource.url.split( '.' )[ 0 ];
		const groupIndex = this.resourceMap.get( filename );
		if ( groupIndex >= 0 && this.resourceGroup.get( groupIndex ).files.indexOf( filenameExt ) === -1 ) {
			this.resourceGroup.get( groupIndex ).totalNum += 1;
		}

		next();
	}

	private beforeFetchAudio ( resource: PIXI.loaders.Resource, next ) {
		const audioExtensions: Array<string> = [ AudioExtension.WAV, AudioExtension.OGG, AudioExtension.MP3, AudioExtension.MGEG ];

		if ( resource && audioExtensions.indexOf( resource.extension ) !== -1 ) {

			// prevent fetch sound resource twice, let Howler.js handle sound loading.
			( resource as any )._setFlag( PIXI.loaders.Resource.STATUS_FLAGS.COMPLETE, true );

			( resource as any )._setFlag( PIXI.loaders.Resource.STATUS_FLAGS.LOADING, true );
			const options = JSON.parse( JSON.stringify( resource.metadata ) );
			options.src = [ resource.url ];

			const audioConfig: IHowlProperties = {
				...options,
			};

			const object = new NAudio( audioConfig );

			object.on( 'load', () => {
				( resource as any )._setFlag( PIXI.loaders.Resource.STATUS_FLAGS.LOADING, false );
				next();
			} );
			object.on( 'loaderror', ( _id, message ) => {
				this.errorHandleService.onReceiveError( { code: '-2' } );
				resource.abort( message );
			} );
			object.load();
			resource.data = object;
		} else {
			next();
		}
	}

	/**
	 * Get file name from given URL
	 *
	 * @private
	 * @param {string} url
	 * @returns {string}
	 * @memberof AssetLoader
	 */
	private filename ( url: string, extension: boolean = true ): string {
		return extension ? url.match( /[^\\/]+$/ )[ 0 ] : url.match( /[^\\/]+$/ )[ 0 ].split( '.' )[ 0 ];
	}
}

export type Asset = {
	/**
	 * Specify the resource group id. The loader will load  PRELOAD, MAIN_GAME, ..., {custom_group_id} in sequence.
	 * Default: ResourceConfig.MAIN_GAME
	 *
	 * @type {boolean}
	 */
	group?: number,

	/**
	 * set onDemandLoad=true if you want to on demand load the assets. Default value is false.
	 *
	 * @type {boolean}
	 */
	onDemandLoad?: boolean;

	/**
	 * For audio file. DEFAULT: true
	 *
	 * @type {boolean}
	 */
	playOnLoad?: boolean,

	assetKey?: string,
	assetURL?: string
};

export enum AudioExtension {
	WAV = 'wav',
	OGG = 'ogg',
	MP3 = 'mp3',
	MGEG = 'mpeg'
}

interface ResourceGroupStatus {

	/**
	 * isReady=true means the required resources by the group is fully loaded.
	 *
	 * @type {boolean}
	 * @memberof ResourceGroupStatus
	 */
	isReady: boolean;

	/**
	 * Is the group enables on demand load.
	 *
	 * @type {boolean}
	 */
	onDemandLoad: boolean;

	/**
	 * files name set. The length usually not equal to totalNum. For example, a .json file can be split to [.json, .atlas, .png]
	 *
	 * @type {Array<string>}
	 * @memberof ResourceGroupStatus
	 */
	files: Array<string>;

	/**
	 * The actual resouce amount that should be load.
	 *
	 * @type {number}
	 * @memberof ResourceGroupStatus
	 */
	totalNum: number;

	/**
	 * loaded amount
	 *
	 * @type {number}
	 * @memberof ResourceGroupStatus
	 */
	loadedNum: number;
}

export class AssetLoadErrorFormatter extends AbstractFormatter {
	public processIncomingMessage ( errorResponse: any ): any {
		return {
			type: ErrorDialogType.FULL_SCREEN,
			code: errorResponse.code,
			msg: errorResponse.msg,
			stack: ''
		};
	}
}

export enum AssetLoaderState {
	DONE = 'DONE',
	LOADING = 'LOADING',
	ERROR = 'ERROR'
}


