import * as MiniSignal from 'mini-signals';
import { Graphics, spine } from 'pixi.js';
import { Asset } from 'corengf/src/core/AssetLoader';
import { LazyCompView } from 'corengf/src/mvc/LazyCompView';
import { CoreUIElementFactory } from 'corengf/src/factory/CoreUIElementFactory';
import { ISpineSoundAnimationConfig } from 'corengf/src/elements/canvas/animation/spine/SpineSoundAnimation';
import { IAnimationViewConfig, AnimationViewConfig } from 'corengf/src/components/animationView/AnimationViewConfig';
import { SpineAnimation, MixinAttribute, ISpineAnimationConfig } from 'corengf/src/elements/canvas/animation/spine/SpineAnimation';

/**
 * Basic spine view handler
 *
 * TODO: support playing sprite.
 *
 * @export
 * @class AnimationView
 * @extends {CompView}
 */
export class AnimationView extends LazyCompView {
	protected animations: Array<SpineAnimation> = new Array<SpineAnimation>();
	protected config: IAnimationViewConfig;

	public animationCompleteSignal: MiniSignal = new MiniSignal();

	public initialize ( config?: AnimationViewConfig ): void {
		this.config = config;
		super.initialize( config );
	}

	public getAssetsList (): Array<Asset> {
		return [];
	}

	protected createElements ( config?: AnimationViewConfig ): void {
		super.createElements( config );
		this.setupAnimation();
		this.setupMask();
	}

	protected setupAnimation (): void {
		this.config.animations.forEach( ( animationElementData ) => {

			const animationData: ISpineAnimationConfig | ISpineSoundAnimationConfig = animationElementData.elementConfig;
			let animation: SpineAnimation = CoreUIElementFactory.createInstance( animationElementData );

			const scale = animationData.scale || { x: 1, y: 1 };
			animation.scale.set( scale.x, scale.y );

			const position = animationData.position || { x: 0, y: 0 };
			animation.position.x = position.x;
			animation.position.y = position.y;

			animation.displayObject.visible = false;
			animation.completeSignal.add( this.onComplete.bind( this ) );

			if ( animationData.mixins ) {
				animationData.mixins.forEach( ( mixins: MixinAttribute[], animationName: string ) => {
					mixins.forEach( ( mixin: MixinAttribute ) => {
						animation.setMix( animationName, mixin.animationName, mixin.mixinTime );
					} );
				} );
			}

			this.addChild( animation );
			this.animations.push( animation );
		} );
	}

	protected setupMask (): void {
		if ( this.config.mask ) {
			let maskRect = this.config.mask;
			let mask = new Graphics();
			mask.beginFill( 0 );
			mask.drawRect( maskRect.x, maskRect.y, maskRect.width, maskRect.height );
			this.mainContainer.mask = mask;
			this.addChild( mask );
		}
	}


	public playAnimation ( stateName: string, context?: any, skipOnDuplicate: boolean = true ): void {
		this.animations.forEach( ( spineAnimation, index ) => {
			const animationConfig = this.config.animations[ index ];

			if ( !animationConfig.elementConfig.stateAnimationMap.has( stateName ) ) {
				return;
			}

			spineAnimation.playByStateName( stateName, context, skipOnDuplicate );

		} );
	}

	protected onComplete ( animation: SpineAnimation, track: spine.core.TrackEntry ): void {
		if ( track.loop ) {
			return;
		}
		this.animationCompleteSignal.dispatch( track.animation.name );
	}
}


