import { Inject } from 'typescript-ioc/es5';
import { CompController } from './../../mvc/CompController';
import { IExternalModuleConfig, IAssetConfig } from './IExternalControl';
import { IExternalComponentConfig } from './IExternalComponentConfig';
import { ExternalModuleManager } from './ExternalModuleManager';
import { ExternalModuleView } from './ExternalModuleView';
import { AudioTrackName, SoundManager } from 'corengf/src/audio/SoundManager';
import { DataHookService } from 'corengf/src/core/DataHookService';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
/**
 *  A controller class for eval external JS.
 *
 * @export
 * @class ExternalModuleController
 * @extends {CompController}
 */
export class ExternalModuleController extends CompController {

	public static readonly CLOSE_EXTERNAL_MODULE: string = 'closeExternalModule';
	public static readonly OPEN_EXTERNAL_MODULE: string = 'openExternalModule';

	protected productLine: string = '';

	@Inject
	protected externalModule: ExternalModuleManager;

	@Inject
	protected soundManager: SoundManager;

	@Inject
	protected dataHook: DataHookService;

	protected view: ExternalModuleView;

	protected config: IExternalComponentConfig;

	protected initialized: boolean = false;

	protected moduleConfig: IExternalModuleConfig = {
		assets: null,
		handleOpen: null,
		handleClose: null,
		language: null,
		playSound: () => { return null; }
	};

	protected sendDataHook ( buttonId: string, action: string, actionData: any = {}, productLine: string = this.productLine ): void {

		const generalActionData = action ? { action } : { action: `action_${ buttonId.replace( 'BUTTON_', '' ) }` };

		this.dataHook.onButtonClicked( buttonId, productLine, Object.assign( generalActionData, actionData ) );
	}

	protected playSound ( soundKey: string ): void {
		this.soundManager.play( soundKey, AudioTrackName.EFFECT );

	}

	protected onOpen (): any {
		GlobalDispatcher.dispatchIntoAllStates( ExternalModuleController.OPEN_EXTERNAL_MODULE );
		return null;
	}

	protected onClose (): any {
		GlobalDispatcher.dispatchIntoAllStates( ExternalModuleController.CLOSE_EXTERNAL_MODULE );
		return null;
	}

	public init ( stateData?: any ): void {
		this.initializeView( true );
	}

	protected initializeView ( finishState: boolean = false ): void {
		if ( this.view != undefined ) {
			this.view.initialize( this.config );
			if ( ( this.view ).code ) {
				try {
					this.executeExternalcode( this.view.code );

					const createExternalMoudule = ( window as any ).createExternalMoudule;
					if ( createExternalMoudule ) {
						this.externalModule[ this.config.moduleName ] = createExternalMoudule( this.config.moduleName, this.moduleConfig );
						delete ( window as any ).createExternalMoudule;
					}
					this.initialized = true;
				} catch ( e ) {
					if ( finishState ) {
						this.finishStateAction();
					}
					console.error( `External module:${ this.config.moduleName } parse error.` );
				}
			}
		}
		if ( finishState ) {
			this.finishStateAction();
		}
	}

	protected convertAssetNames ( loader: PIXI.loaders.Loader, assetNames: string[] ): { [ key: string ]: IAssetConfig } {
		if ( !assetNames ) {
			return null;
		}
		const rtn: { [ key: string ]: IAssetConfig } = {};
		assetNames.forEach( element => {
			// sprite resource key will be ${element}_image, .png has no _image postfix;
			const url = loader.resources[ `${ element }_image` ] ? loader.resources[ `${ element }_image` ].url : loader.resources[ element ].url;
			const data = loader.resources[ `${ element }_image` ] ? loader.resources[ element ].data : null;
			rtn[ element ] = {
				url,
				data
			};
		} );
		return rtn;
	}

	protected executeExternalcode ( code: string ): void {
		const exe = new Function( code );
		exe();
	}
}



