import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { Momentum } from 'corengf/src/utils/Momentum';
import { Viewport } from 'corengf/src/display/Viewport';
import { Sprite as CoreSprite, ISprite } from 'corengf/src/elements/canvas/sprite/Sprite';
import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { InteractiveElement, IInteractiveElementConfig } from 'corengf/src/elements/canvas/InteractiveElement';
import { Point, Container, Graphics, interaction, Sprite } from 'pixi.js';
import { CommonAnimation, ITweenConfig, ICommonAnimationConfig } from 'corengf/src/elements/canvas/animation/CommonAnimation';
import { CoreElement } from 'corengf/src/elements/canvas/CoreElement';

// NOTE:
// In Desktop, PIXI triggers PointerEvent
// In mobile, PIXI triggers TouchEvent

/**
 * A container that is scrollable when content size greater than defined window size.
 * If there are interactive elements inside the container,
 * dev should call e.stopPropagation() in interactive event callback so that ScrollContainer can work properly.
 *
 * TODO: x-axis scrolling handling
 * @export
 * @class ScrollContainer
 * @extends {InteractiveElement}
 */
export class ScrollContainer extends InteractiveElement {

	private leaveBottomThreshold: number;

	private minimumScrollThreshold: number;

	@Inject
	protected viewport: Viewport;

	/**
	 * Invoked each time when scrolling to the bottom.
	 *
	 * @type {MiniSignal}
	 * @memberof ScrollContainer
	 */
	public onReachBottomSignal: MiniSignal = new MiniSignal();

	protected config: IScrollContainerConfig;

	private track: Graphics | Sprite;

	private scroll: Graphics | Sprite;

	private contentContainer: Container;

	private maskContainer: Container;

	private scrollBarContainer: Container;

	private scrollHint: CoreSprite;

	private isScrolling: boolean = false;

	private isScrollable: boolean = false;

	/**
	 * Flag that indicates if user triggers an avaliable scrolling action
	 *
	 * @private
	 * @type {boolean}
	 * @memberof ScrollContainer
	 */
	private isAvaliableMove: boolean = false;

	private momentum: Momentum;

	private startPos: IPoint = { x: 0, y: 0 };

	private pointerDownPosition: IPoint = { x: 0, y: 0 };

	private isEnableScrollBar: boolean = false;

	private isShowScrollHint: boolean = false;

	/**
	 * Presents current scroll bar offset
	 *
	 * @type {Point}
	 * @memberof ScrollContainer
	 */
	public offset: Point = new Point( 0, 0 );

	private maskSprite: Sprite;


	protected initElement ( config: IScrollContainerConfig ): void {
		this.momentum = new Momentum( {
			updateCallback: this.onUpdate.bind( this ),
			onStop: this.onScrollStop.bind( this ),
			boundX: [ 0, this.config.width ],
			boundY: [ -this.config.height, 0 ],
		} );
		this.interactive = true;
		this.leaveBottomThreshold = this.config.leaveBottomThreshold || 0;
		this.minimumScrollThreshold = this.config.minimumScrollThreshold || 0;


		// TODO: enable scroll bar when handle orientation properly
		this.isEnableScrollBar = false;  //this.config.scrollBar ? true : false;

		this.contentContainer = new Container();
		this.scrollBarContainer = new Container();
		this.maskContainer = new Container();
		this.maskContainer.addChild( this.contentContainer );

		this.setupMask( this.config.width, this.config.height );

		this.addChild( this.maskContainer );

		this.addChild( this.scrollBarContainer );

		if ( this.isEnableScrollBar ) {
			this.createScrollBar();
		}
		if ( this.config.scrollHintImg ) {
			this.setupScrollHint();
		}

		this.addListeners();
	}

	protected setupScrollHint (): void {
		const config = this.config.scrollHintImg;


		this.scrollHint = new CoreSprite( config );
		this.scrollHint.anchor.x = ( config.anchor ) ? config.anchor.x : 0.5;
		this.scrollHint.x = ( config.position ) ? config.position.x : ( this.config.width / 2 );
		this.scrollHint.y = ( config.position ) ? config.position.y : ( this.config.height - this.scrollHint.height - 15 );

		this.scrollHint.visible = false;

		if ( config.showTween ) {
			config.showTween.target = this.scrollHint;
		}

		if ( config.hideTween ) {
			config.hideTween.target = this.scrollHint;
		}

		this.addChild( this.scrollHint );
	}

	protected onReachBottom (): void {
		this.hideScrollHint();
		this.onReachBottomSignal.dispatch();
	}

	protected showScollHint (): void {
		if ( !this.scrollHint || this.isShowScrollHint ) {
			return;
		}
		this.scrollHint.visible = true;
		const config = this.config.scrollHintImg;

		if ( config.showTween ) {
			this.tweenScrollHint( config.showTween.tweenConfig );
		}
		this.isShowScrollHint = true;
	}

	protected hideScrollHint (): void {
		if ( !this.scrollHint || !this.isShowScrollHint ) {
			return;
		}

		const config = this.config.scrollHintImg;

		if ( config.hideTween ) {
			this.tweenScrollHint( config.hideTween.tweenConfig );
		} else {
			this.scrollHint.visible = false;
		}
		this.isShowScrollHint = false;
	}

	private tweenScrollHint ( config: ITweenConfig ): void {
		//@ts-ignore
		CommonAnimation.startTween( config ).start();
	}

	protected onScrolling (): void {
		if ( Math.abs( Math.abs( this.contentContainer.y ) - this.getBoundY() ) > this.leaveBottomThreshold && this.scrollHint ) {
			this.showScollHint();
		}
	}

	private addListeners (): void {
		this.maskContainer.on( 'pointerdown', this.pointerdown, this );
		this.maskContainer.on( 'pointermove', this.pointermove, this );

		const element = document.getElementsByTagName( 'canvas' )[ 0 ];
		element.addEventListener( 'pointerup', this.pointerup.bind( this ), false );
		element.addEventListener( 'pointerout', this.pointerup.bind( this ), false );

		if ( !this.isEnableScrollBar ) { // scroll bar doen't handle wheel event properly.
			element.addEventListener( 'wheel', this.wheel.bind( this ), false );
		}
	}

	protected wheel ( e ): void {
		if ( this.isScrolling ) {
			return;
		}

		// reset startPos
		if ( this.startPos.x !== this.contentContainer.x ) {
			this.startPos.x = this.contentContainer.x;
		}
		if ( this.startPos.y !== this.contentContainer.y ) {
			this.startPos.y = this.contentContainer.y;
		}

		// wheel up, scroll content down
		// deltaY is 100, but when container move 100px at one time that looks not smooth. so reduce a half.
		this.startPos.y -= ( e.deltaY * 0.5 );
		if ( this.startPos.y > 0 ) {
			this.startPos.y = 0;
		} else if ( this.startPos.y < -this.getBoundY() ) {
			this.startPos.y = -this.getBoundY();
			this.onReachBottom();
		}

		this.onScrolling();

		this.offset.y = Math.abs( this.startPos.y );

		this.contentContainer.y = this.startPos.y;

		// when the wheel move finish, will reset target's postion of momentum.
		this.momentum.resetTargetPostion( this.contentContainer.x, this.contentContainer.y );
	}

	protected pointerdown ( e: interaction.InteractionEvent ): void {
		this.isScrolling = true;
		this.pointerDownPosition = this.getPositionFromEvent( e );
		this.momentum.start( this.pointerDownPosition );
	}

	protected onUpdate ( data: IPoint ): void {
		if ( this.contentContainer.y === data.y ) {
			return;
		}
		this.onScrolling();

		this.contentContainer.y = data.y;
		this.offset.y = Math.abs( data.y );

		if ( this.isEnableScrollBar && this.isScrollable ) {
			const ratio = Math.abs( data.y ) / this.getBoundY();
			const scrollY = ratio * Math.abs( this.config.height - this.scroll.height );
			this.scroll.y = scrollY;
		}

		if ( this.isReachBottom() ) {
			this.onReachBottom();
		}
	}

	protected isReachBottom (): boolean {
		return Math.abs( this.contentContainer.y ) === this.getBoundY();
	}

	protected pointermove ( e: interaction.InteractionEvent ): void {
		if ( !this.isScrolling ) {
			return;
		}

		const point = this.getPositionFromEvent( e );
		if ( !this.isTriggerAvaliableMove( point ) ) {
			return;
		} else if ( !this.isAvaliableMove ) {
			this.isAvaliableMove = true;
			this.momentum.end();
			this.momentum.start( point );
		}

		this.momentum.move( point );
	}

	protected pointerup ( e: interaction.InteractionEvent ): void {
		if ( this.isScrolling === false ) {
			return;
		}
		const point = this.getPositionFromEvent( e );
		if ( !this.isTriggerAvaliableMove( point ) ) {
			this.onScrollStop();
		}

		this.momentum.end();
	}

	protected isTriggerAvaliableMove ( point: IPoint ): boolean {
		return Math.abs( this.pointerDownPosition.y - point.y ) >= this.minimumScrollThreshold;
	}

	protected onScrollStop (): void {
		this.isScrolling = false;
		this.isAvaliableMove = false;
	}

	protected getPositionFromEvent ( e: any ): IPoint {
		if ( e.data && ( e.data.originalEvent as TouchEvent ).touches ) {
			if ( ( e.data.originalEvent as TouchEvent ).touches[ 0 ] === undefined ) {
				return {
					x: e.clientX,
					y: e.clientY
				};
			} else {
				return {
					x: ( e.data.originalEvent as TouchEvent ).touches[ 0 ].clientX,
					y: ( e.data.originalEvent as TouchEvent ).touches[ 0 ].clientY
				};
			}

		} else if ( e.clientX || e.clientY ) {
			return {
				x: e.clientX,
				y: e.clientY
			};
		} else {
			return {
				x: ( e.data.originalEvent as PointerEvent ).clientX,
				y: ( e.data.originalEvent as PointerEvent ).clientY
			};
		}
	}

	protected setupMask ( width: number = 0, height: number = 0 ): void {
		if ( !width && !height ) {
			return;
		}

		if ( this.maskSprite ) {
			this.maskContainer.mask.width = width;
			this.maskContainer.mask.height = height;
			return;
		}

		// mask with gradient
		const customMask = new Container();

		const gradientKey = ( this.config ) ? this.config.gradientMaskAssetKey || '' : '';
		const img = Sprite.from( gradientKey );
		img.scale.x = this.config.width / img.width;
		img.y = height - img.height;

		const mask = new Graphics();
		mask.beginFill( 0xffffff, 1 );
		mask.drawRect( 0, 0, width, height - img.height );
		mask.endFill();

		customMask.addChild( mask );
		customMask.addChild( img );
		const finalMask = new Sprite( this.viewport.renderer.generateTexture( customMask ) );
		this.maskSprite = finalMask;
		this.maskContainer.addChild( finalMask );
		this.maskContainer.mask = finalMask;
	}

	public addContent ( object: CoreElement ): void {
		this.contentContainer.addChild( object );

		this.updateContentHeight();
	}

	public updateContentHeight (): void {
		if ( this.isEnableScrollBar ) {
			this.updateScrollItemHeight();
			this.updateScrollBarVisible();
		}

		if ( this.scrollHint ) {
			this.showScollHint();
		}

		this.momentum.setBoundY( [ -this.getBoundY(), 0 ] );
	}

	protected getBoundY (): number {
		return ( this.contentContainer.height > this.config.height ) ? Math.abs( this.contentContainer.height - this.config.height ) : 0;
	}

	protected updateScrollItemHeight (): void {
		let scrollSizeRatio = ( this.config.height / this.contentContainer.height );
		if ( scrollSizeRatio > 1 ) {
			return;
		}
		const scrollHeight = this.config.height * scrollSizeRatio;
		this.scroll.height = scrollHeight;
	}

	protected createScrollBar (): void {
		this.createScrollTrack();
		this.createScroll();
		this.updateScrollBarVisible();
	}

	protected updateScrollBarVisible (): void {
		if ( !this.shouldShowScrollBar() ) {
			this.hideScrollBar();
		} else {
			this.showScrollBar();
		}
	}

	protected shouldShowScrollBar (): boolean {
		const scrollSizeRatio = ( this.config.height / this.contentContainer.height );
		if ( scrollSizeRatio < 1 ) {
			this.isScrollable = true;
			return true;
		}
		this.isScrollable = false;
		return false;
	}

	protected createScrollTrack (): void {
		// const trackWidth = this.config.scrollBar.trackWidth || 20;
		// const trackHeight = this.config.height;
		// const color = this.config.scrollBar.trackColor || 0;
		// const alpha = this.config.scrollBar.trackAlpha || 0.3;
		// if ( !trackHeight ) {
		// 	return;
		// }

		// this.track = new Graphics();
		// this.track.beginFill( color, alpha );
		// this.track.drawRoundedRect( this.config.width - trackWidth, 0, trackWidth, this.config.height, Math.floor( trackWidth / 2 ) );
		// this.track.endFill();
		// this.scrollBarContainer.addChild( this.track );
	}

	protected createScroll (): void {
		// const scrollWidth = this.config.scrollBar.scrollWidth || 20;
		// const color = this.config.scrollBar.scrollColor || 0;
		// this.scroll = new Graphics();
		// this.scroll.beginFill( color, 1 );
		// this.scroll.drawRoundedRect( this.config.width - scrollWidth, 0, scrollWidth, this.config.height, Math.floor( scrollWidth / 2 ) );
		// this.scroll.endFill();
		// this.scrollBarContainer.addChild( this.scroll );
	}

	protected showScrollBar (): void {
		this.scrollBarContainer.visible = true;
	}

	protected hideScrollBar (): void {
		this.scrollBarContainer.visible = false;
	}

	public updateAttributes ( config: IScrollContainerConfig ): void {
		super.updateAttributes( config );
		this.setupMask( config.width, config.height );

		this.config.width = config.width;
		this.config.height = config.height;

		if ( this.scrollHint ) {
			this.scrollHint.updateAttributes( config.scrollHintImg );
		}

		this.momentum.setBoundY( [ -this.getBoundY(), 0 ] );
	}
}

export interface IScrollContainerConfig extends IInteractiveElementConfig {
	height: number;
	width: number;

	/**
	 * Set scrollBar config to render graphic-based scroll bar, it's very beginning.
	 * Overwrite the mehtod and use image rendering instead.
	 *
	 * TODO: support drag on scroll bar, orientation issue
	 * @type {ScrollBarConfig}
	 * @memberof IScrollContainerConfig
	 */
	//scrollBar?: ScrollBarConfig;
	scrollHintImg?: ScrollHintImgConfig;
	/**
	 * Greater than the value will show the scrollHint if scrollHint is enabled.
	 * Default leaveBottomThreshold = 150;
	 * @type {number}
	 * @memberof IScrollContainerConfig
	 */
	leaveBottomThreshold?: number;

	/**
	 * Dev may need set this value to prevent trigger unexpected scrolling, especially when it contains interactive item.
	 *
	 * @type {number}
	 * @memberof IScrollContainerConfig
	 */
	minimumScrollThreshold?: number;

	gradientMaskAssetKey?: string;
}

export interface ScrollBarConfig {
	autohide?: boolean;
	trackWidth?: number;
	trackColor?: number;
	trackAlpha?: number;
	scrollWidth?: number;
	scrollColor?: number;
}

export interface ScrollHintImgConfig extends ISprite {
	showTween?: ICommonAnimationConfig;
	hideTween?: ICommonAnimationConfig;
}


