import { IWildLocked } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { ReelActionModel, IReelActionModelConfig } from 'slotngf/src/components/reelV2/ReelActionModel';

export abstract class WildLock extends ReelActionModel {

	public key: string = 'WILDLOCK';

	protected resolveForStop: Function;

	/**
	 * Keeps the final wild locked data
	 *
	 * @protected
	 * @type {IWildLocked}
	 * @memberof WildLock
	 */
	protected data: IWildLocked = null;

	/**
	 * The wild locked data of current round
	 *
	 * @protected
	 * @type {IWildLocked}
	 * @memberof WildLock
	 */
	protected currentData: IWildLocked = null;

	protected totalLockCount: number = 0;

	protected currentLockededCount: number = 0;

	public run ( data: IWildLocked ): Promise<any> {

		this.currentData = data;
		this.totalLockCount = 0;
		this.currentLockededCount = 0;

		return new Promise( ( resolve ) => {
			this.resolveForStop = resolve;
		} );
	}

	protected abstract showLockAnimationAt ( reelId: number, symbolId: number, layer?: number ): void;

	protected updateWildLockData ( data: IWildLocked ): void {
		//
	}

	protected onWildLockCompleted ( reelId: number, symbolId: number ): void {
		this.currentLockededCount++;
		if ( this.currentLockededCount === this.totalLockCount ) {
			this.onAllWildLockCompleted();
		}

	}

	protected onAllWildLockCompleted (): void {
		this.updateWildLockData( this.currentData );
		if ( this.resolveForStop ) {
			this.resolveForStop();
			this.resolveForStop = null;
		}
	}

	protected reset (): void {
		//
	}

}

export interface IWildLockConfig extends IReelActionModelConfig {
}


