import { SingleWildMultiplierFeatureFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/complete/SingleWildMultiplierFeatureFormatter';
import { WildMultiplier } from 'slotngf/src/components/lines/WinLine';
import { ISingleWildMultiplier } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { IWinningData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { ISpinResult, IFeatureInfo } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';

export class DHBSingleWildsMultiplierFeatureFormatter extends SingleWildMultiplierFeatureFormatter {


	public getFeatureShowWinningData ( winningData: IWinningData, spinResult: ISpinResult, featureInfo: IFeatureInfo ): IWinningData {

		winningData.winLines.forEach( ( winline ) => {
			let wildMultiplier: Array<WildMultiplier> = Array<WildMultiplier>();

			winline.reelPositions.forEach( ( reel, index ) => {

				reel.forEach( ( symbol ) => {

					if ( spinResult.reels[ index ].symbols[ symbol ].symbol == 'WILD' ) {
						let multiplier: WildMultiplier = {
							reel_index: index,
							symbol_index: symbol,
							multiplier: ( featureInfo.feature_state as ISingleWildMultiplier ).wild_multiplier
						};
						wildMultiplier.push( multiplier );
					}
				} );

			} );
			winline.wildMultiplier = wildMultiplier;
		} );

		return winningData;
	}
}


