import * as MiniSignal from 'mini-signals';
import { Singleton } from 'typescript-ioc/es5';

@Singleton
export class BankService {
	private _balance: number = 0;
	private _bet: number = 0;
	private _win: number = 0;

	public currency: string = '';

	/**
	 * Bet options should be sorted everytime
	 */
	private _betOptions: Array<number> = [];

	private _currentBetIndex: number = 0;

	public onBalanceUpdatedSignal: MiniSignal;
	public onBetUpdatedSignal: MiniSignal;
	public onWinUpdatedSignal: MiniSignal;

	get maxBet (): number {
		return this._betOptions[ this._betOptions.length - 1 ];
	}

	get minBet (): number {
		return this._betOptions[ 0 ];
	}

	get balance (): number {
		return this._balance;
	}

	set balance ( newBalance: number ) {
		this._balance = newBalance;
		this.onBalanceUpdatedSignal.dispatch( this.balance );
	}

	get bet (): number {
		return this._bet;
	}

	set bet ( newBet: number ) {
		let newBetIndex = this._betOptions.indexOf( newBet );
		if ( newBetIndex >= 0 ) {
			this._bet = newBet;
			this._currentBetIndex = newBetIndex;
			this.onBetUpdatedSignal.dispatch( this.bet );
		} else {
			console.warn( 'Bank service can not set bet ' + newBet + ' because that bet value is illegal' );
		}
	}

	get totalBet (): number {
		return this._bet;
	}

	get betOptions (): Array<number> {
		return this._betOptions;
	}

	set betOptions ( newBetOptions: Array<number> ) {
		if ( newBetOptions ) {
			this._betOptions = newBetOptions.slice( 0 );
			this._betOptions.sort( ( a, b ) => a - b );
		}
	}

	public get win (): number {
		return this._win;
	}

	public set win ( value: number ) {
		this._win = value;
		this.onWinUpdatedSignal.dispatch( this.win );
	}

	constructor () {
		this.onBalanceUpdatedSignal = new MiniSignal();
		this.onBetUpdatedSignal = new MiniSignal();
		this.onWinUpdatedSignal = new MiniSignal();
	}

	public increaseBet (): boolean {
		if ( this._currentBetIndex < this._betOptions.length - 1 ) {
			++this._currentBetIndex;
			this.bet = this._betOptions[ this._currentBetIndex ];
			return true;
		} else {
			console.warn( 'You can\'t increaseBet because currentBet == maxBet' );
			return false;
		}
	}

	public decreaseBet (): boolean {
		if ( this._currentBetIndex > 0 ) {
			--this._currentBetIndex;
			this.bet = this._betOptions[ this._currentBetIndex ];
			return true;
		} else {
			console.warn( 'You can\'t decreaseBet because currentBet == minBet' );
			return false;
		}
	}

	public minimumBet (): void {
		this._currentBetIndex = 0;
		this.bet = this._betOptions[ this._currentBetIndex ];
	}

	public maximumBet (): void {
		this._currentBetIndex = this._betOptions.length - 1;
		this.bet = this._betOptions[ this._currentBetIndex ];
	}

	public get currentBetIndex (): number {
		return this._currentBetIndex;
	}
}


