import * as PIXI from 'pixi.js';
import { ITickConsumer } from 'corengf/src/time/ITickConsumer';


export class PixiTickConsumer implements ITickConsumer {

	protected timeMs: number = 0;

	/**
	 * Main ticker of PIXI that is used for animations play.
	 */
	private mainPixiTicker: PIXI.ticker.Ticker;

	public constructor () {
		// We stop the main PIXI ticker to setup the ticker time in our custom update
		this.mainPixiTicker = PIXI.ticker.shared;
		this.mainPixiTicker.autoStart = false;
		this.mainPixiTicker.stop();
	}

	public isAlive (): boolean {
		return true;
	}

	public tick ( deltaTimeMs: number ) {
		this.timeMs += deltaTimeMs;
		this.mainPixiTicker.update( this.timeMs );
	}
}



