import { UrlParameters } from 'corengf/src/utils/UrlParameters';
import { AbstractApi } from 'corengf/src/server/abstract/AbstractApi';
import { SlotNGHttpTransport } from 'slotngf/src/server/ng/transport/SlotNGHttpTransport';
import { ISlotPathNGServerConfig, ISlotNGServerConfig } from 'slotngf/src/server/ng/config/ISlotNGServerConfig';

/**
 * game init api
 */
export class NGSlotApi extends AbstractApi {

	protected transport: SlotNGHttpTransport;
	protected config: ISlotNGServerConfig;

	public init ( transport: SlotNGHttpTransport, config: ISlotNGServerConfig ): void {
		this.config = config;
		this.transport = transport;
	}

	protected encapsulation ( data?: any ): void {
		//
	}

	protected get apiPath (): ISlotPathNGServerConfig {
		if ( UrlParameters.isRealMode() ) {
			return this.config.path;
		} else {
			return this.config.devPath;
		}
	}
}


