import * as MiniSignal from 'mini-signals';
import { Container, DisplayObject, Point, spine } from 'pixi.js';
import { Helper } from 'corengf/src/utils/Helper';
import { SlotCompView } from './../mvc/SlotCompView';
import { ISlotFreeSpinsConfig } from './SlotFreeSpinsConfig';
import { ViewportAlign } from 'corengf/src/display/Viewport';
import { Animation } from 'corengf/src/elements/canvas/animation/Animation';
import { WebTextField } from 'corengf/src/elements/canvas/text/WebTextField';

// TODO: refacter
export class SlotFreeSpinsView extends SlotCompView {
	protected readonly introOutroAnimationTrack = 0;
	protected readonly idleAnimationTrack = 1;
	protected readonly pickAnimationTrack = 2;

	protected pickAnimation: spine.Spine;
	protected backgroundAnimation: spine.Spine;

	public picks: Array<string>;

	public beforePickSignal: MiniSignal;
	public pickSignal: MiniSignal;
	public outroSignal: MiniSignal;
	public freeSpinsIntroSignal: MiniSignal;

	protected config: ISlotFreeSpinsConfig;

	constructor () {
		super();
		this.beforePickSignal = new MiniSignal();
		this.pickSignal = new MiniSignal();
		this.outroSignal = new MiniSignal();
		this.freeSpinsIntroSignal = new MiniSignal();
	}

	/**
	 * The method where we create and add elements to the view
	 */
	protected createElements ( config?: ISlotFreeSpinsConfig ): void {

		this.visible = false;

		if ( config != undefined ) {
			this.config = config;

			if ( this.config.backgroundAsset != Helper.EMPTY ) {
				this.backgroundAnimation = Animation.createSpineAnimation( this.config.backgroundAsset );
				this.addChild( this.backgroundAnimation );
			}

			if ( this.config.pickAsset != Helper.EMPTY ) {
				this.pickAnimation = Animation.createSpineAnimation( this.config.pickAsset );
				this.pickAnimation.scale.set( 0.5, 0.5 );
				this.pickAnimation.position.set( this.viewport.getAlignedPoint( ViewportAlign.CENTER_MIDDLE ).x, 0 );

				this.pickAnimation.skeleton.slots.forEach( ( value, i ) => {
					this.pickAnimation.slotContainers[ i ].name = value.data.name;
				} );

				this.pickAnimation.slotContainers.forEach( ( container: Container ) => {
					if ( container.name.indexOf( this.config.pickNamePrefix + this.config.pickName ) >= 0 ) {
						container.on( 'pointertap', this.onPickSelect, this );
						container.interactive = true;
					}
				} );

				this.pickAnimation.update( 0 );
				this.addChild( this.pickAnimation );
			}

			this.enableSelection( false );
		}
	}

	public setPicks ( picks: Array<string> ): void {
		this.picks = picks;
	}

	protected setPickLabels (): void {
		let titleLabel = new WebTextField( this.config.titleFont );
		titleLabel.scale.set( 1, -1 );
		titleLabel.anchor = new Point( 0.5, 0.5 );
		titleLabel.position.set( this.config.titleLabelOffsetX, this.config.titleLabelOffsetY );
		let titleSlotIndex = this.pickAnimation.skeleton.findSlotIndex( this.config.titleSlotName );
		let titleSlotContainer: Container = this.pickAnimation.slotContainers[ titleSlotIndex ];
		titleSlotContainer.addChild( titleLabel );

		this.picks.forEach( ( key: string, i: number ) => {
			let fontStyle = this.config.pickFont;
			fontStyle.name = fontStyle.key = key;
			let pickLabel: WebTextField = new WebTextField( fontStyle );
			let slotIndex = this.pickAnimation.skeleton.findSlotIndex( this.config.pickNamePrefix + this.config.pickName + ( i + 1 ).toString() );
			let slotContainer: Container = this.pickAnimation.slotContainers[ slotIndex ];
			pickLabel.scale.set( 1, -1 );
			pickLabel.anchor = new Point( 0.5, 0.5 );
			pickLabel.position.set( this.config.pickLabelOffsetX, this.config.pickLabelOffsetY );
			slotContainer.addChild( pickLabel );
		} );
	}

	protected onPickSelect ( e: PIXI.interaction.InteractionEvent ): void {
		let target: DisplayObject = e.target;
		let pickNumber: string = target.name.match( /\d+/ )[ 0 ];
		let pickIndex: number = parseInt( pickNumber ) - 1;
		this.beforePickSignal.dispatch();
		this.pickAnimation.state.setAnimation( this.pickAnimationTrack, this.config.pickAnimation + pickNumber, false );
		this.pickAnimation.state.tracks[ this.pickAnimationTrack ].listener = {
			complete: () => {
				this.pickSignal.dispatch( this.picks[ pickIndex ] );
			}
		};
		this.enableSelection( false );
	}

	public playIntro (): void {
		this.resetAnimationState();
		this.visible = true;

		if ( this.backgroundAnimation ) {
			this.backgroundAnimation.state.setAnimation( this.idleAnimationTrack, this.config.backgroundIntroAnimation, true );
		}

		this.pickAnimation.state.setEmptyAnimation( this.introOutroAnimationTrack, 0 );
		this.pickAnimation.state.setEmptyAnimation( this.idleAnimationTrack, 0 );
		this.pickAnimation.state.setAnimation( this.introOutroAnimationTrack, this.config.introAnimation, false );
		this.pickAnimation.state.setAnimation( this.idleAnimationTrack, this.config.idleAnimation, true );
		this.pickAnimation.updateTransform();
		this.pickAnimation.update( 0 );
		this.setPickLabels();

	}

	public enableSelection ( enabled: boolean ): void {
		this.mainContainer.interactive = this.mainContainer.interactiveChildren = enabled;
	}

	public playOutro (): void {
		if ( this.backgroundAnimation ) {
			this.backgroundAnimation.state.setAnimation( this.introOutroAnimationTrack, this.config.backgroundOutroAnimation, false );
		}

		this.pickAnimation.state.setAnimation( this.introOutroAnimationTrack, this.config.outroAnimation, false );
		this.pickAnimation.state.tracks[ this.introOutroAnimationTrack ].listener = {
			complete: this.onOutroComplete.bind( this )
		};
	}

	protected onOutroComplete (): void {
		if ( this.backgroundAnimation ) {
			this.backgroundAnimation.state.clearListeners();
		}
		this.pickAnimation.state.clearListeners();

		this.visible = false;
		this.resetAnimationState();

		this.outroSignal.dispatch();
	}

	protected resetAnimationState (): void {
		if ( this.pickAnimation != undefined ) {
			this.pickAnimation.state.clearTracks();
			this.pickAnimation.state.clearListeners();
			this.pickAnimation.skeleton.setToSetupPose();
		}

		if ( this.backgroundAnimation != undefined ) {
			this.backgroundAnimation.state.clearTracks();
			this.backgroundAnimation.state.clearListeners();
			this.backgroundAnimation.skeleton.setToSetupPose();
		}
	}
}


