import { BlankTransport } from 'corengf/src/server/abstract/transport/BlankTransport';
import { TsMap } from 'corengf/src/utils/TsMap';

export class HttpTransport extends BlankTransport {
	/**
	 * Make http request to server
	 * @param method define the request is using GET or POST
	 * @param url
	 * @param name
	 * @param headers define headers of the request
	 * @param content define body of the request
	 */
	protected request ( method: HTTP_METHODS.GET | HTTP_METHODS.POST,
		url: string, name: string,
		headers: TsMap<string, string>, content?: Request, callback?: Function ): void {

		const request = new XMLHttpRequest();
		request.open( method, url, true );
		request.onload = ( event ) => {
			this.onLoad( name, event, callback );
		};

		request.onerror = this.onNetworkError.bind( this );

		if ( headers ) {
			headers.keys().forEach( ( key: string ) => {
				request.setRequestHeader( key, headers.get( key ) );
			} );
		}

		request.setRequestHeader( 'Accept', 'application/json' );

		request.responseType = 'json';

		request.send( JSON.stringify( content ) );
	}

	/**
	 * Dispatch network error
	 * @param reason
	 */
	protected onNetworkError ( reason: any ): void {
		if ( this.onError ) {
			this.onError( reason );
		}
	}

	/**
	 * callback function for get http response
	 *
	 * @protected
	 * @param {string} name
	 * @param {*} event
	 * @param {Function} [callback=null]
	 * @returns {void}
	 * @memberof HttpTransport
	 */
	protected onLoad ( name: string, event: any, callback: Function = null ): void {
		let req = event.target as XMLHttpRequest;
		if ( req.status >= 200 && req.status < 400 ) {
			if ( req.response == 'ERROR' && this.onError ) {
				if ( this.onError ) {
					this.onError( req.response );
					return;
				} else {
					throw new Error( 'Unhandled Network error.' );
				}
			}

			const payload = ( !req.response ) ? {} : req.response;

			const response: HttpResponse = {
				name,
				payload
			};

			if ( this.onReceive ) {
				this.onReceive( response );
			}

			if ( callback ) {
				callback( response );
			}
		} else {
			if ( this.onReceiveError ) {
				if ( callback ) {
					const response: HttpResponse = {
						name: 'ERROR',
						payload: req.response
					};

					callback( response.payload );
				}

				this.onReceiveError.forEach( ( cb: Function ) => {
					cb( req.response );
				} );

			}
		}
	}

	public psend ( data: IPackage ): Promise<any> {
		return new Promise( ( resolve, reject ) => {
			let url = data.url;
			let content = null;
			if ( data.method === HTTP_METHODS.GET ) {
				let urlParameters = '';
				for ( const param in data.urlParameters ) {
					if ( data.urlParameters[ param ] ) {
						urlParameters = urlParameters + `${ param }=${ data.urlParameters[ param ] }&`;
					}
				}
				if ( urlParameters !== '' ) {
					urlParameters = urlParameters.substring( 0, urlParameters.length - 1 );
					url = url + `?${ urlParameters }`;
				}
			} else {
				content = data.content;
			}

			this.request( data.method, url, data.name, data.headers, content, ( payload: any ) => {
				if ( payload.code ) {
					reject( payload );
				} else {
					resolve( payload );
				}
			} );
		} );
	}

}

export interface IPackage {
	method: HTTP_METHODS;
	url: string;
	headers: TsMap<string, string>;
	urlParameters?: Object;
	content?: Object;
	name: string;
}

export enum HTTP_METHODS {
	POST = 'POST',
	GET = 'GET'
}

interface HttpResponse {
	name: string;
	payload: any;
}



