import { Scenario } from 'corengf/src/states/Scenario';
import { CoreState } from 'corengf/src/states/CoreState';
import { IGameState, IFeatureInfo, IInitResponse } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { IFreeRespinReelsDetail, FeatureTypes } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { FreeRespinReelsFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/pending/FreeRespinReelsFormatter';

export class DHBFreeRespinReelsFormatter extends FreeRespinReelsFormatter {

	protected getTransitionToFreeRepinScenario ( responseScenario: Scenario, respinDetail: IFreeRespinReelsDetail,
		featureTriggeredInfo?: IFeatureInfo, message?: IGameState ): Scenario {
		let freeSpinFeatrue = message.features.find( e => e.type === FeatureTypes.FREE_SPIN );

		if ( freeSpinFeatrue == undefined ) {
			responseScenario.addState( new CoreState( this.stateNames.prefeatureWinning ) );
			responseScenario.addState( new CoreState( this.stateNames.startFreeRespin ) );
		}
		return responseScenario;
	}

	public restoreFeatureIncomingMessage (
		respinDetail: IFreeRespinReelsDetail,
		features: Array<IFeatureInfo>,
		type?: string, message?: IInitResponse ): Scenario {

		// getTransitionToFreeRepinScenario
		let responseScenario: Scenario = new Scenario( '', [] );
		let freeSpinFeatrue = features.find( e => e.type === FeatureTypes.FREE_SPIN );
		if ( freeSpinFeatrue == undefined ) {
			responseScenario.addState( new CoreState( this.stateNames.prefeatureWinning ) );
			responseScenario.addState( new CoreState( this.stateNames.startFreeRespin ) );
		}

		// getRespinningScenario
		let respinningScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.freeRespinReels );
		respinningScenario.setStateData( this.stateNames.freeRespinReels, respinDetail );
		responseScenario.addScenario( respinningScenario );


		return responseScenario;
	}

}


