import { UIButtonId } from 'corengf/src/components/ui/buttons/UIButtonId';

/**
 * Slot Button ID. Format: {elementType}_{elementName}
 *
 * @export
 * @class SlotUIButtonId
 * @extends {UIButtonId}
 */
export class SlotUIButtonId extends UIButtonId {

	public static BUTTON_AUTO_PLAY_SELECT_SPINS: string = 'BUTTON_AUTO_PLAY_SELECT_SPINS';

	// spin
	public static BUTTON_START_SPIN: string = 'BUTTON_START_SPIN';
	public static BUTTON_STOP_SPIN: string = 'BUTTON_STOP_SPIN';

	// turbo
	public static BUTTON_TURBO_MODE: string = 'BUTTON_TURBO_MODE';

	// demo
	public static BUTTON_SHOW_DEMO_PANEL: string = 'BUTTON_SHOW_DEMO_PANEL';

	// Auto Play Panel
	public static BUTTON_SHOW_AUTO_PLAY: string = 'BUTTON_SHOW_AUTO_PLAY';
	public static TOGGLE_SINGLE_WIN_LIMIT: string = 'TOGGLE_SINGLE_WIN_LIMIT';
	public static TOGGLE_WIN_LIMIT: string = 'TOGGLE_WIN_LIMIT';
	public static TOGGLE_LOSE_LIMIT: string = 'TOGGLE_LOSE_LIMIT';
	public static BUTTON_AUTO_PLAY_SPIN: string = 'BUTTON_AUTO_PLAY_SPIN';
	public static BUTTON_AUTO_PLAY_BACK: string = 'BUTTON_AUTO_PLAY_BACK';
	public static BUTTON_STOP_AUTO_PLAY: string = 'BUTTON_STOP_AUTO_PLAY';
	// Bet Panel
	public static BUTTON_HIDE_BET_OPTIONS: string = 'BUTTON_HIDE_BET_OPTIONS';
	public static BUTTON_CONFIRM_BET_OPTIONS: string = 'BUTTON_CONFIRM_BET_OPTIONS';
	public static BUTTON_BET_MAX: string = 'BUTTON_BET_MAX';
	public static BUTTON_BET_INCREASE: string = 'BUTTON_BET_INCREASE';
	public static BUTTON_BET_DECREASE: string = 'BUTTON_BET_DECREASE';
	public static BUTTON_BET_INCREASE_END: string = 'BUTTON_BET_INCREASE_END';
	public static BUTTON_BET_DECREASE_END: string = 'BUTTON_BET_DECREASE_END';
	public static BUTTON_LINE_INCREASE: string = 'BUTTON_LINE_INCREASE';
	public static BUTTON_LINE_DECREASE: string = 'BUTTON_LINE_DECREASE';
	public static BUTTON_LINE_INCREASE_END: string = 'BUTTON_LINE_INCREASE_END';
	public static BUTTON_LINE_DECREASE_END: string = 'BUTTON_LINE_DECREASE_END';

}


