import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { UrlParameters } from 'corengf/src/utils/UrlParameters';
import { AbstractApi } from 'corengf/src/server/abstract/AbstractApi';
import { ErrorHandleService } from 'corengf/src/core/ErrorHandleService';
import { ITransport } from 'corengf/src/server/abstract/transport/ITransport';
import { IServerConfig } from 'corengf/src/server/abstract/config/IServerConfig';
import { AbstractSdkController } from 'corengf/src/server/abstract/sdk/AbstractSdkController';

/**
 * Special non-view controller that handles server's business logic of game
 */
export abstract class AbstractServer {

	/**
	 * This signal provides scenario after server respond ( scenario : Scenario ) => {}
	 */
	public serverResponseSignal: MiniSignal;

	@Inject
	private _errorHandleService: ErrorHandleService;

	public get errorHandleService (): ErrorHandleService {
		return this._errorHandleService;
	}

	/**
	 * environment used for communication with end-point
	 */
	protected transport: ITransport;

	/**
	 * settings object for server and its transport
	 */
	protected config: IServerConfig;

	/**
	 * settings object for api and its formatter
	 */
	protected apiMapping: TsMap<string, AbstractApi>;

	constructor () {
		this.serverResponseSignal = new MiniSignal();
	}

	/**
	 * Override to specify target connection client for game
	*/
	public abstract setupTransport (): ITransport;

	/**
	 * Override to pick proper config for server
	 */
	protected abstract obtainConfig (): IServerConfig;

	/**
	 * Override to get specify sdk supporting list.
	 */
	protected abstract get sdkSupportList (): TsMap<string, AbstractSdkController>;

	public connect (): void {
		//get config first
		this.config = this.getServerConfig();
		this.transport = this.setupTransport();

		//do transport login call
		this.transport.setConfig( this.config );

		this.initAPIs();

		this.transport.onConnect = this.onConnect.bind( this );
		this.transport.onReceive = this.onReceive.bind( this );
		this.transport.onReceiveError.push( this.errorHandleService.onReceiveError.bind( this.errorHandleService ) );

		this.setupSDKErrorHandle();

		this.transport.onError = this.onNetworkError.bind( this );
		this.handshake();
	}

	protected setupSDKErrorHandle (): void {
		if ( UrlParameters.sdk && this.sdkSupportList.has( UrlParameters.sdk ) ) {
			this.transport.onReceiveError.push( this.sdkSupportList.get( UrlParameters.sdk ).onReceiveError );
		}
	}

	/**
	 * get server config form sdk or obtainConfig
	 * if sdkServerConfig != undefined, it will return sdkServerConfig
	 */
	protected getServerConfig (): IServerConfig {
		const sdkServerConfig: IServerConfig = this.getSdkServerConfig();
		if ( sdkServerConfig ) {
			return sdkServerConfig;
		} else {
			return this.obtainConfig();
		}
	}

	/**
	 * if there are any sdk been supported,
	 * return server config that need to be handle
	 */
	protected getSdkServerConfig (): IServerConfig {
		if ( UrlParameters.sdk && this.sdkSupportList.has( UrlParameters.sdk ) ) {
			return this.sdkSupportList.get( UrlParameters.sdk ).serverConfig;
		}
		return null;
	}

	/**
	 * init api mapping with sdk apis and server apis,
	 * please note if a api key already setup by sdk api, it won't set as server api.
	 */
	protected initAPIs (): void {
		if ( !this.apiMapping ) {
			this.apiMapping = new TsMap();

			// set sdk customize apis
			let apis = this.getSdkAPIs();
			apis.forEach( ( value, key ) => {
				this.addAPI( key, value );
			} );

			// set ragular apis, skip it if the kay already in the map
			apis = this.getServerAPIs();
			apis.forEach( ( value, key ) => {
				if ( !this.apiMapping.has( key ) ) {
					this.addAPI( key, value );
				}
			} );
		}
	}

	/**
	 * if there are any sdk been supported,
	 * return the api list that need to be handle
	 */
	protected getSdkAPIs (): TsMap<string, AbstractApi> {
		if ( UrlParameters.sdk && this.sdkSupportList.has( UrlParameters.sdk ) ) {
			return this.sdkSupportList.get( UrlParameters.sdk ).getSdkAPI();
		}
		return new TsMap();
	}

	/**
	 * retrun ragular apis list that need to be handle
	 */
	protected getServerAPIs (): TsMap<string, AbstractApi> {
		return new TsMap();
	}

	/**
	 * adding new api setting
	 * @param messageType
	 * @param api
	 */
	public addAPI ( messageType: string, api: AbstractApi ): void {
		if ( !messageType || !api ) {
			return;
		}

		if ( !this.apiMapping ) {
			this.apiMapping = new TsMap();
		}
		this.apiMapping.set( messageType, api );
		this.apiMapping.get( messageType ).init( this.transport, this.config );
	}

	/**
	 * Opens connection and initiate login if required
	 */
	protected handshake (): void {
		this.transport.connect();
	}

	/**
	 * Sends data coded by content-dependant formatter rule
	 * @param messageType type of outgoing message
	 * @param data data-object
	 */
	public abstract send ( messageType: string, data?: any ): void;

	protected abstract onConnect (): void;

	/**
	 * Override this to implement custom parsing of incoming message
	 * @param data data object from server
	 */
	public abstract onReceive ( data: any ): void;

	/**
	 * receives upcoming state
	 * @param {string} stateName
	 */
	public processState ( stateName: string, stateData: any ): void {
		// procesing the state
		if ( UrlParameters.sdk && this.sdkSupportList.has( UrlParameters.sdk ) ) {
			return this.sdkSupportList.get( UrlParameters.sdk ).processState( stateName, stateData );
		}
	}

	protected onNetworkError ( reason: any ): void {
		console.warn( '[AbstractServer] connection ERROR! reason - ' );
		console.warn( reason );
		if ( reason.type == 'error' ) {
			this.errorHandleService.onReceiveError( { code: '-1' } );
		}
	}

	public sendDataHookInfo ( data: any ): void {
		//
	}
}


