import { Inject } from 'typescript-ioc/es5';
import { MLUIBackgroundPanel } from './MLUIBackgroundPanel';
import { IMLUIBackgroundConfig, MLUIBackgroundConfig } from './MLUIBackgroundConfig';
import { UIPanelController } from 'corengf/src/components/ui/components/UIPanelController';


export class MLUIBackgroundController extends UIPanelController {
	protected view: MLUIBackgroundPanel;


	constructor ( @Inject view: MLUIBackgroundPanel ) {
		super( view );
	}

	public init (): void {
		let config = this.gameConfig.getConfig<IMLUIBackgroundConfig>( MLUIBackgroundConfig );

		this.view.initialize( config );
		this.finishStateAction();
	}

	public startPickFeature ( data: any ): void {
		this.view.visible = false;
		this.finishStateAction();
	}

	public endPickFeature ( data: any ): void {
		this.view.visible = true;
		this.finishStateAction();
	}

	public transitionToFreeSpins ( data: any ): void {
		this.view.visible = true;
		this.finishStateAction();
	}
}



