import { Easing } from '@tweenjs/tween.js';
import { Singleton } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Orientation } from 'corengf/src/display/Viewport';
import { IGameElement } from 'corengf/src/core/config/IMainConfig';
import { IReelsConfig, IReelStripConfig, IReelSet, IReelLayoutData, IMask, ReelMask, ISymbolDefinition, SpinDirection } from 'slotngf/src/core/config/IReelsConfig';

/**
 * IMPORTANT!
 * Set all parameters for reels: IReelStripConfig[] first in landscape mode.
 * Them you can set parameters portraitPositionX, portraitPositionY and portraitScale.
 * Reel Component will scale reel contrainer in portrait mode and also the reel container will change
 * position in portrait mode.
 *
 * ReelContainer.x in landscape = 0
 * ReelContainer.y in landscape = 0
 * ReelContainer.scale in landscape = 1
 *
 * ReelContainer.x in portrait = config.portraitPositionX
 * ReelContainer.y in portrait = config.portraitPositionY
 * ReelContainer.scale in portrait = config.portraitScale
 */
@Singleton
export class ReelsConfig implements IReelsConfig {

	public name: string = 'reels';

	protected reelY: number = 800;

	protected basicMask: IMask = {
		x: 10,
		y: 523,
		width: 700,
		height: 518
	};

	public anticipationData = {
		anticipationDelay: 1500,
		animations: [],
		//Each Model needs anticipationData
		modelData: [
			{
				anticipationSymbolCountTrigger: 2,
				anticipationSymbolCountRequired: 3,
			},
			{
				anticipationSymbolCountTrigger: 2,
				anticipationSymbolCountRequired: 3,
			}
		]
	};

	public idleAnimationLoop: boolean = true;
	public idleAnimationDelay: number = 0;
	public reelStopNormalTime: number = 500;

	public avaliableReelSet = [ ReelSet.BASE, ReelSet.FEATURE ];

	public orientationLayoutData: TsMap<Orientation, IReelLayoutData> = null;

	public orientationMaskData: TsMap<number, TsMap<Orientation, ReelMask>> = null;

	public defaultReelsMask = new TsMap( [
		[ ReelSet.BASE, this.basicMask ],
		[ ReelSet.FEATURE, this.basicMask ]
	] );

	public defaultLayoutData: IReelLayoutData = {
		position: { x: -89, y: 650 },
		scale: 0.8
	};

	public defaultReelPosition: Array<IReelLayoutData> = [
		{ position: { x: 130, y: 0 } },
		{ position: { x: 310, y: 0 } },
		{ position: { x: 480, y: 0 } },
		{ position: { x: 652, y: 0 } },
		{ position: { x: 823, y: 0 } }
	];

	public symbolDefinition: ISymbolDefinition = {
		generalSymbol: {
			width: 175,
			height: 175
		},
		specialSymbol: {}
	};

	public reelDefinition: IReelStripConfig = {
		width: 215,
		symbol: {
			width: 175,
			height: 175
		},
		rangeSymbols: [ 'A', 'K', 'Q', 'J', 'T', 'N', 'PIC1', 'PIC2', 'PIC3', 'PIC4', 'WILD', 'SCATTER' ],
		dropSymbols: [ 'WILD', 'SCATTER' ],
		symbolsOnReel: 3,
		reelTween: { duration: 100, ease: Easing.Linear.None },
		stopTween: { duration: 400, ease: Easing.Back.Out },
		startReelDelay: 0,
		stopReelNormalDelay: 200,
		countBottomExtraSymbol: 1,
		countTopExtraSymbol: 1
	};

	public reelSet: IReelSet = {
		[ ReelSet.BASE ]: [
			{
				width: 200,
				startReelDelay: 0,
				stopReelNormalDelay: 200
			},
			{
				startReelDelay: 100
			},
			{
				startReelDelay: 200
			}, {

				startReelDelay: 300
			},
			{
				startReelDelay: 400
			}
		],
		[ ReelSet.FEATURE ]: [
			{
				width: 200,
				startReelDelay: 0,
				stopReelNormalDelay: 200
			},
			{
				startReelDelay: 100
			},
			{
				startReelDelay: 200
			},
			{
				startReelDelay: 300
			},
			{
				startReelDelay: 400
			}
		],
	};

	public reels = [];

	public reelBackground?: IGameElement;
	public reelsFrames?: IGameElement[];

	public individualReelsMask = false;

	public defaultSpinDirection: SpinDirection = SpinDirection.SpinDown;
}

export class ReelSet {
	public static readonly BASE: number = 0;
	public static readonly FEATURE: number = 1;
}

export enum SymbolSet {
	N = 'N',
	A = 'A',
	J = 'J',
	Q = 'Q',
	K = 'K',
	T = 'T',
	PIC1 = 'PIC1',
	PIC2 = 'PIC2',
	PIC3 = 'PIC3',
	PIC4 = 'PIC4',
	SCATTER = 'SCATTER',
	SCATTER2 = 'SCATTER2',
	WILD = 'WILD'
}


