import { Helper } from 'corengf/src/utils/Helper';
import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { WebTextField } from 'corengf/src/elements/canvas/text/WebTextField';
import { ICompViewConfig } from 'corengf/src/core/config/ICompViewConfig';
import { ICoreUITextFieldElementConfig } from 'corengf/src/factory/CoreUIElementFactory';


export class DialogConfig implements IDialogConfig {
	public name = 'DialogWindow';
	public windowAsset = 'dialogWindow.png';
	public position: IPoint = {
		x: 360,
		y: 640
	};

	public title: ICoreUITextFieldElementConfig = {
		elementType: WebTextField.elementType,
		elementConfig: {
			name: 'dialogTitle',
			key: '',
			style: {
				fontFamily: Helper.DEFAULT_FONTS,
				fontSize: 44,
				fill: 'white',
				fontWeight: 'bold',
				strokeThickness: 4,
				align: 'center'
			}
		}
	};

	public text: ICoreUITextFieldElementConfig = {
		elementType: WebTextField.elementType,
		elementConfig: {
			name: 'dialogText',
			key: '',
			style: {
				fontFamily: Helper.DEFAULT_FONTS,
				fontSize: 24,
				fill: 'white',
				fontWeight: 'bold',
				strokeThickness: 4,
				align: 'left'
			}
		}
	};
}

export interface IDialogConfig extends ICompViewConfig {
	windowAsset: string;
	position: IPoint;
	title: ICoreUITextFieldElementConfig;
	text: ICoreUITextFieldElementConfig;
}


