import { INGWebSocketConfig } from 'corengf/src/server/ng/config/INGServerConfig';
import { NGServerConfig } from 'corengf/src/server/ng/config/NGServerConfig';
import { WebSocketFormatter } from 'corengf/src/server/abstract/formatter/WebSocketFormatter';

/**
 * Encapsulates code/encode rules for messages
 */
export class NGWebSocketFormatter extends WebSocketFormatter {
	protected _config: INGWebSocketConfig;
	get config (): INGWebSocketConfig {
		if ( this._config == undefined ) {
			this._config = this.gameConfig.getConfig<INGWebSocketConfig>( NGServerConfig );
		}
		return this._config;
	}
}



