import { Container, Graphics, Point, extras } from 'pixi.js';
import * as MiniSignal from 'mini-signals';
import { NMath } from 'corengf/src/utils/NMath';
import { IPaylineConfig } from './PaylineConfig';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import { Animation } from 'corengf/src/elements/canvas/animation/Animation';


export class Payline {

	/**
	 * line number in paylines configuration
	 */
	public lineNumber: number;

	/**
	 * Has all single payline elements embeded
	 */
	public container: Container;

	protected config: IPaylineConfig;
	protected mask: Graphics;
	protected elements: Array<PaylineElement>;
	protected timers: Array<SimpleTimer>;
	protected activeAnimationsCount: number = 0;

	public onCompleteSignal: MiniSignal = new MiniSignal();

	protected constructor ( config: IPaylineConfig, lineNumber: number ) {
		this.config = config;
		this.container = new Container();
		this.mask = new Graphics();
		this.container.addChild( this.mask );
		this.elements = [];
		this.timers = [];
		this.lineNumber = lineNumber;
	}

	/**
	 * Override this for symbol frame customization
	 */
	protected addFrame ( position: Point ): void {
		let assetNameParts = this.config.frameAssetName.split( '{}' );
		let sprite = Animation.createFrameAnimation(
			Animation.generateFrameNames( assetNameParts[ 0 ], 1, this.config.frameFR, assetNameParts[ 1 ], 2 ) );
		sprite.anchor.set( 0.5, 0.5 );
		sprite.position = position;
		sprite.loop = false;
		sprite.visible = false;
		sprite.animationSpeed = this.config.frameAnimSpeed;
		sprite.onComplete = () => {
			this.onElementAnimComplete( sprite );
		};
		this.container.addChild( sprite );
		this.elements.push( { type: ElementType.FRAME, sprite: sprite, delayMS: this.config.frameDisplayTimeMs } );
	}

	/**
	 * Override this for connection line customization
	 */
	protected addLine ( position: Point, angle: number, scale: number ): void {
		let assetNameParts = this.config.lineAssetName.split( '{}' );
		let sprite = Animation.createFrameAnimation(
			Animation.generateFrameNames( assetNameParts[ 0 ], 1, this.config.lineFR, assetNameParts[ 1 ], 2 ) );
		sprite.anchor.set( 0.5, 0.5 );
		sprite.position = position;
		sprite.rotation = angle;
		sprite.scale.set( scale, 1 );
		sprite.loop = false;
		sprite.visible = false;
		sprite.animationSpeed = this.config.lineAnimSpeed;
		sprite.onComplete = () => {
			this.onElementAnimComplete( sprite );
		};
		//sprite.mask = this.mask;
		this.container.addChild( sprite );
		this.elements.push( { type: ElementType.LINE, sprite: sprite, delayMS: this.config.lineDisplayTimeMs } );
	}

	/**
	 * Override this for dot customization
	 */
	protected addDot ( position: Point ): void {
		//TODO: add dot animation later
	}

	public play (): void {
		this.clearTimers();
		this.activeAnimationsCount = this.elements.length;
		let delayMs: number = 0;
		for ( let i = 0; i < this.elements.length; i++ ) {
			this.timers.push( new SimpleTimer( delayMs, () => {
				this.elements[ i ].sprite.visible = true;
				this.elements[ i ].sprite.gotoAndPlay( 0 );
			} ) );
			delayMs += this.elements[ i ].delayMS;
		}
	}

	public stop (): void {
		this.clearTimers();
		//hide all elements
		for ( let i = 0; i < this.elements.length; i++ ) {
			this.elements[ i ].sprite.visible = false;
		}
	}

	public clearTimers (): void {
		for ( let i = 0; i < this.timers.length; i++ ) {
			this.timers[ i ].stop( false );
		}
		this.activeAnimationsCount = 0;
	}

	protected onSequenceComplete (): void {
		this.clearTimers();
		//dispatch callback
		this.onCompleteSignal.dispatch( this );
	}

	protected onElementAnimComplete ( sprite: extras.AnimatedSprite ): void {
		sprite.visible = false;
		if ( --this.activeAnimationsCount <= 0 ) {
			this.onSequenceComplete();
		}
	}

	/**
	 * creates new animated payline
	 * @param positions relative coords of framed symbols
	 * @param winComboLenght symbols count that should have frame
	 * @param config meta for payline
	 * @param viewport viewport
	 */
	public static payline ( positions: Array<Point>, winComboLength: number,
		config: IPaylineConfig, lineNumber: number ): Payline {
		let payline = new Payline( config, lineNumber );
		for ( let i = 0; i < positions.length; i++ ) {
			//is current node is beginning of line?
			if ( i == 0 ) {
				if ( winComboLength > 0 ) {
					payline.addFrame( positions[ i ] );
				}
			} else {
				if ( winComboLength > 0 && winComboLength > i ) {
					//ensure that we display connection to previous node first
					payline.addLine( new Point( ( positions[ i ].x + positions[ i - 1 ].x ) / 2.0,
						( positions[ i ].y + positions[ i - 1 ].y ) / 2.0 ),
						NMath.angleBetweenPointsRad( positions[ i - 1 ], positions[ i ] ),
						NMath.distanceBetweenPoints( positions[ i - 1 ], positions[ i ] ) / config.lineWidth );
					payline.addFrame( positions[ i ] );
				} else {
					payline.addLine( new Point( ( positions[ i ].x + positions[ i - 1 ].x ) / 2.0,
						( positions[ i ].y + positions[ i - 1 ].y ) / 2.0 ),
						NMath.angleBetweenPointsRad( positions[ i - 1 ], positions[ i ] ),
						NMath.distanceBetweenPoints( positions[ i - 1 ], positions[ i ] ) / config.lineWidth );
					//add dot only if this is not the last node
					if ( positions.length - 1 > i ) {
						payline.addDot( positions[ i ] );
					}
				}
			}
		}
		return payline;
	}

	public destroy (): void {
		for ( let i = 0; i < this.elements.length; i++ ) {
			this.elements[ i ].sprite.destroy();
			this.elements[ i ].sprite = undefined;
		}
		this.elements = undefined;
		for ( let i = 0; i < this.timers.length; i++ ) {
			this.timers[ i ].destroy();
		}
		this.timers = undefined;
		this.container.destroy();
		this.container = undefined;
	}
}

class PaylineElement {
	public type: ElementType;
	public sprite: extras.AnimatedSprite;
	public delayMS: number;
}

enum ElementType {
	FRAME,
	LINE,
	DOT
}



