import { SlotMarqueeConfig } from 'slotngf/src/components/marquee/SlotMarqueeConfig';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Orientation } from 'corengf/src/display/Viewport';
import { ICoreElementConfig } from 'corengf/src/elements/canvas/CoreElement';
import { Helper } from 'corengf/src/utils/Helper';
import { IScrollMarqueeConfig, MarqueeDisplayMode } from 'corengf/src/elements/canvas/scroll/ScrollMarquee';
import { Rectangle } from 'pixi.js';

export class DHBSlotMarqueeConfig extends SlotMarqueeConfig {

	protected portraitMask: Rectangle = new Rectangle( 16, 0, 685, 40 );
	protected landscapeMask: Rectangle = new Rectangle( 16, 0, 705, 40 );

	protected getScrollMarqueeConfig (): IScrollMarqueeConfig {
		return Helper.mergeTwoObjects( super.getScrollMarqueeConfig(), {
			isOrientationSupported: true,
			marqueeDisplayMode: MarqueeDisplayMode.LOOP,
			accelerateSpeed: 1
		} );
	}

	public stateToTextMap = new TsMap( [
		[ 'pickFeature', [ 'Match3symbols' ] ],
		[ 'baseGameIdle', [ 'spinToPlay', 'spinToPlay' ] ],
		[ 'spinning', [ 'goodLuck' ] ],
		[ 'freeSpinsIntroPopup', [ 'goodLuck' ] ],
		[ 'freeSpinsIdle', [ 'goodLuck' ] ],
		[ 'showRetrigger', [ 'retrigger' ] ]
	] );


	public coreElementOrientationConfig: TsMap<Orientation, TsMap<string, ICoreElementConfig>> = new TsMap( [
		[
			Orientation.PORTRAIT, new TsMap( [
				[
					this.scrollMarqueeConfig.name, {
						name: this.scrollMarqueeConfig.name,
						mask: this.portraitMask,
						position: { x: 0, y: 952 },
					}
				]
			] )
		],
		[
			Orientation.LANDSCAPE, new TsMap( [
				[
					this.scrollMarqueeConfig.name, {
						name: this.scrollMarqueeConfig.name,
						mask: this.landscapeMask,
						position: { x: 270, y: 585 },
					}
				]
			] )
		]
	] );
}


