import { Inject } from 'typescript-ioc/es5';
import { AudioTrackName } from 'corengf/src/audio/SoundManager';
import { SlotSoundConfig } from 'slotngf/src/core/config/SlotSoundConfig';
import { BigWinController } from 'slotngf/src/components/bigwin/BigWinController';
import { DHBBigWinView } from 'game/src/components/bigwin/DHBBigWinView';

export class DHBBigWinController extends BigWinController {


	protected view: DHBBigWinView;

	constructor ( @Inject view: DHBBigWinView ) {
		super( view );
	}

	protected cancelCountUp ( forceStop: boolean = false ): void {
		this.soundManager.stop( 'RollupBeep' );
		this.soundManager.stop( `${ SlotSoundConfig.win }0${ BigWinController.getBigWinTierIndex( this.finalBigWinTier ) + 1 }` );
		super.cancelCountUp( forceStop );
	}

	protected completeCountUp (): void {
		this.soundManager.stop( 'RollupBeep' );
		this.soundManager.stop( `${ SlotSoundConfig.win }0${ BigWinController.getBigWinTierIndex( this.finalBigWinTier ) + 1 }` );
		this.soundManager.play( `${ SlotSoundConfig.win }0${ BigWinController.getBigWinTierIndex( this.finalBigWinTier ) + 1 }End` );
		super.completeCountUp();
	}

	protected beginCountUp (): void {
		this.soundManager.play( 'RollupBeep', AudioTrackName.EFFECT, true );
		super.beginCountUp();
	}

	protected allLineWin ( data: any ): void {
		if ( data.wildWin ) {
			this.soundManager.play( 'WildWinAudio' );
			this.soundManager.play( 'WildWinLaugh' );
		}
		super.allLineWin( data );
	}

	protected freeSpinsAllLineWin ( data: any ): void {
		if ( data.wildWin ) {
			this.soundManager.play( 'WildWinAudio' );
			this.soundManager.play( 'WildWinLaugh' );
		}
		super.freeSpinsAllLineWin( data );
	}

}


