import { Singleton, Inject } from 'typescript-ioc/es5';
import { SlotStates } from 'slotngf/src/states/SlotStates';
import { UILocalButtonsController } from 'corengf/src/components/ui/buttons/UILocalButtonsController';


@Singleton
export class SlotUILocalButtonsController extends UILocalButtonsController {

	@Inject
	protected gameStates: SlotStates;

	protected gameControllerButtonAction ( action: string, data?: any ): void {
		this.gameController.handleButtonAction( action, data );
	}
}



