import { SlotFreeSpinsConfig, ISlotFreeSpinsConfig } from 'slotngf/src/components/freeSpins/SlotFreeSpinsConfig';
import { ICoreUISpineAnimationElementConfig } from 'corengf/src/factory/CoreUIElementFactory';
import { SpineAnimation } from 'corengf/src/elements/canvas/animation/spine/SpineAnimation';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Orientation } from 'corengf/src/display/Viewport';
import { ICoreElementLayoutConfig } from 'corengf/src/elements/canvas/CoreElement';
import { IBitmapTextFieldConfig } from 'corengf/src/elements/canvas/text/BitmapTextField';


export class DHBSlotFreeSpinsConfig extends SlotFreeSpinsConfig implements IDHBSlotFreeSpinsConfig {

	public readonly pickAsset = 'featureCoin';

	public pickCoinAnimation: ICoreUISpineAnimationElementConfig = {
		elementType: SpineAnimation.elementType,
		elementConfig: {
			name: 'featureCoin',
			animationName: 'featureCoin',
		}
	};

	public prize: ICoreUISpineAnimationElementConfig = {
		elementType: SpineAnimation.elementType,
		elementConfig: {
			name: 'pickFeaturePrize',
			animationName: 'pickFeaturePrize',

		}
	};

	public triggered: ICoreUISpineAnimationElementConfig = {
		elementType: SpineAnimation.elementType,
		elementConfig: {
			name: 'featureTriggered',
			animationName: 'featureTriggered',
			isOrientationSupported: true
		}
	};

	public winBonus: ICoreUISpineAnimationElementConfig = {
		elementType: SpineAnimation.elementType,
		elementConfig: {
			name: 'featureWinBonus',
			animationName: 'featureWinBonus',
			isOrientationSupported: true,
		}
	};

	public winValueLabel: IBitmapTextFieldConfig = {
		name: 'pickWinValueLabel',
		key: '',
		isOrientationSupported: true,
		position: { x: 360, y: 620 },
		anchor: { x: 0.5, y: 0.5 },
		autoscale: { x: 350, y: 100 },
		style: {
			font: {
				name: 'BigWinFont',
				size: 90,
			},
			align: 'center',
		},
	};

	public coreElementOrientationConfig: TsMap<Orientation, TsMap<string, ICoreElementLayoutConfig>> = new TsMap( [
		[
			Orientation.PORTRAIT, new TsMap( [
				[
					this.triggered.elementConfig.name, {
						position: { x: 375, y: 650 }
					}
				],
				[
					this.winBonus.elementConfig.name, {
						position: { x: 365, y: 675 },
						scale: { x: 0.94, y: 0.94 }
					}
				],
				[
					this.winValueLabel.name, {
						position: { x: 430, y: 650 }
					}
				]
			] )
		],
		[
			Orientation.LANDSCAPE, new TsMap( [
				[
					this.triggered.elementConfig.name, {
						position: { x: 660, y: 350 }
					}
				],
				[
					this.winBonus.elementConfig.name, {
						position: { x: 650, y: 370 },
						scale: { x: 1, y: 1 }
					}
				],
				[
					this.winValueLabel.name, {
						position: { x: 730, y: 345 }
					}
				]
			] )
		]
	] );

}

export interface IDHBSlotFreeSpinsConfig extends ISlotFreeSpinsConfig {

	triggered: ICoreUISpineAnimationElementConfig;

	pickCoinAnimation: ICoreUISpineAnimationElementConfig;

	prize: ICoreUISpineAnimationElementConfig;

	winBonus: ICoreUISpineAnimationElementConfig;

	winValueLabel: IBitmapTextFieldConfig;
}





