import { Helper } from 'corengf/src/utils/Helper';
import { ForcingSpinConfig } from 'slotngf/src/components/demoPanel/ForcingSpinConfig';
import { IDemoConfig, ISelectorButtonConfig } from 'slotngf/src/core/config/IDemoConfig';


export class MLUIForcingSpinConfig extends ForcingSpinConfig implements IDemoConfig {
	protected getSelectorButtonConfig (): ISelectorButtonConfig {
		return Helper.mergeTwoObjects( super.getSelectorButtonConfig(), {
			scaleX: 0.8,
			scaleY: 1,
		} );
	}
}


