import { LocalButtonsController } from 'corengf/src/buttons/LocalButtonsController';
import { Orientation } from 'corengf/src/display/Viewport';
import { Button, IButtonConfig } from 'corengf/src/elements/canvas/button/Button';
import { ITextButtonConfig, TextButton } from 'corengf/src/elements/canvas/button/TextButton';
import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { SimpleTween } from 'corengf/src/time/SimpleTween';
import { Actions } from 'corengf/src/utils/Actions';
import * as MiniSignal from 'mini-signals';
import { Sprite } from 'pixi.js';
import { CoreElement, ICoreElementConfig, ICoreElementLayoutConfig } from 'corengf/src/elements/canvas/CoreElement';

/**
 * A Container Class that holds and displays multiple buttons.
*/
export class MultiButton extends CoreElement {

	protected localButtonController: LocalButtonsController;

	protected buttons: Array<Button>;
	protected background: Sprite;
	protected tweenIn: SimpleTween;
	protected tweenOut: SimpleTween;

	private _defaultIndex: number;

	get defaultIndex (): number {
		return this._defaultIndex;
	}

	set defaultIndex ( value: number ) {
		this._defaultIndex = value;
	}

	public selectedSignal: MiniSignal;

	/**
	 * @param data IButtonContainerConfig object
	 */
	constructor ( data: IMultiButtonConfig, localButtonsController?: LocalButtonsController ) {
		super( data );
		this.selectedSignal = new MiniSignal();
		this.localButtonController = localButtonsController;
		this.init( data );
	}

	protected init ( data: IMultiButtonConfig ): void {
		if ( data.defaultIndex !== undefined ) {
			this.defaultIndex = data.defaultIndex;
		}
		this.setupButtons( data );
	}

	protected isTextButtonConfig ( buttonData: IButtonConfig | ITextButtonConfig ): buttonData is ITextButtonConfig {
		return ( <ITextButtonConfig> buttonData ).text != undefined;
	}

	protected createSingleButton ( buttonData: IButtonConfig | ITextButtonConfig, index: number = 0 ): Button {
		let button: Button;
		if ( this.isTextButtonConfig( buttonData ) ) {
			button = new TextButton( buttonData, this.localButtonController );
		} else {
			button = new Button( buttonData, this.localButtonController );
		}
		return button;
	}

	protected setupButtons ( data: IMultiButtonConfig ): void {
		this.buttons = new Array<Button>();

		data.buttons.forEach( ( buttonData: IButtonConfig | ITextButtonConfig, index: number ) => {
			let button: Button | TextButton = this.createSingleButton( buttonData, index );

			let newX = 0;
			let newY = 0;

			if ( data.offsetOrientation == Orientation.LANDSCAPE ) {
				newX = ( ( button.width + data.offset ) * index );
			} else if ( data.offsetOrientation == Orientation.PORTRAIT ) {
				newY = ( ( button.height + data.offset ) * index );
			}

			if ( buttonData.isLocal ) {
				button.elementSignal.add( this.onSelectButton.bind( this ) );
			}

			button.position.set( newX, newY );
			this.addChild( button );
			this.buttons.push( button );
		} );
	}

	protected onSelectButton ( data: string ): void {
		let index: number = parseInt( data.replace( Actions.CLICK_ACTION_PREFIX, '' ) );
		this.selectedSignal.dispatch( index );
	}

	/**
	 * Enables all buttons in container
	 */
	public enableAllButtons (): void {
		for ( let button of this.buttons ) {
			button.setEnable( true );
		}
	}

	/**
	 * Disables all buttons in container
	 */
	public disableAllButtons (): void {
		for ( let button of this.buttons ) {
			button.setEnable( false );
		}
	}
}

export interface IMultiButtonConfig extends ICoreElementConfig {
	buttons: Array<IButtonConfig | ITextButtonConfig>;
	position: IPoint;
	offsetOrientation: Orientation;
	offset: number;
	speed: number;
	buttonsOrientationConfig?: IButtonsOrientationConfig;
	defaultIndex?: number;
}

export interface IButtonsOrientationConfig extends ICoreElementLayoutConfig {
	offset: number;
}

export interface IButtonContainerLabels {
	use: boolean;
	font?: string;
	position?: BUTTON_LABEL_POSITION;
	offsetX?: number;
	offsetY?: number;
	backgroundColor?: string;
}

export enum BUTTON_LABEL_POSITION {
	TOP,
	BOTTOM,
}



