import { Inject } from 'typescript-ioc/es5';
import { EventParams, GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { CompController } from 'corengf/src/mvc/CompController';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import { BigWinTierType } from './../../core/config/IBigWinConfig';
import { ICoinShowerConfig } from './../../core/config/ICoinShowerConfig';
import { BigWinController } from './../bigwin/BigWinController';
import { CoinShowerConfig } from './CoinShowerConfig';
import { SlotCoinShowerView } from './SlotCoinShowerView';

export class SlotCoinShowerController extends CompController {
	public static readonly STOP_COIN_SHOWER_EVENT: string = 'StopCoinShowerEvent';

	protected coinShowerView: SlotCoinShowerView;
	protected config: ICoinShowerConfig;
	private respawnTimer: SimpleTimer;
	private showering: boolean = false;

	constructor ( @Inject view: SlotCoinShowerView ) {
		super( view );

		this.coinShowerView = view;
	}

	public init (): void {
		this.config = this.gameConfig.getConfig<ICoinShowerConfig>( CoinShowerConfig );

		if ( this.coinShowerView != null ) {
			this.coinShowerView.initialize( this.config );
		}
		this.finishStateAction();
	}

	public addEventListeners (): void {
		GlobalDispatcher.addListener( this, BigWinController.TIER_REACHED_EVENT, this.onTierReached );
		GlobalDispatcher.addListener( this, SlotCoinShowerController.STOP_COIN_SHOWER_EVENT, this.onCoinShowerStop );
	}

	protected onTierReached ( e: EventParams ): void {
		if ( e.data.tier == BigWinTierType.Coins ) {
			switch ( e.data.finalTier ) {
				case BigWinTierType.ExtremeBigWin: {
					this.startCoinShower( this.config.mediumShowerParticleDelay );
				}
				case BigWinTierType.UltraExtremeBigWin: {
					this.startCoinShower( this.config.largeShowerParticleDelay );
				}
				default: {
					this.startCoinShower( this.config.smallShowerParticleDelay );
				}
			}
		}
	}

	protected onCoinShowerStop ( e: EventParams ): void {
		this.stopCoinShower();
	}

	protected baseGameIdle (): void {
		this.stopCoinShower();
		this.finishStateAction();
	}

	protected transitionToBaseGame (): void {
		this.stopCoinShower();
		this.finishStateAction();
	}

	protected stopCoinShower (): void {
		if ( this.showering ) {
			this.respawnTimer.stop();
			this.respawnTimer.destroy();
			this.respawnTimer = null;
			this.showering = false;
		}
	}

	protected onRespawnTimer (): void {
		this.coinShowerView.filterCoins();
		this.coinShowerView.emmitCoin();
	}

	protected startCoinShower ( respawnTime: number ): void {
		if ( !this.showering ) {
			this.showering = true;
			this.respawnTimer = new SimpleTimer( respawnTime, this.onRespawnTimer.bind( this ), true );
		}
	}
}


