import { Inject } from 'typescript-ioc/es5';
import { CompController } from 'corengf/src/mvc/CompController';
import { UIBankPanelView } from 'corengf/src/components/ui/components/bank/UIBankPanelView';
import { IUIBankConfig, UIBankConfig } from 'corengf/src/components/ui/components/bank/UIBankConfig';


export class UIBankController extends CompController {

	protected view: UIBankPanelView;

	constructor ( @Inject view: UIBankPanelView ) {
		super( view );
	}

	public init ( data: any ): void {
		let config = this.gameConfig.getConfig<IUIBankConfig>( UIBankConfig );
		this.view.initialize( config );
		this.totalBetUpdate( data.totalBet );
		this.balanceUpdate( data.balance );
		this.finishStateAction();
	}

	public totalBetUpdate ( totalBet: number ): void {
		this.view.totalBet = totalBet;
	}

	public balanceUpdate ( balance: number ): void {
		this.view.balance = balance;
	}
}



