import { Sprite } from 'pixi.js';
import { MLUIPanel } from 'mlui/src/core/MLUIPanel';
import { IMLUIBackgroundConfig } from 'mlui/src/components/background/MLUIBackgroundConfig';


export class MLUIBackgroundPanel extends MLUIPanel {
	protected background: Sprite;
	protected config: IMLUIBackgroundConfig;

	protected createElements ( config: IMLUIBackgroundConfig ): void {
		this.config = config;
		super.createElements( this.config );

		this.addBackground();
	}

	protected addBackground (): void {
		try {
			this.background = Sprite.fromFrame( this.config.assetName );
			this.background.position.set( this.config.position.x, this.config.position.y );
			this.addChild( this.background );
		} catch ( error ) {
			console.log( 'add ui background assets if you need it' );
		}
	}
}


