import { Graphics, Container } from 'pixi.js';
import { CompView } from 'corengf/src/mvc/CompView';
import { Asset } from 'corengf/src/core/AssetLoader';
import { SlotCoinParticle } from './SlotCoinParticle';

export class SlotCoinShowerView extends CompView {

	private backgroundMask: Graphics;

	protected coinContainer: Container;

	protected coinSystem: CoinSystem[];

	protected createElements ( config?: any ): void {

		this.coinContainer = new Container();
		this.addChild( this.coinContainer );

		this.coinSystem = [];

		for ( let i = 0; i < config.emitters.length; i++ ) {
			let system: CoinSystem = {
				coins: [],
				coinPool: []
			};

			let emmiter = config.emitters[ i ];
			for ( let n = 0; n < emmiter.numberOfCoins; n++ ) {
				let coin = new SlotCoinParticle( config.assetList, emmiter );
				this.coinContainer.addChild( coin );
				system.coinPool.push( coin );
			}

			this.coinSystem.push( system );
		}

		this.setBackgroundMask( this.createBackgroundMask() );
	}

	protected setBackgroundMask ( mask: Graphics ): void {
		this.backgroundMask = mask;
		this.mainContainer.mask = this.backgroundMask;
		this.addChild( this.backgroundMask );
	}

	protected createBackgroundMask (): Graphics {
		var bgMask: Graphics = new Graphics();
		bgMask.beginFill( 0 );
		bgMask.drawRect( 0, 0, this.viewport.width, this.viewport.height );
		bgMask.endFill();
		return bgMask;
	}

	protected layoutElements (): void {
		super.layoutElements();
		this.backgroundMask.width = this.viewport.width;
		this.backgroundMask.height = this.viewport.height;

		this.coinContainer.position.set( this.viewport.width / 2, this.viewport.height / 2 );
	}

	/**
	 * Move "dead" coins from alive array to pool
	 */
	public filterCoins (): void {
		for ( let i = 0; i < this.coinSystem.length; i++ ) {
			let system = this.coinSystem[ i ];

			for ( let n = 0; n < system.coins.length; n++ ) {
				let coin = system.coins[ n ];
				if ( !coin.alive ) {
					system.coinPool.push( coin );
				}
			}

			system.coins = system.coins.filter( coin => coin.alive );
		}
	}

	/**
	 * Add new coin to stage, If their total number  does not exceed "numberOfCoins" value
	 */
	public emmitCoin (): void {
		for ( let i = 0; i < this.coinSystem.length; i++ ) {
			let system = this.coinSystem[ i ];

			if ( system.coinPool.length > 0 ) {
				let coin = system.coinPool.pop();
				coin.alive = true;
				system.coins.push( coin );
			}
		}
	}

	public getAssetsList (): Array<Asset> {
		return [
			{
				assetKey: 'coinShower',
				assetURL: 'assets/coinShower/coinShower.json'
			}
		];
	}
}

export type CoinSystem = {
	coins: Array<SlotCoinParticle>;
	coinPool: Array<SlotCoinParticle>;
};


