import * as MiniSignal from 'mini-signals';
import { Easing } from '@tweenjs/tween.js';
import { ISymbol } from './interfaces/ISymbol';
import { Sprite, Container, extras } from 'pixi.js';
import { SimpleTween } from 'corengf/src/time/SimpleTween';
import { Animation } from 'corengf/src/elements/canvas/animation/Animation';
import { ISymbolConfig, WinTweenData } from './../../../core/config/IReelsConfig';
import { SymbolAnimationPostfix, SymbolState, SymbolStatePostfix } from './../ReelsEnums';


export class Symbol implements ISymbol {

	public static SYMBOL_ANIMATION_FRAME_COUNT: number = 60;

	public animationCompleteSignal: MiniSignal;

	protected id: number;
	public symbolName: string;
	protected symbolLayer: Sprite;
	protected config: ISymbolConfig;
	protected animationTween: SimpleTween;
	protected animationLayer: Container;
	protected itemAnimation: extras.AnimatedSprite;
	protected symbolState: SymbolState;
	protected textureName: string;

	private _symbolContainer: Container;

	private static winAnimationOverrides: Array<string> = new Array<string>();
	private static winTweenData: WinTweenData;

	private shakingTweenTo: SimpleTween;
	private shakingTweenBack: SimpleTween;

	public static addWinAnimationOverrides ( symbolNames: Array<string>, tweenData: WinTweenData ): void {
		this.winAnimationOverrides = symbolNames;
		this.winTweenData = tweenData;
	}

	constructor () {
		this.animationCompleteSignal = new MiniSignal();
		this.symbolLayer = new Sprite();
		this.symbolLayer.anchor.set( 0.5, 0.5 );
		this.textureName = '';

		this.animationLayer = new Container();
		this._symbolContainer = new Container();
		this._symbolContainer.addChild( this.symbolLayer );
		this._symbolContainer.addChild( this.animationLayer );
	}

	public setId ( id: number ): void {
		this.id = id;
	}

	public setConfig ( config: ISymbolConfig ): void {
		this.config = config;
	}

	public setSymbolNameWithState ( symbolName: string, state: SymbolState ): void {
		this.symbolName = symbolName;
		this.setSymbolState( state );
	}

	public setSymbolState ( state: SymbolState ): void {
		if ( this.textureName == this.getFullSymbolName( state ) ) {
			return;
		}

		this.symbolState = state;
		let texture;
		try {
			this.textureName = this.getFullSymbolName( state );
			texture = Sprite.fromFrame( this.textureName ).texture;
		} catch ( e ) {
			if ( state == SymbolState.BLUR || state == SymbolState.LOSE ) {
				this.textureName = this.getFullSymbolName( SymbolState.STATIC );
				texture = Sprite.fromFrame( this.textureName ).texture;
			} else {
				throw 'not found texture, this.textureName = ' + this.textureName;
			}
		}
		this.symbolLayer.position.set( this.config.width / 2, this.config.height / 2 );
		this.symbolLayer.texture = texture;
	}

	public getAssetPostfixForState ( state: SymbolState ): string {
		switch ( state ) {
			case SymbolState.STATIC:
				return SymbolStatePostfix.STATIC;

			case SymbolState.LOSE:
				return SymbolStatePostfix.LOSE;

			case SymbolState.BLUR:
				return SymbolStatePostfix.BLUR;
		}
	}

	public deleteAnimation (): void {
		this.stopAnimation();
		this.stopTweens();
		this.resetSymbol();
		this.animationLayer.removeChildren();
		this.symbolLayer.visible = true;
	}

	protected stopAnimation (): void {
		if ( this.itemAnimation != null ) {
			this.itemAnimation.gotoAndStop( 0 );
		}
	}

	protected stopTweens (): void {
		if ( this.animationTween ) {
			this.animationTween.stop();
			this.animationTween.stopChainedTweens();
			this.animationTween = undefined;
		}
	}

	protected resetSymbol (): void {
		this.symbolLayer.scale.set( 1, 1 );
	}

	public playAnimation ( type: SymbolAnimationPostfix, useNativeSpeed: boolean = false ): boolean {
		this.symbolLayer.visible = true;
		let introAnimation: extras.AnimatedSprite;
		if ( type == SymbolAnimationPostfix.WIN ) {
			if ( this.isSymbolWinOverriden() ) {
				this.playWinAnimationOverride();
				return true;
			}

			introAnimation = Animation.getAnimationFromPool( 'symbol_' + this.symbolName + SymbolAnimationPostfix.WIN_INTRO );
		}

		this.itemAnimation = Animation.getAnimationFromPool( 'symbol_' + this.symbolName + type );

		if ( this.itemAnimation == undefined ) {
			return false;
		}

		this.symbolLayer.visible = false;
		if ( !useNativeSpeed ) {
			this.itemAnimation.animationSpeed = this.itemAnimation.totalFrames / Symbol.SYMBOL_ANIMATION_FRAME_COUNT;
		}
		this.itemAnimation.anchor.set( 0.5 );
		this.itemAnimation.x = this.config.width / 2;
		this.itemAnimation.y = this.config.height / 2;

		this.animationLayer.removeChildren();
		this.animationLayer.addChild( this.itemAnimation );

		this.itemAnimation.gotoAndStop( 0 );
		this.itemAnimation.loop = false;
		this.itemAnimation.onComplete = this.onCompleteItemAnimation.bind( this, this.id, type ); // todo sometimes lost context

		if ( introAnimation != undefined ) {
			this.itemAnimation.visible = false;

			introAnimation.anchor.set( 0.5 );
			introAnimation.x = this.config.width / 2;
			introAnimation.y = this.config.height / 2;
			introAnimation.gotoAndStop( 0 );
			introAnimation.loop = false;
			introAnimation.visible = true;
			this.animationLayer.addChild( introAnimation );

			introAnimation.onComplete = () => {
				introAnimation.visible = false;
				introAnimation.removeChild( introAnimation );
				introAnimation = undefined;

				if ( this.itemAnimation != undefined ) {
					this.itemAnimation.visible = true;
					this.itemAnimation.gotoAndPlay( 0 );
				} else {
					this.animationCompleteSignal.dispatch( this.id, SymbolAnimationPostfix.WIN );
				}
			};

			introAnimation.gotoAndPlay( 0 );
		} else {
			this.itemAnimation.gotoAndPlay( 0 );
		}

		return true;
	}

	protected playWinAnimationOverride (): void {
		this.stopTweens();
		this.setSymbolState( SymbolState.STATIC );
		let tweenData = { scale: 1 };
		let tweenOut = new SimpleTween( tweenData )
			.to( { scale: 1 }, Symbol.winTweenData.outDuration )
			.delay( Symbol.winTweenData.outDelay )
			.onUpdate( () => {
				this.symbolLayer.scale.set( tweenData.scale, tweenData.scale );
			} )
			.onComplete( this.onCompleteItemAnimation.bind( this, this.id, SymbolAnimationPostfix.WIN ) );
		let tweenIn = new SimpleTween( tweenData )
			.to( { scale: Symbol.winTweenData.scale }, Symbol.winTweenData.inDuration )
			.onUpdate( () => {
				this.symbolLayer.scale.set( tweenData.scale, tweenData.scale );
			} )
			.easing( Symbol.winTweenData.ease ? Symbol.winTweenData.ease : Easing.Linear.None )
			.chain( tweenOut );

		this.animationTween = tweenIn.start();
	}

	protected onCompleteItemAnimation ( id: number, type: SymbolAnimationPostfix ): void {
		this.animationCompleteSignal.dispatch( id, type );
	}

	protected getFullSymbolName ( state: SymbolState = this.symbolState ): string {
		return 'symbol_' + this.symbolName + this.getAssetPostfixForState( state ) + '.png';
	}

	get symbolContainer (): Container {
		return this._symbolContainer;
	}

	protected isSymbolWinOverriden (): boolean {
		return Symbol.winAnimationOverrides.indexOf( this.symbolName ) != -1;
	}

	public playShaking (): void {
		if ( this.config.shakeConfig == undefined ) {
			return;
		}

		this.playShakingIn();
	}

	public stopShaking ( restorePosX: number, restorePosY: number ): void {

		if ( this.shakingTweenTo != undefined ) {
			this.shakingTweenTo.stop();
			this.shakingTweenTo = undefined;
		}

		if ( this.shakingTweenBack != undefined ) {
			this.shakingTweenBack.stop();
			this.shakingTweenBack = undefined;
		}

		this._symbolContainer.x = restorePosX;
		this._symbolContainer.y = restorePosY;
	}

	private playShakingIn (): void {
		let intensity: number = this.config.shakeConfig.intensity;
		let delay: number = this.config.shakeConfig.delay;
		this.shakingTweenTo = new SimpleTween( this._symbolContainer )
			.to( { x: this._symbolContainer.x + intensity, y: this._symbolContainer.y + intensity }, delay )
			.onComplete( () => {
				this.shakingTweenTo = new SimpleTween( this._symbolContainer )
					.to( { x: this._symbolContainer.x - intensity, y: this._symbolContainer.y - intensity }, delay )
					.onComplete( () => {
						this.playShakingOut();
					} )
					.start();
			} )
			.start();
	}

	private playShakingOut (): void {
		let intensity: number = this.config.shakeConfig.intensity;
		let delay: number = this.config.shakeConfig.delay;

		this.shakingTweenBack = new SimpleTween( this._symbolContainer )
			.to( { x: this._symbolContainer.x + intensity, y: this._symbolContainer.y - intensity }, delay )
			.onComplete( () => {
				this.shakingTweenBack = new SimpleTween( this._symbolContainer )
					.to( { x: this._symbolContainer.x - intensity, y: this._symbolContainer.y + intensity }, delay )
					.onComplete( () => {
						this.playShakingIn();
					} )
					.start();
			} )
			.start();
	}
}


