import * as MD from 'mobile-detect/mobile-detect';
import { Orientation } from 'corengf/src/display/Viewport';

// The browsers that also using Chrome core:
// ucBrowser, QQBrowser

// Some Browser use Chrome as core, but still use Chrome userAgent:
// 360 browser

// When requestFullScreen, some browser force enter landscape but the device is in portrait. This WLL cause display error.
// 360 browser, SOGOU, LIEBAO, 2345 browser

export enum BrowserName {
	CHROME = 'Chrome',
	IE = 'IE',
	EDGE = 'Edge',
	FIREFOX = 'Firefox',
	UC_BROWSER = 'UCBrowser',
	U_BROWSER = 'UBrowser',
	SAFARI = 'Safari',
	OPERA = 'Opera',
	QQ_BROWSER = 'QQBrowser',

	// unverified browsers
	SOGOU = 'SOGOU',
	BROWSER_360 = '360',
	BAIDU = 'BAIDO',
	LIEBAO = 'LIEBAO',
	BROWSER_2345 = '2345',
	GREEN_TEA = 'GREEN_TEA',
	COC = 'coc_coc_browser',
	YANDEX = 'YaBrowser',
	UNDEFINED = 'Undefined'
}

/**
 * Includes device and browser information, and some browser related help function
 *
 * @export
 * @class Browser
 *
 */
export class Browser {

	public static mobileDetect: MD;

	public static VISIBILITY_CHANGE: string = 'visibilitychange';

	public static HIDDEN: string = 'hidden';

	public static FULL_SCREEN_CHANGE: Array<string> = [ 'fullscreenchange', 'mozfullscreenchange', 'webkitfullscreenchange', 'msfullscreenchange' ];

	private static regex = {
		[ BrowserName.FIREFOX ]: 'Firefox\/([0-9\.]+)(?:\s|$)|FxiOS',
		[ BrowserName.EDGE ]: 'Edge\/([0-9\._]+)',
		[ BrowserName.IE ]: 'Trident\/([0-9\._]+)',
		[ BrowserName.CHROME ]: 'Chrome\/([0-9\._]+)|CriOS\/([0-9\._]+)',
		[ BrowserName.SAFARI ]: 'Safari\/([0-9\._]+)',
		[ BrowserName.OPERA ]: 'OPR\/([0-9\._]+)',
		[ BrowserName.U_BROWSER ]: 'UBrowser\/([0-9\._]+)',
		[ BrowserName.UC_BROWSER ]: 'UCBrowser\/([0-9\._]+)',
		[ BrowserName.QQ_BROWSER ]: 'QQBrowser\/([0-9\._]+)',
		[ BrowserName.BROWSER_2345 ]: 'Mb2345Browser\/([0-9\._]+)',
		[ BrowserName.LIEBAO ]: 'ACHEETAHI\/([0-9\._]+)',
		[ BrowserName.BAIDU ]: 'baidubrowser\/([0-9\._]+)',
		[ BrowserName.SOGOU ]: 'SogouMobileBrowser\/([0-9\._]+)',
		[ BrowserName.GREEN_TEA ]: 'LeBrowser\/([0-9\._]+)',
		[ BrowserName.COC ]: 'coc_coc_browser\/([0-9\._]+)',
		[ BrowserName.YANDEX ]: 'YaBrowser\/([0-9\._]+)'
	};

	private static rules = {
		[ BrowserName.U_BROWSER ]: [ BrowserName.U_BROWSER, BrowserName.CHROME, BrowserName.SAFARI ],
		[ BrowserName.UC_BROWSER ]: [ BrowserName.UC_BROWSER, BrowserName.CHROME, BrowserName.SAFARI ],
		[ BrowserName.QQ_BROWSER ]: [ BrowserName.QQ_BROWSER, BrowserName.CHROME, BrowserName.SAFARI ],
		[ BrowserName.COC ]: [ BrowserName.COC, BrowserName.CHROME, BrowserName.SAFARI ],
		[ BrowserName.YANDEX ]: [ BrowserName.YANDEX, BrowserName.CHROME, BrowserName.SAFARI ],
		[ BrowserName.BROWSER_2345 ]: [ BrowserName.BROWSER_2345, BrowserName.CHROME, BrowserName.SAFARI ],
		[ BrowserName.LIEBAO ]: [ BrowserName.LIEBAO, BrowserName.CHROME, BrowserName.SAFARI ],
		[ BrowserName.BAIDU ]: [ BrowserName.BAIDU, BrowserName.CHROME, BrowserName.SAFARI ],
		[ BrowserName.SOGOU ]: [ BrowserName.SOGOU, BrowserName.CHROME, BrowserName.SAFARI ],
		[ BrowserName.GREEN_TEA ]: [ BrowserName.GREEN_TEA, BrowserName.CHROME, BrowserName.SAFARI ],
		[ BrowserName.FIREFOX ]: [ BrowserName.FIREFOX ],
		[ BrowserName.CHROME ]: [ BrowserName.CHROME, BrowserName.SAFARI ],
		[ BrowserName.SAFARI ]: [ BrowserName.SAFARI ],
		[ BrowserName.EDGE ]: [ BrowserName.EDGE ],
		[ BrowserName.IE ]: [ BrowserName.IE ],
		[ BrowserName.OPERA ]: [ BrowserName.OPERA ]
	};

	public static init (): void {
		Browser.mobileDetect = new MD( window.navigator.userAgent );
		const document = window.document;
		if ( typeof document.hidden !== 'undefined' ) { // Opera 12.10 and Firefox 18 and later support
			Browser.HIDDEN = 'hidden';
			Browser.VISIBILITY_CHANGE = 'visibilitychange';
		} else if ( typeof ( document as any ).msHidden !== 'undefined' ) {
			Browser.HIDDEN = 'msHidden';
			Browser.VISIBILITY_CHANGE = 'msvisibilitychange';
		} else if ( typeof ( document as any ).webkitHidden !== 'undefined' ) {
			Browser.HIDDEN = 'webkitHidden';
			Browser.VISIBILITY_CHANGE = 'webkitvisibilitychange';
		}
	}

	/**
	 * Method to figure out if we support full screen or no
	 * @returns if current web browser support HTML5 full screen
	 */
	public static isFullscreenSupported (): boolean {
		const element: any = document.body;
		return element.requestFullscreen ||
			element.msRequestFullscreen ||
			element.mozRequestFullScreen ||
			element.webkitRequestFullscreen;
	}
	/**
	 * Set the browser to full screen mode. (Some browser may not support this feature). Returns true if successfully executed.
	 *
	 * @static
	 * @returns {boolean}
	 * @memberof Browser
	 */
	public static requestFullScreen (): boolean {
		var element: any = document.body;
		if ( element.requestFullscreen ) {
			element.requestFullscreen();
			return true;
		} else if ( element.msRequestFullscreen ) {
			element.msRequestFullscreen();
			return true;
		} else if ( element.mozRequestFullScreen ) {
			element.mozRequestFullScreen();
			return true;
		} else if ( element.webkitRequestFullscreen ) {
			element.webkitRequestFullscreen();
			return true;
		} else {
			return false;
		}
	}

	/**
	 * Check if is mobile device
	 *
	 * @static
	 * @returns {boolean}
	 * @memberof Browser
	 */
	public static isMobile (): boolean {
		return Browser.mobileDetect.mobile() !== null;
	}

	/**
	 * Check if is iOS device
	 *
	 * @static
	 * @returns {boolean}
	 * @memberof Browser
	 */
	public static get iOS (): boolean {
		return /iP[ao]d|iPhone/i.test( window.navigator.userAgent );
	}


	/**
	 * Get current browser name
	 *
	 * @static
	 * @returns {string}
	 * @memberof Browser
	 */
	public static getName (): BrowserName {
		const browsers: Array<BrowserName> = Object.keys( Browser.rules ) as Array<BrowserName>;

		for ( let i = 0; i < browsers.length; i++ ) {
			const browserName = browsers[ i ];
			const essentialKeys = Browser.rules[ browserName ];
			let mappedKeyCount = 0;

			essentialKeys.forEach( rule => {
				const regex = new RegExp( Browser.regex[ browserName ], 'i' );

				if ( regex.test( window.navigator.userAgent ) === true ) {
					mappedKeyCount += 1;
				}
			} );

			if ( mappedKeyCount === essentialKeys.length ) {
				return browserName;
			}
		}
		return BrowserName.UNDEFINED;
	}

	/**
	 * This mehod checks if the UserAgent has define Chrome or not.
	 *
	 * @static
	 * @returns
	 * @memberof Browser
	 */
	public static isSupportChrome (): boolean {
		const regex = new RegExp( Browser.regex[ BrowserName.CHROME ], 'i' );
		return regex.test( window.navigator.userAgent );
	}

	/**
	 * Foces the browser in full screen.
	 * NOTE: in some browser, after called this method the mobile will enter landscape mode while the mobile actual in portrait.
	 *
	 * @static
	 * @param {Orientation} orientation
	 * @memberof Browser
	 */
	public static lockFullScreen ( orientation: Orientation ): void {
		const lockOrientation =
			( screen as any ).orientation ||
			( screen as any ).lockOrientation ||
			( screen as any ).mozLockOrientation ||
			( screen as any ).msLockOrientation;
		if ( lockOrientation && ( orientation === Orientation.LANDSCAPE || orientation === Orientation.PORTRAIT ) ) {
			if ( Browser.getName() === BrowserName.FIREFOX && ( screen as any ).mozLockOrientation ) {
				( screen as any ).mozLockOrientation( orientation );
			} else {
				lockOrientation.lock( orientation ).catch( () => {
					console.warn( 'The device may not support orientation-lock feature' );
				} );
			}
		}
	}

	public static isFullScreen (): boolean {
		const fullscreenElement =
			( document as any ).isFullScreen ||
			document.fullscreenElement ||
			( document as any ).webkitFullscreenElement ||
			( document as any ).mozFullScreenElement ||
			( document as any ).msFullscreenElement;
		if ( fullscreenElement === undefined ) {
			return ( document as any ).mozFullScreen || ( document as any ).webkitIsFullscreen;
		} else {
			return fullscreenElement !== null || fullscreenElement === true;
		}
	}

	public static tryFullScreen (): void {
		if ( !Browser.isFullScreen() && !Browser.isInFullScreenBlackList() ) {
			Browser.requestFullScreen();
		}
	}

	private static isInFullScreenBlackList (): boolean {
		if ( Browser.getName() == BrowserName.BROWSER_360 ||
			Browser.getName() == BrowserName.BROWSER_2345 ||
			Browser.getName() == BrowserName.SOGOU ||
			Browser.getName() == BrowserName.LIEBAO ) {
			return true;
		} else if ( Browser.getName() == BrowserName.CHROME ) { // check if the browser is fake Chrome
			if ( ( window as any ).Intl.RelativeTimeFormat === undefined ) {
				return true;
			}
		}
		return false;
	}
}


