import { Point } from 'pixi.js';
import { Inject } from 'typescript-ioc/es5';
import { Orientation } from 'corengf/src/display/Viewport';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { ITextButtonConfig } from 'corengf/src/elements/canvas/button/TextButton';
import { IMultiButtonConfig } from 'corengf/src/elements/canvas/button/MultiButton';
import { SlotEvent } from 'slotngf/src/core/SlotEvent';
import { IInitData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { SlotAutoPlayController } from 'slotngf/src/components/ui/components/autoplay/SlotAutoPlayController';
import { BasicAutoplayConfig } from './config/AutoplayRounds';
import { MLUISpinSettingsPanel } from './MLUISpinSettingsPanel';
import { MLUISpinSettingsConfig, IMLUISpinSettingsConfig } from './MLUISpinSettingsConfig';


export class MLUISpinSettingsController extends SlotAutoPlayController {

	protected config: IMLUISpinSettingsConfig;

	protected infiniteSpinValue = -1;

	protected view: MLUISpinSettingsPanel;

	constructor ( @Inject view: MLUISpinSettingsPanel ) {
		super( view );
	}

	public init ( data: IInitData ): void {
		this.config = this.gameConfig.getConfig<IMLUISpinSettingsConfig>( MLUISpinSettingsConfig );

		this.config.autoplayConfig = this.getAutoplayConfig( data );

		this.setButtonActionEventListeners( this.config.confirmButton.elementConfig );
		this.setButtonActionEventListeners( this.config.stopButton.elementConfig );
		this.setButtonActionEventListeners( this.config.closeButton.elementConfig );

		this.view.initialize( this.config );
	}


	protected getAutoplayConfig ( data: IInitData ): IMultiButtonConfig {
		const autoplay = BasicAutoplayConfig;
		const buttonConfig = {} as IMultiButtonConfig;
		buttonConfig.offset = autoplay.buttonOffset;
		buttonConfig.name = autoplay.name;
		buttonConfig.offsetOrientation = Orientation.LANDSCAPE;
		buttonConfig.position = new Point( autoplay.startX, autoplay.startY );
		buttonConfig.buttons = new Array<ITextButtonConfig>();
		buttonConfig.isOrientationSupported = true;
		data.autoSpins.forEach( ( value: number, index ) => {
			const buttonData = {} as ITextButtonConfig;
			buttonData.name = `option${ index }`;
			buttonData.action = value == this.infiniteSpinValue ? 'infinite' : value.toString();
			buttonData.id = 'autoplay_option';
			buttonData.assetName = autoplay.assetName;
			buttonData.isLocal = true;
			buttonData.scale = new Point( autoplay.scaleX, autoplay.scaleY );
			buttonData.text = {
				name: autoplay.selectorText.name,
				key: value == this.infiniteSpinValue ? 'infinite' : value,
				style: autoplay.selectorText.style
			};
			buttonConfig.buttons.push( buttonData );
		} );
		return buttonConfig;
	}

	protected action_autoSpin ( data: any ): void {
		this.view.hidePopup();
		this.view.setStopActionToStopAutoSpin();
		this.onHideBetPanel();
	}

	protected action_hideAutoPlayPanel (): void {
		this.view.hidePopup();
		this.onHideBetPanel();
	}

	protected onHideBetPanel (): void {
		GlobalDispatcher.dispatchIntoAllStates( SlotEvent.HIDE_FULL_SCREEN_UI );
	}

	protected action_stopAutoPlay ( data: any ): void {
		if ( !this.freeSpinsMode ) {
			this.setAutoPlay( false );
			this.autoPlayStopSignal.dispatch();
		}
	}

	protected freeSpinsStart (): void {
		this.view.setStopActionToFreeSpinStop();
	}

	public showPopup (): void {
		this.view.showPopup();
		this.view.totalBetUpdate( this.bankService.totalBet );
		GlobalDispatcher.dispatchIntoAllStates( SlotEvent.SHOW_FULL_SCREEN_UI );
	}

	protected getWinLimitValue ( value: number ): number {
		return this.getLimitValue( value );
	}

	protected getLoseLimitValue ( value: number ): number {
		return this.getLimitValue( value );
	}

	protected getSingleWinLimitValue ( value: number ): number {
		return this.getLimitValue( value );
	}

	protected getLimitValue ( value: number ): number {
		if ( value == -1 || value == Infinity ) {
			return Infinity;
		}

		if ( value == undefined ) {
			return 0;
		}

		return value;
	}
}


