import { ICompViewConfig } from 'corengf/src/core/config/ICompViewConfig';

export class UIInformationHeaderConfig implements IInformationHeaderConfig {
	public name = 'informationHeader';
	public isRealMode: boolean;
	public assetPath: string = MD5HASH + '_' + 'assets/uiInformation/';
	public background: IInformationHeaderBackgroundConfig = {
		portraitRatio: {
			height: 0.025
		},
		landscapeRatio: {
			height: 0.04
		},
		backgroundColor: '#000000',
		opacity: '0.5'
	};
	public clockConfig: IClockConfig = {
		name: 'clock',
		text: {
			baseFontSize: 14,
			portraitRatio: {
				left: 0.9
			},
			landscapeRatio: {
				left: 0.95
			},
			originalValue: '1',
			grayValue: '0.5'
		}
	};
	public titleConfig: ITitleConfig = {
		name: 'title',
		gameName: 'gameName',
		logo: {
			landscapeHeight: 0.05,
			portraitWidth: 0.12,
			originalAssetName: 'logo.png',
			grayAssetName: 'logo_gray.png',
			portraitRatio: {
				left: 0.005,
				top: 0.002
			},
			landscapeRatio: {
				left: 0.005,
				top: 0.006
			}
		},
		text: {
			baseFontSize: 14,
			originalValue: '1',
			grayValue: '0.5'
		}
	};
	public viewConfig: any = {};
}

export interface IInformationHeaderConfig extends ICompViewConfig {
	name: string;
	isRealMode: boolean;
	assetPath: string;
	background: IInformationHeaderBackgroundConfig;
	clockConfig: IClockConfig;
	titleConfig: ITitleConfig;
	viewConfig: any;
}

export interface IInformationHeaderBackgroundConfig {
	portraitRatio: IInformationHeaderRatioConfig;
	landscapeRatio: IInformationHeaderRatioConfig;
	backgroundColor: string;
	opacity: string;
}

export interface IClockConfig {
	name: string;
	text: IInformationHeaderTextConfig;
}

export interface ITitleConfig {
	name: string;
	gameName: string;
	logo: IInformationHeaderImageConfig;
	text: IInformationHeaderTextConfig;
}

export interface IInformationHeaderTextConfig {

	baseFontSize: number;
	portraitRatio?: IInformationHeaderRatioConfig;
	landscapeRatio?: IInformationHeaderRatioConfig;
	originalValue: string;
	grayValue: string;
}

export interface IInformationHeaderImageConfig {
	portraitWidth?: number;
	landscapeHeight?: number;
	originalAssetName: string;
	grayAssetName?: string;
	portraitRatio: IInformationHeaderRatioConfig;
	landscapeRatio: IInformationHeaderRatioConfig;
}

export interface IInformationHeaderRatioConfig {
	left?: number;
	top?: number;
	height?: number;
}


