import { Inject, Container } from 'typescript-ioc/es5';
import { Translator } from 'corengf/src/utils/Translator';
import { IErrorMsgData } from 'corengf/src/server/abstract/ErrorCode';
import { ErrorDialogData } from 'slotngf/src/server/ng/ErrorCode';
import { SlotNGModel } from 'slotngf/src/server/ng/model/SlotNGModel';


export class ErrorMsgFormatter {

	@Inject
	protected slotNGModel: SlotNGModel;

	@Inject
	protected translator: Translator;

	protected errorDialogData: ErrorDialogData;

	constructor () {
		this.errorDialogData = Container.get( ErrorDialogData );
	}

	public processIncomingMessage ( errorResponse: any ): IErrorMsgData {
		if ( this.errorDialogData.ignoredErrorCode && this.errorDialogData.ignoredErrorCode.indexOf( errorResponse.code ) !== -1 ) {
			return null;
		}

		if ( errorResponse.state_tag ) {
			this.slotNGModel.stateTag = errorResponse.state_tag;
		}

		return {
			type: this.errorDialogData.getErrorDialogType( errorResponse.code ),
			code: errorResponse.code,
			msg: this.translator.t( this.errorDialogData.getErrorDialogKey( errorResponse.code ) ),
			stack: errorResponse.stack
		};
	}
}



