import * as MiniSignal from 'mini-signals';
import { Container } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { ISpineAnimationConfig } from 'corengf/src/elements/canvas/animation/spine/SpineAnimation';
import { ReelModel } from 'slotngf/src/components/reelV2/ReelModel';
import { BaseSymbol } from 'slotngf/src/components/reelV2/symbol/BaseSymbol';
import { SpineSymbol } from 'slotngf/src/components/reelV2/symbol/SpineSymbol';
import { SpriteSymbol } from 'slotngf/src/components/reelV2/symbol/SpriteSymbol';
import { ISymbolDefinition, SymbolPosition } from 'slotngf/src/core/config/IReelsConfig';
import { SymbolState, SymbolAnimationPostfix } from 'slotngf/src/components/reels/ReelsEnums';

export enum OverlayType {
	SYMBOL = 'symbol',
	SPRITE = 'sprite',
	SPINE = 'spine'
}

export interface Offset {
	x: number;
	y: number;
}

export interface IReelStripOverlayConfig {
	overlayType: OverlayType;
	rangeSymbols?: Array<string>;
	symbolConfig?: ISymbolDefinition;
	offset?: Offset;

	/**
	 * How many objects will be created
	 *
	 * @type {number}
	 * @memberof IReelStripOverlayConfig
	 */
	size?: number;
	name?: string; // asset name for sprite/spine animaiton
}

export type SymbolObject = BaseSymbol | SpineSymbol | SpriteSymbol;

export class ReelStripOverlay {

	public symbolAnimationCompleteSignal: MiniSignal;

	public get type (): OverlayType {
		return this.config.overlayType;
	}

	protected config: IReelStripOverlayConfig;

	protected _mainContainer: PIXI.Container = new PIXI.Container();

	public get mainContainer (): PIXI.Container {
		return this._mainContainer;
	}

	protected objectPool: TsMap<string, Array<SymbolObject>> = new TsMap<string, Array<SymbolObject>>();

	protected objectsInUse: TsMap<string, SymbolObject> = new TsMap<string, SymbolObject>();

	protected reelModel: ReelModel;

	protected layoutData: Array<IPoint> = [];

	protected symbolMap: TsMap<string, SymbolObject> = new TsMap<string, SymbolObject>();

	protected lockedPositions: Array<string> = [];

	/**
	 * set lock=true will ignore the visible control
	 *
	 * @type {boolean}
	 * @memberof ReelStripOverlay
	 */
	public lock: boolean = false;

	public get visible (): boolean {
		return this._mainContainer.visible;
	}

	public set visible ( value: boolean ) {
		this._mainContainer.visible = value;
	}

	public init ( config?: IReelStripOverlayConfig ): void {
		this.config = config;

		this.symbolAnimationCompleteSignal = new MiniSignal();

		this._mainContainer.x = ( this.config.offset !== undefined ) ? this.config.offset.x : 0;
		this._mainContainer.y = ( this.config.offset !== undefined ) ? this.config.offset.y : 0;

		this._mainContainer.name = config.overlayType;
	}

	public bindModel ( model: ReelModel ): void {
		this.reelModel = model;
	}

	public createPool ( name?: string, size: number = 15 ): void {
		if ( !this.config ) {
			return;
		}

		if ( !name ) { // assume symbol
			for ( let i = 0; i < this.config.rangeSymbols.length; i++ ) {
				const symbolName = this.config.rangeSymbols[ i ];
				for ( let j = 0; j < size; j++ ) {
					this.pushObjectToPool( symbolName );
				}
			}
		} else {
			for ( let j = 0; j < size; j++ ) {
				this.pushObjectToPool( name );
			}
		}
	}

	private pushObjectToPool ( name: string ): void {
		const symbol = this.createObject( name );
		this.initPoolByKey( name );
		this.objectPool.get( name ).push( symbol );
	}

	protected createObject ( name: string ): SymbolObject {
		if ( !name ) {
			return;
		}

		let object: SymbolObject;

		switch ( this.config.overlayType ) {
			case OverlayType.SYMBOL:
				object = this.createSymbol( name );
				break;
			case OverlayType.SPRITE:
				object = new SpriteSymbol( name );
				break;
			case OverlayType.SPINE:
				const config: ISpineAnimationConfig = {
					name: name,
					animationName: name
				};
				object = new SpineSymbol( config );
				break;
		}
		return object;
	}

	private initPoolByKey ( name: string ): void {
		if ( !this.objectPool.has( name ) ) {
			this.objectPool.set( name, [] );
		}
	}

	protected createSymbol ( name: string ): BaseSymbol {
		const symbol: BaseSymbol = Container.get( BaseSymbol );
		symbol.setConfig( this.config.symbolConfig );
		symbol.animationCompleteSignal.add( this.symbolAnimationComplete, this );
		symbol.setSymbolNameWithState( name, SymbolState.STATIC );
		return symbol;
	}

	protected getObject ( name: string ): SymbolObject {
		this.initPoolByKey( name );

		if ( this.objectPool.get( name ).length > 0 ) {
			return this.objectPool.get( name ).pop();
		} else {
			return this.createObject( name );
		}
	}

	public getObjectAt ( animationName: string | SymbolAnimationPostfix, position: SymbolPosition ): SymbolObject {
		let object: SymbolObject = this.symbolMap.get( this.positionToStr( position ) );

		if ( object ) {
			return object;
		}

		if ( this.config.overlayType === OverlayType.SYMBOL ) {
			const symbolName = this.reelModel.currentSymbolsOutcome[ position.reelIndex ][ position.symbolIndex ];
			object = this.getObject( symbolName );
		}

		const objectPos: IPoint = this.getObjectPosition( position );

		object.x = objectPos.x;
		object.y = objectPos.y;

		return object;
	}

	protected getObjectPosition ( position: SymbolPosition ): IPoint {
		const objectPos: IPoint = { x: 0, y: 0 };
		objectPos.x = this.layoutData[ position.reelIndex ].x;
		objectPos.y = this.layoutData[ position.reelIndex ].y + position.symbolIndex * this.config.symbolConfig.generalSymbol.height;
		return objectPos;
	}

	public setSymbolMapAt ( object: SymbolObject, position: SymbolPosition ): void {
		object.position = position;
		this.symbolMap.set( this.positionToStr( position ), object );
	}


	public playAnimationAt ( animationName: string | SymbolAnimationPostfix, position: SymbolPosition ): boolean {
		if ( this.isLockPosition( position ) ) {
			return;
		}

		let object: SymbolObject = this.symbolMap.get( this.positionToStr( position ) );
		let result: boolean = false;

		if ( object ) {
			if ( this.config.overlayType === OverlayType.SYMBOL && object.isContain( position ) ) {
				result = object.playAnimation( animationName );
				if ( result ) {
					this.mainContainer.addChild( object.symbolContainer );
					this.symbolMap.set( this.positionToStr( position ), object );
					this.objectsInUse.set( this.positionToStr( position ), object );
				}
			}
		} else {
			if ( this.config.overlayType === OverlayType.SYMBOL ) {
				const symbolName = this.reelModel.currentSymbolsOutcome[ position.reelIndex ][ position.symbolIndex ];
				object = this.getObject( symbolName );
			} else {
				object = this.getObject( animationName );
			}

			object.x = this.layoutData[ position.reelIndex ].x;
			object.y = this.layoutData[ position.reelIndex ].y + position.symbolIndex * this.config.symbolConfig.generalSymbol.height;

			result = object.playAnimation( animationName );
			if ( result ) {
				this.mainContainer.addChild( object.symbolContainer );
				this.symbolMap.set( this.positionToStr( position ), object );
				this.objectsInUse.set( this.positionToStr( position ), object );
			}

			object.position = position;
		}

		object.name = this.positionToStr( position );
		return result;
	}

	public lockPosition ( symbolPosition: SymbolPosition ): void {
		const positionStr = this.positionToStr( symbolPosition );
		if ( !this.lockedPositions.includes( positionStr ) ) {
			this.lockedPositions.push( this.positionToStr( symbolPosition ) );
		}
	}

	public isLockPosition ( symbolPosition: SymbolPosition ): boolean {
		const positionStr = this.positionToStr( symbolPosition );
		return this.lockedPositions.includes( positionStr );
	}

	public unlockPosition ( symbolPosition?: SymbolPosition ): void {
		if ( !symbolPosition ) {
			this.lockedPositions = [];
		} else {
			const positionStr = this.positionToStr( symbolPosition );
			const index = this.lockedPositions.indexOf( positionStr );
			if ( index > -1 ) {
				this.lockedPositions.splice( index, 1 );
			}
		}
	}

	protected positionToStr ( position: SymbolPosition ): string {
		return `${ position.reelIndex },${ position.symbolIndex }`;
	}

	public placeAnimationAt ( animationName: string, position: SymbolPosition ): SymbolObject {
		if ( this.isLockPosition( position ) ) {
			return;
		}

		const object = this.getObject( animationName );
		object.symbolContainer.x = this.layoutData[ position.reelIndex ].x;
		object.symbolContainer.y = this.layoutData[ position.reelIndex ].y + position.symbolIndex * this.config.symbolConfig.generalSymbol.height;

		this.mainContainer.addChild( object.symbolContainer );
		object.name = `${ position.reelIndex },${ position.symbolIndex }`;
		this.symbolMap.set( this.positionToStr( position ), object );
		object.position = position;
		this.objectsInUse.set( this.positionToStr( position ), object );

		return object;
	}

	/**
	 * update the layoutData of each reel
	 *
	 * @param {Array<IPoint>} positions
	 * @memberof ReelStripOverlay
	 */
	public updateLayout ( positions: Array<IPoint> ): void {
		this.layoutData = positions;

		this.objectsInUse.forEach( ( object ) => {
			let position = object.position;
			object.symbolContainer.x = this.layoutData[ position.reelIndex ].x;
			object.symbolContainer.y = this.layoutData[ position.reelIndex ].y + position.symbolIndex * this.config.symbolConfig.generalSymbol.height;
		} );
	}

	public updateReelLayout ( index: number, position: IPoint ): void {
		this.layoutData[ index ] = position;
	}

	public updateLayerPosition ( position: IPoint ): void {
		this._mainContainer.x = position.x;
		this._mainContainer.y = position.y;
	}

	public hideSymbol ( position: SymbolPosition ): void {

		const object = this.symbolMap.get( this.positionToStr( position ) );

		if ( !object ) {
			return;
		}

		// reset
		object.triggerPositions = null;

		const displayObject = this.mainContainer.getChildByName( `${ position.reelIndex },${ position.symbolIndex }` );
		this.mainContainer.removeChild( displayObject );

		this.objectPool.get( object.symbolName ).push( object );
		this.symbolMap.delete( this.positionToStr( position ) );
		this.objectsInUse.delete( this.positionToStr( position ) );
	}

	/**
	 * remove all display object from container, and push it to pool.
	 *
	 * @memberof ReelStripOverlay
	 */
	public clean (): void {
		this.mainContainer.removeChildren();
		this.symbolMap.clear();
		this.objectsInUse.clear();
	}

	public reset (): void {
		this.unlockPosition();
		this.clean();
	}

	public dispose (): void {
		this.objectPool = null;
	}

	protected symbolAnimationComplete ( position: IPoint, type: string | SymbolAnimationPostfix ): void {
		this.symbolAnimationCompleteSignal.dispatch( position, type );
	}

	public playSymbolShaking ( position: SymbolPosition ): void {
		const symbolName = this.reelModel.currentSymbolsOutcome[ position.reelIndex ][ position.symbolIndex ];

		const object = <BaseSymbol> this.placeAnimationAt( symbolName, {
			reelIndex: position.reelIndex,
			symbolIndex: position.symbolIndex
		} );
		return object.playShaking();
	}

	public stopSymbolShaking ( position: SymbolPosition, restorePosition: IPoint ): void {
		const object: BaseSymbol = <BaseSymbol> this.symbolMap.get( this.positionToStr( position ) );

		if ( !object ) {
			return;
		}

		return object.stopShaking( restorePosition.x, restorePosition.y );
	}

	public getObjectsByReel ( reelIndex: number ): Array<SymbolObject> {

		const objects: Array<SymbolObject> = [];

		this.symbolMap.forEach( ( object, key ) => {
			if ( key.indexOf( `${ reelIndex },` ) !== -1 ) {
				objects.push( object );
			}
		} );

		return objects;
	}

	public getObjectByPosition ( symbolPosition: SymbolPosition ): SymbolObject {
		let result: SymbolObject = null;
		this.symbolMap.forEach( ( object ) => {
			if ( object.isContain( symbolPosition ) ) {
				result = object;
			}
		} );
		return result;
	}
}


