import { Singleton } from 'typescript-ioc/es5';
import * as CurrencyFormatter from 'currency-formatter';


// TODO: should be replace currency-formatter plugin by Intl.NumberFormat.
// https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/NumberFormat
/**
 * Class is reposponsible for money formatting.
 * Reference: https://www.npmjs.com/package/currency-formatter
 */
@Singleton
export class MoneyFormatter {
	public readonly DEFAULT_CURRENCY: string = 'USD';
	public readonly DEFAULT_COIN_SIZE: number = 1;
	public readonly DEFAULT_PRECISION: number = 2;

	private _currency: CurrencyFormatter.Currency = CurrencyFormatter.findCurrency( this.DEFAULT_CURRENCY );
	get currency (): CurrencyFormatter.Currency {
		return this._currency;
	}

	private _currencyCode: string;
	public get currencyCode (): string {
		return this._currencyCode;
	}

	private _precision: number = this.DEFAULT_PRECISION;
	public get precision (): number {
		return this._precision;
	}
	public set precision ( value: number ) {
		this._precision = value;
	}

	private _coinSize: number = this.DEFAULT_COIN_SIZE;
	public get coinSize (): number {
		return this._coinSize;
	}
	public set coinSize ( value: number ) {
		this._coinSize = value;
	}

	/**
	 * Setup the initial values for class.
	 * If URL parameter 'language' is missing we will use the default language.
	 */
	public initMoneyFormater (): void {
		this.setCurrencyByCode( this.DEFAULT_CURRENCY );
	}

	public setCurrencyByCode ( currencyCode: string ): void {
		this._currencyCode = currencyCode;

		// force to USD
		this._currency = CurrencyFormatter.findCurrency( this.DEFAULT_CURRENCY );
	}

	protected toCurrencyFormat ( value: number, currencyOptions?: CurrencyConfig ): string {
		const baseOption = this.currency ? this.currency : {};

		const option = Object.assign( baseOption, currencyOptions );
		if ( JSON.stringify( option ) === '{}' ) {
			console.warn( 'currency option is empty. use default currency instead.' );
			return CurrencyFormatter.format( value, { symbol: this.DEFAULT_CURRENCY, format: '%v' } );
		} else {
			return CurrencyFormatter.format( value, option );
		}
	}

	protected getDividedValue ( value: number, divideBy?: number ): number {
		const divideValue = divideBy ? divideBy : this.coinSize;
		return value / divideValue;
	}

	/**
	 * Format the money with currency config
	 * @param {number} value  The value should be formatted
	 * @param {NumberDisplayMode} displayMode VALUE_ONLY = 12,333.00, VALUE_WITH_CURRENCY = 12,333.00 CNY
	 * @param {boolean} [ showSymbol = false ] set true to show dollar sign
	 * @param {number} [ precision = this.precision ] accurate to n decimal places
	 * @param {number} [ divideBy = this.coinSize ] the value will divide by this param, set to cent (value/100) by default.
	 * @param {CurrencyConfig} overrideOptions extra options
	 * @returns {string}
	 */
	public formatMoney ( value: number,
		displayMode: NumberDisplayMode,
		showSymbol: boolean = false,
		precision: number = this.precision,
		divideBy: number = this.coinSize,
		overrideOptions?: CurrencyConfig ): string {

		let actualValue: number = this.getDividedValue( value, divideBy ) + 0.0000000001;

		// avoid rounding
		let actualValueStr: string = actualValue.toString();
		if ( actualValueStr.includes( '.' ) ) {
			let splits = actualValueStr.split( '.' );
			if ( splits[ 1 ].length > precision ) {
				actualValue = +( splits[ 0 ] + '.' + splits[ 1 ].slice( 0, precision ) );
			}
		}

		let option: CurrencyConfig = { precision };

		if ( !showSymbol ) {
			option.format = '%v';
		}

		option = Object.assign( option, { ...overrideOptions } );

		switch ( displayMode ) {
			case NumberDisplayMode.VALUE_ONLY:
				return this.toCurrencyFormat( actualValue, option );
			case NumberDisplayMode.VALUE_WITH_CURRENCY:
				return this.toCurrencyFormat( actualValue, option ) + ' ' + this.currencyCode;
			case NumberDisplayMode.VALUE_WITH_CURREYCY_SYMBOL:
				return CurrencyFormatter.format( actualValue, {
					precision: this.precision,
					code: this.currencyCode
				} );
			default:
				return value + '';
		}
	}

	/**
	 * Returns original number from given value string, base on currency and coin size.
	 * @param {string} value
	 * @param {number} [ multiplyBy = this.coinSize ]
	 * @param {CurrencyConfig} [ currencyOptions = currencyOptions={} ]
	 * @returns {number}
	 */
	public unformatMoney ( value: string, multiplyBy: number = this.coinSize, currencyOptions: CurrencyConfig = {} ): number {
		const baseOption = this.currency ? this.currency : {};
		const option: CurrencyConfig = Object.assign( baseOption, currencyOptions );

		return CurrencyFormatter.unformat( value, option ) * multiplyBy;
	}
}

export enum NumberDisplayMode {
	VALUE_ONLY, // 1,000.00
	VALUE_WITH_CURRENCY, // 1,000.00 GBP
	VALUE_WITH_CURREYCY_SYMBOL //  £1,000.00
}

export interface CurrencyConfig {
	code?: string;
	locale?: string;
	symbol?: string;
	decimal?: string;
	thousand?: string;
	precision?: number;
	format?: string | {
		pos: string,
		neg: string,
		zero: string
	};
}



