import { Inject } from 'typescript-ioc/es5';
import { EventParams } from 'corengf/src/events/GlobalDispatcher';
import { BigWinTierType } from 'slotngf/src/core/config/IBigWinConfig';
import { SlotCoinShowerController } from 'slotngf/src/components/coinshower/SlotCoinShowerController';
import { DHBCoinShowerView } from 'game/src/components/coinshower/DHBCoinShowerView';

export class DHBCoinShowerController extends SlotCoinShowerController {

	protected coinShowerView: DHBCoinShowerView;

	constructor ( @Inject view: DHBCoinShowerView ) {
		super( view );

		this.coinShowerView = view;
	}

	protected onTierReached ( e: EventParams ): void {
		if ( e.data.tier == BigWinTierType.Coins ) {
			switch ( e.data.finalTier ) {
				case BigWinTierType.ExtremeBigWin:
				case BigWinTierType.UltraExtremeBigWin: {
					this.coinShowerView.startEnvelopeShower();
				}
			}
		}
		super.onTierReached( e );
	}

	protected stopCoinShower (): void {
		this.coinShowerView.endEnvelopeShower();
		super.stopCoinShower();
	}

}


