import * as MiniSignal from 'mini-signals';
import { Container, Point } from 'pixi.js';
import { Inject } from 'typescript-ioc/es5';
import { Viewport } from 'corengf/src/display/Viewport';
import { SimpleTween } from 'corengf/src/time/SimpleTween';
import { Button, IButtonConfig } from './../button/Button';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { TextField } from 'corengf/src/elements/canvas/text/TextField';
import { TextButton } from 'corengf/src/elements/canvas/button/TextButton';
import { Window, IWindowConfig } from 'corengf/src/elements/canvas/window/Window';
import { ICommonAnimationConfig, CommonAnimation } from 'corengf/src/elements/canvas/animation/CommonAnimation';
import { CoreUIElementFactory, ICoreUITextFieldElementConfig } from 'corengf/src/factory/CoreUIElementFactory';
import { CoreElement, ICoreElementConfig } from 'corengf/src/elements/canvas/CoreElement';

export class ToolTip extends CoreElement {
	public static elementType: string = 'ToolTip';
	@Inject
	protected viewport: Viewport;
	public hideSignal: MiniSignal;
	public destroySignal: MiniSignal;
	protected window: Window;
	protected closeButton: Button;
	protected textButton: TextButton;
	protected title: TextField;
	protected text: TextField;
	protected config: IToolTipConfig;
	private timer: SimpleTimer;
	protected tween: SimpleTween;
	private isInClosing: boolean = false;

	protected _actionData: any;
	public get actionData (): any {
		return this._actionData;
	}

	constructor ( config: IToolTipConfig ) {
		super( config );
		this.hideSignal = new MiniSignal();
		this.destroySignal = new MiniSignal();
		this.interactive = true;
	}

	public initElement ( config: IToolTipConfig ): void {
		if ( config.onShowAnimation ) {
			this.visible = false;
		}
		let titleWidth = 0;
		let titleHeight = 0;
		let paddingLeft = config.paddingLeft ? config.paddingLeft : 0;
		let paddingTop = config.paddingTop ? config.paddingTop : 0;

		// #region CREATE TITLE
		if ( config.title !== undefined ) {
			this.title = CoreUIElementFactory.createInstance( config.title );

			titleWidth = this.title.width;
			titleHeight = this.title.height;
		}
		// #endregion CREATE TITLE

		if ( config.text !== undefined ) {
			this.text = CoreUIElementFactory.createInstance( config.text );
		}

		// #region CREATE TOOLTIP DYNAMIC BACKGROUND WINDOW
		let windowWidth: number;
		let windowHeight: number;

		const margin = config.margin ? ( config.margin * 2 ) : 25;
		if ( config.size && !config.fixedWidth && !config.fixedHeight ) {
			windowWidth = config.size.x;
			windowHeight = config.size.y;
		} else if ( config.fixedWidth && this.text ) {
			windowWidth = config.fixedWidth;
			windowHeight = titleHeight + this.text.height + margin;
		} else if ( config.fixedHeight && this.text ) {
			windowWidth = Math.max( titleWidth, this.text.width ) + margin;
			windowHeight = config.fixedHeight;
		} else if ( this.text ) {
			windowWidth = Math.max( titleWidth, this.text.width ) + margin;
			windowHeight = titleHeight + this.text.height + margin;
		}

		config.windowConfig.windowSize = { x: windowWidth, y: windowHeight };
		if ( config.windowConfig.anchor ) {
			this.config.windowConfig.position.x = -( ( this.config.windowConfig.windowSize.x - ( this.config.windowConfig.rightWidth ) ) * config.windowConfig.anchor.x );
			this.config.windowConfig.position.y = -( ( this.config.windowConfig.windowSize.y - ( this.config.windowConfig.topHeight ) ) * config.windowConfig.anchor.y );
		}
		this.window = new Window( config.windowConfig );

		// #endregion CREATE TOOLTIP DYNAMIC BACKGROUND WINDOW

		if ( config.position ) {
			this.position.set( config.position.x, config.position.y );
		} else {
			var mousePosition: IPoint = this.viewport.renderer.plugins.interaction.activeInteractionData[ 1 ].global;
			this.position.set( mousePosition.x, mousePosition.y );
		}

		if ( this.title ) {
			this.title.position.set( config.margin ? config.margin : 25, config.margin ? config.margin : 25 );
		}

		if ( config.closeButton ) {
			this.closeButton = new Button( config.closeButton );
			this.closeButton.clickData = { ...config.closeButton.clickData };
			this.closeButton.elementSignal.add( this.close, this );

			let closeButtonPosition = new PIXI.Point( 0, 0 );
			switch ( config.closeButton.center ) {
				case Align.VERTICAL_CENTER:
					this.closeButton.anchor.set( 0, .5 );
					closeButtonPosition.x = config.closeButton.position.x;
					closeButtonPosition.y = Math.floor( this.window.height / 2 );
					break;
				case Align.HORIZONTAL_CENTER:
					this.closeButton.anchor.set( 0.5, 0 );
					closeButtonPosition.x = Math.floor( this.window.width / 2 );
					closeButtonPosition.y = config.closeButton.position.y;
					break;
				default:
					closeButtonPosition = new PIXI.Point( config.closeButton.position.x, config.closeButton.position.y );
					break;
			}

			this.closeButton.position = closeButtonPosition;

			const hitWidth = this.closeButton.width * 3;
			const hitHeight = this.closeButton.height * 3;
			this.closeButton.setRectangleHitArea( hitWidth, hitHeight, -hitWidth / 2, -hitHeight / 2 );
		}

		if ( this.text ) {
			let textPosition: PIXI.Point;
			if ( config.textTooltipTextFieldConfig ) {
				switch ( config.textTooltipTextFieldConfig.center ) {
					case Align.VERTICAL_CENTER:
						this.text.anchor = new Point( 0, .5 );
						textPosition = new PIXI.Point( this.text.position.x, Math.floor( this.window.height / 2 ) );
						break;
					case Align.HORIZONTAL_CENTER:
						this.text.anchor = new Point( 0.5, 0 );
						textPosition = new PIXI.Point( Math.floor( this.window.width / 2 ), this.text.position.y );
						break;
					default:
						textPosition = new PIXI.Point( this.text.position.x, this.text.position.y );
						break;
				}

				this.text.position.set( paddingLeft + textPosition.x, paddingTop + textPosition.y + titleHeight );
			} else {
				textPosition = new PIXI.Point( this.text.position.x, this.text.position.y );
				this.text.position.set( paddingLeft + textPosition.x, paddingTop + textPosition.y + titleHeight );
			}
		}

		this.addChild( this.window );
		if ( this.title ) {
			this.addChild( this.title );
		}

		if ( config.leftsideDisplayObject ) {
			if ( config.leftsideDisplayObject.y == 0 ) {
				config.leftsideDisplayObject.y = Math.floor( this.window.height / 2 );
			}
			this.addChild( config.leftsideDisplayObject );
			if ( config.leftsideTween ) {
				const leftsideTween = config.leftsideTween;
				leftsideTween.start();
			}
		}

		if ( this.text ) {
			this.addChild( this.text );
		}

		if ( this.closeButton ) {
			this.addChild( this.closeButton );
		}

		if ( config.textButton ) {
			this.textButton = config.textButton;

			let xPoint = this.textButton.anchor ? this.textButton.anchor.x : 0;
			let yPoint = this.textButton.anchor ? this.textButton.anchor.y : 0;

			if ( config.textButton.position.x == 0 ) {
				xPoint = 0.5;
				this.textButton.x = Math.floor( this.window.width / 2 );
			}
			if ( config.textButton.position.y == 0 ) {
				yPoint = 0.5;
				this.textButton.y = Math.floor( this.window.height / 2 );
			}

			this.textButton.anchor = new Point( xPoint, yPoint );
			this.textButton.elementSignal.add( this.close, this );
			this.addChild( this.textButton );
		}

		if ( config.timeout !== undefined ) {
			this.timer = new SimpleTimer( config.timeout, () => {
				this.close();
			} );
		}

		if ( config.onShowAnimation ) {
			this.visible = true;
			let showConfig = config.onShowAnimation;
			showConfig.target = this;
			this.tween = CommonAnimation.startTween( showConfig );
			this.tween.start();
		}
		this.config = config;
	}

	public show (): void {
		this.visible = true;
		this.isInClosing = false;
		if ( this.config.onShowAnimation ) {
			let showConfig = this.config.onShowAnimation;
			showConfig.target = this;
			this.tween = CommonAnimation.startTween( showConfig );
			this.tween.start();
		}
		if ( this.config.timeout !== undefined ) {
			this.timer = new SimpleTimer( this.config.timeout, () => {
				this.close();
			} );
		}
		this.parent.addChild( this );  //Resets to top of display order
	}

	protected close ( data?: any ): void {
		if ( this.isInClosing ) {
			return;
		}

		this.isInClosing = true;
		this._actionData = data;

		if ( this.config.onCloseAnimation ) {
			let closeConfig = this.config.onCloseAnimation;
			closeConfig.target = this;
			this.tween = CommonAnimation.startTween( closeConfig );
			this.tween.onComplete( () => {
				if ( this.config.closeAction === CloseAction.HIDE ) {
					this.hide();
				} else {
					this.destroy();
				}
			} ).start();
		} else {
			if ( this.config.closeAction === CloseAction.HIDE ) {
				this.hide();
			} else {
				this.destroy();
			}
		}
	}

	public hide (): void {
		if ( this.tween ) {
			this.tween.stop();
		}
		if ( this.timer ) {
			this.timer.stop();
		}
		this.hideSignal.dispatch( this );
		this.visible = false;
	}

	public destroy ( forceClean: boolean = false ): void {
		if ( this.timer ) {
			this.timer.destroy();
		}
		if ( this.tween ) {
			this.tween.stop();
			this.tween = null;
		}

		this.window.destroy();

		if ( this.title ) {
			this.title.destroy();
		}
		if ( this.text ) {
			this.text.destroy();
		}
		if ( this.closeButton ) {
			this.closeButton.destroy();
		}

		super.destroy();

		if ( forceClean ) {
			this._actionData = { action: 'action_' + DestroyAction.CLEAN };
		}
		this.destroySignal.dispatch( this );
	}
}

export interface IToolTipConfig extends ICoreElementConfig {
	titleTooltipTextFieldConfig?: ITooltipTextFieldConfig;
	title?: ICoreUITextFieldElementConfig;
	textTooltipTextFieldConfig?: ITooltipTextFieldConfig;
	text?: ICoreUITextFieldElementConfig;
	closeButton?: ITooltipButton;
	textButton?: TextButton;
	size?: IPoint;

	/**
	 * Fix the Tooltip's background window width.
	 *
	 * @type {number}
	 * @memberof IToolTipConfig
	 */
	fixedWidth?: number;

	/**
	 * Fix the Tooltip's background window height.
	 *
	 * @type {number}
	 * @memberof IToolTipConfig
	 */
	fixedHeight?: number;
	margin?: number;
	paddingLeft?: number;
	paddingTop?: number;
	windowConfig: IWindowConfig;
	leftsideDisplayObject?: PIXI.DisplayObject;
	timeout?: number;
	closeAction?: CloseAction;
	onShowAnimation?: ICommonAnimationConfig;
	onCloseAnimation?: ICommonAnimationConfig;
	leftsideTween?: SimpleTween;
}

export interface ITooltipTextFieldConfig {
	center?: Align;
	offset?: IPoint;
}

interface ITooltipButton extends IButtonConfig {
	center?: Align;
	position: IPoint;
	offset?: IPoint;
	clickData?: any;
}

export interface ITooltipEventParam {
	name: string;
	data?: any;
}

export enum Align {
	VERTICAL_CENTER = 'VERTICAL_CENTER',
	HORIZONTAL_CENTER = 'HORIZONTAL_CENTER'
}

export enum CloseAction {
	HIDE,
	DESTORY
}

export enum DestroyAction {
	CLOSE = 'CLOSE',
	CLEAN = 'CLEAN',
	FADE = 'FADE'
}


