import { Easing } from '@tweenjs/tween.js';
import { NMath } from 'corengf/src/utils/NMath';
import { SimpleTween } from 'corengf/src/time/SimpleTween';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import { AudioTrackName } from 'corengf/src/audio/SoundManager';
import { SymbolPosition, SpinDirection } from 'slotngf/src/core/config/IReelsConfig';
import { SymbolState } from 'slotngf/src/components/reels/ReelsEnums';
import { SlotSoundConfig } from 'slotngf/src/core/config/SlotSoundConfig';
import { ReelActionData } from 'slotngf/src/components/reelV2/ReelActionHandler';
import { SpinBase, ISpinBaseConfig } from 'slotngf/src/components/reelV2/action/SpinBase';

export class Spin extends SpinBase {

	public key: string = 'SPIN';

	protected config: ISpinConfig;

	protected spinDirection: SpinDirection;

	protected setup ( config?: ISpinConfig ): void {
		this.config = config;
	}

	public run ( data: SpinActionData ): Promise<any> {
		this.stopRequested = false;
		this.reelModel.currentReelIndex = 0;
		this.spinDirection = data.spinDirection;

		return new Promise( ( resolve ) => {
			this._activeReels = data.reels;
			this.soundManager.play( `${ SlotSoundConfig.reelSpin }0${ NMath.randomInt( 1, 5 ) }`, AudioTrackName.EFFECT );
			for ( var i = 0; i < this.activeReels.length; i++ ) {
				const reelId = this.activeReels[ i ];

				new SimpleTimer( this.reelModel.currentReelStrips[ reelId ].config.startReelDelay || 0, this.spin.bind( this, reelId ) );
			}
			resolve();
		} );
	}

	/**
	 * In some situation, we have to tween down overlay with base reel view.
	 *
	 * @protected
	 * @param {number} layer
	 * @param {number} reelIndex
	 * @memberof Spin
	 */
	protected tweenOverlayDown ( layer: number, reelIndex: number ): void {
		const objects = this.view.getObjectsAtOverlay( layer, reelIndex );
		const symbolsOnReel = this.reelModel.currentReelStrips[ reelIndex ].config.symbolsOnReel;

		var layerLastOverlayReel = 0;
		for ( var i = 0; i < this.reelModel.currentReelStrips.length; i++ ) {
			if ( this.view.getObjectsAtOverlay( layer, i ).length > 0 ) {
				layerLastOverlayReel = i;
			}
		}

		objects.forEach( ( object ) => {
			const position: SymbolPosition = {
				reelIndex: +object.name.split( ',' )[ 0 ],
				symbolIndex: +object.name.split( ',' )[ 1 ],
			};

			let tween = new SimpleTween( { y: object.y } )
				.to( { y: object.y + this.reelModel.generalSymbolConfig.height * symbolsOnReel }, this.config.duration * symbolsOnReel )
				.easing( Easing.Linear.None )
				.onUpdate( ( coords ) => {
					object.y = coords.y;
				} )
				.onComplete( () => {
					this.view.hideOverlaySymbol( layer, position );
					if ( reelIndex == layerLastOverlayReel ) {
						this.view.cleanOverlays( [ layer ] );
					}
				} );

			tween.start();
		} );
	}


	protected spin ( reelIndex: number ): void {
		this.animStep( reelIndex );

		//// TODO
		if ( this.config.OverlayLayers ) {
			this.config.OverlayLayers.forEach( ( layer ) => {
				if ( layer != this.view.baseOverlayIndex ) {
					this.tweenOverlayDown( layer, reelIndex );
				}
			} );
		}
	}


	protected animStep ( reelId: number ): void {

		if ( this.reelModel.shouldMoveLastSymbolToTop( reelId, this.spinDirection ) ) {
			const symbolData = this.reelModel.createNextSymbol( reelId, this.spinDirection );
			this.view.moveLastSymbolToTop( reelId, symbolData, SymbolState.BLUR, this.spinDirection );
		}

		this.tweenSymbolsDown( [ reelId ], {
			duration: this.config.duration,
			distanceY: this.reelModel.generalSymbolConfig.height,
			easing: Easing.Linear.None,
			onComplete: this.tweenComplete.bind( this )
		}, this.spinDirection );
	}

	protected onStateChanged ( stateName: string ): void {
		//
	}

	protected tweenComplete ( reelId: number ): void {
		if ( this.reelModel.currentReelStrips[ reelId ].stopRequested ) {
			this.reelModel.onTweenSymbolComplete.dispatch( reelId );
		} else {
			this.animStep( reelId );
		}

	}
}

export interface SpinActionData extends ReelActionData {
	reels: Array<number>;
	spinDirection: SpinDirection;
}

export interface ISpinConfig extends ISpinBaseConfig {
	/**
	 * The duration of tween a symbol down
	 *
	 * @type {number}
	 * @memberof ISpinConfig
	 */
	duration: number;
	OverlayLayers?: Array<number>;
}


