import { Inject } from 'typescript-ioc/es5';
import { Point, Text, extras } from 'pixi.js';
import { IPoint } from './../../config/IElementsConfig';
import { Translator } from './../../../utils/Translator';
import { CoreElement, ICoreElementConfig } from './../CoreElement';


export abstract class TextField extends CoreElement {

	@Inject
	protected translator: Translator;

	protected label: Text | extras.BitmapText;
	protected key: string | number;

	protected config: ITextFieldConfig;

	private _maxWidth: number = -1;
	protected get maxWidth (): number {
		if ( this.config.autoscale ) {
			return this.config.autoscale.x;
		} else {
			return this._maxWidth;
		}
	}
	protected set maxWidth ( value: number ) {
		if ( this.config.autoscale ) {
			this.config.autoscale.x = value;
		} else {
			this._maxWidth = value;
		}
	}

	private _maxHeight: number = -1;
	protected get maxHeight (): number {
		if ( this.config.autoscale ) {
			return this.config.autoscale.y;
		} else {
			return this._maxHeight;
		}
	}
	protected set maxHeight ( value: number ) {
		if ( this.config.autoscale ) {
			this.config.autoscale.y = value;
		} else {
			this._maxHeight = value;
		}
	}

	protected initElement ( config: ITextFieldConfig ): void {
		this.config = config;
		this.key = config.key;
		this.translator.languageChangedSignal.add( this.onLocalisationUpdated, this );

		if ( config.autoscale !== undefined ) {
			this.setAutoscale( config.autoscale.x, config.autoscale.y );
		}

		if ( config.anchor !== undefined ) {
			this.anchor = new Point( config.anchor.x, config.anchor.y );
		}

		this.visible = ( config.visible == undefined ) ? true : config.visible;
	}

	protected onLocalisationUpdated (): void {
		this.setTextByKey( this.key );
	}

	public setTextByKey ( key: string | number ): void {
		this.text = this.isKey( key ) ? this.translator.t( key ) : key.toString();
	}

	protected isKey ( keyOrNumber: string | number ): keyOrNumber is string {
		return ( keyOrNumber as string ).indexOf !== undefined;
	}

	public getTextByKey ( key: string | number ): string {
		if ( key == undefined ) {
			return '';
		} else if ( this.isKey( key ) ) {
			return key.length > 0 ? this.translator.t( key ) : '';
		} else {
			return key.toString();
		}
	}

	protected autoResize (): void {
		if ( this.maxHeight < 0 && this.maxWidth < 0 ) {
			return;
		}

		let scaleWidth = 1;
		let scaleHeight = 1;
		this.label.scale.x = scaleWidth;
		this.label.scale.y = scaleHeight;

		if ( this.width > this.maxWidth ) {
			scaleWidth = this.maxWidth / this.width;
		}

		if ( this.height > this.maxHeight ) {
			scaleHeight = this.maxHeight / this.height;
		}

		let scale = Math.min( scaleWidth, scaleHeight );

		if ( scale != 1 ) {
			this.label.scale.x = scale;
			this.label.scale.y = scale;
		}
	}

	public setAutoscale ( maxWidth: number, maxHeight: number ): void {
		this.maxWidth = maxWidth;
		this.maxHeight = maxHeight;

		if ( maxWidth == -1 ) {
			this.label.scale.x = 1;
		}

		if ( maxHeight == -1 ) {
			this.label.scale.y = 1;
		}

		this.updateTextField();
	}

	protected updateTextField (): void {
		this.autoResize();
	}

	public set anchor ( anchor: IPoint ) {
		this.label.anchor = new Point( anchor.x, anchor.y );
	}

	public set text ( text: string ) {
		this.label.text = text;
		this.updateTextField();
	}

	public get text () {
		return this.label.text;
	}

	public get width (): number {
		return this.label.width;
	}

	public get height (): number {
		return this.label.height;
	}

	public updateAttributes ( config: ITextFieldConfig ): void {
		super.updateAttributes( config );

		if ( config.anchor ) {
			this.anchor = config.anchor;
		}

		if ( config.autoscale != undefined ) {
			this.setAutoscale( config.autoscale.x, config.autoscale.y );
		}

		if ( config.key != undefined ) {
			this.key = config.key;
			this.setTextByKey( this.key );
		}
	}
}

export interface ITextFieldConfig extends ICoreElementConfig {
	autoscale?: IPoint;
	key: string | number;
}


