import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { UIPanelController } from 'corengf/src/components/ui/components/UIPanelController';
import { SlotEvent } from 'slotngf/src/core/SlotEvent';
import { MLUIMenuPanel } from 'mlui/src/components/menu/MLUIMenuPanel';
import { IMLUIMenuConfig, MLUIMenuConfig } from 'mlui/src/components/menu/MLUIMenuConfig';


export class MLUIMenuController extends UIPanelController {

	protected view: MLUIMenuPanel;
	public showMenuPopupSignal: MiniSignal;

	constructor ( @Inject view: MLUIMenuPanel ) {
		super( view );
		this.showMenuPopupSignal = new MiniSignal();
	}

	public init (): void {
		let config = this.gameConfig.getConfig<IMLUIMenuConfig>( MLUIMenuConfig );

		this.view.initialize( config );

		this.setButtonActionEventListeners( config.settingsButton );
		this.setButtonActionEventListeners( config.closeButton );
		this.finishStateAction();
	}

	public addEventListeners (): void {
		super.addEventListeners();
		this.view.showSignal.add( this.onDisplayChange, this );
	}

	protected action_showSettings (): void {
		this.showMenuPopupSignal.dispatch( 'settings' );
	}

	protected action_hideMenuOptions (): void {
		this.view.hidePopup();
	}

	protected onDisplayChange ( value: boolean ): void {
		if ( value ) {
			GlobalDispatcher.dispatchIntoAllStates( SlotEvent.SHOW_FULL_SCREEN_UI );
		} else {
			GlobalDispatcher.dispatchIntoAllStates( SlotEvent.HIDE_FULL_SCREEN_UI );
		}
	}

	public showPopup (): void {
		this.view.showPopup();
	}

	public closeExternalModules (): void {
		this.view.closeExternalModules();
	}
}



