import { AbstractFormatter } from 'corengf/src/server/abstract/formatter/AbstractFormatter';

/**
 * Encapsulates code/encode rules for messages
 */
export class SlotHttpFormatter extends AbstractFormatter {

	/**
	 * Regular slot spin command
	 */
	public static readonly SPIN: string = 'spin';

	/**
	 * Command for starting gamble and showing options for player
	 */
	public static readonly START_GAMBLE: string = 'gamble_start';

	/**
	 * Command for starting gamble and showing options for player
	 */
	public static readonly GAMBLE_RESULT: string = 'gamble_result';

	/**
	 * Command for starting gamble and showing options for player
	 */
	public static readonly GAMBLE_CHOICE: string = 'gamble_choice';

	/**
	 * Command for requesting run pick feature
	 */
	public static readonly RUN_PICK_FEATURE: string = 'pick_feature';

	/**
	 * Command for requesting run free respin feature
	 */
	public static readonly FREE_RESPIN_REELS: string = 'free_respin_reels';

	/**
	 * Command for requesting run free spin feature
	 */
	public static readonly RUN_FREE_SPIN_FEATURE: string = 'free_spin_feature';

	/**
	 * Command for requesting run jackpot feature
	 */
	public static readonly RUN_JACKPOT_FEATURE: string = 'jackpot_feature';

	/**
	 * Command for requesting in game history
	 */
	public static readonly HISTORY_SUMMARY: string = 'direct_history_summary';
	public static readonly HISTORY_DETAIL: string = 'direct_history_detail';

	/**
	 * Command for requesting in game history for result panel, but it's same with HISTORY_SUMMARY and HISTORY_DETAIL
	 */
	public static readonly RESULT_HISTORY_SUMMARY: string = 'direct_result_history_summary';
	public static readonly RESULT_HISTORY_DETAIL: string = 'direct_result_history_detail';

	/**
	 * Forced slot spin command
	 */
	public static readonly FORCE_SPIN: string = 'force_spin';

	public static readonly GAMBLE_REQUEST: string = 'gamble_request';

	public static readonly TAKE_WIN: string = 'take_win';
	public static readonly PAYOUT: string = 'payout';
}


