import { IExternalControl, IExternalModuleConfig } from 'corengf/src/components/externalModule/IExternalControl';

export interface IHistoryConfig extends IExternalModuleConfig {
	locale: string;
	engagements: Array<string>;
	sendHistoryDetailQuery ( query: IHistoryDetailQuery ): void;
	sendHistorySummaryQuery ( query: IHistoryDetailQuery ): void;
}

export interface IHistoryQuery {
	type: string;
}

export interface IHistoryDetailQuery extends IHistoryQuery {
	roundID: string;
}

export interface IHistorySummaryQuery extends IHistoryQuery {
	dayAmount: number;
	pageNum: number;
	pageSize: number;
}

export enum DATE_INTERVAL {
	DAY = 'DAY',
	WEEK = 'WEEK',
	MONTH = 'MONTH',
	BETWEEN = 'BETWEEN'
}

// callbacks:
export interface IHistoryModuleControl extends IExternalControl {
	updateHistorySummary ( response: any ): void;
	updateHistoryDetail ( response: any ): void;
}


