import { Graphics } from 'pixi.js';
import { CompView } from 'corengf/src/mvc/CompView';
import { ViewportAlign } from 'corengf/src/display/Viewport';
import { Button } from 'corengf/src/elements/canvas/button/Button';
import { WebTextField } from 'corengf/src/elements/canvas/text/WebTextField';
import { CoreUIElementFactory } from 'corengf/src/factory/CoreUIElementFactory';
import { ErrorDialogType, IErrorDialogConfig } from 'corengf/src/components/ErrorDialog/ErrorDialogConfig';


export class ErrorDialogView extends CompView {

	private errorCode: WebTextField;

	private errorMsg: WebTextField;

	private closeButton: Button;

	protected config: IErrorDialogConfig;

	private background: Graphics;

	public createElements ( config: IErrorDialogConfig ): void {
		this.config = config;
		this.mainContainer = this.gameContainerManager.errorDialogContainer;

		if ( !this.background ) {
			this.background = new Graphics();
			this.background.beginFill( config.background.color, config.background.alpha );
			this.background.drawRect( 0, 0, this.viewport.width, this.viewport.height );
			this.background.endFill();
			this.background.interactive = true;
			this.addChild( this.background );
		}

		if ( !this.errorCode ) {
			this.errorCode = new WebTextField( config.errorCode );
			this.addChild( this.errorCode );
		}

		if ( !this.errorMsg ) {
			this.errorMsg = new WebTextField( config.errorMsg );
			this.addChild( this.errorMsg );
		}

		try {
			if ( !this.closeButton ) {
				this.closeButton = CoreUIElementFactory.createInstance( config.closeButton );
				this.closeButton.elementSignal.add( this.closeDialog.bind( this ) );
				// it's only add in second createElements.
				this.addChild( this.closeButton );
			}
		} catch ( err ) {
			// don't need to do anything because the game will stop.
			console.log( err );
		}

		this.visible = false;
	}

	protected layoutElements (): void {
		super.layoutElements();
		this.background.width = this.viewport.width;
		this.background.height = this.viewport.height;

		const center = this.viewport.getAlignedPoint( ViewportAlign.CENTER_MIDDLE );
		this.errorCode.x = center.x;
		this.errorCode.y = center.y + this.config.errorCodeOffset.y;

		this.errorMsg.x = center.x;
		this.errorMsg.y = center.y + this.config.errorMsgOffset.y;
	}

	public openDialog ( type: ErrorDialogType, errorCode: string, msg: string ): void {
		this.visible = true;
		const content = msg;

		this.errorCode.text = `Error ${ errorCode }`;
		this.errorMsg.text = content;

		// if closeButton exist
		if ( this.closeButton ) {
			this.setCloseButtonActionData( errorCode );
			switch ( type ) {
				case ErrorDialogType.FULL_SCREEN:
					this.closeButton.visible = false;
					break;
				case ErrorDialogType.WITH_OPTION:
					this.closeButton.visible = true;
					break;
				case ErrorDialogType.WITHOUT_OPTION:
					this.closeButton.visible = false;
					break;
			}
		}
	}

	public closeDialog (): void {
		this.visible = false;
		this.config.onCloseDialog();
	}

	protected setCloseButtonActionData ( errorCode: string ): void {
		this.closeButton.clickData = { errorCode };
	}
}



