import { TsMap } from 'corengf/src/utils/TsMap';
import { Singleton } from 'typescript-ioc/es5';
import { ButtonBehaviour } from 'corengf/src/buttons/ButtonsStateSet';
import { UIButtonId } from 'corengf/src/components/ui/buttons/UIButtonId';
import { LocalButtonsStateSet } from 'corengf/src/buttons/LocalButtonsStateSet';

/**
 * Local Buttons State Class
 * Set state for the local buttons
 */
@Singleton
export class UILocalButtonsStateSet extends LocalButtonsStateSet {

	protected buttonStates: TsMap<string, TsMap<string, ButtonBehaviour>>;

	protected default (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = new TsMap<string, ButtonBehaviour>();
		buttonState.set( UIButtonId.BUTTON_SHOW_BET_OPTIONS, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.BUTTON_CONFIRM_BET_OPTIONS, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.BUTTON_CANCEL_BET_OPTIONS, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.BUTTON_SHOW_MENU_OPTIONS, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.BUTTON_HIDE_MENU_OPTIONS, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.BUTTON_SHOW_SETTINGS, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.BUTTON_HIDE_SETTINGS, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.BUTTON_HOME, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.BUTTON_SHOW_PAYTABLE, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.TOGGLE_SOUND, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.TOGGLE_MUSIC, new ButtonBehaviour( false, false ) );
		return buttonState;
	}

	protected init (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = new TsMap<string, ButtonBehaviour>();
		buttonState.set( UIButtonId.BUTTON_SHOW_BET_OPTIONS, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.BUTTON_CONFIRM_BET_OPTIONS, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.BUTTON_CANCEL_BET_OPTIONS, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.BUTTON_SHOW_MENU_OPTIONS, new ButtonBehaviour( true, true ) );
		buttonState.set( UIButtonId.BUTTON_HIDE_MENU_OPTIONS, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.BUTTON_SHOW_SETTINGS, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.BUTTON_HIDE_SETTINGS, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.BUTTON_HOME, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.BUTTON_SHOW_PAYTABLE, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.TOGGLE_SOUND, new ButtonBehaviour( false, false ) );
		buttonState.set( UIButtonId.TOGGLE_MUSIC, new ButtonBehaviour( false, false ) );
		return buttonState;
	}
}



