import * as MiniSignal from 'mini-signals';
import { TsMap } from 'corengf/src/utils/TsMap';
import { CoreState } from 'corengf/src/states/CoreState';
import { ReelModel } from 'slotngf/src/components/reelV2/ReelModel';
import { ISpinConfig } from 'slotngf/src/components/reelV2/action/Spin';
import { ISpinStopData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { ReelActionModel } from 'slotngf/src/components/reelV2/ReelActionModel';
import { ISpinningConfig } from 'slotngf/src/components/reelV2/action/Spinning';
import { ISpinDownConfig } from 'slotngf/src/components/reelV2/action/SpinDown';
import { IReplacingConfig } from 'slotngf/src/components/reelV2/action/Replacing';
import { ReelControllerView } from 'slotngf/src/components/reelV2/ReelControllerView';
import { SpinDirection } from 'slotngf/src/core/config/IReelsConfig';

/**
 * A class that combines required reel actions with corresponding states.
 *
 * @export
 * @class ReelActionHandler
 */
export class ReelActionHandler {

	private _startSpin: ReelActionModel;
	private _spinning: ReelActionModel;
	private _spinStop: ReelActionModel;
	private _allReelStopped: ReelActionModel;
	private _featureAction: ReelActionModel;

	private _actionMap: TsMap<string, ReelActionModel> = new TsMap<string, ReelActionModel>();

	private actionPromise: Promise<any> = Promise.resolve();

	private currentStateName: string;

	private onStateChanged: MiniSignal = new MiniSignal();

	public initialize ( reelModel: ReelModel, view: ReelControllerView, config: IReelActionHandlerConfig ): void {

		if ( config.startSpin ) {
			this._startSpin = config.startSpin.model;
			this._actionMap.set( this._startSpin.key, this._startSpin );

			this._startSpin.initialize( reelModel, view, this.onStateChanged, config.startSpin.config );
		}

		if ( config.spinning ) {
			this._spinning = config.spinning.model;
			this._actionMap.set( this._spinning.key, this._spinning );
			this._spinning.initialize( reelModel, view, this.onStateChanged, config.spinning.config );
		}

		if ( config.spinStop ) {
			this._spinStop = config.spinStop.model;
			this._actionMap.set( this._spinStop.key, this._spinStop );
			this._spinStop.initialize( reelModel, view, this.onStateChanged, config.spinStop.config );
		}

		if ( config.allReelStopped ) {
			this._allReelStopped = config.allReelStopped.model;
			this._actionMap.set( this._allReelStopped.key, this._allReelStopped );
			this._allReelStopped.initialize( reelModel, view, this.onStateChanged, config.allReelStopped.config );
		}

		if ( config.featureAction ) {
			this._featureAction = config.featureAction.model;
			this._actionMap.set( this._featureAction.key, this._featureAction );
			this._featureAction.initialize( reelModel, view, this.onStateChanged, config.featureAction.config );
		}

		if ( config.customActionMap ) {
			config.customActionMap.forEach( ( value, key ) => {
				this._actionMap.set( key, value.model );
				value.model.initialize( reelModel, view, this.onStateChanged, value.config );
			} );
		}
	}

	public setState ( state: CoreState ): void {
		if ( this.currentStateName !== state.stateName ) {
			this.currentStateName = state.stateName;
			this.onStateChanged.dispatch( this.currentStateName );
		}
	}

	public startSpin ( data: ReelActionData ): Promise<any> {
		this.actionPromise = this._startSpin.run( data );
		return this.actionPromise;
	}

	public spinning ( data: any ): Promise<any> {
		this.actionPromise = this.actionPromise.then( () => {
			return this._spinning.run( data );
		} );
		return this.actionPromise;
	}

	public spinStop ( data: SpinStopData ): Promise<any> {
		this.actionPromise = this.actionPromise.then( () => {
			return this._spinStop.run( data );
		} );
		return this.actionPromise;
	}

	public allReelStopped ( data: any ): void {
		if ( !this._allReelStopped ) {
			return;
		}
		this._allReelStopped.run( data );
	}

	public featureAction ( data: any ): Promise<any> {
		if ( !this._featureAction ) {
			return Promise.resolve();
		}
		this.actionPromise = this.actionPromise.then( () => {
			return this._featureAction.run( data );
		} );
		return this.actionPromise;
	}

	/**
	 * Trigger features by given action items
	 *
	 * @param {Array<ReelActionItem>} items
	 * @param {boolean} [async=false] run in asynchronous mode. Default value is false.
	 * @returns {Promise<any>}
	 * @memberof ReelActionHandler
	 */
	public triggerActionByKey ( items: Array<ReelActionItem>, async: boolean = false ): Promise<any> {
		if ( this._actionMap.size === 0 ) {
			return;
		}
		this.actionPromise = this.actionPromise.then( () => {
			if ( async === true ) {
				return Promise.all( items.map( item => this._actionMap.get( item.key ).run( item.data ) ) );
			} else {
				const actions = items.map( item => this._actionMap.get( item.key ) );
				return actions.reduce( ( prev, reelActionModel, currentIndex ) => {
					return prev.then( () => {
						return reelActionModel.run( items[ currentIndex ].data );
					} );
				}, Promise.resolve() );
			}
		} );
		return this.actionPromise;
	}
}

export interface SpinStopData extends ISpinStopData {
	reels: Array<number>;
	spinDirection: SpinDirection;
}

export interface IReelActionHandlerConfig {
	startSpin: ActionModelConfig;
	spinning: ActionModelConfig;
	spinStop: ActionModelConfig;
	allReelStopped: ActionModelConfig;

	/**
	 * basic feature action like lock wild, replacing.
	 *
	 * @type {ActionModelConfig}
	 * @memberof IReelActionHandlerConfig
	 */
	featureAction: ActionModelConfig;

	/**
	 *  If you want to trigger multiple actions, set the customActionMap, and use triggerActionByKey() to trigger multiple actions.
	 *
	 * @type {TsMap<string, ActionModelConfig>}
	 * @memberof IReelActionHandlerConfig
	 */
	customActionMap?: TsMap<string, ActionModelConfig>;
}

export interface ActionModelConfig {
	model: ReelActionModel;
	config?: ISpinConfig | ISpinningConfig | ISpinDownConfig | IReplacingConfig;
}

export interface ReelActionData {

}

export interface ReelActionItem {
	key: string;
	data?: any;
}


