import { Inject } from 'typescript-ioc/es5';
import { States } from 'corengf/src/states/States';
import { TsMap } from 'corengf/src/utils/TsMap';
import { BaseButtonsStateSet, BaseButtonBehaviour } from 'corengf/src/buttons/BaseButtonStateSet';

/**
 * Buttons State Class
 * Set state for the buttons
 */
export class ButtonsStateSet extends BaseButtonsStateSet {

	protected buttonStates: TsMap<string, TsMap<string, ButtonBehaviour>>;

	@Inject
	protected stateNames: States;

	/**
	 * set states for the buttons
	 */
	protected initStates (): void {
		this.buttonStates.set( ButtonsStateSet.DEFAULT_TEMPLATE, this.default() );
		this.buttonStates.set( this.stateNames.init, this.init() );
		this.buttonStates.set( this.stateNames.baseGameIdle, this.baseGameIdle() );
	}

	protected baseGameIdle (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = new TsMap<string, ButtonBehaviour>();
		return buttonState;
	}
}

export class ButtonBehaviour extends BaseButtonBehaviour {
}



