import { Helper } from 'corengf/src/utils/Helper';
import { Scenario } from 'corengf/src/states/Scenario';
import { WinLine } from 'slotngf/src/components/lines/WinLine';
import { IWinningData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { IWildMultiplier } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { IGameState, IFeatureInfo, ISpinResultReelWins, ISpinResult } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { SpinResponseFormatter } from 'slotngf/src/server/ng/formatter/http/in/SpinResponseFormatter';
import { AbstractFeatureScenarioFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/AbstractFeatureScenarioFormatter';

/**
 * formatting wild multiplier feature
 */
export class WildMultiplierFeatureFormatter extends SpinResponseFormatter implements AbstractFeatureScenarioFormatter {

	/**
	 * formatting run feature api response
	 * @param message
	 */
	public processIncomingMessage ( message: IGameState, featureInfo?: IFeatureInfo ): Scenario {
		console.warn( 'here shouldn\'t be triggered, since wild multiplier feature don\'t need another transaction to complete feature' );
		return null;
	}

	public getFeatureScenarioBeforeFreeSpinStop ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return freeSpinStopScenario;
	}

	public getFeatureScenarioAfterSpinStop ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		if ( freeSpinStopScenario.hasStateName( this.stateNames.freeSpinsOutroPopup ) ) {
			this.margeScenarioStateData( freeSpinStopScenario,
				this.stateNames.freeSpinsOutroPopup,
				{ wildMultiplier: this.getWildMultiplierData( message, featureInfo ) }
			);
		}
		return freeSpinStopScenario;
	}

	protected getWildMultiplierData ( message: IGameState, featureInfo: IFeatureInfo ): any {
		return ( message.spin_result ) ? featureInfo.feature_state : undefined;
	}

	/**
	 * formatting feature in feature_triggered to get feature scenario
	 */
	public triggeredFeatureIncomingMessage ( message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		if ( featureInfo == undefined ) {
			return null;
		}

		return this.getWildMultiplierScenario( message, featureInfo );
	}

	protected getWildMultiplierScenario ( message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		let responseScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.freeSpinsWithWildMultipliers );
		responseScenario.setStateData( this.stateNames.freeSpinsWildMultipliers, { wildMultiplier: this.getWildMultiplierData( message, featureInfo ) } );
		return responseScenario;
	}

	public getFeatureShowWinningData ( winningData: IWinningData, spinResult: ISpinResult, featureInfo: IFeatureInfo ): IWinningData {
		const wildConfig: any = featureInfo.feature_state;
		const winTable = this.createWinTable( spinResult );
		let winLines: Array<WinLine> = new Array<WinLine>();

		if ( spinResult.reel_wins != null && spinResult.reel_wins.length > 0 ) {
			spinResult.reel_wins.forEach( ( winElement: ISpinResultReelWins ) => {
				winLines.push( this.getWinline( winElement, winTable, wildConfig ) );
			} );
		}

		winningData.winLines = Helper.mergeTwoObjects( winningData.winLines, winLines ) as Array<WinLine>;

		return winningData;
	}

	public getFeatureScenarioAfterWin ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return freeSpinStopScenario;
	}

	/**
	 * formatting feature while restore feature
	 */
	public restoreFeatureIncomingMessage ( wildMultiplierFeatureDetail: IWildMultiplier ): Scenario {
		let wildMultiplierFeatureScenario: Scenario = null;
		console.warn( 'didn\'t plan for restore wild multiplie feature, since it won\'t create another transection' );
		return wildMultiplierFeatureScenario;
	}

	public getFeatureRestoreScenario ( restoreScenario: Scenario, featureInfo: IFeatureInfo ): Scenario {
		return restoreScenario;
	}
}



