import { Inject } from 'typescript-ioc/es5';
import { CompController } from 'corengf/src/mvc/CompController';
import { IButtonConfig } from 'corengf/src/elements/canvas/button/Button';
import { UILocalButtonsController } from 'corengf/src/components/ui/buttons/UILocalButtonsController';


export class UIPanelController extends CompController {

	@Inject
	protected localButtonsController: UILocalButtonsController;

	protected setButtonActionEventListeners ( buttonConfig: IButtonConfig ): void {
		this.localButtonsController.setAction( buttonConfig.action, this );
	}
}


