import { ICompViewConfig } from 'corengf/src/core/config/ICompViewConfig';
import { IGameElement } from 'corengf/src/core/config/IMainConfig';
import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Orientation } from 'corengf/src/display/Viewport';

export interface IReelsConfig extends ICompViewConfig {
	anticipationData: AnticipationData;
	idleAnimationDelay: number;

	/**
	 * If you want the animations to loop continusly use true otherwise they will be played at random
	 *
	 * @type {boolean}
	 * @memberof IReelsConfig
	 */
	idleAnimationLoop: boolean;
	idleAnimationRandomChance?: IRandomChance;
	reelStopNormalTime: number;

	/**
	 * An array that contains all possible reel set name that might be used.
	 *
	 * @type {Array<number>}
	 * @memberof IReelsConfig
	 */
	avaliableReelSet: Array<number>;
	reelDefinition: IReelStripConfig;
	symbolDefinition: ISymbolDefinition;
	reelSet: IReelSet;
	reels: Array<IReelStripConfig>;
	reelBackground?: IGameElement;
	reelsFrames?: Array<IGameElement>;

	defaultSpinDirection?: SpinDirection;

	individualReelsMask: boolean;

	/**
	 * Presents the orientation-related mask layout data.
	 * If not set, fallback to defaultReelsMask.
	 * Layer1 key: reel set index
	 * Layer2 key: orientation. Avaliable value: portrait | landscape
	 *
	 * @type {TsMap<number, TsMap<Orientation, ReelMask>>}
	 * @memberof IReelsConfig
	 */
	orientationMaskData?: TsMap<number, TsMap<Orientation, ReelMask>>;

	/**
	 * Presents the orientation-related main reel container layout data.
	 * If not set, fallback to defaultLayoutData.
	 *
	 * @type {TsMap<Orientation, IReelLayoutData>}
	 * @memberof IReelsConfig
	 */
	orientationLayoutData?: TsMap<Orientation, IReelLayoutData>;

	/**
	 * Presents the default mask for each reelSet.
	 * Key : ReelSet index
	 * @type {TsMap<number, ReelMask>}
	 * @memberof IReelsConfig
	 */
	defaultReelsMask?: TsMap<number, ReelMask>;

	/**
	 * Presents the default main reel container layout data.
	 *
	 * @type {IReelLayoutData}
	 * @memberof IReelsConfig
	 */
	defaultLayoutData?: IReelLayoutData;

	/**
	 * Presents the default position of each single reel.
	 *
	 * @type {Array<IReelLayoutData>}
	 * @memberof IReelsConfig
	 */
	defaultReelPosition?: Array<IReelLayoutData>;
}

export interface IReelLayoutData {
	position: IPoint;
	scale?: number;
}

export interface SymbolPosition {
	reelIndex: number;
	symbolIndex: number;
}

export interface IRandomChance {
	time: number;	//every x seconds it attempts to play the animation
	percent: number;	// percent of chance idle animations will play when timer ends. Use between 0 and 1
	startDelayMin: number;	//If the idle animation plays it will choost a random start time between this and the startelayMin
	startDelayMax: number;
}

export interface IReelSet {
	[ key: string ]: Array<IReelStripConfig>;
}

export type ReelMask = string | IMask;

export interface IReelStripConfig {

	offsetX?: number;
	offsetY?: number;

	/**
	 * unique id of the reel strip
	 *
	 * @type {number}
	 * @memberof IReelStripConfig
	 */
	id?: number;

	/**
	 * reel position data when orientation is in portrait mode
	 *
	 * @type {IReelLayoutData}
	 * @memberof IReelStripConfig
	 */
	portraitPosition?: IReelLayoutData;

	/**
	 * reel position data when orientation is in landscape mode
	 *
	 * @type {IReelLayoutData}
	 * @memberof IReelStripConfig
	 */
	landscapePosition?: IReelLayoutData;

	/**
	 * Width of the reel strip
	 *
	 * @type {number}
	 * @memberof IReelStripConfig
	 */
	width?: number;

	symbol?: ISymbolConfig;
	rangeSymbols?: Array<string>;
	dropSymbols?: Array<string>;
	stripOrder?: Array<string>;
	symbolsOnReel?: number;
	reelTween?: ReelTweenData;
	stopTween?: ReelTweenData;
	startReelDelay?: number;
	stopReelNormalDelay?: number;
	winAnimationOverride?: WinAnimationOverride;
	countTopExtraSymbol?: number;
	countBottomExtraSymbol?: number;

	visible?: boolean;
}

export interface ISymbolDefinition {
	generalSymbol: ISymbolConfig;
	specialSymbol: { [ key: string ]: ISymbolConfig };
	shakeConfig?: ISymbolShakeConfig;
}

export interface ReelTweenData {
	duration: number;
	ease: ( k: number ) => number;
}
export interface ISymbolShakeConfig {
	intensity: number;
	delay: number;
}
export interface ISymbolConfig {
	width: number;
	height: number;
	animationOffsetX?: number;
	animationOffsetY?: number;
	shakeConfig?: ISymbolShakeConfig;
}

export interface IAnticipationAnimation {
	type: AnticipationAnimationType;
	name: string;
	introTime?: number;
	reelTransitionTime?: number;
	outroTime?: number;
}

export interface IAnticipationParticle extends IAnticipationAnimation {
	images: string[];  //ONLY USED FOR PARTICLE ANIMATIONS
}

export interface IAnticipationModelData {
	anticipationSymbolCountTrigger: number;
	anticipationSymbolCountRequired: number;
}

export enum AnticipationAnimationType {
	SPINE = 'spine',
	SEQUENCE = 'sequence',
	PARTICLE = 'particle'
}

export interface AnticipationData {
	animations: Array<IAnticipationAnimation | IAnticipationParticle>;

	/**
	 * Anticipation delay duration in milliseconds
	 *
	 * @type {number}
	 * @memberof AnticipationData
	 */
	anticipationDelay: number;

	modelData: Array<IAnticipationModelData>;
}

export interface WinTweenData {
	scale: number;
	inDuration: number;
	outDuration: number;
	outDelay: number;
	ease?: ( k: number ) => number;
}

export interface WinAnimationOverride {
	symbolOverrides: Array<string>;
	tweenData: WinTweenData;
}

export interface IMask {
	x: number;
	y: number;
	width: number;
	height: number;
}

export enum SpinDirection {
	SpinUp,
	SpinDown
}

export enum RotateDirection {
	Clockwise = 1,
	CounterClockwise = -1
}


