import { Container, Singleton } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Button } from 'corengf/src/elements/canvas/button/Button';
import { ButtonsStateSet } from 'corengf/src/buttons/ButtonsStateSet';
import { BaseButtonsController } from 'corengf/src/buttons/BaseButtonsController';

@Singleton
export class ButtonsController extends BaseButtonsController {

	protected buttons: TsMap<string, Button>;
	protected buttonStates: ButtonsStateSet;

	protected lastTemplateName: string;

	protected init (): void {
		this.buttons = new TsMap<string, Button>();
		this.buttonStates = Container.get( ButtonsStateSet );
	}

	public applyButtonsTemplate ( templateName: string ): void {
		super.applyButtonsTemplate( templateName );
		this.lastTemplateName = templateName;
	}
}


