import * as MiniSignal from 'mini-signals';
import { ToggleButton } from './ToggleButton';
import { IButtonConfig, Button } from './../button/Button';
import { TextToggleButton } from './ToggleTextButton';
import { ITextButtonConfig } from './../button/TextButton';
import { MultiButton, IMultiButtonConfig } from './../button/MultiButton';

/**
 * A Container Class that holds and displays multiple Toggle buttons.
*/
export class ToggleMultiButton extends MultiButton {

	protected static readonly TOGGLE_ALL_SWITCH: string = 'TOGGLE_ALL_SWITCH';

	public onToggle: MiniSignal = new MiniSignal();

	private _currentIndex: number;

	get currentIndex (): number {
		return this._currentIndex;
	}

	set currentIndex ( value: number ) {
		this._currentIndex = value;
	}

	protected buttons: Array<ToggleButton | TextToggleButton>;

	protected toggleAllButton: ToggleButton;

	protected config: IToggleMultiButtonConfig;

	protected init ( config: IToggleMultiButtonConfig ): void {
		super.init( config );

		if ( config.toggleAllButton ) {
			this.setupToggleAllButton( config.toggleAllButton );
		}

		if ( this.defaultIndex !== undefined ) {
			this.selectButton( this.buttons[ this.defaultIndex ], true );
		}
	}

	protected setupToggleAllButton ( buttonData: IButtonConfig ): void {
		this.toggleAllButton = new ToggleButton( buttonData, this.localButtonController );

		this.toggleAllButton.elementSignal.add( this.onClickToggleAllButton.bind( this ) );

		this.toggleAllButton.name = ToggleMultiButton.TOGGLE_ALL_SWITCH;

		this.addChild( this.toggleAllButton );
	}

	protected onClickToggleAllButton (): void {
		this.onToggle.dispatch( this.toggleAllButton.selected );
		if ( this.toggleAllButton.selected ) {
			this.enableAllButtons( this.config.defaultIndexWhenOn );
		} else {
			this.disableAllButtons();
		}
	}

	protected createSingleButton ( buttonData: IButtonConfig | ITextButtonConfig, index: number ): Button {
		let button: Button;
		if ( this.isTextButtonConfig( buttonData ) ) {
			button = new TextToggleButton( buttonData, this.localButtonController );
		} else {
			button = new ToggleButton( buttonData, this.localButtonController );
		}

		button.clickData = {
			index
		};

		( button as ToggleButton ).selected = ( index == this.defaultIndex && this.config.autoPickDefaultButton );

		return button;
	}

	protected onSelectButton ( data: any ): void {
		let selectedButton = data.target.parent as ToggleButton;
		this.buttons.forEach( ( button: ToggleButton ) => {
			this.selectButton( button, selectedButton == button );
		} );
		this.selectedSignal.dispatch( selectedButton.getAction() );
	}

	protected selectButton ( button: ToggleButton, select: boolean ): void {
		button.selected = select;
		if ( select ) {
			this.currentIndex = button.clickData.index;
		}
	}

	public updateAttributes ( config: IToggleMultiButtonConfig ): void {
		super.updateAttributes( config );

		if ( !config ) {
			return;
		}

		this.children.forEach( ( child: Button, index: number ) => {
			if ( child.name === ToggleMultiButton.TOGGLE_ALL_SWITCH ) {
				if ( config.toggleAllButton ) {
					this.toggleAllButton.updateAttributes( config.toggleAllButton );
				}
			} else if ( config.buttonsOrientationConfig ) {
				const buttonConfig = config.buttonsOrientationConfig;

				child.scale.x = ( buttonConfig.scale ) ? buttonConfig.scale.x : 1;
				child.scale.y = ( buttonConfig.scale ) ? buttonConfig.scale.y : 1;
				child.position.x = ( child.width + buttonConfig.offset ) * index;
			}
		} );
	}

	public enableAllButtons ( index?: number ): void {
		super.enableAllButtons();
		if ( this.toggleAllButton ) {
			this.toggleAllButton.selected = true;
		}
		this.buttons.forEach( ( button: ToggleButton, buttonIndex ) => {
			this.selectButton( button, buttonIndex == index );
		} );
	}

	public disableAllButtons (): void {
		super.disableAllButtons();
		if ( this.toggleAllButton ) {
			this.toggleAllButton.selected = false;
		}
	}

	public resetSelection ( overrideIndex?: number ): void {
		if ( overrideIndex != undefined ) {
			this.currentIndex = overrideIndex;
		} else {
			this.currentIndex = this.defaultIndex ? this.defaultIndex : undefined;
		}

		this.buttons.forEach( ( button: ToggleButton, index: number ) => {
			let selectButton: boolean = this.currentIndex != undefined && overrideIndex == index || ( this.defaultIndex == index && overrideIndex == undefined );
			this.selectButton( button, selectButton );
		} );
	}
}

export interface IToggleMultiButtonConfig extends IMultiButtonConfig {
	/**
	 * An optional button that controls enable/disable status of all toggle buttons.
	 *
	 * @type {IButtonConfig}
	 * @memberof IToggleMultiButtonConfig
	 */
	toggleAllButton?: IButtonConfig | ITextButtonConfig;

	autoPickDefaultButton?: boolean;

	defaultIndexWhenOn?: number;
}



