import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { CompView } from 'corengf/src/mvc/CompView';
import { Actions } from 'corengf/src/utils/Actions';
import { GameController } from 'corengf/src/core/GameController';
import { MoneyFormatter } from 'corengf/src/utils/MoneyFormatter';
import { UIPanelController } from 'corengf/src/components/ui/components/UIPanelController';
import { SlotActions } from 'slotngf/src/utils/SlotActions';
import { SlotBankService } from 'slotngf/src/core/SlotBankService';
import { SlotFreeSpinsController } from 'slotngf/src/components/freeSpins/SlotFreeSpinsController';


export class SlotAutoPlayController extends UIPanelController {

	protected readonly infiniteSpins = -1;

	public onAutoSpinEventSignal: MiniSignal;

	@Inject
	protected gameController: GameController;

	@Inject
	protected bankService: SlotBankService;

	@Inject
	protected moneyFormatter: MoneyFormatter;

	protected autoPlay: boolean = false;
	protected freeSpinsMode: boolean = false;
	private _autoPlayCount: number = 0;

	protected get autoPlayCount (): number {
		return this._autoPlayCount;
	}

	protected set autoPlayCount ( value: number ) {
		this._autoPlayCount = value;
	}

	protected loseLimit: number = Infinity;
	protected winLimit: number = Infinity;
	protected singleWinLimit: number = Infinity;
	protected winDifference: number = 0;
	protected singleWinDifference: number = 0;

	public autoPlayStartSignal: MiniSignal;
	public autoPlayStopSignal: MiniSignal;

	constructor ( view?: CompView ) {
		super( view );
		this.onAutoSpinEventSignal = new MiniSignal();
		this.autoPlayStartSignal = new MiniSignal();
		this.autoPlayStopSignal = new MiniSignal();
	}

	// #region state function
	public init ( data: any ): void {
		this.autoPlay = ( data.spinType == SlotFreeSpinsController.TYPE_FREE_SPINS );
		this.freeSpinsMode = ( data.spinType == SlotFreeSpinsController.TYPE_FREE_SPINS );
		this.finishStateAction();
	}

	public autoSpinStartSpin (): void {
		this.winDifference -= this.bankService.totalBet;
		this.finishStateAction();
	}

	protected autoSpinSpinStop ( data: any ): void {
		if ( this.autoPlay && this.autoPlayCount != this.infiniteSpins ) {
			this.autoPlayCount--;
		}
		this.finishStateAction();
	}

	protected showRetrigger ( data: any ): void {
		this.autoPlayCount = data.totalFreeSpinCount - data.currentFreeSpinCount + 1;
		this.finishStateAction();
	}

	protected freeSpinsStop ( data: any ): void {
		this.autoPlayCount--;
		this.autoPlay = ( this.autoPlayCount > 0 || data.retriggered );
		this.finishStateAction();
	}

	protected transitionToBaseGame ( data: any ): void {
		this.autoPlay = false;
		this.freeSpinsMode = false;
		this.finishStateAction();
	}

	protected transitionToFreeSpins ( data: any ): void {
		this.autoPlayCount = data.spins;
		this.freeSpinsMode = true;
		this.finishStateAction();
	}

	protected transitionToBonusSpins ( data: any ): void {
		this.transitionToFreeSpins( data );
	}

	protected transitionBonusToBase ( stateData: any ): void {
		this.transitionToBaseGame( stateData );
	}

	protected transitionToAutoSpin ( data: any ): void {
		this.freeSpinsMode = false;
		this.autoPlayCount = data.spins === 'infinite' ? Number.MAX_SAFE_INTEGER : parseInt( data.spins );
		this.singleWinLimit = data.singleWinLimit ? this.getWinOrLoseLimitValue( data.singleWinLimit.value ) : undefined;
		this.winLimit = data.winLimit ? this.getWinOrLoseLimitValue( data.winLimit.value ) : undefined;
		this.loseLimit = this.getWinOrLoseLimitValue( data.lossLimit.value );
		this.winDifference = 0;
		this.singleWinDifference = 0;
		this.setAutoPlay( true );
		this.triggerNextTurn();
		this.finishStateAction();
	}

	protected freeSpinsIdle ( data: any ): void {
		this.checkAndStopAutoPlay();  //TODO : Remove this for spin to start autoplay
		this.triggerNextTurn();
		this.finishStateAction();
	}

	protected autoSpinShowWinning (): void {
		this.triggerNextTurn();
		this.finishStateAction();
	}

	protected freeSpinsShowWinning (): void {
		this.triggerNextTurn();
		this.finishStateAction();
	}

	protected autoSpinDashboardIdle ( data: any ): void {
		this.triggerNextTurn();
		this.finishStateAction();
	}

	protected allReelStopped ( data: any ): void {
		if ( data ) {
			this.singleWinDifference = data.totalWin;
			this.winDifference += data.totalWin;
		}
		this.checkAndStopAutoPlay( data.balance );
		this.finishStateAction();
	}

	// #endregion state function

	protected getWinOrLoseLimitValue ( value: number ): number {
		if ( value == undefined ) {
			return 0;
		} else if ( value == -1 || value == Infinity ) {
			return Infinity;
		} else {
			return value;
		}
	}

	protected onStopAutoSpins (): void {
		this.setAutoPlay( false );
	}

	protected checkAndStopAutoPlay ( balance?: number ): void {
		this.setAutoPlay( this.isAutoPlayCountAllowed() && this.isAutoPlayBalanceAllowed( balance ) && this.hitAutoLimit() );
	}

	protected isAutoPlayCountAllowed (): boolean {
		return this.autoPlayCount > 0;
	}

	protected isAutoPlayBalanceAllowed ( balance?: number ): boolean {
		if ( this.freeSpinsMode ) {
			return true;
		}
		balance = ( balance == undefined ) ? this.bankService.balance : balance;
		return balance >= this.bankService.totalBet;
	}


	/**
	 * returm false is hit auto spin win/lose limit
	 */
	protected hitAutoLimit (): boolean {
		if ( this.freeSpinsMode ) {
			return true;
		}

		return !( this.isHitAutoWinLimit() || this.isHitAutoLoseLimit() || this.isHitAutoSingleWinLimit() );
	}

	/**
	 * check is hit auto spin win limit.
	 * retutn true if hit win limit
	 */
	protected isHitAutoWinLimit (): boolean {
		if ( Number.isFinite( this.winLimit ) && this.winDifference >= this.winLimit ) {
			return true;
		}
		return false;
	}

	/**
	 * check is hit auto spin single win limit.
	 * retutn true if hit single win limit
	 */
	protected isHitAutoSingleWinLimit (): boolean {
		if ( !this.singleWinLimit ) {
			return false;
		}
		if ( Number.isFinite( this.singleWinLimit ) && this.singleWinDifference >= this.singleWinLimit ) {
			return true;
		}
		return false;
	}

	/**
	 * check is hit auto spin lose limit
	 * retutn true if hit lose limit
	 */
	protected isHitAutoLoseLimit (): boolean {
		if ( Number.isFinite( this.loseLimit ) && ( 0 >= this.winDifference && Math.abs( this.winDifference ) >= this.loseLimit ) ) {
			return true;
		}
		return false;
	}

	protected startPickFeature ( stateData: any ): void {
		this.onStopAutoSpins();
		this.finishStateAction();
	}

	protected setAutoPlay ( value: boolean ): void {
		if ( this.autoPlay != value ) {
			this.autoPlay = value;
		}

		if ( !this.autoPlay ) {
			this.stopAutoPlay();
		}
	}

	protected triggerNextTurn (): void {
		if ( this.autoPlay ) {
			this.onAutoSpinEventSignal.dispatch();
		}
	}

	protected stopAutoPlay (): void {
		this.gameController.handleButtonAction( Actions.CLICK_ACTION_PREFIX + SlotActions.STOP_AUTO_PLAY );  //TODO Change to free spin
		this.autoPlayCount = this.winDifference = 0;
		this.autoPlayStopSignal.dispatch();
	}

	protected actionsOnSetAutoPlay (): void {
		this.onAutoSpinEventSignal.dispatch();
	}
}


