import { Inject } from 'typescript-ioc/es5';
import { SlotNGModel } from 'slotngf/src/server/ng/model/SlotNGModel';
import { NGDataHookLogFormatter } from 'corengf/src/server/ng/formatter/websocket/out/NGDataHookLogFormatter';

/**
 * formatting data hook log data
 */
export class SlotNGDataHookLogFormatter extends NGDataHookLogFormatter {

	@Inject
	protected slotNGModel: SlotNGModel;

	protected getPartnerCode (): string {
		return this.slotNGModel.partnerCode;
	}
}



