import { Singleton, Inject } from 'typescript-ioc/es5';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { SlotEvent } from 'slotngf/src/core/SlotEvent';
import { SlotStates } from 'slotngf/src/states/SlotStates';

/**
 * RNG tool object
 * use below command to use it
 * rngTool.setRngSpinData([
				[
					[ "T", "PIC1", "A" ],
					[ "J", "PIC1", "J" ],
					[ "Q", "PIC1", "Q" ],
					[ "T", "PIC1", "K" ],
					[ "K", "PIC1", "T" ]
				],
				[
					[ "T", "PIC2", "A" ],
					[ "J", "PIC2", "J" ],
					[ "Q", "PIC2", "Q" ],
					[ "T", "PIC2", "K" ],
					[ "K", "PIC2", "T" ]
				]
			])
 */
@Singleton
export class SlotRngToolPortal {

	@Inject
	protected stateNames: SlotStates;

	public setupRngTool (): void {
		global[ 'rngTool' ] = this;
		window[ 'rngTool' ] = this;
	}

	public setRngSpinData ( data: any ): void {

		let rngToolSpinResultQueue: Array<Array<Array<string>>> = data;
		let eventData: any = [];

		rngToolSpinResultQueue.forEach( result => {
			eventData.push( { symbols: result } );
		} );

		GlobalDispatcher.dispatchIntoExpectedStates( SlotEvent.UPDATE_RNG_TOOL_SPIN_DATA,
			[ this.stateNames.baseGameIdle, this.stateNames.showWinning, this.stateNames.showWinningLoop ], eventData
		);
	}
}



