import { TsMap } from 'corengf/src/utils/TsMap';
import { Helper } from 'corengf/src/utils/Helper';
import { Orientation } from 'corengf/src/display/Viewport';
import { Keycode } from 'corengf/src/keyboard/KeyboardManager';
import { ICompViewConfig } from 'corengf/src/core/config/ICompViewConfig';
import { WebTextField } from 'corengf/src/elements/canvas/text/WebTextField';
import { ICoreElementConfig } from 'corengf/src/elements/canvas/CoreElement';
import { SlotActions } from 'slotngf/src/utils/SlotActions';
import { MLUIConfig } from 'mlui/src/config/IMLUIConfig';
import { MLUIButtonId } from 'mlui/src/buttons/MLUIButtonId';
import { MLUISoundConfig } from 'mlui/src/config/MLUISoundConfig';
import { ISpinButtonConfig } from 'mlui/src/elements/button/SpinButton';
import { SpinButtonAutoSpinStyle } from 'mlui/src/elements/styles/MLUIFontStyles';

export class MLUISpinConfig extends MLUIConfig implements IMLUISpinConfig {

	public name = 'spinPanel';

	protected getSpinButtonConfig (): ISpinButtonConfig {
		return {
			isOrientationSupported: true,
			name: 'spin_button',
			position: {
				x: 360,
				y: 1114
			},
			soundKey: MLUISoundConfig.spin,
			keyCode: Keycode.space,
			assetName: 'spin',
			action: SlotActions.SPIN,
			id: MLUIButtonId.BUTTON_START_SPIN,
			productLine: this.productLine,
			isLocal: true,
			spinIconAsset: '_spin.png',
			disabledIconAsset: '_icon_Disabled.png',
			stopIconAsset: '_stop.png',
			autoIconAsset: '_bar.png',
			normalGlowAsset: Helper.EMPTY,
			spinGlowAsset: '_glow_gradient.png',
			autoSpinCounterLabel: {
				elementType: WebTextField.elementType,
				elementConfig: {
					name: 'autoSpinCounterLabel',
					key: '',
					position: {
						x: 0,
						y: -25
					},
					anchor: {
						x: 0.5,
						y: 0.5
					},
					style: SpinButtonAutoSpinStyle
				}
			},
			autoSpinTotalLabel: {
				elementType: WebTextField.elementType,
				elementConfig: {
					name: 'autoSpinTotalLabel',
					key: '',
					position: {
						x: 0,
						y: 25
					},
					anchor: {
						x: 0.5,
						y: 0.5
					},
					style: SpinButtonAutoSpinStyle
				}
			}
		};
	}

	public spinButton: ISpinButtonConfig = this.getSpinButtonConfig();

	public coreElementOrientationConfig: TsMap<Orientation, TsMap<string, ICoreElementConfig>> = new TsMap( [
		[
			Orientation.PORTRAIT, new TsMap( [
				[
					this.spinButton.name, {
						name: this.spinButton.name,
						position: this.spinButton.position,
						circleHitArea: {
							radius: 90
						}
					}
				],
			] )
		],
		[
			Orientation.LANDSCAPE, new TsMap( [
				[
					this.spinButton.name, {
						name: this.spinButton.name,
						position: { x: 1156, y: 426 },
						circleHitArea: {
							radius: 90
						}
					}
				],
			] )
		]
	] );
}

export interface IMLUISpinConfig extends ICompViewConfig {
	spinButton: ISpinButtonConfig;
}



