import { Inject } from 'typescript-ioc/es5';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { UIPanelController } from 'corengf/src/components/ui/components/UIPanelController';
import { SlotEvent } from 'slotngf/src/core/SlotEvent';
import { SlotGameType } from 'slotngf/src/core/SlotGameType';
import { IInitData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { MLUIBetPanel } from 'mlui/src/components/bet/MLUIBetPanel';
import { IMLUIBetConfig, MLUIBetConfig } from 'mlui/src/components/bet/MLUIBetConfig';


export class MLUIBetController extends UIPanelController {
	protected view: MLUIBetPanel;

	private betIndex: number;
	private gameType: SlotGameType;

	constructor ( @Inject view: MLUIBetPanel ) {
		super( view );
	}

	public init ( data: IInitData ): void {

		this.gameType = data.gametype;

		if ( !SlotGameType[ data.gametype ] ) {
			console.error( 'MLUIBetController, unknow game type : ' + data.gametype );
		}

		this.view.setGameType( this.gameType );

		let config = this.gameConfig.getConfig<IMLUIBetConfig>( MLUIBetConfig );

		this.view.initialize( config );
		this.view.onBetChangeSignal.add( this.onBetChange.bind( this ) );
		this.setLocalButtonEvents( config );
		this.finishStateAction();
	}

	/**
	 * setup button callback for local button events
	 * @param config
	 */
	protected setLocalButtonEvents ( config: IMLUIBetConfig ): void {
		this.setButtonActionEventListeners( config.confirmButton );
		this.setButtonActionEventListeners( config.closeButton );
	}

	protected action_hideBetOptions (): void {
		this.view.onClose();
		this.onHideBetPanel();
	}

	protected action_confirmBetOptions (): void {
		this.view.confirmBet();
		this.onHideBetPanel();
	}

	protected onHideBetPanel (): void {
		GlobalDispatcher.dispatchIntoAllStates( SlotEvent.HIDE_FULL_SCREEN_UI );
	}

	protected onBetChange ( betIndex: number ) {
		this.betIndex = betIndex;
	}

	public showPopup (): void {
		this.view.showPopup();
		GlobalDispatcher.dispatchIntoAllStates( SlotEvent.SHOW_FULL_SCREEN_UI );
	}
}


