import { Container, Inject } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Helper } from 'corengf/src/utils/Helper';
import { CoreState } from 'corengf/src/states/CoreState';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import { IGameElement } from 'corengf/src/core/config/IMainConfig';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { Spin } from 'slotngf/src/components/reelV2/action/Spin';
import { ReelModel } from 'slotngf/src/components/reelV2/ReelModel';
import { ReelConfig, ReelSet } from 'slotngf/src/components/reelV2/ReelConfig';
import { SymbolState } from 'slotngf/src/components/reels/ReelsEnums';
import { Spinning } from 'slotngf/src/components/reelV2/action/Spinning';
import { SpinDown } from 'slotngf/src/components/reelV2/action/SpinDown';
import { ReelStripModel } from 'slotngf/src/components/reelV2/ReelStripModel';
import { WinLine, WinLineDisplayData } from 'slotngf/src/components/lines/WinLine';
import { SlotCompController } from 'slotngf/src/components/mvc/SlotCompController';
import { ReelActionHandler, IReelActionHandlerConfig } from 'slotngf/src/components/reelV2/ReelActionHandler';
import { ReelControllerView } from 'slotngf/src/components/reelV2/ReelControllerView';
import { IReelSet, IReelStripConfig, SpinDirection } from 'slotngf/src/core/config/IReelsConfig';
import { IReplacingSymbolsDetail, IFreeRespinReelsDetail, IWildLocked } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { IInitData, ISpinStopData, IFreeSpinsStopData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { SlotEvent } from 'slotngf/src/core/SlotEvent';

export class ReelController extends SlotCompController {
	public static readonly SHOW_WIN_LINE_NOTIFICATION: string = 'reelsShowWinLineNotification';
	public static readonly ANTICIPATE_REEL_NOTIFICATION: string = 'anticipateReel';
	public static readonly ANTICIPATE_END_NOTIFICATION: string = 'anticipateEnd';

	protected view: ReelControllerView;

	protected reelActionHandler: ReelActionHandler;

	protected componentConfig: ReelConfig;

	/**
	 * keeps all the reel strips final config
	 *
	 * @protected
	 * @type {IReelSet}
	 * @memberof ReelController
	 */
	protected reelSet: IReelSet;

	protected reels: Array<ReelStripModel>;

	protected reelModel: ReelModel;

	protected reelBackgroundConfig: IGameElement;

	private isFeatureReelsInited: boolean = false;

	private initData: IInitData = null;

	/**
	 * Delay timer for normal when spin is over
	 */
	protected normalTimer: SimpleTimer;

	protected spinTimeoutTimer: SimpleTimer;

	protected zoom: boolean = false;

	/**
	 * Array with reels id that are active.
	 * Active means that we can spin it and od some actions.
	 * It will be used for respin feature. When you can change the active strips id to just one reel.
	 */
	protected _activeReelsID: Array<number>;

	private _spinDirection: SpinDirection;

	protected set spinDirection ( spinDirection: SpinDirection ) {
		this._spinDirection = spinDirection;
	}

	protected get spinDirection (): SpinDirection {
		return this._spinDirection;
	}

	protected get activeReelsID (): Array<number> {
		if ( !this._activeReelsID ) {
			this._activeReelsID = new Array<number>();
			this.currentReelSet.forEach( ( stripConfig: IReelStripConfig, index: number ) => {
				this._activeReelsID.push( index );
			} );
		}
		return this._activeReelsID;
	}

	protected set activeReelsID ( value: Array<number> ) {
		this._activeReelsID = value;
	}

	protected get currentReelSet (): Array<IReelStripConfig> {
		return this.reelSet[ this.reelModel.currentReelSetIndex ];
	}

	/**
	 * Delay for playing idle animation loop
	 */
	protected idleTimer: SimpleTimer;

	// TODO: Identify difference between idleTimer and idleRandomTimer
	protected idleRandomTimer: SimpleTimer;

	protected _stopReelDelay: TsMap<number, Array<number>>;

	protected get stopReelDelay (): TsMap<number, Array<number>> {

		if ( this._stopReelDelay ) {
			return this._stopReelDelay;
		}

		this._stopReelDelay = new TsMap( [
			[ ReelSet.BASE, new Array<number>() ], [ ReelSet.FEATURE, new Array<number>() ]
		] );

		for ( const reelSetIndex in this.componentConfig.reelSet ) {
			if ( ( this.componentConfig.reelSet as Object ).hasOwnProperty( reelSetIndex ) ) {
				this.componentConfig.reelSet[ reelSetIndex ].forEach( ( reelStrip ) => {
					this._stopReelDelay.get( +reelSetIndex ).push( reelStrip.stopReelNormalDelay );
				} );
			}
		}

		return this._stopReelDelay;
	}

	constructor ( @Inject view: ReelControllerView ) {
		super( view );
	}

	public addEventListeners (): void {
		GlobalDispatcher.addListener( this, ReelController.SHOW_WIN_LINE_NOTIFICATION, this.onShowWinLine );
	}

	protected onShowWinLine ( e: GlobalWinData ): void {
		let winLine: WinLineDisplayData = e.data as WinLineDisplayData;
		this.view.setSymbolStateFromPositions( winLine.winTable, SymbolState.STATIC, SymbolState.LOSE );
		this.view.showWinLine( winLine );
	}

	public init ( data: IInitData ): void {
		this.initReelsConfig();
		this.initData = data;
		this.reelModel = Container.get( ReelModel );

		this.reelModel.initialize( {
			countTopExtraSymbol: this.componentConfig.reelDefinition.countTopExtraSymbol,
			countBottomExtraSymbol: this.componentConfig.reelDefinition.countBottomExtraSymbol,
			symbolDefinition: this.componentConfig.symbolDefinition
		} );

		this.initViewController();

		this.initReelStripViewAndModel( data.strips, data.idleSymbols );

		this.initReelActionHandler();

		this.finishStateAction();
	}

	protected initViewController (): void {
		this.view.initialize( this.componentConfig );
		this.view.bindModel( this.reelModel );
	}

	protected initReelsConfig (): void {
		this.componentConfig = this.gameConfig.getConfig<ReelConfig>( ReelConfig );
		this.reelSet = this.componentConfig.reelSet;
		this.spinDirection = this.componentConfig.defaultSpinDirection;

		this.componentConfig.avaliableReelSet.forEach( ( reelSetIndex ) => {
			const reelSet: Array<IReelStripConfig> = this.reelSet[ reelSetIndex ];
			const reels = [];
			reelSet.forEach( ( reel, index ) => {
				const base = Helper.mergeTwoObjects( {}, this.componentConfig.reelDefinition );
				const reelStripConfig: IReelStripConfig = Helper.mergeTwoObjects( base, reel );
				reelStripConfig.id = index;
				reels.push( reelStripConfig );
			} );
			this.reelSet[ reelSetIndex ] = reels;
		} );

		this.reelBackgroundConfig = this.componentConfig.reelBackground;
	}

	protected initReelStripViewAndModel ( strips: Array<Array<string>>, idleSymbols: Array<Array<string>> ): void {
		this.reels = new Array<ReelStripModel>();
		let stripIndex = 0;

		const currentSymbolsOutcome: Array<Array<string>> = new Array<Array<string>>();

		this.currentReelSet.forEach( ( stripConfig, index ) => {
			const reelStripModel: ReelStripModel = Container.get( ReelStripModel );
			reelStripModel.setConfig( stripConfig, this.componentConfig.symbolDefinition );

			if ( strips == undefined || strips.length == 0 ) {
				reelStripModel.allStripSymbols = undefined;
				stripIndex = index % this.activeReelsID.length;
			} else {
				stripIndex = index % strips.length;
				reelStripModel.allStripSymbols = strips[ stripIndex ];
			}

			// if last spin didn't exist
			if ( idleSymbols == undefined || idleSymbols.length == 0 ) {
				reelStripModel.symbolsOutcome = this.getIdleSymbolFromReelStrip( reelStripModel.allStripSymbols );
			} else {
				reelStripModel.symbolsOutcome = idleSymbols[ stripIndex ];
			}

			currentSymbolsOutcome.push( reelStripModel.symbolsOutcome );

			reelStripModel.initReel( reelStripModel.symbolsOutcome );
			this.view.reelInitView( index, reelStripModel );

			this.reels.push( reelStripModel );
		} );

		if ( !this.reelModel ) {
			this.reelModel = new ReelModel();
		}
		this.reelModel.currentSymbolsOutcome = currentSymbolsOutcome.slice( 0 );
		this.reelModel.setReelStripModel( this.reelModel.currentReelSetIndex, this.reels );

		this.view.updateVisibleByActiveReelSet();
	}

	protected initReelActionHandler (): void {
		this.reelActionHandler = Container.get( ReelActionHandler );
		this.reelActionHandler.initialize( this.reelModel, this.view, this.getReelActionHandlerConfig() );
	}

	protected getReelActionHandlerConfig (): IReelActionHandlerConfig {
		return {
			startSpin: {
				model: Container.get( Spin ),
				config: {
					symbolDefinition: this.componentConfig.symbolDefinition,
					duration: this.componentConfig.reelDefinition.reelTween.duration
				}
			},
			spinning: { model: Container.get( Spinning ) },
			allReelStopped: null,
			spinStop: {
				model: Container.get( SpinDown ),
				config: {
					anticipationData: this.componentConfig.anticipationData,
					dropSymbols: this.componentConfig.reelDefinition.dropSymbols,
					symbolDefinition: this.componentConfig.symbolDefinition,
					stopReelNormalDelay: this.stopReelDelay.get( this.reelModel.currentReelSetIndex )
				}
			},
			featureAction: null,
			customActionMap: null
		};
	}


	protected setReelSet ( set: number ): void {
		this.reelModel.currentReelSetIndex = set;
		this.view.updateVisibleByActiveReelSet();
	}

	protected updateSymbolsOutcome (): void {
		//Override it if you want to modify the symbol outcome like Repacing or wild lock.
	}

	/**
	 * Get idleSymbols from reel strips
	 * @param strips
	 */
	protected getIdleSymbolFromReelStrip ( strip: Array<string> ): Array<string> {
		let idleSymbols = new Array<string>();
		const sample = this.currentReelSet[ 0 ];
		for ( let i: number = 0; i < sample.symbolsOnReel; i++ ) {
			idleSymbols.push( strip[ i + 1 ] );
		}

		return idleSymbols;
	}

	protected idleAnimationsComplete (): void {
		if ( this.componentConfig.idleAnimationLoop ) {
			this.idleTimer = new SimpleTimer( this.componentConfig.idleAnimationDelay, this.playIdleAnimations.bind( this ) );
		}
	}

	protected playIdleAnimations (): void {
		if ( this.componentConfig.idleAnimationLoop ) {
			this.view.playIdleAnimationsLoop();
		} else {
			this.idleRandomTimer = new SimpleTimer( this.componentConfig.idleAnimationRandomChance.time, this.view.playRandomIdleAnimations.bind( this.view ), true );
		}
	}

	protected executeFeatureAction ( data: any ): void {
		this.reelActionHandler.featureAction( data ).then( () => {
			this.finishStateAction();
		} );
	}

	//#region STATE_FUNCTIONS

	public baseGameIdle (): void {
		this.view.deleteAllAnimations();
		this.view.setSymbolsStateForAll( SymbolState.STATIC );
		this.idleTimer = new SimpleTimer( this.componentConfig.idleAnimationDelay, this.playIdleAnimations.bind( this ) );
		this.finishStateAction();
	}

	public startSpin (): void {
		this.resetReels();
		this.reelModel.reset();
		this.reelActionHandler.startSpin( {
			reels: this.activeReelsID,
			spinDirection: this.spinDirection
		} ).then( () => {
			this.finishStateAction();
		} );
	}

	public respin ( index: number ): void {
		this.resetReels();
		this.reelActionHandler.startSpin( {
			reels: [ index ],
			spinDirection: this.spinDirection
		} );
		this.finishStateAction();
	}

	public freeRespinReels ( data: IFreeRespinReelsDetail ): void {
		//TODO
		this.reelActionHandler.featureAction( data ).then( () => {
			GlobalDispatcher.dispatchIntoExpectedState( SlotEvent.RESPIN_REELS, this.stateNames.freeRespinReels );
			this.finishStateAction();
		} );
	}

	public wildLockTriggered ( data: IWildLocked ): void {
		//TODO
		this.executeFeatureAction( data );
	}

	public spinning (): void {
		this.normalTimer = new SimpleTimer( this.componentConfig.reelStopNormalTime, this.onSpinningComepleted.bind( this ) );
	}

	public spinStop ( stateData: ISpinStopData ): void {
		if ( stateData && stateData.reelIndexes ) {
			this.reelModel.isLegalResponse = true;
			this.reelModel.currentSymbolsOutcome = stateData.reelIndexes.slice( 0 );
			this.reelModel.setSymbolOutcome( stateData.reelIndexes );
			this.reelModel.setScatterDropped( stateData.reelIndexes );
		} else {
			this.reelModel.isLegalResponse = false;
		}

		this.reelActionHandler.spinStop( {
			reels: this.activeReelsID,
			spinDirection: this.spinDirection,
			...stateData
		} ).then( () => {
			this.finishStateAction();
		} );
	}

	public pickFeature (): void {
		this.view.visible = false;
		this.finishStateAction();
	}

	public transitionToFreeSpins (): void {
		this.setReelSet( ReelSet.FEATURE );
		this.reelModel.lastSymbolsOutcome = this.reelModel.currentSymbolsOutcome.slice( 0 );

		if ( !this.isFeatureReelsInited ) {
			this.initReelStripViewAndModel( this.initData.strips, this.initData.idleSymbols );
			this.isFeatureReelsInited = true;
		}
		this.setSymbolsToTransition( this.reelModel.lastSymbolsOutcome );
		this.view.positionUpdateOnTransition();
		this.view.visible = true;
		this.finishStateAction();
	}

	public freeSpinsIntroPopup (): void {
		this.view.setSymbolsStateForAll( SymbolState.STATIC );
		this.finishStateAction();
	}

	public freeSpinsOutroPopup (): void {
		this.view.cleanAllOverlays();
		this.finishStateAction();
	}

	public freeSpinsStart (): void {
		this.startSpin();
	}

	public freeSpinning (): void {
		this.spinning();
	}

	public freeSpinsStop ( stateData: IFreeSpinsStopData ): void {
		this.spinStop( stateData );
	}

	public freeRespinsStop ( stateData: any ): void {
		this.freeSpinsStop( stateData );
	}

	public replacingSymbols ( data: IReplacingSymbolsDetail ): void {
		//TODO
		this.executeFeatureAction( data );
	}

	public transitionToBaseGame (): void {
		this.setReelSet( ReelSet.BASE );

		this.setSymbolsToTransition( this.reelModel.currentSymbolsOutcome );
		this.view.positionUpdateOnTransition();
		this.view.visible = true;
		this.finishStateAction();
	}

	protected setSymbolsToTransition ( symbolsOutcome: Array<Array<string>> ): void {
		symbolsOutcome.forEach( ( reel, reelIndex ) => {
			reel.forEach( ( symbol, symbolIndex ) => {
				this.view.setSymbolNameWithState( {
					reelIndex,
					symbolIndex
				}, symbol, SymbolState.STATIC );
			} );
		} );
	}

	/**
	 * player clicked spin for skip spinning animation
	 */
	public skip_spinning (): void {
		this.normalTimer.stop();
		this.reelModel.isFastStopPending = true;
		this.finishStateAction();
	}

	/**
	 * player clicked spin for skip stop animations
	 */
	public skip_spinStop (): void {
		this.reelModel.isFastStopPending = true;
	}

	public skip_freeSpinning (): void {
		this.skip_spinning();
	}

	public skip_freeSpinsStop (): void {
		this.skip_spinStop();
	}

	//#endregion STATE_FUNCTIONS


	/**
	 * Triggered when minimum spinning duration elapsed.
	 *
	 * @protected
	 * @memberof ReelController
	 */
	protected onSpinningComepleted (): void {
		this.reelActionHandler.spinning( null );
		this.finishStateAction();
	}

	/**
	 * spin timeout handler
	 */
	protected onSpinTimeout (): void {
		if ( this.currentStateName !== this.stateNames.spinStop ) {
			console.error( 'server spin response timeout' );
		}
	}

	/**
	 * spin timeout handler
	 */
	protected onFreeSpinTimeout (): void {
		if ( this.currentStateName !== this.stateNames.freeSpinsStop ) {
			console.error( 'server free spin response timeout' );
		}
	}

	protected resetReels (): void {
		this.view.deleteAllAnimations();
		this.view.setSymbolsStateForAll( SymbolState.STATIC );
		this.view.cleanOverlays( [ this.view.baseOverlayIndex ] );
		this.view.resetOverlay( this.view.baseOverlayIndex );

		if ( this.idleTimer != null ) {
			this.idleTimer.stop();
		}
		if ( this.idleRandomTimer != null ) {
			this.idleRandomTimer.stop();
		}
	}
	public setState ( state: CoreState ): boolean {
		if ( this.reelActionHandler ) {
			this.reelActionHandler.setState( state );
		}

		return super.setState( state );
	}
}

interface GlobalWinData {
	type: string;
	data: WinLine;
}


