import { Singleton } from 'typescript-ioc/es5';
import { Helper } from 'corengf/src/utils/Helper';
import { UrlParameters } from 'corengf/src/utils/UrlParameters';
import { WebSocketTransport } from 'corengf/src/server/abstract/transport/WebSocketTransport';
import { INGWebSocketConfig } from 'corengf/src/server/ng/config/INGServerConfig';
import { IServerConfig } from 'corengf/src/server/abstract/config/IServerConfig';

/**
 * Handle m4 web socket transport.
 */
@Singleton
export class NGWebSocketTransport extends WebSocketTransport {

	protected readonly DOMAIN: string = '{domain}';
	protected readonly GAME_CODE: string = '{game_code}';
	protected readonly PARTNER_TOKEN: string = '{partner}';
	protected readonly SESSION_TOKEN: string = '{session}';
	protected readonly DEVICE: string = '{device}';
	protected readonly PORT: string = '{port}';
	protected readonly GS: string = '{gs}';

	protected config: INGWebSocketConfig;

	public setConfig ( config: IServerConfig ): void {
		this.config = config as INGWebSocketConfig;
	}

	/**
	 * Get web socket connection url
	 */
	protected getConnectionURL (): string {
		let returnURL: string = this.config.notificationPath;
		returnURL = returnURL.replace( this.DOMAIN, this.getDynamicDomainName() );

		let partnerToken: string = UrlParameters.partnerToken;
		if ( !UrlParameters.isRealMode ) {
			if ( UrlParameters.isDemoMode ) {
				partnerToken = this.config.demoPartnerToken;
			} else {
				partnerToken = this.config.playPartnerToken;
			}
		}

		returnURL = returnURL.replace( this.PARTNER_TOKEN, partnerToken );
		returnURL = returnURL.replace( this.SESSION_TOKEN, UrlParameters.sessionToken );
		returnURL = returnURL.replace( this.GAME_CODE, this.config.gameCode );
		returnURL = returnURL.replace( this.DEVICE, Helper.getDeviceInfo() );
		returnURL = returnURL.replace( this.PORT, this.config.connectionPort );
		returnURL = returnURL.replace( this.GS, this.getDynamicGameServerName() );
		returnURL = this.config.protocol + returnURL;

		return returnURL;
	}

	public getDynamicGameServerName (): string {
		let gsName: string = UrlParameters.gs;
		if ( gsName == undefined ) {
			gsName = this.config.rgsName;
		}
		return gsName;
	}
}



