import { Point } from 'pixi.js';
import { Inject } from 'typescript-ioc/es5';
import { IMLUISettingsConfig } from './MLUISettingsConfig';
import { MLUIFullScreenPanel } from './../../core/MLUIFullScreenPanel';
import { SoundManager, AudioTrackName } from 'corengf/src/audio/SoundManager';
import { Button, IButtonConfig } from 'corengf/src/elements/canvas/button/Button';
import { TextToggleButton } from 'corengf/src/elements/canvas/toggle/ToggleTextButton';
import { WebTextField, IWebTextFieldConfig } from 'corengf/src/elements/canvas/text/WebTextField';


export class MLUISettingsPanel extends MLUIFullScreenPanel {

	@Inject
	protected soundManager: SoundManager;

	protected closeButton: Button;
	protected title: WebTextField;
	protected config: IMLUISettingsConfig;

	protected soundToggle: TextToggleButton;
	protected musicToggle: TextToggleButton;

	protected createElements ( config: IMLUISettingsConfig ) {
		this.config = config;
		super.createElements();
		this.addTitle( config.title );
		this.addSoundTextToggle( config.soundToggleData );
		this.addMusicTextToggle( config.musicToggleData );
		this.addCloseButton( config.closeButton );

		this.hidePopup();
	}

	protected addTitle ( data: IWebTextFieldConfig ): void {
		this.title = new WebTextField( data );
		this.addChild( this.title );
	}

	protected addSoundTextToggle ( data: any ): void {
		this.soundToggle = new TextToggleButton( data );
		this.soundToggle.elementSignal.add( this.onSoundToggle, this );
		this.addChild( this.soundToggle );
	}

	protected addMusicTextToggle ( data: any ): void {
		this.musicToggle = new TextToggleButton( data );
		this.musicToggle.elementSignal.add( this.onMusicToggle, this );
		this.addChild( this.musicToggle );
	}

	protected addCloseButton ( data: IButtonConfig ): void {
		this.closeButton = new Button( data );
		this.setInitialActionData();
		this.closeButton.elementSignal.add( this.hidePopup.bind( this ) );
		this.closeButton.anchor = new Point( 0.5, 0.5 );
		this.addChild( this.closeButton );
	}

	protected onSoundToggle ( data: any ): void {
		const clickData = this.closeButton.clickData;
		clickData.data.sound = data.selected;
		this.closeButton.clickData = clickData;

		data.selected ? this.soundManager.unmuteTrack( AudioTrackName.EFFECT ) : this.soundManager.muteTrack( AudioTrackName.EFFECT );
	}

	protected onMusicToggle ( data: any ): void {
		const clickData = this.closeButton.clickData;
		clickData.data.music = data.selected;
		this.closeButton.clickData = clickData;

		data.selected ? this.soundManager.unmuteTrack( AudioTrackName.MUSIC ) : this.soundManager.muteTrack( AudioTrackName.MUSIC );
	}

	protected setInitialActionData (): void {
		this.closeButton.clickData = {
			data: {
				sound: true,
				music: true
			}
		};
	}
}


