import { Inject } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { GameConfig } from 'corengf/src/core/GameConfig';
import { AbstractSdkController } from 'corengf/src/server/abstract/sdk/AbstractSdkController';

/**
 * SDK support list for intergate with third party sdk
 */
export abstract class AbstractSdkSupportList {

	@Inject
	protected gameConfig: GameConfig;

	/**
	 * Map of incoming formats with message type as a key
	 */
	private _sdkSupportList: TsMap<string, AbstractSdkController>;

	public get SDKSupportList (): TsMap<string, AbstractSdkController> {
		if ( this._sdkSupportList == undefined ) {
			this._sdkSupportList = this.setupSDKSupportList();
		}
		return this._sdkSupportList;
	}

	/**
	 * Formatters that receive messages from server
	 */
	protected setupSDKSupportList (): TsMap<string, AbstractSdkController> {
		let map = new TsMap<string, AbstractSdkController>();
		return map;
	}
}

export class SDK {
	//
}



