import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import { AudioTrackName } from 'corengf/src/audio/SoundManager';
import { GlobalDispatcher, IListener } from 'corengf/src/events/GlobalDispatcher';
import { SymbolState } from 'slotngf/src/components/reels/ReelsEnums';
import { AnticipationData, SpinDirection } from 'slotngf/src/core/config/IReelsConfig';
import { SlotSoundConfig } from 'slotngf/src/core/config/SlotSoundConfig';
import { SymbolSet } from 'slotngf/src/components/reels/logic/ReelsConfig';
import { SpinStopData } from 'slotngf/src/components/reelV2/ReelActionHandler';
import { SpinBase, TweenSymbolsDownConfig, ISpinBaseConfig } from 'slotngf/src/components/reelV2/action/SpinBase';

export class SpinDown extends SpinBase implements IListener {

	public key: string = 'SPIN_DOWN';

	public static readonly ANTICIPATE_REEL_NOTIFICATION: string = 'anticipateReel';
	public static readonly ANTICIPATE_END_NOTIFICATION: string = 'anticipateEnd';

	protected config: ISpinDownConfig;

	protected spinDirection: SpinDirection;

	get activeReels (): Array<number> {
		return this._activeReels;
	}

	protected pendingForStopReels: Array<number>;

	protected stoppedReelCount: number = 0;
	protected currentSpinningReelsCounter: number = 0;
	protected stopRequestCounter: number;

	protected currentActiveReelIndex: number = 0;

	protected isAnticipating: boolean = false;

	protected isRunning: boolean = false;

	protected nextReelStopTimer: SimpleTimer;

	protected setup ( config?: ISpinDownConfig ): void {
		this.config = config;

		this.reelModel.onTweenSymbolComplete.add( this.onSpinTweenComplete.bind( this ) );
		this.reelModel.onFastStopReels.add( this.onFastStopReels.bind( this ) );
	}

	public addEventListeners (): void {
		//
	}

	public run ( data: SpinStopData ): Promise<any> {
		this.stoppedReelCount = 0;
		this.isRunning = true;
		this.currentSpinningReelsCounter = data.reels.length;
		this.stopRequestCounter = data.reels.length;
		this.currentActiveReelIndex = 0;
		this.spinDirection = data.spinDirection;

		return new Promise( ( resolve, reject ) => {
			this.resolveForStop = resolve;
			this.pendingForStopReels = data.reels;
			if ( this.reelModel.isFastStopPending ) {
				this.fastStopReels();
			} else {
				this.reelModel.currentReelStrips[ data.reels[ 0 ] ].stopRequested = true;
			}
		} );
	}

	protected fastStopReels (): void {
		this.pendingForStopReels.forEach( ( reelId ) => {
			this.reelModel.currentReelStrips[ reelId ].stopRequested = true;
			this.currentActiveReelIndex++;
		} );
	}

	protected getReelStopDelay ( reelId: number ): number {
		const anticipationDelay: number = this.isAnticipationReel( reelId, true ) ? this.config.anticipationData.anticipationDelay : 0;
		const stopReelNormalDelay: number = this.config.stopReelNormalDelay[ reelId ];
		return stopReelNormalDelay + anticipationDelay;
	}

	/**
	 * recurvise triggered when every time tween down completed.
	 *
	 * @protected
	 * @param {number} reelId - current spining reelId
	 * @memberof SpinDown
	 */
	protected spinDown ( reelId: number ): void {
		const stripModel = this.reelModel.currentReelStrips[ reelId ];
		const counter = stripModel.outcomeSymbolsCounter;
		const index: number = ( this.spinDirection == SpinDirection.SpinDown ) ? ( stripModel.symbolsOutcome.length - 1 ) - counter : counter;


		if ( stripModel.currentTweenCount < this.reelModel.countTopExtraSymbol + stripModel.spinDownTweenCount ) {

			// trigger next reel to stop
			if ( stripModel.currentTweenCount == 0 ) {
				if ( this.currentActiveReelIndex < this.pendingForStopReels.length - 1 ) {
					const nextReelDelay = this.getReelStopDelay( this.getNextReelId( reelId ) );
					this.currentActiveReelIndex++;
					this.nextReelStopTimer = new SimpleTimer( nextReelDelay, this.stopNext.bind( this, reelId ) );
				}
			}

			if ( stripModel.outcomeSymbolsCounter <= stripModel.symbolsOutcome.length && this.reelModel.shouldMoveLastSymbolToTop( reelId, this.spinDirection ) ) {
				const symbolName: string = index > -1 ? stripModel.symbolsOutcome[ index ] : stripModel.nextSymbol();
				const symbolData = this.reelModel.createNextSymbol( reelId, this.spinDirection, symbolName );
				this.view.moveLastSymbolToTop( reelId, symbolData, SymbolState.STATIC, this.spinDirection );
				stripModel.outcomeSymbolsCounter++;
			}

			const isCompleted = ( stripModel.currentTweenCount == stripModel.spinDownTweenCount );

			const tweenDownConfig: TweenSymbolsDownConfig = {
				duration: ( isCompleted ) ? stripModel.config.stopTween.duration : stripModel.config.reelTween.duration,
				distanceY: stripModel.symbolConfig.generalSymbol.height,
				easing: ( isCompleted ) ? stripModel.config.stopTween.ease : stripModel.config.reelTween.ease,
				onComplete: this.tweenComplete.bind( this, reelId )
			};

			this.tweenSymbolsDown( [ reelId ], tweenDownConfig, this.spinDirection );

			stripModel.currentTweenCount++;
		} else {
			this.currentReelStopped( reelId );
		}
	}

	protected tweenComplete ( reelId: number ): void {
		this.spinDown( reelId );
	}

	protected stopNext ( reelId: number ): void {

		const nextReelId: number = this.getNextReelId( reelId );

		if ( this.reelModel.currentReelStrips[ nextReelId ] ) {
			this.reelModel.currentReelStrips[ nextReelId ].stopRequested = true;
			this.stopRequestCounter--;
		}
	}

	/**
	 * Hendler for spinning tween state completed.
	 *
	 * @protected
	 * @param {number} reelId
	 * @memberof SpinDown
	 */
	protected onSpinTweenComplete ( reelId: number ): void {
		this.reelModel.currentReelStrips[ reelId ].setTweenCount();
		this.reelModel.currentReelStrips[ reelId ].currentTweenCount = 0;
		this.reelModel.currentReelStrips[ reelId ].outcomeSymbolsCounter = 0;

		this.spinDown( reelId );
	}

	protected onFastStopReels (): void {
		if ( this.isRunning ) {
			if ( this.nextReelStopTimer ) {
				this.nextReelStopTimer.stop();
			}
			this.fastStopReels();
		}
	}

	protected currentReelStopped ( reelId: number ): void {
		this.stoppedReelCount++;
		this.reelStoppedAction( reelId );

		if ( this.stoppedReelCount === this.pendingForStopReels.length ) {
			this.reelModel.onAllReelStopped.dispatch();
			this.resolveForStop();
			this.resolveForStop = null;
			this.isRunning = false;
		}
	}

	protected reelStoppedAction ( reelId: number ): void {
		this.currentSpinningReelsCounter--;
		if ( !this.reelModel.isLegalResponse ) {
			return;
		}
		this.view.anticipateScatters( reelId, this.reelModel.scattersDropped );
		this.reelStoppedEffect( reelId );

		if ( this.currentSpinningReelsCounter == 0 ) {
			this.stopAnticipate( reelId );
		} else {
			const nextReelId: number = this.getNextReelId( reelId );
			if ( !this.reelModel.isFastStopPending && this.isAnticipationReel( nextReelId, false ) ) {
				this.triggerAnticipateReel( reelId, nextReelId );
				this.anticipationReelStoppedEffect( reelId );
			} else {
				this.stopAnticipate( reelId );
			}
			if ( this.currentSpinningReelsCounter == 1 ) {
				if ( !this.reelModel.isFastStopPending ) {
					//this.fastStopTimeout = true;
				}
			}
		}
	}

	protected getNextReelId ( reelId: number ): number {
		return reelId + 1;
	}

	protected triggerAnticipateReel ( currentReelId: number, nextReelId: number ): void {
		GlobalDispatcher.dispatchIntoExpectedStates( SpinDown.ANTICIPATE_REEL_NOTIFICATION,
			[
				'spinStop',  // this.stateNames.spinStop,
				'freeSpinsStop', // this.stateNames.freeSpinsStop
			],
			currentReelId,
		);
		this.view.anticipateReel( nextReelId );
		this.isAnticipating = true;
	}

	protected isAnticipationReel ( reelId: number, preStop: boolean ): boolean {
		const scatterCount: number = this.countScatter( reelId );
		const reelSetIndex = this.reelModel.currentReelSetIndex;

		let symbolCountTrigger = this.config.anticipationData.modelData[ reelSetIndex ].anticipationSymbolCountTrigger;
		return ( !this.reelModel.isFastStopPending && scatterCount >= symbolCountTrigger && this.isFeaturePossible( scatterCount, preStop ) );
	}

	protected countScatter ( reelId: number ): number {
		let scatterCount: number = 0;
		for ( let column = 0; column < reelId; column++ ) {
			for ( let row = 0; row < this.reelModel.currentReelStrips[ column ].symbolsOutcome.length; row++ ) {
				if ( this.reelModel.currentReelStrips[ column ].symbolsOutcome[ row ] == SymbolSet.SCATTER ) {
					scatterCount++;
				}
			}
		}
		return scatterCount;
	}

	protected stopAnticipate ( reelId: number ): void {
		if ( this.isAnticipating ) {
			this.isAnticipating = false;
			GlobalDispatcher.dispatchIntoExpectedStates( SpinDown.ANTICIPATE_END_NOTIFICATION,
				[
					'spinStop',  // this.stateNames.spinStop,
					'freeSpinsStop', // this.stateNames.freeSpinsStop
				],
				reelId
			);
			this.stopShaking();
			this.view.stopAnticipate();
		}
	}

	protected isFeaturePossible ( scatterCount: number, preStop: boolean ): boolean {
		const reelSetIndex = this.reelModel.currentReelSetIndex;

		let symbolCountRequired = this.config.anticipationData.modelData[ reelSetIndex ].anticipationSymbolCountRequired;
		return symbolCountRequired - scatterCount <= ( preStop ? this.stopRequestCounter : this.currentSpinningReelsCounter );
	}

	protected reelStoppedEffect ( reelId: number ): void {
		this.soundManager.play( `${ SlotSoundConfig.reelStopped }0${ reelId + 1 }`, AudioTrackName.EFFECT );
		if ( this.config.dropSymbols ) {
			this.showDropAnimation( reelId );
		}
	}

	protected anticipationReelStoppedEffect ( reelId: number ): void {
		this.startShaking( reelId );
	}

	//#region EFFECTS

	/**
	 * Checking all symbols, are there any among them - dropSymbols from config
	 */
	protected showDropAnimation ( reelId: number ): void {
		const reel = this.reelModel.currentSymbolsOutcome[ reelId ];
		for ( let j = 0; j < reel.length; j++ ) {
			if ( this.config.dropSymbols.includes( reel[ j ] ) ) {
				this.soundManager.play( `${ SlotSoundConfig.scatter }0${ reelId + 1 }`, AudioTrackName.EFFECT );
				this.view.symbolDropAnimation( reelId, j );
			}
		}
	}

	protected startShaking ( reelId: number ): void {
		if ( !this.reelModel.isShaking ) {
			for ( let i = 0; i <= reelId; i++ ) {
				this.reelModel.isShaking = this.view.playShaking( i );
			}
		} else {
			this.view.playShaking( reelId );
		}
	}

	protected stopShaking (): void {
		if ( this.reelModel.isShaking ) {
			this.view.stopShaking();
			this.reelModel.isShaking = false;
		}
	}

	//#endregion EFFECTS

}

export interface ISpinDownConfig extends ISpinBaseConfig {
	anticipationData: AnticipationData;
	dropSymbols?: Array<string>;
	stopReelNormalDelay: Array<number>;
}


