import { Scenario } from 'corengf/src/states/Scenario';
import { IGameState } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { SpinMsgFormatter } from 'slotngf/src/server/ng/formatter/http/in/SpinMsgFormatter';
import { FeatureTypes } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';

/**
 * formatting response for base spin api
 */
export class DHBSpinMsgFormatter extends SpinMsgFormatter {

	// add wild win data for wild win effect on state allLineWin
	public processIncomingMessage ( message: IGameState ): Scenario {
		let responseScenario: Scenario = super.processIncomingMessage( message );

		if ( responseScenario.hasStateName( this.stateNames.allLineWin ) ) {
			let wildWin = false;
			message.spin_result.reel_wins.forEach( ( winline ) => {
				if ( winline.wild_win ) {
					wildWin = true;
				}
			} );

			responseScenario.addStateData( this.stateNames.allLineWin, {
				wildWin: wildWin
			} );
		}
		if ( responseScenario.hasStateName( this.stateNames.prefeatureWinning ) ) {
			let respinData = message.features.find( e => e.type === FeatureTypes.FREE_RESPIN_REELS );
			let pickData = message.features.find( e => e.type === FeatureTypes.PICK );
			responseScenario.addStateData( this.stateNames.prefeatureWinning, {
				respin: respinData,
				pick: pickData
			} );
		}
		return responseScenario;
	}

}


