import { Inject } from 'typescript-ioc/es5';
import { CompController } from 'corengf/src/mvc/CompController';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { TooltipView } from 'corengf/src/components/tooltip/TooltipView';
import { ToolTipEvent } from 'corengf/src/components/tooltip/ToolTipEvent';
import { ToolTip, ITooltipEventParam } from 'corengf/src/elements/canvas/tooltip/Tooltip';


export class TooltipController extends CompController {
	protected activeTooltips: Array<ToolTip>;
	protected view: TooltipView;

	constructor ( @Inject view: TooltipView ) {
		super( view );
	}

	public addEventListeners (): void {
		GlobalDispatcher.addListener( this, ToolTipEvent.SHOW_TOOLTIP_EVENT, this.onShowTooltip );
		GlobalDispatcher.addListener( this, ToolTipEvent.HIDE_TOOLTIP_EVENT, this.onHideTooltip );
		GlobalDispatcher.addListener( this, ToolTipEvent.CLEAR_TOOLTIP_EVENT, this.onClearTooltip );
		this.view.destroySignal.add( this.destroyTooltip, this );
	}

	protected destroyTooltip ( data: ITooltipEventParam ): void {
		GlobalDispatcher.dispatchIntoAllStates( ToolTipEvent.DESTROY_TOOLTIP_EVENT, data );
	}

	protected onShowTooltip ( e: any ): void {
		this.view.showTooltip( e.data );
	}

	protected onHideTooltip ( e: any ): void {
		this.view.hideTooltip( e.data );
	}

	protected onClearTooltip ( e: any ): void {
		this.view.clearTooltip( e.data );
	}
}


