import { AudioTrackName } from 'corengf/src/audio/SoundManager';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { SpinDown } from 'slotngf/src/components/reelV2/action/SpinDown';
import { SlotSoundConfig } from 'slotngf/src/core/config/SlotSoundConfig';
import { DHBReelControllerView } from 'game/src/components/reels/DHBReelControllerView';

export class DHBSpinDown extends SpinDown {

	protected view: DHBReelControllerView;

	protected triggerAnticipateReel ( currentReelId: number, nextReelId: number ): void {
		super.triggerAnticipateReel( currentReelId, nextReelId );
		this.view.setSymbolsScaleFromCurrentView( currentReelId, false );
		this.view.setSymbolsScaleFromCurrentView( nextReelId, true );
	}

	protected stopAnticipate ( reelId: number ): void {
		super.stopAnticipate( reelId );
		this.view.setSymbolsScaleFromCurrentView( reelId, false );
	}

	protected reelStoppedAction ( reelId: number ): void {
		GlobalDispatcher.dispatchIntoAllStates( 'REEL_STOP_ACTION', { reel: reelId } );
		super.reelStoppedAction( reelId );
	}

	protected showDropAnimation ( reelId: number ): void {
		const reel = this.reelModel.currentSymbolsOutcome[ reelId ];
		for ( let j = 0; j < reel.length; j++ ) {
			if ( this.config.dropSymbols.includes( reel[ j ] ) ) {
				this.view.symbolDropAnimation( reelId, j );

				// play scatter drop audio if symbol IS SCATTER
				if ( reel[ j ] == 'SCATTER' ) {
					this.soundManager.play( `${ SlotSoundConfig.scatter }0${ reelId + 1 }`, AudioTrackName.EFFECT );
				}
			}
		}
	}

	protected getNextReelId ( reelId: number ): number {
		// should not return reelId + 1
		return this.pendingForStopReels[ this.pendingForStopReels.indexOf( reelId ) + 1 ];
	}

}


