import { Inject } from 'typescript-ioc/es5';
import { Scenario } from 'corengf/src/states/Scenario';
import { SlotNGHttpFormatter } from './../../SlotNGHttpFormatter';
import { FeaturesFormatterList } from './features/FeaturesFormatterList';
import { IGameState, IFeatureInfo } from './../../../data/SlotNGDataDefinitions';

/**
 * Formatting all pending feature response.
 * If the game feature didn't support in framework,
 * add game feature in FeaturesFormatterList by override it,
 */
export class RunFeaturesMsgFormatter extends SlotNGHttpFormatter {

	@Inject
	protected featureFormatterList: FeaturesFormatterList;

	public processIncomingMessage ( message: IGameState ): Scenario {

		let responseScenario: Scenario = null;

		for ( let i: number = 0; i < message.features.length; i++ ) {
			let scenario = this.getFeatureScenario( message, message.features[ i ] );

			if ( scenario != undefined ) {
				if ( responseScenario == undefined ) {
					responseScenario = scenario;
				} else {
					responseScenario.addScenario( scenario );
				}
			}

			if ( !message.features[ i ].complete ) {
				break;
			}
		}

		let roundCompleted = false;
		if ( !message.features || this.isAllFeatureCompleted( message.features ) ) {
			roundCompleted = true;
		}

		this.setRoundCompletedFlag( responseScenario, [
			this.stateNames.baseGameIdle
		], roundCompleted );

		return responseScenario;
	}

	protected getFeatureScenario ( message: IGameState, feature: IFeatureInfo ): Scenario {
		let formatter = this.featureFormatterList.inFeatureFormatters.get( feature.type );
		if ( formatter != undefined ) {
			return formatter.processIncomingMessage( message, feature );
		} else {
			return null;
		}
	}
}



