import { Inject, Singleton } from 'typescript-ioc/es5';
import { SlotScenarioManager } from 'slotngf/src/core/SlotScenarioManager';
import { DHBSlotScenarioNames } from 'game/src/states/DHBSlotScenarioNames';
import { DHBSlotStates } from 'game/src/states/DHBSlotStates';

@Singleton
export class DHBSlotScenarioManager extends SlotScenarioManager {

	@Inject
	protected stateName: DHBSlotStates;

	@Inject
	protected scenarioName: DHBSlotScenarioNames;

	protected createScenarios (): void {
		super.createScenarios();

		this.scenarioFlows.set(
			this.scenarioName.pickFeature, this.getScenarioByStateNames( this.scenarioName.pickFeature,
				[
					this.stateName.pickFeature,
					this.stateName.endPickFeature
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.pickFeatureWithWinning, this.getScenarioByStateNames( this.scenarioName.pickFeatureWithWinning,
				[
					this.stateName.pickFeatureWin
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.freeSpinsWithWinning, this.getScenarioByStateNames( this.scenarioName.freeSpinsWithWinning,
				[
					this.stateName.freeSpinsAllLineWin
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.freeSpinsRotateReelWithWinning, this.getScenarioByStateNames( this.scenarioName.freeSpinsRotateReelWithWinning,
				[
					this.stateName.freeSpinsRotate,
					this.stateName.freeSpinsAllLineWin
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.bonusSpinsWithWinning, this.getScenarioByStateNames( this.scenarioName.bonusSpinsWithWinning,
				[
					this.stateName.freeSpinsAllLineWin,
					this.stateName.freeSpinsIdle
				]
			)
		);

	}
}


