import { Inject } from 'typescript-ioc/es5';
import { WinLineDisplayData } from 'slotngf/src/components/lines/WinLine';
import { WinDisplayView } from './../WinDisplayView';
import { WinDisplayConfig } from './../WinDisplayConfig';
import { CompController } from 'corengf/src/mvc/CompController';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { IWinDisplayConfig } from 'slotngf/src/core/config/IWinDisplayConfig';
import { IReelsConfig } from 'slotngf/src/core/config/IReelsConfig';
import { ReelConfig } from 'slotngf/src/components/reelV2/ReelConfig';


export class WinDisplayV2Controller extends CompController {

	public static readonly SHOW_WIN_LINE = 'paylinesShowWinLine';
	public static readonly SHOW_WIN_TABLE = 'showWinTable';

	protected view: WinDisplayView;

	constructor ( @Inject view: WinDisplayView ) {
		super( view );
	}

	public init (): void {
		let config = this.gameConfig.getConfig<IWinDisplayConfig>( WinDisplayConfig );
		this.view.setReelsConfig( this.gameConfig.getConfig<IReelsConfig>( ReelConfig ) );
		this.view.initialize( config );
		this.finishStateAction();
	}

	public addEventListeners (): void {
		GlobalDispatcher.addListener( this, WinDisplayV2Controller.SHOW_WIN_LINE, this.onShowWinLine );
	}

	public baseGameIdle (): void {
		this.view.hide();
		this.finishStateAction();
	}

	protected onShowWinLine ( e: GlobalWinDisplayData ): void {
		this.view.showPayline( e.data );
	}
}

interface GlobalWinDisplayData {
	type: string;
	data: ShowWinLineData;
}

export interface ShowWinLineData {
	winData: WinLineDisplayData;
	showPaylineLabel: boolean;
	totalWinLines: number;
}



