import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { Window } from 'corengf/src/elements/canvas/window/Window';
import { Sprite, ISprite } from 'corengf/src/elements/canvas/sprite/Sprite';
import { Button, IButtonConfig } from 'corengf/src/elements/canvas/button/Button';
import { ITextButtonConfig, TextButton } from 'corengf/src/elements/canvas/button/TextButton';
import { MoneyFormatter, NumberDisplayMode } from 'corengf/src/utils/MoneyFormatter';
import { WebTextField, IWebTextFieldConfig } from 'corengf/src/elements/canvas/text/WebTextField';
import { IValueStepperConfig, ValueStepper } from 'corengf/src/elements/canvas/valueStepper/ValueStepper';
import { SlotGameType } from 'slotngf/src/core/SlotGameType';
import { SlotBankService } from 'slotngf/src/core/SlotBankService';
import { MLUIPanel } from 'mlui/src/core/MLUIPanel';
import { IMLUIBetConfig } from 'mlui/src/components/bet/MLUIBetConfig';
import { Translator } from 'corengf/src/utils/Translator';
import { Point } from 'pixi.js';
import { TextField } from 'corengf/src/elements/canvas/text/TextField';
import { CoreUIElementFactory } from 'corengf/src/factory/CoreUIElementFactory';


export class MLUIBetPanel extends MLUIPanel {

	@Inject
	protected translator: Translator;

	@Inject
	protected bankService: SlotBankService;

	@Inject
	private moneyFormatter: MoneyFormatter;

	public onBetChangeSignal: MiniSignal = new MiniSignal();

	protected betStartIndex: number = 0;
	protected linesStartIndex: number = 0;

	protected lineStepper: ValueStepper;
	protected betStepper: ValueStepper;

	protected showTotalBetValue: WebTextField;

	protected separateLine: Sprite;
	protected confirmSprite: Sprite;

	protected closeButton: Button;
	protected confirmButton: Button;

	protected maxBetButton: TextButton;

	protected config: IMLUIBetConfig;
	protected gameType: SlotGameType;


	protected createElements ( config: IMLUIBetConfig ): void {
		super.createElements( config );
		this.createPanelBackground();
		this.createMainTitle();

		this.addConfirmButton( config.confirmButton );
		this.createCloseButton( config.closeButton );
		this.createMaxBetButton( config.maxBetButton );

		this.addShowTotalBetValue( config.showTotalBetValue );

		this.createBetStepper( this.config.betStepper );
		this.createLineStepper( this.config.lineStepper );

		this.createSeparateLine( this.config.separateLine );

		this.createButtonsSprites( this.config );

		this.hidePopup();
	}

	protected createSeparateLine ( config: ISprite ): void {
		this.separateLine = new Sprite( config );
		this.addChild( this.separateLine );
	}

	protected createMainTitle (): void {
		const title = new WebTextField( this.config.mainTitle );
		this.addChild( title );
	}

	protected createButtonsSprites ( config: IMLUIBetConfig ): void {
		this.confirmSprite = new Sprite( config.confirmSprite );
		this.addChild( this.confirmSprite );
	}

	protected createMaxBetButton ( data: ITextButtonConfig ): void {
		this.maxBetButton = new TextButton( data );
		this.maxBetButton.elementSignal.add( this.maxBet.bind( this ) );
		this.maxBetButton.anchor = new Point( 0.5, 0.5 );
		this.addChild( this.maxBetButton );
	}

	private maxBet (): void {
		this.onBetChange( this.bankService.betOptions.length - 1 );
		if ( this.betStepper != null ) { this.betStepper.index = this.bankService.betOptions.length - 1; }
		if ( this.gameType == SlotGameType.LINEGAME ) {
			this.onLinesChange( this.bankService.availableLines.length - 1 );
			if ( this.lineStepper != null ) { this.lineStepper.index = this.bankService.availableLines.length - 1; }
		}
		this.confirmButton.setEnable( true );
	}

	protected createPanelBackground (): void {
		let background = new Window( this.config.background );
		this.addChild( background );
	}

	protected addShowTotalBetValue ( data: IWebTextFieldConfig ): void {
		this.showTotalBetValue = new WebTextField( data );
		this.showTotalBetValue.text = this.moneyFormatter.formatMoney( this.bankService.totalBet, NumberDisplayMode.VALUE_WITH_CURRENCY );
		this.showTotalBetValue.x = this.viewport.width / 2 - this.showTotalBetValue.width / 2;
		this.addChild( this.showTotalBetValue );
	}

	protected addConfirmButton ( data: ITextButtonConfig ): void {
		this.confirmButton = new Button( data, this.localButtonsController );
		this.setConfirmClickData();
		this.addChild( this.confirmButton );
	}

	protected setConfirmClickData (): void {
		this.confirmButton.clickData = {
			data: {}
		};
	}

	protected createCloseButton ( data: IButtonConfig ): void {
		this.closeButton = new Button( data, this.localButtonsController );
		this.addChild( this.closeButton );
	}

	protected createBetStepper ( valueStepperConfig: IValueStepperConfig ): void {
		this.betStepper = new ValueStepper( valueStepperConfig );
		let bets: Array<string> = new Array<string>();
		for ( let i = 0; i < this.bankService.betOptions.length; i++ ) {
			bets.push( this.moneyFormatter.formatMoney( this.bankService.betOptions[ i ], NumberDisplayMode.VALUE_ONLY ) );
		}

		this.betStepper.onIndexChanged.add( this.onBetChange, this );
		this.betStepper.setItems( bets, this.bankService.betOptions.indexOf( this.bankService.bet ) );
		this.addChild( this.betStepper );
	}

	protected createLineStepper ( valueStepperConfig: IValueStepperConfig ): void {
		this.lineStepper = new ValueStepper( valueStepperConfig );
		if ( this.gameType === SlotGameType.LINEGAME ) {
			this.lineStepper.setItems( this.bankService.availableLines, this.bankService.availableLines.indexOf( this.bankService.lines ) );
		} else if ( this.gameType === SlotGameType.FIXED_LINEGAME ) {
			this.lineStepper.setItems( [ this.bankService.maxLines ] );
		} else if ( this.gameType === SlotGameType.ALLPAYS ) {
			this.setAllPaysConfigData();
		}
		this.lineStepper.onIndexChanged.add( this.onLinesChange, this );
		this.addChild( this.lineStepper );
	}

	protected onBetChange ( newBet: number ): void {
		this.onBetChangeSignal.dispatch( newBet );
		this.bankService.bet = this.bankService.betOptions[ newBet ];
		if ( this.gameType === SlotGameType.LINEGAME || this.gameType === SlotGameType.FIXED_LINEGAME ) {
			let clickData = this.confirmButton.clickData;
			clickData.data.betAmount = this.bankService.bet;
			clickData.data.lines = this.bankService.lines;
			this.confirmButton.clickData = clickData;
		}

		this.totalBetUpdate();
		this.confirmButton.setEnable( true );
	}

	protected onLinesChange ( newLines: number ): void {
		if ( this.gameType === SlotGameType.LINEGAME ) {
			this.bankService.lines = this.bankService.availableLines[ newLines ];
			let clickData = this.confirmButton.clickData;
			clickData.data.betAmount = this.bankService.bet;
			clickData.data.lines = this.bankService.lines;
			this.confirmButton.clickData = clickData;
		}
		this.totalBetUpdate();

		this.confirmButton.setEnable( true );
	}

	protected totalBetUpdate (): void {
		const index = this.bankService.betOptions.indexOf( this.bankService.bet );
		const totalBet = this.bankService.betOptions[ index ] * this.bankService.lines;

		let clickData = this.confirmButton.clickData;
		clickData.data.totalBet = totalBet;
		this.confirmButton.clickData = clickData;
		this.showTotalBetValueUpdate();
		this.setMaxButtonEnable();
	}

	protected showTotalBetValueUpdate (): void {
		if ( this.showTotalBetValue != null ) {
			// tslint:disable-next-line: max-line-length
			this.showTotalBetValue.text = this.translator.t( 'totalBet' ) + ' ' + this.moneyFormatter.formatMoney( this.bankService.totalBet, NumberDisplayMode.VALUE_WITH_CURRENCY );
			this.showTotalBetValue.x = this.viewport.width / 2 - this.showTotalBetValue.width / 2;
		}
	}

	public setGameType ( type: SlotGameType ) {
		this.gameType = type;
	}

	public portraitLayout (): void {
		super.portraitLayout();
		this.showTotalBetValueUpdate();
	}

	public landscapeLayout (): void {
		super.landscapeLayout();
		this.showTotalBetValueUpdate();
	}

	public showPopup (): void {
		this.betStartIndex = this.bankService.betOptions.indexOf( this.bankService.bet );
		if ( this.gameType === SlotGameType.LINEGAME ) {
			this.linesStartIndex = this.bankService.availableLines.indexOf( this.bankService.lines );
		}
		this.reset();

		this.confirmButton.setEnable( false );
		this.setMaxButtonEnable();
		this.visible = true;
	}

	public onClose (): void {
		this.reset();
		this.hidePopup();
	}

	protected reset (): void {
		this.betStepper.index = this.betStartIndex;
		this.lineStepper.index = this.linesStartIndex;
	}

	public confirmBet (): void {
		this.hidePopup();
	}

	protected hidePopup (): void {
		this.visible = false;
	}

	protected setAllPaysConfigData (): void {
		let allPaysCoins: TextField;
		allPaysCoins = CoreUIElementFactory.createInstance( this.config.lineStepper.value );
		allPaysCoins.text = allPaysCoins.getTextByKey( this.config.lineStepper.value.elementConfig.key );

		this.config.lineStepper.title.elementConfig.key = 'coinValue';
		this.lineStepper.setItems( [ allPaysCoins.text ] );
		this.lineStepper.updateAttributes( this.config.lineStepper );

		this.lineStepper.setNextButtonVisible( false );
		this.lineStepper.setPrevButtonVisible( false );
	}

	protected setMaxButtonEnable (): void {
		if ( this.bankService.lines == this.bankService.availableLines[ this.bankService.availableLines.length - 1 ] &&
			this.bankService.bet == this.bankService.betOptions[ this.bankService.betOptions.length - 1 ] ) {
			this.maxBetButton.setEnable( false );
		} else {
			this.maxBetButton.setEnable( true );
		}
	}
}


