import { Singleton, Inject } from 'typescript-ioc/es5';
import { Actions } from 'corengf/src/utils/Actions';
import { SlotActions } from './../utils/SlotActions';
import { SlotBankService } from './../core/SlotBankService';
import { SlotBankData } from './SlotSeleniumDataDefinitions';
import { GameController } from 'corengf/src/core/GameController';
import { SeleniumPortal, SeleniumHistoryMessage, SeleniumResponse, MessageDirection } from 'corengf/src/selenium/SeleniumPortal';


@Singleton
export class SlotSeleniumPortal extends SeleniumPortal {

	@Inject
	protected gameController : GameController;

	@Inject
	protected slotBankService : SlotBankService;

	/**
	 * spin with command line.
	 */
	public spin() : void {
		this.gameController.handleButtonAction( Actions.CLICK_ACTION_PREFIX + SlotActions.SPIN );
	}

	/**
	 * get date from back service.
	 */
	public getBankData() : SlotBankData {
		return new SlotBankData ( this.slotBankService.balance, this.slotBankService.currency, this.slotBankService.bet, this.slotBankService.lines );
	}
}

export class SlotSeleniumHistoryMessage implements SeleniumHistoryMessage {
	public name : string;
	public direction : MessageDirection;
	public timestamp : number;

	constructor( name : string, direction : MessageDirection ) {
		this.name = name;
		this.direction = direction;
		this.timestamp = Date.now();
	}
}

export class SlotSeleniumResponse implements SeleniumResponse {
	public stateName : string;
	public receivedMessageHistory : Array< SeleniumHistoryMessage >;

	constructor ( stateName : string, receivedMessageHistory : Array< SeleniumHistoryMessage >) {
		this.stateName = stateName;
		this.receivedMessageHistory = receivedMessageHistory;
	}
}


