import { TsMap } from 'corengf/src/utils/TsMap';
import { ISize } from 'corengf/src/core/config/IMainConfig';
import { IWebTextConfig } from './text/WebTextElement';
import { IWebImageConfig } from './image/WebImageElement';
import { IWebLineConfig } from './line/WebLineElement';
import { IWebPayoutConfig } from './payout/WebPayoutElement';
import { ICoreWebElementConfig } from './CoreWebElement';
import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { UIButtonId } from 'corengf/src/components/ui/buttons/UIButtonId';
import { IWebPatternStyleConfig } from 'corengf/src/elements/webpage/linePattern/WebLinePatternElement';

export interface IGeneralWebPageLayoutConfig {
	width: number;
	height: number;
	blockStyle?: string;
	headerConfig: IHeaderConfig;
	containerConfig: IContainerConfig;
}

export interface IHelpScreenLayoutConfig extends IGeneralWebPageLayoutConfig {
	containerConfig: IHelpScreenContainerConfig;
}

export interface IHistoryLayoutConfig extends IGeneralWebPageLayoutConfig {
	headerConfig: IHistoryHeaderConfig;
	subHeaderConfig: ISubHeaderConfig;
	containerConfig: IHistoryContainerConfig;
}

export interface IHeaderConfig {
	blockStyle?: string;
	titleConfig?: IWebTextConfig | IDataSource;
	closeButton: IWebButton;
}

export interface IContainerConfig {
	blockStyle?: string;
	innerBlockStyle?: string;
}

export interface IHelpScreenContainerConfig extends IContainerConfig {
	paragraphs: Array<ICoreWebElementConfig>;
}

export interface IHistoryContainerConfig extends IContainerConfig {
	historyTabConfigs: TsMap<string, IHistoryTabConfigs>;
}

export interface IHistoryTabConfigs {
	type: string;
	withDetailHeader?: IWebTextConfig;
	summaryConfig: ISummaryConfig;
	detailConfig: IDetailConfig;
}

export interface IHistoryHeaderConfig extends IHeaderConfig {
	dateDisplayBar?: IDataSource;
	dateFilterConfig: IDateFilterConfig;
	backButton: IWebButton;
}

/**
 * show subHeader when there is summary
 */
export interface ISubHeaderConfig {
	blockStyle?: string;
	tabOptionsConfig?: ITabOptionsConfig;
	config: Array<IWebpageElement>;
}

/**
 * tabBlockStyle: define each tab block style
 * supportType: define which types are supported
 */
export interface ITabOptionsConfig {
	blockStyle?: string;
	tabOptions: TsMap<string, ITabOptionConfig>;
}

export interface ITabOptionConfig {
	tabStyle?: string;
	type: string;
	buttonConfig: IWebButton;
}

/**
 * defult timeZone: local
 */
export interface IDateFilterConfig {
	timeZone?: number;
	style?: string;
	closeBoxAreaStyle?: string;
	filterLanguageKey: string;
	dateFilterMenuConfig: IDateFilterMenuConfig;
}

export interface IWebButton {
	blockStyle?: string;
	dataHookTrackerConfig?: IDataHookTrackerConfig;
	buttonImage?: IWebImageConfig;
	languageKey?: string;
	soundKey?: string;
}

/**
 * defult type: GENERAL
 */
export interface IDataHookTrackerConfig {
	buttonID: UIButtonId;
	type?: DataHookType;
}

export enum DataHookType {
	GENERAL = 'GENERAL',
	GENPLUS = 'GENPLUS'
}

export interface IWebpageElement {
	type: WebElementType;
	blockStyle?: string;
}

export enum WebElementType {
	WEB_ELEMENT_BLOCK = 'WEB_ELEMENT_BLOCK',
	WEB_IMAGE_ELEMENT = 'WEB_IMAGE_ELEMENT', //IMAGE
	WEB_TEXT_CONTENT_ELEMENT = 'WEB_TEXT_CONTENT_ELEMENT', //CONTENT
	WEB_LINE_ELEMENT = 'WEB_LINE_ELEMENT', //DEVISION_LINE
	WEB_TEXT_ELEMENT = 'WEB_TEXT_ELEMENT', //TEXT
	WEB_PAYOUT_ELEMENT = 'WEB_PAYOUT_ELEMENT', //PAYOUT
	//FOOTER = 'FOOTER',
	//RTP = 'RTP',
	//SYMBOL_BLOCKS = 'SYMBOL_BLOCKS',

	HELPSCREEN_PAGING = 'HELPSCREEN_PAGING',
	LINE_PATTERN = 'LINE_PATTERN',
	RESULTS_PAGING = 'RESULTS_PAGING',
	REEL_RESULTS = 'REEL_RESULTS',
	GIANT_REEL_RESULTS = 'GIANT_REEL_RESULTS',
	REELWINS_PAGING = 'REELWINS_PAGING',
	FEATURES = 'FEATRUES',
	WEBPAGE_ELEMENT = 'WEBPAGE_ELEMENT',
	DATA_SOURCE = 'DATA_SOURCE',
}

export interface IPaging {
	arrowButton?: IArrowButton;
	dropDownBox?: IDropDownBox;
}

/**
 * pageSize = 0 : no limit
 * backgroundConfig : use transaction type get image config. can set defualt.
 */
export interface IResultsPaging extends IWebpageElement {
	pagingConfig: IPaging;
	variableName?: string;
	config?: Array<IWebpageElement>;
	pageSize: number;
}

export interface IGeneralResultsPaging extends IResultsPaging {
	backgroundConfig?: TsMap<string, IWebImageConfig>;
}

export interface IGenplusResultsPaging extends IResultsPaging {
	lootBoxConfig: ILootBoxConfig;
}

/**
 * set one loot box.
 * boxTitle's text = loot box type + IText config.
 * itemConfig set one item config for loot box.
 */
export interface ILootBoxConfig {
	blockStyle?: string;
	boxTitle?: ILootBoxTitle;
	itemConfig: ILootBoxItemConfig;
}

export interface ILootBoxTitle {
	blockStyle?: string;
	languageKey: string;
}

export interface ILootBoxItemConfig {
	blockStyle?: string;
	typeStyle?: string;
	amountStyle?: string;
}

export interface IReelwinsPaging extends IWebpageElement {
	pagingConfig: IPaging;
	OnePageWinAmount: number;
}

export interface IArrowButton {
	blockStyle?: string;
	nextButton: IWebButton;
	preButton: IWebButton;
	pageDot?: IPageDot;
	showNumber?: boolean;
	numberStyle?: string;
}

export interface IPageDot {
	blockStyle?: string;
	dotStyle?: string;
	brightColor?: string;
	darkColor?: string;
}

export interface IImageData {
	ratio: number;
}

export interface IImageSprite extends IImageData {
	assetName: string;
	imageName: string;
}

export interface IImageTexture extends IImageData {
	imagePath: string;
	width: number;
	height: number;
}

export interface IImageURL extends IImageData {
	url: string;
	width: number;
	height: number;
}

export interface ISymbolBlocks extends IWebpageElement {
	decorativeBlocks?: Array<IWebImageConfig | IWebpageElement>;
	symbolBlockElements: Array<ISymbolBlockElement>;
}

export interface ISymbolBlockElement {
	blockStyle?: string;
	elementTitle?: IWebTextConfig;
	SymbolImage: IWebImageConfig;
	payoutConfig: IWebPayoutConfig;
}

export interface IPayout {
	num: number;
	multi: number;
}

export interface IDateFilterMenuConfig {
	blockStyle?: string;
	closeButton: IWebButton;
}

export interface ISummaryConfig {
	blockStyle?: string;
	titleHeight: number;
	rowHeight: number;
	rowStyle?: string;
	rowDataHookConfig?: IDataHookTrackerConfig;
	rowConfig: Array<IWebpageElement>;
	pagingButtonConfig?: IArrowButton;
	pageSize: number;
	noSummaryConfig: INoSummaryConfig;
}

export interface IGeneralSummaryConfig extends ISummaryConfig {
	titleConfig?: IGeneralWebComponents;
	extraWinConfig?: IExtraWinConfig;
}

export interface IGenplusSummaryConfig extends ISummaryConfig {
	haveDetailIcon: IWebImageConfig;
	cardinalStyle?: string;
	evenStyle?: string;
}

export interface IGeneralWebComponents {
	blockStyle?: string;
	config: Array<IWebpageElement>;
}

export interface INoSummaryConfig extends IGeneralWebComponents {
	buttonConfig?: IWebButton;
}

export interface IExtraWinConfig {
	iconStyle?: string;
	blockStyle?: string;
	textStyle?: string;
	featureTrackIcons?: TsMap<string, IWebImageConfig>;
	boosterImageConfig?: IImageSource;
}

/**only used "SPRITE"
 * ex: imageName: symbol_{name}.png
 * will replced {name}
 */
export interface IImageSource {
	sourceName: string;
	imageName: string;
	ratio: number;
}

export interface IDetailConfig {
	blockStyle?: string;
	resultConfig: IResultsPaging;
}

export interface IGeneralDetailConfig extends IDetailConfig {
	informationConfig: ITransactionInformationConfig;
	resultConfig: IGeneralResultsPaging;
}

export interface IGenplusDetailConfig extends IDetailConfig {
	config: Array<IWebpageElement>;
	totalPrizeDropDownLootBox: IDropDownListLootBox;
	resultConfig: IGenplusResultsPaging;
}

export interface ITransactionInformationConfig {
	blockStyle?: string;
	dropDownBox?: IDropDownBox;
	boosterImageConfig?: IImageSource;
	boosterStyle?: string;
	idBlockConfig?: ITransactionInformationBlock;
	betBlockConfig?: ITransactionInformationBlock;
	winBlockConfig?: ITransactionInformationBlock;
	balanceBlockConfig?: ITransactionInformationBlock;
	textStyle?: string;
}

export interface ITransactionInformationBlock {
	blockStyle?: string;
	titleConfig: IWebTextConfig;
	valueConfig: IDataSource;
}

export interface IReelResults extends IWebpageElement {
	devisionLine?: IWebLineConfig;
	reelsConfig: IReels;
	reelWinsConfig: IReelWins;
}

export interface IGiantReelResults extends IReelResults {
	featureConfig: IFeatures;
	titleConfig: IWebTextConfig | IDataSource;
}

export interface IReels {
	blockStyle?: string;
	maskFrameStyle?: string;
	symbolImageCofig: IImageSource;
	reelSymbolPosition?: Array<Array<IPoint>>;
}

// pageSize = 0 : no limit
export interface IReelWins {
	blockStyle?: string;
	lineWinConfig?: ILineWinConfig;
	rowHeight: number;
	symbolImageCofig: IImageSource;
	symbolStyle?: string;
	showAll: boolean;

	winCountStyle?: string;
	winTextConfig: IWinTextConfig;
	paging?: IReelwinsPaging;
}

/**
 * @param {number} reelAmountOnColumn :used for format to line pattern, define several reels in a column, can't not use with symbolAmountOnReel.
 * @param {number} symbolAmountOnReel :used for format to line pattern, define symbol amount in one reel, can't not use with reelAmountOnColumn.
 */
export interface ILineWinConfig {
	linePattern: IWebPatternStyleConfig;
	reelAmountOnColumn?: number;
	symbolAmountOnReel?: number;
}

export interface IWinTextConfig {
	languageKey: string;
	textStyle?: string;
	maxNumberAmount: number;
	winNumberStyle?: string;
	multiplierStyle?: string;
}

/**
 * Display corresponding variable value or text
 */
export interface IDataSource extends IWebpageElement {
	variableName?: string;
	maxAmount?: number;
}

/**
 * variableName: input array's name
 * arrowTurnStyle: this variable represents a animation
 */
export interface IDropDownBox {
	blockStyle?: string;
	dataHookTrackerConfig?: IDataHookTrackerConfig;
	soundKey?: string;
	dropDownStyle?: string;
	dropDownBoxTitleStyle?: string;
	dropDownMenuStyle?: string;
	dropDownMenuRowStyle?: string;
	arrowImage?: IWebImageConfig;
	arrowTurnStyle?: string;
	variableName?: string;
	closeBoxAreaStyle?: string;
}

/**
 * Only the display content has no click function.
 * The displayed content is created by the user.
 */
export interface IDropDownList {
	blockStyle?: string;
	dataHookTrackerConfig?: IDataHookTrackerConfig;
	soundKey?: string;
	dropDownStyle?: string;
	dropDownMenuStyle?: string;
	arrowImage?: IWebImageConfig;
	arrowTurnStyle?: string;
	closeBoxAreaStyle?: string;
	titleConfig: IWebTextConfig;
}

/**
 * The displayed content setting
 */
export interface IDropDownListLootBox extends IDropDownList {
	contentconfig: ILootBoxConfig;
}

/**
 * config: config one result layout
 */
export interface IFeatures extends IWebpageElement {
	featureType: string;
	config?: Array<IWebpageElement>;
}

export interface IFeatureSuperSymbols extends IFeatures {
	reelSymbolPosition?: Array<Array<IPoint>>;
}

export interface IFeatureMegaSymbols extends IFeatures {
	reelSymbolPosition: Array<Array<IPoint>>;
	megaSymbolConfigs: TsMap<string, IMegaSymbolConfig>;
}

/**
 * Set the corresponding image of each MEGA symbol,
 * In units of 1 symbol.
 */
export interface IMegaSymbolConfig {
	image: IWebImageConfig;
	symbolSize: ISize;
	distancePerUnit: number;
}

/**
 * Giant Reel consists of multiple reel sets
 * "reelSetConfig" set up one reel set layout
 *
 * Giant Reel layout:
 *
 * 		reel set 1 :reel
 * 		reel set 2 :reel
 * 		reel set 3 :reel
 *  --------------------------- devisionLine
 * 		reel set 1 :title
 * 		reel set 1 :reel win
 * 		reel set 2 :title
 * 		reel set 2 :reel win
 * 		reel set 3 :title
 * 		reel set 3 :reel win
 */
export interface IFeatureGiantReel extends IFeatures {
	reelSetConfig: IGiantReelResults;
}

export interface IFeatrueReplacingSymbols extends IFeatures {
	replacingSymbolsConfig?: IReplacingSymbolsConfig;
	replaceSymbolImage?: IReplaceSymbolImage;
}

/**
 * Set some kind of Symbol to another picture
 */
export interface IReplaceSymbolImage {
	blockStyle?: string;
	replaceImage: TsMap<string, IWebImageConfig>;
	reelSymbolPosition: Array<Array<IPoint>>;
}

/**
 * Render a symbol type into another symbol
 */
export interface IReplacingSymbolsConfig {
	blockStyle?: string;
	symbolSource: IImageSource;
	symbolStyle?: string;
	symbolSize?: number;
	arrowImage?: Array<IWebImageConfig | IWebpageElement>;
}

export interface IFeatureExpandingWild extends IFeatures {
	reelSymbolPosition?: Array<Array<IPoint>>;
	expandingWildImage: IWebImageConfig;
}

/**
 * history detail feature STAGE
 */
export interface IStageConfig extends IFeatures {
	stageBlock?: IStageBlock;
}

export interface IStageBlock {
	blockStyle?: string;
	totalLayer: number;
	perUnitOfLayer: number;
}

/*
 * choiceSymbolConfigs : config every choice symbol image
 */
export interface IFeaturePick extends IFeatures {
	choiceSymbolConfig: IChoiceSymbolConfig;
}

export interface IChoiceSymbolConfig {
	blockStyle?: string;
	choiceSymbolPosition: Array<IPoint>;
	choiceSymbolImageConfigs: TsMap<string, IMegaSymbolConfig>;
}


