import { Scenario } from 'corengf/src/states/Scenario';
import { IJackpotDetail } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { SlotNGHttpFormatter } from 'slotngf/src/server/ng/formatter/SlotNGHttpFormatter';
import { IGameState, IFeatureInfo } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';

/**
 *  formatting jackpot feature
 */
export class JackpotFeatureFormatter extends SlotNGHttpFormatter {

	/**
	 * formatting run feature api response
	 * @param message
	 * @param jackpotFeatureInfo
	 */
	public processIncomingMessage ( message: IGameState, jackpotFeatureInfo?: IFeatureInfo ): Scenario {

		if ( jackpotFeatureInfo == undefined || !jackpotFeatureInfo.complete ) {
			return null;
		}

		let jackpotDetail: IJackpotDetail = <IJackpotDetail> jackpotFeatureInfo.feature_state;
		if ( jackpotDetail == undefined ) {
			return null;
		}

		let responseScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.stopJackpotFeature );
		responseScenario.setStateData( this.stateNames.jackpotStop, {
			symbol: jackpotDetail.symbol,
			position: jackpotDetail.position,
			prize: jackpotDetail.prize,
		} );

		// TODO: check is there other features
		if ( false ) {
			//
		} else {
			responseScenario.addScenario( this.scenarioManager.getScenario( this.scenarioNames.gameIdle ) );
		}

		this.slotNGModel.stateTag = message.state_tag;
		return responseScenario;
	}

	/**
	 * formatting feature in feature_triggered
	 */
	public triggeredFeatureIncomingMessage ( message: IGameState, featureTriggeredInfo?: IFeatureInfo ): Scenario {

		if ( featureTriggeredInfo == undefined ) {
			return null;
		}

		let jackpotReelFeatureDetail: IFeatureInfo = featureTriggeredInfo;
		if ( jackpotReelFeatureDetail == undefined || jackpotReelFeatureDetail.complete ) {
			return null;
		}

		let jackpotDetail: IJackpotDetail = <IJackpotDetail> jackpotReelFeatureDetail.feature_state;
		if ( jackpotDetail == undefined ) {
			return null;
		}

		let responseScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.jackpotFeatureIdle );

		// set to jackpot reel view.
		responseScenario.setStateData( this.stateNames.transitionToJackpot, {
			reelConfig: jackpotDetail.reels
		} );

		return responseScenario;
	}

	/**
	 * formatting feature while restore feature
	 */
	public restoreFeatureIncomingMessage ( jackpotFeatureDetail: IJackpotDetail ): Scenario {
		if ( jackpotFeatureDetail == undefined ) {
			return null;
		}

		let jackpotFeatureScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.jackpotFeatureIdle );

		// set to jackpot reel view.
		jackpotFeatureScenario.setStateData( this.stateNames.transitionToJackpot, {
			reelConfig: jackpotFeatureDetail.reels
		} );

		return jackpotFeatureScenario;
	}
}



