import { Inject } from 'typescript-ioc/es5';
import { Helper } from 'corengf/src/utils/Helper';
import { CoreState } from 'corengf/src/states/CoreState';
import { Translator } from 'corengf/src/utils/Translator';
import { SlotMarqueeView } from 'slotngf/src/components/marquee/SlotMarqueeView';
import { GlobalDispatcher, EventParams } from 'corengf/src/events/GlobalDispatcher';
import { MarqueeController } from 'corengf/src/components/marquee/MarqueeController';
import { MoneyFormatter, NumberDisplayMode } from 'corengf/src/utils/MoneyFormatter';
import { MarqueeDisplayMode } from 'corengf/src/elements/canvas/scroll/ScrollMarquee';
import { SlotMarqueeConfig, ISlotMarqueeConfig } from 'slotngf/src/components/marquee/SlotMarqueeConfig';
import { BigWinController } from 'slotngf/src/components/bigwin/BigWinController';
import { SlotStates } from 'slotngf/src/states/SlotStates';
import {
	ITransitionToBaseGameData,
	ITransitionToFreeSpinsData,
	IWinningData,
	IPrefeatureWinningData,
	IFreeSpinsStartData,
	IfreeSpinsShowWinningData
} from 'slotngf/src/states/SlotStatesDataInterfaces';


export class SlotMarqueeController extends MarqueeController {

	@Inject
	protected stateNames: SlotStates;

	protected view: SlotMarqueeView;
	protected config: ISlotMarqueeConfig;

	private _marqueeState: string;

	public get marqueeState (): string {
		return this._marqueeState;
	}
	public set marqueeState ( value: string ) {
		this._marqueeState = value;
	}

	@Inject
	protected translator: Translator;

	@Inject
	protected moneyFormatter: MoneyFormatter;

	protected currentMassageIndex: number = 0;

	constructor ( @Inject view: SlotMarqueeView ) {
		super( view );
	}

	public init (): void {
		super.init();
		this.config = this.gameConfig.getConfig<ISlotMarqueeConfig>( SlotMarqueeConfig );
		this.view.onMarqueeTweenCompleteSignal.add( this.onMarqueeTweenComplete, this );
	}

	public addEventListeners (): void {
		GlobalDispatcher.addListener( this, BigWinController.BIG_WIN_COUNT_COMPLETE_EVENT, this.onBigWinCountComplete );
		GlobalDispatcher.addListener( this, BigWinController.BIG_WIN_CANCEL_COUNT__EVENT, this.onBigWinCountComplete );
	}

	protected transitionToFreeSpins ( _data: ITransitionToFreeSpinsData ): void {
		this.view.visible = true;
		this.view.transitionToFreeSpins();
		this.finishStateAction();
	}

	protected transitionToBaseGame ( _data: ITransitionToBaseGameData ): void {
		this.view.visible = true;
		this.view.transitionToBaseGame();
		this.finishStateAction();
	}

	protected onBigWinCountComplete ( e: EventParams ) {
		let win: number;
		if ( e.data.stateName == this.stateNames.freeSpinsAllLineWin ) {
			win = e.data.stateData.featureState.feature_state.total_free_spin_win_amount;
			this.setMarquee( this.getTotalFeatureWinInfo( win, this.config.marqueeLabelFeatureTotalWinKey ), MarqueeDisplayMode.FIXED );
		} else {
			win = e.data.stateData.totalWin;
			this.setMarquee( this.getTotalWinInfo( win, this.config.marqueeLabelTotalWinKey ), MarqueeDisplayMode.FIXED );
		}
	}

	protected prefeatureWinning ( data: IPrefeatureWinningData ): void {
		if ( data.totalWin > 0 ) {
			this.setMarquee( this.getTotalWinInfo( data.totalWin, this.config.marqueeLabelTotalWinKey ), MarqueeDisplayMode.FIXED );
		} else {
			this.setMarquee( this.getPrefeatureNoWinInfo( this.config.marqueeLabelPrefeatureNoWinKey ), MarqueeDisplayMode.FIXED );
		}
		this.finishStateAction();
	}

	protected getTotalWinInfo ( win: number, translatorKey: string ): string {
		return this.translator.t( translatorKey ).replace( '{totalWin}',
			this.moneyFormatter.formatMoney( win, NumberDisplayMode.VALUE_ONLY ) );
	}

	protected getPrefeatureNoWinInfo ( translatorKey: string ): string {
		let currentString: string = this.translator.t( translatorKey );
		return currentString;
	}

	protected getTotalFeatureWinInfo ( win: number, translatorKey: string ): string {
		return this.translator.t( translatorKey ).replace( '{featureWin}',
			this.moneyFormatter.formatMoney( win, NumberDisplayMode.VALUE_ONLY ) );
	}

	public checkState ( state: CoreState ): void {
		if ( this.config != undefined && this.config.stateToTextMap != undefined && this.config.stateToTextMap.has( state.stateName ) ) {
			this.currentMassageIndex = 0;
			this.marqueeState = this.currentStateName;
			let stateToTextMap: Array<string> = this.config.stateToTextMap.get( this.currentStateName );
			let currentString: string = this.translator.t( stateToTextMap[ this.currentMassageIndex ] );
			if ( stateToTextMap.length <= 1 ) {
				this.setMarquee( currentString, MarqueeDisplayMode.FIXED );
			} else {
				this.setMarquee( currentString, MarqueeDisplayMode.ONCE );
			}
		}
	}

	protected onMarqueeTweenComplete (): void {
		this.setMarquee( this.getStateText(), MarqueeDisplayMode.ONCE );
	}

	protected getStateText (): string {
		let stateToTextMap: Array<string> = this.config.stateToTextMap.get( this.marqueeState );
		if ( stateToTextMap == undefined || stateToTextMap.length <= 0 ) {
			return Helper.EMPTY;
		}

		if ( this.currentMassageIndex >= 0 ) {

			if ( ( this.currentMassageIndex + 1 ) < stateToTextMap.length ) {
				this.currentMassageIndex++;
			} else {
				this.currentMassageIndex = 0;
			}
		}

		let currentString: string = this.translator.t( stateToTextMap[ this.currentMassageIndex ] );
		return currentString;
	}

	public setState ( state: CoreState ): boolean {
		let isHasStateMethod: boolean = super.setState( state );
		this.checkState( state );
		return isHasStateMethod;
	}

	protected setMarquee ( currentString: string, mode: MarqueeDisplayMode ): void {
		if ( currentString != Helper.EMPTY ) {
			this.view.setMarqueeLabel( currentString, mode );
		}
	}
}



