/**
 * General Button ID. Format: {elementType}_{elementName}
 *
 * @export
 * @class UIButtonId
 */
export class UIButtonId {
	// Menu Panel
	public static BUTTON_SHOW_MENU_OPTIONS: string = 'BUTTON_SHOW_MENU_OPTIONS';
	public static BUTTON_HIDE_MENU_OPTIONS: string = 'BUTTON_HIDE_MENU_OPTIONS';
	//BET PANEL
	public static BUTTON_SHOW_BET_OPTIONS: string = 'BUTTON_SHOW_BET_OPTIONS';
	public static BUTTON_CONFIRM_BET_OPTIONS: string = 'BUTTON_CONFIRM_BET_OPTIONS';
	public static BUTTON_CANCEL_BET_OPTIONS: string = 'BUTTON_CANCEL_BET_OPTIONS';
	//SETTINGS PANEL
	public static BUTTON_SHOW_SETTINGS: string = 'BUTTON_SHOW_SETTINGS';
	public static BUTTON_HIDE_SETTINGS: string = 'BUTTON_HIDE_SETTINGS';
	//HISTORY PANEL
	public static BUTTON_SHOW_HISTORY: string = 'BUTTON_SHOW_HISTORY';
	public static BUTTON_HIDE_HISTORY: string = 'BUTTON_HIDE_HISTORY';
	public static BUTTON_HOME: string = 'BUTTON_HOME';
	//tutorial
	public static BUTTON_ONBOARDING_ARROW: string = 'BUTTON_ONBOARDING_ARROW';
	public static BUTTON_ONBOARDING_CLOSE: string = 'BUTTON_ONBOARDING_CLOSE';
	// Settings Panel
	public static TOGGLE_SOUND: string = 'TOGGLE_SOUND';
	public static TOGGLE_MUSIC: string = 'TOGGLE_MUSIC';
	// Paytable Panel
	public static BUTTON_SHOW_PAYTABLE: string = 'BUTTON_SHOW_PAYTABLE';
	public static BUTTON_HIDE_PAYTABLE: string = 'BUTTON_HIDE_PAYTABLE';
	//Rules Panel
	public static BUTTON_SHOW_RULES: string = 'BUTTON_SHOW_RULES';
	public static BUTTON_HIDE_RULES: string = 'BUTTON_HIDE_RULES';
	// turbo
	public static BUTTON_TURBO_MODE: string = 'BUTTON_TURBO_MODE';
}


