import { Helper } from 'corengf/src/utils/Helper';
import { SlotGameType } from 'slotngf/src/core/SlotGameType';
import { SlotNGHttpFormatter } from 'slotngf/src/server/ng/formatter/SlotNGHttpFormatter';
import { ISymbolsData, ISymbolData } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';

/**
 * Format base spin request
 */
export class SpinRequestMsgFormatter extends SlotNGHttpFormatter {

	protected spin_action = 'SPIN';

	/**
	 * sends button press emulation package
	 * @param data - ignored
	 * @returns {any} message object
	 */
	public codeMessage ( data?: any ): ISpinRequestMsgConfig {
		this.updateBalance();

		let msg: ISpinRequestMsgConfig = this.getRequestPayload( data );

		if ( this.slotNGModel.gameType === SlotGameType.LINEGAME ) {
			msg.line = this.bankService.lines;
		}

		return msg;
	}

	protected getRequestPayload ( data?: any ): ISpinRequestMsgConfig {
		let requestData = {
			player_id: this.slotNGModel.playerId,
			session_token: this.slotNGModel.sessionId,
			partner_code: this.slotNGModel.partnerCode,
			game_code: this.config.gameCode,
			action: this.spin_action,
			state_tag: this.slotNGModel.stateTag,
			bet_denom_index: this.bankService.currentBetIndex,
			name: SlotNGHttpFormatter.SPIN
		};

		requestData = this.mergeForceSpinData( requestData, data );

		return requestData;
	}

	protected updateBalance (): void {
		// take total bet off from balance first
		if ( this.bankService.balance >= this.bankService.totalBet ) {
			this.bankService.balance = this.bankService.balance - this.bankService.totalBet;
		}
	}

	protected mergeForceSpinData ( requestData: any, data?: any ): any {
		if ( data && data.symbols ) {
			requestData = Helper.mergeTwoObjects( requestData, { force_data: { reels: this.getMatrix( data.symbols ) } } );
		}
		return requestData;
	}

	/**
	 * get matrix for reel result
	 * @param symbolsArray
	 */
	protected getMatrix ( symbolsArray: Array<Array<string>> ): Array<ISymbolsData> {
		let matrix: Array<ISymbolsData> = new Array<ISymbolsData>();
		for ( var index = 0; index < symbolsArray.length; index++ ) {
			matrix.push( {
				symbols: this.getMatrixReel( symbolsArray[ index ] )
			}
			);
		}
		return matrix;
	}

	/**
	 * get matrix for single reel
	 * @param symbolArray
	 */
	protected getMatrixReel ( symbolArray: Array<string> ): Array<ISymbolData> {
		let matrix: Array<ISymbolData> = new Array<ISymbolData>();
		for ( var index = 0; index < symbolArray.length; index++ ) {
			matrix.push( {
				symbol: symbolArray[ index ]
			}
			);
		}
		return matrix;
	}
}

export interface ISpinRequestMsgConfig {
	player_id: string;
	session_token: string;
	partner_code: string;
	game_code: string;
	action: string;
	state_tag: string;
	bet_denom_index: number;
	name: string;
	line?: number;
	force_data?: any;
}



