import { extras } from 'pixi.js';
import { TextField, ITextFieldConfig } from './TextField';


export class BitmapTextField extends TextField {

	public static elementType: string = 'BitmapTextField';

	protected label: extras.BitmapText;


	protected initElement ( config: IBitmapTextFieldConfig ): void {
		const text = ( config.text ) ? config.text : this.getTextByKey( config.key );
		this.label = new extras.BitmapText( text, config.style );

		this.addChild( this.label );
		super.initElement( config );
	}

	public wrapText (): void {
		this.label.maxWidth = this.maxWidth;
		this.label.calculateBounds();
		this.label.updateTransform();
		this.label.dirty = true;
	}
}

export interface IBitmapTextFieldConfig extends ITextFieldConfig {
	text?: string;
	style: extras.BitmapTextStyle;
}



