import { SoundManager } from 'corengf/src/audio/SoundManager';

export class DHBSoundManager extends SoundManager {

	public stop ( key: string ): void {
		if ( !this.audios.get( key ) ) {
			return;
		}

		// don't wait for time out
		// fix for anticipation effect
		this.audios.get( key ).stop();
	}
}


