import { Container, Inject } from 'typescript-ioc/es5';
import { NMath } from 'corengf/src/utils/NMath';
import { Helper } from 'corengf/src/utils/Helper';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import { AudioTrackName } from 'corengf/src/audio/SoundManager';
import { IGameElement } from 'corengf/src/core/config/IMainConfig';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { SymbolState } from './../ReelsEnums';
import { WinLineDisplayData } from './../../lines/WinLine';
import { ReelStripModel, SymbolData } from './ReelStripModel';
import { IReelStripModel } from './interfaces/IReelStripModel';
import { ReelsConfig, ReelSet, SymbolSet } from './ReelsConfig';
import { ReelsControllerView } from './../view/ReelsControllerView';
import { SlotCompController } from './../../mvc/SlotCompController';
import { SlotSoundConfig } from 'slotngf/src/core/config/SlotSoundConfig';
import { IInitData, ISpinStopData, IFreeSpinsStopData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { IReelSet, IReelsConfig, IReelStripConfig } from 'slotngf/src/core/config/IReelsConfig';
import { SlotEvent } from 'slotngf/src/core/SlotEvent';


export class ReelsController extends SlotCompController {

	public static readonly SHOW_WIN_LINE_NOTIFICATION: string = 'reelsShowWinLineNotification';
	public static readonly ANTICIPATE_REEL_NOTIFICATION: string = 'anticipateReel';
	public static readonly ANTICIPATE_END_NOTIFICATION: string = 'anticipateEnd';

	protected reelBackgroundConfig: IGameElement;
	protected reelSet: IReelSet;

	protected componentConfig: IReelsConfig;

	protected reels: Array<IReelStripModel>;

	private _currentReelSetIndex: number = ReelSet.BASE;

	protected get currentReelSetIndex (): number {
		return this._currentReelSetIndex;
	}

	protected set currentReelSetIndex ( value: number ) {
		this._currentReelSetIndex = value;
	}

	protected get currentReelSet (): Array<IReelStripConfig> {
		return this.reelSet[ this.currentReelSetIndex ];
	}

	/**
	 * Array with reels id that are active.
	 * Active means that we can spin it and od some actions.
	 * It will be used for respin feature. When you can change the active strips id to just one reel.
	 */
	protected activeReelsID: Array<number>;

	/**
	 * Index of activeStripsID array
	 */
	protected activeReelsIndex: number;

	/**
	 * Describes how many spins right now are spinning
	 */
	protected currentSpinningReelsCounter: number;

	/**
 	* Describes how many spins right now have not been requested to stop
 	*/
	protected stopRequestCounter: number;

	/**
	 * Delay for playing idle animation loop
	 */
	protected idleTimer: SimpleTimer;

	// TODO: Identify difference between idleTimer and idleRandomTimer
	protected idleRandomTimer: SimpleTimer;

	/**
	 * Delay timer for normal when spin is over
	 */
	protected normalTimer: SimpleTimer;

	protected nextReelStopTimer: SimpleTimer;

	/**
	 * Controller visual part of reels
	 */
	protected view: ReelsControllerView;

	/**
	 * will be true when the player does an action for skip spinning
	 */
	protected isFastStopPending: boolean;

	protected fastStopTimeout: boolean;

	protected scattersDroped: Array<number>;

	protected isAnticipating: boolean = false;

	private isFeatureReelsInited: boolean = false;

	private initData: IInitData = null;

	private isShaking: boolean = false;

	constructor ( @Inject view: ReelsControllerView ) {
		super( view );
	}

	public addEventListeners (): void {
		GlobalDispatcher.addListener( this, ReelsController.SHOW_WIN_LINE_NOTIFICATION, this.onShowWinLine );
	}

	public init ( data: IInitData ): void {
		this.initData = data;
		this.initReelsConfig();
		this.initViewController();
		this.initReelsModel( data.strips, data.idleSymbols );
		this.finishStateAction();
	}

	protected initReelsConfig (): void {
		this.componentConfig = this.gameConfig.getConfig<IReelsConfig>( ReelsConfig );
		this.reelSet = this.componentConfig.reelSet;
		this.reels = [];

		this.componentConfig.avaliableReelSet.forEach( ( index ) => {
			const reelSet: Array<IReelStripConfig> = this.reelSet[ index ];
			const reels = [];
			reelSet.forEach( ( reel ) => {
				const base = Helper.mergeTwoObjects( {}, this.componentConfig.reelDefinition );

				reels.push( Helper.mergeTwoObjects( base, reel ) );
			} );
			this.reelSet[ index ] = reels;
			this.reels = this.reels.concat( reels );
		} );

		this.reelBackgroundConfig = this.componentConfig.reelBackground;
	}

	public baseGameIdle (): void {
		this.view.deleteAllAnimations();
		this.view.setSymbolsStateForAll( SymbolState.STATIC );
		this.idleTimer = new SimpleTimer( this.componentConfig.idleAnimationDelay, this.playIdleAnimations.bind( this ) );
		this.finishStateAction();
	}

	public startSpin (): void {
		this.resetReels();
		this.activeReelsID = this.getActiveReelsID();
		this.startReels( this.activeReelsID );
		this.finishStateAction();
	}

	public pickFeature (): void {
		this.view.visible = false;
		this.finishStateAction();
	}

	public transitionToFreeSpins (): void {
		let lastSpin = this.reels;
		this.setReelSet( ReelSet.FEATURE );
		if ( !this.isFeatureReelsInited ) {
			this.initReelsModel( this.initData.strips, this.initData.idleSymbols );
			this.isFeatureReelsInited = true;
		}
		this.setSymbolsToTransition( lastSpin );
		this.view.positionUpdateOnTransition();
		this.view.visible = true;
		this.finishStateAction();
	}

	public transitionToBaseGame (): void {
		let lastSpin = this.reels;
		this.setReelSet( ReelSet.BASE );
		this.view.setSymbolsStateForAll( SymbolState.STATIC );
		this.setSymbolsToTransition( lastSpin );
		this.view.positionUpdateOnTransition();
		this.view.visible = true;
		this.finishStateAction();
	}

	public setSymbolsToTransition ( reels: Array<IReelStripModel> ): void {
		reels.forEach( ( reelStrip, reelIndex ) => {
			reelStrip.symbolsOutcome.forEach( ( symbol, symbolIndex ) => {
				//copy reels (from base to free spins)/(free spins to base) during scren transition
				this.view.reelSetSymbolName( reelIndex, symbolIndex + 1, symbol, SymbolState.STATIC );
			} );
		} );
	}

	//TODO
	public freeRespinReels ( data: any ): void {
		this.resetReels();
		GlobalDispatcher.dispatchIntoExpectedState( SlotEvent.RESPIN_REELS, this.stateNames.freeRespinReels );
		this.finishStateAction();
	}

	public freeSpinsIntroPopup (): void {
		this.view.setSymbolsStateForAll( SymbolState.STATIC );
		this.finishStateAction();
	}

	public freeSpinsStart (): void {
		this.startSpin();
	}

	public spinning (): void {
		this.normalTimer = new SimpleTimer( this.componentConfig.reelStopNormalTime, this.onSpinTimeout.bind( this ) );
	}

	/**
	 * player clicked spin for skip spinning animation
	 */
	public skip_spinning (): void {
		this.normalTimer.stop();
		this.isFastStopPending = true;
		this.finishStateAction();
	}

	public freeSpinning (): void {
		this.spinning();
	}

	public skip_freeSpinning (): void {
		this.skip_spinning();
	}

	public spinStop ( stateData: ISpinStopData ): void {
		for ( var i: number = 0; i < this.reels.length; i++ ) {
			if ( stateData && stateData.reelIndexes ) {
				this.reels[ i ].symbolsOutcome = stateData.reelIndexes[ i ];
			}
		}
		this.activeReelsIndex = 0;
		if ( this.isFastStopPending ) {
			this.fastStopReels();
		} else {
			const delay = ( this.currentReelSet[ 0 ].stopReelNormalDelay ) ? this.currentReelSet[ 0 ].stopReelNormalDelay : 0;
			this.nextReelStopTimer = new SimpleTimer( delay, () => {
				this.stopNextStrip();
			} );
		}
	}

	/**
	 * player clicked spin for skip stop animations
	 */
	public skip_spinStop (): void {
		if ( !this.isFastStopPending ) {
			this.isFastStopPending = true;
			if ( !this.fastStopTimeout ) {
				if ( this.nextReelStopTimer != null ) {
					this.nextReelStopTimer.stop();
				}
				this.fastStopReels();
			}
		}
	}

	public freeSpinsStop ( stateData: IFreeSpinsStopData ): void {
		this.spinStop( stateData );
	}

	public skip_freeSpinsStop (): void {
		this.skip_spinStop();
	}

	public freeRespinsStop ( stateData: any ): void {
		this.freeSpinsStop( stateData );
	}

	public skip_freeRepinsStop (): void {
		this.skip_freeSpinsStop();
	}

	protected initViewController (): void {
		this.view.idleAnimationsCompleteSignal.add( this.idleAnimationsComplete, this );

		this.view.initialize( this.componentConfig );
	}

	/**
	 * Initial reels model from init state.
	 * If idleSymbols didn't exist, make one from reel strips.
	 * @param strips
	 * @param idleSymbols
	 */
	protected initReelsModel ( strips: Array<Array<string>>, idleSymbols: Array<Array<string>> ): void {
		this.reels = new Array<IReelStripModel>();
		this.activeReelsID = this.getActiveReelsID();
		this.activeReelsIndex = 0;

		let stripIndex = 0;

		this.currentReelSet.forEach( ( stripConfig, index ) => {
			this.view.reelInitView( index, stripConfig );
			this.view.reelInitOverView( index, stripConfig );
			var reelModelStrip: IReelStripModel = this.createReelStripModel();
			reelModelStrip.setConfig( stripConfig, index );

			this.addModelListeners( reelModelStrip );

			if ( strips == undefined || strips.length == 0 ) {
				reelModelStrip.allStripSymbols = undefined;
				stripIndex = index % this.activeReelsID.length;
			} else {
				stripIndex = index % strips.length;
				reelModelStrip.allStripSymbols = strips[ stripIndex ];
			}

			// if last spin didn't exist
			if ( idleSymbols == undefined || idleSymbols.length == 0 ) {
				reelModelStrip.symbolsOutcome = this.getIdleSymbolFromReelStrip( reelModelStrip.allStripSymbols );
			} else {
				reelModelStrip.symbolsOutcome = idleSymbols[ stripIndex ];
			}
			reelModelStrip.initReel();
			this.reels.push( reelModelStrip );
		} );

		this.setReelSet( this.currentReelSetIndex );
	}

	/**
	 * Get idleSymbols from reel strips
	 * @param strips
	 */
	protected getIdleSymbolFromReelStrip ( strip: Array<string> ): Array<string> {
		let idleSymbols = new Array<string>();

		const sample = this.currentReelSet[ 0 ];
		for ( let i: number = 0; i < sample.symbolsOnReel; i++ ) {
			idleSymbols.push( strip[ i + 1 ] );
		}

		return idleSymbols;
	}

	protected addModelListeners ( reelModel: IReelStripModel ): void {
		reelModel.reelStopedSignal.add( this.onReelStoped, this );
		reelModel.stopRequestedSignal.add( this.onStopRequested, this );
		reelModel.hideBlurSymbolSignal.add( this.onHideBlurSymbolRequested, this );
		reelModel.dropAnimationSignal.add( this.onSymbolDropAnimation, this );
		reelModel.setSymbolNameSignal.add( this.onSetSymbolNameToView, this );
		reelModel.spinTweenUpdateSignal.add( this.onTweenUpdateSymbols, this );
		reelModel.moveLastSymbolToTopSignal.add( this.onMoveLastSymbolToTop, this );
	}

	protected createReelStripModel (): IReelStripModel {
		return Container.get( ReelStripModel );
	}

	protected playIdleAnimations (): void {
		if ( this.componentConfig.idleAnimationLoop ) {
			this.view.playIdleAnimationsLoop();
		} else {
			this.idleRandomTimer = new SimpleTimer( this.componentConfig.idleAnimationRandomChance.time, this.view.playRandomIdleAnimations.bind( this.view ), true );
		}
	}

	protected resetReels (): void {
		this.isFastStopPending = false;
		this.fastStopTimeout = false;
		this.view.deleteAllAnimations();
		this.view.setSymbolsStateForAll( SymbolState.STATIC );
		if ( this.idleTimer != null ) {
			this.idleTimer.stop();
		}
		if ( this.idleRandomTimer != null ) {
			this.idleRandomTimer.stop();
		}
		if ( this.normalTimer != null ) {
			this.normalTimer.stop();
		}
	}

	protected getActiveReelsID (): Array<number> {
		let activeReelsID = new Array<number>();

		this.currentReelSet.forEach( ( stripConfig: IReelStripConfig, index: number ) => {
			activeReelsID.push( index );
		} );

		return activeReelsID;
	}

	protected setReelSet ( set: number ): void {
		this.view.setActiveReelStripSet( set );
		this.currentReelSetIndex = set;
	}

	protected startReels ( reelsId: Array<number> ): void {
		this.stopRequestCounter = this.currentSpinningReelsCounter = reelsId.length;
		this.soundManager.play( `${ SlotSoundConfig.reelSpin }0${ NMath.randomInt( 1, 5 ) }`, AudioTrackName.EFFECT );

		for ( var i = 0; i < this.currentSpinningReelsCounter; i++ ) {
			this.reels[ reelsId[ i ] ].startSpinning();
		}
	}

	/**
	 * checks how many remaining spinning reels
	 * if all reels stopped then finishStateAction
	 * Before the last reel spinning, the user has time to press the stopSpin button
	 * @param {number} reelId of the stopped reel
	 */
	protected onReelStoped ( reelId: number ): void {
		this.reelStoppedAction( reelId );

		this.playSound( SlotSoundConfig.reelStopped, reelId );
		if ( this.reels[ reelId ].symbolsOutcome.indexOf( SymbolSet.SCATTER ) !== -1 ) {
			this.playSound( SlotSoundConfig.scatter, reelId );
		}
	}

	protected playSound ( soundType: string, reelId: number ): void {
		switch ( soundType ) {
			case SlotSoundConfig.reelStopped:
				this.soundManager.play( `${ SlotSoundConfig.reelStopped }0${ reelId + 1 }`, AudioTrackName.EFFECT );
				break;
			case SlotSoundConfig.scatter:
				this.soundManager.play( `${ SlotSoundConfig.scatter }0${ reelId + 1 }`, AudioTrackName.EFFECT );
				break;
		}
	}

	protected reelStoppedAction ( reelId: number ): void {
		this.currentSpinningReelsCounter--;

		if ( this.currentSpinningReelsCounter == 0 ) {
			this.stopAnticipate( reelId );
			this.finishStateAction();
		} else {
			if ( !this.isFastStopPending && this.isAnticipationReel( reelId + 1, false ) ) {
				GlobalDispatcher.dispatchIntoExpectedStates( ReelsController.ANTICIPATE_REEL_NOTIFICATION,
					[
						this.stateNames.spinStop,
						this.stateNames.freeSpinsStop,
						this.stateNames.freeRespinsStop
					],
					reelId,
				);

				this.startShaking( reelId );
				this.view.anticipateReel( reelId + 1 );
				this.view.anticipateScatters( this.scattersDroped );
				this.isAnticipating = true;
			} else {
				this.stopAnticipate( reelId );
			}
			if ( this.currentSpinningReelsCounter == 1 ) {
				if ( !this.isFastStopPending ) {
					this.fastStopTimeout = true;
				}
			}
		}
	}

	protected stopAnticipate ( reelId: number ): void {
		if ( this.isAnticipating ) {
			this.isAnticipating = false;
			GlobalDispatcher.dispatchIntoExpectedStates( ReelsController.ANTICIPATE_END_NOTIFICATION,
				[
					this.stateNames.spinStop,
					this.stateNames.freeSpinsStop,
					this.stateNames.freeRespinsStop
				],
				reelId
			);
			this.stopShaking();
			this.view.stopAnticipate();
		}
	}

	protected onStopRequested ( reelId: number ): void {
		this.stopRequestCounter--;
		if ( !this.isFastStopPending ) {
			if ( this.activeReelsIndex < this.activeReelsID.length - 1 ) {
				this.activeReelsIndex++;
				let stripConfigIndex = this.activeReelsID[ this.activeReelsIndex ];
				let anticipationDelay: number = this.isAnticipationReel( reelId + 1, true ) ? this.componentConfig.anticipationData.anticipationDelay : 0;
				this.nextReelStopTimer = new SimpleTimer(
					this.currentReelSet[ stripConfigIndex ].stopReelNormalDelay + anticipationDelay,
					this.stopNextStrip.bind( this ) );
			}
		}
	}

	private startShaking ( reelId ): void {
		if ( !this.isShaking ) {
			// frist shaking, ths before reels , need together shake.
			for ( let i = 0; i <= reelId; i++ ) {
				this.isShaking = this.view.playShaking( i );
			}
		} else {
			this.view.playShaking( reelId );
		}
	}

	private stopShaking (): void {
		this.isShaking = false;
		this.view.stopShaking();
	}

	protected isAnticipationReel ( reelId: number, preStop: boolean ): boolean {
		let scatterCount: number = 0;
		this.scattersDroped = [];
		for ( let column = 0; column < reelId; column++ ) {
			this.scattersDroped.push( -1 );
			for ( let row = 0; row < this.reels[ column ].symbolsOutcome.length; row++ ) {
				if ( this.reels[ column ].symbolsOutcome[ row ] == SymbolSet.SCATTER ) {
					scatterCount++;
					this.scattersDroped[ column ] = row + 1;
				}
			}
		}

		let symbolCountTrigger = this.componentConfig.anticipationData.modelData[ this.currentReelSetIndex ].anticipationSymbolCountTrigger;
		return ( !this.isFastStopPending && scatterCount >= symbolCountTrigger && this.isFeaturePossible( scatterCount, preStop ) );
	}

	protected isFeaturePossible ( scatterCount: number, preStop: boolean ): boolean {
		let symbolCountRequired = this.componentConfig.anticipationData.modelData[ this.currentReelSetIndex ].anticipationSymbolCountRequired;
		return symbolCountRequired - scatterCount <= ( preStop ? this.stopRequestCounter : this.currentSpinningReelsCounter );
	}

	protected fastStopReels (): void {
		for ( let i: number = 0; i < this.activeReelsID.length; i++ ) {
			if ( this.activeReelsIndex < this.activeReelsID.length ) {
				this.reels[ this.activeReelsID[ this.activeReelsIndex ] ].requestStop();
				if ( this.activeReelsIndex < this.activeReelsID.length - 1 ) {
					this.activeReelsIndex++;
				} else {
					break;
				}
			}
		}
	}

	protected onShowWinLine ( e: GlobalWinData ): void {
		let winLine: WinLineDisplayData = e.data;
		this.view.setSymbolStateFromPositions( winLine.winTable, SymbolState.STATIC, SymbolState.LOSE );
		this.view.showWinLine( winLine );
	}

	protected stopNextStrip (): void {
		this.reels[ this.activeReelsID[ this.activeReelsIndex ] ].requestStop();
	}

	/**
	 * time for normal spin is over
	 */
	protected onSpinTimeout (): void {
		this.finishStateAction();
	}

	protected onSetSymbolNameToView ( reelId: number, itemId: number,
		symbolName: string, state: SymbolState ): void {
		this.view.reelSetSymbolName( reelId, itemId, symbolName, state );
	}

	protected onHideBlurSymbolRequested ( reelId: number, symbolIndex: number, state: SymbolState ): void {
		this.view.reelSetSymbolState( reelId, symbolIndex, state );
	}

	protected onMoveLastSymbolToTop ( reelId: number, symbolData: SymbolData, state: SymbolState ): void {
		this.view.moveLastSymbolToTop( reelId, symbolData, state );
	}

	protected onTweenUpdateSymbols ( reelId: number, symbolId: number, y: number ): void {
		this.view.tweenSymbolsUpdate( reelId, symbolId, y );
	}

	protected onSymbolDropAnimation ( reelId: number, symbolId: number ): void {
		this.view.symbolDropAnimation( reelId, symbolId );
	}

	protected idleAnimationsComplete (): void {
		if ( this.componentConfig.idleAnimationLoop ) {
			this.idleTimer = new SimpleTimer( this.componentConfig.idleAnimationDelay, this.playIdleAnimations.bind( this ) );
		}
	}
}

export interface GlobalWinData {
	type: string;
	data: WinLineDisplayData;
}


