import { TsMap } from 'corengf/src/utils/TsMap';
import { Container, Inject } from 'typescript-ioc/es5';
import { AbstractApi } from 'corengf/src/server/abstract/AbstractApi';
import { MessageDirection } from 'corengf/src/selenium/SeleniumPortal';
import { NGWebSocketServer } from 'corengf/src/server/NGWebSocketServer';
import { ITransport } from 'corengf/src/server/abstract/transport/ITransport';
import { INGServerConfig } from 'corengf/src/server/ng/config/INGServerConfig';
import { IServerConfig } from 'corengf/src/server/abstract/config/IServerConfig';
import { IIncommingPackage } from 'corengf/src/server/ng/data/NGHttpDataDefinitions';
import { AbstractFormatter } from 'corengf/src/server/abstract/formatter/AbstractFormatter';
import { SlotSeleniumHistoryMessage, SlotSeleniumPortal } from 'slotngf/src/selenium/SlotSeleniumPortal';
import { SlotStates } from 'slotngf/src/states/SlotStates';
import { SlotNGServerConfig } from 'slotngf/src/server/ng/config/SlotNGServerConfig';
import { SlotNGHttpFormatter } from 'slotngf/src/server/ng/formatter/SlotNGHttpFormatter';
import { SlotNGHttpTransport } from 'slotngf/src/server/ng/transport/SlotNGHttpTransport';
import { ErrorMsgFormatter } from 'slotngf/src/server/ng/formatter/http/in/ErrorMsgFormatter';
import { Spin } from 'slotngf/src/server/ng/api/Spin';
import { GameInit } from 'slotngf/src/server/ng/api/GameInit';
import { ForceSpin } from 'slotngf/src/server/ng/api/ForceSpin';
import { HistoryDetail } from 'slotngf/src/server/ng/api/HistoryDetail';
import { HistorySummary } from 'slotngf/src/server/ng/api/HistorySummary';
import { RunPickFeature } from 'slotngf/src/server/ng/api/RunPickFeature';
import { SlotRngToolPortal } from 'slotngf/src/rngTool/SlotRngToolPortal';
import { RunJackpotFeature } from 'slotngf/src/server/ng/api/RunJackpotFeature';
import { RunFreeSpinFeature } from 'slotngf/src/server/ng/api/RunFreeSpinFeature';
import { SlotNGDataHookLogFormatter } from 'slotngf/src/server/ng/formatter/websocket/out/SlotNGDataHookLogFormatter';
import { FreeRespin } from 'slotngf/src/server/ng/api/FreeRespin';
import { ResultHistorySummary } from 'slotngf/src/server/ng/api/ResultHistorySummary';
import { ResultHistoryDetail } from 'slotngf/src/server/ng/api/ResultHistoryDetail';


export class SlotNGServer extends NGWebSocketServer {

	@Inject
	protected transport: SlotNGHttpTransport;

	@Inject
	protected stateNames: SlotStates;

	@Inject
	protected selenium: SlotSeleniumPortal;

	@Inject
	protected rngTool: SlotRngToolPortal;

	protected isSynchronized: boolean = true;

	/**
	 * init message from server can emerge twice, this prevents from duplicated call
	 */
	protected isInited: boolean = false;

	constructor () {
		super();
		this.errorHandleService.init( Container.get( ErrorMsgFormatter ) );
	}

	public setupTransport (): ITransport {
		return Container.get( SlotNGHttpTransport );
	}

	public obtainConfig (): IServerConfig {
		return this.gameConfig.getConfig<INGServerConfig>( SlotNGServerConfig );
	}

	protected getDataHookLogFormatter (): SlotNGDataHookLogFormatter {
		return Container.get( SlotNGDataHookLogFormatter );
	}

	protected getServerAPIs (): TsMap<string, AbstractApi> {
		let serverApis: TsMap<string, AbstractApi> = new TsMap<string, AbstractApi>();
		serverApis.set( SlotNGHttpFormatter.INIT, Container.get( GameInit ) );
		serverApis.set( SlotNGHttpFormatter.SPIN, Container.get( Spin ) );
		serverApis.set( SlotNGHttpFormatter.RUN_PICK_FEATURE, Container.get( RunPickFeature ) );
		serverApis.set( SlotNGHttpFormatter.RUN_FREE_SPIN_FEATURE, Container.get( RunFreeSpinFeature ) );
		serverApis.set( SlotNGHttpFormatter.HISTORY_SUMMARY, Container.get( HistorySummary ) );
		serverApis.set( SlotNGHttpFormatter.HISTORY_DETAIL, Container.get( HistoryDetail ) );
		serverApis.set( SlotNGHttpFormatter.RESULT_HISTORY_SUMMARY, Container.get( ResultHistorySummary ) );
		serverApis.set( SlotNGHttpFormatter.RESULT_HISTORY_DETAIL, Container.get( ResultHistoryDetail ) );
		serverApis.set( SlotNGHttpFormatter.FORCE_SPIN, Container.get( ForceSpin ) );
		serverApis.set( SlotNGHttpFormatter.RUN_JACKPOT_FEATURE, Container.get( RunJackpotFeature ) );
		serverApis.set( SlotNGHttpFormatter.FREE_RESPIN_REELS, Container.get( FreeRespin ) );
		return serverApis;
	}

	/**
	 * Opens connection and initiate login if required
	 */
	protected handshake (): void {
		this.selenium.setupSelenium( !this.transport.isUsingRealEnvironment() );
		this.transport.onConnect = this.onConnect.bind( this );

		this.apiMapping.get( SlotNGHttpFormatter.INIT ).send( SlotNGHttpFormatter.INIT ).then( ( data ) => {
			this.onInitResponse( data );
			this.transport.init( data.payload );
		} ).catch( ( data ) => {
			console.log( '[LOGIN_FAIL]', data );
		} );
	}

	public send ( messageType: string, data?: any ): Promise<any> {
		this.selenium.addToHistory( new SlotSeleniumHistoryMessage( messageType, MessageDirection.OUTBOUND ) );

		if ( messageType.indexOf( 'direct' ) === -1 ) {
			this.apiMapping.get( messageType ).send( messageType, data ).then( ( response ) => {
				this.onResponse( response );
			} );
			return null;
		} else {
			return this.apiMapping.get( messageType ).send( messageType, data );
		}
	}

	public onReceive ( data: IIncommingPackage ): void {
		super.onReceive( data );
		if ( data.name == AbstractFormatter.INIT ) {
			this.partnerCode = data.payload.state.partner_code;
			this.playerId = data.payload.state.player_id;
			this.engagements = data.payload.state.engagements;

			// check if rng tool need to be enable
			if ( data.payload.force_spin_enabled != undefined && data.payload.force_spin_enabled ) {
				this.rngTool.setupRngTool();
			}

			this.connectToNotificationServer();
		}
	}

	protected onResponse ( response: any ): void {
		this.selenium.addToHistory( new SlotSeleniumHistoryMessage( response ? response.name : '', MessageDirection.INBOUND ) );
		this.serverResponseSignal.dispatch( response );
	}

	public onInitResponse ( data: IIncommingPackage ): void {
		if ( this.isInited ) {
			console.log( '[ NGServer ]: received init response second time' );
			return;
		} else {
			this.isInited = true;
			this.serverResponseSignal.dispatch( data );
		}
	}

	public processState ( stateName: string, stateData: any ): void {
		this.selenium.setState( stateName );
		super.processState( stateName, stateData );
	}
}



