import { ReelConfig, ReelSet } from 'slotngf/src/components/reelV2/ReelConfig';
import { IMask, ReelMask, IReelLayoutData, IReelStripConfig, ISymbolDefinition, IReelSet } from 'slotngf/src/core/config/IReelsConfig';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Orientation } from 'corengf/src/display/Viewport';
import { Easing } from '@tweenjs/tween.js';

export class DHBReelConfig extends ReelConfig {

	public anticipationData = {
		anticipationDelay: 1500,
		animations: [],
		//Each Model needs anticipationData
		modelData: [
			{
				anticipationSymbolCountTrigger: 2,
				anticipationSymbolCountRequired: 3,
			},
			{
				anticipationSymbolCountTrigger: 6,
				anticipationSymbolCountRequired: 3,
			}
		]
	};

	public orientationLayoutData: TsMap<Orientation, IReelLayoutData> = new TsMap<Orientation, IReelLayoutData>( [
		[ Orientation.PORTRAIT,
		{
			position: { x: 10, y: 361 },
			scale: 0.94
		}
		],
		[ Orientation.LANDSCAPE,
		{
			position: { x: 266, y: 28 },
			scale: 1
		}
		]
	] );

	public defaultReelPosition: Array<IReelLayoutData> = [
		{ position: { x: 0, y: 0 } },
		{ position: { x: 150, y: 0 } },
		{ position: { x: 300, y: 0 } },
		{ position: { x: 450, y: 0 } },
		{ position: { x: 600, y: 0 } }
	];

	protected portraitMask: IMask = {
		x: 0,
		y: 358,
		width: 714,
		height: 508
	};

	protected landscapeMask: IMask = {
		x: 265,
		y: 30,
		width: 753,
		height: 537
	};


	public orientationMaskData: TsMap<number, TsMap<Orientation, ReelMask>> = new TsMap<number, TsMap<Orientation, ReelMask>>( [
		[ ReelSet.BASE, new TsMap<Orientation, ReelMask>( [
			[ Orientation.PORTRAIT, this.portraitMask ],
			[ Orientation.LANDSCAPE, this.landscapeMask ],
		] ) ],
		[ ReelSet.FEATURE, new TsMap<Orientation, ReelMask>( [
			[ Orientation.PORTRAIT, this.portraitMask ],
			[ Orientation.LANDSCAPE, this.landscapeMask ],
		] ) ],
	] );

	public symbolDefinition: ISymbolDefinition = {
		generalSymbol: {
			width: 150,
			height: 135
		},
		specialSymbol: {}
	};

	public reelDefinition: IReelStripConfig = {
		width: 150,
		symbol: {
			width: 150,
			height: 135
		},
		rangeSymbols: [ 'A', 'K', 'Q', 'J', 'T', 'PIC1', 'PIC2', 'PIC3', 'PIC4', 'PIC5', 'WILD', 'SCATTER' ],
		dropSymbols: [ 'WILD', 'SCATTER' ],
		symbolsOnReel: 4,
		reelTween: { duration: 50, ease: Easing.Linear.None },
		stopTween: { duration: 400, ease: Easing.Back.Out },
		startReelDelay: 0,
		stopReelNormalDelay: 200,
		countBottomExtraSymbol: 1,
		countTopExtraSymbol: 1
	};

	public reelSet: IReelSet = {
		[ ReelSet.BASE ]: [
			{
				width: 150,
				startReelDelay: 0,
				stopReelNormalDelay: 200
			},
			{
				startReelDelay: 100
			},
			{
				startReelDelay: 200
			},
			{
				startReelDelay: 300
			},
			{
				startReelDelay: 400
			}
		],
		[ ReelSet.FEATURE ]: [
			{
				width: 150,
				startReelDelay: 0,
				stopReelNormalDelay: 200
			},
			{
				startReelDelay: 100
			},
			{
				startReelDelay: 200
			},
			{
				startReelDelay: 300
			},
			{
				startReelDelay: 400
			}
		],
	};

}


