/**
 * Class that contains all actions and markers for actions that we use in game
 */
export class Actions {
	/**
	 * The marker that determines the beginning of action.
	 * It will help us to sort all types of actions.
	 */
	public static CLICK_ACTION_PREFIX: string = 'action_';
	public static EMPTY: string = 'empty';
	public static TOGGLE_SELECTED_MARKER: string = 'toggleSelected:';
	public static TOGGLE_UNSELECTED_MARKER: string = 'toggleUnselected:';
	public static SKIP_STATE: string = 'skipState';
	public static MENU: string = 'menu';
	public static HELP: string = 'help';
	public static SOUND_TOGGLE: string = 'soundToggle';
	public static ON_GAME_START: string = 'onGameStart';

	public static CLOSE_ERROR_DIALOG: string = 'closeErrorDialog';
	public static CLOSE_TUTORIAL: string = 'closeTutorial';
	public static ARROW_TUTORIAL: string = 'arrowTutorial';
}


