import { Scenario } from 'corengf/src/states/Scenario';
import { IWinningData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { IRotateFeatureDetail } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { IGameState, IFeatureInfo, ISpinResult } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { SpinResponseFormatter } from 'slotngf/src/server/ng/formatter/http/in/SpinResponseFormatter';
import { AbstractFeatureScenarioFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/AbstractFeatureScenarioFormatter';

/**
 * formatting rotate feature
 */
export class RotateFeatureFormatter extends SpinResponseFormatter implements AbstractFeatureScenarioFormatter {

	/**
	 * formatting run feature api response
	 * @param message
	 */
	public processIncomingMessage ( message: IGameState, featureInfo?: IFeatureInfo ): Scenario {
		console.warn( 'here shouldn\'t be triggered, since rotate feature don\'t need another transaction to complete feature' );
		return null;
	}

	/**
	 * formatting feature in feature_triggered
	 */
	public triggeredFeatureIncomingMessage ( message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return null;
	}

	public getFeatureScenarioBeforeFreeSpinStop ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return freeSpinStopScenario;
	}

	public getFeatureScenarioAfterSpinStop ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return freeSpinStopScenario;
	}

	public getFeatureShowWinningData ( winningData: IWinningData, spinResult: ISpinResult, featureInfo: IFeatureInfo ): IWinningData {
		const rotatesConfig: any = featureInfo.feature_state;

		winningData.balance -= rotatesConfig.total_rotate_wins;
		winningData.totalWin -= rotatesConfig.total_rotate_wins;

		return winningData;
	}

	public getFeatureScenarioAfterWin ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		freeSpinStopScenario.addScenario( this.getRotateReelScenario( message, featureInfo ) );
		return freeSpinStopScenario;
	}

	public getFeatureRestoreScenario ( restoreScenario: Scenario, featureInfo: IFeatureInfo ): Scenario {
		return restoreScenario;
	}

	protected getFeatureDetail ( featureInfo: any ): any {
		return featureInfo.feature_state;
	}

	/**
	 * this should be remove after rgs fix parameter in rotate feature
	 * scatter_wins should be scatter_win in package
	 * scatter_wins.position should be scatter_win.line_config
	 * @param featureDetail
	 * @param featureInfo
	 */
	protected resetScatterWin ( featureDetail: IRotateFeatureDetail, featureInfo: any ): IRotateFeatureDetail {
		for ( let i = 0; i < featureInfo.feature_state.rotates.length; i++ ) {
			if ( featureInfo.feature_state.rotates[ i ].scatter_wins ) {
				featureDetail.rotates[ i ].scatter_win = featureInfo.feature_state.rotates[ i ].scatter_wins;
				featureDetail.rotates[ i ].scatter_win.line_config = featureInfo.feature_state.rotates[ i ].scatter_wins.position;
			}
		}
		return featureDetail;
	}

	protected getRotateReelScenario ( message: IGameState, freeSpinRotateInfo: IFeatureInfo ): Scenario {
		let responseScenario: Scenario = null;
		let featureDetail: IRotateFeatureDetail = this.getFeatureDetail( freeSpinRotateInfo );
		featureDetail = this.resetScatterWin( featureDetail, freeSpinRotateInfo );

		let rotatesBalance = message.balance - featureDetail.total_rotate_wins;

		let featureTotalWin = message.feature_win - featureDetail.total_rotate_wins;

		for ( let i = 0; i < featureDetail.rotates.length; i++ ) {

			let rotateSymbols = new Array<Array<string>>();
			for ( let j: number = 0; j < featureDetail.rotates[ i ].reels.length; j++ ) {
				rotateSymbols[ j ] = new Array<string>();
				for ( let k: number = 0; k < featureDetail.rotates[ i ].reels[ j ].symbols.length; k++ ) {
					rotateSymbols[ j ].push( featureDetail.rotates[ i ].reels[ j ].symbols[ k ].symbol );
				}
			}

			if ( featureDetail.rotates[ i ].win_amount > 0 ) {
				let rotateWinScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.freeSpinsRotateReelWithWinning );
				rotatesBalance += featureDetail.rotates[ i ].win_amount;
				let winData: IWinningData = this.getShowWinningData( rotatesBalance, featureDetail.rotates[ i ], message.features, message.bet_value );

				if ( rotateWinScenario.hasStateName( this.stateNames.freeSpinsAllLineWin ) ) {
					rotateWinScenario.setStateData( this.stateNames.freeSpinsAllLineWin, winData );
				}
				if ( rotateWinScenario.hasStateName( this.stateNames.freeSpinsShowWinning ) ) {
					rotateWinScenario.setStateData( this.stateNames.freeSpinsShowWinning, winData );
				}

				if ( rotateWinScenario.hasStateName( this.stateNames.freeSpinsRotate ) ) {
					rotateWinScenario.setStateData( this.stateNames.freeSpinsRotate, { symbols: rotateSymbols, totalWin: featureTotalWin } );
				}

				if ( responseScenario != null ) {
					responseScenario.addScenario( rotateWinScenario );
				} else {
					responseScenario = rotateWinScenario;
				}

				featureTotalWin += featureDetail.rotates[ i ].win_amount;

			} else {
				let rotateScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.freeSpinsRotateReelNoWinning );

				if ( rotateScenario.hasStateName( this.stateNames.freeSpinsRotate ) ) {
					rotateScenario.setStateData( this.stateNames.freeSpinsRotate, { symbols: rotateSymbols, totalWin: featureTotalWin } );
				}

				if ( responseScenario != undefined ) {
					responseScenario.addScenario( rotateScenario );
				} else {
					responseScenario = rotateScenario;
				}
			}
		}

		let idleSymbols = new Array<Array<string>>();
		for ( let i: number = 0; i < message.spin_result.reels.length; i++ ) {
			idleSymbols[ i ] = new Array<string>();
			for ( let j: number = 0; j < message.spin_result.reels[ i ].symbols.length; j++ ) {
				idleSymbols[ i ].push( message.spin_result.reels[ i ].symbols[ j ].symbol );
			}
		}

		// rotate back to first place
		let backRotateScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.freeSpinsRotateReelNoWinning );
		if ( backRotateScenario.hasStateName( this.stateNames.freeSpinsRotate ) ) {
			backRotateScenario.setStateData( this.stateNames.freeSpinsRotate, { symbols: idleSymbols, totalWin: featureTotalWin } );
		}
		responseScenario.addScenario( backRotateScenario );

		return responseScenario;
	}

	/**
	 * formatting feature while restore feature
	 */
	public restoreFeatureIncomingMessage ( rotateFeatureDetail: IRotateFeatureDetail ): Scenario {
		let rotateFeatureFeatureScenario: Scenario = null;
		console.warn( 'didn\'t plan for restore rotate feature, since it won\'t create another transection' );
		return rotateFeatureFeatureScenario;
	}
}



