import { Animation } from 'corengf/src/elements/canvas/animation/Animation';
import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { BaseSymbol } from 'slotngf/src/components/reelV2/symbol/BaseSymbol';
import { SymbolAnimationPostfix } from 'slotngf/src/components/reels/ReelsEnums';

export class DHBBaseSymbol extends BaseSymbol {

	public setSymbolScale ( size: IPoint ): void {
		this.symbolLayer.scale.set( size.x, size.y );
	}

	// wild lock loop animation
	public playLoopAnimation ( type: string | SymbolAnimationPostfix ): boolean {
		this.symbolLayer.visible = true;

		this.itemAnimation = Animation.getAnimationFromPool( 'symbol_' + this.symbolName + type );

		this.symbolLayer.visible = false;
		this.itemAnimation.animationSpeed = this.itemAnimation.totalFrames / BaseSymbol.SYMBOL_ANIMATION_FRAME_COUNT;
		this.itemAnimation.anchor.set( 0.5 );

		const symbolSize = this.getSymbolSizeByName( this.symbolName );

		this.itemAnimation.x = symbolSize.width / 2;
		this.itemAnimation.y = symbolSize.height / 2;

		this.animationLayer.removeChildren();
		this.animationLayer.addChild( this.itemAnimation );

		this.itemAnimation.gotoAndStop( 0 );
		this.itemAnimation.loop = true;

		this.itemAnimation.gotoAndPlay( 0 );

		return true;
	}

}


