import { Container } from 'typescript-ioc/es5';
import { GameController } from 'corengf/src/core/GameController';
import { TsMap } from 'corengf/src/utils/TsMap';


export class EventParams {
	public type: string;
	public data: any;

	constructor ( type: string, data: any ) {
		this.type = type;
		this.data = data;
	}
}

export class EventMap {
	public context: any;
	public event: string;
	public callback: Function;

	constructor ( context: any, event: string, callback: Function ) {
		this.context = context;
		this.event = event;
		this.callback = callback;
	}

	public dispose (): void {
		this.callback = null;
	}
}

export interface IListener {
	addEventListeners (): void;
}

export class GlobalDispatcher {

	public static eventToHandlers: TsMap<string, Array<EventMap>> = new TsMap<string, Array<EventMap>>();

	public static addListener ( context: IListener, event: string, callback: Function ): void {
		let eventMap = new EventMap( context, event, callback );
		if ( this.eventToHandlers.has( event ) ) {
			let handler = this.eventToHandlers.get( event );
			handler.push( eventMap );
		} else {
			let handler = new Array<EventMap>();
			handler.push( eventMap );
			this.eventToHandlers.set( event, handler );
		}
	}

	public static removeListener ( event: string, callback: Function ): void {
		if ( this.eventToHandlers.has( event ) ) {
			let handlers: Array<EventMap> = this.eventToHandlers.get( event );
			handlers.forEach( ( handler: EventMap, i: number ) => {
				if ( handler.callback == callback ) {
					handlers.splice( i, 1 );
					handler.dispose();
					if ( handlers.length == 0 ) {
						this.eventToHandlers.delete( event );
					}
				}
				return;
			} );
		}
	}

	private static dispatch ( event: string, params: Object = null ): void {
		if ( this.eventToHandlers.has( event ) ) {
			let handlers: Array<EventMap> = this.eventToHandlers.get( event );
			handlers.forEach( ( handler: EventMap ) => {
				this.call( handler.context, handler.callback, new EventParams( event, params ) );
			} );
		} else {
			console.warn( 'this event:[' + event + '] no one is waiting' );
		}
	}

	private static call ( context: any, fn: Function, param: EventParams = undefined ): void {
		if ( param.data != undefined ) {
			fn.call( context, param );
		} else {
			if ( fn.length > 0 ) {
				fn.call( context, param );
			} else {
				fn.call( context );
			}
		}
	}

	public static removeListenersByEventName ( event: string ): void {
		if ( this.eventToHandlers.has( event ) ) {
			this.eventToHandlers.set( event, [] );
		}
	}

	public static removeAllListeners (): void {
		for ( var event in this.eventToHandlers ) {
			if ( event != undefined ) {
				let handlers: Array<EventMap> = this.eventToHandlers.get( event );
				handlers.forEach( ( handler: EventMap ) => {
					handler.dispose();
					this.eventToHandlers.delete( event );
				} );
			}
		}
	}

	/**
	 * dispatches event only if current state is same as expected
	 * @param {string} event to dispatch
	 * @param {string} expected state
	 * @param {Object} params
	 */
	public static dispatchIntoExpectedState ( event: string, state: string, params: Object = undefined ): void {
		const gameController = Container.get( GameController );
		if ( gameController.currentStateName == state ) {
			this.dispatch( event, params );
		} else {
			console.warn( 'this event:[' + event + '] do not dispatch because error in current state: [' +
				gameController.currentStateName + '] do not match with target state [' + state + ']' );
		}
	}

	/**
	 * dispatches event only if current state is one of expected
	 * @param {string} event
	 * @param {Array<string>} states
	 * @param {Object} params
	 */
	public static dispatchIntoExpectedStates ( event: string, states: Array<string>, params: Object = undefined ): void {
		if ( states.indexOf( Container.get( GameController ).currentStateName ) >= 0 ) {
			this.dispatch( event, params );
		} else {
			console.warn( 'this event:[' + event + '] do not dispatch because error in current state: [' +
				Container.get( GameController ).currentStateName + '] do not match with target states [' + states + ']' );
		}
	}

	/**
	 * dispatches event only if current state is one of expected
	 * @param {string} event
	 * @param {Array<string>} states
	 * @param {Object} params
	 */
	public static dispatchIntoAllStates ( event: string, params: Object = undefined ): void {
		this.dispatch( event, params );
	}
}


