import * as MiniSignal from 'mini-signals';
import { Singleton } from 'typescript-ioc/es5';
import { AbstractFormatter } from 'corengf/src/server/abstract/formatter/AbstractFormatter';


@Singleton
export class ErrorHandleService {

	public onReceiveErrorSignal: MiniSignal = new MiniSignal();


	private formatter: AbstractFormatter;

	public init ( formatter: AbstractFormatter ): void {
		this.formatter = formatter;
	}

	public onReceiveError ( e ): void {
		if ( !this.formatter ) {
			return;
		}

		let data: any = this.formatter.processIncomingMessage( e );

		if ( data ) {
			if ( data.stack ) {
				console.error( data.stack );
			}
		}

		this.onReceiveErrorSignal.dispatch( data );
	}
}


