import * as MiniSignal from 'mini-signals';
import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { TextField } from 'corengf/src/elements/canvas/text/TextField';
import { ICoreElementConfig } from 'corengf/src/elements/canvas/CoreElement';
import { IButtonConfig, Button } from 'corengf/src/elements/canvas/button/Button';
import { Window, IWindowConfig } from 'corengf/src/elements/canvas/window/Window';
import { InteractiveElement } from 'corengf/src/elements/canvas/InteractiveElement';
import { CoreUIElementFactory, ICoreUITextFieldElementConfig } from 'corengf/src/factory/CoreUIElementFactory';

/**
 *  ValueStepper
 *
 * @export
 * @class ValueStepper
 * @extends {InteractiveElement}
 */
export class ValueStepper extends InteractiveElement {

	/**
	 * Invoked every time when the value changed.
	 *
	 * @type {MiniSignal}
	 * @memberof ValuePicker
	 */
	public onIndexChanged: MiniSignal = new MiniSignal();

	protected config: IValueStepperConfig;

	protected titleText: TextField;

	protected valueText: TextField;

	protected prevButton: Button;

	protected nextButton: Button;

	protected mainBackground: Window;

	protected textBackground: Window;

	private _index = 0;

	set index ( index: number ) {
		if ( index !== this._index ) {
			this.onIndexChanged.dispatch( index );
		}
		this._index = index;
		this.updateValueText();
		this.updateButtonStatus();
	}

	get index (): number {
		return this._index;
	}

	public get currentValue (): string | number {
		return this._items[ this.index ];
	}

	private _items: Array<string | number> = [];

	get items (): Array<string | number> {
		return this.items;
	}

	protected initElement ( config: IValueStepperConfig ): void {
		this.config = config;

		if ( config.mainBoxBackground ) {
			const mainBackground = this.config.mainBoxBackground;
			this.mainBackground = new Window( config.mainBoxBackground );
			this.mainBackground.x = mainBackground.position.x || 0;
			this.mainBackground.y = mainBackground.position.y || 0;
			this.addChild( this.mainBackground );
		}

		if ( config.textBoxBackground ) {
			const textBackground = this.config.textBoxBackground;
			this.textBackground = new Window( config.textBoxBackground );
			this.textBackground.x = textBackground.position.x || 0;
			this.textBackground.y = textBackground.position.y || 0;
			this.addChild( this.textBackground );
		}

		if ( config.prevButton ) {
			this.prevButton = new Button( config.prevButton );
			this.prevButton.elementSignal.add( this.prev.bind( this ) );
			this.addChild( this.prevButton );
		}

		if ( config.nextButton ) {
			this.nextButton = new Button( this.config.nextButton );
			this.nextButton.elementSignal.add( this.next.bind( this ) );
			this.addChild( this.nextButton );
		}

		this.titleText = CoreUIElementFactory.createInstance( config.title );
		this.valueText = CoreUIElementFactory.createInstance( config.value );

		this.addChild( this.titleText );
		this.addChild( this.valueText );

		if ( config.items ) {
			this.setItems( config.items, config.defaultIndex );
		}
	}

	public updateAttributes ( config: IValueStepperConfig ): void {
		super.updateAttributes( config );
		if ( config == undefined ) {
			return;
		}

		if ( config.anchor ) {
			this.setAnchor( config.anchor );
		}

		if ( config.mainBoxBackground ) {
			this.mainBackground.updateAttributes( config.mainBoxBackground );
		}

		if ( config.textBoxBackground ) {
			this.textBackground.updateAttributes( config.textBoxBackground );
		}

		if ( config.title ) {
			this.titleText.updateAttributes( config.title.elementConfig );
		}

		if ( config.value ) {
			this.valueText.updateAttributes( config.value.elementConfig );
		}

		if ( config.nextButton ) {
			this.nextButton.updateAttributes( config.nextButton );
		}

		if ( config.prevButton ) {
			this.prevButton.updateAttributes( config.prevButton );
		}
	}

	protected setAnchor ( anchor: IPoint ): void {
		this.children.forEach( ( child: Button | TextField ) => {
			try {
				child.anchor = { x: anchor.x, y: anchor.y };
			} catch {
				//
			}
		} );
	}

	public getCurrentIndex (): number {
		return this.index;
	}

	public setItems ( _items: Array<string | number>, defaultIndex: number = 0 ): void {
		this._items = _items.slice( 0 );
		if ( this.valueText ) {
			this.index = defaultIndex;
		}
	}

	protected next (): void {
		if ( this.index < this._items.length - 1 ) {
			++this.index;
		}
	}

	protected prev (): void {
		if ( this.index > 0 ) {
			--this.index;
		}
	}

	/**
	 * Set to last item
	 *
	 * @memberof ValueStepper
	 */
	public last (): void {
		this.index = this.items.length - 1;
	}

	/**
	 * Set to first item
	 *
	 * @memberof ValueStepper
	 */
	public first (): void {
		this.index = 0;
	}

	protected setPrevButtonEnable ( enable: boolean ): void {
		if ( this.prevButton ) {
			this.prevButton.setEnable( enable );
		}
	}

	protected setNextButtonEnable ( enable: boolean ): void {
		if ( this.nextButton ) {
			this.nextButton.setEnable( enable );
		}
	}

	protected updateButtonStatus (): void {
		if ( this._items.length === 1 ) {
			this.setPrevButtonEnable( false );
			this.setNextButtonEnable( false );
			return;
		}

		if ( this._index === 0 && this.config.disableOnEnd ) {
			this.setPrevButtonEnable( false );
			this.setNextButtonEnable( true );
		} else if ( this._index === this._items.length - 1 && this.config.disableOnEnd ) {
			this.setNextButtonEnable( false );
			this.setPrevButtonEnable( true );
		} else {
			this.setPrevButtonEnable( true );
			this.setNextButtonEnable( true );
		}
	}

	protected updateValueText (): void {
		this.valueText.text = this.currentValue.toString();
	}

	public setPrevButtonVisible ( enable: boolean ): void {
		if ( this.prevButton !== undefined ) {
			this.prevButton.visible = enable;
		}
	}

	public setNextButtonVisible ( enable: boolean ): void {
		if ( this.nextButton !== undefined ) {
			this.nextButton.visible = enable;
		}
	}
}

export interface IValueStepperConfig extends ICoreElementConfig {

	/**
	 * The config of previous button
	 *
	 * @type {IButtonConfig}
	 * @memberof IValueStepperConfig
	 */
	prevButton?: IButtonConfig;

	/**
	 * The config of next button
	 *
	 * @type {IButtonConfig}
	 * @memberof IValueStepperConfig
	 */
	nextButton?: IButtonConfig;

	/**
	 * The text config of stepper's title
	 */
	title: ICoreUITextFieldElementConfig;

	/**
	 * The text config of the value
	 */
	value: ICoreUITextFieldElementConfig;

	textBoxBackground?: IWindowConfig;

	mainBoxBackground?: IWindowConfig;

	/**
	 * If disableOnEnd=true, disable prevButton/nextButton on first/last element
	 *
	 * @type {boolean}
	 * @memberof IValueStepperConfig
	 */
	disableOnEnd?: boolean;

	/**
	 * The value array
	 *
	 * @type {(Array<number|string>)}
	 * @memberof IValueStepperConfig
	 */
	items?: Array<number | string>;

	/**
	 * The default Index on given items.
	 * Defaults to 0.
	 *
	 * @type {number}
	 * @memberof IValueStepperConfig
	 */
	defaultIndex?: number;
}


