import { Container } from 'typescript-ioc/es5';
import { NGSlotApi } from 'slotngf/src/server/ng/api/NGSlotApi';
import { ISlotNGServerConfig } from 'slotngf/src/server/ng/config/ISlotNGServerConfig';
import { SlotNGHttpTransport } from 'slotngf/src/server/ng/transport/SlotNGHttpTransport';
import { IPackage, HTTP_METHODS } from 'corengf/src/server/abstract/transport/HttpTransport';
import { RunFeaturesMsgFormatter } from 'slotngf/src/server/ng/formatter/http/in/RunFeaturesMsgFormatter';
import { FreeSpinFeatureRequestMsgFormatter } from 'slotngf/src/server/ng/formatter/http/out/FreeSpinFeatureRequestMsgFormatter';

/**
 * run free spin api
 */
export class RunFreeSpinFeature extends NGSlotApi {

	public init ( transport: SlotNGHttpTransport, config: ISlotNGServerConfig ): void {
		super.init( transport, config );
		this.responseFormatter = Container.get( RunFeaturesMsgFormatter );
		this.requestFormatter = Container.get( FreeSpinFeatureRequestMsgFormatter );
	}

	protected encapsulation ( messageType: string ): IPackage {
		return {
			url: this.transport.url + this.apiPath.spin,
			name: messageType,
			method: HTTP_METHODS.POST,
			headers: this.transport.getHeaders(),
			content: this.request
		};
	}
}


