import { Asset } from 'corengf/src/core/AssetLoader';
import { Orientation } from 'corengf/src/display/Viewport';
import { SlotBackgroundView } from 'slotngf/src/components/background/SlotBackgroundView';
import { ITransitionToBaseGameData, ITransitionToFreeSpinsData } from 'slotngf/src/states/SlotStatesDataInterfaces';

export class DHBBackgroundView extends SlotBackgroundView {

	protected isFreeSpin: boolean = false;

	public transitionToBaseGame ( _data: ITransitionToBaseGameData ): void {
		this.isFreeSpin = false;
		if ( this.viewport.orientation === Orientation.PORTRAIT ) {
			this.playAnimation( 'portraitLayout_Transition_base' );
		} else if ( this.viewport.orientation === Orientation.LANDSCAPE ) {
			this.playAnimation( 'landscapeLayout_Transition_base' );
		}
	}

	public transitionToFreeSpins ( _data: ITransitionToFreeSpinsData ): void {
		this.isFreeSpin = true;
		if ( this.viewport.orientation === Orientation.PORTRAIT ) {
			this.playAnimation( 'portraitLayout_Transition_free' );
		} else if ( this.viewport.orientation === Orientation.LANDSCAPE ) {
			this.playAnimation( 'landscapeLayout_Transition_free' );
		}
	}

	public portraitLayout (): void {
		super.portraitLayout();
		this.playAnimation( 'portraitLayout' );
		if ( this.isFreeSpin ) {
			this.playAnimation( 'portraitLayout_free' );
		} else {
			this.playAnimation( 'portraitLayout_base' );
		}
	}

	public landscapeLayout (): void {
		super.landscapeLayout();
		this.playAnimation( 'landscapeLayout' );
		if ( this.isFreeSpin ) {
			this.playAnimation( 'landscapeLayout_free' );
		} else {
			this.playAnimation( 'landscapeLayout_base' );

		}
	}

	public getAssetsList (): Array<Asset> {
		let assetsList: Array<Asset> = super.getAssetsList();

		assetsList.push( ...[
			{
				assetKey: 'baseBackground',
				assetURL: 'assets/background/DHBG_Bg.json'
			},
			{
				assetKey: 'backgroundPetals',
				assetURL: 'assets/background/DHBG_BG_Animation.json'
			},
			{
				assetKey: 'logo',
				assetURL: 'assets/background/{locale}/Logo.png'
			},
			{
				assetKey: 'backgroundReels',
				assetURL: 'assets/background/DHBG_Reel.json'
			},
			{
				assetKey: 'backgroundMarquee',
				assetURL: 'assets/background/DHBG_Marquee.json'
			},
			{
				assetKey: 'pickgameLoop',
				assetURL: 'assets/sound/game/pick/Pick_Feature_Loop.mp3'
			}
		] );

		return assetsList;
	}

}


