import { WinLine, WinLineDisplayData } from 'slotngf/src/components/lines/WinLine';
import { WinLinesConfig } from 'slotngf/src/components/lines/WinLinesConfig';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import { MainConfig } from 'corengf/src/core/config/MainConfig';
import { SlotCompController } from 'slotngf/src/components/mvc/SlotCompController';
import { IMainConfig } from 'corengf/src/core/config/IMainConfig';
import { ReelsController } from 'slotngf/src/components/reels/logic/ReelsController';
import { IWinLinesConfig } from 'slotngf/src/core/config/IWinLinesConfig';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { IWinningData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { ReelsControllerView } from 'slotngf/src/components/reels/view/ReelsControllerView';
import { WinDisplayController } from 'slotngf/src/components/winDisplay/base/WinDisplayController';
import { BigWinController } from 'slotngf/src/components/bigwin/BigWinController';
import { BigWinTierType } from 'slotngf/src/core/config/IBigWinConfig';
import { FeatureTypes, IFreeSpinFeatureDetail } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { SymbolSet } from 'slotngf/src/components/reels/logic/ReelsConfig';


export class BaseWinLinesController extends SlotCompController {

	public static readonly SHOW_WIN_LINE_LOOP_COMPLETE: string = 'showWinLineLoopComplete';

	protected loopedShow: boolean;
	protected showComplete: boolean;
	protected currentWinLineIndex: number;
	protected showLinesStopped: boolean;
	protected winLines: Array<WinLine>;

	/**
	 * Delay for playing animation
	 */
	protected delayAnimationTimer: SimpleTimer;

	private config: IWinLinesConfig;
	protected mainConfig: IMainConfig;

	protected isInFreeGame: boolean;

	protected data: IWinningData;

	protected freeSpinLastspinDelay: number = 0;

	protected winTable: Array<Array<boolean>> = [];

	protected skipDisplay: boolean = false;

	public addEventListeners (): void {
		GlobalDispatcher.addListener( this, ReelsControllerView.ALL_SYMBOLS_ANIMATION_COMPLETE,
			this.onCombinationAnimateComplete );
		GlobalDispatcher.addListener( this, BaseWinLinesController.SHOW_WIN_LINE_LOOP_COMPLETE,
			this.showWinningLoopComplete );
	}

	public init (): void {
		this.initWinLinesConfig();
		this.finishStateAction();
		this.freeSpinLastspinDelay = this.config.freeSpinLastspinDelay;
	}

	protected baseGameIdle (): void {
		this.reset();
		this.finishStateAction();
	}

	protected freeSpinsIdle (): void {
		this.reset();
		this.finishStateAction();
	}

	protected startSpin (): void {
		this.reset();
		this.finishStateAction();
	}

	protected transitionToBaseGame (): void {
		this.isInFreeGame = false;
		this.reset();
		this.finishStateAction();
	}

	protected transitionToFreeSpins (): void {
		this.isInFreeGame = true;
		this.finishStateAction();
	}

	protected freeSpinsAllLineWin ( data: IWinningData ): void {
		this.skipDisplay = false;
		this.allLineWin( data );
	}

	protected skip_freeSpinsAllLineWin ( data: IWinningData ): void {
		this.skipDisplay = true;
		this.reset();
		this.finishStateAction();
	}

	protected skip_freeSpinsShowWinning ( data: IWinningData ): void {
		this.reset();
		this.finishStateAction();
	}

	protected freeRespinsAllLineWin ( data: IWinningData ): void {
		this.allLineWin( data );
	}

	protected allLineWin ( data: IWinningData ): void {
		if ( !data.winLines || data.winLines.length === 0 ) {
			this.finishStateAction();
			return;
		}

		this.data = data;

		this.showLinesStopped = false;
		this.showComplete = false;
		this.loopedShow = false;

		const winline: WinLine = {
			line: undefined,
			payout: data.totalWin,
			symbol: undefined,
			reelPositions: this.getReelPositionFromWinTable( data.winTable ),
			wildMultiplier: undefined,
			winningCombinations: undefined,
			rightToLeft: undefined
		};

		( winline as WinLineDisplayData ).winTable = data.winTable.slice( 0 );

		const winLines: Array<WinLine> = [ winline ];

		this.winLines = winLines;
		this.winTable = data.winTable.slice( 0 );
		this.showWinLine();
	}

	protected showWinning ( data: IWinningData ): void {
		if ( !data.winLines || data.winLines.length === 0 ) {
			this.finishStateAction();
			return;
		}

		this.data = data;

		this.winLines = data.winLines;
		this.showLinesStopped = false;
		this.showComplete = false;
		this.loopedShow = false;

		const finalBigWinTier = BigWinController.getBigWinTier( data.totalWin, data.totalBet );

		const showPaylineLabel = ( data.totalWin != 0 && finalBigWinTier === BigWinTierType.Balance );

		this.currentWinLineIndex = 0;
		this.setWinTable( data.winTable, data.winLines.slice( 0, 1 ) );

		this.winTable = data.winTable.slice( 0 );

		this.showWinLine( 0, showPaylineLabel );
	}

	protected skip_showWinning ( data: IWinningData ): void {
		this.reset();
		this.finishStateAction();
	}

	protected skip_allLineWin (): void {
		this.reset();
		this.finishStateAction();
	}

	public freeSpinsShowWinning ( data: IWinningData ): void {
		if ( this.skipDisplay ) {
			this.finishStateAction();
			return;
		}
		this.data = data;
		this.showWinning( data );
	}

	protected prefeatureWinning ( data: IWinningData ): void {
		this.allLineWin( data );
	}

	protected showWinningLoop ( data: IWinningData ): void {
		if ( !data.winLines || data.winLines.length === 0 ) {
			this.finishStateAction();
			return;
		}

		this.winLines = data.winLines;
		if ( this.delayAnimationTimer != null ) {
			this.delayAnimationTimer.stop();
			this.delayAnimationTimer = null;
		}
		this.showLinesStopped = false;
		this.showComplete = true;
		this.loopedShow = true;
		this.showWinLine();
	}

	protected skip_showWinningLoop ( data: IWinningData ): void {
		this.showWinningLoopComplete();
	}

	protected showWinningLoopComplete (): void {
		this.reset();
		this.finishStateAction();
	}

	protected reset (): void {
		this.winLines = [];
		this.showLinesStopped = true;
		this.loopedShow = false;
		this.showComplete = false;
		if ( this.delayAnimationTimer != null ) {
			this.delayAnimationTimer.stop();
			this.delayAnimationTimer = null;
		}
	}

	protected initWinLinesConfig (): void {
		this.mainConfig = this.gameConfig.getConfig<IMainConfig>( MainConfig );
		this.config = this.gameConfig.getConfig<IWinLinesConfig>( WinLinesConfig );
	}

	protected onCombinationAnimateComplete (): void {
		if ( !this.showLinesStopped ) {
			let deltaMS: number = this.loopedShow ? this.config.winLineLoopAnimationDelay : this.config.winLineAnimationDelay;

			if ( deltaMS === 0 ) {
				++this.currentWinLineIndex;

				this.showWinLine( this.currentWinLineIndex );
			} else {
				this.delayAnimationTimer = new SimpleTimer( deltaMS, ( () => {
					this.showWinLine( ++this.currentWinLineIndex );
				} )
				);
			}
		}
	}

	protected showWinLine ( id: number = 0, showPaylineLabel: boolean = true ): void {
		if ( this.showLinesStopped || this.winLines == null ) {
			return;
		}

		this.currentWinLineIndex = id;
		if ( this.currentWinLineIndex >= this.winLines.length ) {
			this.currentWinLineIndex = 0;
			this.loopedShow = true;
		}

		var winData: WinLineDisplayData = this.winLines[ this.currentWinLineIndex ] as WinLineDisplayData;

		this.setWinTable( this.winTable, this.winLines.slice( this.currentWinLineIndex, this.currentWinLineIndex + 1 ) );
		winData.winTable = this.winTable.slice( 0 );
		winData.currentState = this.currentStateName;

		if ( this.loopedShow ) {
			if ( this.showComplete ) {
				this.dispatchWinLine( winData, !this.loopedShow && showPaylineLabel, this.winLines.length, [ this.stateNames.showWinningLoop ] );
			} else {
				this.showComplete = true;
				if ( this.isInFreeGame && this.data.featureState && this.data.featureState.type === FeatureTypes.FREE_SPIN ) {
					if ( ( this.data.featureState.feature_state as IFreeSpinFeatureDetail ).free_spins_left === 0 ) {
						const timer = new SimpleTimer( this.freeSpinLastspinDelay, () => {
							this.finishStateAction();
							timer.destroy();
						} );
					} else {
						this.finishStateAction();
					}
				} else if ( this.isSpecialLine( this.winLines[ this.winLines.length - 1 ] ) ) {
					this.specialSymbolLineAction();
				} else {
					this.finishStateAction();
				}
			}
		} else if ( this.isAllLineWinState() && !this.config.showAllLineWin ) {
			this.finishStateAction();
		} else {
			this.dispatchWinLine(
				winData,
				!this.loopedShow && showPaylineLabel,
				this.winLines.length,
				[
					this.stateNames.freeRespinsAllLineWin,
					this.stateNames.freeRespinsShowWinning,
					this.stateNames.allLineWin,
					this.stateNames.showWinning,
					this.stateNames.freeSpinsShowWinning,
					this.stateNames.freeSpinsAllLineWin,
					this.stateNames.prefeatureWinning
				]
			);
		}
	}

	protected isAllLineWinState (): boolean {
		return ( this.currentStateName === this.stateNames.allLineWin
			|| this.currentStateName === this.stateNames.freeSpinsAllLineWin
			|| this.currentStateName === this.stateNames.freeRespinsAllLineWin );
	}

	protected specialSymbolLineAction (): void {
		const timer = new SimpleTimer( this.config.specialSymbolDelay, () => {
			this.finishStateAction();
			timer.destroy();
		} );
	}

	protected isSpecialLine ( winline: WinLine ): boolean {
		// TODO: need support for not only symbol. ex: win 3 symbols in one reel.
		return winline.symbol === SymbolSet.SCATTER;
	}

	protected dispatchWinLine ( winData: WinLineDisplayData, showPaylineLabel: boolean, totalWinLines: number, states: Array<string> ): void {
		GlobalDispatcher.dispatchIntoExpectedStates( ReelsController.SHOW_WIN_LINE_NOTIFICATION,
			states, winData );

		GlobalDispatcher.dispatchIntoExpectedStates( WinDisplayController.SHOW_WIN_LINE,
			states, {
			winData: winData,
			showPaylineLabel: showPaylineLabel,
			totalWinLines: totalWinLines
		} );
	}

	protected getReelPositionFromWinTable ( winTable: Array<Array<boolean>> ): Array<Array<number>> {
		const reelPosition: Array<Array<number>> = [];

		winTable.forEach( ( reel ) => {
			const reelInfo = [];
			reel.forEach( ( value, y ) => {
				if ( value ) {
					reelInfo.push( y );
				}
			} );
			reelPosition.push( reelInfo );
		} );

		return reelPosition;
	}

	protected setWinTable ( winTable: Array<Array<boolean>>, winData: Array<WinLine> ): void {
		winData.forEach( ( winline ) => {
			winline.reelPositions.forEach( ( winSymbolIndexArr, x ) => {
				winTable[ x ].forEach( ( _value, y ) => { winTable[ x ][ y ] = false; } );

				winSymbolIndexArr.forEach( ( index ) => {
					winTable[ x ][ index ] = true;
				} );
			} );
		} );
	}
}


