import { TsMap } from 'corengf/src/utils/TsMap';
import { Orientation } from 'corengf/src/display/Viewport';
import { ICompViewConfig } from 'corengf/src/core/config/ICompViewConfig';
import { ICoreElementLayoutConfig } from 'corengf/src/elements/canvas/CoreElement';
import { SlotActions } from 'slotngf/src/utils/SlotActions';
import { ITurboButtonConfig } from 'mlui/src/elements/button/TurboButton';
import { MLUIButtonId } from 'mlui/src/buttons/MLUIButtonId';
import { MLUIConfig } from 'mlui/src/config/IMLUIConfig';


export class MLUITurboConfig extends MLUIConfig implements IMLUITurboConfig {

	public name = 'turboPanel';
	public turboButton = {
		name: 'turboButton',
		isOrientationSupported: true,
		position: { x: 204, y: 1115 },
		anchor: { x: 0.5, y: 0.5 },
		action: SlotActions.TURBO_MODE,
		id: MLUIButtonId.BUTTON_TURBO_MODE,
		productLine: this.productLine,
		assetName: 'turbo',
		visible: true,
		isLocal: true,
		speedMode: [ 1, 3 ],
		speedTextures: [ '', '-Level2' ]
	};

	public coreElementOrientationConfig: TsMap<Orientation, TsMap<string, ICoreElementLayoutConfig>> = new TsMap( [
		[
			Orientation.PORTRAIT, new TsMap( [
				[
					this.turboButton.name, {
						position: this.turboButton.position,
						rectangleHitArea: {
							width: 75,
							height: 75
						}
					}
				]
			] )
		],
		[
			Orientation.LANDSCAPE, new TsMap( [
				[
					this.turboButton.name, {
						position: { x: 1204, y: 660 },
						rectangleHitArea: {
							width: 100,
							height: 100
						}
					}
				]
			] )
		]
	] );
}

export interface IMLUITurboConfig extends ICompViewConfig {
	turboButton: ITurboButtonConfig;
}


