import { TsMap } from 'corengf/src/utils/TsMap';
import { IMarqueeConfig, MarqueeConfig } from 'corengf/src/components/marquee/MarqueeConfig';


export class SlotMarqueeConfig extends MarqueeConfig implements ISlotMarqueeConfig {

	public stateToTextMap = new TsMap( [
		[ 'baseGameIdle', [ 'spinToPlay' ] ],
		[ 'spinning', [ 'goodLuck' ] ],
		[ 'freeSpinsIntroPopup', [ 'goodLuck' ] ]
	] );

	public marqueeLabelTotalWinKey = 'marqueeTotalWin';
	public marqueeLabelLineWinKey = 'marqueeLineWin';
	public marqueeLabelAllPaysWinKey = 'marqueeAllPaysWin';
	public marqueeLabelPrefeatureNoWinKey = 'marqueePrefeatureNoWin';
	public marqueeLabelFeatureTotalWinKey = 'marqueeFeatureTotalWin';
}

export interface ISlotMarqueeConfig extends IMarqueeConfig {
	stateToTextMap: TsMap<string, Array<string>>;
	marqueeLabelTotalWinKey: string;
	marqueeLabelLineWinKey: string;
	marqueeLabelAllPaysWinKey: string;
	marqueeLabelPrefeatureNoWinKey: string;
	marqueeLabelFeatureTotalWinKey: string;
}


