import { Singleton } from 'typescript-ioc/es5';
import { States } from 'corengf/src/states/States';

@Singleton
export class SlotStates extends States {

	// base game
	public readonly startSpin: string = 'startSpin';
	public readonly spinning: string = 'spinning';
	public readonly spinStop: string = 'spinStop';
	public readonly allReelStopped: string = 'allReelStopped';
	public readonly skip_spinning: string = 'skip_spinning';
	public readonly skip_spinStop: string = 'skip_spinStop';

	/**
	 * State that is responsible for showing winning on reels.
	 * Format of data:
	 * { winning: 150 }
	 */
	public readonly allLineWin: string = 'allLineWin';
	public readonly skip_allLineWin: string = 'skip_allLineWin';
	public readonly showWinning: string = 'showWinning';
	public readonly skip_showWinning: string = 'skip_showWinning';
	public readonly showWinningLoop: string = 'showWinningLoop';
	public readonly prefeatureWinning: string = 'prefeatureWinning';

	// transitions
	public readonly transitionToFreeSpins: string = 'transitionToFreeSpins';
	public readonly transitionToBaseGame: string = 'transitionToBaseGame';
	public readonly transitionToAutoSpin: string = 'transitionToAutoSpin';
	public readonly transitionToBonusSpins: string = 'transitionToBonusSpins';
	public readonly transitionBonusToBase: string = 'transitionBonusToBase';

	// free spins
	public readonly freeSpinsIdle: string = 'freeSpinsIdle';
	public readonly freeSpinsStart: string = 'freeSpinsStart';
	public readonly freeSpinsIntroPopup: string = 'freeSpinsIntroPopup';
	public readonly freeSpinsOutroPopup: string = 'freeSpinsOutroPopup';
	public readonly freeSpinning: string = 'freeSpinning';
	public readonly freeSpinsStop: string = 'freeSpinsStop';
	public readonly freeSpinsAllReelStopped: string = 'freeSpinsAllReelStopped';
	public readonly freeSpinsShowWinning: string = 'freeSpinsShowWinning';
	public readonly freeSpinsAllLineWin: string = 'freeSpinsAllLineWin';
	public readonly skip_freeSpinsAllLineWin: string = 'skip_freeSpinsAllLineWin';
	public readonly freeSpinsWildMultipliers: string = 'freeSpinsWildMultipliers';
	public readonly wildLockTriggered: string = 'wildLockTriggered';
	public readonly freeSpinsRotate: string = 'freeSpinsRotate';
	public readonly freeSpinsEnd: string = 'freeSpinsEnd';
	public readonly showRetrigger: string = 'showRetrigger';
	public readonly showBonusResult: string = 'showBonusResult';
	public readonly skip_freeSpinning: string = 'skip_freeSpinning';
	public readonly skip_freeSpinsStop: string = 'skip_freeSpinsStop';
	public readonly replacingSymbols: string = 'replacingSymbols';

	//free respin
	public readonly startFreeRespin: string = 'startFreeRespin';
	public readonly freeRespinReels: string = 'freeRespinReels';
	public readonly freeRespinning: string = 'freeRespinning';
	public readonly freeRespinsStop: string = 'freeRespinsStop';
	public readonly skip_freeRepinsStop: string = 'skip_freeRepinsStop';
	public readonly freeRespinsAllReelStopped: string = 'freeRespinsAllReelStopped';

	public readonly freeRespinsShowWinning: string = 'freeRespinsShowWinning';
	public readonly freeRespinsAllLineWin: string = 'freeRespinsAllLineWin';
	public readonly skip_freeRespinsAllLineWin: string = 'skip_freeRespinsAllLineWin';
	public readonly freeRespinFinish: string = 'freeRespinFinish';
	public readonly multiplierApplied: string = 'multiplierApplied';
	public readonly multiplierAdded: string = 'multiplierAdded';

	public readonly symbolCrawlTriggered: string = 'symbolCrawlTriggered';

	// Gamble
	/*
	public readonly showGamblePanel: string = 'showGamblePanel';
	public readonly gambleWin: string = 'gambleWin';
	public readonly gambleLoss: string = 'gambleLoss';
	public readonly showGambleCard: string = 'showGambleCard';
	public readonly showPaytables: string = 'showPaytables';
	public readonly hidePaytables: string = 'hidePaytables';
	public readonly openMenu: string = 'openMenu';
	public readonly closeMenu: string = 'closeMenu';
	public readonly realityCheck: string = 'realityCheck';
	public readonly winTaken: string = 'winTaken';
	*/

	// Pick feature
	public readonly startPickFeature: string = 'startPickFeature';
	public readonly pickFeature: string = 'pickFeature';
	public readonly endPickFeature: string = 'endPickFeature';

	// wheel fortune
	public readonly transitionToWheelFortune: string = 'transitionToWheelFortune';
	public readonly wheelFortuneStart: string = 'wheelFortuneStart';
	public readonly wheelFortunRun: string = 'wheelFortunRun';
	public readonly wheelFortunStop: string = 'wheelFortunStop';
	public readonly wheelFortunClose: string = 'wheelFortunClose';

	// Jackpot
	public readonly transitionToJackpot: string = 'transitionToJackpot';
	public readonly jackpotStart: string = 'jackpotStart';
	public readonly jackpotIdle: string = 'jackpotIdle';
	public readonly jackpotRun: string = 'jackpotRun';
	public readonly jackpotStop: string = 'jackpotStop';
	public readonly jackpotShowWinning: string = 'jackpotShowWinning';
	public readonly jackpotClose: string = 'jackpotClose';
}



