import { Inject, Container } from 'typescript-ioc/es5';
import { Orientation } from 'corengf/src/display/Viewport';
import { ICoreWebElementConfig } from 'corengf/src/elements/webpage/CoreWebElement';
import { IHelpScreenContainerConfig } from 'corengf/src/elements/webpage/WebpageElement';
import { ExternalModuleView } from 'corengf/src/components/externalModule/ExternalModuleView';
import { SlotGameType } from 'slotngf/src/core/SlotGameType';
import { IInitData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { MLUIExternalModuleController } from 'mlui/src/core/MLUIExternalModuleController';
import { MLUIPaytableConfig } from './MLUIPaytableConfig';
import { WebFactoryElementType } from 'corengf/src/factory/WebElementFactory';
import { IWebGeneralPagingConfig } from 'corengf/src/elements/webpage/paging/WebGeneralPagingElement';
import { PartnerHelper } from 'corengf/src/utils/PartnerHelper';

export class MLUIPaytableController extends MLUIExternalModuleController {

	protected config: MLUIPaytableConfig;

	@Inject
	protected partnerHelper: PartnerHelper;

	constructor ( @Inject view: ExternalModuleView ) {
		super( view );

		this.config = Container.get( MLUIPaytableConfig );
		this.productLine = this.config.productLine;
	}

	protected getPortraitHelpScreenContainerConfig (): any {
		return this.config.portraitLayoutConfig.containerConfig;
	}

	public init ( data: IInitData ): void {
		const loader = PIXI.loader;
		this.moduleConfig = {
			assets: this.convertAssetNames( loader, this.config.assetNames ),
			handleClose: this.onClose.bind( this ),
			handleOpen: this.onOpen.bind( this ),
			language: this.getLocalisationMap( loader ),
			soundKey: this.config.soundKey,
			dataHook: this.sendDataHook.bind( this ),
			playSound: this.playSound.bind( this ),
			style: this.getExternalModuleConfig( data )
		};
		super.init();
		this.view.onOrientationChangeSignal.add( this.onOrientationChanged, this );
	}

	protected onOrientationChanged ( orientation: Orientation ): void {
		if ( this.externalModule.GenesisPayTable ) {
			this.externalModule.GenesisPayTable.update( orientation );
		}
	}

	protected getExternalModuleConfig ( data: IInitData ): MLUIPaytableConfig {
		let portraitContainerConfig: IHelpScreenContainerConfig = this.getPortraitHelpScreenContainerConfig();
		let landscapeContainerConfig: IHelpScreenContainerConfig = this.getLandscapeContainerConfig();

		portraitContainerConfig.paragraphs = this.getPortraitParagraphsConfig( portraitContainerConfig.paragraphs, data );

		landscapeContainerConfig.paragraphs = this.getLandscapeParagraphsConfig( landscapeContainerConfig.paragraphs, data );

		return this.config;
	}

	protected getPortraitParagraphsConfig ( paragraphs: Array<ICoreWebElementConfig>, data: IInitData ): Array<ICoreWebElementConfig> {
		let gameTypeConfig: Array<ICoreWebElementConfig> = [];
		if ( data.gametype == SlotGameType.ALLPAYS ) {
			gameTypeConfig = this.config.getPortraitAllPaysElements();
		} else {
			gameTypeConfig = this.config.getPortraitLineGameElements();
		}

		if ( this.partnerHelper.partner !== 'GPK' ) {
			return paragraphs.concat( gameTypeConfig
				.concat( this.config.getPortraitCopyrightElements() )
				.concat( this.config.getPortraitVersionElements() ) );
		} else {
			return paragraphs.concat( gameTypeConfig
				.concat( this.config.getPortraitVersionElements() ) );
		}
	}

	protected getLandscapeParagraphsConfig ( paragraphs: Array<ICoreWebElementConfig>, data: IInitData ): Array<ICoreWebElementConfig> {

		// check paragraphs use pagging element
		let pagingElement: IWebGeneralPagingConfig<ICoreWebElementConfig>
			= paragraphs.find( e => e.type === WebFactoryElementType.WEB_GENERAL_PAGING_ELEMENT ) as IWebGeneralPagingConfig<ICoreWebElementConfig>;

		if ( pagingElement ) {

			if ( data.gametype == SlotGameType.ALLPAYS ) {
				pagingElement.pages = pagingElement.pages.concat( this.config.getLandscapeAllPaysElements() );
			} else {
				pagingElement.pages = pagingElement.pages.concat( this.config.getLandscapeLineGameElements() );
			}

			if ( this.partnerHelper.partner !== 'GPK' ) {
				pagingElement.pages = pagingElement.pages.concat( this.config.getLandscapeCopyrightElements() );
			}

			let length = pagingElement.pages.length;

			pagingElement.pages[ length - 1 ].config.push( this.config.getLandscapeVersionElements() );

		}

		return paragraphs;
	}

	protected onClose (): any {
		//
	}

	protected onOpen (): any {
		//
	}
}


