import { SpinRequestMsgFormatter } from 'slotngf/src/server/ng/formatter/http/out/SpinRequestMsgFormatter';
import { FeatureTypes } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';

export class FreeRespinReelsRequestMsgFormatter extends SpinRequestMsgFormatter {

	/**
	 * request free respin reels
	 * @param data
	 */
	public codeMessage ( data: any ): any {

		let requestData = {
			player_id: this.slotNGModel.playerId,
			session_token: this.slotNGModel.sessionId,
			partner_code: this.slotNGModel.partnerCode,
			game_code: this.config.gameCode,
			action: FeatureTypes.FREE_RESPIN_REELS,
			state_tag: this.slotNGModel.stateTag
		};


		requestData = this.mergeForceSpinData( requestData, data );

		return requestData;
	}

}


