import { Inject } from 'typescript-ioc/es5';
import { MoneyFormatter, NumberDisplayMode } from 'corengf/src/utils/MoneyFormatter';
import { SlotNGHttpFormatter } from 'slotngf/src/server/ng/formatter/SlotNGHttpFormatter';
import { IHistoryDetailData, IHistoryDetailResultLog, IHistoryDetailResult, IHistoryDetailScatterWin, IHistoryDetailResultReelWins } from 'slotngf/src/states/SlotStatesDataInterfaces';

/**
 * formatter for history detail api. (* reminder: RESULT_HISTORY_DETAIL same with HISTORY_DETAIL )
 * currently, only use by result panel. 2020/03/12
 */
export class HistoryDetailFormatter extends SlotNGHttpFormatter {

	@Inject
	protected moneyFormatter: MoneyFormatter;

	public processIncomingMessage ( message: IHistoryDetailData ): any {
		return this.getDetailLog( message );
	}

	protected getDetailLog ( data: IHistoryDetailData ): IHistoryDetailResult {
		let logs: Array<IHistoryDetailResultLog> = new Array<IHistoryDetailResultLog>();
		// sort by time
		data.spin_results.sort( ( a, b ) => {
			return ( new Date( a.transaction_time ) ).getTime() - ( new Date( b.transaction_time ) ).getTime();
		} );

		for ( let i = 0; i < data.spin_results.length; i++ ) {
			let sWin: any = {};
			if ( data.spin_results[ i ].scatter_win && data.spin_results[ i ].scatter_win.symbol ) {
				sWin = {
					symbol: data.spin_results[ i ].scatter_win.symbol,
					ofKind: data.spin_results[ i ].scatter_win.of_kind,
					lineConfig: data.spin_results[ i ].scatter_win.line_config,
					prize: this.moneyFormatter.formatMoney( data.spin_results[ i ].scatter_win.prize, NumberDisplayMode.VALUE_ONLY )
				};
			} else {
				sWin = null;
			}

			let rWins: any = [];
			if ( data.spin_results[ i ].reel_wins ) {
				data.spin_results[ i ].reel_wins.forEach( ( element ) => {
					let w: IHistoryDetailResultReelWins = {
						symbol: element.symbol,
						ofKind: element.of_kind,
						wildWin: element.wild_win,
						lineNumber: element.line_number,
						lineConfig: element.line_config,
						prize: this.moneyFormatter.formatMoney( element.prize, NumberDisplayMode.VALUE_ONLY )
					};
					rWins.push( w );
				} );
			}

			const log: IHistoryDetailResultLog = {
				transactionId: data.spin_results[ i ].transaction_id,
				totalBet: this.moneyFormatter.formatMoney( data.spin_results[ i ].total_bet, NumberDisplayMode.VALUE_ONLY ),
				betValue: this.moneyFormatter.formatMoney( data.spin_results[ i ].bet_value, NumberDisplayMode.VALUE_ONLY ),
				totalWin: this.moneyFormatter.formatMoney( data.spin_results[ i ].total_win, NumberDisplayMode.VALUE_ONLY ),
				balance: this.moneyFormatter.formatMoney( data.spin_results[ i ].balance, NumberDisplayMode.VALUE_ONLY ),
				reelWins: rWins,
				scatterWin: sWin,
				transactionType: data.spin_results[ i ].transaction_type,
				featuresTriggered: data.spin_results[ i ].features_triggered,
				features: data.spin_results[ i ].features
			};

			logs.push( log );
		}

		const result: IHistoryDetailResult = {
			roundId: data.round_id,
			spinResults: logs
		};

		return result;
	}
}



