import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { States } from 'corengf/src/states/States';
import { CompView } from 'corengf/src/mvc/CompView';
import { GameConfig } from 'corengf/src/core/GameConfig';
import { CoreState } from 'corengf/src/states/CoreState';
import { StateMachine } from 'corengf/src/core/StateMachine';
import { SoundManager } from 'corengf/src/audio/SoundManager';
import { IListener } from 'corengf/src/events/GlobalDispatcher';
import { Asset, AssetLoader } from 'corengf/src/core/AssetLoader';
import { ResourceConfig } from 'corengf/src/core/config/ResourceConfig';
import { IButtonConfig } from 'corengf/src/elements/canvas/button/Button';

/**
 * CompController is basic class for MVC components that NGF has.
 * The class is responsible for ALL business logic of component. It stores all data.
 * Controller and Model are combined together is this class.
 */
export class CompController implements IListener {

	@Inject
	protected gameConfig: GameConfig;

	@Inject
	protected stateNames: States;

	@Inject
	protected soundManager: SoundManager;

	@Inject
	protected assetLoader: AssetLoader;

	/**
	 * Unique id of component
	 */
	public componentID: string;

	protected view: CompView;

	protected currentStateName: string;

	protected prevStateName: string = null;

	public getCurrentStateName (): string {
		return this.currentStateName;
	}

	/**
	 * Determines if component finished all needed actions for state.
	 * For example, it can be playing of animation, disabling some actions, etc.
	 */
	public isStateActionCompleted: boolean;

	/**
	 * Signal to notify component manager that state action is finished.
	 */
	public finishStateActionSignal: MiniSignal;

	constructor ( view?: CompView ) {

		this.isStateActionCompleted = false;

		this.finishStateActionSignal = new MiniSignal();

		if ( view != undefined ) {
			this.view = view;
		}

		this.addAudio();
		this.addEventListeners();
	}

	public init ( stateData: any ): void {
		if ( this.view != undefined ) {
			this.view.initialize();
		}
		this.finishStateAction();
	}

	/**
	 * List of assets that will be loaded for this component.
	 * The format of assets should be in the next format:
	 * {
	 *			assetKey 	: 'symbols',
	 *			assetURL	: 'assets/animations/symbols.json'
	 *	}
	 * @return list of assets needed for this component
	 */
	public get loaderList (): Array<Asset> {
		var assetsList: Array<Asset> = new Array<Asset>();
		var config: Asset = this.getConfigAsset();

		if ( this.view != undefined ) {
			assetsList = this.view.getAssetsList();
			assetsList.forEach( ( asset ) => {
				asset.group = ResourceConfig.MAIN_GAME;
			} );
		}

		if ( config.assetKey != undefined ) {
			assetsList.push( config );
		}

		return assetsList;
	}

	public getConfigAsset (): Asset {
		return {};
	}

	public addEventListeners (): void {
		// Example of code:
		// GlobalDispatcher.addListener(this, BaseAutoPlayController.SET_AUTO_PLAY, this.onSomeMethod);
	}

	/**
	 * [Deprecated] Please add sound resource just like normal image file : View.getAssetsList()
	 */
	public addAudio (): void {
		//TODO
	}

	/**
	 * Method notifies subscribes that all required actions for current state has been finished.
	 */
	protected finishStateAction (): void {
		this.isStateActionCompleted = true;
		this.prevStateName = this.currentStateName;
		this.finishStateActionSignal.dispatch( this.componentID );
	}

	/**
	 * Set the state for component AND
	 * invoke the method with name of state for CONTROLLER.
	 *
	 * CONTROLLER handling of state is priority but if we do not have it it will be done in VIEW.
	 *
	 * If CONTROLLER does NOT have method "nameOfState()" we will mark the component
	 * as the one that is ready to go to the next state.
	 *
	 * @param state the state that we will set for component
	 */
	public setState ( state: CoreState ): boolean {
		this.currentStateName = state.stateName;
		if ( typeof this[ state.stateName ] === 'function' ) {
			this.isStateActionCompleted = false;
			this.invokeStateMethod( state );
			return true;
		} else {
			this.finishStateAction();
			return false;
		}
	}

	public skipState ( state: CoreState ): void {
		if ( typeof this[ StateMachine.PREFIX_SKIP + state.stateName ] === 'function' ) {
			this[ StateMachine.PREFIX_SKIP + state.stateName ]( state.stateData );
		}
	}

	/**
	 * Calls method in current controller that has same name as state
	 * @param {CoreState} state
	 */
	public invokeStateMethod ( state: CoreState ): void {
		if ( typeof this[ state.stateName ] === 'function' ) {
			this[ state.stateName ]( state.stateData );
		}
	}

	protected onResourceLoaded ( groupIdx: number ): void {
		// TODO
	}

	public onGroupProgress ( groupIdx: number, ratio: number ): void {
		//TODO
	}

	/**
	 * override for set bttton action callback
	 *
	 * @protected
	 * @param {IButtonConfig} buttonConfig
	 * @memberof CompController
	 */
	protected setButtonActionEventListeners ( buttonConfig: IButtonConfig ): void {
		/**
		 * exsample:
		 * ButtonsController.setAction( ... );
		 */
	}

	public onGroupCompleted ( groupIdx: number ): void {
		//
	}

	public onLandscapeViewLoaded (): void {
		//
	}

	public onPortraitViewLoaded (): void {
		//
	}
}

export type Component = {
	id: string,
	class: Function,
	group?: number
};



