import { TsMap } from 'corengf/src/utils/TsMap';

export class BaseButtonsStateSet {

	public static DEFAULT_TEMPLATE: string = 'default';

	protected buttonStates: TsMap<string, TsMap<string, BaseButtonBehaviour>>;

	/**
	 * to check if a state name contains in buttonStates
	 * @param stateName
	 */
	public has ( stateName: string ): boolean {
		return this.buttonStates.has( stateName );
	}

	/**
	 * get list of local button behaviours by state name
	 * @param stateName
	 */
	public get ( stateName: string ): TsMap<string, BaseButtonBehaviour> {
		return this.buttonStates.get( stateName );
	}

	constructor () {
		this.buttonStates = new TsMap<string, TsMap<string, BaseButtonBehaviour>>();
		this.initStates();
	}

	/**
	 * set states for the buttons
	 */
	protected initStates (): void {
		this.buttonStates.set( BaseButtonsStateSet.DEFAULT_TEMPLATE, this.default() );
	}

	protected default (): TsMap<string, BaseButtonBehaviour> {
		return new TsMap<string, BaseButtonBehaviour>();
	}

	protected init (): TsMap<string, BaseButtonBehaviour> {
		return new TsMap<string, BaseButtonBehaviour>();
	}
}

export class BaseButtonBehaviour {
	public enabled: boolean;
	public visible: boolean;

	public interactive: boolean;

	constructor ( enabled: boolean = true, visible: boolean = true, interactive: boolean = true ) {
		this.enabled = enabled;
		this.visible = visible;
		this.interactive = interactive;
	}
}


