import { Inject, Container } from 'typescript-ioc/es5';
import { NGSlotApi } from 'slotngf/src/server/ng/api/NGSlotApi';
import { UrlParameters } from 'corengf/src/utils/UrlParameters';
import { SlotNGModel } from 'slotngf/src/server/ng/model/SlotNGModel';
import { IPackage, HTTP_METHODS } from 'corengf/src/server/abstract/transport/HttpTransport';
import { SlotNGHttpTransport } from 'slotngf/src/server/ng/transport/SlotNGHttpTransport';
import { ISlotNGServerConfig } from 'slotngf/src/server/ng/config/ISlotNGServerConfig';
import { HistoryRequestMsgFormatter } from 'slotngf/src/server/ng/formatter/http/out/HistoryRequestMsgFormatter';
import { HistorySummaryFormatter } from 'slotngf/src/server/ng/formatter/http/in/HistorySummaryFormatter';

/**
 * history summary api for result panel
 */
export class ResultHistorySummary extends NGSlotApi {

	@Inject
	protected slotNGModel: SlotNGModel;

	public init ( transport: SlotNGHttpTransport, config: ISlotNGServerConfig ): void {
		super.init( transport, config );
		this.responseFormatter = Container.get( HistorySummaryFormatter );
		this.requestFormatter = Container.get( HistoryRequestMsgFormatter );
	}

	protected encapsulation ( messageType: string ): IPackage {
		const url = this.getURL( this.request );
		const content: any = this.request;
		content.game_code = this.config.gameCode;

		return {
			url: url,
			name: messageType,
			method: HTTP_METHODS.GET,
			headers: this.transport.getHeaders(),
			urlParameters: content
		};
	}

	protected getURL ( urlParam: any ): string {
		let url = this.config.protocol + this.config.historyApiDomain + this.config.path.historySummaries;
		url = url.replace( '{domain}', this.transport.getDynamicDomainName() );
		url = url.replace( '{partner_token}', UrlParameters.partnerToken );
		url = url.replace( '{partner_code}', this.slotNGModel.partnerCode );
		url = url.replace( '{player_id}', this.slotNGModel.playerId );

		return url;
	}
}


