import { Point } from 'pixi.js';
import { MLUIPanel } from './../../core/MLUIPanel';
import { IMLUISpinConfig } from './MLUISpinConfig';
import { SpinButton, SpinButtonState } from './../../elements/button/SpinButton';


export class MLUISpinPanel extends MLUIPanel {

	protected spinConfig: IMLUISpinConfig;
	protected spinButton: SpinButton;

	protected createElements ( config: IMLUISpinConfig ): void {
		this.spinConfig = config;

		this.spinButton = new SpinButton( config.spinButton, this.localButtonsController );
		this.spinButton.anchor = new Point( 0.5, 0.5 );
		this.addChild( this.spinButton );
	}

	public onPressEvent (): void {
		this.spinButton.onPressEvent();
	}

	public onSpinStart (): void {
		this.spinButton.onSpinStart();
	}

	public onSpinStop (): void {
		this.spinButton.onSpinStop();
	}

	public showSpinButton (): void {
		this.spinButton.switchIconState( SpinButtonState.SPIN );
	}

	public showStopSpinButton (): void {
		this.spinButton.switchIconState( SpinButtonState.STOP_SPIN );
	}

	public showStopReelButton (): void {
		this.spinButton.switchIconState( SpinButtonState.STOP_REELS );
	}

	public transitionToFreeSpins ( currentFeatureCount: number, totalFreeSpinCount: number ): void {
		this.spinButton.isFreeSpin = true;
		this.setSpinButtonAutoSpins( totalFreeSpinCount, currentFeatureCount );
	}

	public transitionToBaseGame (): void {
		this.spinButton.isFreeSpin = false;
		this.stopAutoSpins();
	}

	public setSpinButtonAutoSpins ( value: number, currentSpin: number ): void {
		this.spinButton.updateClickDataCounts( currentSpin, value );
		this.spinButton.setAutospins( currentSpin, value );
		this.spinButton.switchIconState( SpinButtonState.SPIN );
	}

	public updateClickData ( currentCount: number, total: number ): void {
		this.spinButton.updateClickDataCounts( currentCount, total );
	}

	public setSpinButtonFreeSpins ( currentCount: number, totalCount: number ): void {
		this.spinButton.setFreeSpinCount( currentCount, totalCount );
	}

	public checkAndStopAutoSpins (): void {
		this.spinButton.checkAndStopAutoSpins();
	}

	public stopAutoSpins (): void {
		this.spinButton.stopAutoSpins();
		this.spinButton.switchIconState( this.spinButton.getCurrentIconState() );
	}

	public setInteractive ( value: boolean ): void {
		this.spinButton.interactive = value;
	}

	public showDisabledButton (): void {
		this.spinButton.switchIconState( SpinButtonState.DISABLED );
	}
}


