import { Helper } from 'corengf/src/utils/Helper';

/**
 * CoreState contains the name of state and data needed for this state.
 * For example, data can look like { winning: 150 } what describes the winning after spin
 */
export class CoreState {
	private name: string;
	private data: any;
	private group: string;

	constructor ( pName: string, pData?: any, pGroup: string = Helper.EMPTY ) {
		this.name = pName;

		if ( pData != undefined ) {
			this.data = pData;
		} else {
			this.data = new Object();
		}

		this.group = pGroup;
	}

	public get stateName (): string {
		return this.name;
	}

	public get stateData (): any {
		return this.data;
	}

	public set stateData ( pData: any ) {
		this.data = pData;
	}

	public get stateGroup (): string {
		return this.group;
	}

	public set stateGroup ( group: string ) {
		this.group = group;
	}

	public toString (): string {
		console.log( ' ( ' + this.name + ', { ' + this.data + ' } ' );
		return ' ( ' + this.name + ', { ' + this.data + ' } ';
	}
}


