import { Asset, AssetLoaderState } from 'corengf/src/core/AssetLoader';
import { ResourceConfig } from 'corengf/src/core/config/ResourceConfig';
import { CompController } from './CompController';
import { LazyCompView } from './LazyCompView';
import { TsMap } from 'corengf/src/utils/TsMap';

/**
 * LazyCompController enables lazy load feature.
 *
 */
export class LazyCompController extends CompController {

	protected resolveArray: TsMap<number, Function>;
	private _waitGroupIndex: Array<number> = this.getWaitGroupIndex();
	protected lazyGroupIndex: number | Array<number>;

	protected view: LazyCompView;

	constructor ( view?: LazyCompView, lazyGroupIndex?: number | Array<number> ) {
		super( view );
		this.view = view;

		this.setLazyGroupIndex( lazyGroupIndex );
	}


	/**
	 * need to wait group Index,
	 * ex:{
	 * 		view: set group
	 * 			{
	 * 				assetKey : 'symbols',
	 * 				assetURL : 'assets/animations/symbols.json'
	 * 				group : ResourceConfig.MAIN_GAME
	 * 			},
	 * 			{
	 * 				assetKey : 'freeSpinSymbols',
	 * 				assetURL : 'assets/animations/freeSpinSymbols.json'
	 * 				group : SlotResourceConfig.FREE_GAME
	 * 			}
	 *
	 * 		controller: override getWaitGroupIndex
	 * 			protected getWaitGroupIndex (): Array<number> {
	 * 				return [ ResourceConfig.MAIN_GAME, SlotResourceConfig.FREE_GAME ];
	 * 			}
	 * }
	 */
	protected getWaitGroupIndex (): Array<number> {
		//TODO
		return null;
	}

	protected setLazyGroupIndex ( value: number | Array<number> ): void {
		this.lazyGroupIndex = value;
		if ( this.view ) {
			this.view.lazyGroupIndex = this.lazyGroupIndex;
		}
	}

	/**
	 * List of assets that will be loaded for this component.
	 * The format of assets should be in the next format:
	 * {
	 *			assetKey 	: 'symbols',
	 *			assetURL	: 'assets/animations/symbols.json'
	 *	}
	 * @return list of assets needed for this component
	 */
	public get loaderList (): Array<Asset> {
		var assetsList: Array<Asset> = new Array<Asset>();
		var config: Asset = this.getConfigAsset();

		if ( this.view != undefined ) {
			assetsList = this.view.getAssetsList();
			assetsList.forEach( ( asset ) => {
				asset.group = this.getGroupIndex( asset );
			} );
		}

		if ( config.assetKey != undefined ) {
			assetsList.push( config );
		}

		return assetsList;
	}

	protected getGroupIndex ( asset: Asset ): number {
		if ( asset.group !== undefined && asset.group > ResourceConfig.MAIN_GAME ) {
			return asset.group;
		} else if ( this.lazyGroupIndex !== undefined && !Array.isArray( this.lazyGroupIndex ) ) {
			return this.lazyGroupIndex || ResourceConfig.MAIN_GAME;
		} else {
			return ResourceConfig.MAIN_GAME;
		}
	}

	/**
	 * When controller state need to wait target resoure group
	 * @param groupIdx ex : SlotResourceConfig.FREE_GAME
	 */
	protected waitTargetGroupResoure ( groupIdx: number ): Promise<any> {
		if ( !this._waitGroupIndex || this._waitGroupIndex.indexOf( groupIdx ) == -1 ) {
			return Promise.resolve();
		}

		const loaderState: AssetLoaderState = this.assetLoader.isLazyResourceReady();
		if ( loaderState === AssetLoaderState.DONE ) {
			this._waitGroupIndex.splice( this._waitGroupIndex.indexOf( groupIdx ), 1 );
			return Promise.resolve();
		} else if ( loaderState === AssetLoaderState.ERROR ) {
			this.assetLoader.errorHandleService.onReceiveError( { code: '-2' } );
			return Promise.reject();
		} else {
			return new Promise( ( resolve ) => {
				if ( this.resolveArray ) {
					this.resolveArray.set( groupIdx, resolve );
				} else {
					this.resolveArray = new TsMap<number, Function>();
					this.resolveArray.set( groupIdx, resolve );
				}
			} );
		}
	}

	/**
	 * Triggered when custom group index is ready.
	 *
	 * @protected
	 * @memberof CompController
	 */
	public onGroupCompleted ( groupIdx: number ): void {
		if ( !this.view ) {
			return;
		}
		if ( this.lazyGroupIndex && this.lazyGroupIndex === groupIdx ) {
			this.view.setupLazyAssets( this.currentStateName );
		}

		if ( this._waitGroupIndex && this._waitGroupIndex.indexOf( groupIdx ) > -1 && this.resolveArray && this.resolveArray.get( groupIdx ) ) {
			this._waitGroupIndex.splice( this._waitGroupIndex.indexOf( groupIdx ), 1 );
			let resolve = this.resolveArray.get( groupIdx );
			resolve();
			this.resolveArray.delete( groupIdx );
		}
	}

	public onGroupProgress ( groupIdx: number, ratio: number ): void {
		//TODO
	}

	/**
	 * Triggered when the group LANDSCAPE_VIEW is ready.
	 *
	 * @returns {void}
	 * @memberof LazyCompController
	 */
	public onLandscapeViewLoaded (): void {
		if ( !this.view ) {
			return;
		}
		this.view.onLandscapeViewLoaded( this.currentStateName != undefined );
	}

	/**
	 * Triggered when the group PORTRAIT_VIEW is ready.
	 *
	 * @returns {void}
	 * @memberof LazyCompController
	 */
	public onPortraitViewLoaded (): void {
		if ( !this.view ) {
			return;
		}
		this.view.onPortraitViewLoaded( this.currentStateName != undefined );
	}
}



