import { MainConfig } from 'corengf/src/core/config/MainConfig';
import { Orientation } from 'corengf/src/display/Viewport';
import { TsMap } from 'corengf/src/utils/TsMap';


export class DHBConfig extends MainConfig {
	public orientation = Orientation.BOTH;

	public localisation = {
		supportMultipleLoading: false,
		preloadLanguages: [ 'en' ],
		assets: new TsMap<string, string>( [
			[ 'en', 'assets/localisation/en.json' ],
			[ 'zh-hant', 'assets/localisation/zh-hant.json' ],
			[ 'zh-hans', 'assets/localisation/zh-hans.json' ],
			[ 'th', 'assets/localisation/th.json' ],
			[ 'vi', 'assets/localisation/vi.json' ]
		] )
	};

	public partnerConfig = {
		defaultPartner: 'GNS',
		certainPartners: [ 'GPK' ]
	};
}


