import { SimpleTween } from 'corengf/src/time/SimpleTween';
import { ISymbolDefinition, SpinDirection } from 'slotngf/src/core/config/IReelsConfig';
import { ReelActionModel, IReelActionModelConfig } from 'slotngf/src/components/reelV2/ReelActionModel';

export class SpinBase extends ReelActionModel {
	protected _activeReels: Array<number>;

	protected config: ISpinBaseConfig;

	get activeReels (): Array<number> {
		return this._activeReels.slice( this.reelModel.currentReelIndex, this._activeReels.length );
	}

	protected resolveForStop: Function;

	protected setup ( config?: any ): void {
		this.config = config;
	}

	protected animStep ( reelId: number ): void {
		//
	}

	public run ( data: any ): Promise<any> {
		return Promise.resolve();
	}

	protected getSymbolHeightByName ( symbolName: string ): number {
		if ( this.config.symbolDefinition.specialSymbol[ symbolName ] ) {
			return this.config.symbolDefinition.specialSymbol[ symbolName ].height;
		} else {
			return this.config.symbolDefinition.generalSymbol.height;
		}
	}

	protected tweenSymbolsDown ( activeReels: Array<number>, config: TweenSymbolsDownConfig, spinDirection: SpinDirection ): void {
		let startPointMap: { [ key: number ]: Array<number> } = {};

		for ( var i = 0; i < activeReels.length; i++ ) {
			const reelId = activeReels[ i ];
			const arr: Array<number> = [];
			const size = this.reelModel.getReelSymbols( reelId );
			for ( let symbolIndex = 0; symbolIndex < size; symbolIndex++ ) {
				arr.push( this.reelModel.getSymbolDataByIndex( reelId, symbolIndex ).posY );
			}
			startPointMap[ reelId ] = arr;

			let tween = new SimpleTween( { y: 0 } )
				.to( { y: this.reelModel.generalSymbolConfig.height }, config.duration )
				.easing( config.easing )
				.onUpdate( ( coords ) => {
					const size = this.reelModel.getReelSymbols( reelId );
					for ( let symbolIndex = 0; symbolIndex < size; symbolIndex++ ) {
						const symbolData = this.reelModel.getSymbolDataByIndex( reelId, symbolIndex );
						const startPoint = startPointMap[ reelId ][ symbolIndex ];
						symbolData.posY = ( spinDirection == SpinDirection.SpinDown ) ? startPoint + Math.round( coords.y ) : startPoint - Math.round( coords.y );
						this.view.tweenSymbolsUpdate( reelId, symbolIndex, symbolData.posY );
					}
				} )
				.onComplete( () => {
					config.onComplete( reelId );
				} );

			tween.start();

		}
	}
}

export interface TweenSymbolsDownConfig {
	duration: number;

	/**
	 *  The distance depends on different symbol.
	 *
	 * @type {number}
	 * @memberof TweenSymbolsDownConfig
	 */
	distanceY: number;

	easing: ( k: number ) => number;

	onComplete: ( reelId: number ) => void;
}

export interface ISpinBaseConfig extends IReelActionModelConfig {
	symbolDefinition: ISymbolDefinition;
}


