import { Rectangle } from 'pixi.js';
import { Helper } from 'corengf/src/utils/Helper';
import { ICompViewConfig } from 'corengf/src/core/config/ICompViewConfig';
import { WebTextField } from 'corengf/src/elements/canvas/text/WebTextField';
import { IScrollMarqueeConfig, MarqueeDisplayMode } from 'corengf/src/elements/canvas/scroll/ScrollMarquee';


export class MarqueeConfig implements IMarqueeConfig {

	public name: string = 'marquee';

	public scrollMarqueeConfig: IScrollMarqueeConfig = this.getScrollMarqueeConfig();

	protected getScrollMarqueeConfig (): IScrollMarqueeConfig {
		return {
			name: 'scrollMarquee',
			messageSpeed: 0.13,
			accelerateSpeed: 3,
			mask: new Rectangle( 16, 0, 686, 40 ),
			marqueeDisplayMode: MarqueeDisplayMode.FIXED,
			marqueeText: {
				elementType: WebTextField.elementType,
				elementConfig: {
					name: 'marquee',
					key: '',
					position: { x: null, y: 8 },
					autoscale: { x: 670, y: 32 },
					style: {
						fontFamily: Helper.DEFAULT_FONTS,
						fontSize: 30,
						fontWeight: 'bold',
						fill: 'white',
						align: 'center'
					}
				}
			}
		};
	}
}

export interface IMarqueeConfig extends ICompViewConfig {
	scrollMarqueeConfig: IScrollMarqueeConfig;
}


