import { Inject } from 'typescript-ioc/es5';
import { CompView } from 'corengf/src/mvc/CompView';
import { UILocalButtonsController } from 'corengf/src/components/ui/buttons/UILocalButtonsController';


export class UIPanelView extends CompView {

	@Inject
	protected localButtonsController: UILocalButtonsController;

	get enabled (): boolean {
		return this.mainContainer.interactive;
	}

	/**
	 * Makes the component and its children unresponsive to clicks
	 */
	set enabled ( enabled: boolean ) {
		this.mainContainer.interactive = this.mainContainer.interactiveChildren = enabled;
	}
}


