import { Singleton } from 'typescript-ioc/es5';

@Singleton
export class States {
	public readonly connect: string = 'connect';
	public readonly init: string = 'init';
	public readonly preInit: string = 'preInit';
	public readonly watingEnterGame: string = 'watingEnterGame';
	public readonly baseGameIdle: string = 'baseGameIdle';
	public readonly loading: string = 'loading';
	public readonly gameStartTutorial: string = 'gameStartTutorial';
	public readonly tutorialOutro: string = 'tutorialOutro';
}


