import { Inject } from 'typescript-ioc/es5';
import { Sprite, Texture, Point } from 'pixi.js';
import { Helper } from 'corengf/src/utils/Helper';
import { Actions } from 'corengf/src/utils/Actions';
import { Viewport } from 'corengf/src/display/Viewport';
import { SimpleTween } from 'corengf/src/time/SimpleTween';
import { TextField } from 'corengf/src/elements/canvas/text/TextField';
import { Button, IButtonConfig } from 'corengf/src/elements/canvas/button/Button';
import { ICoreUITextFieldElementConfig, CoreUIElementFactory } from 'corengf/src/factory/CoreUIElementFactory';
import { SlotActions } from 'slotngf/src/utils/SlotActions';
import { MLUISoundConfig } from 'mlui/src/config/MLUISoundConfig';

/**
 * base game idle -> action spin
 * base game spining -> action empty
 * base game ready for stop -> action stop
 * base game spin stop -> action empty
 * base game with out win -> action spin
 * base game with win -> action spin
 * base game win loop  -> action spin
 *
 * free spin idle -> action free spin
 * free spin spining -> action empty
 * free spin ready for stop -> action free stop
 * free spin spin stop -> action empty
 * free spin with out win -> action free spin
 * free spin with win -> action free spin
 */
export class SpinButton extends Button {

	@Inject
	protected viewport: Viewport;

	protected iconElement: Sprite;
	protected spinIcon: Texture;
	protected disabledIcon: Texture;
	protected stopIcon: Texture;
	protected autoSpinIcon: Texture;

	protected autoSpinCounterLabel: TextField;
	protected autoSpinTotalLabel: TextField;

	protected iconState: SpinButtonState;
	public get currentButtonState (): SpinButtonState {
		return this.iconState;
	}

	protected glowElement: Sprite;
	protected normalGlow: Texture;
	protected spinGlow: Texture;

	protected spinGlowTween: SimpleTween;

	protected _isAutoSpins: boolean;
	protected autoSpinCounter: number;
	protected autoSpins: number;

	private _isFreeSpin: boolean;
	public get isFreeSpin (): boolean {
		return this._isFreeSpin;
	}
	public set isFreeSpin ( value: boolean ) {
		this._isFreeSpin = value;
	}

	private _isSpinning: boolean;
	public get isSpinning (): boolean {
		return this._isSpinning;
	}
	public set isSpinning ( value: boolean ) {
		this._isSpinning = value;
	}

	set anchor ( anchorPoint: Point ) {
		this.activeElement.anchor.set( anchorPoint.x, anchorPoint.y );
		this.iconElement.anchor.set( anchorPoint.x, anchorPoint.y );
		this.glowElement.anchor.set( anchorPoint.x, anchorPoint.y );
	}

	get isAutoSpins (): boolean {
		return this._isAutoSpins;
	}

	protected createElements ( config: ISpinButtonConfig ): void {
		super.createElements( config );

		this._isAutoSpins = false;
		this.autoSpinCounter = 0;
		this.autoSpins = 0;

		this.spinIcon = this.getTexture( config.spinIconAsset );
		this.disabledIcon = this.getTexture( config.disabledIconAsset );
		this.stopIcon = this.getTexture( config.stopIconAsset );
		this.autoSpinIcon = this.getTexture( config.autoIconAsset );

		this.iconElement = new Sprite();
		this.iconElement.texture = this.spinIcon;
		this.glowElement = new Sprite();

		if ( config.normalGlowAsset != Helper.EMPTY ) {
			this.normalGlow = this.getTexture( config.normalGlowAsset, false );
			this.glowElement.texture = this.normalGlow;
		} else {
			this.normalGlow = this.glowElement.texture;
		}

		this.spinGlow = this.getTexture( config.spinGlowAsset );

		this.activeElement.addChild( this.iconElement );
		this.activeElement.addChild( this.glowElement );

		this.spinGlowTween = new SimpleTween( this.glowElement )
			.to( { rotation: 360 * Math.PI / 180 }, 350 )
			.onComplete( () => {
				this.glowElement.rotation = 0;
			} )
			.onStop( () => {
				this.glowElement.rotation = 0;
			} )
			.repeat( Infinity );

		this.autoSpinCounterLabel = CoreUIElementFactory.createInstance( config.autoSpinCounterLabel );
		this.autoSpinTotalLabel = CoreUIElementFactory.createInstance( config.autoSpinTotalLabel );

		this.autoSpinCounterLabel.visible = false;
		this.autoSpinTotalLabel.visible = false;

		this.addChild( this.autoSpinCounterLabel );
		this.addChild( this.autoSpinTotalLabel );
	}

	public switchIconState ( state: SpinButtonState ): void {
		this.iconState = state;

		switch ( state ) {
			case SpinButtonState.STOP_SPIN:
				this.switchIconToStopSpin();
				this.soundKey = '';
				break;
			case SpinButtonState.STOP_REELS:
				this.switchIconToStopReels();
				this.soundKey = '';
				break;
			case SpinButtonState.SPIN:
				this.switchIconToSpin();
				this.soundKey = MLUISoundConfig.spin;
				break;
			case SpinButtonState.DISABLED:
				this.switchIconToDisabled();
				this.soundKey = '';
				break;
		}
	}

	public getCurrentIconState (): SpinButtonState {
		return this.iconState;
	}

	protected switchIconToSpin (): void {
		this.spinGlowTween.stop();
		this.isSpinning = false;
		if ( this._isAutoSpins ) {
			this.iconElement.texture = this.autoSpinIcon;
			this.glowElement.texture = this.spinGlow;
			if ( this.isFreeSpin ) {
				this.action = SlotActions.FREE_SPIN;
			} else {
				this.action = SlotActions.SPIN;
			}
		} else {
			this.iconElement.texture = this.spinIcon;
			this.glowElement.texture = this.normalGlow;
			this.autoSpinCounterLabel.visible = false;
			this.autoSpinTotalLabel.visible = false;
			this.action = SlotActions.SPIN;
		}
	}

	protected switchIconToStopSpin (): void {
		this.spinGlowTween.start();
		this.isSpinning = true;
		this.switchIconToStopReels();
	}

	protected switchIconToStopReels (): void {
		if ( this._isAutoSpins ) {
			this.iconElement.texture = this.autoSpinIcon;
			this.glowElement.texture = this.spinGlow;
			if ( this.isFreeSpin ) {
				this.action = SlotActions.FREE_SPIN_STOP;
			} else {
				this.action = SlotActions.STOP_SPIN;
			}
		} else {
			this.iconElement.texture = this.stopIcon;
			this.glowElement.texture = this.spinGlow;
			this.autoSpinCounterLabel.visible = false;
			this.autoSpinTotalLabel.visible = false;
			this.action = SlotActions.STOP_SPIN;
		}
	}

	protected switchIconToDisabled (): void {
		this.iconElement.texture = this.disabledIcon;
	}

	public stopAutoSpins (): void {
		this._isAutoSpins = false;
		this.autoSpins = 0;
		this.autoSpinCounter = 0;
	}

	public setAutospins ( currentSpin: number, value: number ): void {
		this._isAutoSpins = true;
		this.autoSpins = value;
		this.autoSpinCounter = currentSpin ? currentSpin : 0;

		if ( this.isFreeSpin ) {
			this.autoSpinCounter--;
		}
		this.updateCurrentSpinLabel();
		this.autoSpinTotalLabel.text = this.autoSpins == -1 ? '∞' : this.autoSpins.toString();

		this.clickData.totalSpin = this.autoSpins == -1 ? '∞' : this.autoSpins;
		this.autoSpinCounterLabel.visible = true;
		this.autoSpinTotalLabel.visible = true;
	}

	public setFreeSpinCount ( currentCount: number, totalCount: number ): void {
		this.autoSpinCounterLabel.text = currentCount.toString();
		this.autoSpinTotalLabel.text = totalCount.toString();
		this.clickData.totalSpin = totalCount;
	}

	public onSpinStart (): void {
		this.updateCurrentSpinLabel();
	}

	protected updateCurrentSpinLabel (): void {
		let currentSpin: number;
		currentSpin = this.autoSpinCounter;
		if ( currentSpin == 0 ) { currentSpin = 1; }
		this.autoSpinCounterLabel.text = currentSpin.toString();
	}

	public onSpinStop (): void {
		if ( !this._isAutoSpins || this.autoSpins == -1 ) {
			return;
		}
		this.autoSpins--;
	}

	public checkAndStopAutoSpins (): void {
		if ( this.autoSpins == 0 ) {
			this.stopAutoSpins();
		}
	}

	public updateClickDataCounts ( currentCount: number, totalCount: number ): void {
		this.clickData.totalSpin = totalCount;
		this.clickData.currentSpin = currentCount ? currentCount : 1;
	}

	public onPressEvent (): void {
		this.onClick( null );
	}

	/**
	 * if it's triggered by clicking, the spin button send current spin,
	 * or it send current spin + 1 because current spin hasn't update yet.
	 * * @param {*} event
	 */
	protected onClick ( event: any ): void {
		this.setInteractive( false );
		this.clickData.action = Actions.CLICK_ACTION_PREFIX + this.action;
		if ( this._isAutoSpins ) {	// including free spins
			if ( this.clickData.action == 'action_freeSpin' || this.clickData.action == 'action_spin' ) {
				this.autoSpinCounter++;
			}
			this.updateClickDataCounts( this.autoSpinCounter, this.clickData.totalSpin );
		} else {
			if ( this.clickData.currentSpin ) {
				delete this.clickData.currentSpin;
			}
			if ( this.clickData.totalSpin ) {
				delete this.clickData.totalSpin;
			}
		}
		this.clickData.orientation = this.viewport.orientation;
		super.onClick( event );
	}
}

export enum SpinButtonState {
	SPIN,
	STOP_SPIN,
	STOP_REELS,
	DISABLED
}

export interface ISpinButtonConfig extends IButtonConfig {
	spinIconAsset: string;
	disabledIconAsset: string;
	stopIconAsset: string;
	autoIconAsset: string;
	normalGlowAsset: string;
	spinGlowAsset: string;
	autoSpinCounterLabel: ICoreUITextFieldElementConfig;
	autoSpinTotalLabel: ICoreUITextFieldElementConfig;
}



