import { Point, extras, spine } from 'pixi.js';
import { Animation } from 'corengf/src/elements/canvas/animation/Animation';
import { Asset } from 'corengf/src/core/AssetLoader';
import { WinBoxView } from 'slotngf/src/components/winDisplay/winBoxes/WinBoxView';
import { IShowWinLineData } from 'slotngf/src/components/winDisplay/base/WinDisplayController';
import { IDHBWinBoxConfig } from './DHBWinBoxConfig';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import * as MiniSignal from 'mini-signals';
import { IWinBoxConfig } from 'slotngf/src/components/winDisplay/winBoxes/WinBoxConfig';

export class DHBWinBoxView extends WinBoxView {

	protected config: IDHBWinBoxConfig;
	protected freeSpinAnimations: Array<extras.AnimatedSprite | spine.Spine>;
	public tutorialOutroCompleteSignal: MiniSignal = new MiniSignal();
	protected tutorialCount: number = 0;


	protected createElements ( config?: IWinBoxConfig ): void {
		super.createElements( config );
		this.createMultiplier( 1 );
	}

	protected createMultiplier ( number: number ): void {
		this.freeSpinAnimations = new Array<extras.AnimatedSprite>();
		for ( let i = 0; i < number; i++ ) {
			let animation: extras.AnimatedSprite | spine.Spine;
			animation = Animation.createSpineAnimation( this.config.freeSpinAnimationFiles );
			animation.visible = false;
			this.freeSpinAnimations.push( animation );
			this.animationContainer.addChild( animation );
		}
	}

	public showPayline ( data: IShowWinLineData, isFreeSpin: boolean = false ): void {
		if ( !isFreeSpin ) {
			return super.showPayline( data );
		}

		// this.visible = true;
		// this.stopAnimating();
		super.showPayline( data );
		if ( !data.winData.wildMultiplier || data.winData.wildMultiplier.length === 0 ) {
			return;
		}
		let reelDefinition = this.reelsConfig.reelDefinition;
		let width = reelDefinition.symbol.width;
		let height = reelDefinition.symbol.height;

		let winBoxIndex = 0;
		data.winData.winTable.forEach( ( reel, i ) => {
			if ( i !== this.config.freeSpinMultiplierReelId ) {
				if ( data.winData.winningCombinations[ this.config.freeSpinMultiplierReelId ].length < 1 && i == 1 ) {
					// don't skip if no winning symbol on reel 2
				} else {
					return;
				}
			}
			const orientationData = this.getSingleReelLayoutData( 0, i );

			reel.forEach( ( value, j ) => {
				if ( !value || ( value && this.isSpecialSymbol( data.winData.symbol ) ) ) {
					return;
				}

				let xStart = orientationData.position.x + ( width / 2 ) + this.config.boxOffset.x;
				let yStart = orientationData.position.y + ( height / 2 ) + ( height * j ) + this.config.boxOffset.y;
				let position = new Point( xStart, yStart );

				let animation = this.freeSpinAnimations[ winBoxIndex ];

				if ( !animation ) { // create new animaion and get it again.
					this.createMultiplier( 1 );
					animation = this.freeSpinAnimations[ winBoxIndex ];
				}

				animation.position = position;
				animation.visible = true;
				animation.updateTransform();
				if ( this.isSpineAnimation( animation ) ) {
					animation.state.setAnimation( 0, this.config.freeSpinAnimationName, this.config.loop );
				} else {
					animation.play();
				}
				winBoxIndex++;
			} );
		} );
	}

	public tutorialOutroSymbolShow (): void {
		this.tutorialCount = 0;
		let count = 0;
		let ms = 0;
		for ( let i = 0; i < 4; i++ ) {
			let pos = 4;
			for ( let j = 0; j < 5; j++ ) {
				if ( i % 2 == 0 ) {
					new SimpleTimer( ms, () => {
						this.showSymbolFromCurrentView( j, i );
					} );
				} else {
					let index = pos - j;
					new SimpleTimer( ms, () => {
						this.showSymbolFromCurrentView( index, i );
					} );
				}
				count++;
				ms = 140 * count;
			}
		}

	}

	protected showSymbolFromCurrentView ( reelIndex: number, symbolIndex: number ): void {
		let reelDefinition = this.reelsConfig.reelDefinition;
		let width = reelDefinition.symbol.width;
		let height = reelDefinition.symbol.height;
		const orientationData = this.getSingleReelLayoutData( 0, reelIndex );
		let xStart = orientationData.position.x + ( width / 2 ) + this.config.boxOffset.x;
		let yStart = orientationData.position.y + ( height / 2 ) + ( height * symbolIndex ) + this.config.boxOffset.y;
		let position = new Point( xStart, yStart );

		let animation: spine.Spine = this.animations[ this.tutorialCount ] as spine.Spine;
		if ( !animation ) { // create new animaion and get it again.
			this.createBoxes( 1 );
			animation = this.animations[ this.tutorialCount ] as spine.Spine;
		}
		this.animations.push( animation );
		this.animationContainer.addChild( animation );
		animation.position = position;
		animation.visible = true;
		animation.updateTransform();
		animation.state.setAnimation( 0, this.config.animationName, false );

		let coinAnimation: spine.Spine = Animation.createSpineAnimation( 'RevealCoins' );
		this.animationContainer.addChild( coinAnimation );
		coinAnimation.position = position;
		coinAnimation.visible = true;
		coinAnimation.updateTransform();
		coinAnimation.state.setAnimation( 0, 'RevealCoins', false );
		coinAnimation.state.tracks[ 0 ].listener = {
			complete: () => {
				this.destroyCoinAnimation( coinAnimation );
			}
		};

		this.tutorialCount++;
		if ( this.tutorialCount == 20 ) {
			animation.state.tracks[ 0 ].listener = {
				complete: () => {
					this.tutorialComplete();
				}
			};
		}

	}

	protected destroyCoinAnimation ( element: spine.Spine ): void {

		new SimpleTimer( 200, () => {
			element.state.tracks[ 0 ].listener = {};
			element.state.setEmptyAnimation( 0, 0 );
			element.parent.removeChild( element );
		} );
	}

	protected tutorialComplete (): void {
		this.tutorialOutroCompleteSignal.dispatch();
		this.stopAnimating();
	}

	public stopAnimating (): void {
		super.stopAnimating();
		this.freeSpinAnimations.forEach( ( animation: extras.AnimatedSprite | spine.Spine ) => {
			if ( this.isSpineAnimation( animation ) ) {
				animation.state.setEmptyAnimation( 0, 0 );
			} else {
				animation.gotoAndStop( 0 );
			}
			animation.visible = false;
		} );
	}

	public getAssetsList (): Array<Asset> {
		return [
			{
				assetKey: 'Highlight',
				assetURL: 'assets/winDisplay/Highlight.json'
			},
			{
				assetKey: 'Wild_Multiplier',
				assetURL: 'assets/winDisplay/Wild_Multiplier.json'
			},
			{
				assetKey: 'RevealCoins',
				assetURL: 'assets/winDisplay/RevealCoins.json'
			}
		];
	}
}


