import { Inject } from 'typescript-ioc/es5';
import { SlotFreeSpinsView } from './SlotFreeSpinsView';
import { AudioTrackName } from 'corengf/src/audio/SoundManager';
import { SlotCompController } from './../mvc/SlotCompController';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { SlotSoundConfig } from 'slotngf/src/core/config/SlotSoundConfig';
import { ISlotFreeSpinsConfig, SlotFreeSpinsConfig } from './SlotFreeSpinsConfig';
import { IStartPickFeatureData } from 'slotngf/src/states/SlotStatesDataInterfaces';


export class SlotFreeSpinsController extends SlotCompController {

	public static readonly PICK_FEATURE: string = 'pickFeature';
	public static readonly FREE_SPINS_FINISH: string = 'freeSpinsFinish';
	public static readonly TYPE_FREE_SPINS: string = 'free_spins';
	public static readonly PICK_SELECT_EVENT: string = 'pickSelectEvent';

	protected view: SlotFreeSpinsView;
	protected config: ISlotFreeSpinsConfig;

	constructor ( @Inject view: SlotFreeSpinsView ) {
		super( view );
	}

	public addEventListeners (): void {
		this.view.beforePickSignal.add( this.beforePickSelection, this );
		this.view.pickSignal.add( this.onPickSelection, this );
		this.view.outroSignal.add( this.onOutroAnimationComplete, this );
		this.view.freeSpinsIntroSignal.add( this.onFreeSpinIntroAnimationComplete, this );
	}

	public init (): void {
		this.config = this.gameConfig.getConfig<ISlotFreeSpinsConfig>( SlotFreeSpinsConfig );

		if ( this.view != null ) {
			this.view.initialize( this.config );
		}
		this.finishStateAction();
	}

	public beforePickSelection (): void {
		this.soundManager.play( SlotSoundConfig.cardPick, AudioTrackName.EFFECT );
	}

	public onPickSelection ( pick: string ): void {
		GlobalDispatcher.dispatchIntoExpectedStates(
			SlotFreeSpinsController.PICK_SELECT_EVENT,
			[
				this.stateNames.endPickFeature,
				this.stateNames.pickFeature
			],
			{ pick: pick } );
		this.finishStateAction();
	}

	public startPickFeature ( data: IStartPickFeatureData ): void {
		this.view.setPicks( data.playerChoices );
		this.view.playIntro();
		this.finishStateAction();
	}

	public pickFeature (): void {
		this.view.enableSelection( true );
	}

	public endPickFeature (): void {
		this.view.playOutro();
		//Finish State action called on animation complete
	}

	protected onOutroAnimationComplete (): void {
		this.finishStateAction();
	}

	protected onFreeSpinIntroAnimationComplete (): void {
		this.finishStateAction();
	}
}


