import { Inject, Singleton } from 'typescript-ioc/es5';
import { GlobalDispatcher, EventParams } from 'corengf/src/events/GlobalDispatcher';
import { TransitionSceneView } from 'corengf/src/components/transitionScene/TransitionSceneView';
import { TransitionSceneConfig } from 'corengf/src/components/transitionScene/TransitionSceneConfig';
import { LazyCompController } from 'corengf/src/mvc/LazyCompController';
import { ResourceConfig } from 'corengf/src/core/config/ResourceConfig';

/**
 * Basic transition scene handler.
 * The class monitor the specific resource group.
 * User should handle the UI while the resource is loading.
 * @export
 * @class TransitionSceneView
 * @extends {CompView}
 */
@Singleton
export class TransitionSceneController extends LazyCompController {

	protected view: TransitionSceneView;

	public static readonly MONITOR_GROUP: string = 'MONITOR_GROUP';
	public static readonly TRIGGER_LOAD_ASSET: string = 'TRIGGER_LOAD_ASSET';

	/**
	 * The target resource group should be loaded before switch to next state.
	 *
	 * @protected
	 * @type {number}
	 * @memberof TransitionSceneController
	 */
	protected target: Array<number>;

	protected isReady: boolean = false;

	protected resolveForCheckResource: Function;

	constructor ( @Inject view: TransitionSceneView ) {
		super( view );
		this.view = view;
	}

	public addEventListeners (): void {
		GlobalDispatcher.addListener( this, TransitionSceneController.MONITOR_GROUP, this.onChangeGroupIndex );
		GlobalDispatcher.addListener( this, TransitionSceneController.TRIGGER_LOAD_ASSET, this.triggerLoadAsset );
	}

	public init ( stateData: any ): void {
		let config = this.gameConfig.getConfig<TransitionSceneConfig>( TransitionSceneConfig );
		this.target = [ config.target ];
		this.view.initialize( config );
		this.finishStateAction();
	}

	public onGroupProgress ( groupIdx: number, ratio: number ): void {
		if ( this.lazyGroupIndex === groupIdx && this.view.initialized && this.view.visible ) {
			this.updateViewProgress( ratio );
		}
	}

	protected updateViewProgress ( ratio: number ): void {
		this.view.updateProgress( ratio );
	}

	protected onChangeGroupIndex ( message: EventParams ): void {
		const data: MonitorGroupMessage = message.data;
		this.isReady = false;
		this.target = ( Array.isArray( data.group ) ) ? data.group : [ data.group ];
	}

	protected triggerLoadAsset ( message: EventParams ): void {
		const data: MonitorGroupMessage = message.data;
		if ( Array.isArray( data.group ) ) {
			data.group.forEach( ( name ) => {
				this.assetLoader.start( null, name );
			} );
		}
	}

	protected checkResource (): Promise<any> {
		if ( !this.target ) {
			this.isReady = true;
			return Promise.resolve();
		}
		return new Promise( ( resolve ) => {
			if ( this.isResourceReady() ) {
				this.isReady = true;
				resolve();
			} else {
				this.resolveForCheckResource = resolve;
			}
		} );
	}

	protected isResourceReady (): boolean {
		let retrunValue = true;
		const cloneTarget = this.target.slice( 0 );
		cloneTarget.forEach( ( group ) => {
			const isReady = this.assetLoader.isResourceReady( group );
			if ( isReady ) {
				// remove group from watch list if it is ready.
				const index = this.target.indexOf( group );
				if ( index !== -1 ) {
					this.target.splice( index, 1 );
				}
			}

			retrunValue = retrunValue && isReady;
		} );
		return retrunValue;
	}

	public onGroupCompleted ( groupIdx: number ): void {
		if ( groupIdx <= ResourceConfig.PORTRAIT_VIEW || !this.target ) {
			return;
		}


		const index = this.target.indexOf( groupIdx );
		if ( index !== -1 ) {
			this.target.splice( index, 1 );
		}

		if ( this.canResolve( groupIdx ) && this.resolveForCheckResource ) {
			this.isReady = true;
			this.resolveForCheckResource();
			this.resolveForCheckResource = null;
		}
	}

	protected canResolve ( groupIdx: number ): boolean {
		return this.target.length === 0;
	}
}

export interface MonitorGroupMessage {
	group: number | Array<number>;
}


