import { AnimationViewConfig } from 'corengf/src/components/animationView/AnimationViewConfig';
import { ICoreUISpineAnimationElementConfig } from 'corengf/src/factory/CoreUIElementFactory';
import { SpineAnimation } from 'corengf/src/elements/canvas/animation/spine/SpineAnimation';
import * as animationViewMapConfig from 'game/src/components/animationView/MapConfig';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Orientation } from 'corengf/src/display/Viewport';
import { ICoreElementLayoutConfig } from 'corengf/src/elements/canvas/CoreElement';


export class DHBAnimationViewConfig extends AnimationViewConfig {

	public name: string = 'animationView';

	public animations: Array<ICoreUISpineAnimationElementConfig> = [
		{
			elementType: SpineAnimation.elementType,
			elementConfig: {
				name: 'reelAnticipation',
				isOrientationSupported: true,
				animationName: 'reelAnticipation',
				position: { x: null, y: 305 },
				stateAnimationMap: animationViewMapConfig.anticipationMap
			}
		},
		{
			elementType: SpineAnimation.elementType,
			elementConfig: {
				name: 'IntroSplash',
				isOrientationSupported: true,
				animationName: 'IntroSplash',
				stateAnimationMap: animationViewMapConfig.introMap
			}
		},
		{
			elementType: SpineAnimation.elementType,
			elementConfig: {
				name: 'featureTriggered',
				isOrientationSupported: true,
				animationName: 'featureTriggered',
				stateAnimationMap: animationViewMapConfig.respinTriggerMap
			}
		}
	];

	public coreElementOrientationConfig: TsMap<Orientation, TsMap<string, ICoreElementLayoutConfig>> = new TsMap( [
		[
			Orientation.PORTRAIT, new TsMap( [
				[
					'reelAnticipation', {
						position: { x: null, y: 620 },
						scale: { x: 0.94, y: 0.94 }
					}
				],
				[
					'IntroSplash', {
						position: { x: 360, y: 685 }
					}
				],
				[
					'featureTriggered', {
						position: { x: 375, y: 600 }
					}
				]
			] )
		],
		[
			Orientation.LANDSCAPE, new TsMap( [
				[
					'reelAnticipation', {
						position: { x: null, y: 305 },
						scale: { x: 1, y: 1 }
					}
				],
				[
					'IntroSplash', {
						position: { x: 650, y: 370 }
					}
				],
				[
					'featureTriggered', {
						position: { x: 660, y: 300 }
					}
				]
			] )
		]
	] );
}


