import { Helper } from 'corengf/src/utils/Helper';
import { SlotNGHttpFormatter } from './../../SlotNGHttpFormatter';
import { UrlParameters } from 'corengf/src/utils/UrlParameters';

export interface GameInitRequestPayload {
	partner_token: string;
	player_token: string;
	game_code: string;
	device: string;
	engagements?: Array<string>;
}

/**
 * Format login request
 */
export class GameInitRequestMsgFormatter extends SlotNGHttpFormatter {

	/**
	 * Builds setting mode message
	 * @param data - ignored
	 * @returns {any} message object
	 */
	public codeMessage ( data?: any ): GameInitRequestPayload {

		const payload: GameInitRequestPayload = {
			partner_token: UrlParameters.partnerToken,
			player_token: UrlParameters.sessionToken,
			game_code: this.config.gameCode,
			device: Helper.getDeviceInfo().toLocaleUpperCase()
		};

		if ( !UrlParameters.isRealMode() ) {
			if ( UrlParameters.engagements ) {
				payload.partner_token = this.config.demoPartnerToken;
			} else {
				payload.partner_token = this.config.playPartnerToken;
			}
			payload.engagements = UrlParameters.engagements;
		}

		return payload;
	}
}


