import { Inject, Singleton } from 'typescript-ioc/es5';
import { GameConfig } from 'corengf/src/core/GameConfig';
import { UrlParameters } from 'corengf/src/utils/UrlParameters';

@Singleton
export class PartnerHelper {
	@Inject
	protected gameConfig: GameConfig;

	protected partnerCode: string;

	get partner (): string {
		return this.partnerCode;
	}

	public init (): void {
		this.setPartner();
	}

	public setPartner (): void {
		let partner: string = this.partnerCodeRouter();
		const partnerConfig = this.gameConfig.mainConfig.partnerConfig;

		if ( partnerConfig != undefined ) {
			if ( partner == undefined || !partnerConfig.certainPartners.includes( partner ) ) {
				partner = partnerConfig.defaultPartner;
			}
		}

		this.partnerCode = partner;
	}

	protected partnerCodeRouter (): string {
		let partner: string = UrlParameters.partnerCode;

		if ( partner == 'GPKTEST' || partner == 'GPK_Staging' ) {
			partner = 'GPK';
		}

		if ( partner == 'GTDemo' ) {
			partner = 'GT';
		}

		return partner;
	}
}


