import { IPoint } from 'corengf/src/elements/config/IElementsConfig';

/**
 * A class extends by all customize ui elements
 */
export class CoreElement extends PIXI.Container {

	/**
	 * override by ui elements, if the ui element need create by CoreUIElementFactory
	 */
	public static elementType: string = 'CoreElement';

	public isOrientationSupported: boolean = false;

	protected config: ICoreElementConfig;

	constructor ( config: ICoreElementConfig ) {
		super();
		this.name = config.name;
		this.config = config;
		this.isOrientationSupported = config.isOrientationSupported;
		this.initElement( config );
		this.updateAttributes( config );
	}

	/**
	 * override for to init element
	 * @param config
	 */
	protected initElement ( config: ICoreElementConfig ): void {
		//
	}

	/**
	 * update attributes based on new element config
	 * @param config
	 */
	public updateAttributes ( config: ICoreElementLayoutConfig ): void {
		if ( config == undefined ) {
			return;
		}

		//set alpha
		this.setAlpha( config.alpha );
		// set visible
		this.setVisible( config.visible );
		// set position
		this.setPosition( config.position );
		// set scale
		this.setScale( config.scale );
		// set rotation
		this.setRotation( config.rotation );
	}

	/**
	 * set alpha based on value in config.
	 */
	protected setAlpha ( alpha: number ): void {
		if ( alpha != undefined ) {
			this.alpha = alpha;
		}
	}

	/**
	 * set is visible based on value in config.
	 */
	protected setVisible ( visible: boolean ): void {
		if ( visible != undefined ) {
			this.visible = visible;
		}
	}

	/**
	 * set position based on value in config.
	 */
	protected setPosition ( position: IPoint ): void {
		if ( position != undefined ) {
			this.position.set( position.x, position.y );
		}
	}

	/**
	 * set scale based on value in config.
	 */
	protected setScale ( scale: IPoint ): void {
		if ( scale != undefined ) {
			this.scale.set( scale.x, scale.y );
		}
	}

	protected setRotation ( rotate: number ): void {
		if ( rotate ) {
			this.rotation = rotate;
		}
	}
}

export interface ICoreElementConfig extends ICoreElementLayoutConfig {
	name: string;
	isOrientationSupported?: boolean;
}

/**
 *
 * NOTE: while create a new CoreElement,
 * please notice, then config must extends interface [ ICoreElementConfig ], but this one.
 *
 * @export
 * @interface ICoreElementLayoutConfig
 */
export interface ICoreElementLayoutConfig {
	alpha?: number;
	visible?: boolean;
	position?: IPoint;
	scale?: IPoint;
	anchor?: IPoint;
	rotation?: number;
}



