import { Inject } from 'typescript-ioc/es5';
import { Helper } from 'corengf/src/utils/Helper';
import { Asset } from 'corengf/src/core/AssetLoader';
import { CoreEvent } from 'corengf/src/core/CoreEvent';
import { CoreState } from 'corengf/src/states/CoreState';
import { HeartTicker } from 'corengf/src/time/HeartTicker';
import { Component } from 'corengf/src/mvc/CompController';
import { GlobalDispatcher, EventParams } from 'corengf/src/events/GlobalDispatcher';
import { UIBankController } from 'corengf/src/components/ui/components/bank/UIBankController';
import { SlotEvent } from 'slotngf/src/core/SlotEvent';
import { SlotUIController } from 'slotngf/src/components/ui/SlotUIController';
import { MLUIStates } from 'mlui/src/states/MLUIStates';
import { MLUISoundConfig } from 'mlui/src/config/MLUISoundConfig';
import { MLUIBetController } from 'mlui/src/components/bet/MLUIBetController';
import { MLUISpinController } from 'mlui/src/components/spin/MLUISpinController';
import { MLUIMenuController } from 'mlui/src/components/menu/MLUIMenuController';
import { MLUIRulesController } from 'mlui/src/components/rules/MLUIRulesController';
import { MLUIExternalModuleController } from 'mlui/src/core/MLUIExternalModuleController';
import { MLUIHistoryController } from 'mlui/src/components/history/MLUIHistoryController';
import { MLUIPaytableController } from 'mlui/src/components/paytable/MLUIPaytableController';
import { MLUISettingsController } from 'mlui/src/components/settings/MLUISettingsController';
import { MLUISlotPanelController } from 'mlui/src/components/slotPanel/MLUISlotPanelController';
import { MLUIBackgroundController } from 'mlui/src/components/background/MLUIBackgroundController';
import { MLUITurboController } from 'mlui/src/components/turbo/MLUITurboController';
import { MLUISpinSettingsController } from 'mlui/src/components/spinSettings/MLUISpinSettingsController';
import { SlotInformationHeaderController } from 'slotngf/src/components/InformationHeader/SlotInformationHeaderController';

/**
 * Handel all the element behavior for user interface.
 * It has it's own local machine
 */
export class MLUIController extends SlotUIController {

	@Inject
	protected states: MLUIStates;

	protected readonly slotPanelId: string = 'slotPanel';
	protected slotPanelController: MLUISlotPanelController;

	@Inject
	protected heartTicker: HeartTicker;
	protected readonly turboPanelId: string = 'turbo';
	protected turboController: MLUITurboController;

	protected readonly bankPanelId: string = 'bank';
	protected bankController: UIBankController;

	protected readonly maxBetPanelId: string = 'maxBet';

	protected readonly spinPanelId: string = 'spin';
	protected spinController: MLUISpinController;

	protected readonly spinSettingsPanelId: string = 'spinSettings';
	protected spinSettingsController: MLUISpinSettingsController;

	protected readonly menuPanelId: string = 'menu';
	protected menuController: MLUIMenuController;

	protected readonly settingPanelId: string = 'settings';
	protected settingsControler: MLUISettingsController;

	protected readonly betPanelId: string = 'bet';
	protected betController: MLUIBetController;

	protected createComponentList ( components: Array<Component> ): void {
		components.push( { id: this.slotPanelId, class: MLUISlotPanelController } );

		// layer 1 panel
		components.push( { id: this.bankPanelId, class: UIBankController } );
		components.push( { id: this.turboPanelId, class: MLUITurboController } );
		components.push( { id: this.spinPanelId, class: MLUISpinController } );
		components.push( { id: 'uiBackground', class: MLUIBackgroundController } );
		components.push( { id: this.spinSettingsPanelId, class: MLUISpinSettingsController } );

		components.push( { id: this.betPanelId, class: MLUIBetController } );
		components.push( { id: this.menuPanelId, class: MLUIMenuController } );

		// layer 2 panel
		components.push( { id: this.settingPanelId, class: MLUISettingsController } );

		// External DOM pages
		components.push( { id: 'paytable', class: MLUIPaytableController } );
		components.push( { id: 'rules', class: MLUIRulesController } );
		components.push( { id: 'history', class: MLUIHistoryController } );

		components.push( { id: 'informationHeader', class: SlotInformationHeaderController } );

		//DOM elements
		/* add this feature back while we have broadcast system
		if ( Helper.isShowMessage() ) {
			components.push( { id: 'messenger', class: MLUIMessengerController } );
		}
		*/
	}

	protected getAssetsList (): Array<Asset> {
		var assetsList: Array<Asset> = super.getAssetsList();
		assetsList.push( ...[
			{
				assetKey: 'mlui_main_view',
				assetURL: 'assets/ngmoonlightui/mlui_main_view.json'
			},
			{
				assetKey: 'mlui_menu',
				assetURL: 'assets/ngmoonlightui/mlui_menu.json'
			},
			{
				assetKey: 'external-helpscreens',
				assetURL: 'assets/helpscreens/helpscreens.ejs'
			},
			{
				assetKey: 'external-history',
				assetURL: 'assets/history/history.ejs'
			},
			{
				assetKey: 'BankFont',
				assetURL: 'assets/fonts/bitmap/BankFont.xml'
			},
			{
				assetKey: MLUISoundConfig.spin,
				assetURL: `assets/sound/ui/${ MLUISoundConfig.spin }.mp3`
			},
			{
				assetKey: MLUISoundConfig.betDown,
				assetURL: `assets/sound/ui/${ MLUISoundConfig.betDown }.mp3`
			},
			{
				assetKey: MLUISoundConfig.betUp,
				assetURL: `assets/sound/ui/${ MLUISoundConfig.betUp }.mp3`
			}
		] );

		return assetsList;
	}

	public addEventListeners (): void {
		super.addEventListeners();
		GlobalDispatcher.addListener( this, CoreEvent.ON_OPEN_ERROR_DIALOG, this.onOpenErrorDialog.bind( this ) );
		GlobalDispatcher.addListener( this, SlotEvent.SHOW_FULL_SCREEN_UI, this.onFullScreenUIShow.bind( this ) );
		GlobalDispatcher.addListener( this, SlotEvent.HIDE_FULL_SCREEN_UI, this.onFullScreenUIHide.bind( this ) );
		GlobalDispatcher.addListener( this, MLUIExternalModuleController.OPEN_EXTERNAL_MODULE, this.onFullScreenUIShow.bind( this ) );
		GlobalDispatcher.addListener( this, MLUIExternalModuleController.CLOSE_EXTERNAL_MODULE, this.onFullScreenUIHide.bind( this ) );
	}

	protected onOpenErrorDialog ( e: EventParams ): void {
		if ( this.menuController ) {
			this.menuController.closeExternalModules();
		}
	}

	protected onStateInit ( stateData: any ): void {
		this.slotPanelController = this.dashboardComponentManager.getComponentByID( this.slotPanelId );
		this.bankController = this.dashboardComponentManager.getComponentByID( this.bankPanelId );
		this.turboController = this.dashboardComponentManager.getComponentByID( this.turboPanelId );
		this.spinController = this.dashboardComponentManager.getComponentByID( this.spinPanelId );
		this.spinSettingsController = this.dashboardComponentManager.getComponentByID( this.spinSettingsPanelId );
		this.menuController = this.dashboardComponentManager.getComponentByID( this.menuPanelId );
		this.settingsControler = this.dashboardComponentManager.getComponentByID( this.settingPanelId );
		this.betController = this.dashboardComponentManager.getComponentByID( this.betPanelId );

		this.addComponentsEventListeners();
		super.onStateInit( stateData );
	}

	protected addComponentsEventListeners (): void {
		this.slotPanelController.showBetPanelEventSignal.add( this.onShowBetPanel, this );
		this.slotPanelController.showSpinSettingsPanelEventSignal.add( this.onShowSpinSettingsPanel, this );
		this.slotPanelController.showMenuPanelEventSignal.add( this.onShowMenuPlayPanel, this );
		this.slotPanelController.betIncraseSignal.add( this.onBetChange, this );
		this.slotPanelController.betDecraseSignal.add( this.onBetChange, this );

		this.bankService.onBetUpdatedSignal.add( this.onBetChange, this );
		this.bankService.onLinesUpdatedSignal.add( this.onLineChange, this );
		this.bankService.onBalanceUpdatedSignal.add( this.onBalanceChange, this );
		this.bankService.onWinUpdatedSignal.add( this.onWinChange, this );

		this.spinSettingsController.autoPlayStartSignal.add( this.onAutoPlayStart, this );
		this.spinSettingsController.onAutoSpinEventSignal.add( this.onAutoSpinEvent, this );
		this.spinSettingsController.autoPlayStopSignal.add( this.onAutoPlayStop, this );

		this.menuController.showMenuPopupSignal.add( this.onSubMenuPopup, this );

		this.turboController.turboModeSignal.add( this.onTurboModeChange, this );
	}

	protected onTurboModeChange ( speed: number ): void {
		this.heartTicker.turnOnTurbo( speed );
		this.turboController.setTurboButtonMode( speed );
	}

	protected onShowBetPanel (): void {
		this.betController.showPopup();
	}

	protected onShowSpinSettingsPanel (): void {
		this.spinSettingsController.showPopup();
	}

	protected onShowMenuPlayPanel (): void {
		this.menuController.showPopup();
	}

	protected onAutoSpinEvent (): void {
		this.spinController.onAutoSpinEvent();
	}

	protected onBetChange ( newbet: number ): void {
		this.bankController.totalBetUpdate( this.bankService.totalBet );
	}

	protected onLineChange ( newline: number ): void {
		this.bankController.totalBetUpdate( this.bankService.totalBet );
	}

	protected onBalanceChange ( balance: number ) {
		this.bankController.balanceUpdate( balance );
	}

	protected onWinChange ( win: number ) {
		//
	}

	protected freeSpinsStart ( stateData: any ): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.freeSpinsStart, Helper.mergeTwoObjects( {}, stateData ) ) );
		if ( stateData && stateData.currentFreeSpinCount !== undefined ) {
			this.spinController.setSpinButtonFreeSpins( stateData.currentFreeSpinCount, stateData.totalFreeSpinCount );
		}
		this.finishStateAction();
	}

	protected onSubMenuPopup ( subMenu: string ): void {
		if ( subMenu == this.settingPanelId ) {
			this.settingsControler.showPopup();
		}
	}

	protected onFullScreenPanel ( e: any ): void {
		console.log( e );
	}

	protected onAutoPlayStop (): void {
		super.onAutoPlayStop();
		if ( !this.isFreeSpin ) {
			this.spinController.stopAutoSpins();
		}
	}

	protected onLocalStateChanged ( stateName: string ): void {
		if ( this.dashboardLocalStateMachine.currentState.stateName == this.states.autoSpinDashboardIdle ) {
			if ( !this.isAutoSpin ) {
				stateName = this.states.baseGameIdle;
			}
		}
		this.localButtonsController.applyButtonsTemplate( stateName );
	}

	protected onFullScreenUIShow (): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.dashboardDisable, null ) );
		this.isShowFullScreenUI = true;
	}

	protected onFullScreenUIHide (): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.baseGameIdle, null ) );
		this.isShowFullScreenUI = false;
	}

	protected gameStartTutorial (): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.gameStartTutorial, null ) );
		this.finishStateAction();
	}
}


