import { TsMap } from 'corengf/src/utils/TsMap';
import { AbstractApi } from 'corengf/src/server/abstract/AbstractApi';
import { IServerConfig } from 'corengf/src/server/abstract/config/IServerConfig';

/**
 * if the game has any third party impelemt sdk needed,
 * implements the clss to meet your needs
 */
export abstract class AbstractSdkController {

	/**
	 * get sdk customize api setting
	 */
	public abstract getSdkAPI (): TsMap<string, AbstractApi>;

	/**
	 *  get sdk customize server config
	 */
	public get serverConfig (): IServerConfig {
		return null;
	}

	/**
	 * receives upcoming state
	 * @param {string} stateName
	 */
	public processState ( stateName: string, stateData: any ): void {
		if ( typeof this[ stateName ] === 'function' ) {
			this[ stateName ]( stateData );
		}
	}

	public init (): void {
		//
	}

	public onReceiveError ( message: string ): void {
		//
	}
}


