import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { Point, Container, Graphics } from 'pixi.js';
import { SimpleTween } from 'corengf/src/time/SimpleTween';
import { Orientation } from 'corengf/src/display/Viewport';
import { UrlParameters } from 'corengf/src/utils/UrlParameters';
import { CoreElement } from 'corengf/src/elements/canvas/CoreElement';
import { MLUIFullScreenPanel } from 'mlui/src/core/MLUIFullScreenPanel';
import { IMLUIMenuConfig } from 'mlui/src/components/menu/MLUIMenuConfig';
import { MLUIExternalModuleManager } from 'mlui/src/core/MLUIExternalModuleManager';
import { Tween, remove } from '@tweenjs/tween.js';
import { TextButton, ITextButtonConfig } from 'corengf/src/elements/canvas/button/TextButton';


export class MLUIMenuPanel extends MLUIFullScreenPanel {

	protected menuBackground: Graphics;
	protected menuContainer: Container;
	protected closedContainer: Container;

	protected settingsButton: TextButton;
	protected historyButton: TextButton;
	protected homeButton: TextButton;
	protected paytableButton: TextButton;
	protected rulesButton: TextButton;
	protected closeButton: TextButton;
	protected menuButtons: Array<TextButton> = new Array<TextButton>();

	@Inject
	protected externalModuleManager: MLUIExternalModuleManager;

	public showSubMenuPanelEventSignal: MiniSignal;

	public showSignal: MiniSignal;

	protected config: IMLUIMenuConfig;

	get showHomeButton (): boolean {
		return ( UrlParameters.returnUrl != undefined );
	}

	constructor () {
		super();
		this.showSubMenuPanelEventSignal = new MiniSignal();
		this.showSignal = new MiniSignal();
	}

	protected createElements ( config: IMLUIMenuConfig ): void {
		super.createElements( config );

		this.background.alpha = 0;
		this.menuContainer = new Container();
		this.mainContainer.addChild( this.menuContainer );
		this.addBackground();

		this.addSettingsButton( config.settingsButton );
		this.addHistoryButton( config.historyButton );
		this.addHomeButton( config.homeButton );
		this.addPaytableButton( config.paytableButton );
		this.addRulesButton( config.rulesButton );
		this.addCloseButton( config.closeButton );

		this.closedContainer.visible = true;
		this.menuContainer.visible = false;
		this.visible = false;
		this.addListeners();

	}

	protected addListeners (): void {
		this.panelContainer.interactive = true;
		this.panelContainer.on( 'pointerdown', ( e: PIXI.interaction.InteractionEvent ) => {

			if ( this.visible &&
				( this.viewport.isPortraitMode ? e.data.getLocalPosition( this.menuContainer ).y < 0
					: e.data.getLocalPosition( this.menuContainer ).y > this.menuBackground.height ) ) {
				this.hidePopup();
			}
		} );
	}

	protected addBackground (): void {
		this.menuBackground = new Graphics();
		this.menuBackground.beginFill( 0, 0.9 );
		this.menuBackground.drawRect( 0, 0, this.viewport.width, this.config.menuTwoRowHeight );
		this.menuBackground.endFill();

		this.menuContainer.addChildAt( this.menuBackground, 0 );
	}

	protected addSettingsButton ( data: ITextButtonConfig ): void {
		this.settingsButton = new TextButton( data, this.localButtonsController );
		this.settingsButton.anchor = new Point( 0.5, 0.5 );
		this.menuContainer.addChild( this.settingsButton );
		this.menuButtons.push( this.settingsButton );
	}

	protected addHistoryButton ( data: ITextButtonConfig ): void {
		this.historyButton = new TextButton( data, this.localButtonsController );
		this.historyButton.elementSignal.add( this.showHistory.bind( this ) );
		this.historyButton.anchor = new Point( 0.5, 0.5 );
		this.historyButton.visible = UrlParameters.isHistoryVisible();
		this.menuContainer.addChild( this.historyButton );
		this.menuButtons.push( this.historyButton );
	}

	protected addHomeButton ( data: ITextButtonConfig ): void {
		this.homeButton = new TextButton( data );
		this.homeButton.anchor = new Point( 0.5, 0.5 );
		this.homeButton.visible = this.showHomeButton;
		this.homeButton.elementSignal.add( this.onClickHome.bind( this ) );

		this.homeButton.clickData = {
			url: UrlParameters.returnUrl
		};

		this.menuContainer.addChild( this.homeButton );
		this.menuButtons.push( this.homeButton );
	}

	protected addPaytableButton ( data: ITextButtonConfig ): void {
		this.paytableButton = new TextButton( data );
		this.paytableButton.elementSignal.add( this.showPaytable.bind( this ) );
		this.paytableButton.anchor = new Point( 0.5, 0.5 );
		this.menuContainer.addChild( this.paytableButton );
		this.menuButtons.push( this.paytableButton );
	}

	protected addRulesButton ( data: ITextButtonConfig ): void {
		this.rulesButton = new TextButton( data );
		this.rulesButton.elementSignal.add( this.showRules.bind( this ) );
		this.rulesButton.anchor = new Point( 0.5, 0.5 );
		this.menuContainer.addChild( this.rulesButton );
		this.menuButtons.push( this.rulesButton );
	}

	protected addCloseButton ( data: ITextButtonConfig ): void {
		this.closeButton = new TextButton( data, this.localButtonsController );
		this.closeButton.anchor = new Point( 0.5, 0.5 );
		this.menuContainer.addChild( this.closeButton );
		this.menuButtons.push( this.closeButton );
	}

	protected onClickHome ( data: any ): void {
		window.location.href = data.url;
	}

	protected onShowSettings (): void {
		this.showSubMenuPanelEventSignal.dispatch( 'settings' );
	}

	public showPopup (): void {
		this.visible = true;
		this.closedContainer.visible = false;
		this.menuContainer.visible = true;

		const y = this.showY;
		this.menuTween = new SimpleTween( this.menuContainer )
			.to( { y }, 100 )
			.onComplete( () => { this.menuContainer.y = y; } )
			.start();
		this.showSignal.dispatch( true );
	}

	public showPaytable (): void {
		this.externalModuleManager.GenesisPayTable.open();
	}

	public showRules (): void {
		this.externalModuleManager.GenesisRule.open();
	}

	public showHistory (): void {
		this.externalModuleManager.GenesisHistory.open();
	}

	public closeExternalModules (): void {
		this.externalModuleManager.GenesisPayTable.close();
		this.externalModuleManager.GenesisRule.close();
		this.externalModuleManager.GenesisHistory.close();
	}

	public hidePopup (): void {
		this.visible = false;
		this.closedContainer.visible = true;

		const y = this.hideY;
		this.menuTween = new SimpleTween( this.menuContainer )
			.to( { y }, 100 )
			.onComplete( () => { this.menuContainer.visible = false; } )
			.start();
		this.showSignal.dispatch( false );
	}

	private menuTween: Tween;
	private _showY: number;
	private _hideY: number;
	private _backgroundHeight: number;

	protected get showY (): number {
		if ( this.viewport.isPortraitMode ) {
			this._showY = this.viewport.height - this.config.menuTwoRowHeight;
		} else {
			this._showY = 0;
		}

		return this._showY;
	}

	protected get hideY (): number {
		if ( this.viewport.isPortraitMode ) {
			this._hideY = this.viewport.height;
		} else {
			this._hideY = 0 - this.config.menuTwoRowHeight;
		}

		return this._hideY;
	}

	protected get backgroundHeight (): number {
		if ( this.viewport.isPortraitMode ) {
			this._backgroundHeight = 350;
		} else {
			this._backgroundHeight = 200;
		}

		return this._backgroundHeight;
	}

	protected layoutElements (): void {
		super.layoutElements();

		if ( this.menuContainer.visible ) {
			this.menuContainer.y = this.showY;
			remove( this.menuTween );
		} else {
			this.menuContainer.y = this.hideY;
		}
		this.menuBackground.height = this.backgroundHeight;
		this.menuBackground.width = this.viewport.width;
	}

	protected coreElementLayout ( orientation: Orientation ): void {
		super.coreElementLayout( orientation );

		if ( this.config != undefined ) {
			if ( this.config.coreElementOrientationConfig != undefined ) {
				const orientationConfig = this.config.coreElementOrientationConfig.get( orientation );
				this.menuButtons.forEach( element => {
					if ( orientationConfig.has( element.name ) ) {
						( element as CoreElement ).updateAttributes( orientationConfig.get( element.name ) );
					}
				} );
			}
		}
	}
}


