import { Inject } from 'typescript-ioc/es5';
import { PickFeatureFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/pending/PickFeatureFormatter';
import { IGameState, IFeatureInfo, IInitResponse } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { Scenario } from 'corengf/src/states/Scenario';
import { IPickFeatureDetail, FeatureTypes } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { FeaturesFormatterList } from 'slotngf/src/server/ng/formatter/http/in/features/FeaturesFormatterList';
import { DHBSlotScenarioManager } from 'game/src/core/DHBSlotScenarioManager';
import { DHBSlotScenarioNames } from 'game/src/states/DHBSlotScenarioNames';
import { DHBSlotStates } from 'game/src/states/DHBSlotStates';


export class DHBPickFeatureFormatter extends PickFeatureFormatter {

	@Inject
	protected featureFormatterList: FeaturesFormatterList;

	@Inject
	protected scenarioManager: DHBSlotScenarioManager;

	@Inject
	protected scenarioNames: DHBSlotScenarioNames;

	@Inject
	protected stateNames: DHBSlotStates;

	public processIncomingMessage ( message: IGameState, pickFeatureInfo?: IFeatureInfo ): Scenario {

		if ( pickFeatureInfo == undefined ) {
			return null;
		}

		// console.error( '+++++ processIncomingMessage ' );

		let freeSpin = message.features.find( e => e.type === FeatureTypes.FREE_SPIN );

		if ( freeSpin ) {
			return null;
		}
		let pickFeatureDetail: IDHBPickFeatureDetail = this.getFeatureDetail( pickFeatureInfo );
		let responseScenario: Scenario = null;

		if ( message.spin_result.features_triggered != undefined ) {
			responseScenario = this.getFormatFeatureTriggeredsScenario( message, responseScenario );
		}

		if ( responseScenario.hasStateName( this.stateNames.pickFeatureWin ) ) {
			responseScenario.addScenario( this.scenarioManager.getScenario( this.scenarioNames.gameIdle ) );
		}

		// console.error( '+++++ responseScenario ' );
		// console.error( responseScenario );
		this.slotNGModel.stateTag = message.state_tag;
		return responseScenario;
	}

	public triggeredFeatureIncomingMessage ( message: IGameState, pickFeatureInfo?: IFeatureInfo ): Scenario {

		if ( pickFeatureInfo == undefined ) {
			return null;
		}

		// console.error( '----- triggeredFeatureIncomingMessage ' );

		let pickFeatureDetail: IDHBPickFeatureDetail = this.getFeatureDetail( pickFeatureInfo );
		let responseScenario: Scenario;
		let freeSpin = message.features.find( e => e.type === FeatureTypes.FREE_SPIN );
		if ( pickFeatureInfo.complete && message.spin_result.win_amount > 0 ) {
			responseScenario = this.scenarioManager.getScenario( this.scenarioNames.pickFeatureWithWinning );

			responseScenario.setStateData( this.stateNames.pickFeatureWin, {
				balance: message.balance,
				win_amount: message.spin_result.win_amount,
				selected_index: pickFeatureDetail.selected_index,
				playerChoices: this.getPickFeatureDetailChoices( pickFeatureDetail )
			} );

		} else if ( pickFeatureDetail.selected_index ) {

			responseScenario = this.scenarioManager.getScenario( this.scenarioNames.pickFeature );

			responseScenario.addStateData( this.stateNames.pickFeature, {
				selected_index: pickFeatureDetail.selected_index,
				playerChoices: this.getPickFeatureDetailChoices( pickFeatureDetail )
			} );
		} else {

			if ( freeSpin == null ) {
				if ( message.spin_result.win_amount > 0 || message.spin_result.scatter_win != undefined || freeSpin ) {
					responseScenario = this.scenarioManager.getScenario( this.scenarioNames.featureWithWinning );
				}
			}
			if ( responseScenario ) {
				responseScenario.addScenario( this.scenarioManager.getScenario( this.scenarioNames.runPickFeature ) );
			} else {
				responseScenario = this.scenarioManager.getScenario( this.scenarioNames.runPickFeature );
			}

			let pickFeatureDetail: IPickFeatureDetail = this.getFeatureDetail( pickFeatureInfo );

			responseScenario.setStateData( this.stateNames.startPickFeature, {
				playerChoices: this.getPickFeatureDetailChoices( pickFeatureDetail )
			} );
		}

		// console.error( '----- responseScenario ' );
		// console.error( responseScenario );
		this.slotNGModel.stateTag = message.state_tag;
		return responseScenario;
	}


	public restoreFeatureIncomingMessage ( featureState?: any, features?: Array<IFeatureInfo>,
		type?: string, message?: IInitResponse ): Scenario {

		let pickFeature = features.find( e => e.type == FeatureTypes.PICK );
		let pickFeatureDetail: IDHBPickFeatureDetail = pickFeature.feature_state as IDHBPickFeatureDetail;
		let pickFeatureScenario: Scenario = null;

		pickFeatureScenario = super.restoreFeatureIncomingMessage( pickFeatureDetail );

		pickFeatureScenario.addStateData( this.stateNames.startPickFeature, {
			selected_index: pickFeatureDetail.selected_index,
			restoreChoices: pickFeatureDetail.choices
		} );

		// console.error( 'pickFeatureScenario' );
		// console.error( pickFeatureScenario );

		return pickFeatureScenario;
	}

	protected getFeatureFormatter ( type: string ): any {
		return ( this.featureFormatterList.inFeatureFormatters.get( type ) );
	}

}


export interface IDHBPickFeatureDetail extends IPickFeatureDetail {
	selected_index: number;
}


