import { Inject, Container } from 'typescript-ioc/es5';
import { Orientation } from 'corengf/src/display/Viewport';
import { IHelpScreenContainerConfig } from 'corengf/src/elements/webpage/WebpageElement';
import { ExternalModuleView } from 'corengf/src/components/externalModule/ExternalModuleView';
import { IInitData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { MLUIExternalModuleController } from 'mlui/src/core/MLUIExternalModuleController';
import { MLUIRulesConfig } from './MLUIRulesConfig';
import { Helper } from 'corengf/src/utils/Helper';
import { IWebGeneralPagingConfig } from 'corengf/src/elements/webpage/paging/WebGeneralPagingElement';
import { ICoreWebElementConfig } from 'corengf/src/elements/webpage/CoreWebElement';
import { WebFactoryElementType } from 'corengf/src/factory/WebElementFactory';


export class MLUIRulesController extends MLUIExternalModuleController {
	protected config: MLUIRulesConfig;

	constructor ( @Inject view: ExternalModuleView ) {
		super( view );

		this.config = Container.get( MLUIRulesConfig );
		this.productLine = this.config.productLine;
	}

	public init ( stateData: IInitData ): void {
		const loader = PIXI.loader;
		const interruptionAdditionalRule = ( stateData.gameViewConfig && stateData.gameViewConfig.incomplete_game_desc )
			? stateData.gameViewConfig.incomplete_game_desc : Helper.EMPTY;
		this.moduleConfig = {
			assets: this.convertAssetNames( loader, this.config.assetNames ),
			handleClose: this.onClose.bind( this ),
			handleOpen: this.onOpen.bind( this ),
			language: this.getLocalisationMap( loader ),
			soundKey: this.config.soundKey,
			dataHook: this.sendDataHook.bind( this ),
			playSound: this.playSound.bind( this ),
			style: this.getExternalModuleConfig( {
				interruptionAdditionalRule: interruptionAdditionalRule,
				rtp: stateData.rtp
			} )
		};
		super.init();
		this.view.onOrientationChangeSignal.add( this.onOrientationChanged, this );
	}

	protected onOrientationChanged ( orientation: Orientation ): void {
		if ( this.externalModule.GenesisRule ) {
			this.externalModule.GenesisRule.update( orientation );
		}
	}

	protected getExternalModuleConfig ( data: any ): MLUIRulesConfig {
		let portraitContainerConfig: IHelpScreenContainerConfig = this.getPortraitContainerConfig();
		let landscapeContainerConfig: IHelpScreenContainerConfig = this.getLandscapeContainerConfig();

		let pagingElement: IWebGeneralPagingConfig<ICoreWebElementConfig>
			= landscapeContainerConfig.paragraphs.find(
				e => e.type === WebFactoryElementType.WEB_GENERAL_PAGING_ELEMENT
			) as IWebGeneralPagingConfig<ICoreWebElementConfig>;

		//add "How To Win" and "Others"
		portraitContainerConfig.paragraphs =
			portraitContainerConfig.paragraphs.concat( this.config.getPortraitHowToWinElement() );
		portraitContainerConfig.paragraphs =
			portraitContainerConfig.paragraphs.concat( this.config.getPortraitOthersElement( data.interruptionAdditionalRule ) );
		if ( pagingElement ) {
			pagingElement.pages = pagingElement.pages.concat( this.config.getLandscapeHowToWinElement() );
			pagingElement.pages = pagingElement.pages.concat( this.config.getLandscapeOthersElement( data.interruptionAdditionalRule ) );
		}

		//add "rtp"
		if ( data.rtp ) {
			portraitContainerConfig.paragraphs = portraitContainerConfig.paragraphs.concat( this.config.getPortraitRTPElements( data.rtp.toString() ) );
			if ( pagingElement ) {
				pagingElement.pages = pagingElement.pages.concat( this.config.getLandscapeRTPElements( data.rtp.toString() ) );
			}
		}

		return this.config;
	}

	protected onClose (): any {
		//
	}

	protected onOpen (): any {
		//
	}
}


