import * as MiniSignal from 'mini-signals';
import { CompView } from 'corengf/src/mvc/CompView';
import { ToolTip, IToolTipConfig } from 'corengf/src/elements/canvas/tooltip/Tooltip';


export class TooltipView extends CompView {

	protected activeTooltips: Array<ToolTip>;
	public destroySignal: MiniSignal;

	constructor () {
		super();
		this.activeTooltips = new Array<ToolTip>();
		this.destroySignal = new MiniSignal();
	}

	public showTooltip ( tooltipData: IToolTipConfig ): void {
		let isHaveTooltip: boolean = false;
		for ( let index = 0; index < this.activeTooltips.length; index++ ) {
			const tip = this.activeTooltips[ index ];

			if ( tip.name == tooltipData.name ) {
				isHaveTooltip = true;
				tip.show();
			}
		}

		if ( !isHaveTooltip ) {
			let tooltip = new ToolTip( tooltipData );
			tooltip.hideSignal.add( this.oncloseTooltip, this );
			tooltip.destroySignal.add( this.onDestroyTooltip, this );
			this.activeTooltips.push( tooltip );
			this.addChild( tooltip );
		}
	}

	public hideTooltip ( name: string ): void {
		let tip: ToolTip = this.activeTooltips.find( e => e.name === name );
		if ( tip ) {
			tip.hide();
		}
	}

	public clearTooltip ( names: Array<string> ): void {
		for ( let index = 0; index < names.length; index++ ) {
			const tip: ToolTip = this.activeTooltips.find( e => e.name === names[ index ] );
			if ( tip ) {
				tip.destroy( true );
			}
		}
	}

	protected oncloseTooltip ( tooltip: ToolTip ): void {
		// tooltip.hide();
	}

	protected onDestroyTooltip ( tooltip: ToolTip ): void {
		this.removeChild( tooltip );
		let index = this.activeTooltips.indexOf( tooltip );
		this.activeTooltips.splice( index, 1 );
		this.destroySignal.dispatch( { name: tooltip.name, data: tooltip.actionData } );
	}

	protected layoutElements (): void {
		super.layoutElements();
		for ( var i = 0; i < this.activeTooltips.length; i++ ) {
			this.activeTooltips[ i ].destroy();
		}
	}
}



