import { Container } from 'pixi.js';
import * as MiniSignal from 'mini-signals';
import { SpineAnimation } from 'corengf/src/elements/canvas/animation/spine/SpineAnimation';
import { SymbolPosition } from 'slotngf/src/core/config/IReelsConfig';
import { SymbolAnimationPostfix } from 'slotngf/src/components/reels/ReelsEnums';

export abstract class AbstractSymbol {
	public symbolName: string;
	public triggerPositions: Array<SymbolPosition> = null;

	public position: SymbolPosition;

	public animationCompleteSignal: MiniSignal;

	public get x (): number {
		return this._symbolContainer.x;
	}

	public set x ( value: number ) {
		this._symbolContainer.x = value;
	}

	public get y (): number {
		return this._symbolContainer.y;
	}

	public set y ( value: number ) {
		this._symbolContainer.y = value;
	}

	public get name (): string {
		return this._symbolContainer.name;
	}

	public set name ( value: string ) {
		this._symbolContainer.name = value;
	}

	public get visible (): boolean {
		return this._symbolContainer.visible;
	}

	public set visible ( value: boolean ) {
		this._symbolContainer.visible = value;
	}

	protected _symbolContainer: Container | SpineAnimation | PIXI.extras.AnimatedSprite;

	public abstract playAnimation ( type: string | SymbolAnimationPostfix, useNativeSpeed?): boolean;

	public isContain ( symbolPosition: SymbolPosition ): boolean {
		let result = false;
		this.triggerPositions.forEach( ( item ) => {
			if ( item.reelIndex === symbolPosition.reelIndex && item.symbolIndex === symbolPosition.symbolIndex ) {
				result = true;
			}
		} );
		return result;
	}
}


