import { TsMap } from 'corengf/src/utils/TsMap';
import { Helper } from 'corengf/src/utils/Helper';
import { SoundConfig } from 'corengf/src/core/config/SoundConfig';
import { IWebTextConfig } from 'corengf/src/elements/webpage/text/WebTextElement';
import { IWebLineConfig } from 'corengf/src/elements/webpage/line/WebLineElement';
import { ICoreWebElementConfig } from 'corengf/src/elements/webpage/CoreWebElement';
import { ImageSourceType } from 'corengf/src/elements/webpage/image/WebImageElement';
import { WebElementFactory, WebFactoryElementType } from 'corengf/src/factory/WebElementFactory';
import { IWebPatternStyleConfig } from 'corengf/src/elements/webpage/linePattern/WebLinePatternElement';
import { IHelpScreenContainerConfig, IWebpageElement } from 'corengf/src/elements/webpage/WebpageElement';
import { MLUIButtonId } from 'mlui/src/buttons/MLUIButtonId';
import { GeneralWebPageConfig } from 'mlui/src/config/GeneralWebPageConfig';
import { IWebElementBlockConfig } from 'corengf/src/elements/webpage/block/WebElementBlock';

export class MLUIPaytableConfig extends GeneralWebPageConfig {
	public name = 'paytable';
	public externalKey = 'external-helpscreens';
	public moduleName = 'GenesisPayTable';
	public localisationKeys = [ 'paytable_Copyright' ];

	protected productionYear: string = '2018';
	protected numberOfWays: string = '243';

	protected getButtonIds (): TsMap<string, MLUIButtonId> {
		return new TsMap( [
			[
				'closeButton', MLUIButtonId.BUTTON_HIDE_PAYTABLE
			]
		] );
	}

	protected getPortraitContainerConfig (): IHelpScreenContainerConfig {

		return Helper.mergeTwoObjects( super.getPortraitContainerConfig(),
			{
				paragraphs: this.getPortraitGameElements()
			}
		);
	}


	public getPortraitGameElements (): Array<ICoreWebElementConfig> {
		return [] as Array<ICoreWebElementConfig>;
	}

	/**
	 * set up all pays config block layout for portrait
	 */
	public getPortraitAllPaysElements (): Array<ICoreWebElementConfig> {
		return [
			this.getPortraitWebLineConfig(),
			WebElementFactory.createWebTextElement( {
				type: WebFactoryElementType.WEB_TEXT_ELEMENT,
				languageKey: 'allPaysTitle',
				superscriptLanguageKey: 'allPaysSuperscript'
			} ),
			WebElementFactory.createWebTextContentElement( {
				type: WebFactoryElementType.WEB_TEXT_CONTENT_ELEMENT,
				languageKey: 'allPaysContent',
				textToReplace: [
					{
						replaceKey: '{NumberOfWays}',
						replaceValue: this.translator.t( this.numberOfWays )
					}
				]
			} )
		];
	}

	/**
	 * set up line game line config block layout for portrait
	 */
	public getPortraitLineGameElements (): Array<ICoreWebElementConfig> {
		return [
			WebElementFactory.createWebElementBlock( {
				type: WebFactoryElementType.WEB_ELEMENT_BLOCK,
				config: [
					this.getPortraitWebLineConfig(),
					{
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						languageKey: 'lineGameTitle',
					},
					{
						type: WebFactoryElementType.WEB_LINE_PATTERN_ELEMENT,
						blockStyle: this.getPortraitLinePatternBlockStyleConfig(),
						patternStyleConfig: this.getPortraitLinePatternStyleConfig(),
						patternPosition: {
							x: 0,
							y: 10
						}
					}
				]
			} )
		];
	}

	public getPortraitLinePatternBlockStyleConfig (): string {
		return `
			width: 431px;
			left: -30px;
			font-size: 14px;
			position: relative;
		`;
	}

	/**
	 * set portrait line pattern style config
	 */
	public getPortraitLinePatternStyleConfig (): IWebPatternStyleConfig {
		return {} as IWebPatternStyleConfig;
	}

	/**
	 * set up portrait copyright block layout
	 */
	public getPortraitCopyrightElements (): Array<ICoreWebElementConfig> {
		return [
			this.getPortraitWebLineConfig(),
			WebElementFactory.createWebTextElement( {
				type: WebFactoryElementType.WEB_TEXT_ELEMENT,
				languageKey: 'copyrightTitle'
			} ),
			//logo
			WebElementFactory.createWebImageElement( {
				type: WebFactoryElementType.WEB_IMAGE_ELEMENT,
				blockStyle: `
					position:relative;
					height: 80px;
					width: 224px;
				`,
				imageSource: ImageSourceType.TEXTURE,
				imageData: {
					imagePath: 'assets/helpscreens/logo.png',
					width: 320,
					height: 114,
					ratio: 0.7,
				}
			} ),
			// copyright content
			WebElementFactory.createWebTextContentElement( {
				type: WebFactoryElementType.WEB_TEXT_CONTENT_ELEMENT,
				languageKey: 'copyrightContent',
				textToReplace: [
					{
						replaceKey: '{GameName}',
						replaceValue: this.translator.t( 'gameName' )
					},
					{
						replaceKey: '{BrandName}',
						replaceValue: this.translator.t( 'brandName' )
					},
					{
						replaceKey: '{ProductionYear}',
						replaceValue: this.productionYear
					}
				]
			} ),
			//link
			WebElementFactory.createWebTextElement( {
				type: WebFactoryElementType.WEB_TEXT_ELEMENT,
				blockStyle: `
					margin: 35px 0 35px 0;
					font-size:16px;
					font-style: italic;
					text-align:center;
				`,
				languageKey: 'www.genesis-games.com'
			} )
		];
	}

	/**
	 * set up landscape copyright block layout
	 */
	public getPortraitVersionElements (): Array<ICoreWebElementConfig> {
		//version & date
		return [
			WebElementFactory.createWebElementBlock( {
				type: WebFactoryElementType.WEB_ELEMENT_BLOCK,
				blockStyle: `
					text-align:center;
				`,
				config: [
					{
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						blockStyle: `
							font-size:16px;
							font-weight:16px;
							display: inline-block;
							opacity:0.6;
						`,
						languageKey: 'gameVersion'
					},
					{
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						blockStyle: `
							font-size:16px;
							font-weight:16px;
							display: inline-block;
							opacity:0.6;
							white-space: pre-wrap;
						`,
						languageKey: ` ${ VERSION } / ${ RELEASE_DATE }`
					}
				]
			} )
		];
	}

	/**
	 * Style grid lines between paragraphs
	 */
	protected getPortraitWebLineConfig (): IWebLineConfig {
		return WebElementFactory.createWebLineElement( {
			type: WebFactoryElementType.WEB_LINE_ELEMENT,
			show: true
		} );
	}

	// remove defualt main title
	protected getLandscapeHeaderMainTitleConfig (): IWebTextConfig {
		return null;
	}

	protected getLandscapeContainerConfig (): IHelpScreenContainerConfig {

		return Helper.mergeTwoObjects( super.getLandscapeContainerConfig(),
			{
				paragraphs: this.getLandscapeGameElements()
			}
		);
	}

	/**
	 * set up landscape all pays block layout
	 */
	public getLandscapeAllPaysElements (): Array<ICoreWebElementConfig> {

		return [
			WebElementFactory.createWebElementBlock( {
				type: WebFactoryElementType.WEB_ELEMENT_BLOCK,
				config: [
					{
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						blockStyle: this.getLandscapeMainTitleStyle(),
						languageKey: 'allPaysTitle',
						superscriptLanguageKey: 'allPaysSuperscript'
					},
					{
						type: WebFactoryElementType.WEB_TEXT_CONTENT_ELEMENT,
						languageKey: 'allPaysContent',
						textToReplace: [
							{
								replaceKey: '{NumberOfWays}',
								replaceValue: this.numberOfWays
							}
						]
					}
				]
			} )
		];
	}

	/**
	 * set up landscape line game line config block layout
	 */
	public getLandscapeLineGameElements (): Array<ICoreWebElementConfig> {
		return [
			WebElementFactory.createWebElementBlock( {
				type: WebFactoryElementType.WEB_ELEMENT_BLOCK,
				config: [
					{
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						blockStyle: this.getLandscapeMainTitleStyle(),
						languageKey: 'lineGameTitle',
					},
					{
						type: WebFactoryElementType.WEB_LINE_PATTERN_ELEMENT,
						blockStyle: this.getLandscapeLinePatternBlockStyleConfig(),
						patternStyleConfig: this.getLandscapeLinePatternStyleConfig(),
						patternPosition: {
							x: 0,
							y: 7
						}
					},
				]
			} )
		];
	}

	public getLandscapeLinePatternBlockStyleConfig (): string {
		return `
			position: relative;
			font-size: 16px;
			width: 550px;
			margin: auto;
		`;
	}

	public getLandscapeLinePatternStyleConfig (): IWebPatternStyleConfig {
		return {} as IWebPatternStyleConfig;
	}

	/**
	 * set up landscape copyright block layout
	 */
	public getLandscapeCopyrightElements (): Array<ICoreWebElementConfig> {
		return [
			WebElementFactory.createWebElementBlock( {
				type: WebFactoryElementType.WEB_ELEMENT_BLOCK,
				config: [
					{
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						blockStyle: this.getLandscapeMainTitleStyle(),
						languageKey: 'copyrightTitle'
					},
					//logo
					{
						type: WebFactoryElementType.WEB_IMAGE_ELEMENT,
						blockStyle: `
								position:relative;
								width: 224px;
								height: 80px;
								margin: 14px 175px 21px 175px;
							`,
						imageSource: ImageSourceType.TEXTURE,
						imageData: {
							imagePath: 'assets/helpscreens/logo.png',
							width: 320,
							height: 114,
							ratio: 0.7,
						},
						imageStyle: 'margin-left:0px;',
					},
					//copyright content
					{
						type: WebFactoryElementType.WEB_TEXT_CONTENT_ELEMENT,
						languageKey: 'copyrightContent',
						textToReplace: [
							{
								replaceKey: '{GameName}',
								replaceValue: this.translator.t( 'gameName' )
							},
							{
								replaceKey: '{BrandName}',
								replaceValue: this.translator.t( 'brandName' )
							},
							{
								replaceKey: '{ProductionYear}',
								replaceValue: this.productionYear
							}
						]
					},
					//link
					{
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						blockStyle: `
							margin:40px 0px 0 10px;
							font-size:16px;
							font-style: italic;
							text-align:center;
						`,
						languageKey: 'www.genesis-games.com'
					}
				]
			} )
		];
	}

	/**
	 * set up landscape version & date layout
	 */
	public getLandscapeVersionElements (): IWebElementBlockConfig<IWebTextConfig> {
		//version & date

		return {
			type: WebFactoryElementType.WEB_ELEMENT_BLOCK,
			blockStyle: `
				text-align:center;
				margin:10px 0px;
			`,
			config: [
				{
					type: WebFactoryElementType.WEB_TEXT_ELEMENT,
					blockStyle: `
						font-size:16px;
						font-weight:24px;
						display: inline-block;
						opacity:0.6;
					`,
					languageKey: 'gameVersion'
				},
				{
					type: WebFactoryElementType.WEB_TEXT_ELEMENT,
					blockStyle: `
						font-size:16px;
						font-weight:24px;
						display: inline-block;
						opacity:0.6;
						white-space: pre-wrap;
					`,
					languageKey: ` ${ VERSION } / ${ RELEASE_DATE }`
				}
			]
		};
	}

	/**
	 * Landscape screen config
	 */
	protected getLandscapeGameElements (): Array<ICoreWebElementConfig> {
		return [
			WebElementFactory.createGeneralPagingElement( {
				type: WebFactoryElementType.WEB_GENERAL_PAGING_ELEMENT,
				blockStyle: `
					position: absolute;
					width: 616px;
					height: 364px;
				`,
				pagingConfig: {
					arrowButton: {
						blockStyle: 'top:0;',
						pageDot: {},
						nextButton: {
							dataHookTrackerConfig: {
								buttonID: MLUIButtonId.BUTTON_PAYTABLE_NEXT
							},
							soundKey: SoundConfig.buttonClick,
							buttonImage: WebElementFactory.createWebImageElement( {
								type: WebFactoryElementType.WEB_IMAGE_ELEMENT,
								blockStyle: `
									right:-105px;
									top:140px;
									transform:scaleX(-1);
									z-index: 10;
									width:60px;
									height:60px;
									position: absolute;
								`,
								imageSource: ImageSourceType.SPRITE,
								imageData: {
									assetName: 'mlui_menu',
									imageName: 'arrow_Enabled.png',
									ratio: 0.55
								},
							} )
						},
						preButton: {
							dataHookTrackerConfig: {
								buttonID: MLUIButtonId.BUTTON_PAYTABLE_PREV
							},
							soundKey: SoundConfig.buttonClick,
							buttonImage: WebElementFactory.createWebImageElement( {
								type: WebFactoryElementType.WEB_IMAGE_ELEMENT,
								blockStyle: `
									left:-105px;
									top:140px;
									z-index: 10;
									width:60px;
									height:60px;
									position: absolute;
								`,
								imageSource: ImageSourceType.SPRITE,
								imageData: {
									assetName: 'mlui_menu',
									imageName: 'arrow_Enabled.png',
									ratio: 0.55
								},
							} )
						}
					}
				},
				pages: this.getLandscapeGerneralPagingElementPages(),
			} )
		];
	}

	protected getLandscapeGerneralPagingElementPages (): Array<ICoreWebElementConfig> {
		return [];
	}

	protected getLandscapeParagraphs (): Array<IWebpageElement> {
		return [] as Array<IWebpageElement>;
	}
}


