import { Singleton } from 'typescript-ioc/es5';
import { SlotStates } from 'slotngf/src/states/SlotStates';


@Singleton
export class SlotUIStates extends SlotStates {
	public readonly dashboardIdle: string = 'dashboardIdle';
	public readonly dashboardDisable: string = 'dashboardDisable';

	public readonly spinStopReady: string = 'spinStopReady';
	public readonly freeSpinStopReady: string = 'freeSpinStopReady';

	public readonly autoSpinDashboardIdle: string = 'autoSpinDashboardIdle';
	public readonly autoSpinStartSpin: string = 'autoSpinStartSpin';
	public readonly autoSpinSpinning: string = 'autoSpinSpinning';
	public readonly autoSpinSpinStop: string = 'autoSpinSpinStop';
	public readonly autoSpinSpinStopReady: string = 'autoSpinSpinStopReady';
	public readonly skip_autoSpinSpinning: string = 'skip_autoSpinSpinning';
	public readonly skip_autoSpinSpinStop: string = 'skip_autoSpinSpinStop';
	public readonly autoSpinAllLineWin: string = 'autoSpinAllLineWin';
	public readonly autoSpinShowWinning: string = 'autoSpinShowWinning';
	public readonly skip_autoSpinShowWinning: string = 'skip_autoSpinShowWinning';
	public readonly genplusSpinning: string = 'genplusSpinning';
}



