import { Helper } from 'corengf/src/utils/Helper';
import { WebTextField } from 'corengf/src/elements/canvas/text/WebTextField';
import { ISliderConfig, SliderFormat } from 'corengf/src/elements/canvas/slider/Slider';
import { MLUIConfig } from 'mlui/src/config/IMLUIConfig';
import { MLUIButtonId } from 'mlui/src/buttons/MLUIButtonId';


const mainConfig = new MLUIConfig();

const labelStyle = {
	fontFamily: Helper.DEFAULT_FONTS,
	fontSize: 24,
	align: 'left',
	fill: 'white',
};

const sliderData = {
	name: 'slider',
	trackAssetName: 'track',
	interactiveButtonConfig: {
		name: 'sliderButton',
		isOrientationSupported: true,
		scale: { x: 0.75, y: 0.75 },
		action: 'sliderButton',
		id: 'slider',
		productLine: mainConfig.productLine,
		assetName: 'button',
		isLocal: true
	},
	valueTextField: {
		elementType: WebTextField.elementType,
		elementConfig: {
			name: 'valueLimitTitle',
			visible: true,
			key: '',
			style: labelStyle
		}
	},
	sliderStepData: {
		defaultIndex: 6,
		items: [ 1, 2, 5, 10, 20, 40, -1 ]
	},
	width: 443,
	format: SliderFormat.CURRENCY_VALUE_ONLY
};

export const lossLimit: ISliderConfig = {
	...sliderData,
	name: 'lossLimit',
	isOrientationSupported: true,
	id: MLUIButtonId.SLIDER_LOSE,
	titleTextField: {
		elementType: WebTextField.elementType,
		elementConfig: {
			name: 'lossLimit',
			key: 'lossLimit',
			style: labelStyle
		}
	}
};

export const singleWinLimit: ISliderConfig = {
	...sliderData,
	name: 'singleWinLimit',
	isOrientationSupported: true,
	id: MLUIButtonId.SLIDER_SINGLE_WIN,
	titleTextField: {
		elementType: WebTextField.elementType,
		elementConfig: {
			name: 'singleWinLimit',
			key: 'singleWinLimit',
			style: labelStyle
		}
	}
};

export const winLimit: ISliderConfig = {
	...sliderData,
	name: 'winLimit',
	isOrientationSupported: true,
	id: MLUIButtonId.SLIDER_WIN,
	titleTextField: {
		elementType: WebTextField.elementType,
		elementConfig: {
			name: 'winLimit',
			key: 'winLimit',
			style: labelStyle
		}
	}
};



