import { Container } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Button } from 'corengf/src/elements/canvas/button/Button';
import { BaseButtonsStateSet, BaseButtonBehaviour } from 'corengf/src/buttons/BaseButtonStateSet';

export class BaseButtonsController {

	protected buttons: TsMap<string, Button>;
	protected buttonStates: BaseButtonsStateSet;

	constructor () {
		this.init();
	}

	protected init (): void {
		this.buttons = new TsMap<string, Button>();
		this.buttonStates = Container.get( BaseButtonsStateSet );
	}

	public addButton ( button: Button ): void {
		if ( !this.buttonStates.get( 'default' ).has( button.id ) ) {
			console.log( 'button [name]:' + button.id + ' has no deafult template declared' );
		}
		this.buttons.set( button.id, button );
	}

	public getButton ( buttonId: string ): Button {
		return this.buttons.get( buttonId );
	}

	/**
	 * Go through all buttons and sets their properties from ButtonsStateSet
	 * In most cases 'templateName' same as logic state name
	 * @param {string} templateName name for template, declared into 'BaseButtonsStateSet'
	 */
	public applyButtonsTemplate ( templateName: string ): void {
		if ( this.buttonStates.has( templateName ) ) {
			var defaultTemplate: TsMap<string, BaseButtonBehaviour> = this.buttonStates.get( BaseButtonsStateSet.DEFAULT_TEMPLATE );
			var appliedTemplate: TsMap<string, BaseButtonBehaviour> = this.buttonStates.get( templateName );

			this.buttons.keys().forEach( ( buttonId: string ) => {
				if ( appliedTemplate.has( buttonId ) ) {
					this.updateButton( this.buttons.get( buttonId ), appliedTemplate.get( buttonId ) );
				} else if ( defaultTemplate.has( buttonId ) ) {
					this.updateButton( this.buttons.get( buttonId ), defaultTemplate.get( buttonId ) );
				} else {
					console.warn(
						'There is no template for button ' + buttonId + ' in upcoming logical state ' + templateName +
						'\n Please add default tempalte or set template for state ' + templateName
					);
				}
			} );
		}
	}

	protected updateButton ( button: Button, behavior: BaseButtonBehaviour ): void {
		button.setEnable( behavior.enabled );
		button.visible = behavior.visible;
	}
}


