import { Inject } from 'typescript-ioc/es5';
import { Translator } from './../../utils/Translator';
import { DialogView } from './DialogView';
import { DialogConfig, IDialogConfig } from './DialogConfig';
import { TransitionSceneController } from './../transitionScene/TransitionSceneController';


export class DialogController extends TransitionSceneController {
	protected view: DialogView;

	@Inject
	protected config: DialogConfig;

	@Inject
	protected translator: Translator;

	constructor ( @Inject view: DialogView ) {
		super( view );
		this.view = view;
		this.view.dialogClosedSignal.add( this.onDialogClosed, this );
	}

	public init ( stateData: any ): void {
		const config = this.gameConfig.getConfig<IDialogConfig>( DialogConfig );
		if ( this.view != undefined ) {
			this.view.initialize( config );
		}
		this.finishStateAction();
	}

	protected onDialogClosed (): void {
		this.finishStateAction();
	}
}



