import { SlotNGHttpFormatter } from './../../SlotNGHttpFormatter';
import { FeatureTypes } from './../../../data/SlotNGFeatureDataDefinitions';

/**
 * Format pick feature request
 */
export class PickFeatureRequestMsgFormatter extends SlotNGHttpFormatter {

	/**
	 * sends button press emulation package
	 * @param data
	 */
	public codeMessage ( data: any ): any {
		return {
			player_id: this.slotNGModel.playerId,
			session_token: this.slotNGModel.sessionId,
			partner_code: this.slotNGModel.partnerCode,
			game_code: this.config.gameCode,
			action: FeatureTypes.PICK,
			state_tag: this.slotNGModel.stateTag,
			choice_id: this.getPickData( data )
		};
	}

	protected getPickData ( data: any ): any {
		return data.pick;
	}
}



