import { DialogController } from 'corengf/src/components/dialog/DialogController';
import { SlotDialogView } from './SlotDialogView';
import { Inject } from 'typescript-ioc/es5';
import { IFreeSpinsIntroPopupData, IFreeSpinsOutroPopupData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { NumberDisplayMode, MoneyFormatter } from 'corengf/src/utils/MoneyFormatter';

export class SlotDialogController extends DialogController {

	@Inject
	protected moneyFormatter: MoneyFormatter;

	protected view: SlotDialogView;

	constructor ( @Inject view: SlotDialogView ) {
		super( view );
	}

	protected freeSpinsIntroPopup ( data: IFreeSpinsIntroPopupData ): void {
		//TODO Fix messaging when design is complete

		const isInteractive = ( this.target === undefined );

		let titleKey: string = ( data.isRestore ) ? 'freeSpinDialogTitle_Restore' : 'freeSpinDialogTitle_Start';
		let textKey: string = 'Free Spins : ' + data.spins.toString();

		this.view.showDialogFromKey( titleKey, textKey, 10000, 500, true, isInteractive )
			.then( () => {
				if ( !this.target ) {
					this.view.setInteractive( true );
				}
			} )
			.then( () => {
				this.view.closeDialog();
			} );
	}

	protected freeSpinsOutroPopup ( data: IFreeSpinsOutroPopupData ): void {
		//TODO Fix messaging when design is complete
		const textKey = 'Total Won: ' + this.moneyFormatter.formatMoney( data.featureWin, NumberDisplayMode.VALUE_WITH_CURRENCY );
		this.view.showDialogFromKey( 'freeSpinDialogTitle_End', textKey, 10000, 500, true );
	}

	protected checkResource (): Promise<any> {
		return super.checkResource().then( () => {
			this.view.setInteractive( true );
			return Promise.resolve();
		} );
	}

	public onGroupCompleted ( groupIdx: number ): void {
		super.onGroupCompleted( groupIdx );

		if ( this.isReady ) {
			this.view.setInteractive( true );
		}
	}
}


