import { Container } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Scenario } from 'corengf/src/states/Scenario';
import { IWinningData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { FeatureTypes, IFreeSpinFeatureDetail } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { IGameState, IFeatureInfo, IInitResponse, ISpinResult } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { SlotNGHttpFormatter } from 'slotngf/src/server/ng/formatter/SlotNGHttpFormatter';
import { SpinResponseFormatter } from 'slotngf/src/server/ng/formatter/http/in/SpinResponseFormatter';
import { RotateFeatureFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/complete/RotateFeatureFormatter';
import { WildLockedFeatureFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/complete/WildLockedFeatureFormatter';
import { WildMultiplierFeatureFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/complete/WildMultiplierFeatureFormatter';
import { ReplacingSymbolsFeatureFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/complete/ReplacingSymbolsFeatureFormatter';
import { AbstractFeatureScenarioFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/AbstractFeatureScenarioFormatter';
import { FreeSpinRetriggeredFeatureFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/complete/FreeSpinRetriggeredFeatureFormatter';
import { SingleWildMultiplierFeatureFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/complete/SingleWildMultiplierFeatureFormatter';
import { FreeRespinReelsFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/pending/FreeRespinReelsFormatter';
import { MultiplierFeatureFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/complete/MultiplierFeatureFormatter';
import { SymbolCrawlFeatureFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/complete/SymbolCrawlFeatureFormatter';

/**
 *  formatting free spin feature
 */
export class FreeSpinFeatureFormatter extends SpinResponseFormatter {

	/**
	 * Map of incoming formats with message type as a key
	 */
	private _subFeatureFormatters: TsMap<string, SlotNGHttpFormatter>;

	protected get subFeatureFormatters (): TsMap<string, SlotNGHttpFormatter> {
		if ( this._subFeatureFormatters == undefined ) {
			this._subFeatureFormatters = this.setupFeatureFormatters();
		}
		return this._subFeatureFormatters;
	}

	/**
	 * Formatters that receive messages from server
	 */
	protected setupFeatureFormatters (): TsMap<string, SlotNGHttpFormatter> {
		let map = new TsMap<string, SlotNGHttpFormatter>();
		map.set( FeatureTypes.RETRIGGER, Container.get( FreeSpinRetriggeredFeatureFormatter ) );
		map.set( FeatureTypes.WILD_MULTIPLIER, Container.get( WildMultiplierFeatureFormatter ) );
		map.set( FeatureTypes.SINGLE_WILD_MULTIPLIER, Container.get( SingleWildMultiplierFeatureFormatter ) );
		map.set( FeatureTypes.WILD_LOCKED, Container.get( WildLockedFeatureFormatter ) );
		map.set( FeatureTypes.REPLACING_SYMBOLS, Container.get( ReplacingSymbolsFeatureFormatter ) );
		map.set( FeatureTypes.ROTATE, Container.get( RotateFeatureFormatter ) );
		map.set( FeatureTypes.FREE_RESPIN_REELS, Container.get( FreeRespinReelsFormatter ) );
		map.set( FeatureTypes.MULTIPLIER, Container.get( MultiplierFeatureFormatter ) );
		map.set( FeatureTypes.SYMBOL_CRAWL, Container.get( SymbolCrawlFeatureFormatter ) );
		return map;
	}

	/**
	 * formatting run feature api response
	 * @param message
	 */
	public processIncomingMessage ( message: IGameState, pendingFeatureInfo?: IFeatureInfo ): Scenario {
		if ( pendingFeatureInfo == undefined ) {
			return null;
		}

		let freeSpinFeatureState: IFreeSpinFeatureDetail = this.getFeatureDetail( pendingFeatureInfo );

		// if it's the beginning of the feature
		if ( freeSpinFeatureState.total_free_spin_count == freeSpinFeatureState.free_spins_left ) {
			return this.triggeredFeatureIncomingMessage( message, pendingFeatureInfo );
		} else {
			return this.freeSpinResponseScenario( message, pendingFeatureInfo );
		}
	}

	protected freeSpinResponseScenario ( message: IGameState, pendingFeatureInfo: IFeatureInfo ): Scenario {

		this.bankService.balance = message.balance - message.spin_result.win_amount;

		let responseScenario: Scenario = null;

		// format features before free spin stop
		responseScenario = this.getFeatureBeforeSpinStopScenario( message, responseScenario );

		// setup free spin stop scenario
		if ( responseScenario ) {
			responseScenario.addScenario( this.getFreeSpinStopScenario( message ) );
		} else {
			responseScenario = this.getFreeSpinStopScenario( message );
		}

		// format features after free spin stop
		responseScenario = this.getFormatFeatureTriggeredsScenario( message, responseScenario );

		responseScenario = this.getFormatFeaturesAfterSpinStopScenario( message, responseScenario );

		// setup free spin win scenario
		const winScenario: Scenario = this.getFreeSpinWinScenario( message );
		if ( winScenario ) {
			responseScenario.addScenario( winScenario );
		}

		// format features after free spin win/ no win
		responseScenario = this.getFeatureAfterWinScenario( message, responseScenario );

		responseScenario = this.getFreeSpinsScenario( responseScenario, message, pendingFeatureInfo );
		return responseScenario;
	}

	protected getFeatureBeforeSpinStopScenario ( message: IGameState, freeSpinStopScenario: Scenario ): Scenario {
		return this.getFeatureScenarioByFunction( 'getFeatureScenarioBeforeFreeSpinStop', message, freeSpinStopScenario );
	}

	/**
	 * get free spin stop scenario with state data
	 * @param message
	 */
	protected getFreeSpinStopScenario ( message: IGameState ): Scenario {
		let freeSpinStopScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.freeSpinsStop );
		if ( freeSpinStopScenario.hasStateName( this.stateNames.freeSpinsStop ) ) {
			freeSpinStopScenario.setStateData( this.stateNames.freeSpinsStop, {
				balance: message.balance,
				bet: this.bankService.bet,
				totalWin: message.spin_result.win_amount,
				reelIndexes: this.getReelSymbols( message.spin_result.reels ),
				lines: this.bankService.lines
			} );
		}

		freeSpinStopScenario = this.getFeatureSpinStopScenario( message, freeSpinStopScenario );

		return freeSpinStopScenario;
	}

	protected getFeatureSpinStopScenario ( message: IGameState, freeSpinStopScenario: Scenario ): Scenario {
		return this.getFeatureScenarioByFunction( 'getFeatureScenarioAfterSpinStop', message, freeSpinStopScenario );
	}

	/**
	 * get free spin win or without win scenario with state data
	 * @param message
	 */
	protected getFreeSpinWinScenario ( message: IGameState ): Scenario {
		let freeSpinWinScenario: Scenario = null;
		if ( message.spin_result.win_amount > 0 || message.spin_result.scatter_win != undefined ) {
			freeSpinWinScenario = this.scenarioManager.getScenario( this.scenarioNames.freeSpinsWithWinning );
			freeSpinWinScenario = this.setWinningData( freeSpinWinScenario, message );
		}
		return freeSpinWinScenario;
	}

	protected getFeatureAfterWinScenario ( message: IGameState, freeSpinStopScenario: Scenario ): Scenario {
		return this.getFeatureScenarioByFunction( 'getFeatureScenarioAfterWin', message, freeSpinStopScenario );
	}

	/**
	 * formatting feature in feature_triggered
	 */
	public triggeredFeatureIncomingMessage ( message: IGameState, featureTriggeredInfo: IFeatureInfo, ): Scenario {

		let responseScenario: Scenario = null;

		if ( message.spin_result ) {
			if ( message.spin_result.win_amount > 0 || message.spin_result.scatter_win != undefined ) {
				responseScenario = this.scenarioManager.getScenario( this.scenarioNames.featureWithWinning );
			}

			if ( responseScenario ) {
				responseScenario.addScenario( this.scenarioManager.getScenario( this.scenarioNames.transitionToFreeSpins ) );
			} else {
				responseScenario = this.scenarioManager.getScenario( this.scenarioNames.transitionToFreeSpins );
			}

		} else {
			responseScenario = this.scenarioManager.getScenario( this.scenarioNames.transitionToFreeSpins );
		}

		responseScenario.addScenario( this.scenarioManager.getScenario( this.scenarioNames.freeSpinsIdle ) );
		responseScenario.addScenario( this.scenarioManager.getScenario( this.scenarioNames.freeSpinContinue ) );
		return this.getFreeSpinsScenario( responseScenario, message, featureTriggeredInfo );
	}

	/**
	 * get feature formatter list
	 * @param type
	 */
	protected getFeatureFormatter ( type: string ): any {
		return ( this.subFeatureFormatters.get( type ) );
	}

	/**
	 * get show winning data with feature formatter
	 */
	protected getShowWinningData ( current_balance: number, spinResult: ISpinResult, featureInfos: Array<IFeatureInfo>, betValue: number ): IWinningData {
		let winningData: IWinningData = super.getShowWinningData( current_balance, spinResult, featureInfos, betValue );

		if ( spinResult.features_triggered && spinResult.features_triggered.length > 0 ) {
			spinResult.features_triggered.forEach( element => {
				let featureFormatter: AbstractFeatureScenarioFormatter = this.getFeatureFormatter( element.type );
				if ( featureFormatter != undefined && featureFormatter[ 'getFeatureShowWinningData' ] ) {
					winningData = featureFormatter.getFeatureShowWinningData( winningData, spinResult, element );
				}
			} );
		}
		return winningData;
	}

	/**
	 * reture free spins scenario,
	 * include feature triggered in free spin
	 * @param message
	 */
	protected getFreeSpinsScenario ( responseScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {

		let freeSpinFeatureState: IFreeSpinFeatureDetail = this.getFeatureDetail( featureInfo );

		const currentFreeSpinCount = freeSpinFeatureState.total_free_spin_count - freeSpinFeatureState.free_spins_left + 1;
		const totalFreeSpinCount = freeSpinFeatureState.total_free_spin_count;
		const currentFeatureWin = message.feature_win;

		if ( responseScenario.hasStateName( this.stateNames.transitionToFreeSpins ) ) {
			responseScenario.setStateData( this.stateNames.transitionToFreeSpins, {
				provider: message.provider,
				spins: freeSpinFeatureState.free_spins_left,
				totalFreeSpinCount: totalFreeSpinCount,
				currentFreeSpinCount: currentFreeSpinCount
			} );
			responseScenario.setStateData( this.stateNames.freeSpinsIntroPopup, {
				provider: message.provider,
				spins: freeSpinFeatureState.free_spins_left
			} );
			responseScenario.setStateData( this.stateNames.freeSpinsStart, {
				currentFreeSpinCount,
				totalFreeSpinCount,
				currentFeatureWin
			} );

		} else if ( !this.getPendingSubFeatures( featureInfo.subfeatures, message.features ) ) {
			// is end of free spin
			if ( freeSpinFeatureState.free_spins_left > 0 ) {
				let continueScenario = this.scenarioManager.getScenario( this.scenarioNames.freeSpinsIdle );
				continueScenario.addScenario( this.scenarioManager.getScenario( this.scenarioNames.freeSpinContinue ) );
				continueScenario.setStateData( this.stateNames.freeSpinsStart, {
					currentFreeSpinCount,
					totalFreeSpinCount,
					currentFeatureWin
				} );

				responseScenario.addScenario( continueScenario );
			} else {
				let endFreeSpinsScenario = this.scenarioManager.getScenario( this.scenarioNames.freeSpinsFinish );
				endFreeSpinsScenario.addStateData( this.stateNames.transitionToBaseGame, { featureWin: message.feature_win } );
				endFreeSpinsScenario.addStateData( this.stateNames.freeSpinsOutroPopup, { featureWin: message.feature_win } );
				responseScenario.addScenario( endFreeSpinsScenario );
			}
		}

		this.slotNGModel.stateTag = message.state_tag;
		return responseScenario;
	}

	protected getFeatureDetail ( featureInfo: IFeatureInfo ): any {
		return featureInfo.feature_state;
	}

	/**
	 * formatting feature while restore feature
	 */
	public restoreFeatureIncomingMessage ( freeSpinFeatureDetail: IFreeSpinFeatureDetail, features: Array<IFeatureInfo>,
		type?: string, message?: IInitResponse ): Scenario {
		let freeSpinFeatureScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.transitionToFreeSpins );

		// restore sub features, if there are any
		let freeSpinFeature: IFeatureInfo = features.find( e => e.type === FeatureTypes.FREE_SPIN );

		let subFeaturesScenario: Scenario = this.getSubfeaturesScenario( freeSpinFeature.subfeatures, features );
		if ( subFeaturesScenario != undefined ) {
			freeSpinFeatureScenario.addScenario( subFeaturesScenario );
		}

		// add scenarios to start free spins
		let startFreeSpinScenario: Scenario = this.getRestoreStartFreeSpinsScenario( freeSpinFeatureDetail, features, type, message );
		if ( startFreeSpinScenario != undefined ) {
			freeSpinFeatureScenario.addScenario( startFreeSpinScenario );
		}

		freeSpinFeatureScenario.setStateData( this.stateNames.freeSpinsIntroPopup, {
			type: type,
			spins: freeSpinFeatureDetail.free_spins_left,
			isRestore: true
		} );

		freeSpinFeatureScenario.setStateData( this.stateNames.transitionToFreeSpins, {
			type,
			provider: ( message ) ? message.state.provider : null,
			spins: freeSpinFeatureDetail.free_spins_left,
			totalFreeSpinCount: freeSpinFeatureDetail.total_free_spin_count,
			currentFreeSpinCount: freeSpinFeatureDetail.total_free_spin_count - freeSpinFeatureDetail.free_spins_left + 1
		} );

		freeSpinFeatureScenario = this.getFeatureRestoreScenario( features, freeSpinFeatureScenario );

		return freeSpinFeatureScenario;
	}

	protected getRestoreStartFreeSpinsScenario ( freeSpinFeatureDetail: IFreeSpinFeatureDetail, features: Array<IFeatureInfo>,
		type?: string, message?: IInitResponse ): Scenario {
		let startFreeSpinScenario: Scenario = null;

		let freeSpinFeature: IFeatureInfo = features.find( e => e.type === FeatureTypes.FREE_SPIN );
		if ( this.getPendingSubFeatures( freeSpinFeature.subfeatures, features ) ) {
			return startFreeSpinScenario;
		}

		startFreeSpinScenario = this.scenarioManager.getScenario( this.scenarioNames.freeSpinsIdle );
		startFreeSpinScenario.addScenario( this.scenarioManager.getScenario( this.scenarioNames.freeSpinContinue ) );

		startFreeSpinScenario.setStateData( this.stateNames.freeSpinsStart, {
			currentFreeSpinCount: freeSpinFeatureDetail.total_free_spin_count - freeSpinFeatureDetail.free_spins_left + 1,
			totalFreeSpinCount: freeSpinFeatureDetail.total_free_spin_count,
			currentFeatureWin: freeSpinFeatureDetail.total_free_spin_win_amount
		} );

		return startFreeSpinScenario;
	}

	protected getPendingSubFeatures ( subfeatures: Array<string>, features: Array<IFeatureInfo> ): boolean {
		let pendingSubFeature: boolean = false;

		subfeatures.forEach( element => {
			let featureFormatter: any = this.getFeatureFormatter( element );
			if ( featureFormatter != undefined ) {
				let subFeature: IFeatureInfo = features.find( e => e.type === element );
				if ( subFeature != undefined && !subFeature.complete ) {
					pendingSubFeature = true;
				}
			}
		} );

		return pendingSubFeature;
	}


	protected getSubfeaturesScenario ( subfeatures: Array<string>, features: Array<IFeatureInfo> ): Scenario {
		let subFeaturesScenario: Scenario = null;

		subfeatures.forEach( element => {
			let featureFormatter: any = this.getFeatureFormatter( element );
			if ( featureFormatter != undefined ) {
				let subFeature: IFeatureInfo = features.find( e => e.type === element );
				if ( subFeature != undefined && subFeature.complete ) {
					let subfeatureScenario: Scenario = featureFormatter.restoreFeatureIncomingMessage(
						subFeature.feature_state,
						features
					);

					if ( subfeatureScenario != undefined ) {
						if ( subFeaturesScenario == undefined ) {
							subFeaturesScenario = subfeatureScenario;
						} else {
							subFeaturesScenario.addScenario( subfeatureScenario );
						}
					}
				}
			}
		} );
		return subFeaturesScenario;
	}

	protected getFeatureRestoreScenario ( features: Array<IFeatureInfo>, restoreScenario: Scenario ): Scenario {
		if ( features ) {
			features.forEach( element => {
				let featureFormatter: AbstractFeatureScenarioFormatter = this.getFeatureFormatter( element.type );
				if ( featureFormatter != undefined && featureFormatter[ 'getFeatureRestoreScenario' ] ) {
					restoreScenario = featureFormatter.getFeatureRestoreScenario( restoreScenario, element );
				}
			} );
		}
		return restoreScenario;
	}

	protected getFormatFeaturesAfterSpinStopScenario ( message: IGameState, responseScenario: Scenario ): Scenario {
		let features: Array<IFeatureInfo> = message.features;
		if ( features ) {
			features.forEach( element => {
				let featureFormatter: AbstractFeatureScenarioFormatter = this.getFeatureFormatter( element.type );
				if ( featureFormatter != undefined && featureFormatter.getFeatureScenarioAfterSpinStop != undefined ) {
					featureFormatter.getFeatureScenarioAfterSpinStop( responseScenario, message, element );
				}
			} );
		}

		return responseScenario;
	}
}



