import { Inject } from 'typescript-ioc/es5';
import * as MiniSignal from 'mini-signals';
import { CompView } from 'corengf/src/mvc/CompView';
import { Asset } from 'corengf/src/core/AssetLoader';
import { SlotSoundConfig } from 'slotngf/src/core/config/SlotSoundConfig';
import { MoneyFormatter, NumberDisplayMode } from 'corengf/src/utils/MoneyFormatter';
import { SpineAnimation } from 'corengf/src/elements/canvas/animation/spine/SpineAnimation';
import { TweenBitmapTextField } from 'corengf/src/elements/canvas/text/TweenBitmapTextField';
import { BigWinTierType, IBigWinConfig } from 'slotngf/src/core/config/IBigWinConfig';


export class BigWinView extends CompView {

	@Inject
	protected moneyFormatter: MoneyFormatter;

	protected config: IBigWinConfig;
	protected bigWinValueLabel: TweenBitmapTextField;

	protected bigWinAnimation: SpineAnimation;

	protected isShowing: boolean = false;

	private _finalBigWinTier: string;

	public screenTapSignal: MiniSignal;

	public get finalBigWinTier (): string {
		return this._finalBigWinTier;
	}

	public set finalBigWinTier ( value: string ) {
		this._finalBigWinTier = value;
	}

	protected createElements ( config: IBigWinConfig ): void {
		this.config = config;

		this.setupBigWinAnimation();
		this.setupBigWinLabel( config );
		this.screenTapSignal = new MiniSignal();
	}

	public registerEvents (): void {
		super.registerEvents();
		this.gameContainerManager.stageContainer.on( 'pointertap', () => {
			this.screenTapSignal.dispatch();
		}, this );
	}

	public layoutElements (): void {
		super.layoutElements();
		if ( this.config.bigWinValueLabel.autoscale == undefined ) {
			this.bigWinValueLabel.setAutoscale( this.viewport.width * 0.9, this.viewport.height );
		}
	}

	protected setupBigWinLabel ( config: IBigWinConfig ): void {
		this.bigWinValueLabel = new TweenBitmapTextField( config.bigWinValueLabel );
		this.bigWinValueLabel.text = '0.00';
		this.bigWinValueLabel.alpha = 0;
		this.addChild( this.bigWinValueLabel );
	}

	protected setupBigWinAnimation (): void {
		this.bigWinAnimation = new SpineAnimation( this.config.bigWinAnimation );
		this.addChild( this.bigWinAnimation );
	}

	public beginCountUp (): void {
		this.isShowing = true;

		this.bigWinValueLabel.text = '0.00';
		this.animateBigWinIn();
		this.playBigWinAnimation( this.finalBigWinTier );
	}

	protected playBigWinAnimation ( finalBigWinTier: string ): void {
		if ( this.hasUniqueAnimation( finalBigWinTier ) ) {
			this.bigWinAnimation.play( 0, finalBigWinTier, false );
		}
	}

	protected animateBigWinIn (): void {
		this.bigWinValueLabel.alpha = 1;
		this.bigWinValueLabel.startTween();
	}

	public updateCountUp ( value: number ): void {
		if ( value > this.moneyFormatter.unformatMoney( this.bigWinValueLabel.text ) ) {
			this.bigWinValueLabel.text = this.moneyFormatter.formatMoney( value, NumberDisplayMode.VALUE_ONLY );
		}
	}

	public newTierReached ( bigWinTier: string ): void {
		//
	}

	public completeCountUp ( completeValue: number, forceStop: boolean = false ): Promise<any> {
		return Promise.all( [ this.spriteCompleteAnimation( forceStop ), this.labelCompleteAnimation( completeValue, forceStop ) ] ).then( () => {
			this.isShowing = false;
		} );
	}

	public cancelCountUp ( completeValue: number, forceStop: boolean = false ): Promise<any> {
		return this.completeCountUp( completeValue, forceStop );
	}

	protected spriteCompleteAnimation ( forceStop: boolean = false ): Promise<any> {
		if ( this.bigWinAnimation ) {
			this.bigWinAnimation.reset( true );
		}
		return Promise.resolve();
	}

	protected labelCompleteAnimation ( completeValue: number, forceStop: boolean = false ): Promise<any> {
		this.updateCountUp( completeValue );

		if ( forceStop ) {
			this.bigWinValueLabel.stopTween();
			this.bigWinValueLabel.alpha = 0;
			return Promise.resolve();
		}

		return new Promise( ( resolve ) => {

			this.bigWinValueLabel.startTween(
				this.config.valueLabelTweenOutConfig,
				( () => {
					this.bigWinValueLabel.alpha = 0;
					resolve();
				} )
			);
		} );
	}

	protected hasUniqueAnimation ( tier: string ): boolean {
		return [ BigWinTierType.BigWin, BigWinTierType.ExtremeBigWin, BigWinTierType.UltraExtremeBigWin ].indexOf( tier ) >= 0;
	}

	public getAssetsList (): Array<Asset> {
		return [
			{
				assetKey: 'bigwinSpineAnimation',
				assetURL: 'assets/bigwin/{locale}/BigWinAnimation.json'
			},
			{
				assetKey: 'BigWinFont',
				assetURL: 'assets/fonts/bitmap/BigWinFont.xml'
			},
			{
				assetKey: SlotSoundConfig.creditLoop,
				assetURL: 'assets/sound/game/Credit_Loop.mp3'
			},
			{
				assetKey: SlotSoundConfig.creditLoopEnd,
				assetURL: 'assets/sound/game/Credit_Loop_End.mp3'
			},
		];
	}
}


