import { WebElementFactory } from 'corengf/src/factory/WebElementFactory';
import { IWebImageConfig } from 'corengf/src/elements/webpage/image/WebImageElement';
import { CoreWebElement, ICoreWebElementConfig } from 'corengf/src/elements/webpage/CoreWebElement';

/**
 * Payout element for symbol
 */
export class WebPayoutElement extends CoreWebElement {

	public static elementType: string = 'WEB_PAYOUT_ELEMENT';

	public createElement ( config: IWebPayoutConfig ): IWebPayoutConfig {

		if ( config.arrowSymbol ) {
			config.arrowSymbol = WebElementFactory.createInstance( config.arrowSymbol );
		}

		return config;
	}
}

export interface IWebPayoutConfig extends ICoreWebElementConfig {
	lineFeed: number;
	payoutPrize: Array<any>;
	payoutFormat: IPayoutFormat;
	arrowSymbol?: IWebImageConfig;
}

export interface IPayoutFormat {
	payoutStyle: string;
	numberStyle?: string;
	listStyle?: string;
}


