import { TsMap } from 'corengf/src/utils/TsMap';
import { Singleton, Container } from 'typescript-ioc/es5';
import { AbstractSdkController } from 'corengf/src/server/abstract/sdk/AbstractSdkController';
import { SDK, AbstractSdkSupportList } from 'corengf/src/server/abstract/sdk/AbstractSdkSupportList';
import { NGBNGController } from 'corengf/src/server/ng/sdk/bng/NGBNGController';

/**
 * SDK support list for intergate with third party sdk
 */
@Singleton
export class NGSDKSupportList extends AbstractSdkSupportList {

	/**
	 * Formatters that receive messages from server
	 */
	protected setupSDKSupportList (): TsMap<string, AbstractSdkController> {
		let map = new TsMap<string, AbstractSdkController>();

		const controller: AbstractSdkController = Container.get( NGBNGController );
		controller.init();

		map.set( NGSDK.BNG, controller );
		return map;
	}
}

export class NGSDK extends SDK {
	public static readonly BNG: string = 'BNG';
}



