import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { CompView } from 'corengf/src/mvc/CompView';
import { Translator } from 'corengf/src/utils/Translator';
import { MarqueeConfig, IMarqueeConfig } from 'corengf/src/components/marquee/MarqueeConfig';
import { ScrollMarquee, MarqueeDisplayMode } from 'corengf/src/elements/canvas/scroll/ScrollMarquee';


export class MarqueeView extends CompView {

	public onMarqueeTweenCompleteSignal: MiniSignal;

	@Inject
	protected translator: Translator;

	protected config: IMarqueeConfig;

	protected scrollMarquee: ScrollMarquee;

	protected createElements ( config: MarqueeConfig ): void {
		if ( config == undefined ) {
			return;
		}

		this.onMarqueeTweenCompleteSignal = new MiniSignal();

		this.scrollMarquee = new ScrollMarquee( config.scrollMarqueeConfig );
		this.scrollMarquee.onMarqueeTweenCompleteSignal.add( this.onMarqueeTweenComplete, this );
		this.addChild( this.scrollMarquee );
	}

	protected onMarqueeTweenComplete (): void {
		this.onMarqueeTweenCompleteSignal.dispatch();
	}

	public setMarqueeLabel ( message: string, mode?: MarqueeDisplayMode ): void {
		this.scrollMarquee.setMarqueeText( message, mode );
	}
}


