import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { Helper } from 'corengf/src/utils/Helper';
import { CoreState } from 'corengf/src/states/CoreState';
import { CompController } from 'corengf/src/mvc/CompController';
import { Asset, AssetLoader } from 'corengf/src/core/AssetLoader';
import { ResourceConfig } from 'corengf/src/core/config/ResourceConfig';

/**
 * ComponentManager contains all components and it delivers events from components to GameController.
 */
export class ComponentManager {

	@Inject
	protected assetLoader: AssetLoader;

	protected _components: Array<CompController>;

	public get components (): Array<CompController> {
		return this._components;
	}

	get componentsLength (): number {
		return this._components.length;
	}

	/**
	 * Event that notifies others that ALL components are ready to go to the next state.
	 */
	public componentsFinishedStateSignal: MiniSignal;

	/**
	 * Determine if all components are ready to go to the next state from scenario
	 */
	protected _isReadyForNextState: boolean;

	/**
	 * List of ID of components that are ready to go to the next state.
	 */
	private readyComponents: Array<string>;

	constructor () {
		this._components = new Array<CompController>();
		this.readyComponents = new Array<string>();

		this.componentsFinishedStateSignal = new MiniSignal();

		this._isReadyForNextState = true;

		this.assetLoader.onGroupCompleted.add( this.onGroupCompleted.bind( this ) );
		this.assetLoader.onGroupProgress.add( this.onGroupProgress.bind( this ) );
	}

	/**
	 * Set the state for ALL components.
	 * @param state current state
	 */
	public setState ( state: CoreState ): void {
		this._isReadyForNextState = false;
		this.readyComponents = [];

		this._components.forEach( ( element ) => {
			element.setState( state );
		} );
	}

	/**
	 * Update state data for state of components
	 * @param state state with updated stateData
	 */
	public updateState ( state: CoreState ): void {
		this._components.forEach( ( element ) => {
			element.invokeStateMethod( state );
		} );
	}

	public skipState ( state: CoreState ): void {
		this._components.forEach( ( element ) => {
			element.skipState( state );
		} );
	}

	/**
	 * Add Component to manager
	 * @param component component to add
	 */
	public addComponent ( component: CompController ): void {
		component.finishStateActionSignal.add( this.onComponentHandleState, this );

		this._components.push( component );
	}

	/**
	 * Remove component from manager
	 *
	 * @param {CompController} component
	 * @memberof ComponentManager
	 */
	public removeComponent ( component: CompController ): void {
		component.finishStateActionSignal.detachAll();
		Helper.removeItemsFromArray( this.components, component );
	}

	/**
	 * Retrieves assets list
	 * It is used in Preloader.ts
	 */
	get listOfFilesToLoad (): Array<Asset> {
		var assetsToLoad: Array<Asset> = new Array<Asset>();
		this._components.forEach( element => {
			assetsToLoad = assetsToLoad.concat( element.loaderList );
		} );

		return assetsToLoad;
	}

	/**
	 * Register components that finished all actions needed for this state.
	 * @param componentID id of component that completed all actions for this state
	 */
	public onComponentHandleState ( componentID: string ): void {
		if ( this.readyComponents.indexOf( componentID ) < 0 ) {
			this.readyComponents.push( componentID );
		}

		if ( this.readyComponents.length == this._components.length ) {
			this._isReadyForNextState = true;
			this.componentsFinishedStateSignal.dispatch();
		}
	}

	get isReadyForNextState (): boolean {
		return this._isReadyForNextState;
	}

	protected onGroupCompleted ( groupIndex: number ): void {
		this._components.forEach( component => {
			if ( groupIndex === ResourceConfig.LANDSCAPE_VIEW ) {
				component.onLandscapeViewLoaded();
			} else if ( groupIndex === ResourceConfig.PORTRAIT_VIEW ) {
				component.onPortraitViewLoaded();
			} else {
				component.onGroupCompleted( groupIndex );
			}
		} );
	}

	protected onGroupProgress ( groupIndex: number, ratio: number ): void {
		this._components.forEach( component => {
			component.onGroupProgress( groupIndex, ratio );
		} );
	}

	public getComponentByID ( componentID: string ): any {
		return this._components.find( e => e.componentID === componentID );
	}
}


