import { Inject } from 'typescript-ioc/es5';
import { AudioTrackName } from 'corengf/src/audio/SoundManager';
import { NumberDisplayMode } from 'corengf/src/utils/MoneyFormatter';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { IFreeSpinsOutroPopupData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { SlotDialogController } from 'slotngf/src/components/dialog/SlotDialogController';
import { DHBSlotDialogView } from 'game/src/components/dialog/DHBSlotDialogView';
import { DHBDialogLabelTickConsumer } from 'game/src/components/dialog/DHBDialogLabelTickConsumer';

export class DHBSlotDialogController extends SlotDialogController {

	protected dialgState: DialogState = DialogState.DEFAULT;
	public static readonly OUTRO_DIALOG_CLOSED: string = 'OutroDialogClosed';
	protected view: DHBSlotDialogView;
	protected featureWin: number;

	@Inject
	protected labelTickConsumer: DHBDialogLabelTickConsumer;

	constructor ( @Inject view: DHBSlotDialogView ) {
		super( view );
	}

	public addEventListeners (): void {
		super.addEventListeners();
		this.view.startLabelCountUpSignal.add( this.beginCountUp, this );
		this.labelTickConsumer.updateCountSignal.add( this.updateCountUp, this );
		this.labelTickConsumer.completeCountSignal.add( this.completeCountUp, this );
	}

	protected beginCountUp (): void {
		this.labelTickConsumer.beginCountUp( this.featureWin );
		this.soundManager.play( 'RollupBeep', AudioTrackName.EFFECT, true );
		this.view.beginCountUp();
	}

	protected updateCountUp ( value: number ): void {
		this.view.updateCountUp( value );
	}

	protected completeCountUp ( value: number ): void {
		this.soundManager.stop( 'RollupBeep' );
		this.view.completeCountUp( value );
	}

	protected freeSpinsIntroPopup ( data: any ): void {
		this.dialgState = DialogState.INTRO;
		this.view.playIntroAnimation( data.freeSpinType );
	}

	protected freeSpinsOutroPopup ( data: IFreeSpinsOutroPopupData ): void {
		this.dialgState = DialogState.OUTRO;
		//unused winAmount
		const winAmount = this.moneyFormatter.formatMoney( data.featureWin, NumberDisplayMode.VALUE_ONLY );
		this.view.playOutroAnimation( 'animation', winAmount );
		this.featureWin = data.featureWin;
	}

	protected onDialogClosed (): void {
		if ( this.dialgState == DialogState.OUTRO ) {
			GlobalDispatcher.dispatchIntoAllStates( DHBSlotDialogController.OUTRO_DIALOG_CLOSED, 'closeOutroDialog' );
		}
		this.dialgState = DialogState.DEFAULT;
		super.onDialogClosed();
	}
}

enum DialogState {
	INTRO,
	OUTRO,
	DEFAULT
}


