import { DisplayObject } from 'pixi.js';
import { ICoreElementConfig } from './../elements/canvas/CoreElement';
import { Sprite, ISprite } from 'corengf/src/elements/canvas/sprite/Sprite';
import { Button, IButtonConfig } from 'corengf/src/elements/canvas/button/Button';
import { WebTextField, IWebTextFieldConfig } from 'corengf/src/elements/canvas/text/WebTextField';
import { BitmapTextField, IBitmapTextFieldConfig } from 'corengf/src/elements/canvas/text/BitmapTextField';
import { SpineAnimation, ISpineAnimationConfig } from 'corengf/src/elements/canvas/animation/spine/SpineAnimation';
import { SpineSoundAnimation, ISpineSoundAnimationConfig } from 'corengf/src/elements/canvas/animation/spine/SpineSoundAnimation';

/**
 * elements that can be produce by core ui element factory
 */
const ElementList: any = {
	WebTextField,
	BitmapTextField,
	Button,
	Sprite,
	SpineSoundAnimation,
	SpineAnimation
};

/**
 * a factory to create all core ui element.
 */
export class CoreUIElementFactory {
	public static createInstance<T extends DisplayObject> ( coreUIElementConfig: ICoreUIFactoryElementConfig, ...args: Array<any> ): T {
		if ( ElementList[ coreUIElementConfig.elementType ] != undefined ) {
			return new ElementList[ coreUIElementConfig.elementType ]( coreUIElementConfig.elementConfig, ...args );
		}
		throw new Error( `Class type \'${ coreUIElementConfig.elementType }\' is not in element list` );
	}
}

export interface ICoreUIFactoryElementConfig {
	elementType: string;
	elementConfig: ICoreElementConfig;
}

export interface ICoreUITextFieldElementConfig extends ICoreUIFactoryElementConfig {
	elementConfig: IWebTextFieldConfig | IBitmapTextFieldConfig;
}

export interface ICoreUISpineAnimationElementConfig extends ICoreUIFactoryElementConfig {
	elementConfig: ISpineAnimationConfig | ISpineSoundAnimationConfig;
}

export interface ICoreUIButtonElementConfig extends ICoreUIFactoryElementConfig {
	elementConfig: IButtonConfig;
}

export interface ICoreUISpriteElementConfig extends ICoreUIFactoryElementConfig {
	elementConfig: ISprite;
}


