import { Inject } from 'typescript-ioc/es5';
import { MoneyFormatter, NumberDisplayMode } from 'corengf/src/utils/MoneyFormatter';
import { SlotNGHttpFormatter } from 'slotngf/src/server/ng/formatter/SlotNGHttpFormatter';
import { IHistorySummaryData, IHistorySummary, IHistorySummaryResult, IHistorySummaryResultLog } from 'slotngf/src/states/SlotStatesDataInterfaces';

/**
 * formatter for history summary api. (* reminder: RESULT_HISTORY_SUMMAY same with HISTORY_SUMMARY )
 * currently, only use by result panel. 2020/03/12
 */
export class HistorySummaryFormatter extends SlotNGHttpFormatter {

	@Inject
	protected moneyFormatter: MoneyFormatter;

	public processIncomingMessage ( message: IHistorySummary ): any {
		return this.getSummaryLog( message.data );
	}

	protected getSummaryLog ( data: IHistorySummaryData ): IHistorySummaryResult {
		// sort by time
		data.payload.sort( ( a, b ) => {
			return ( new Date( a.date ) ).getTime() - ( new Date( b.date ) ).getTime();
		} );

		let summary: Array<IHistorySummaryResultLog> = new Array<IHistorySummaryResultLog>();
		for ( let i = 0; i < data.payload.length; i++ ) {
			let payload: IHistorySummaryResultLog = {
				date: data.payload[ i ].date.toString(),
				roundId: data.payload[ i ].round_id,
				bet: this.moneyFormatter.formatMoney( data.payload[ i ].bet_value, NumberDisplayMode.VALUE_ONLY ),
				win: this.moneyFormatter.formatMoney( data.payload[ i ].total_win, NumberDisplayMode.VALUE_ONLY ),
				balance: this.moneyFormatter.formatMoney( data.payload[ i ].balance, NumberDisplayMode.VALUE_ONLY ),
				roundType: data.payload[ i ].round_type,
				withFreeSpin: data.payload[ i ].with_free_spin,
				featureTrack: data.payload[ i ].feature_track,
				isWin: ( data.payload[ i ].total_win > 0 )
			};
			summary.push( payload );
		}

		const result: IHistorySummaryResult = {
			summaryLog: summary,
			query: data.query
		};

		return result;
	}
}



