import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { UIPanelController } from 'corengf/src/components/ui/components/UIPanelController';
import { MLUITurboPanel } from './MLUITurboPanel';
import { IMLUITurboConfig, MLUITurboConfig } from './MLUITurboConfig';

export class MLUITurboController extends UIPanelController {

	protected view: MLUITurboPanel;

	public turboModeSignal: MiniSignal;

	constructor ( @Inject view: MLUITurboPanel ) {
		super( view );
		this.turboModeSignal = new MiniSignal();
	}

	public init ( data: any ): void {
		let config = this.gameConfig.getConfig<IMLUITurboConfig>( MLUITurboConfig );
		this.view.initialize( config );
		this.setButtonActionEventListeners( config.turboButton );
		this.turboModeSignal.dispatch( config.turboButton.speedMode[ 0 ] );
		this.finishStateAction();
	}

	/**
	 * action callback from local button controller.
	 * on turbo button clicked
	 * @param turboModeData
	 */
	protected action_turboModeOption ( turboModeData: any ): void {
		this.turboModeSignal.dispatch( turboModeData.speed );
	}

	public setTurboButtonMode ( speed: number ): void {
		this.view.setTurboButtonMode( speed );
	}

}


