import { ButtonsController } from 'corengf/src/buttons/ButtonsController';
import { ComponentManager } from 'corengf/src/components/ComponentManager';
import { GameConfig } from 'corengf/src/core/GameConfig';
import { ScenarioManager } from 'corengf/src/core/ScenarioManager';
import { StateMachine } from 'corengf/src/core/StateMachine';
import { IListener } from 'corengf/src/events/GlobalDispatcher';
import { AbstractServer } from 'corengf/src/server/abstract/AbstractServer';
import { Scenario } from 'corengf/src/states/Scenario';
import { ScenarioNames } from 'corengf/src/states/ScenarioNames';
import { States } from 'corengf/src/states/States';
import * as MiniSignal from 'mini-signals';
import { Container, Inject, Singleton } from 'typescript-ioc/es5';

/**
 * GameController is the main class that connects Server, StateMachine and Components.
 * This class is responsible for communications between them.
 */
@Singleton
export class GameController implements IListener {

	protected _currentStateName: string;
	get currentStateName (): string {
		return this._currentStateName;
	}

	protected componentManager: ComponentManager;
	protected stateMachine: StateMachine;
	protected server: AbstractServer;

	@Inject
	protected buttonsController: ButtonsController;

	@Inject
	protected gameConfig: GameConfig;

	@Inject
	protected scenarioManager: ScenarioManager;

	@Inject
	protected scenarioNames: ScenarioNames;

	@Inject
	protected stateNames: States;

	public gameReadySignal: MiniSignal;

	constructor () {
		this.addEventListeners();
		this.gameReadySignal = new MiniSignal();
	}

	/**
	 * Processing actions from component
	 */
	public addEventListeners (): void {
		//
	}

	/**
	 * Trigered
	 * @param action meta
	 */
	public handleButtonAction ( action: string, data?: any ): void {
		if ( typeof this[ action ] === 'function' ) {
			this[ action ]( data );
		}
	}

	public createStateMachine ( componentManager: ComponentManager ): void {
		if ( this.stateMachine == undefined ) {
			this.componentManager = componentManager;
			this.stateMachine = Container.get( StateMachine );
			this.stateMachine.initialize( componentManager );
			this.stateMachine.isPause = true;
			this.stateMachine.stateIsChangedSignal.add( this.onStateIsChanged, this );
			this.stateMachine.finishStackSignal.add( this.onFinishStack, this );
		} else {
			throw 'stateMachine is already created';
		}
	}

	public setServer ( server: AbstractServer ): void {
		if ( this.server == undefined ) {
			this.server = server;
			this.server.serverResponseSignal.add( this.onServerResponse, this );
		} else {
			throw 'server is already set';
		}
	}

	protected onStateIsChanged ( stateName: string, stateData: any ): void {
		this._currentStateName = stateName;
		this.buttonsController.applyButtonsTemplate( stateName );
		this.server.processState( stateName, stateData );
		if ( stateName === this.stateNames.watingEnterGame ) {
			this.stateMachine.isPause = true;
			this.gameReadySignal.dispatch();
		}

	}

	protected onFinishStack (): void {
		//do nothing
	}

	protected onServerResponse ( scenario: Scenario ): void {
		this.stateMachine.pushStates( scenario.getStates() );
	}

	public action_onGameStart (): void {
		this.stateMachine.isPause = false;
	}

	public send ( messageType: string, data?: any ): void {
		return this.server.send( messageType, data );
	}
}


