import * as MiniSignal from 'mini-signals';
import { Graphics, spine } from 'pixi.js';
import { Inject } from 'typescript-ioc/es5';
import { Asset } from 'corengf/src/core/AssetLoader';
import { SimpleTween } from 'corengf/src/time/SimpleTween';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import { CoreUIElementFactory } from 'corengf/src/factory/CoreUIElementFactory';
import { BitmapTextField } from 'corengf/src/elements/canvas/text/BitmapTextField';
import { MoneyFormatter, NumberDisplayMode } from 'corengf/src/utils/MoneyFormatter';
import { SpineAnimation } from 'corengf/src/elements/canvas/animation/spine/SpineAnimation';
import { SlotDialogView } from 'slotngf/src/components/dialog/SlotDialogView';
import { IDHBDialogConfig } from 'game/src/components/dialog/DHBDialogConfig';

export class DHBSlotDialogView extends SlotDialogView {

	@Inject
	protected moneyFormatter: MoneyFormatter;

	protected config: IDHBDialogConfig;

	protected introAnimation: SpineAnimation;
	protected outroAnimation: SpineAnimation;

	protected winValueLabel: BitmapTextField;
	public startLabelCountUpSignal: MiniSignal;

	private readonly completeAnimations: Array<string> = [ 'DragonSpinsFeature', 'FortuneSpinsFeature', 'LuckySpinFeature', 'animation' ];

	constructor () {
		super();
		this.startLabelCountUpSignal = new MiniSignal();
	}

	protected createElements ( config?: any ): void {
		this.config = config;
		this.background = new Graphics();
		this.background.beginFill( 0, 0.8 );
		this.background.drawRect( 0, 0, this.viewport.width, this.viewport.height );

		this.addChild( this.background );

		this.introAnimation = CoreUIElementFactory.createInstance( this.config.introAnimation );
		this.introAnimation.completeSignal.add( this.animationComplete, this );
		this.introAnimation.alpha = 0;
		this.outroAnimation = CoreUIElementFactory.createInstance( this.config.outroAnimation );
		this.outroAnimation.completeSignal.add( this.animationComplete, this );
		this.outroAnimation.alpha = 1;

		this.introAnimation.setEmptyAnimation( 0 );
		this.addChild( this.introAnimation );
		this.outroAnimation.setEmptyAnimation( 0 );
		this.addChild( this.outroAnimation );

		this.winValueLabel = new BitmapTextField( this.config.winValueLabel );
		this.winValueLabel.text = '0.00';
		this.winValueLabel.visible = false;
		this.addChild( this.winValueLabel );

		this.visible = false;

	}

	public beginCountUp (): void {
		this.winValueLabel.text = '0.00';
	}

	public updateCountUp ( value: number ): void {
		if ( value > this.moneyFormatter.unformatMoney( this.winValueLabel.text ) ) {
			this.winValueLabel.text = this.moneyFormatter.formatMoney( value, NumberDisplayMode.VALUE_ONLY );
		}
	}

	public completeCountUp ( completeValue: number ): void {
		this.winValueLabel.text = this.moneyFormatter.formatMoney( completeValue, NumberDisplayMode.VALUE_ONLY );
	}

	public playIntroAnimation ( name: string ): void {
		this.visible = true;
		this.winValueLabel.visible = false;
		this.mainContainer.alpha = 1;
		this.setInteractive( true );
		new SimpleTimer( 1000, () => {
			this.introAnimation.alpha = 1;
			this.introAnimation.play( 0, name, false );
		} );

	}

	public playOutroAnimation ( name: string, winAmount: string ): void {
		this.visible = true;
		this.mainContainer.alpha = 1;
		this.setInteractive( true );
		new SimpleTimer( 1000, () => {
			this.outroAnimation.alpha = 1;
			this.outroAnimation.play( 0, name, false );
		} );
	}

	protected animationComplete ( animation: SpineAnimation, track?: spine.core.TrackEntry ): void {

		if ( track.animation.name == 'animation' ) {
			this.winValueLabel.visible = true;
			this.startLabelCountUpSignal.dispatch();
			new SimpleTween( this.mainContainer )
				.delay( 4000 )
				.to( { alpha: 0 }, 1000 )
				.onComplete( () => {
					animation.setEmptyAnimation( 0 );
					animation.alpha = 0;
					this.visible = false;
					this.setInteractive( false );
					this.dialogClosedSignal.dispatch();
				} )
				.start();
		} else if ( this.completeAnimations.indexOf( track.animation.name ) >= 0 ) {
			new SimpleTween( this.mainContainer )
				.to( { alpha: 0 }, 1000 )
				.onComplete( () => {
					animation.setEmptyAnimation( 0 );
					animation.alpha = 0;
					this.visible = false;
					this.setInteractive( false );
					this.dialogClosedSignal.dispatch();
				} )
				.start();
		}

	}

	public portraitLayout (): void {
		super.portraitLayout();
		this.background.position.set( 10, 359 );
		this.background.width = 704;
		this.background.height = 509;
	}

	public landscapeLayout (): void {
		super.landscapeLayout();
		this.background.position.set( 265, 25 );
		this.background.width = 750;
		this.background.height = 540;
	}

	public getAssetsList (): Array<Asset> {
		const asset = [
			{
				assetKey: 'spinsFeature',
				assetURL: 'assets/dialog/{locale}/SpinsFeature.json'
			},
			{
				assetKey: 'featureComplete',
				assetURL: 'assets/dialog/{locale}/Feature Complete.json'
			}
		];

		return asset;
	}

}


