import { ISymbolsData } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { SpinRequestMsgFormatter, ISpinRequestMsgConfig } from './SpinRequestMsgFormatter';

/**
 * force all symbols on reel
 */
export class ForceSpinMsgFormatter extends SpinRequestMsgFormatter {

	public codeMessage ( data?: any ): any {
		let spinRequest: IFroceSpinRequestMsgConfig = <IFroceSpinRequestMsgConfig> super.codeMessage( data );
		if ( data && data.symbols ) {
			spinRequest.reels = this.getMatrix( data.symbols );
		}

		if ( spinRequest.force_data ) {
			delete spinRequest.force_data;
		}
		return spinRequest;
	}

}

export interface IFroceSpinRequestMsgConfig extends ISpinRequestMsgConfig {
	reels: Array<ISymbolsData>;
}


