import { Inject } from 'typescript-ioc/es5';
import { IReelsConfig } from 'slotngf/src/core/config/IReelsConfig';
import { ReelConfig } from 'slotngf/src/components/reelV2/ReelConfig';
import { WinDisplayV2Controller } from 'slotngf/src/components/winDisplay/base/reelV2/WinDisplayV2Controller';
import { IWinBoxConfig, WinBoxConfig } from 'slotngf/src/components/winDisplay/winBoxes/WinBoxConfig';
import { WinBoxView } from 'slotngf/src/components/winDisplay/winBoxes/WinBoxView';

export class WinBoxV2Controller extends WinDisplayV2Controller {

	protected view: WinBoxView;

	constructor ( @Inject view: WinBoxView ) {
		super( view );
	}

	public init (): void {
		let config = this.gameConfig.getConfig<IWinBoxConfig>( WinBoxConfig );
		this.view.setReelsConfig( this.gameConfig.getConfig<IReelsConfig>( ReelConfig ) );
		this.view.initialize( config );
		this.finishStateAction();
	}

	protected startSpin (): void {
		this.view.hide();
		this.finishStateAction();
	}

	protected startPickFeature (): void {
		this.view.hide();
		this.finishStateAction();
	}

	public freeSpinsIdle (): void {
		this.view.hide();
		this.finishStateAction();
	}

	public transitionToBaseGame (): void {
		this.view.hide();
		this.finishStateAction();
	}

	public transitionToFreeSpins (): void {
		this.view.hide();
		this.finishStateAction();
	}

	protected transitionToBonusSpins (): void {
		this.view.hide();
		this.finishStateAction();
	}

	public skip_freeSpinsAllLineWin (): void {
		this.view.hide();
		this.finishStateAction();
	}

	public skip_freeSpinsShowWinning (): void {
		this.view.hide();
		this.finishStateAction();
	}
}


