import { Inject } from 'typescript-ioc/es5';
import { CompView } from 'corengf/src/mvc/CompView';
import { Orientation } from 'corengf/src/display/Viewport';
import { AssetLoader } from 'corengf/src/core/AssetLoader';
import { ResourceConfig } from 'corengf/src/core/config/ResourceConfig';


export class OrientationHintView extends CompView {

	private container: HTMLDivElement = null;

	private supportOrientation: Orientation = null;

	@Inject
	protected assetLoader: AssetLoader;

	public initialize ( supportOrientation?: any ): void {
		if ( supportOrientation ) {
			this.supportOrientation = supportOrientation;
		} else {
			console.warn( 'gameConfig:orientation is undefined' );
		}

		//if ( supportOrientation !== Orientation.BOTH ) {
		this.createElements( supportOrientation );
		this.registerEvents();
		//}
		this.layoutElements();
	}

	public portraitLayout (): void {
		if ( !this.viewport.isPortraitMode || !this.assetLoader.isResourceReady( ResourceConfig.PORTRAIT_VIEW ) ) {
			this.container.style.display = 'block';
		} else {
			this.container.style.display = 'none';
		}
	}

	public landscapeLayout (): void {
		if ( this.viewport.isPortraitMode || !this.assetLoader.isResourceReady( ResourceConfig.LANDSCAPE_VIEW ) ) {
			this.container.style.display = 'block';
		} else {
			this.container.style.display = 'none';
		}
	}

	/**
	 * Create the orientation hint DOM. For PORTRAIT-only mode, rotates the image by 90 degee.
	 *
	 * @protected
	 * @param {*} [supportOrientation]
	 * @memberof OrientationHintView
	 */
	protected createElements ( supportOrientation?: any ): void {
		const body = document.body;
		this.container = document.createElement( 'div' );
		const img = document.createElement( 'img' );

		let url = 'assets/core/orientation.png';
		try {
			url = MD5HASH + '_' + url;
		} catch {
			console.warn( 'please add MD5HASH setting into your project' );
		}
		img.setAttribute( 'src', url );

		this.container.style.position = 'fixed';
		this.container.style.zIndex = '100';
		this.container.style.top = '0px';
		this.container.style.width = '100%';
		this.container.style.height = '100%';
		this.container.style.background = 'black';
		this.container.style.display = 'none';
		img.style.width = '75%';
		img.style.position = 'absolute';
		img.style.top = '0';
		img.style.bottom = '0';
		img.style.right = '0';
		img.style.left = '0';
		img.style.margin = 'auto';
		if ( supportOrientation === Orientation.PORTRAIT ) {
			img.style.width = '50%';
			img.style.transform = 'rotate(90deg)';
		}
		this.container.appendChild( img );
		body.appendChild( this.container );
	}
}



