import { Scenario } from 'corengf/src/states/Scenario';
import { IWinningData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { IWildLocked } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { IGameState, IFeatureInfo, ISpinResult } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { SpinResponseFormatter } from 'slotngf/src/server/ng/formatter/http/in/SpinResponseFormatter';
import { AbstractFeatureScenarioFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/AbstractFeatureScenarioFormatter';

/**
 * formatting wild locked feature
 */
export class WildLockedFeatureFormatter extends SpinResponseFormatter implements AbstractFeatureScenarioFormatter {

	/**
	 * formatting run feature api response
	 * @param message
	 */
	public processIncomingMessage ( message: IGameState, featureInfo?: IFeatureInfo ): Scenario {
		console.warn( 'here shouldn\'t be triggered, since wild locked feature don\'t need another transaction to complete feature' );
		return null;
	}

	public getFeatureScenarioBeforeFreeSpinStop ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return freeSpinStopScenario;
	}

	public getFeatureScenarioAfterSpinStop ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		if ( freeSpinStopScenario.hasStateName( this.stateNames.freeSpinsStop ) ) {
			this.margeScenarioStateData( freeSpinStopScenario,
				this.stateNames.freeSpinsStop,
				{ wildLocked: this.getWildLocked( featureInfo ) }
			);
		}

		if ( freeSpinStopScenario.hasStateName( this.stateNames.freeSpinsOutroPopup ) ) {
			this.margeScenarioStateData( freeSpinStopScenario,
				this.stateNames.freeSpinsOutroPopup,
				{ wildLocked: this.getWildLocked( featureInfo ) }
			);
		}
		return freeSpinStopScenario;
	}

	/**
	 * formatting feature in feature_triggered
	 */
	public triggeredFeatureIncomingMessage ( message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return this.getWildLockedScenario( message, featureInfo );
	}

	/**
	 * get wild locked scenario
	 * @param message
	 * @param featureInfo
	 */
	protected getWildLockedScenario ( message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		let responseScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.wildLockTriggered );
		responseScenario.setStateData( this.stateNames.wildLockTriggered,
			this.getWildLocked( featureInfo ) );
		return responseScenario;
	}

	protected getWildLocked ( featureInfo: IFeatureInfo ): any {
		return featureInfo.feature_state;
	}

	public getFeatureShowWinningData ( winningData: IWinningData, spinResult: ISpinResult, featureInfo: IFeatureInfo ): IWinningData {
		return winningData;
	}

	public getFeatureScenarioAfterWin ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return freeSpinStopScenario;
	}

	/**
	 * formatting feature while restore feature
	 */
	public restoreFeatureIncomingMessage ( wildLockedFeatureDetail: IWildLocked ): Scenario {
		if ( wildLockedFeatureDetail == undefined ) {
			return null;
		}

		let wildLockedFeatureScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.wildLockTriggered );
		wildLockedFeatureScenario.setStateData( this.stateNames.wildLockTriggered, wildLockedFeatureDetail );
		return wildLockedFeatureScenario;
	}

	public getFeatureRestoreScenario ( restoreScenario: Scenario, featureInfo: IFeatureInfo ): Scenario {
		if ( restoreScenario.hasStateName( this.stateNames.freeSpinsIntroPopup ) ) {
			this.margeScenarioStateData( restoreScenario,
				this.stateNames.freeSpinsIntroPopup,
				{ wildLocked: this.getWildLocked( featureInfo ) }
			);
		}
		return restoreScenario;
	}
}



