import { TsMap } from 'corengf/src/utils/TsMap';
import { Helper } from 'corengf/src/utils/Helper';
import { IWebLineConfig } from 'corengf/src/elements/webpage/line/WebLineElement';
import { ICoreWebElementConfig } from 'corengf/src/elements/webpage/CoreWebElement';
import { WebElementFactory, WebFactoryElementType } from 'corengf/src/factory/WebElementFactory';
import { IHelpScreenContainerConfig } from 'corengf/src/elements/webpage/WebpageElement';
import { MLUIButtonId } from 'mlui/src/buttons/MLUIButtonId';
import { GeneralWebPageConfig } from 'mlui/src/config/GeneralWebPageConfig';
import { SoundConfig } from 'corengf/src/core/config/SoundConfig';
import { ImageSourceType } from 'corengf/src/elements/webpage/image/WebImageElement';
import { IWebTextConfig } from 'corengf/src/elements/webpage/text/WebTextElement';


export class MLUIRulesConfig extends GeneralWebPageConfig {
	public name = 'rule';
	public externalKey = 'external-helpscreens';
	public moduleName = 'GenesisRule';
	public localisationKeys = [ 'rules_HowToWin', 'rules_InterfaceAndShortcuts', 'rules_Others', 'rules_Interruption', 'rules_RTP' ];

	protected getButtonIds (): TsMap<string, MLUIButtonId> {
		return new TsMap( [
			[
				'closeButton', MLUIButtonId.BUTTON_HIDE_RULES
			]
		] );
	}

	protected getPortraitContainerConfig (): IHelpScreenContainerConfig {

		return Helper.mergeTwoObjects( super.getPortraitContainerConfig(),
			{
				paragraphs: this.getPortraitGameElements()
			}
		);
	}

	public getPortraitGameElements (): Array<ICoreWebElementConfig> {
		return [] as Array<ICoreWebElementConfig>;
	}

	/**
	 * config portrait "How to win" block content
	 */
	public getPortraitHowToWinElement (): Array<ICoreWebElementConfig> {
		return [
			WebElementFactory.createWebElementBlock( {
				type: WebFactoryElementType.WEB_ELEMENT_BLOCK,
				config: [
					this.getPortraitWebLineConfig(),
					{
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						blockStyle: `
							text-align: center;
						`,
						languageKey: 'howToWinTitle1'
					},
					{
						blockStyle: `
							margin: 35px 0px 7px;
							font-size: 21px;`,
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						languageKey: 'howToWinSubtitle1'
					},
					{
						type: WebFactoryElementType.WEB_TEXT_CONTENT_ELEMENT,
						languageKey: 'howToWinContent1'
					},
					{
						blockStyle: `
							margin: 35px 0px 7px;
							font-size: 21px;`,
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						languageKey: 'howToWinSubtitle2'
					},
					{
						type: WebFactoryElementType.WEB_TEXT_CONTENT_ELEMENT,
						languageKey: 'howToWinContent2'
					}
				]
			} )
		];
	}

	/**
	 * set up portrait rtp block
	 */
	public getPortraitRTPElements ( rtp: string ): Array<ICoreWebElementConfig> {
		return [
			WebElementFactory.createWebElementBlock( {
				type: WebFactoryElementType.WEB_ELEMENT_BLOCK,
				config: [
					this.getPortraitWebLineConfig(),
					{
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						languageKey: 'rtpTitle'
					},
					{
						type: WebFactoryElementType.WEB_TEXT_CONTENT_ELEMENT,
						languageKey: 'rtpContent',
						textToReplace: [
							{
								replaceKey: '{rtp}',
								replaceValue: rtp
							}
						]
					},
				]
			} )
		];
	}

	/**
	 * config portrait "Others" block content
	 */
	public getPortraitOthersElement ( additionalElements: string ): Array<ICoreWebElementConfig> {

		if ( additionalElements == undefined || additionalElements == Helper.EMPTY ) {
			return [];
		} else {
			return [
				WebElementFactory.createWebElementBlock( {
					type: WebFactoryElementType.WEB_ELEMENT_BLOCK,
					config: [
						this.getPortraitWebLineConfig(),
						{
							type: WebFactoryElementType.WEB_TEXT_ELEMENT,
							blockStyle: `
								text-align: center;
							`,
							languageKey: 'othersTitle1'
						},
						{
							type: WebFactoryElementType.WEB_TEXT_CONTENT_ELEMENT,
							languageKey: additionalElements
						}
					]
				} )
			];
		}
	}

	/**
	 * Style grid lines between paragraphs
	 */
	protected getPortraitWebLineConfig (): IWebLineConfig {
		return WebElementFactory.createWebLineElement( {
			type: WebFactoryElementType.WEB_LINE_ELEMENT,
			show: true
		} );
	}

	protected getLandscapeContainerConfig (): IHelpScreenContainerConfig {

		return Helper.mergeTwoObjects( super.getLandscapeContainerConfig(),
			{
				paragraphs: this.getLandscapeGameElements()
			}
		);
	}

	/**
	 * Landscape screen config
	 */
	protected getLandscapeGameElements (): Array<ICoreWebElementConfig> {
		return [
			WebElementFactory.createGeneralPagingElement( {
				type: WebFactoryElementType.WEB_GENERAL_PAGING_ELEMENT,
				blockStyle: `
					position: absolute;
					width: 616px;
					height: 364px;
				`,
				pagingConfig: {
					arrowButton: {
						blockStyle: 'top:0;',
						pageDot: {},
						nextButton: {
							dataHookTrackerConfig: {
								buttonID: MLUIButtonId.BUTTON_PAYTABLE_NEXT
							},
							soundKey: SoundConfig.buttonClick,
							buttonImage: WebElementFactory.createWebImageElement( {
								type: WebFactoryElementType.WEB_IMAGE_ELEMENT,
								blockStyle: `
									right:-105px;
									top:140px;
									transform:scaleX(-1);
									z-index: 10;
									position: absolute;
								`,
								imageSource: ImageSourceType.SPRITE,
								imageData: {
									assetName: 'mlui_menu',
									imageName: 'arrow_Enabled.png',
									ratio: 0.55
								},
							} )
						},
						preButton: {
							dataHookTrackerConfig: {
								buttonID: MLUIButtonId.BUTTON_PAYTABLE_PREV
							},
							soundKey: SoundConfig.buttonClick,
							buttonImage: WebElementFactory.createWebImageElement( {
								type: WebFactoryElementType.WEB_IMAGE_ELEMENT,
								blockStyle: `
									left:-105px;
									top:140px;
									z-index: 10;
									position: absolute;
								`,
								imageSource: ImageSourceType.SPRITE,
								imageData: {
									assetName: 'mlui_menu',
									imageName: 'arrow_Enabled.png',
									ratio: 0.55
								},
							} )
						}
					}
				},
				pages: this.getLandscapeGerneralPagingElementPages(),
			} )
		];
	}

	protected getLandscapeGerneralPagingElementPages (): Array<ICoreWebElementConfig> {
		return [];
	}

	// remove defualt main title
	protected getLandscapeHeaderMainTitleConfig (): IWebTextConfig {
		return null;
	}

	/**
	 * config landscape "How to win" block content
	 */
	public getLandscapeHowToWinElement (): Array<ICoreWebElementConfig> {
		return [
			WebElementFactory.createWebElementBlock( {
				type: WebFactoryElementType.WEB_ELEMENT_BLOCK,
				config: [
					{
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						blockStyle: this.getLandscapeMainTitleStyle(),
						languageKey: 'howToWinTitle1'
					},
					{
						blockStyle: `
							margin: 35px 0px 7px;
						`,
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						languageKey: 'howToWinSubtitle1',
						concentStyle: `
							font-size: 21px;
						`
					},
					{
						type: WebFactoryElementType.WEB_TEXT_CONTENT_ELEMENT,
						languageKey: 'howToWinContent1'
					}
				]
			} ),
			WebElementFactory.createWebElementBlock( {
				type: WebFactoryElementType.WEB_ELEMENT_BLOCK,
				config: [
					{
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						blockStyle: this.getLandscapeMainTitleStyle(),
						languageKey: 'howToWinTitle1'
					},
					{
						blockStyle: `
							margin: 35px 0px 7px;
						`,
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						languageKey: 'howToWinSubtitle2',
						concentStyle: `
							font-size: 21px;
						`
					},
					{
						type: WebFactoryElementType.WEB_TEXT_CONTENT_ELEMENT,
						languageKey: 'howToWinContent2'
					}
				]
			} )
		];
	}

	/**
	 * set up landscape rtp block
	 */
	public getLandscapeRTPElements ( rtp: string ): Array<ICoreWebElementConfig> {
		return [
			WebElementFactory.createWebElementBlock( {
				type: WebFactoryElementType.WEB_ELEMENT_BLOCK,
				config: [
					{
						type: WebFactoryElementType.WEB_TEXT_ELEMENT,
						blockStyle: this.getLandscapeMainTitleStyle(),
						languageKey: 'rtpTitle'
					},
					{
						type: WebFactoryElementType.WEB_TEXT_CONTENT_ELEMENT,
						languageKey: 'rtpContent',
						textToReplace: [
							{
								replaceKey: '{rtp}',
								replaceValue: rtp
							}
						]
					},
				]
			} )
		];
	}

	/**
	 * config landscape "Others" block content
	 */
	public getLandscapeOthersElement ( additionalElements: string ): Array<ICoreWebElementConfig> {

		if ( additionalElements == undefined || additionalElements == Helper.EMPTY ) {
			return [];
		} else {
			return [
				WebElementFactory.createWebElementBlock( {
					type: WebFactoryElementType.WEB_ELEMENT_BLOCK,
					config: [
						{
							type: WebFactoryElementType.WEB_TEXT_ELEMENT,
							blockStyle: this.getLandscapeMainTitleStyle(),
							languageKey: 'othersTitle1'
						},
						{
							type: WebFactoryElementType.WEB_TEXT_CONTENT_ELEMENT,
							languageKey: additionalElements
						}
					]
				} )
			];
		}
	}
}


