import { Inject } from 'typescript-ioc/es5';
import { PaylinesView } from 'slotngf/src/components/winDisplay/paylines/PaylinesView';
import { ReelsConfig } from 'slotngf/src/components/reels/logic/ReelsConfig';
import { PaylineConfig, IPaylineConfig } from 'slotngf/src/components/winDisplay/paylines/PaylineConfig';
import { IReelsConfig } from 'slotngf/src/core/config/IReelsConfig';
import { WinDisplayController } from 'slotngf/src/components/winDisplay/base/WinDisplayController';


export class PaylinesController extends WinDisplayController {

	protected view: PaylinesView;

	constructor ( @Inject view: PaylinesView ) {
		super( view );
	}

	public init (): void {
		let config = this.gameConfig.getConfig<IPaylineConfig>( PaylineConfig );
		this.view.setReelsConfig( this.gameConfig.getConfig<IReelsConfig>( ReelsConfig ) );
		this.view.initialize( config );
		this.finishStateAction();
	}
}


