import { NMath } from 'corengf/src/utils/NMath';
import * as MiniSignal from 'mini-signals';
import { IReelStripConfig, ISymbolDefinition } from 'slotngf/src/core/config/IReelsConfig';

/**
 * Class contains the logic for single reel
 */
export class ReelStripModel implements IReelStripModel {
	public countTopExtraSymbol: number = 1;
	public countBottomExtraSymbol: number = 1;

	/**
	 * List of symbols names that we got from server.
	 * We will show them in stopSpin state
	 */
	public _symbolsOutcome: Array<string>;

	public set symbolsOutcome ( symbolsOutcome: Array<string> ) {
		this._symbolsOutcome = symbolsOutcome;
	}

	public get symbolsOutcome (): Array<string> {
		return this._symbolsOutcome;
	}

	/**
	 * The array of ordered symbols according to ReelConfig
	 */
	private _allStripSymbols: Array<string>;

	/**
	 * setter for _allStripSymbols
	 * if stripSymbols didn't exist, try create strips from predefined reel in config,
	 * if there is no strips been predefined, create it randomly based on rangeSymbols.
	 */
	public set allStripSymbols ( stripSymbols: Array<string> ) {
		this._allStripSymbols = new Array<string>();
		if ( stripSymbols != undefined ) {
			this._allStripSymbols = stripSymbols;
		} else {
			if ( this.config.stripOrder != undefined ) {
				this._allStripSymbols = this.config.stripOrder;
			} else {
				for ( let j: number = 0; j < this.defaultStripLength; j++ ) {
					this._allStripSymbols.push( this.config.rangeSymbols[ NMath.randomInt( 0, this.config.rangeSymbols.length - 1 ) ] );
				}
			}
		}
	}

	public get allStripSymbols (): Array<string> {
		return this._allStripSymbols;
	}

	/**
	 * Unique identifier of reel
	 */
	protected _id: number;

	/**
	 * If reel strip didn't come from server and config,
	 * it will generate fake strip,
	 * the length of the fake strip will equals to defaultStripLength.
	 */
	protected defaultStripLength: number = 30;

	/**
	 * Visible symbols + extra symbols
	 */
	public reelSymbols: Array<SymbolData>;

	protected _config: IReelStripConfig;
	public get config (): IReelStripConfig {
		return this._config;
	}


	protected _symbolConfig: ISymbolDefinition;
	public get symbolConfig (): ISymbolDefinition {
		return this._symbolConfig;
	}

	/**
	 * The index of reel strip of symbols that we show right now.
	 */
	protected _reelsStripIndex: number = 0;
	public get reelsStripIndex (): number {
		return this._reelsStripIndex;
	}

	/**
	 * How many outcome symbols we have already in reel
	 */
	public outcomeSymbolsCounter: number = 0;

	public spinDownTweenCount: number = 0;
	public currentTweenCount: number = 0;

	/**
	 * Count of all symbols that are on stage( extra + visible )
	 */
	protected totalStageSymbolsCount: number;

	/**
	 * True once we get stop spin notification
	 */
	public stopRequested: boolean;

	/**
	 * ( reelId: number, itemId: number, symbolName: string, state: SymbolStatePostfix )
	 * It sets the symbol with name _symbolName_ for reel with _reelId_ on
	 * reel index position _itemId_ with state.
	 * We dispatch it to controller and then he pass it to strip view.
	 */
	public setSymbolNameSignal: MiniSignal;

	public spinTweenUpdateSignal: MiniSignal;

	/**
	 * ( reelId: number, symbolName: string, state: SymbolStatePostfix )
	 * We dispatch that signal at the end of tweening one symbol.
	 * We move the last hidden symbol to top and then start spinning all symbols down again.
	 */
	public moveLastSymbolToTopSignal: MiniSignal;

	/**
	 * ( reelId: number )
	 * It is dispatched once reel stopped.
	 */
	public reelStopedSignal: MiniSignal;

	/**
	 * ( reelId: number )
	 * We dispatch it once we requested the stopping of reel.
	 * It is when we get stop spin state.
	 */
	public stopRequestedSignal: MiniSignal;

	/**
	 * ( reelId: number, symbolId: number )
	 * It is dispatched once when found symbol which will be animated when reel stop.
	 */
	public dropAnimationSignal: MiniSignal;

	public hideBlurSymbolSignal: MiniSignal; // add comment

	get id (): number {
		return this._id;
	}

	/**
	 * The lower bound offsetY of the strip view. For multiple symbol height, use this value to determine should we move last symbol to top.
	 *
	 * @readonly
	 * @type {number}
	 * @memberof ReelStripModel
	 */
	get lowerBound (): number {
		return this.config.symbolsOnReel * this.symbolConfig.generalSymbol.height;
	}

	get upperBoundOfTopExtraY (): number {
		return -this.countTopExtraSymbol * this.symbolConfig.generalSymbol.height;
	}

	constructor () {
		this.setSymbolNameSignal = new MiniSignal();
		this.moveLastSymbolToTopSignal = new MiniSignal();
		this.reelStopedSignal = new MiniSignal();
		this.stopRequestedSignal = new MiniSignal();
		this.spinTweenUpdateSignal = new MiniSignal();
		this.dropAnimationSignal = new MiniSignal();
		this.hideBlurSymbolSignal = new MiniSignal(); // add comment

		this.reset();
	}

	public reset (): void {
		this.outcomeSymbolsCounter = 0;
		this.stopRequested = false;
	}


	public setConfig ( config: IReelStripConfig, symbolConfig: ISymbolDefinition ): void {
		this._id = config.id;
		this._config = config;
		this._symbolConfig = symbolConfig;
		this.countTopExtraSymbol = ( config.countTopExtraSymbol !== undefined ) ? config.countTopExtraSymbol : 1;
		this.countBottomExtraSymbol = ( config.countBottomExtraSymbol !== undefined ) ? config.countBottomExtraSymbol : 1;
		this.totalStageSymbolsCount = this.countTopExtraSymbol + this.config.symbolsOnReel + this.countBottomExtraSymbol;
	}

	/**
	 * Filling in the symbols according to strips
	 */
	public initReel ( symbolsOutcome: Array<string> ): void {
		this.reelSymbols = new Array<SymbolData>();

		this.setRandomReelsStripIndex();

		let offSetY = 0;
		for ( let i = this.countTopExtraSymbol - 1; i >= 0; i-- ) {
			const name: string = this.nextSymbol();
			const symbolheight = this.getSymbolHeightByName( name );
			offSetY -= symbolheight;
			this.reelSymbols.push( new SymbolData( name, offSetY ) );
		}

		this.reelSymbols.reverse();
		offSetY = 0;

		for ( let i = 0; i < this.totalStageSymbolsCount - this.countTopExtraSymbol; i++ ) {
			const name: string = ( symbolsOutcome[ i ] ) ? symbolsOutcome[ i ] : this.nextSymbol();
			const symbolheight = this.getSymbolHeightByName( name );
			this.reelSymbols.push( new SymbolData( name, offSetY ) );
			offSetY += symbolheight;
		}
	}

	public getSymbolHeightByName ( symbolName: string ): number {
		if ( this.symbolConfig.specialSymbol[ symbolName ] ) {
			return this.symbolConfig.specialSymbol[ symbolName ].height;
		} else {
			return this.symbolConfig.generalSymbol.height;
		}
	}

	/**
	 * Returns the next symbol from wholeStripSymbols.
	 *
	 * ex. this.allStripSymbols : [ 'A', 'K', 'Q', 'J', 'T','PIC1' ]
	 *     spin_result : [ 'K', 'Q', 'J', 'T' ]
	 * 	   nextSymbol is 'A'
	 */
	public nextSymbol (): string {
		if ( this.allStripSymbols && this.allStripSymbols.length > 0 ) {
			this._reelsStripIndex--;
			if ( this._reelsStripIndex < 0 ) {
				this.setRandomReelsStripIndex();

			}
			return this.allStripSymbols[ this._reelsStripIndex ];
		}
		return this.config.rangeSymbols[ NMath.randomInt( 0, this.config.rangeSymbols.length - 1 ) ];
	}

	public setRandomReelsStripIndex (): void {
		if ( this.allStripSymbols && this.allStripSymbols.length > 0 ) {
			this._reelsStripIndex = NMath.randomInt( 0, this.allStripSymbols.length - 1 );
		}
	}

	/**
	 * Remove the last element from reelSymbols and insert the new one at the beginning.
	 * @return the symbol id of the next symbol in strip
	 */
	protected createNextSymbol ( name: string ): SymbolData {
		let data: SymbolData = new SymbolData( name, this.reelSymbols[ 0 ].posY - this.config.symbol.height );
		this.reelSymbols.pop();
		this.reelSymbols.unshift( data );
		return data;
	}

	/**
	 * Set how many times should perform tween to complete spin down
	 *
	 * @memberof ReelStripModel
	 */
	public setTweenCount (): void {
		const firstSymbol = this.reelSymbols[ 0 ];
		let symbolOutcomeHeight = 0;
		for ( let i = this.symbolsOutcome.length - 1; i >= 0; i-- ) {
			symbolOutcomeHeight += this.getSymbolHeightByName( this.symbolsOutcome[ i ] );
		}

		if ( firstSymbol.posY < this.upperBoundOfTopExtraY ) { // check is first element a N times height symbol.
			symbolOutcomeHeight += Math.abs( firstSymbol.posY - this.upperBoundOfTopExtraY );
		}

		this.spinDownTweenCount = symbolOutcomeHeight / this.symbolConfig.generalSymbol.height;
	}
}

export interface IReelStripModel {
	id: number;
	symbolsOutcome: Array<string>;
	allStripSymbols: Array<string>;

	setSymbolNameSignal: MiniSignal;
	moveLastSymbolToTopSignal: MiniSignal;
	reelStopedSignal: MiniSignal;
	stopRequestedSignal: MiniSignal;
	spinTweenUpdateSignal: MiniSignal;
	dropAnimationSignal: MiniSignal;
	hideBlurSymbolSignal: MiniSignal;

	initReel ( symbolsOutcome: Array<string> ): void;
	setConfig ( config: IReelStripConfig, symbolConfig: ISymbolDefinition ): void;
}

export class SymbolData {
	public symbolName: string;
	public posY: number;
	constructor ( symbolName: string, posY: number ) {
		this.symbolName = symbolName;
		this.posY = posY;
	}
}


