import { TsMap } from 'corengf/src/utils/TsMap';
import { Actions } from 'corengf/src/utils/Actions';
import { Button, IButtonConfig } from 'corengf/src/elements/canvas/button/Button';


export class TurboButton extends Button {

	protected readonly TURBO_ON: string = 'On';
	protected readonly TURBO_OFF: string = 'Off';
	protected speedMode: Array<number>;
	protected currentSpeedIndex: number;
	protected speedTextureMap: TsMap<number, string>;
	protected config: ITurboButtonConfig;

	protected createElements ( buttonConfig: ITurboButtonConfig ): void {
		super.createElements( buttonConfig );
		this.speedMode = buttonConfig.speedMode;
		this.speedTextureMap = new TsMap();
		for ( let i: number = 0; i < this.speedMode.length; i++ ) {
			this.speedTextureMap.set( this.speedMode[ i ], buttonConfig.speedTextures[ i ] );
		}
		this.currentSpeedIndex = 0;
	}

	protected onClick ( event: any ): void {
		this.playSoundEffect();

		let action: string = Actions.CLICK_ACTION_PREFIX + this.action;

		this.currentSpeedIndex += 1;
		if ( this.currentSpeedIndex >= this.config.speedMode.length ) {
			this.currentSpeedIndex = 0;
			this.clickData.speedMode = this.TURBO_OFF;
		} else {
			this.clickData.speedMode = this.TURBO_ON;
		}

		if ( this.action != Actions.EMPTY ) {
			this.clickData.speed = this.speedMode[ this.currentSpeedIndex ];
			this.localButtonsController.handleButtonAction( action, this.clickData );
			this.changeTexture( this.clickData.speed );
		} else {
			throw 'need action for button:' + this.id;
		}

		this.hookPlayerAction();
	}

	protected updateTexture (): void {
		let speedName: string = this.speedTextureMap ? this.speedTextureMap.get( this.speedMode[ this.currentSpeedIndex ] ) : '';
		let fullName: string = this.assetName + speedName;
		this.enabledElement = this.findTexture( fullName + this.enabledFrame );
		this.disabledElement = this.findTexture( fullName + this.disabledFrame, false );
		this.overElement = this.findTexture( fullName + this.overFrame, false );
		this.downElement = this.findTexture( fullName + this.downFrame, false );
	}

	public changeTexture ( speed: number ): void {
		let speedName: string = this.speedTextureMap.get( speed );
		let fullName: string = this.assetName + speedName;

		this.enabledElement = this.findTexture( fullName + this.enabledFrame );
		this.disabledElement = this.findTexture( fullName + this.disabledFrame, false );
		this.overElement = this.findTexture( fullName + this.overFrame, false );
		this.downElement = this.findTexture( fullName + this.downFrame, false );
		this.setEnable( true );
		this.currentSpeedIndex = this.speedMode.indexOf( speed );
	}

	protected findTexture ( fullName: string, isRequired: boolean = true ): PIXI.Texture {
		if ( PIXI.utils.TextureCache[ fullName ] == undefined ) {
			if ( isRequired ) {
				throw 'button texture doesn\'t exist: ' + fullName;
			} else {
				console.log( 'button texture doesn\'t exist: ' + fullName );
			}
		}
		return PIXI.utils.TextureCache[ fullName ];
	}
}

export interface ITurboButtonConfig extends IButtonConfig {
	/** turbo button speed mode */
	speedMode: Array<number>;
	/** textures match to turbo button speed mode */
	speedTextures: Array<string>;
}


