import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Helper } from 'corengf/src/utils/Helper';
import { Actions } from 'corengf/src/utils/Actions';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { ToolTipEvent } from 'corengf/src/components/tooltip/ToolTipEvent';
import { IToolTipConfig } from 'corengf/src/elements/canvas/tooltip/Tooltip';
import { SoundManager, AudioTrackName } from 'corengf/src/audio/SoundManager';
import { KeyboardManager, Keycode } from 'corengf/src/keyboard/KeyboardManager';
import { CoreElement, ICoreElementConfig } from 'corengf/src/elements/canvas/CoreElement';


export class InteractiveElement extends CoreElement {

	@Inject
	protected keyboardManager: KeyboardManager;

	@Inject
	protected soundManager: SoundManager;

	public elementSignal: MiniSignal;

	public state: string;

	protected action: string;

	protected soundKey: string;

	protected config: IInteractiveElementConfig;

	protected toolTipTimer: SimpleTimer;
	protected toolTipData: IToolTipConfig;

	private isFirstTouchEvent: boolean;

	/**
	 * Set the keycode with corresponding action
	 *
	 * @protected
	 * @type {{ [ key: string ]: Function }}
	 * @memberof InteractiveElement
	 */
	private _actionKeycodeMap: TsMap<Keycode, Function>;

	public static ENABLED_STATE = 'enable';
	public static DISABLED_STATE = 'disable';

	constructor ( config: IInteractiveElementConfig ) {
		super( config );
		this.elementSignal = new MiniSignal();
		this.action = Actions.EMPTY;

		if ( config.tooltip ) {
			this.toolTipData = config.tooltip;
			this.on( 'pointerdown', this.onToolTipTouchStart, this );
			this.on( 'pointerup', this.onToolTipTouchStop, this );
			this.isFirstTouchEvent = false;
		}
	}

	public setAction ( action: string ) {
		this.action = action;
	}

	public getAction (): string {
		return this.action;
	}

	public setState ( stateName: string ) {
		if ( this.state !== stateName ) {
			this.onStateChanged( stateName );
		}
		this.state = stateName;
	}

	public onStateChanged ( stateName: string ) {
		//TODO
	}

	/**
	 * Returns whether or not the button is currently enabled
	 */
	get enabled (): boolean {
		return this.state == InteractiveElement.ENABLED_STATE;
	}

	get actionKeycodeMap (): TsMap<Keycode, Function> {
		return this._actionKeycodeMap;
	}

	set actionKeycodeMap ( map: TsMap<Keycode, Function> ) {
		this._actionKeycodeMap = map;

		this._actionKeycodeMap.forEach( ( _value, key, _map ) => {
			this.keyboardManager.register( key, this );
		} );
	}

	protected onToolTipTouchStop (): void {
		this.removeTimer();
	}

	protected onToolTipTouchStart (): void {
		if ( this.config.tooltipMode
			&& this.isFirstTouchEvent
			&& this.config.tooltipMode == InteractiveElementTooltipMode.ONCE ) {
			return;
		} else {
			this.isFirstTouchEvent = true;
			this.showToolTip();
		}
	}

	protected showToolTip (): void {
		this.removeTimer();
		const showToolTipDelay: number = ( this.config.showToolTipDelay ) ? this.config.showToolTipDelay : 0;
		this.toolTipTimer = new SimpleTimer( showToolTipDelay, this.dispatchTooltipEvent.bind( this ), false );
		this.toolTipTimer.start();
	}

	protected removeTimer (): void {
		if ( this.toolTipTimer ) {
			this.toolTipTimer.stop();
			this.toolTipTimer.destroy();
			this.toolTipTimer = undefined;
		}
	}

	protected dispatchTooltipEvent (): void {
		GlobalDispatcher.dispatchIntoAllStates( ToolTipEvent.SHOW_TOOLTIP_EVENT, this.toolTipData );
	}

	protected playSoundEffect (): void {
		if ( this.soundKey != undefined ) {
			this.soundManager.play( this.soundKey, AudioTrackName.EFFECT );
		}
	}

	public updateAttributes ( config: IInteractiveElementConfig ): void {
		super.updateAttributes( config );
		if ( config.tooltip && this.toolTipData ) {
			this.toolTipData = Helper.mergeTwoObjects( this.toolTipData, config.tooltip );
		}
	}
}


export interface IInteractiveElementConfig extends ICoreElementConfig {
	tooltipMode?: InteractiveElementTooltipMode;
	showToolTipDelay?: number;
	tooltip?: IToolTipConfig;
	soundKey?: string;
}

export enum InteractiveElementTooltipMode {
	ONCE = 'ONCE',
	LOOP = 'LOOP'
}


