import { Inject } from 'typescript-ioc/es5';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { IInformationHeaderConfig } from 'corengf/src/components/ui/components/informationHeader/UIInformationHeaderConfig';
import { UIInformationHeaderController } from 'corengf/src/components/ui/components/informationHeader/UIInformationHeaderController';
import { SlotEvent } from 'slotngf/src/core/SlotEvent';
import { SlotStates } from 'slotngf/src/states/SlotStates';
import { SlotInformationHeaderView } from 'slotngf/src/components/informationHeader/SlotInformationHeaderView';


export class SlotInformationHeaderController extends UIInformationHeaderController {

	@Inject
	protected stateNames: SlotStates;

	protected view: SlotInformationHeaderView;

	constructor ( @Inject view: SlotInformationHeaderView ) {
		super( view );
	}

	protected getViewConfig ( stateData: any, config: IInformationHeaderConfig ): any {
		let viewConfig = {};
		if ( stateData.gameViewConfig ) {
			if ( !config.isRealMode ) {
				stateData.gameViewConfig.show_title = true;
			}
			viewConfig = stateData.gameViewConfig;
		} else if ( !config.isRealMode ) {
			viewConfig = {
				show_title: true
			};
		}
		return viewConfig;
	}

	protected initialize ( config: IInformationHeaderConfig ): void {
		super.initialize( config );
		GlobalDispatcher.addListener( this, SlotEvent.SHOW_FULL_SCREEN_UI, this.onFullScreenUIShow.bind( this ) );
		GlobalDispatcher.addListener( this, SlotEvent.HIDE_FULL_SCREEN_UI, this.onFullScreenUIHide.bind( this ) );
	}

	protected onClickBackground (): void {
		GlobalDispatcher.dispatchIntoExpectedStates( SlotEvent.SHOW_DEMO_SCREEN_UI,
			[
				this.stateNames.baseGameIdle,
				this.stateNames.allLineWin,
				this.stateNames.showWinning,
				this.stateNames.showWinningLoop
			] );
	}

	protected baseGameIdle (): void {
		this.view.setInteractive( true );
		this.finishStateAction();
	}

	protected prefeatureWinning (): void {
		this.view.setInteractive( false );
		this.finishStateAction();
	}

	protected onFullScreenUIShow (): void {
		this.view.fullScreenUIShow();
	}

	protected onFullScreenUIHide (): void {
		this.view.fullScreenUIHide();
	}
}


