import { ScenarioManager } from 'corengf/src/core/ScenarioManager';
import { SlotScenarioNames } from './../states/SlotScenarioNames';
import { SlotStates } from './../states/SlotStates';
import { Inject, Singleton } from 'typescript-ioc/es5';

@Singleton
export class SlotScenarioManager extends ScenarioManager {
	@Inject
	protected stateName: SlotStates;

	@Inject
	protected scenarioName: SlotScenarioNames;

	protected createScenarios (): void {
		this.scenarioFlows.set(
			this.scenarioName.gameInit, this.getScenarioByStateNames( this.scenarioName.gameInit,
				[
					this.stateName.preInit,
					this.stateName.watingEnterGame,
					this.stateName.init,
					this.stateName.gameStartTutorial,
					this.stateName.tutorialOutro,
					this.stateName.baseGameIdle
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.gameIdle, this.getScenarioByStateNames( this.scenarioName.gameIdle,
				[
					this.stateName.baseGameIdle
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.startSpin, this.getScenarioByStateNames( this.scenarioName.startSpin,
				[
					this.stateName.startSpin,
					this.stateName.spinning
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.startAutoSpin, this.getScenarioByStateNames( this.scenarioName.startAutoSpin,
				[
					this.stateName.transitionToAutoSpin,
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.baseSpinStop, this.getScenarioByStateNames( this.scenarioName.baseSpinStop,
				[
					this.stateName.spinStop,
					this.stateName.allReelStopped
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.baseSpinWithWinning, this.getScenarioByStateNames( this.scenarioName.baseSpinWithWinning,
				[
					this.stateName.allLineWin,
					this.stateName.showWinning,
					this.stateName.showWinningLoop,
					this.stateName.baseGameIdle
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.freeSpinsIdle, this.getScenarioByStateNames( this.scenarioName.freeSpinsIdle,
				[
					this.stateName.freeSpinsIdle
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.freeSpinContinue, this.getScenarioByStateNames( this.scenarioName.freeSpinContinue,
				[
					this.stateName.freeSpinsStart,
					this.stateName.freeSpinning
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.transitionToFreeSpins, this.getScenarioByStateNames( this.scenarioName.transitionToFreeSpins,
				[
					this.stateName.transitionToFreeSpins,
					this.stateName.freeSpinsIntroPopup
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.freeSpinsStop, this.getScenarioByStateNames( this.scenarioName.freeSpinsStop,
				[
					this.stateName.freeSpinsStop,
					this.stateName.freeSpinsAllReelStopped
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.freeSpinsFinish, this.getScenarioByStateNames( this.scenarioName.freeSpinsFinish,
				[
					this.stateName.transitionToBaseGame,
					this.stateName.freeSpinsOutroPopup,
					this.stateName.baseGameIdle
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.freeSpinsWithWinning, this.getScenarioByStateNames( this.scenarioName.freeSpinsWithWinning,
				[
					this.stateName.freeSpinsAllLineWin,
					this.stateName.freeSpinsShowWinning
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.freeSpinsWithWildMultipliers, this.getScenarioByStateNames( this.scenarioName.freeSpinsWithWildMultipliers,
				[
					this.stateName.freeSpinsWildMultipliers
				]
			)
		);

		/** Base game free respin flows */
		this.scenarioFlows.set(
			this.scenarioName.startFreeRespin, this.getScenarioByStateNames( this.scenarioName.startFreeRespin,
				[
					this.stateName.startFreeRespin,
					this.stateName.freeRespinReels,
					this.stateName.freeRespinning
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.freeRespinReels, this.getScenarioByStateNames( this.scenarioName.freeRespinReels,
				[
					this.stateName.freeRespinReels,
					this.stateName.freeRespinning
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.freeRespinStop, this.getScenarioByStateNames( this.scenarioName.freeRespinStop,
				[
					this.stateName.freeRespinsStop,
					this.stateName.freeRespinsAllReelStopped
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.freeRespinWithWinning, this.getScenarioByStateNames( this.scenarioName.freeRespinWithWinning,
				[
					this.stateName.freeRespinsAllLineWin,
					this.stateName.freeRespinsShowWinning,
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.freeRespinFinish, this.getScenarioByStateNames( this.scenarioName.freeRespinFinish,
				[
					this.stateName.freeRespinFinish
				]
			)
		);


		this.scenarioFlows.set(
			this.scenarioName.wildLockTriggered, this.getScenarioByStateNames( this.scenarioName.wildLockTriggered,
				[
					this.stateName.wildLockTriggered
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.freeSpinsRetriggered, this.getScenarioByStateNames( this.scenarioName.freeSpinsRetriggered,
				[
					this.stateName.showRetrigger
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.replacingSymbols, this.getScenarioByStateNames( this.scenarioName.replacingSymbols,
				[
					this.stateName.replacingSymbols
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.runPickFeature, this.getScenarioByStateNames( this.scenarioName.runPickFeature,
				[
					this.stateName.startPickFeature,
					this.stateName.pickFeature,
					this.stateName.endPickFeature
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.wheelFortune, this.getScenarioByStateNames( this.scenarioName.wheelFortune,
				[
					this.stateName.transitionToWheelFortune,
					this.stateName.wheelFortuneStart,
					this.stateName.wheelFortunRun,
					this.stateName.wheelFortunStop,
					this.stateName.wheelFortunClose
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.featureWithWinning, this.getScenarioByStateNames( this.scenarioName.featureWithWinning,
				[
					this.stateName.prefeatureWinning
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.freeSpinsRotateReelNoWinning, this.getScenarioByStateNames( this.scenarioName.freeSpinsRotateReelNoWinning,
				[
					this.stateName.freeSpinsRotate
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.freeSpinsRotateReelWithWinning, this.getScenarioByStateNames( this.scenarioName.freeSpinsRotateReelWithWinning,
				[
					this.stateName.freeSpinsRotate,
					this.stateName.freeSpinsAllLineWin,
					this.stateName.freeSpinsShowWinning
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.jackpotFeatureIdle, this.getScenarioByStateNames( this.scenarioName.jackpotFeatureIdle,
				[
					this.stateName.transitionToJackpot,
					this.stateName.jackpotIdle
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.startJackpotFeature, this.getScenarioByStateNames( this.scenarioName.startJackpotFeature,
				[
					this.stateName.jackpotStart,
					this.stateName.jackpotRun,
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.stopJackpotFeature, this.getScenarioByStateNames( this.scenarioName.stopJackpotFeature,
				[
					this.stateName.jackpotStop,
					this.stateName.jackpotShowWinning,
					this.stateName.jackpotClose
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.transitionToBonusSpin, this.getScenarioByStateNames( this.scenarioName.transitionToBonusSpin,
				[
					this.stateName.transitionToBonusSpins,
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.bonusSpinsFinish, this.getScenarioByStateNames( this.scenarioName.bonusSpinsFinish,
				[
					this.stateName.transitionBonusToBase,
					this.stateName.baseGameIdle
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.bonusSpinContinue, this.getScenarioByStateNames( this.scenarioName.bonusSpinContinue,
				[
					this.stateName.freeSpinsStart,
					this.stateName.freeSpinning
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.bonusSpinsIdle, this.getScenarioByStateNames( this.scenarioName.bonusSpinsIdle,
				[
					this.stateName.freeSpinsIdle
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.bonusSpinsStop, this.getScenarioByStateNames( this.scenarioName.bonusSpinsStop,
				[
					this.stateName.freeSpinsStop,
					this.stateName.freeSpinsAllReelStopped
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.bonusSpinsWithWinning, this.getScenarioByStateNames( this.scenarioName.bonusSpinsWithWinning,
				[
					this.stateName.freeSpinsAllLineWin,
					this.stateName.freeSpinsShowWinning,
					this.stateName.freeSpinsIdle
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.bonusSpinsNoWinning, this.getScenarioByStateNames( this.scenarioName.bonusSpinsNoWinning,
				[
					this.stateName.freeSpinsIdle
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.multiplierAdded, this.getScenarioByStateNames( this.scenarioName.multiplierAdded,
				[
					this.stateName.multiplierAdded
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.multiplierApplied, this.getScenarioByStateNames( this.scenarioName.multiplierApplied,
				[
					this.stateName.multiplierApplied
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.symbolCrawl, this.getScenarioByStateNames( this.scenarioName.symbolCrawl,
				[
					this.stateName.symbolCrawlTriggered
				]
			)
		);
	}
}



