import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { IWinDisplayConfig } from '../../../core/config/IWinDisplayConfig';


export class WinBoxConfig implements IWinBoxConfig {
	public name = 'WinBox';
	public animationFiles: Array<string> | string = [
		'PaylineBox_Loop_001.png',
		'PaylineBox_Loop_002.png',
		'PaylineBox_Loop_003.png',
		'PaylineBox_Loop_004.png',
		'PaylineBox_Loop_005.png',
		'PaylineBox_Loop_006.png',
		'PaylineBox_Loop_007.png',
		'PaylineBox_Loop_008.png',
		'PaylineBox_Loop_009.png',
		'PaylineBox_Loop_010.png',
		'PaylineBox_Loop_011.png',
		'PaylineBox_Loop_012.png',
		'PaylineBox_Loop_013.png',
		'PaylineBox_Loop_014.png',
		'PaylineBox_Loop_015.png',
	];
	public boxOffset = {
		x: 0,
		y: 0
	};
	public animationName: string = 'loop';
	public loop: boolean = true;

	public defaultPoolSize: number = 6; // 2 line
}

export interface IWinBoxConfig extends IWinDisplayConfig {
	animationFiles: Array<string> | string;  //Use array for sequence and string for spine
	boxOffset: IPoint;
	animationName: string;
	loop: boolean;

	/**
	 * Default pool size
	 *
	 * @type {number}
	 * @memberof IWinBoxConfig
	 */
	defaultPoolSize?: number;
}


