import { SimpleTween } from 'corengf/src/time/SimpleTween';
import { Easing } from '@tweenjs/tween.js';


export class CommonAnimation {

	public static startTween ( config: ICommonAnimationConfig ): SimpleTween {

		if ( config.tweenConfig.from ) {
			for ( let property in config.tweenConfig.from ) {
				if ( config.target[ property ] === undefined ) {
					continue;
				}
				config.target[ property ] = config.tweenConfig.from[ property ];
			}
		}

		const easing = config.easing || Easing.Linear.None;

		return new SimpleTween( config.target )
			.to( config.tweenConfig.to, config.tweenConfig.duration )
			.easing( easing )
			.onComplete( () => {
				if ( config.onComplete ) {
					config.onComplete();
				}
			} );
	}

}

export interface ICommonAnimationConfig {
	tweenConfig: ITweenConfig;
	target?: PIXI.DisplayObject;
	onComplete?: Function;
	easing?: ( k: number ) => number;
}

export interface ITweenConfig {
	from?: object;
	to: object;
	duration: number;
}


