import { TsMap } from 'corengf/src/utils/TsMap';
import { Container, Inject } from 'typescript-ioc/es5';
import { Translator } from 'corengf/src/utils/Translator';
import { DataHookService } from 'corengf/src/core/DataHookService';
import { WebSocketService } from 'corengf/src/core/WebSocketService';
import { ITransport } from 'corengf/src/server/abstract/transport/ITransport';
import { IServerConfig } from 'corengf/src/server/abstract/config/IServerConfig';
import { NGWebSocketTransport } from 'corengf/src/server/ng/transport/NGWebSocketTransport';
import { IWebSocketRequestData } from 'corengf/src/server/abstract/transport/WebSocketTransport';
import { INGWebSocketConfig, INGServerConfig } from 'corengf/src/server/ng/config/INGServerConfig';
import { IWebSocketIncommingPackage, NGWebSocketData } from 'corengf/src/server/ng/data/NGWebSocketDataDefinitions';
import { AbstractServer } from 'corengf/src/server/abstract/AbstractServer';
import { NGServerConfig } from 'corengf/src/server/ng/config/NGServerConfig';
import { GameConfig } from 'corengf/src/core/GameConfig';
import { WebSocketFormatter } from 'corengf/src/server/abstract/formatter/WebSocketFormatter';
import { NGHttpTransport } from 'corengf/src/server/ng/transport/NGHttpTransport';
import { AbstractSdkController } from 'corengf/src/server/abstract/sdk/AbstractSdkController';
import { NGSDKSupportList } from 'corengf/src/server/ng/sdk/NGSDKSupportList';

/**
 * NGWebSocketServer with web socket conection.
 * connect to nurgs notification server.
 */
export class NGWebSocketServer extends AbstractServer {

	protected readonly FEATURE: string = '{feature}';
	protected readonly PARTNER_CODE: string = '{partnerCode}';
	protected readonly PLAYER_ID: string = '{playerId}';
	protected readonly LOCALE: string = '{locale}';

	@Inject
	protected gameConfig: GameConfig;

	@Inject
	protected translator: Translator;

	@Inject
	protected webSocketService: WebSocketService;

	@Inject
	protected dataHookService: DataHookService;

	@Inject
	protected _sdkSupportList: NGSDKSupportList;
	protected get sdkSupportList (): TsMap<string, AbstractSdkController> {
		return this._sdkSupportList.SDKSupportList;
	}

	@Inject
	protected _webSocketTransport: ITransport;

	private isInitialized: boolean = false;

	protected get webSocketTransport (): NGWebSocketTransport {
		return <NGWebSocketTransport> this._webSocketTransport;
	}

	public setupTransport (): ITransport {
		return Container.get( NGHttpTransport );
	}

	/**
	 * Mapping address with features
	 * key: address
	 * value: feature
	 */
	public addressMap = new TsMap<string, string>();

	protected partnerCode: string;
	protected playerId: string;
	protected engagements: Array<string>;

	protected get webSocketConfig (): INGWebSocketConfig {
		return this.config as INGWebSocketConfig;
	}

	public obtainConfig (): IServerConfig {
		return this.gameConfig.getConfig<INGServerConfig>( NGServerConfig );
	}

	public onReceive ( data: any ): void {
		//
	}

	protected onConnect (): void {
		//do something
	}

	public send ( messageType: string, data?: any ): Promise<any> {
		return null;
	}

	/**
	 * connect to notification server
	 */
	protected connectToNotificationServer (): void {
		if ( this.webSocketConfig.useWebSocket ) {
			this.dataHookService.init( this, this.getDataHookLogFormatter() );
			this._webSocketTransport = this.setupWebSocketTransport();
			this.webSocketTransport.setConfig( this.webSocketConfig );
			this.webSocketTransport.onConnect = this.onWebSocketConnected.bind( this );
			this.webSocketTransport.onError = this.onNetworkError.bind( this );
			this.webSocketTransport.onReceive = this.onWebSocketReceive.bind( this );
			this.webSocketTransport.onNotificationSignal.add( this.onWebSocketNotification, this );
			this.webSocketTransport.connect();
		}
	}

	protected getDataHookLogFormatter (): WebSocketFormatter {
		return Container.get( WebSocketFormatter );
	}

	public sendDataHookInfo ( data: any ): void {
		this.webSocketService.onWebSocketRequest( data );
	}

	/**
	 * Setup web socket transport
	 */
	public setupWebSocketTransport (): ITransport {
		return Container.get( NGWebSocketTransport );
	}

	/**
	 * On web socket connected
	 */
	protected onWebSocketConnected (): void {
		if ( this.isInitialized ) {
			this.setDirectRegister();
			return;
		}
		this.setRegisterHandler();
		this.registerEvents();
		this.isInitialized = true;
	}

	/**
	 * Register handler for direct and channel
	 */
	protected setRegisterHandler (): void {
		this.setDirectRegister();
		this.setChannelRegister();
		this.setRequestRegister();
	}

	protected registerEvents (): void {
		this.webSocketService.onWebSocketRequestSignal.add( this.onWebSocketRequest, this );
	}

	/**
	 * Register handler for direct by address
	 */
	protected setDirectRegister (): void {
		this.webSocketConfig.directAddressFormat = this.webSocketConfig.directAddressFormat.replace( this.PARTNER_CODE, this.partnerCode );
		this.webSocketConfig.directAddressFormat = this.webSocketConfig.directAddressFormat.replace( this.PLAYER_ID, this.playerId );

		if ( this.engagements != undefined ) {
			for ( let i = 0; i < this.engagements.length; i++ ) {
				let address = this.getDirectAddress( this.engagements[ i ].toLocaleLowerCase() );
				this.addressMap.set( address, this.engagements[ i ].toLocaleLowerCase() );
				this.webSocketTransport.onSetRegisterHandler( address );
			}
		}

		if ( this.webSocketConfig.directFeatures != undefined ) {
			for ( let i = 0; i < this.webSocketConfig.directFeatures.length; i++ ) {
				let address = this.getDirectAddress( this.webSocketConfig.directFeatures[ i ] );
				this.addressMap.set( address, this.webSocketConfig.directFeatures[ i ] );
				this.webSocketTransport.onSetRegisterHandler( address );
			}
		}
	}

	/**
	 * Get direct address by feature
	 * @param feature
	 */
	protected getDirectAddress ( feature: string ): string {
		var directAdress: string = this.webSocketConfig.directAddressFormat;
		directAdress = directAdress.replace( this.FEATURE, feature );
		return directAdress;
	}

	/**
	 * Register handler for channel by address
	 */
	protected setChannelRegister (): void {
		if ( this.webSocketConfig.channelFeatures != undefined ) {
			this.webSocketConfig.channelAddressFormat = this.webSocketConfig.channelAddressFormat.replace( this.PARTNER_CODE, this.partnerCode );
			this.webSocketConfig.channelAddressFormat = this.webSocketConfig.channelAddressFormat.replace( this.LOCALE, this.translator.language );

			for ( var i = 0; i < this.webSocketConfig.channelFeatures.length; i++ ) {
				var address = this.getChannelAddress( this.webSocketConfig.channelFeatures[ i ] );
				this.addressMap.set( address, this.webSocketConfig.channelFeatures[ i ] );
				this.webSocketTransport.onSetRegisterHandler( address );
			}
		}
	}

	/**
	 * Get channel address by feature
	 * @param feature
	 */
	protected getChannelAddress ( feature: string ): string {
		var channeAdress: string = this.webSocketConfig.channelAddressFormat;
		channeAdress = channeAdress.replace( this.FEATURE, feature );
		return channeAdress;
	}

	/**
	 * Register handler for request by address
	 */
	protected setRequestRegister (): void {
		if ( this.webSocketConfig.requestAddressFormat != undefined ) {
			for ( var i = 0; i < this.webSocketConfig.requestFeatures.length; i++ ) {
				var address = this.getRequestAddress( this.webSocketConfig.requestFeatures[ i ] );
				this.addressMap.set( address, this.webSocketConfig.requestFeatures[ i ] );
			}
		}
	}

	/**
	 * Get request address by feature
	 * @param feature
	 */
	protected getRequestAddress ( feature: string ): string {
		var requestAdress: string = this.webSocketConfig.requestAddressFormat;
		requestAdress = requestAdress.replace( this.FEATURE, feature );
		return requestAdress;
	}

	/**
	 * Send request through web socket by address
	 * @param address
	 * @param data
	 */
	public onWebSocketRequest ( data: IWebSocketRequestData ): void {
		this.webSocketTransport.send( data );
	}

	/**
	 * Calling when receive response from server
	 * @param data
	 */
	public onWebSocketReceive ( data: IWebSocketIncommingPackage ): void {
		if ( this.addressMap.has( data.address ) ) {
			var webSocketData: NGWebSocketData = new NGWebSocketData( this.addressMap.get( data.address ), data.body.eventType, data.body );
			this.webSocketService.onWebSocketResponse( webSocketData );
		}
	}

	/**
	 * Calling when receive notification from server
	 * @param data
	 */
	protected onWebSocketNotification ( data: IWebSocketIncommingPackage ): void {
		if ( this.addressMap.has( data.address ) ) {
			var webSocketData: NGWebSocketData = new NGWebSocketData( this.addressMap.get( data.address ), data.body.eventType, data.body.body );
			this.webSocketService.onWebSocketNotification( webSocketData );
		}
	}
}



