import { ITransport } from 'corengf/src/server/abstract/transport/ITransport';
import { IServerConfig } from 'corengf/src/server/abstract/config/IServerConfig';


export class BlankTransport implements ITransport {

	public static readonly LOCAL_HOST: string = 'localhost';
	public static readonly GEN_TEST: string = 'testing.gen-game.net';
	public static readonly GEN_REVIEW: string = 'review.gen-game.net';

	protected config: IServerConfig;

	public onConnect (): void {
		//do nothing
	}
	public onReceive ( message: any ) {
		//do nothing
	}
	public onReceiveError: Array<( message: any ) => void> = [];

	public onError ( message: any ) {
		//do nothing
	}

	public setConfig ( config: IServerConfig ) {
		//do nothing
		this.config = config;
	}

	public connect ( data: any ) {
		//do nothing
	}

	public send ( data: any ) {
		//do nothing
	}

	public psend ( data: any ): Promise<any> {
		return Promise.resolve();
	}

	public disconnect () {
		//do nothing
	}

	public isHostNameValid ( ipStr: string ): boolean {
		var reg: RegExp = new RegExp( '^((25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$' );
		return reg.test( ipStr );
	}

	public isUsingRealEnvironment (): boolean {
		let isLocalOrTest = this.isLocalEnvironment() || this.isTestEnvironment();
		let isValidHostName = this.isHostNameValid( window.location.hostname );
		return !isLocalOrTest && !isValidHostName;
	}

	protected isLocalEnvironment (): boolean {
		return window.location.hostname == BlankTransport.LOCAL_HOST;
	}

	protected isTestEnvironment (): boolean {
		return window.location.hostname == BlankTransport.GEN_TEST || window.location.hostname == BlankTransport.GEN_REVIEW;
	}

	public getDynamicDomainName (): string {
		let domainName: string = this.getDefaultDomain();
		let completeHostName: string = window.location.hostname;
		if ( this.isUsingRealEnvironment() ) {
			let completeHostNameSplit: Array<string> = completeHostName.split( '.' );
			if ( completeHostNameSplit.length > 1 ) {
				domainName = completeHostNameSplit[ completeHostNameSplit.length - 2 ] + '.' + completeHostNameSplit[ completeHostNameSplit.length - 1 ];
			}
		}
		return domainName;
	}

	protected getDefaultDomain (): string {
		let domainName: string = this.config.connectionURL;
		return domainName;
	}
}


