import { Point } from 'pixi.js';
import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { CompView } from './../../mvc/CompView';
import { Button } from './../../elements/canvas/button/Button';
import { IExternalComponentConfig } from './IExternalComponentConfig';
import { LocalButtonsController } from './../../buttons/LocalButtonsController';

/**
 * Create dom container for external .ejs template.
 * If the ejs file contains a script, the code will be excuted in ExternalModuleController. (Only support one script tag for now. )
 * @export
 * @class ExternalModuleView
 * @extends {CompView}
 */
export class ExternalModuleView extends CompView {
	private loader = PIXI.loader;

	public code: string;

	public config: IExternalComponentConfig;

	public get onOrientationChangeSignal (): MiniSignal {
		return this.viewport.onOrientationChangedSignal;
	}

	@Inject
	protected localButtonsController: LocalButtonsController;

	protected button: Button;

	public createButton ( config?: IExternalComponentConfig ): void {
		if ( config && config.debug ) {
			this.button = new Button( config.buttonConfig, this.localButtonsController );
			this.button.anchor = new Point( 0.5, 0.5 );
			this.addChild( this.button );
		}
	}

	protected createElements ( config?: IExternalComponentConfig ): void {
		super.createElements( config );
		this.createButton( config );
		const innerHTML = this.loader.resources[ config.externalKey ].data;
		const element = document.getElementById( config.domID );
		var node = document.createElement( 'div' );
		node.id = config.moduleName;
		element.appendChild( node );
		node.innerHTML = innerHTML;
		const scripts = element.getElementsByTagName( 'script' );
		this.code = scripts[ scripts.length - 1 ].innerHTML;
	}
}


