import { MLUIButtonId } from './MLUIButtonId';
import { TsMap } from 'corengf/src/utils/TsMap';
import { MLUIStates } from './../states/MLUIStates';
import { Inject, Singleton } from 'typescript-ioc/es5';
import { ButtonBehaviour } from 'corengf/src/buttons/ButtonsStateSet';
import { SlotUIButtonId } from 'slotngf/src/components/ui/buttons/SlotUIButtonId';
import { SlotUILocalButtonsStateSet } from 'slotngf/src/components/ui/buttons/SlotUILocalButtonsStateSet';

/**
 * Local Buttons State Class
 * Set state for the local buttons
 */
@Singleton
export class MLUILocalButtonsStateSet extends SlotUILocalButtonsStateSet {

	@Inject
	protected stateNames: MLUIStates;

	protected initStates (): void {
		super.initStates();
		this.buttonStates.set( this.stateNames.gameStartTutorial, this.gameStartTutorial() );
	}

	protected init (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.init();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( true, true ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( true, true ) );
		return buttonState;
	}

	protected gameStartTutorial (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = this.init();
		buttonState.set( SlotUIButtonId.BUTTON_START_SPIN, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotUIButtonId.BUTTON_STOP_SPIN, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotUIButtonId.BUTTON_SHOW_AUTO_PLAY, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotUIButtonId.BUTTON_STOP_AUTO_PLAY, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotUIButtonId.BUTTON_TURBO_MODE, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotUIButtonId.BUTTON_BET_MAX, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotUIButtonId.BUTTON_SHOW_MENU_OPTIONS, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotUIButtonId.BUTTON_SHOW_BET_OPTIONS, new ButtonBehaviour( false, false ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, false ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, false ) );
		return buttonState;
	}

	protected autoSpinReadyForStopSpin (): TsMap<string, ButtonBehaviour> {
		return this.autoSpinSpinStop();
	}

	protected autoSpinShowWinning (): TsMap<string, ButtonBehaviour> {
		return this.autoSpinSpinStop();
	}

	protected spinning (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.spinning();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, true ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, true ) );
		return buttonState;
	}

	protected dashboardDisable (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.dashboardDisable();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, true ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, true ) );
		return buttonState;
	}

	protected startSpin (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.startSpin();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, true ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, true ) );
		return buttonState;
	}

	protected autoSpinSpinStop (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.autoSpinSpinStop();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, false ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, false ) );
		return buttonState;
	}

	protected spinStop (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.spinStop();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, true ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, true ) );
		return buttonState;
	}

	protected skip_autoSpinSpinning (): TsMap<string, ButtonBehaviour> {
		return this.autoSpinSpinStop();
	}

	protected autoSpinSpinning (): TsMap<string, ButtonBehaviour> {
		return this.autoSpinStartSpin();
	}

	protected autoSpinStartSpin (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.autoSpinStartSpin();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, false ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, false ) );
		return buttonState;
	}

	protected transitionToAutoSpin (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.transitionToAutoSpin();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, false ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, false ) );
		return buttonState;
	}

	protected transitionToFreeSpins (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.transitionToFreeSpins();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, false ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, false ) );
		return buttonState;
	}

	protected autoSpinDashboardEnable (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.autoSpinDashboardEnable();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, false ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, false ) );
		return buttonState;
	}

	protected skipSpinning (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.skipSpinning();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, true ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, true ) );
		return buttonState;
	}

	protected gameReadyForStopSpin (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.gameReadyForStopSpin();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, true ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, true ) );
		return buttonState;
	}

	protected freeSpinsStart (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.freeSpinsStart();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, false ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, false ) );
		return buttonState;
	}

	protected freeSpinning (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.freeSpinning();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, false ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, false ) );
		return buttonState;
	}

	protected freeSpinsStop (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.freeSpinsStop();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, false ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, false ) );
		return buttonState;
	}

	protected freeSpinsShowWinning (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.freeSpinsShowWinning();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, false ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, false ) );
		return buttonState;
	}

	protected prefeatureWinning (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.prefeatureWinning();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, false ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, false ) );
		return buttonState;
	}

	protected transitionToBaseGame (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.transitionToBaseGame();
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, false ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, false ) );
		return buttonState;
	}
}



