import { Point } from 'pixi.js';
import { Payline } from 'slotngf/src/components/winDisplay/paylines/Payline';
import { WinDisplayView } from 'slotngf/src/components/winDisplay/base/WinDisplayView';
import { IPaylineConfig } from 'slotngf/src/components/winDisplay/paylines/PaylineConfig';
import { IShowWinLineData } from 'slotngf/src/components/winDisplay/base/WinDisplayController';


export class PaylinesView extends WinDisplayView {

	protected activePaylines: Array<Payline> = new Array<Payline>();

	protected config: IPaylineConfig;

	protected createElements ( config?: IPaylineConfig ): void {
		this.config = config;
	}

	public showPayline ( data: IShowWinLineData ): void {
		//check to prevent animating same line with 2 objects
		for ( let i = 0; i < this.activePaylines.length; i++ ) {
			if ( this.activePaylines[ i ].lineNumber == data.winData.line ) {
				throw '[PaylinesView] : attempt to animate same payline twice line number : ' + data.winData.line;
			}
		}

		let canvasPositions = new Array<Point>();
		//TODO: need support Both landscape and portrait

		const symbolInfo = this.reelsConfig.reelDefinition.symbol;

		data.winData.winTable.forEach( ( reel, x ) => {
			const winSymbolIndex = this.getWinSymbolIndex( reel );

			const pointX = this.layoutData.position.x + symbolInfo.width / 2;
			const pointY = this.layoutData.position.y + symbolInfo.height / 2 + symbolInfo.height * winSymbolIndex;
			canvasPositions.push( new Point( pointX, pointY ) );
		} );

		this.activePaylines.push( Payline.payline( canvasPositions, data.winData.winTable.length, this.config, data.winData.line ) );
		this.addChild( this.activePaylines[ this.activePaylines.length - 1 ].container );
		this.activePaylines[ this.activePaylines.length - 1 ].play();
		//delete itself when animation is done
		this.activePaylines[ this.activePaylines.length - 1 ].onCompleteSignal.add( this.onPaylineAnimationComplete, this );
	}

	public hide (): void {
		this.activePaylines.forEach( payline => {
			payline.destroy();
		} );
		this.activePaylines = [];
	}

	protected onPaylineAnimationComplete ( payline: Payline ): void {
		let index = this.activePaylines.indexOf( payline );
		if ( index > -1 ) {
			this.activePaylines[ index ].destroy();
			this.activePaylines.splice( index, 1 );
		} else {
			throw '[PaylinesView] : no payline index found to remove! lineNumber : ' + payline.lineNumber;
		}
	}

	/**
	 * Get the first win index.
	 *
	 * @protected
	 * @param {Array<boolean>} reel
	 * @returns {number}
	 * @memberof PaylinesView
	 */
	protected getWinSymbolIndex ( reel: Array<boolean> ): number {
		let index = -1;
		reel.forEach( ( value, i ) => {
			if ( value ) {
				index = i;
			}
		} );
		return index;
	}
}


