import { Animation } from 'corengf/src/elements/canvas/animation/Animation';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import * as MiniSignal from 'mini-signals';
import * as particles from 'pixi-particles';
import { extras, spine, Sprite, Texture, Graphics } from 'pixi.js';
import { ReelSet } from 'slotngf/src/components/reels/logic/ReelsConfig';
import { Container } from 'typescript-ioc/es5';
import { isString } from 'util';
import { AnticipationAnimationType, IAnticipationAnimation, IAnticipationParticle, IMask, IReelsConfig, IReelSet, IReelStripConfig, IReelLayoutData } from './../../../core/config/IReelsConfig';
import { WinLineDisplayData } from './../../lines/WinLine';
import { SlotCompView } from './../../mvc/SlotCompView';
import { SymbolData } from './../logic/ReelStripModel';
import { SymbolAnimationPostfix, SymbolState } from './../ReelsEnums';
import { IReelStripView } from './interfaces/IReelStripView';
import { ReelStripOverlay } from './ReelStripOverlay';
import { ReelStripView } from './ReelStripView';
import { Orientation } from 'corengf/src/display/Viewport';
import { TsMap } from 'corengf/src/utils/TsMap';

interface IReelsView {
	[ key: string ]: Array<IReelStripView>;
}

interface IReelsOverlay {
	[ key: string ]: Array<IReelStripView>;
}

/**
 * Please review ReelConfig class to understand how to set position for reel strips.
 */
export class ReelsControllerView extends SlotCompView {
	public static readonly ALL_SYMBOLS_ANIMATION_COMPLETE: string = 'allSymbolsAnimationComplete';

	/**
	 * array of items that only with static picture
	 * and have reel mask
	 */
	protected reelsView: IReelsView;

	/**
	 * array of items that only may the size of symbol can be larger than usual
	 * and don't have reel mask
	 */
	protected reelsOverlay: IReelsOverlay;

	/**
	 * Containes all reel containers
	 */
	protected reelsContainer: Container;

	/**
	 * this container only for reelsView
	 */
	protected reelsViewContainer: PIXI.Container;

	/**
	 * this container only for reelsOverlay
	 */
	protected reelsOverlayContainer: PIXI.Container;

	/**
	 * Reel background sprite
	 */
	protected reelBackground: Sprite;

	/**
	 * Reel frame sprites
	 */
	protected reelFrames: Array<Sprite>;

	/**
	 * Describe how many animations are completed right now on strips.
	 */
	protected completedAnimationCounter: number = 0;

	/**
	 * We simulate the time that we would spend on symbol animation, when there are none
	 * // TODO only for DEV moments when we have not symbols animation
	 */
	protected debugAnimationTimer: SimpleTimer;

	protected reelSet: IReelSet;

	protected reelConfig: IReelsConfig;

	protected anticipationAnimations: Array<extras.AnimatedSprite | spine.Spine | particles.Emitter>;

	public idleAnimationsCompleteSignal: MiniSignal;

	public anticipationContainer: PIXI.Container;

	protected currentReelSetIndex: number = ReelSet.BASE;

	protected idleTimerArray: Array<SimpleTimer>;

	protected defaultSpineTransitionTime: number = 0;

	protected get currentReelSet (): Array<IReelStripConfig> {
		return this.reelSet[ this.currentReelSetIndex ];
	}

	protected get currentReelView (): Array<IReelStripView> {
		return this.reelsView[ this.currentReelSetIndex ];
	}

	protected get currentReelsOverlay (): Array<IReelStripView> {
		return this.reelsOverlay[ this.currentReelSetIndex ];
	}

	protected reelViewMap: TsMap<number, Container> = new TsMap<number, Container>();
	protected reelOverlayMap: TsMap<number, Container> = new TsMap<number, Container>();

	protected get maskConfig (): string | IMask {
		let config = ( this.reelConfig.orientationMaskData )
			? this.reelConfig.orientationMaskData.get( this.currentReelSetIndex ).get( this.viewport.properOrientation )
			: null;

		const maskConfig = config || this.reelConfig.defaultReelsMask.get( this.currentReelSetIndex );

		if ( !maskConfig ) {
			throw new Error( 'can\'t find mask layout config' );
		}

		return maskConfig;
	}

	protected get layoutData (): IReelLayoutData {
		let orientationLayout: IReelLayoutData;
		if ( this.reelConfig.orientationLayoutData ) {
			orientationLayout = this.reelConfig.orientationLayoutData.get( this.viewport.properOrientation );
		}

		const layoutData = orientationLayout || this.reelConfig.defaultLayoutData;

		if ( !layoutData ) {
			throw new Error( 'can\'t layout config.' );
		}
		return layoutData;
	}

	constructor () {
		super();
		this.idleAnimationsCompleteSignal = new MiniSignal();
		this.idleTimerArray = new Array<SimpleTimer>();
	}

	public reelInitView ( reelIndex: number, config: IReelStripConfig ): void {
		this.currentReelView[ reelIndex ].reelInit( config );
	}

	public reelInitOverView ( reelIndex: number, config: IReelStripConfig ): void {
		this.currentReelsOverlay[ reelIndex ].reelInit( config );
		this.currentReelsOverlay[ reelIndex ].symbolAnimationCompleteSignal.add( this.onSymbolAnimationComplete, this );
	}

	public reelSetSymbolName ( reelIndex: number, symbolId: number, symbolName: string, state: SymbolState ): void {
		this.currentReelView[ reelIndex ].setSymbolNameWithState( symbolId, symbolName, state );
		this.currentReelsOverlay[ reelIndex ].setSymbolNameWithState( symbolId, symbolName, state );
	}

	//hiding blur symbols
	public reelSetSymbolState ( reelIndex: number, symbolIndex: number, state: SymbolState ): void {
		this.currentReelView[ reelIndex ].setSymbolState( symbolIndex, state );
		this.currentReelsOverlay[ reelIndex ].setSymbolState( symbolIndex, state );
	}

	public moveLastSymbolToTop ( reelIndex: number, symbolData: SymbolData, state: SymbolState ): void {
		this.currentReelView[ reelIndex ].moveLastSymbolToTop( symbolData, state );
		this.currentReelsOverlay[ reelIndex ].moveLastSymbolToTop( symbolData, state );
	}

	public tweenSymbolsUpdate ( reelIndex: number, symbolId: number, y: number ): void {
		this.currentReelView[ reelIndex ].tweenSymbolsUpdate( symbolId, y );
		this.currentReelsOverlay[ reelIndex ].tweenSymbolsUpdate( symbolId, y );
	}

	public createElements ( reelConfig: IReelsConfig ): void {
		this.reelSet = reelConfig.reelSet;
		this.reelConfig = reelConfig;
		this.initReelsBackground();
		this.initReelsView();
		this.initReelsOverlay();
		this.initReelsFrame();
		this.createAnticipations();
		this.createSymbolAnimationPool();
	}

	protected createSymbolAnimationPool (): void {
		let symbols: Array<string> = this.reelConfig.reelDefinition.rangeSymbols;
		let numColumns: Array<number> = new Array<number>();
		for ( const reelSetIndex in this.reelSet ) {
			if ( ( this.reelSet as Object ).hasOwnProperty( reelSetIndex ) ) {
				numColumns.push( this.reelSet[ reelSetIndex ].length );
			}
		}
		let maxAnimNeeded: number = Math.max( ...numColumns );

		symbols.forEach( ( symbolName: string ) => {
			for ( let type in SymbolAnimationPostfix ) {
				if ( isString( type ) ) {
					let name = 'symbol_' + symbolName + SymbolAnimationPostfix[ type ];
					Animation.createAnimationPool( name, maxAnimNeeded );
				}
			}
		} );
	}

	protected createAnticipations (): void {
		this.anticipationContainer = new PIXI.Container();
		this.anticipationContainer.name = 'AnticipationContainer';
		this.anticipationContainer.visible = false;
		this.addChild( this.anticipationContainer );
		this.anticipationAnimations = new Array<extras.AnimatedSprite | spine.Spine | particles.Emitter>();

		if ( this.reelConfig.anticipationData.animations == undefined || this.reelConfig.anticipationData.animations.length == 0 ) {
			return;
		}

		this.reelConfig.anticipationData.animations.forEach( ( animation: IAnticipationAnimation | IAnticipationParticle ) => {
			if ( animation.type == AnticipationAnimationType.SPINE ) {
				this.anticipationAnimations.push( this.createSpineAnimation( animation ) );
			} else if ( animation.type == AnticipationAnimationType.SEQUENCE ) {
				this.anticipationAnimations.push( this.createSequenceAnimation( animation.name ) );
			} else {
				this.anticipationAnimations.push( this.createParticleAnimation( animation as IAnticipationParticle ) );
			}
		} );
	}

	protected createSpineAnimation ( data: IAnticipationAnimation ): spine.Spine {
		let animation = Animation.createSpineAnimation( data.name );

		let totalReels: Array<number> = new Array<number>();
		for ( const reelSetIndex in this.reelSet ) {
			if ( ( this.reelSet as Object ).hasOwnProperty( reelSetIndex ) ) {
				totalReels.push( this.reelSet[ reelSetIndex ].length );
			}
		}
		let largestNumReels: number = Math.max( ...totalReels );
		for ( let i = ( 1 + this.reelConfig.anticipationData.modelData[ 0 ].anticipationSymbolCountTrigger ); i < largestNumReels; i++ ) {
			animation.stateData.setMix(
				animation.name + '_Reel' + i.toString(),
				animation.name + '_Reel' + ( i + 1 ).toString(),
				data.reelTransitionTime ? data.reelTransitionTime : this.defaultSpineTransitionTime
			);
		}
		animation.skeleton.slots.forEach( ( value, i ) => {
			animation.slotContainers[ i ].name = value.data.name;
		} );

		this.anticipationContainer.addChild( animation );
		return animation;
	}

	protected createSequenceAnimation ( name: string ): extras.AnimatedSprite {
		let animation = Animation.getAnimationByName( name );
		animation.gotoAndStop( 0 );
		animation.loop = true;
		this.anticipationContainer.addChild( animation );

		return animation;
	}

	protected createParticleAnimation ( data: IAnticipationParticle ): particles.Emitter {
		let particleResource = PIXI.loader.resources[ data.name ].data;
		let particleImages: Array<Texture> = new Array<Texture>();

		let container = new PIXI.particles.ParticleContainer();
		this.anticipationContainer.addChild( container );
		data.images.forEach( ( image: string ) => {
			particleImages.push( PIXI.Texture.fromImage( image ) );
		} );

		let animation = new particles.Emitter(
			container,
			particleImages,
			particleResource
		);
		animation.emit = false;
		animation.autoUpdate = true;

		return animation;
	}

	protected isSpineAnimation ( animation: extras.AnimatedSprite | spine.Spine | particles.Emitter ): animation is spine.Spine {
		return ( <spine.Spine> animation ).spineData !== undefined;
	}

	protected isSequenceAnimation ( animation: extras.AnimatedSprite | spine.Spine | particles.Emitter ): animation is extras.AnimatedSprite {
		return ( <extras.AnimatedSprite> animation ).playing !== undefined;
	}

	protected isMaskData ( mask: string | IMask ): mask is IMask {
		return ( <IMask> mask ).x !== undefined;
	}

	protected updateContainerPosition (): void {
		this.reelsViewContainer.position.x = this.layoutData.position.x;
		this.reelsViewContainer.position.y = this.layoutData.position.y;

		const scale = this.layoutData.scale || 1;

		this.reelsViewContainer.scale.set( scale, scale );

		this.reelsOverlayContainer.position.x = this.reelsViewContainer.position.x;
		this.reelsOverlayContainer.position.y = this.reelsViewContainer.position.y;
		this.reelsOverlayContainer.scale.set( this.reelsViewContainer.scale.x, this.reelsViewContainer.scale.y );
	}

	protected updateReelPosition ( orientation: Orientation ): void {
		const reelSet = this.reelSet[ this.currentReelSetIndex ];

		const viewContainer = this.reelViewMap.get( this.currentReelSetIndex );
		const overlayContainer = this.reelOverlayMap.get( this.currentReelSetIndex );

		if ( viewContainer.children.length === 0 ) {
			return;
		}

		for ( let index = 0; index < reelSet.length; index++ ) {
			let positionData = ( orientation === Orientation.PORTRAIT ) ? reelSet[ index ].portraitPosition : reelSet[ index ].landscapePosition;
			positionData = positionData || this.reelConfig.defaultReelPosition[ index ];

			viewContainer.children[ index ].position.x = overlayContainer.children[ index ].position.x = positionData.position.x;
			viewContainer.children[ index ].position.y = overlayContainer.children[ index ].position.y = positionData.position.y;
		}
	}

	public positionUpdateOnTransition (): void {
		this.updateReelPosition( this.viewport.properOrientation );
	}

	public portraitLayout (): void {
		super.portraitLayout();
		this.updateContainerPosition();

		this.updateReelPosition( Orientation.PORTRAIT );

		this.updateMainReelMask();
	}

	public landscapeLayout (): void {
		super.landscapeLayout();
		this.updateContainerPosition();
		this.updateReelPosition( Orientation.LANDSCAPE );
		this.updateMainReelMask();
	}

	/**
	 * initialize array with static picture and with reel mask
	 */
	protected initReelsView (): void {
		this.reelsContainer = new PIXI.Container();
		this.addChild( this.reelsContainer );

		this.reelsViewContainer = new PIXI.Container();
		this.reelsView = {};

		for ( const reelSetIndex of Object.keys( this.reelSet ) ) {
			this.reelsView[ reelSetIndex ] = this.reelsView[ reelSetIndex ] || [];

			let reelSetContainer: PIXI.Container = new PIXI.Container();
			reelSetContainer.name = `${ reelSetIndex }`;
			this.reelViewMap.set( +reelSetIndex, reelSetContainer );
			this.reelSet[ reelSetIndex ].forEach( ( reelConfig, reelIndex ) => {
				const layoutData = this.getSingleReelLayoutData( +reelSetIndex, reelIndex );

				let reelContainer: PIXI.Container = new PIXI.Container();

				reelContainer.position.x = layoutData.position.x;
				reelContainer.position.y = layoutData.position.y;
				if ( this.reelConfig.individualReelsMask ) {
					let reelMask: Graphics = this.getReelMask(
						0,
						0,
						reelConfig.width,
						reelConfig.symbol.height * reelConfig.symbolsOnReel
					);
					reelContainer.mask = reelMask;
					reelContainer.addChild( reelMask );
				}
				reelSetContainer.addChild( reelContainer );

				let baseReelView: IReelStripView = this.createReelStripView();
				baseReelView.setContainer( reelContainer );
				baseReelView.setId( reelIndex );
				this.reelsView[ reelSetIndex ].push( baseReelView );
			} );

			this.reelsViewContainer.addChild( reelSetContainer );
		}
		this.reelsContainer.addChild( this.reelsViewContainer );
	}

	protected getReelMask ( x: number, y: number, width: number, height: number ): Graphics {
		var reelMask: Graphics = new Graphics();
		reelMask.beginFill( 0 );
		reelMask.drawRect( 0, 0, width, height );
		reelMask.endFill();
		reelMask.position.x = x;
		reelMask.position.y = y;

		return reelMask;
	}

	/**
	 * initialize array of items that only may the size of symbol can be larger than usual
	 */
	protected initReelsOverlay (): void {
		this.reelsOverlayContainer = new PIXI.Container();
		this.reelsOverlay = {};
		for ( const reelSetIndex of Object.keys( this.reelSet ) ) {
			this.reelsOverlay[ reelSetIndex ] = this.reelsOverlay[ reelSetIndex ] || [];

			let reelSetContainer: PIXI.Container = new PIXI.Container();
			reelSetContainer.name = `${ reelSetIndex }`;
			this.reelOverlayMap.set( +reelSetIndex, reelSetContainer );
			this.reelSet[ reelSetIndex ].forEach( ( reelConfig, reelIndex ) => {
				const position = this.getSingleReelLayoutData( +reelSetIndex, reelIndex ).position;

				let reelContainer: PIXI.Container = new PIXI.Container();
				reelContainer.position.x = position.x;
				reelContainer.position.y = position.y;

				reelSetContainer.addChild( reelContainer );

				let baseReelView: IReelStripView = this.createReelStripOverlay();
				baseReelView.setContainer( reelContainer );
				baseReelView.setId( reelIndex );
				this.reelsOverlay[ reelSetIndex ].push( baseReelView );

			} );
			this.reelsOverlayContainer.addChild( reelSetContainer );
		}

		this.reelsContainer.addChild( this.reelsOverlayContainer );
	}

	protected createReelMask (): Graphics | Sprite {
		if ( this.reelConfig.orientationMaskData || this.reelConfig.defaultReelsMask ) {
			const mask = this.maskConfig;
			if ( this.isMaskData( mask ) ) {
				return this.getReelMask( mask.x, mask.y, mask.width, mask.height );
			} else {
				return Sprite.fromFrame( mask );
			}
		} else {
			return this.getDefaultGameMask();
		}
	}

	protected updateMainReelMask (): void {
		if ( this.reelsContainer.mask ) {
			let oldMask = this.reelsContainer.mask;
			this.reelsContainer.mask = undefined;
			this.reelsContainer.removeChild( oldMask );
		}
		const mask = this.createReelMask();
		this.reelsContainer.mask = mask;
		this.reelsContainer.addChild( mask );
	}

	protected getDefaultGameMask (): Graphics {
		const firstConfig = this.reelSet[ this.currentReelSetIndex ][ 0 ];
		const firstConfigPosition = this.getSingleReelLayoutData( this.currentReelSetIndex, 0 ).position;

		const lastConfig = this.reelSet[ this.currentReelSetIndex ][ this.reelSet[ this.currentReelSetIndex ].length - 1 ];
		const lastConfigPosition = this.getSingleReelLayoutData( this.currentReelSetIndex, this.reelSet[ this.currentReelSetIndex ].length - 1 ).position;
		return this.getReelMask(
			firstConfigPosition.x,
			firstConfigPosition.y,
			lastConfigPosition.x + lastConfig.width - firstConfigPosition.x,
			firstConfig.symbol.height * firstConfig.symbolsOnReel
		);
	}

	protected createReelStripView (): IReelStripView {
		return Container.get( ReelStripView );
	}

	protected createReelStripOverlay (): IReelStripView {
		return Container.get( ReelStripOverlay );
	}

	public setActiveReelStripSet ( set: number ) {
		this.currentReelSetIndex = set;

		this.reelsViewContainer.children.forEach( ( reelSetView, index ) => {
			reelSetView.visible = ( index === set );
		} );

		this.reelsOverlayContainer.children.forEach( ( reelSetView, index ) => {
			reelSetView.visible = ( index === set );
		} );
		this.updateMainReelMask();
	}

	/**
	 * Add reel background to stage according to setting in config
	 */
	protected initReelsBackground (): void {
		if ( this.reelConfig.reelBackground != null ) {
			this.reelBackground = Sprite.fromFrame( this.reelConfig.reelBackground.assetName );
			this.reelBackground.position.set( this.reelConfig.reelBackground.x, this.reelConfig.reelBackground.y );
			this.addChild( this.reelBackground );
		}
	}

	/**
	 * Add reel frame to stage according to setting in config
	 */
	protected initReelsFrame (): void {
		if ( this.reelConfig.reelsFrames != null ) {
			for ( let i = 0; i < this.reelConfig.reelsFrames.length; i++ ) {
				const reelsFrame = this.reelConfig.reelsFrames[ i ];

				if ( this.reelFrames == undefined ) {
					this.reelFrames = new Array<Sprite>();
				}
				this.reelFrames.push( Sprite.fromImage( reelsFrame.assetName ) );
				this.reelFrames[ i ].position.set( reelsFrame.x, reelsFrame.y );
				if ( reelsFrame.scaleX != undefined && reelsFrame.scaleY != undefined ) {
					this.reelFrames[ i ].scale.set( reelsFrame.scaleX, reelsFrame.scaleY );
				}
				this.addChild( this.reelFrames[ i ] );
			}
		}
	}

	public deleteAllAnimations (): void {
		let numTimers = this.idleTimerArray.length;
		for ( let i = 0; i < numTimers; i++ ) {
			let timer = this.idleTimerArray.pop();
			timer.stop();
			timer.destroy();
		}

		if ( this.debugAnimationTimer != null ) {
			this.debugAnimationTimer.stop();
		}
		this.completedAnimationCounter = 0;

		this.reelsView[ this.currentReelSetIndex ].forEach( ( stripView, index ) => {
			for ( var j = 0; j < this.reelSet[ this.currentReelSetIndex ][ index ].symbolsOnReel; j++ ) {
				stripView.showSymbol( j );
			}
			this.currentReelsOverlay[ index ].deleteSymbolsAnimation();
		} );
	}

	public stopAnticipate (): void {
		this.anticipationContainer.visible = false;
		this.anticipationAnimations.forEach( ( animation: extras.AnimatedSprite | spine.Spine | particles.Emitter ) => {
			if ( this.isSpineAnimation( animation ) ) {
				animation.state.setEmptyAnimation( 0, 0 );
				animation.state.setEmptyAnimation( 1, 0 );
			} else if ( this.isSequenceAnimation( animation ) ) {
				animation.gotoAndStop( 0 );
			} else {
				animation.emit = false;
			}
		} );
	}

	public anticipateReel ( reelId: number ): void {
		this.anticipationContainer.visible = true;
		this.anticipationAnimations.forEach( ( animation: extras.AnimatedSprite | spine.Spine | particles.Emitter ) => {
			if ( this.isSpineAnimation( animation ) ) {
				animation.state.setAnimation( 0, animation.name + '_Idle', true );
				animation.state.setAnimation( 1, animation.name + '_Reel' + ( reelId + 1 ), false ); //Need reelID +1 because reelIDs are 0 based and animations are 1 based
			} else if ( this.isSequenceAnimation( animation ) ) {
				animation.position.x = this.currentReelView[ reelId ].position.x;
				animation.position.y = this.currentReelView[ reelId ].position.y;
				animation.play();
			} else {
				animation.parent.x = this.currentReelView[ reelId ].position.x;
				animation.parent.y = this.currentReelView[ reelId ].position.y;
				animation.emit = true;
			}
		} );
	}

	public anticipateScatters ( dropped: Array<number> ): void {
		dropped.forEach( ( symbolId: number, index: number ) => {
			if ( symbolId >= 0 ) {
				if ( this.currentReelsOverlay[ index ].playSymbolAnimation( symbolId, SymbolAnimationPostfix.ANTICIPATION ) ) {
					this.currentReelView[ index ].hideSymbol( symbolId );
					this.currentReelsOverlay[ index ].showSymbol( symbolId );
				}
			}
		} );
	}

	public setSymbolsStateForAll ( state: SymbolState ): void {
		this.currentReelSet.forEach( ( reel, index ) => {
			for ( let j = 1; j <= reel.symbolsOnReel; j++ ) {
				this.currentReelView[ index ].setSymbolState( j, state );
				this.currentReelsOverlay[ index ].setSymbolState( j, state );
			}
		} );
	}

	protected onSymbolAnimationComplete ( reelId: number, symbolId: number, type: string ): void {
		this.currentReelsOverlay[ reelId ].hideSymbol( symbolId );
		this.currentReelView[ reelId ].showSymbol( symbolId );


		if ( type != SymbolAnimationPostfix.DROPPED ) {
			this.completedAnimationCounter--;
			if ( this.completedAnimationCounter == 0 ) {
				if ( type == SymbolAnimationPostfix.WIN || this.checkWildMultiplierAnimationType( type ) ) {
					this.dispatchAllSymbolsAnimationComplete();
				} else if ( type == SymbolAnimationPostfix.IDLE ) {
					this.idleAnimationsCompleteSignal.dispatch();
				}
			}
		}
	}

	protected checkWildMultiplierAnimationType ( type: string ): boolean {
		if ( type == SymbolAnimationPostfix.WILD_X2 ||
			type == SymbolAnimationPostfix.WILD_X3 ||
			type == SymbolAnimationPostfix.WILD_X4 ) {
			return true;
		}
		return false;
	}

	public playShaking ( reelId: number ): boolean {
		let isShaking: boolean = false;
		this.currentReelSet.forEach( ( reel, index ) => {
			if ( reelId == index ) {
				for ( let j = 1; j <= reel.symbolsOnReel; j++ ) {
					this.currentReelView[ index ].hideSymbol( j );
					this.currentReelsOverlay[ index ].showSymbol( j );
					this.currentReelsOverlay[ index ].playSymbolShaking( j );
				}
				isShaking = true;
			}
		} );
		return isShaking;
	}

	public stopShaking (): void {
		this.currentReelSet.forEach( ( reel, index ) => {
			for ( let j = 1; j <= reel.symbolsOnReel; j++ ) {
				this.currentReelView[ index ].showSymbol( j );
				this.currentReelsOverlay[ index ].hideSymbol( j );

				this.currentReelsOverlay[ index ].stopSymbolShaking( j,
					this.currentReelView[ index ].getSymbolContainer( j ).x,
					this.currentReelView[ index ].getSymbolContainer( j ).y );
			}
		} );
	}

	/**
	 * when we try show idle animation, we hide static symbol and show overlay symbol
	 */
	public playIdleAnimationsLoop (): void {
		this.deleteAllAnimations();
		this.currentReelSet.forEach( ( reel, index ) => {
			for ( let j = 1; j <= reel.symbolsOnReel; j++ ) {
				if ( this.currentReelsOverlay[ index ].playSymbolAnimation( j, SymbolAnimationPostfix.IDLE ) ) {
					this.currentReelView[ index ].hideSymbol( j );
					this.currentReelsOverlay[ index ].showSymbol( j );
					this.completedAnimationCounter++;
				}
			}
		} );
	}

	public playRandomIdleAnimations (): void {
		this.deleteAllAnimations();
		this.currentReelSet.forEach( ( reel, index ) => {
			for ( let j = 1; j <= reel.symbolsOnReel; j++ ) {
				let randomData = this.reelConfig.idleAnimationRandomChance;
				if ( Math.random() < randomData.percent ) {
					let time = 1000 * ( ( Math.random() * randomData.startDelayMax ) + randomData.startDelayMin );
					this.idleTimerArray.push( new SimpleTimer( time, () => {
						if ( this.currentReelsOverlay[ index ].playSymbolAnimation( j, SymbolAnimationPostfix.IDLE ) ) {
							this.currentReelView[ index ].hideSymbol( j );
							this.currentReelsOverlay[ index ].showSymbol( j );
							this.completedAnimationCounter++;
						}
					} ) );
				}
			}
		} );
	}

	/**
	 * when we try show drop animation, we hide static symbol and show overlay symbol
	 */
	public symbolDropAnimation ( reelId: number, symbolId: number ): void {
		if ( this.currentReelsOverlay[ reelId ].playSymbolAnimation( symbolId, SymbolAnimationPostfix.DROPPED ) ) {
			this.currentReelView[ reelId ].hideSymbol( symbolId );
			this.currentReelsOverlay[ reelId ].showSymbol( symbolId );
		}
	}

	/**
	 * when we try show win line animation, we hide static symbol and show overlay symbol
	 * @param {WinLine} winItemsData for show win symbols
	 * @param {Array< Array <number> >} loseSymbolsPosition the position of symbols that we should transform
	 * to LOSE state
	 */
	public showWinLine ( winItemsData: WinLineDisplayData ): void {
		this.deleteAllAnimations();

		let isWildMultiplier: boolean = false;
		let countTopExtraSymbol: number = 1; // the previous symbol on the reelview top

		winItemsData.winTable.forEach( ( reel, reelIndex ) => {
			if ( reel === null || reel.length === 0 ) {
				return;
			}
			reel.forEach( ( value, symbolIndex ) => {
				if ( !value ) { // skip lose symbol
					return;
				}

				// if the symbol type is wild and wild_multiplier feature, we need change wild animation.
				isWildMultiplier = false;
				const symbolId: number = symbolIndex + countTopExtraSymbol;
				if ( winItemsData.wildMultiplier != undefined && winItemsData.wildMultiplier.length > 0 ) {
					winItemsData.wildMultiplier.forEach( ( feature ) => {
						let wildId: number = feature.symbol_index + countTopExtraSymbol;

						if ( feature.reel_index == reelIndex && wildId == symbolId ) { // is wild Symbol
							isWildMultiplier = true;
							this.playWildMultiplierAnimation( feature.reel_index, wildId, feature.multiplier );
						}
					} );
				}

				if ( !isWildMultiplier && this.currentReelsOverlay[ reelIndex ].playSymbolAnimation( symbolId, SymbolAnimationPostfix.WIN ) ) {
					this.currentReelView[ reelIndex ].hideSymbol( symbolId );
					this.currentReelsOverlay[ reelIndex ].showSymbol( symbolId );
					this.completedAnimationCounter++;
				}
			} );
		} );

		if ( this.completedAnimationCounter == 0 ) { // when we do not symbols animation
			this.debugAnimationTimer = new SimpleTimer( 3000, this.dispatchAllSymbolsAnimationComplete.bind( this ) );
		}
	}

	protected playWildMultiplierAnimation ( reelId: number, symbolId: number, multiplier: number ): void {
		let wildType: string;
		switch ( multiplier ) {
			case 2: wildType = SymbolAnimationPostfix.WILD_X2; break;
			case 3: wildType = SymbolAnimationPostfix.WILD_X3; break;
			case 4: wildType = SymbolAnimationPostfix.WILD_X4; break;
		}

		if ( wildType != undefined && this.currentReelsOverlay[ reelId ].playSymbolAnimation( symbolId, wildType ) ) {
			this.currentReelView[ reelId ].hideSymbol( symbolId );
			this.currentReelsOverlay[ reelId ].showSymbol( symbolId );
			this.completedAnimationCounter++;
		}
	}

	public setSymbolStateFromPositions ( winTable: Array<Array<boolean>>, winState: SymbolState, loseState: SymbolState ): void {
		winTable.forEach( ( reelPosition, i ) => {
			reelPosition.forEach( ( value, j ) => {
				const symbolState = ( value ) ? winState : loseState;
				this.currentReelView[ i ].setSymbolState( j + 1, symbolState );
				this.currentReelsOverlay[ i ].setSymbolState( j + 1, symbolState );
			} );
		} );
	}

	protected dispatchAllSymbolsAnimationComplete (): void {
		GlobalDispatcher.dispatchIntoExpectedStates(
			ReelsControllerView.ALL_SYMBOLS_ANIMATION_COMPLETE, [
			this.stateNames.freeRespinsAllLineWin,
			this.stateNames.freeRespinsShowWinning,
			this.stateNames.allLineWin,
			this.stateNames.skip_allLineWin,
			this.stateNames.showWinning,
			this.stateNames.showWinningLoop,
			this.stateNames.prefeatureWinning,
			this.stateNames.freeSpinsAllLineWin,
			this.stateNames.skip_freeSpinsAllLineWin,
			this.stateNames.freeSpinsShowWinning
		] );
	}

	protected getSingleReelLayoutData ( reelSetIndex: number, reelIndex: number ): IReelLayoutData {
		let orientationLayout: IReelLayoutData;
		if ( this.viewport.properOrientation === Orientation.LANDSCAPE ) {
			orientationLayout = this.reelConfig.reelSet[ reelSetIndex ][ reelIndex ].landscapePosition;
		} else if ( this.viewport.properOrientation === Orientation.PORTRAIT ) {
			orientationLayout = this.reelConfig.reelSet[ reelSetIndex ][ reelIndex ].portraitPosition;
		}
		const layoutData: IReelLayoutData = orientationLayout || this.reelConfig.defaultReelPosition[ reelIndex ];
		return layoutData;
	}
}


