import * as MiniSignal from 'mini-signals';
import { Singleton } from 'typescript-ioc/es5';

/**
 * Test portal for selenium.
 * This object will allow users enter `selenium.method()` in console window to excute method.
 */
@Singleton
export class SeleniumPortal {

	public closePreloaderSignal: MiniSignal = new MiniSignal();

	private readonly maxHistoryLength = 50;
	private messageHistory: Array<SeleniumHistoryMessage> = [];
	private currentState: string = '';

	protected isTestEnvironment: boolean;

	/**
	 * if it's test envriment, create authority for window
	 * @param isTestEnvironment
	 */
	public setupSelenium ( isTestEnvironment: boolean ): void {
		this.isTestEnvironment = isTestEnvironment;
		if ( isTestEnvironment ) {
			global[ 'selenium' ] = this;
			window[ 'selenium' ] = this;
		}
	}

	public addToHistory ( message: SeleniumHistoryMessage ): void {
		if ( this.isTestEnvironment ) {
			this.messageHistory.push( message );
			if ( this.messageHistory.length > this.maxHistoryLength ) {
				this.messageHistory.shift();
			}
		}
	}

	public setState ( state: string ): void {
		this.currentState = state;
	}

	public getHistory (): Array<SeleniumHistoryMessage> {
		return this.messageHistory;
	}

	public getState (): string {
		return this.currentState;
	}

	public getData (): SeleniumResponse {
		return {
			stateName: this.currentState,
			receivedMessageHistory: this.messageHistory
		};
	}

	public closePreloader (): void {
		this.closePreloaderSignal.dispatch();
	}
}

export interface SeleniumHistoryMessage {
	name: string;
	direction: MessageDirection;
	timestamp: number;
}

export interface SeleniumResponse {
	stateName: string;
	receivedMessageHistory: Array<SeleniumHistoryMessage>;
}

export enum MessageDirection {
	INBOUND = 'inbound',
	OUTBOUND = 'outbound'
}


