import { ICompViewConfig } from 'corengf/src/core/config/ICompViewConfig';
import { ICoreUISpriteElementConfig, ICoreUITextFieldElementConfig } from 'corengf/src/factory/CoreUIElementFactory';


export class UIBankConfig implements IUIBankConfig {
	public name = 'bankPanel';
	public totalBetIcon: ICoreUISpriteElementConfig;
	public totalBetValue: ICoreUITextFieldElementConfig;
	public balanceIcon: ICoreUISpriteElementConfig;
	public balanceValue: ICoreUITextFieldElementConfig;
	public spacing: IUIBankSpacingConfig;
}

export interface IUIBankConfig extends ICompViewConfig {
	totalBetIcon: ICoreUISpriteElementConfig;
	totalBetValue: ICoreUITextFieldElementConfig;
	balanceIcon: ICoreUISpriteElementConfig;
	balanceValue: ICoreUITextFieldElementConfig;
	spacing: IUIBankSpacingConfig;
}

export interface IUIBankSpacingConfig {
	padding: number;
	iconSpacing: number;
}

export interface IBankBackgroundConfig {
	draw: boolean;
	RowOneHeight: number;
	RowOneNineSlice: NineSlicePlaneConfig;
	RowTwoHeight: number;
	RowTwoColor: number;
	RowTwoAlpha: number;
	assetName: string;
}

export interface NineSlicePlaneConfig {
	leftWidth: number;
	topHeight: number;
	rightWidth: number;
	bottomHeight: number;
}


