import { spine } from 'pixi.js';
import * as MiniSignal from 'mini-signals';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Animation } from 'corengf/src/elements/canvas/animation/Animation';
import { ICoreElementConfig, CoreElement } from 'corengf/src/elements/canvas/CoreElement';
import { CommonAnimation, ICommonAnimationConfig } from 'corengf/src/elements/canvas/animation/CommonAnimation';

/**
 * General spine control.
 * Reference: http://zh.esotericsoftware.com/spine-api-reference#AnimationState
 *
 * @export
 * @class SpineAnimation
 * @extends {AnimationBase}
 */
export class SpineAnimation extends CoreElement {

	public static elementType: string = 'SpineAnimation';

	protected mainObject: spine.Spine;

	protected config: ISpineAnimationConfig;

	/**
	 * Triggers on each animation start
	 *
	 * @type {MiniSignal}
	 * @memberof SpineAnimation
	 */
	public startSignal: MiniSignal;

	/**
	 * Triggers on each animation completed
	 *
	 * @type {MiniSignal}
	 * @memberof SpineAnimation
	 */
	public completeSignal: MiniSignal;

	/**
	 * the callback arrays map
	 *
	 * @protected
	 * @type {SpineCallbacks}
	 * @memberof SpineAnimation
	 */
	protected registeredCallbacks: SpineCallbacks;

	/**
	 * The spine object
	 *
	 * @readonly
	 * @type {spine.Spine}
	 * @memberof SpineAnimation
	 */
	public get displayObject (): spine.Spine {
		return this.mainObject;
	}

	/**
	 * Controls the spine's animation speed.
	 *
	 * @type {number}
	 * @memberof SpineAnimation
	 */
	public get timeScale (): number {
		return this.mainObject.state.timeScale;
	}

	public set timeScale ( value: number ) {
		this.mainObject.state.timeScale = value;
	}

	/**
	 * The Map that records the track should be cleared when the animation completed.
	 * key: trackEntry.animation.name
	 *
	 * @private
	 * @type {TsMap<string, boolean>}
	 * @memberof SpineAnimation
	 */
	private trackMap: TsMap<string, boolean> = new TsMap<string, boolean>();


	/**
	 * Level1 key: ${animationName}_${eventType}
	 *
	 * Level2 key: spriteAttribute.key
	 *
	 * @private
	 * @type {TsMap<string, TsMap<string, PIXI.extras.AnimatedSprite>>}
	 * @memberof SpineAnimation
	 */
	private spriteMap: TsMap<string, TsMap<string, PIXI.extras.AnimatedSprite>> = new TsMap<string, TsMap<string, PIXI.extras.AnimatedSprite>>();



	constructor ( config: ISpineAnimationConfig ) {
		super( config );
		this.config = config;
		this.startSignal = new MiniSignal();
		this.completeSignal = new MiniSignal();

		this.mainObject = Animation.createSpineAnimation( config.animationName );

		const scale = this.config.scale || { x: 1, y: 1 };
		const position = this.config.position || { x: 0, y: 0 };

		this.scale.set( scale.x, scale.y );
		this.position.set( position.x, position.y );

		this.addChild( this.mainObject );

		this.setContainerNames();
		this.resetCallbacks();
		this.setupAnimatedSprite();
		this.addBasicListener();
	}

	protected resetCallbacks (): void {
		this.registeredCallbacks = {
			complete: [],
			start: []
		};
	}

	protected setupAnimatedSprite (): void {
		if ( !this.config.spriteMap ) {
			return;
		}
		this.config.spriteMap.forEach( ( spriteConfig: SpriteConfig, animationName: string ) => {
			let spriteKey: string = null;
			let animation: PIXI.extras.AnimatedSprite = null;
			for ( const eventType in spriteConfig ) {
				if ( spriteConfig[ eventType ] ) {
					const spriteAttributes: Array<SpriteAttribute> = spriteConfig[ eventType ];
					spriteAttributes.forEach( ( spriteAttribute: SpriteAttribute ) => {
						animation = ( spriteKey !== spriteAttribute.key ) ? Animation.getAnimationFromPool( spriteAttribute.key ) : animation;
						if ( !animation ) {
							return;
						}

						if ( spriteAttribute.tweenConfig ) {
							spriteAttribute.tweenConfig.target = animation;
						}
						const subSpriteMap = this.spriteMap.get( `${ animationName }_${ eventType }` ) || new TsMap<string, PIXI.extras.AnimatedSprite>();


						subSpriteMap.set( spriteAttribute.key, animation );
						this.spriteMap.set( `${ animationName }_${ eventType }`, subSpriteMap );

						if ( spriteKey !== spriteAttribute.key ) {
							animation.x = spriteAttribute.x || 0;
							animation.y = spriteAttribute.y || 0;
							animation.scale.x = spriteAttribute.scaleX || 1;
							animation.scale.y = spriteAttribute.scaleY || 1;
							animation.animationSpeed = spriteAttribute.animationSpeed || animation.totalFrames / 60;
							animation.visible = false;
							animation.loop = spriteAttribute.loop || false;
							this.addChild( animation );
						}

						spriteKey = spriteAttribute.key;
					} );

				}
			}
		} );
	}

	protected addBasicListener (): void {
		this.mainObject.state.addListener( {
			start: this.onStart.bind( this ),
			complete: this.onComplete.bind( this )
		} );
	}

	protected setContainerNames (): void {
		this.mainObject.skeleton.slots.forEach( ( value, i ) => {
			this.mainObject.slotContainers[ i ].name = value.data.name;
		} );
	}

	/**
	 * triggers on each animation started.
	 *
	 * @protected
	 * @param {spine.core.TrackEntry} trackEntry
	 * @memberof SpineAnimation
	 */
	protected onStart ( trackEntry: spine.core.TrackEntry ): void {
		this.startSignal.dispatch( this, trackEntry );

		this.updateSprite( trackEntry.animation.name, PIXI.spine.core.EventType.start );

		this.callbackRegisteredEvents( trackEntry );
	}

	/**
	 * triggers on each animation completed.
	 *
	 * @protected
	 * @param {spine.core.TrackEntry} trackEntry
	 * @memberof SpineAnimation
	 */
	protected onComplete ( trackEntry: spine.core.TrackEntry ): void {
		this.completeSignal.dispatch( this, trackEntry );

		this.updateSprite( trackEntry.animation.name, PIXI.spine.core.EventType.complete );

		if ( this.trackMap[ trackEntry.animation.name ] ) {
			this.mainObject.state.clearTrack( trackEntry.trackIndex );
		}

		this.callbackRegisteredEvents( trackEntry );
	}


	/**
	 * Callback to registered events for each animation
	 * @param trackEntry
	 */
	protected callbackRegisteredEvents ( trackEntry: spine.core.TrackEntry ): void {
		this.registeredCallbacks.complete.forEach( ( callbackItem: AnimationCallbackItem ) => {

			// triggers specific animation callback
			if ( callbackItem.animationName === trackEntry.animation.name ) {
				callbackItem.callback();
			}
		} );
	}

	/**
	 * update sprite status when event triggered.
	 *
	 * @protected
	 * @param {string} animationName
	 * @param {PIXI.spine.core.EventType} eventType
	 * @returns {void}
	 * @memberof SpineAnimation
	 */
	protected updateSprite ( animationName: string, eventType: PIXI.spine.core.EventType ): void {
		if ( !this.hasSpriteOnAnimation( animationName ) ) {
			return;
		}
		const type: string = PIXI.spine.core.EventType[ eventType ];
		const sprites: TsMap<string, PIXI.extras.AnimatedSprite> = this.spriteMap.get( `${ animationName }_${ type }` );

		if ( !sprites ) {
			return;
		}

		const spriteAttributes: Array<SpriteAttribute> = this.config.spriteMap.get( animationName )[ type ];

		spriteAttributes.forEach( ( spriteAttribute: SpriteAttribute ) => {
			const spriteAnimMap: TsMap<string, PIXI.extras.AnimatedSprite> = this.spriteMap.get( `${ animationName }_${ type }` );
			const spriteAnimation: PIXI.extras.AnimatedSprite = spriteAnimMap.get( spriteAttribute.key );
			spriteAnimation.visible = true;
			spriteAnimation.play();

			if ( spriteAttribute.tweenConfig ) {
				//@ts-ignore
				CommonAnimation.startTween( spriteAttribute.tweenConfig ).start();
			}
		} );
	}

	protected hasSpriteOnAnimation ( animationName ): boolean {
		if ( this.config.spriteMap && this.config.spriteMap.has( animationName ) ) {
			return true;
		}
		return false;
	}

	/**
	 * Removes all animations from all tracks, leaving skeletons in their current pose.
	 *
	 * @memberof SpineAnimation
	 */
	public stop (): void {
		this.mainObject.state.clearTracks();
	}

	public setMix ( name: string, mixinName: string, mixinTime: number ): void {
		this.mainObject.stateData.setMix( name, mixinName, mixinTime );
	}

	public reset ( shouldClearTrack: boolean = false ): void {
		if ( shouldClearTrack ) {
			this.mainObject.state.clearTracks();
		}
		this.mainObject.skeleton.setToSetupPose();
	}

	public tracks ( index: number ): spine.core.TrackEntry {
		return this.mainObject.state.tracks[ index ];
	}

	/**
	 * Play by state name
	 *
	 * @param {string} stateName
	 * @param {*} [context]
	 * @param {boolean} [skipOnDuplicate=true] set true will prevent play same animation twice
	 * @memberof SpineAnimation
	 */
	public playByStateName ( stateName: string, context?: any, skipOnDuplicate: boolean = true ): void {
		let animationsMap: AnimationMap = this.config.stateAnimationMap.get( stateName );
		this.playByAnimationMap( animationsMap, context, skipOnDuplicate );
	}

	public playByAnimationMap ( animationsMap: AnimationMap, context?: any, skipOnDuplicate: boolean = true ): void {
		this.resetCallbacks();
		this.hideAllSprite();

		this.trackMap = new TsMap<string, boolean>();

		if ( this.mainObject.state.timeScale === 0 ) {
			console.warn( 'Tying to play but timeScale is 0' );
		}

		for ( const trackIndex in animationsMap ) {
			if ( animationsMap[ trackIndex ] ) {
				const trackInfo = animationsMap[ trackIndex ];
				if ( typeof trackInfo === 'string' ) {
					if ( trackInfo === 'hide' ) {
						this.hide();
					}
				} else if ( trackInfo.length === 0 ) {
					this.setEmptyAnimation( +trackIndex );
				} else {
					this.show();

					trackInfo.forEach( ( trackItem, index ) => {
						if ( this.tracks( +trackIndex ) && this.tracks( +trackIndex ).animation.name == trackItem.name && skipOnDuplicate ) {
							return;
						}

						const generalTrackConfig = ( this.config.tracks ) ? this.config.tracks[ index ] : {
							alpha: 1,
							mix: 0,
							delay: 0
						};
						let entry: spine.core.TrackEntry;

						const alpha = generalTrackConfig.alpha || 1;
						let mixDuration = generalTrackConfig.mix || 0;
						let delay = generalTrackConfig.delay || 0;

						delay = trackItem.delay || delay;
						mixDuration = trackItem.mixDuration || mixDuration;

						if ( trackItem.listener ) {
							trackItem.listener.forEach( ( listenerItem ) => {
								let callback = () => { return; };

								if ( context && context[ listenerItem.functionName ] ) {
									callback = context[ listenerItem.functionName ].bind( context );
								} else if ( this[ listenerItem.functionName ] ) {
									callback = this[ listenerItem.functionName ];
								}

								this.addCallbacks( listenerItem.event, {
									animationName: trackItem.name,
									callback
								} );

							} );
						}

						if ( !trackItem.name ) {
							this.setEmptyAnimation( +trackIndex );
						} else {
							if ( index === 0 ) {
								entry = this.play( +trackIndex, trackItem.name, trackItem.loop );
							} else {
								entry = this.append( +trackIndex, trackItem.name, trackItem.loop, delay );
							}
							entry.alpha = alpha;
						}

						if ( trackItem.stop ) {
							this.appendEmptyAnimation( +trackIndex, mixDuration, delay );
						} else if ( trackItem.clearTrack ) {
							this.trackMap.set( trackItem.name, true );
						}

					} );
				}
			} else {
				this.setEmptyAnimation( +trackIndex );
			}
		}
	}

	protected hideAllSprite (): void {
		this.spriteMap.forEach( ( spriteMap ) => {
			for ( const key in spriteMap ) {
				if ( spriteMap[ key ] ) {
					spriteMap[ key ].visible = false;
				}
			}
		} );
	}

	protected addCallbacks ( eventType: PIXI.spine.core.EventType, callbackItem: AnimationCallbackItem ): void {
		this.registeredCallbacks[ PIXI.spine.core.EventType[ eventType ] ].push( callbackItem );
	}

	public hide (): void {
		this.mainObject.renderable = false;
		this.mainObject.visible = false;
	}

	public show (): void {
		this.mainObject.renderable = true;
		this.mainObject.visible = true;
	}

	public setEmptyAnimation ( track: number, mixDuration: number = 0 ): spine.core.TrackEntry {
		return this.mainObject.state.setEmptyAnimation( track, mixDuration );
	}

	public appendEmptyAnimation ( track: number, mixDuration: number = 0, delay: number = 0 ): spine.core.TrackEntry {
		return this.mainObject.state.addEmptyAnimation( track, mixDuration, delay );
	}

	public play ( track: number, name: string, loop: boolean = false ): spine.core.TrackEntry {
		this.mainObject.renderable = true;
		return this.mainObject.state.setAnimation( track, name, loop );
	}

	/**
	 * Append an animation on given track
	 *
	 * @param {number} track
	 * @param {string} name
	 * @param {boolean} [loop=false]
	 * @param {number} [delay=0]
	 * @returns {spine.core.TrackEntry}
	 * @memberof SpineAnimation
	 */
	public append ( track: number, name: string, loop: boolean = false, delay: number = 0 ): spine.core.TrackEntry {
		return this.mainObject.state.addAnimation( track, name, loop, delay );
	}

	/**
	 * Bind event listener on animationState object
	 *
	 * @param {spine.core.AnimationStateListener2} listener
	 * @memberof SpineAnimation
	 */
	public addAnimationListener ( listener: spine.core.AnimationStateListener2 ): void {
		this.mainObject.state.addListener( listener );
	}

	/**
	 * Clears all events on animationState object
	 *
	 * @memberof SpineAnimation
	 */
	public clearAnimationListeners (): void {
		this.mainObject.state.clearListeners();
	}
}

export interface SpineCallbacks {
	start: Array<AnimationCallbackItem>;
	complete: Array<AnimationCallbackItem>;
}

export interface AnimationCallbackItem {
	animationName: string;
	callback: Function;
}

export type AnimationMap = { [ key: string ]: string | Array<AnimationAttribute> };


export interface ISpineAnimationConfig extends ICoreElementConfig {
	animationName: string;
	stateAnimationMap?: TsMap<string, AnimationMap>;
	mixins?: TsMap<string, Array<MixinAttribute>>;
	tracks?: Array<AnimationTrack>;
	spriteMap?: TsMap<string, SpriteConfig>;
}

export interface SpriteConfig {
	complete?: Array<SpriteAttribute>;
	start?: Array<SpriteAttribute>;
}

export interface SpriteAttribute {
	key: string; // the sprite name
	loop?: boolean;
	x?: number;
	y?: number;
	scaleX?: number;
	scaleY?: number;
	animationSpeed?: number;
	tweenConfig?: ICommonAnimationConfig;
}

export interface AnimationAttribute {
	name: string;
	loop: boolean;

	/**
	 * Delay start time in seconds
	 *
	 * @type {number}
	 * @memberof AnimationAttribute
	 */
	delay?: number;

	/**
	 * stop=true will remove all animations from the track once the animation completed, leaving skeleton in current pose.
	 *
	 * @type {boolean}
	 * @memberof AnimationAttribute
	 */
	clearTrack?: boolean;

	/**
	 * stop=true will append additional empty animation on the track.
	 *
	 * @type {boolean}
	 * @memberof AnimationAttribute
	 */
	stop?: boolean;

	/**
	 * mixDuration time in seconds
	 *
	 * @type {number}
	 * @memberof AnimationAttribute
	 */
	mixDuration?: number;
	listener?: Array<AnimListener>;
}

export interface AnimListener {
	event: PIXI.spine.core.EventType;
	functionName: string;
}

export interface MixinAttribute {
	animationName: string;
	mixinTime: number;
}

export interface AnimationTrack {
	alpha: number;
	mix: number;

	/**
	 * Delay start time in seconds
	 *
	 * @type {number}
	 * @memberof AnimationTrack
	 */
	delay: number;
}

export class SoundAttribute {
	constructor ( key: string, loop?: boolean ) {
		this.key = key;
		this.loop = loop;
	}
	public key: string;
	public loop: boolean = false;
}

export enum SpineEventType {
	start = 'start',
	end = 'end',
	complete = 'complete',
}


