import { Container } from 'typescript-ioc/es5';
import { NGSlotApi } from 'slotngf/src/server/ng/api/NGSlotApi';
import { UrlParameters } from 'corengf/src/utils/UrlParameters';
import { IPackage, HTTP_METHODS } from 'corengf/src/server/abstract/transport/HttpTransport';
import { SlotNGHttpTransport } from 'slotngf/src/server/ng/transport/SlotNGHttpTransport';
import { ISlotNGServerConfig } from 'slotngf/src/server/ng/config/ISlotNGServerConfig';
import { HistoryDetailRequestMsgFormatter } from 'slotngf/src/server/ng/formatter/http/out/HistoryDetailRequestMsgFormatter';

/**
 * history detail api
 */
export class HistoryDetail extends NGSlotApi {

	public init ( transport: SlotNGHttpTransport, config: ISlotNGServerConfig ): void {
		super.init( transport, config );
		this.responseFormatter = null;
		this.requestFormatter = Container.get( HistoryDetailRequestMsgFormatter );
	}

	protected responseFormat ( data: any ): void {
		this.response = data.payload;
	}

	protected encapsulation ( messageType: string ): IPackage {
		const url = this.getURL( this.request );

		return {
			url: url,
			name: messageType,
			method: HTTP_METHODS.GET,
			headers: this.transport.getHeaders()
		};
	}

	public getURL ( urlParam: any ): string {
		let url = this.config.protocol + this.config.historyApiDomain + this.config.path.historyDetail;
		url = url.replace( '{domain}', this.transport.getDynamicDomainName() );
		url = url.replace( '{partner_token}', UrlParameters.partnerToken );
		url = url.replace( '{round_id}', urlParam.roundID );

		return url;
	}
}


