import { Inject } from 'typescript-ioc/es5';
import { AudioTrackName } from 'corengf/src/audio/SoundManager';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { SlotSoundConfig } from 'slotngf/src/core/config/SlotSoundConfig';
import { SlotBackgroundController } from 'slotngf/src/components/background/SlotBackgroundController';
import { ITransitionToBaseGameData, ITransitionToFreeSpinsData, IStartPickFeatureData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { DHBBackgroundView } from 'game/src/components/background/DHBBackgroundView';
import { DHBSlotDialogController } from 'game/src/components/dialog/DHBSlotDialogController';
import { DHBSlotFreeSpinsController } from 'game/src/components/freeSpins/DHBSlotFreeSpinsController';

export class DHBBackgroundController extends SlotBackgroundController {

	protected view: DHBBackgroundView;
	protected delayBackgroundTransitionData: ITransitionToBaseGameData;
	protected currentAudioGameState: AudioGameState = AudioGameState.BASEGAME;

	constructor ( @Inject view: DHBBackgroundView ) {
		super( view );
	}

	public addEventListeners (): void {
		GlobalDispatcher.addListener( this, DHBSlotDialogController.OUTRO_DIALOG_CLOSED, this.onDialogClosed );
		GlobalDispatcher.addListener( this, DHBSlotFreeSpinsController.PICKGAME_OUTRO_COMPLETE, this.pickFeatureComplete );
	}

	protected transitionToBaseGame ( _data: ITransitionToBaseGameData ): void {
		this.delayBackgroundTransitionData = _data;
		this.finishStateAction();
	}

	protected onDialogClosed (): void {
		this.view.transitionToBaseGame( this.delayBackgroundTransitionData );
		this.currentAudioGameState = AudioGameState.BASEGAME;
		super.transitionToBaseGame( this.delayBackgroundTransitionData );
	}

	protected transitionToFreeSpins ( _data: ITransitionToFreeSpinsData ): void {
		this.view.transitionToFreeSpins( _data );
		this.currentAudioGameState = AudioGameState.FREEGAME;
		super.transitionToFreeSpins( _data );
	}

	protected startPickFeature ( _data: IStartPickFeatureData ): void {
		this.soundManager.play( 'featureTriggerAudio', AudioTrackName.MUSIC, false, () => {
			this.soundManager.play( 'pickgameLoop', AudioTrackName.MUSIC, true );
		} );
		this.finishStateAction();
	}

	protected pickFeatureComplete (): void {
		if ( this.currentAudioGameState == AudioGameState.BASEGAME ) {
			this.soundManager.play( SlotSoundConfig.backgroundLoop, AudioTrackName.MUSIC, true );
		} else {
			this.soundManager.play( SlotSoundConfig.bonusIntro, AudioTrackName.MUSIC, false, () => {
				this.soundManager.play( SlotSoundConfig.bonusLoop, AudioTrackName.MUSIC, true );
			} );
		}
	}

}

enum AudioGameState {
	BASEGAME,
	FREEGAME
}


