import { Container, Inject } from 'typescript-ioc/es5';
import { NumberDisplayMode } from 'corengf/src/utils/MoneyFormatter';
import { NGBNGController } from 'corengf/src/server/ng/sdk/bng/NGBNGController';
import { BNGBaseAPI } from 'slotngf/src/server/ng/sdk/bng/api/BNGBaseAPI';
import { SlotNPSModel } from 'slotngf/src/server/ng/sdk/bng/model/SlotNPSModel';
import { SlotNGHttpTransport } from 'slotngf/src/server/ng/transport/SlotNGHttpTransport';
import { BNGGenerateEntropy } from 'slotngf/src/server/ng/sdk/bng/api/BNGGenerateEntropy';


export class BNGEntropyAPI extends BNGBaseAPI {

	@Inject
	protected slotNGModel: SlotNPSModel;

	protected generateEntropyAPI: BNGGenerateEntropy;

	private resolve: Function;
	private reject: Function;

	public init ( transport: SlotNGHttpTransport, config?: any ): void {
		super.init( transport, config );
		this.generateEntropyAPI = Container.get( BNGGenerateEntropy );
		this.generateEntropyAPI.init( transport, config );
		NGBNGController.onIframeResponse.add( this.onIframeResponse.bind( this ) );
	}

	protected onIframeResponse ( message: BNG.IMessageFromPortal ): void {
		if ( message.method !== 'makeBetTx' && message.method !== 'makeSeedTx' ) {
			return;
		}
		const payload = message.payload;

		if ( payload.error && this.reject ) {
			this.reject( payload.error );
			this.reject = null;
		} else if ( this.resolve ) {
			this.resolve( payload.result );
			this.resolve = null;
		}
	}

	protected generateEntropy (): Promise<any> {
		return this.generateEntropyAPI.send( 'generate_entropy', null );
	}

	protected postRequestMessage ( method: BNG.Method, data: any ): Promise<any> {
		return new Promise( ( resolve, reject ) => {
			this.resolve = resolve;
			this.reject = reject;
			NGBNGController.postMessage( method, data );
		} );
	}

	public makeBetTx ( data: any ): Promise<any> {

		const playerInfo = this.slotNGModel.playerInfo;
		console.log( '[BNG_PLAYERINFO]', playerInfo );

		let totalBet: string = this.moneyFormatter.formatMoney( this.bankService.totalBet, NumberDisplayMode.VALUE_WITH_CURRENCY,
			false, this.slotNGModel.fraction );
		totalBet = totalBet.replace( ',', '' );

		const requestData: BNG.MakeBetRequest = {
			totalBet: totalBet,
			hashedEntropy: data.entropy_hex,
			gameId: this.config.gameCode
		};

		return this.postRequestMessage( 'makeBetTx', requestData );
	}

	public makeSeedTx ( data: any ): Promise<any> {

		const playerInfo = this.slotNGModel.playerInfo;
		console.log( '[BNG_PLAYERINFO]', playerInfo );

		let totalBet: string = this.moneyFormatter.formatMoney( this.bankService.totalBet, NumberDisplayMode.VALUE_WITH_CURRENCY,
			false, this.slotNGModel.fraction );
		totalBet = totalBet.replace( ',', '' );

		const requestData: BNG.MakeSeedRequest = {
			totalBet: totalBet,
			hashedEntropy: data.entropy_hex,
			gameId: this.config.gameCode
		};

		return this.postRequestMessage( 'makeSeedTx', requestData );
	}
}



