import { SlotNGHttpFormatter } from './../../SlotNGHttpFormatter';

/**
 * Format history detail request
 */
export class HistoryDetailRequestMsgFormatter extends SlotNGHttpFormatter {

	protected requestKey: string;

	/**
	 * sends button press emulation package
	 * @param data - ignored
	 * @returns {any} message object
	 */
	public codeMessage ( data?: any ): any {
		return {
			roundID: data.roundID
		};
	}
}



