import { Inject } from 'typescript-ioc/es5';
import { MLUISpinPanel } from 'mlui/src/components/spin/MLUISpinPanel';
import { MLUISpinController } from 'mlui/src/components/spin/MLUISpinController';

export class DHBMLUISpinController extends MLUISpinController {

	constructor ( @Inject view: MLUISpinPanel ) {
		super( view );
	}

	protected autoSpinAllLineWin (): void {
		this.view.showSpinButton();
		this.finishStateAction();
	}
}


