import { CoreState } from 'corengf/src/states/CoreState';
import { Helper } from 'corengf/src/utils/Helper';

/**
 * Contains set of states that describes the game flow.
 */
export class Scenario {

	/**
	 * Order of states
	 */
	private states: Array<CoreState>;

	private _name: string;

	get length (): number {
		return this.states.length;
	}

	get name (): string {
		return this._name;
	}

	/**
	 * Create clones of states and put them into this.states
	 * @param name of scenario
	 * @param states order of states
	 */
	constructor ( name: string, states?: Array<CoreState> ) {
		this._name = name;
		this.states = new Array<CoreState>();

		if ( states != undefined ) {
			states.forEach( ( element: CoreState ) => {
				this.states.push( new CoreState(
					element.stateName,
					Helper.mergeTwoObjects( {}, element.stateData ),
					element.stateGroup
				) );
			} );
		}
	}

	public addState ( state: CoreState ): void {
		this.states.push( state );
	}

	public addStateAt ( state: CoreState, index: number ): void {
		if ( index <= this.states.length - 1 ) {
			this.states.splice( index, 0, state );
		} else {
			throw 'The index [' + index + '] is out of range [' + ( this.states.length - 1 ) + ']';
		}
	}

	public setStateData ( stateName: string, data: Object, group: string = Helper.EMPTY ): void {
		this.getStateByName( stateName, group ).stateData = data;
	}

	public addStateData ( stateName: string, data: Object, group: string = Helper.EMPTY ): void {
		let state: CoreState = this.getStateByName( stateName, group );
		state.stateData = Helper.mergeTwoObjects( state.stateData, data );
	}

	public getStateByName ( stateName: string, stateGroup: string = Helper.EMPTY ): CoreState {
		for ( let index = 0; index < this.states.length; index++ ) {
			let state = this.states[ index ];
			if ( state.stateName == stateName && state.stateGroup == stateGroup ) {
				return state;
			}
		}
		throw 'invalid name of state: ' + stateName;
	}

	public getStatesByGroup ( stateGroup: string ): Array<CoreState> {
		let result: Array<CoreState> = [];
		for ( let i = 0; i < this.states.length; i++ ) {
			if ( this.states[ i ].stateGroup === stateGroup ) {
				result.push( this.states[ i ] );
			}
		}
		return result;
	}

	public getStates (): Array<CoreState> {
		return this.states;
	}

	public removeStateByName ( stateName: string, stateGroup: string = Helper.EMPTY ): void {
		for ( let i = this.states.length - 1; i >= 0; i-- ) {
			if ( this.states[ i ].stateName === stateName && this.states[ i ].stateGroup === stateGroup ) {
				this.states.splice( i, 1 );
			}
		}
	}

	public removeStatesByGroup ( stateGroup: string ): void {
		for ( let i = this.states.length - 1; i >= 0; i-- ) {
			if ( this.states[ i ].stateGroup === stateGroup ) {
				this.states.splice( i, 1 );
			}
		}
	}

	public getGroupsByStateName ( stateName: string ): Array<string> {
		let result: Array<string> = [];
		for ( let i = 0; i < this.states.length; i++ ) {
			if ( this.states[ i ].stateName === stateName ) {
				result.push( this.states[ i ].stateGroup );
			}
		}
		return result;
	}

	public clone (): Scenario {
		return new Scenario( this.name, this.states );
	}

	public addScenario ( scenarioToAdd: Scenario ): void {
		scenarioToAdd.states.forEach( ( state: CoreState ) => {
			this.states.push( state );
		} );
	}

	public hasStateName ( stateName: string, stateGroup: string = Helper.EMPTY ): boolean {
		for ( let index = 0; index < this.states.length; index++ ) {
			let state = this.states[ index ];
			if ( state.stateName == stateName && state.stateGroup == stateGroup ) {
				return true;
			}
		}

		return false;
	}

	public hasStateGroup ( stateGroup: string ): boolean {
		for ( let i = 0; i < this.states.length; i++ ) {
			if ( this.states[ i ].stateGroup === stateGroup ) {
				return true;
			}
		}

		return false;
	}

	public setAllStatesGroup ( stateGroup: string ): void {
		for ( let i = 0; i < this.states.length; i++ ) {
			this.states[ i ].stateGroup = stateGroup;
		}
	}
}


