import { Inject } from 'typescript-ioc/es5';
import { CompView } from 'corengf/src/mvc/CompView';
import { Translator } from 'corengf/src/utils/Translator';
import { IReelsConfig, IReelLayoutData } from 'slotngf/src/core/config/IReelsConfig';
import { IWinDisplayConfig } from 'slotngf/src/core/config/IWinDisplayConfig';
import { IShowWinLineData } from 'slotngf/src/components/winDisplay/base/WinDisplayController';
import { Orientation } from 'corengf/src/display/Viewport';
import { MoneyFormatter } from 'corengf/src/utils/MoneyFormatter';

export class WinDisplayView extends CompView {

	@Inject
	protected translator: Translator;

	@Inject
	protected moneyFormatter: MoneyFormatter;

	protected config: IWinDisplayConfig;
	protected reelsConfig: IReelsConfig;

	protected createElements ( config?: IWinDisplayConfig ): void {
		this.config = config;
		//Add payline elements
	}

	public setReelsConfig ( config: IReelsConfig ): void {
		this.reelsConfig = config;
	}

	public showPayline ( data: IShowWinLineData ): void {
		//Add logic to show payline
	}

	public hide (): void {
		this.visible = false;
	}

	protected get layoutData (): IReelLayoutData {
		let orientationLayout: IReelLayoutData;
		if ( this.reelsConfig.orientationLayoutData ) {
			orientationLayout = this.reelsConfig.orientationLayoutData.get( this.viewport.properOrientation );
		}

		const layoutData = orientationLayout || this.reelsConfig.defaultLayoutData;
		layoutData.scale = layoutData.scale || 1;

		if ( !layoutData ) {
			throw new Error( 'can\'t layout config.' );
		}
		return layoutData;
	}

	protected getSingleReelLayoutData ( reelSetIndex: number, reelIndex: number ): IReelLayoutData {
		let orientationLayout: IReelLayoutData;
		if ( this.viewport.properOrientation === Orientation.LANDSCAPE ) {
			orientationLayout = this.reelsConfig.reelSet[ reelSetIndex ][ reelIndex ].landscapePosition;
		} else if ( this.viewport.properOrientation === Orientation.PORTRAIT ) {
			orientationLayout = this.reelsConfig.reelSet[ reelSetIndex ][ reelIndex ].portraitPosition;
		}
		const layoutData: IReelLayoutData = orientationLayout || this.reelsConfig.defaultReelPosition[ reelIndex ];
		return layoutData;
	}
}


