import { ICompViewConfig } from 'corengf/src/core/config/ICompViewConfig';
import { ISpineAnimationConfig } from 'corengf/src/elements/canvas/animation/spine/SpineAnimation';
import { IBitmapTextFieldTweenConfig, ITweenBitmapTextField } from 'corengf/src/elements/canvas/text/TweenBitmapTextField';

/**
 * https://staroad.atlassian.net/wiki/spaces/PHFRAM/pages/81429370/BigWin+JSON+description
 */
export interface IBigWinConfig extends ICompViewConfig {
	/**
	 * Win value conter label
	 */
	bigWinValueLabel: ITweenBitmapTextField;
	valueLabelTweenInConfig: IBitmapTextFieldTweenConfig;
	valueLabelTweenOutConfig: IBitmapTextFieldTweenConfig;

	bigWinAnimation: ISpineAnimationConfig;

	winTierSettings: Array<IWinTierSettings>;
}

export interface IWinTierSettings {
	/**
	 * Type of Big Win
	 */
	tier: string;
	/**
	 * Upper and lower bound of win multiplier
	 */
	winSpan: ISpan;
	/**
	 * Count time of big win text
	 */
	countTime: number;
	/**
	 * Stay time after count complete
	 */
	stayTime: number;
	/**
	 * Time settings (start and end time) for each BigWinTier
	 */
	timeTable: Array<IBigWinTiming>;
}

/**
 * Span description
 */
interface ISpan {
	start: number;
	end: number;
}

/**
 * Start and end times for the specified win
 */

interface IBigWinTiming {
	tier: string;
	timeSpan: ISpan;
}

/**
 * Use the same structure for tiers and phases within a tier because
 * they are identical.
 */
export class BigWinTierType {
	/**
	* Don't do anything.
	*/
	public static readonly None: string = 'None';

	/**
	* Count up balance only.
	*/
	public static readonly Balance: string = 'Balance';

	/**
	* Count up balance and numbers only.
	*/
	public static readonly Numbers: string = 'Numbers';

	/**
	* Count up balance, numbers and show coin shower.
	*/
	public static readonly Coins: string = 'Coins';

	/**
	* Count up balance and numbers, show coin shower and big win message.
	*/
	public static readonly BigWin: string = 'BigWin';

	/**
	* Count up balance and numbers, show coin shower and extreme big win message.
	*/
	public static readonly ExtremeBigWin: string = 'ExtremeBigWin';

	/**
	* Count up balance and numbers, show coin shower and ultra extreme big win message.
	*/
	public static readonly UltraExtremeBigWin: string = 'UltraExtremeBigWin';
}


