import { Inject } from 'typescript-ioc/es5';
import { Asset, AssetLoader } from 'corengf/src/core/AssetLoader';
import { SlotInformationHeaderView } from 'slotngf/src/components/informationHeader/SlotInformationHeaderView';

export class MLUIInformationHeaderView extends SlotInformationHeaderView {

	@Inject
	protected assetLoader: AssetLoader;
	public getAssetsList (): Array<Asset> {
		const asset = [
			{
				assetKey: 'InformationHeaderLogo',
				assetURL: 'assets/uiInformation/{partner}/logo.png'
			},
			{
				assetKey: 'InformationHeaderLogo_gray',
				assetURL: 'assets/uiInformation/{partner}/logo_gray.png'
			}
		];

		return asset;
	}

	protected getImageSource ( assetName: string ): string {
		return this.assetLoader.toTargetPath( 'assets/uiInformation/{partner}/' + assetName );
	}
}


