import { Inject } from 'typescript-ioc/es5';
import { ShowWinLineData } from 'slotngf/src/components/winDisplay/base/reelV2/WinDisplayV2Controller';
import { WinBoxV2Controller } from 'slotngf/src/components/winDisplay/winBoxes/reelV2/WinBoxV2Controller';
import { DHBWinBoxView } from 'game/src/components/winDisplay/winBoxes/DHBWinBoxView';

export class DHBWinBoxV2Controller extends WinBoxV2Controller {

	protected view: DHBWinBoxView;
	protected isFreeSpin: boolean = false;

	constructor ( @Inject view: DHBWinBoxView ) {
		super( view );
	}

	public addEventListeners (): void {
		super.addEventListeners();
		this.view.tutorialOutroCompleteSignal.add( this.animationComplete, this );
	}

	protected onShowWinLine ( e: GlobalWinDisplayData ): void {
		let state: string = e.data.winData.currentState;
		this.view.showPayline( e.data, this.isFreeSpin );
	}

	public transitionToFreeSpins (): void {
		this.isFreeSpin = true;
		super.transitionToFreeSpins();
	}

	public wildLockTriggered (): void {
		this.view.stopAnimating();
		this.finishStateAction();
	}

	protected tutorialOutro ( data?: any ): void {
		this.view.tutorialOutroSymbolShow();
	}
	protected animationComplete ( e?: any, data?: any ): void {
		this.finishStateAction();
	}

	public transitionToBaseGame (): void {
		this.isFreeSpin = false;
		super.transitionToBaseGame();
	}
}

interface GlobalWinDisplayData {
	type: string;
	data: ShowWinLineData;
}


