import { Singleton } from 'typescript-ioc/es5';

@Singleton
export class SlotNPSModel {
	private _playerInfo: BNG.IGetPlayerInfoResult;

	public get playerInfo (): BNG.IGetPlayerInfoResult {
		return this._playerInfo;
	}
	public set playerInfo ( value: BNG.IGetPlayerInfoResult ) {
		this._playerInfo = value;
	}

	private _makeBetInfo: BNG.IMakeBetTxResult;

	public get makeBetInfo (): BNG.IMakeBetTxResult {
		return this._makeBetInfo;
	}
	public set makeBetInfo ( value: BNG.IMakeBetTxResult ) {
		this._makeBetInfo = value;
	}

	private _makeSeedInfo: BNG.IMakeSeedTxResult;

	public get makeSeedInfo (): BNG.IMakeSeedTxResult {
		return this._makeSeedInfo;
	}

	public set makeSeedInfo ( value: BNG.IMakeSeedTxResult ) {
		this._makeSeedInfo = value;
	}

	/**
	 * the money format need matched to fraction though sdk
	 * ex: fraction = 4, will pass 1.000 to sdk
	 */
	private _fraction: number;
	public get fraction (): number {
		return this._fraction;
	}
	public set fraction ( value: number ) {
		this._fraction = value;
	}
}


