import * as MiniSignal from 'mini-signals';
import { spine } from 'pixi.js';
import { TsMap } from 'corengf/src/utils/TsMap';
import { CoreElement, ICoreElementConfig } from 'corengf/src/elements/canvas/CoreElement';
import { SpineAnimation, AnimationMap } from 'corengf/src/elements/canvas/animation/spine/SpineAnimation';
import { ICoreUISpineAnimationElementConfig, CoreUIElementFactory } from 'corengf/src/factory/CoreUIElementFactory';
import { ILoadingBar } from './ILoadingBar';


export class SpineLoadingBar extends CoreElement implements ILoadingBar {

	public finishedLoadingSignal: MiniSignal;

	protected animation: SpineAnimation;
	protected currentPercentage: number = 0;
	protected currentAnimationState: string = '';
	protected updateAnimationMap: TsMap<string, AnimationMap>;
	protected config: ISpineLoadingBarConfig;
	protected loadingDone: boolean = false;

	protected initElement ( config: ISpineLoadingBarConfig ): void {
		this.finishedLoadingSignal = new MiniSignal();
		this.updateAnimationMap = config.animationConfig.elementConfig.stateAnimationMap;

		this.createAnimation( config.animationConfig );
	}

	protected createAnimation ( animationConfig: ICoreUISpineAnimationElementConfig ): void {
		this.animation = CoreUIElementFactory.createInstance( animationConfig );
		this.animation.completeSignal.add( this.onAnimationComplete, this );
		this.addChild( this.animation );
	}

	protected onAnimationComplete ( animation: SpineAnimation, entry: spine.core.TrackEntry ): void {
		if ( entry.animation.name == this.config.completeAnimationName ) {
			//this.animation.renderable = false;
			this.finishedLoadingSignal.dispatch();
		}
	}

	public update ( percentage: number ): void {
		if ( this.loadingDone ) {
			return;
		}
		this.currentPercentage = percentage;
		let keyValue: string;
		this.updateAnimationMap.keys().forEach( ( key: string ) => {
			let percentValue: number = +key;
			if ( percentValue <= this.currentPercentage * 100 ) {
				keyValue = key;
			}
		} );
		if ( keyValue != this.currentAnimationState ) {
			this.currentAnimationState = keyValue;
			this.animation.playByStateName( this.currentAnimationState );
		}
	}

	public playCompleteAnimation (): void {
		this.loadingDone = true;
		this.animation.playByStateName( this.config.completeAnimationName );
	}

	public dispose (): void {

		if ( this.animation != undefined ) {
			this.animation.destroy();
			this.animation = undefined;
		}

		this.config = undefined;
		this.finishedLoadingSignal.detachAll();
		this.finishedLoadingSignal = undefined;
	}
}

export interface ISpineLoadingBarConfig extends ICoreElementConfig {
	animationConfig: ICoreUISpineAnimationElementConfig;
	completeAnimationName: string;
	loadingCompleteAnimationName: string;
}


