import { UIInformationHeaderView } from 'corengf/src/components/ui/components/informationHeader/UIInformationHeaderView';


export class SlotInformationHeaderView extends UIInformationHeaderView {

	protected addClockCompoment (): void {
		if ( this.config.viewConfig.show_time ) {
			super.addClockCompoment();
		}
	}

	protected addTitleCompoment (): void {
		if ( this.config.viewConfig.show_title ) {
			super.addTitleCompoment();
		}
	}

	protected onClickBackground (): void {
		if ( this.mainContainer.interactive ) {
			this.clickBackgroundSignal.dispatch();
		}
	}

	public fullScreenUIShow (): void {
		this.mainContainer.interactive = false;
		if ( this.config.viewConfig.show_time ) {
			this.modifyClockTextOpacity( this.config.clockConfig.text.grayValue );
		}

		if ( this.config.viewConfig.show_title ) {
			this.modifyTitleTextOpacity( this.config.titleConfig.text.grayValue );
			this.modifyImageSource( this.config.titleConfig.logo.grayAssetName );
		}
	}

	public fullScreenUIHide (): void {
		this.mainContainer.interactive = true;
		if ( this.config.viewConfig.show_time ) {
			this.modifyClockTextOpacity( this.config.clockConfig.text.originalValue );
		}

		if ( this.config.viewConfig.show_title ) {
			this.modifyTitleTextOpacity( this.config.titleConfig.text.originalValue );
			this.modifyImageSource( this.config.titleConfig.logo.originalAssetName );
		}
	}

	public modifyClockTextOpacity ( value: string ): void {
		const dom = document.getElementById( this.DIV_INFORMATION_HEADER_CLOCK_ID );

		if ( dom ) {
			dom.style.opacity = value;
		}
	}

	public modifyTitleTextOpacity ( value: string ): void {
		const dom = document.getElementById( this.DIV_INFORMATION_HEADER_TITLE_TEXT_ID );

		if ( dom ) {
			dom.style.opacity = value;
		}
	}

	public modifyImageSource ( fileName: string ): void {
		const dom = document.getElementById( this.IMG_INFORMATION_HEADER_TITLE_LOGO_ID );
		if ( dom ) {
			dom.setAttribute( 'src', this.getImageSource( fileName ) );
		}
	}
}


