import { Inject } from 'typescript-ioc/es5';
import { Helper } from 'corengf/src/utils/Helper';
import { AssetLoader } from 'corengf/src/core/AssetLoader';
import { CoreWebElement, ICoreWebElementConfig } from 'corengf/src/elements/webpage/CoreWebElement';

/**
 * Image element for web page
 */
export class WebImageElement extends CoreWebElement {

	public static elementType: string = 'WEB_IMAGE_ELEMENT';

	@Inject
	public loader: AssetLoader;

	public createElement ( config: IWebImageConfig ): IWebImageConfig {
		return Helper.mergeTwoObjects( super.createElement( config ),
			{
				type: config.type,
				blockStyle: config.blockStyle,
				imageSource: config.imageSource,
				imageData: this.getImageSourceData( config.imageSource, config.imageData ),
				imageStyle: config.imageStyle
			}
		);
	}

	protected getImageSourceData ( imageSource: ImageSourceType, imageData ): IImageSourceData {
		let data: IImageSourceData = null;
		switch ( imageSource ) {
			case ImageSourceType.SPRITE:
				data = this.getSpriteData( imageData );
				break;
			case ImageSourceType.TEXTURE:
				data = this.getTextureData( imageData );
				break;
			case ImageSourceType.URL:
				data = this.getURLData( imageData );
				break;
		}
		return data;
	}

	protected getSpriteData ( spriteData: IImageSprite ): IImageSprite {
		spriteData.imagePath = this.loader.toTargetPath( spriteData.imagePath );
		return spriteData;
	}

	protected getTextureData ( textureData: IImageTexture ): IImageTexture {
		textureData.imagePath = this.loader.toTargetPath( textureData.imagePath );
		return textureData;
	}

	protected getURLData ( urlData: IImageURL ): IImageURL {
		return urlData;
	}
}

export interface IWebImageConfig extends ICoreWebElementConfig {
	imageSource: ImageSourceType;
	imageData: IImageSprite | IImageTexture | IImageURL;
	imageStyle?: string;
}

export interface IImageSourceData {
	ratio: number;
}

export interface IImageSprite extends IImageSourceData {
	assetName: string;
	imageName: string;
	imagePath?: string;
}

export interface IImageTexture extends IImageSourceData {
	imagePath: string;
	width: number;
	height: number;
}

export interface IImageURL extends IImageSourceData {
	url: string;
	width: number;
	height: number;
}

export enum ImageSourceType {
	TEXTURE = 'TEXTURE',
	SPRITE = 'SPRITE',
	URL = 'URL'
}


