import * as MiniSignal from 'mini-signals';
import { Singleton } from 'typescript-ioc/es5';

/**
 * Service for broadcast web socket notification and response.
 */
@Singleton
export class WebSocketService {

	public onWebSocketRequestSignal: MiniSignal = new MiniSignal();
	public onWebSocketNotificationSignal: MiniSignal = new MiniSignal();
	public onWebSocketResponseSignal: MiniSignal = new MiniSignal();

	public onWebSocketRequest ( webSocketData: any ): void {
		this.onWebSocketRequestSignal.dispatch( webSocketData );
	}

	public onWebSocketResponse ( webSocketData: any ): void {
		this.onWebSocketResponseSignal.dispatch( webSocketData );
	}

	public onWebSocketNotification ( webSocketData: any ): void {
		this.onWebSocketNotificationSignal.dispatch( webSocketData );
	}
}


