import { Container, Singleton } from 'typescript-ioc/es5';
import { UILocalButtonsStateSet } from 'corengf/src/components/ui/buttons/UILocalButtonsStateSet';
import { LocalButtonsController } from 'corengf/src/buttons/LocalButtonsController';


@Singleton
export class UILocalButtonsController extends LocalButtonsController {

	constructor () {
		super();
		this.buttonStates = Container.get( UILocalButtonsStateSet );
	}
}



