import { Inject, Container } from 'typescript-ioc/es5';
import { AudioTrackName } from 'corengf/src/audio/SoundManager';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { SlotEvent } from 'slotngf/src/core/SlotEvent';
import { Spin } from 'slotngf/src/components/reelV2/action/Spin';
import { SymbolState } from 'slotngf/src/components/reels/ReelsEnums';
import { Spinning } from 'slotngf/src/components/reelV2/action/Spinning';
import { SlotSoundConfig } from 'slotngf/src/core/config/SlotSoundConfig';
import { ReelController } from 'slotngf/src/components/reelV2/ReelController';
import { IFreeRespinReelsDetail } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { IReelActionHandlerConfig, ActionModelConfig } from 'slotngf/src/components/reelV2/ReelActionHandler';
import { DHBSpinDown } from 'game/src/components/reels/action/DHBSpinDown';
import { DHBReelControllerView } from 'game/src/components/reels/DHBReelControllerView';
import { DHBWildLock, IDHBWildLocked } from 'game/src/components/reels/action/DHBWildLock';

export class DHBReelController extends ReelController {

	protected view: DHBReelControllerView;
	protected respinReels: Array<number> = new Array<number>();
	protected isRespinAnticipation: boolean = false;
	protected wildLockReset: boolean = false;

	constructor ( @Inject view: DHBReelControllerView ) {
		super( view );
	}

	public addEventListeners (): void {
		super.addEventListeners();
		GlobalDispatcher.addListener( this, 'REEL_STOP_ACTION', this.onReelStopAction.bind( this ) );
		this.view.tutorialOutroCompleteSignal.add( this.animationComplete, this );
	}

	protected getReelActionHandlerConfig (): IReelActionHandlerConfig {

		let action: ActionModelConfig = {
			model: Container.get( DHBWildLock ),
			config: {
				symbolName: 'symbol_WILD',
				overlayLayerIndex: 1
			}
		};



		return {
			startSpin: {
				model: Container.get( Spin ),
				config: {
					symbolDefinition: this.componentConfig.symbolDefinition,
					duration: this.componentConfig.reelDefinition.reelTween.duration
				}
			},
			spinning: { model: Container.get( Spinning ) },
			allReelStopped: null,
			spinStop: {
				model: Container.get( DHBSpinDown ),
				config: {
					anticipationData: this.componentConfig.anticipationData,
					dropSymbols: this.componentConfig.reelDefinition.dropSymbols,
					symbolDefinition: this.componentConfig.symbolDefinition,
					stopReelNormalDelay: this.stopReelDelay.get( this.reelModel.currentReelSetIndex )
				}
			},
			featureAction: action,
			customActionMap: null
		};
	}

	public startSpin (): void {
		this.soundManager.play( SlotSoundConfig.reelSpin, AudioTrackName.EFFECT );
		this._activeReelsID = null;
		this.resetReels();
		this.reelModel.reset();
		this.view.showOverlay( 0 );
		if ( this.wildLockReset ) {
			this.view.resetOverlay( 0 );
			this.wildLockReset = false;
		}
		this.reelActionHandler.startSpin( {
			reels: this.activeReelsID,
			spinDirection: this.spinDirection
		} ).then( () => {
			this.finishStateAction();
		} );
	}

	protected onReelStopAction ( data: any ): void {
		if ( this.respinReels.length > 0
			&& this.respinReels.includes( data.data.reel )
			&& this.respinReels.indexOf( data.data.reel ) < this.respinReels.length - 1 ) {
			this.freeRespinAnticipation( this.respinReels[ this.respinReels.indexOf( data.data.reel ) + 1 ] );
		}
	}

	public freeRespinReels ( data: IFreeRespinReelsDetail ): void {
		this.respinReels = data.reels_to_spin;
		this.resetReels();
		this.view.setSymbolsStateForAll( SymbolState.LOSE );
		this.reelModel.reset();
		this.activeReelsID = data.reels_to_spin;
		this.freeRespinAnticipation( this.respinReels[ 0 ] );
		this.reelActionHandler.startSpin( {
			reels: data.reels_to_spin,
			spinDirection: this.spinDirection
		} ).then( () => {
			GlobalDispatcher.dispatchIntoExpectedState( SlotEvent.RESPIN_REELS, this.stateNames.freeRespinReels );
			this.finishStateAction();
		} );
	}

	protected freeRespinFinish ( data: any ): void {
		this.respinReels = [];
		if ( this.isRespinAnticipation ) {
			this.isRespinAnticipation = false;
			GlobalDispatcher.dispatchIntoExpectedStates( DHBReelController.ANTICIPATE_END_NOTIFICATION,
				[
					this.stateNames.freeRespinFinish,
					this.stateNames.freeRespinsStop
				],
				this.respinReels[ this.respinReels.length - 1 ]
			);
			if ( this.reelModel.isShaking ) {
				this.view.stopShaking();
				this.reelModel.isShaking = false;
			}
			this.view.stopAnticipate();
		}
		this.finishStateAction();
	}

	protected freeRespinAnticipation ( reelid: number ): void {
		this.isRespinAnticipation = true;
		GlobalDispatcher.dispatchIntoExpectedStates( DHBReelController.ANTICIPATE_REEL_NOTIFICATION,
			[
				this.stateNames.freeRespinReels,
				this.stateNames.freeRespinning,
				this.stateNames.freeRespinsStop
			],
			reelid - 1,
		);
		this.view.anticipateReel( reelid );
		if ( !this.reelModel.isShaking ) {
			for ( let i = 0; i <= reelid - 1; i++ ) {
				this.reelModel.isShaking = this.view.playShaking( i );
			}
		} else {
			this.view.playShaking( reelid - 1 );
		}
	}

	public pickFeatureWin (): void {
		this.view.visible = true;
		this.finishStateAction();
	}

	protected gameStartTutorial ( data?: any ): void {
		this.view.allSymoblHide();
		this.soundManager.play( 'IntroAmbiance', AudioTrackName.EFFECT );
		this.finishStateAction();
	}

	protected tutorialOutro ( data?: any ): void {
		this.soundManager.play( 'tutorialOutroSymbolShow', AudioTrackName.EFFECT );
		this.view.tutorialOutroSymbolShow();
	}

	public wildLockTriggered ( data: IDHBWildLocked ): void {
		this.view.showOverlay( 0 );
		if ( data.reset ) {
			// reset overlay on start spin
			this.wildLockReset = true;
			this.finishStateAction();
		} else {
			this.reelActionHandler.featureAction( data ).then( () => {
				this.finishStateAction();
			} );
		}
	}

	protected freeSpinsAllLineWin ( data?: any ): void {
		this.view.hideOverlay( 0 );
		this.finishStateAction();
	}

	protected animationComplete ( e?: any, data?: any ): void {
		this.finishStateAction();
	}

}


