import { IWinDisplayConfig } from './../../../core/config/IWinDisplayConfig';

export class PaylineConfig implements IPaylineConfig {
	public name = 'payline';
	public frameAssetName = 'paylineFrame_{}.png';
	public frameAnimSpeed = 0.5;
	public frameFR = 15;
	public frameWidth = 225;
	public frameHeight = 225;
	public frameDisplayTimeMs = 200;

	public lineAssetName = 'payline2_{}.png';
	public lineAnimSpeed = 0.4;
	public lineFR = 14;
	public lineWidth = 470;
	public lineHeight = 55;
	public lineDisplayTimeMs = 200;

	public dotAssetName = '';
	public dotAnimSpeed = 0;
	public dotFR = 0;
	public dotWidth = 0;
	public dotHeight = 0;
	public dotDisplayTimeMs = 0;

	public labelSideFrame = 'End.png';
	public labelMidFrame = 'Middle.png';
}

export interface IPaylineConfig extends IWinDisplayConfig {
	frameAssetName: string;
	frameAnimSpeed: number;
	frameFR: number;
	frameWidth: number;
	frameHeight: number;
	frameDisplayTimeMs: number;

	lineAssetName: string;
	lineAnimSpeed: number;
	lineFR: number;
	lineWidth: number;
	lineHeight: number;
	lineDisplayTimeMs: number;

	dotAssetName: string;
	dotAnimSpeed: number;
	dotFR: number;
	dotWidth: number;
	dotHeight: number;
	dotDisplayTimeMs: number;

	labelSideFrame: string;
	labelMidFrame: string;
}



