import { Inject, Singleton } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Helper } from 'corengf/src/utils/Helper';
import { Scenario } from 'corengf/src/states/Scenario';
import { CoreEvent } from 'corengf/src/core/CoreEvent';
import { GameController } from 'corengf/src/core/GameController';
import { EventParams, GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { ForcingSpinController } from './../components/demoPanel/ForcingSpinController';
import { SlotFreeSpinsController } from './../components/freeSpins/SlotFreeSpinsController';
import { SlotScenarioNames } from './../states/SlotScenarioNames';
import { SlotStates } from './../states/SlotStates';
import { SlotEvent } from './SlotEvent';
import { SlotScenarioManager } from './SlotScenarioManager';
import { SlotHttpFormatter } from './../server/abstract/formatter/SlotHttpFormatter';
import { SlotBankService } from 'slotngf/src/core/SlotBankService';
import { AbstractStateHandler } from './stateHandler/AbstractStateHandler';


interface RegeisterStateHandler {
	engagement: string;
	stateHandler: AbstractStateHandler;
}

/**
 * GameController is the main class that connects Server, StateMachine and Components.
 * This class is responsible for communications between them.
 */
@Singleton
export class SlotGameController extends GameController {

	public static readonly REGISTER_STATE_HANDLER = 'REGISTER_STATE_HANDLER';

	@Inject
	protected scenarioManager: SlotScenarioManager;

	@Inject
	protected scenarioNames: SlotScenarioNames;

	@Inject
	protected stateNames: SlotStates;

	@Inject
	protected bankService: SlotBankService;

	protected resolveToSpin: Function;

	protected rngToolSpinResultQueue: Array<Array<Array<string>>>;

	private _engagementStateHandlerList: TsMap<string, AbstractStateHandler>;
	protected get engagementStateHandlerList (): TsMap<string, AbstractStateHandler> {
		if ( !this._engagementStateHandlerList ) {
			this._engagementStateHandlerList = new TsMap<string, AbstractStateHandler>();
		}
		return this._engagementStateHandlerList;
	}

	protected set engagementStateHandlerList ( value: TsMap<string, AbstractStateHandler> ) {
		this._engagementStateHandlerList = value;
	}

	public addEventListeners (): void {
		super.addEventListeners();
		GlobalDispatcher.addListener( this, SlotEvent.UPDATE_RNG_TOOL_SPIN_DATA, this.setRngToolSpinData );
		GlobalDispatcher.addListener( this, ForcingSpinController.FORCE_SPIN_RESULTS, this.forceSpin );
		GlobalDispatcher.addListener( this, SlotFreeSpinsController.FREE_SPINS_FINISH, this.freeSpinsFinish );
		GlobalDispatcher.addListener( this, SlotFreeSpinsController.PICK_SELECT_EVENT, this.pickGameSelect );

		GlobalDispatcher.addListener( this, SlotEvent.SHOW_DEMO_SCREEN_UI, this.onOpenDemoPanel );
		GlobalDispatcher.addListener( this, SlotEvent.HISTORY_SUMMARY, this.getHistorySummary );
		GlobalDispatcher.addListener( this, SlotEvent.HISTORY_DETAIL, this.getHistoryDetail );
		GlobalDispatcher.addListener( this, SlotEvent.RESULT_HISTORY_SUMMARY, this.getResultHistorySummary );
		GlobalDispatcher.addListener( this, SlotEvent.RESULT_HISTORY_DETAIL, this.getResultHistoryDetail );
		GlobalDispatcher.addListener( this, SlotEvent.STOP_SPIN, this.stopSpin );

		GlobalDispatcher.addListener( this, CoreEvent.ON_CLOSE_ERROR_DIALOG, this.restoreFromSpinning );

		GlobalDispatcher.addListener( this, SlotGameController.REGISTER_STATE_HANDLER, this.addStateHandler );
		GlobalDispatcher.addListener( this, SlotEvent.RESPIN_REELS, this.freeRespinReels );
	}

	protected addStateHandler ( message: EventParams ): void {
		this.engagementStateHandlerList.set( message.data.engagement, message.data.stateHandler );
	}

	protected restoreFromSpinning (): void {
		let scenario: Scenario;
		if ( this.currentStateName == this.stateNames.spinning ) {
			scenario = this.scenarioManager.getScenario( this.scenarioNames.baseSpinStop );
			scenario.addScenario( this.scenarioManager.getScenario( this.scenarioNames.gameIdle ) );

			this.stateMachine.pushStates( scenario.getStates() );
			this.stateMachine.skipState( this.currentStateName );

		} else {
			scenario = this.scenarioManager.getScenario( this.scenarioNames.gameIdle );
			this.stateMachine.pushStates( scenario.getStates() );
			this.stateMachine.skipState( this.currentStateName );
		}
	}

	protected gambleChoice ( data: any ): void {
		this.server.send( SlotHttpFormatter.GAMBLE_CHOICE, data );
	}

	protected freeSpinsFinish ( e: EventParams ): void {
		this.stateMachine.pushStates( this.scenarioManager.getScenario( this.scenarioNames.freeSpinsFinish ).getStates() );
	}

	protected pickGameSelect ( e: EventParams ): void {
		this.server.send( SlotHttpFormatter.RUN_PICK_FEATURE, e.data );
	}

	protected action_freeSpin ( e: EventParams ): void {
		this.server.send( SlotHttpFormatter.RUN_FREE_SPIN_FEATURE, this.getRngToolExpectedResult() );
	}

	protected action_skipLineWin ( e: EventParams ): void {
		this.stateMachine.skipState( this.stateNames.freeSpinsShowWinning );
	}

	protected setRngToolSpinData ( e: EventParams ): void {
		this.rngToolSpinResultQueue = e.data;
	}

	protected forceSpin ( e: EventParams ): void {
		this.startSpin();
		this.server.send( SlotHttpFormatter.FORCE_SPIN, e.data );
	}

	protected action_payout ( data: any ): void {
		this.server.send( SlotHttpFormatter.PAYOUT, data );
	}

	protected action_spin (): void {
		this.startSpin();
		this.readyToSpin().then( () => {
			// set next spin result
			this.server.send( SlotHttpFormatter.SPIN, this.getRngToolExpectedResult() );
			this.resolveToSpin = undefined;
		} );
	}

	protected getRngToolExpectedResult (): any {
		let expectedResult = undefined;
		if ( this.rngToolSpinResultQueue && this.rngToolSpinResultQueue.length > 0 ) {
			expectedResult = this.rngToolSpinResultQueue.shift();
		}
		return expectedResult;
	}

	protected action_autoSpin ( data: any ): void {
		this.startAutoSpin( data );
	}

	protected startAutoSpin ( data: any ): void {
		let scenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.startAutoSpin );
		scenario.setStateData( this.stateNames.transitionToAutoSpin, data );
		this.stateMachine.pushStates( scenario.getStates(), true );
	}

	protected startSpin (): void {
		this.stateMachine.pushStates( this.scenarioManager.getScenario( this.scenarioNames.startSpin ).getStates(), true );
	}

	protected freeRespinReels ( e: EventParams ): void {
		this.server.send( SlotHttpFormatter.FREE_RESPIN_REELS, this.getRngToolExpectedResult() );
	}

	protected action_startGamble (): void {
		this.server.send( SlotHttpFormatter.START_GAMBLE );
	}

	protected action_stopSpin (): void {
		this.stopSpin();
	}

	protected stopSpin (): void {
		if ( this.currentStateName === this.stateNames.spinning || this.currentStateName === this.stateNames.spinStop ) {
			this.stateMachine.skipState( this.currentStateName );
		}
	}

	protected action_freeSpinStop (): void {
		const avaliableStates = [
			this.stateNames.freeSpinsAllLineWin,
			this.stateNames.freeSpinsShowWinning,
			this.stateNames.freeSpinsStop
		];
		if ( avaliableStates.indexOf( this.currentStateName ) !== -1 ) {
			this.stateMachine.skipState( this.currentStateName );
		}
	}

	protected action_startJackpot (): void {
		//console.error( 'action_startJackpot' );
		this.stateMachine.pushStates( this.scenarioManager.getScenario( this.scenarioNames.startJackpotFeature ).getStates() );
		this.server.send( SlotHttpFormatter.RUN_JACKPOT_FEATURE );
	}

	protected action_takeWin (): void {
		this.server.send( SlotHttpFormatter.TAKE_WIN );
	}

	protected onServerResponse ( scenario: Scenario ): void {
		if ( scenario == null ) {
			return;
		}

		if ( scenario.name === this.scenarioNames.gameInit ) {
			this.stateMachine.isPause = false;
		}
		// if reels are spinning we want to notify Buttons controller that
		// we can show Stop button
		if ( this.currentStateName == this.stateNames.spinning ) {
			GlobalDispatcher.dispatchIntoExpectedState( SlotEvent.GAME_READY_FOR_STOP, this.stateNames.spinning );
		}

		super.onServerResponse( scenario );
	}

	protected getHistorySummary ( request: EventParams ): void {
		( this.send( SlotHttpFormatter.HISTORY_SUMMARY, request.data ) as any ).then( ( response ) => {
			GlobalDispatcher.dispatchIntoAllStates( SlotEvent.RECV_HISTORY_SUMMARY, response );
		} );
	}

	protected getHistoryDetail ( request: EventParams ): void {
		( this.send( SlotHttpFormatter.HISTORY_DETAIL, request.data ) as any ).then( ( response ) => {
			GlobalDispatcher.dispatchIntoAllStates( SlotEvent.RECV_HISTORY_DETAIL, response );
		} );
	}

	protected getResultHistorySummary ( request: EventParams ): void {
		( this.send( SlotHttpFormatter.RESULT_HISTORY_SUMMARY, request.data ) as any ).then( ( response ) => {
			GlobalDispatcher.dispatchIntoAllStates( SlotEvent.RECV_RESULT_HISTORY_SUMMARY, response );
		} );
	}

	protected getResultHistoryDetail ( request: EventParams ): void {
		( this.send( SlotHttpFormatter.RESULT_HISTORY_DETAIL, request.data ) as any ).then( ( response ) => {
			GlobalDispatcher.dispatchIntoAllStates( SlotEvent.RECV_RESULT_HISTORY_DETAIL, response );
		} );
	}

	public featureSpin ( engagement: string, messageType: string, data?: any, isFreeGame?: boolean ): void {
		if ( this.engagementStateHandlerList.has( engagement ) ) {
			this.engagementStateHandlerList.get( engagement ).updateStateForfeatureSpin( this.stateMachine, this.currentStateName, data );
		}
		const expectedResult = this.getRngToolExpectedResult();
		if ( expectedResult ) {
			data = Helper.mergeTwoObjects( data, expectedResult );
		}

		if ( isFreeGame ) {
			this.server.send( messageType, data );
		} else {
			this.readyToSpin().then( () => {
				this.server.send( messageType, data );
				this.resolveToSpin = undefined;
			} );
		}
	}

	protected async readyToSpin (): Promise<any> {
		return new Promise( ( resolve ) => {
			if ( this._currentStateName === this.stateNames.spinning ) {
				resolve();
			} else {
				this.resolveToSpin = resolve;
			}
		} );
	}

	protected onStateIsChanged ( stateName: string, stateData: any ): void {
		super.onStateIsChanged( stateName, stateData );

		switch ( stateName ) {
			case this.stateNames.spinning:
				this.bankService.win = 0;
				if ( this.resolveToSpin ) {
					this.resolveToSpin();
				}
				break;
			case this.stateNames.transitionToFreeSpins:
				this.bankService.win = 0;
				break;
		}
	}

	protected onOpenDemoPanel (): void {
		this.handleButtonAction( 'action_showDemoPanel' );
	}

	protected isTakeWinEnable ( action: string ): boolean {
		const avaliableStates = [
			this.stateNames.showWinning,
			this.stateNames.showWinningLoop,
			this.stateNames.allLineWin
		];

		const avaliableTakewinActions = [
			'action_showBetOptions',
			'action_showAutoPlayPanel',
			'action_showMenuOptions',
			'action_betMax',
			'action_showDemoPanel',
			'action_betIncrease',
			'action_betDecrease'
		];
		return avaliableTakewinActions.indexOf( action ) !== -1 && avaliableStates.indexOf( this.currentStateName ) !== -1;
	}

	public handleButtonAction ( action: string, data?: any ): void {
		// takewin: click in win display states, switch to baseGameIdle state.
		if ( this.isTakeWinEnable( action ) ) {
			this.stateMachine.pushStates( this.scenarioManager.getScenario( this.scenarioNames.gameIdle ).getStates(), true );
		}

		super.handleButtonAction( action, data );
	}
}


