import { ISlotNGWebSocketServerConfig } from './ISlotNGServerConfig';
import { NGServerConfig } from 'corengf/src/server/ng/config/NGServerConfig';


export class SlotNGServerConfig extends NGServerConfig implements ISlotNGWebSocketServerConfig {
	public devPath = {
		init: '/dev/sessions/',
		spin: '/dev/take-turn/',
		historySummaries: '/api/slot/partnercode/{partner_code}/playerid/{player_id}',
		historyDetail: '/api/slot/roundid/{round_id}'
	};
	public path = {
		init: '/ng/sessions/',
		spin: '/ng/take-turn/',
		historySummaries: '/api/slot/partnercode/{partner_code}/playerid/{player_id}',
		historyDetail: '/api/slot/roundid/{round_id}'
	};
	public header = {
		contentType: 'Content-Type',
		userId: 'X-Genesis-UserId',
		partnerToken: 'X-Genesis-PartnerToken'
	};
}



