import { Helper } from 'corengf/src/utils/Helper';
import { NumberDisplayMode } from 'corengf/src/utils/MoneyFormatter';
import { TweenBitmapTextField } from 'corengf/src/elements/canvas/text/TweenBitmapTextField';
import { WinDisplayView } from 'slotngf/src/components/winDisplay/base/WinDisplayView';
import { LineScoreConfig } from 'slotngf/src/components/winDisplay/lineScore/LineScoreConfig';
import { IShowWinLineData } from 'slotngf/src/components/winDisplay/base/WinDisplayController';


export class LineScoreView extends WinDisplayView {

	protected config: LineScoreConfig;

	protected lineScoreLabel: TweenBitmapTextField;

	protected createElements ( config?: LineScoreConfig ): void {
		super.createElements( config );
		this.lineScoreLabel = new TweenBitmapTextField( config.lineScoreLabel );
		this.lineScoreLabel.alpha = 1;
		this.addChild( this.lineScoreLabel );
	}

	public layoutElements (): void {
		super.layoutElements();
		if ( this.config.lineScoreLabel.autoscale == undefined ) {
			this.lineScoreLabel.setAutoscale( this.viewport.width, this.viewport.height );
		}
	}

	public showPayline ( data: IShowWinLineData ): void {
		if ( data.winData == undefined || data.winData.payout == 0 ) {
			this.lineScoreLabel.text = Helper.EMPTY;
			return;
		}
		this.setLineScoreLabel( this.moneyFormatter.formatMoney( data.winData.payout, NumberDisplayMode.VALUE_ONLY ) );
	}

	protected setLineScoreLabel ( text: string ): void {
		this.lineScoreLabel.text = text;
		this.lineScoreLabel.startTween();
	}

	public show (): void {
		if ( this.lineScoreLabel.text == Helper.EMPTY ) {
			return;
		}
		this.visible = true;
	}
}


