import { Container, Inject } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { GameConfig } from 'corengf/src/core/GameConfig';
import { AbstractApi } from 'corengf/src/server/abstract/AbstractApi';
import { INGServerConfig } from 'corengf/src/server/ng/config/INGServerConfig';
import { NGBNGController } from 'corengf/src/server/ng/sdk/bng/NGBNGController';
import { IServerConfig } from 'corengf/src/server/abstract/config/IServerConfig';
import { BNGSpin } from 'slotngf/src/server/ng/sdk/bng/api/BNGSpin';
import { BNGGameInit } from 'slotngf/src/server/ng/sdk/bng/api/BNGGameInit';
import { SlotNGHttpFormatter } from 'slotngf/src/server/ng/formatter/SlotNGHttpFormatter';
import { IBaseGameIdleData, IWinningData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { SlotBNGServerConfig } from 'slotngf/src/server/ng/sdk/bng/config/SlotBNGServerConfig';
import { BNGRunFreeSpinFeature } from 'slotngf/src/server/ng/sdk/bng/api/BNGRunFreeSpinFeature';

/**
 * integrate with NPS sdk
 */
export class SlotNGBNGController extends NGBNGController {
	private isRoundcompleted: boolean = true;

	@Inject
	protected gameConfig: GameConfig;

	public get serverConfig (): IServerConfig {
		return this.gameConfig.getConfig<INGServerConfig>( SlotBNGServerConfig );
	}

	public getSdkAPI (): TsMap<string, AbstractApi> {
		let apiMapping: TsMap<string, AbstractApi> = new TsMap<string, AbstractApi>();

		apiMapping.set( SlotNGHttpFormatter.INIT, Container.get( BNGGameInit ) );
		apiMapping.set( SlotNGHttpFormatter.SPIN, Container.get( BNGSpin ) );
		apiMapping.set( SlotNGHttpFormatter.RUN_FREE_SPIN_FEATURE, Container.get( BNGRunFreeSpinFeature ) );

		return apiMapping;
	}

	/**
	 * where can I get data from
	 * @param data
	 */
	protected baseGameIdle ( data: IBaseGameIdleData ): void {
		if ( data && data.roundCompleted ) {
			this.isRoundcompleted = true;
			this.dispatchRoundComplete();
		}
	}

	public startSpin (): void {
		if ( this.isRoundcompleted ) {
			this.isRoundcompleted = false;
			this.dispatchRoundStart();
		}
	}

	/**
	 * where can I get data from
	 * @param data
	 */
	protected allLineWin ( data: IWinningData ): void {
		if ( data.roundCompleted ) {
			this.isRoundcompleted = true;
			this.dispatchRoundComplete();
		}
	}

	protected dispatchRoundStart (): void {
		NGBNGController.postMessage( 'roundStarted' );
	}

	protected dispatchRoundComplete (): void {
		NGBNGController.postMessage( 'roundFinished' );
	}
}


