import * as MiniSignal from 'mini-signals';
import { Singleton } from 'typescript-ioc/es5';
import { ITickConsumer } from 'corengf/src/time/ITickConsumer';
import { Animation } from 'corengf/src/elements/canvas/animation/Animation';
import { Browser } from 'corengf/src/utils/Browser';
import { IMainConfig } from 'corengf/src/core/config/IMainConfig';


@Singleton
export class HeartTicker {

	/**
	 * Indicates time exceeding what tick method is executed as many as needed to cover long pause
	 * between system update calls.
	 */
	public static MAX_UPDATE_INTERVAL_MS: number = 5;

	/**
	 * If equal to true, manager is stopped from tick calls.
	 */
	protected _isPaused: boolean = false;

	/**
	 * Set this to true to stop manager from tick calls.
	 */
	public set isPaused ( isPaused: boolean ) {
		this._isPaused = isPaused;
	}

	/**
	 * Get is manager stopped from tick calls.
	 */
	public get isPaused (): boolean {
		return this._isPaused;
	}

	protected timers: Array<ITickConsumer> = new Array<ITickConsumer>();
	protected savedTimeMs: number = 0;

	/**
	 * Defult turbo mode time scale.
	 */
	protected turboTimeScale: number = 1.5;

	/**
	 * Defult time scale without turbo mode.
	 */
	protected normalTimeScale: number = 1.0;

	/**
	 * Fires when time scale been changed
	 */
	public onTimeScaleChanged: MiniSignal;

	private _timeScale: number;

	/**
	 * get current time scale.
	 */
	public get timeScale (): number {
		return this._timeScale;
	}

	private _tmpTimeScale: number;
	private set tmpTimeScale ( value: number ) {
		this._tmpTimeScale = value;
	}

	private get tmpTimeScale (): number {
		return this._tmpTimeScale;
	}

	constructor () {
		this.onTimeScaleChanged = new MiniSignal();
		this.setTimeScale( this.normalTimeScale );
		requestAnimationFrame( this.embededUpdate.bind( this ) );
	}

	public init ( config: IMainConfig ): void {
		if ( config.pauseWhenLoseFocus ) {
			document.addEventListener( Browser.VISIBILITY_CHANGE, this.onVisibilityChanged.bind( this ), false );
		}
	}

	/**
	 * set current time scale.
	 * It will fire event onTimeScaleChanged when time scale been changed.
	 */
	private setTimeScale ( timeScale: number ) {
		if ( this._timeScale != timeScale ) {
			this._timeScale = timeScale;
			Animation.TIME_SCALE = timeScale;
			this.onTimeScaleChanged.dispatch();
		}
	}

	protected onVisibilityChanged (): void {
		if ( document[ Browser.HIDDEN ] ) {
			window.setTimeout( () => {
				this.tmpTimeScale = this.timeScale;
				this.setTimeScale( 0 );
			}, 0 );
		} else {
			window.setTimeout( () => {
				this.setTimeScale( this.tmpTimeScale );
				this.tmpTimeScale = 1;
			}, 50 );
		}
	}

	/**
	 * callback for embeded JS frame update hook
	 * @param timeMs time in ms since app start
	 */
	protected embededUpdate ( timeMs: number ): void {
		if ( !this.isPaused ) {
			this.doTick( ( timeMs - this.savedTimeMs ) * this._timeScale );
		}
		this.savedTimeMs = timeMs;

		requestAnimationFrame( this.embededUpdate.bind( this ) );
	}

	/**
	 * Updates all the listeners and runs the actual update loop
	 * @param deltaTimeMs timestamp in miliseconds from the beginning of loop
	 */
	public doTick ( deltaTimeMs: number ): void {

		while ( deltaTimeMs > 0 ) {
			//do update call
			//reversed for loop to manage dead items within same pass
			for ( let i = this.timers.length - 1; i >= 0; i-- ) {
				if ( this.timers[ i ].isAlive() ) {
					this.timers[ i ].tick( Math.min( deltaTimeMs, HeartTicker.MAX_UPDATE_INTERVAL_MS ) );
				} else {
					this.timers.splice( i, 1 );
				}
			}

			deltaTimeMs -= HeartTicker.MAX_UPDATE_INTERVAL_MS;
		}
	}

	/**
	 * Adds new listener for tick calls
	 * @param tickConsumer new listener
	 */
	public register ( tickConsumer: ITickConsumer ): void {
		if ( this.timers.indexOf( tickConsumer ) > -1 ) {
			console.log( '[HeartTicker:] this listener already in the list! ' );
		} else {
			this.timers.push( tickConsumer );
		}
	}

	/**
	 * Force remove listener from array
	 * @param tickConsumer listener
	 */
	public unregister ( tickConsumer: ITickConsumer ): void {
		let i = this.timers.indexOf( tickConsumer );
		if ( i > -1 ) {
			this.timers[ i ] = null;
		} else {
			console.log( '[HeartTicker:] can\'t remove element, it is not in the array! ' );
		}
	}

	/**
	 * Enable turbo mode
	 */
	public turnOnTurbo ( turboTimeScale?: number ): void {
		if ( turboTimeScale == undefined ) {
			turboTimeScale = this.turboTimeScale;
		}
		this.setTimeScale( turboTimeScale );
	}

	/**
	 * Enable turbo mode
	 */
	public turnOffTurbo (): void {
		this.setTimeScale( this.normalTimeScale );
	}
}


