import { TsMap } from 'corengf/src/utils/TsMap';
import { Helper } from 'corengf/src/utils/Helper';
import { Actions } from 'corengf/src/utils/Actions';
import { Orientation } from 'corengf/src/display/Viewport';
import { Button } from 'corengf/src/elements/canvas/button/Button';
import { ICompViewConfig } from 'corengf/src/core/config/ICompViewConfig';
import { ICoreElementConfig } from 'corengf/src/elements/canvas/CoreElement';
import { IWebTextFieldConfig } from 'corengf/src/elements/canvas/text/WebTextField';
import { ICoreUIButtonElementConfig } from 'corengf/src/factory/CoreUIElementFactory';

/**
 * WITH_OPTION: The dialog with YES or NO, and close button
 * WITHOUT_OPTION: The dialog only with close button
 * FULL_SCREEN: The dialog which has no button
 *
 * @export
 * @enum {number}
 */
export enum ErrorDialogType {
	WITH_OPTION,
	WITHOUT_OPTION,
	FULL_SCREEN
}

export class ErrorDialogConfig implements IErrorDialogConfig {
	public name = 'errorDialog';
	public background = { color: 0, alpha: 0.8 };

	public closeButton: ICoreUIButtonElementConfig = {
		elementConfig: {
			name: 'closeButton',
			position: {
				x: 648,
				y: 96
			},
			anchor: { x: 0.5, y: 0.5 },
			id: 'ERROR_CLOSE',
			productLine: 'NGF',
			assetName: 'close',
			action: Actions.CLOSE_ERROR_DIALOG,
			isLocal: true,
			visible: false,
			isOrientationSupported: true
		},
		elementType: Button.elementType
	};

	public errorCode: IWebTextFieldConfig = {
		name: 'errorCode',
		key: '',
		style: {
			fontFamily: Helper.DEFAULT_FONTS,
			fontSize: 48,
			fontWeight: 'bold',
			fill: 'white',
			align: 'left',
			padding: 50
		},
		anchor: { x: 0.5, y: 0.5 }
	};

	public errorCodeOffset = new PIXI.Point( 0, -100 );

	public errorMsg: IWebTextFieldConfig = {
		name: 'errorMsg',
		key: '',
		style: {
			fontFamily: Helper.DEFAULT_FONTS,
			fontSize: 22,
			fill: 'white',
			align: 'center',
		},
		anchor: { x: 0.5, y: 0.5 }
	};

	public errorMsgOffset = new PIXI.Point( 0, 40 );

	public onCloseDialog = () => { return null; };

	public coreElementOrientationConfig: TsMap<Orientation, TsMap<string, ICoreElementConfig>> = new TsMap( [
		[
			Orientation.PORTRAIT, new TsMap( [
				[
					this.closeButton.elementConfig.name, {
						name: this.closeButton.elementConfig.name,
						position: this.closeButton.elementConfig.position
					}
				]
			] )
		],
		[
			Orientation.LANDSCAPE, new TsMap( [
				[
					this.closeButton.elementConfig.name, {
						name: this.closeButton.elementConfig.name,
						position: { x: 1120, y: 80 }
					}
				]
			] )
		]
	] );
}


export interface IErrorDialogConfig extends ICompViewConfig {
	background: { color: number, alpha: number };
	closeButton: ICoreUIButtonElementConfig;
	errorCode: IWebTextFieldConfig;
	errorCodeOffset: PIXI.Point;
	errorMsg: IWebTextFieldConfig;
	errorMsgOffset: PIXI.Point;
	onCloseDialog: Function;
}



