import { Singleton } from 'typescript-ioc/es5';
import { AbstractServer } from 'corengf/src/server/abstract/AbstractServer';
import { AbstractFormatter } from 'corengf/src/server/abstract/formatter/AbstractFormatter';

/**
 * Service for sending data hook data to server.
 */
@Singleton
export class DataHookService {

	protected _server: AbstractServer;

	protected _dataHookLogFormatter: AbstractFormatter;

	public init ( server: AbstractServer, formatter: AbstractFormatter ): void {
		this._server = server;
		this._dataHookLogFormatter = formatter;
	}

	public onButtonClicked ( buttonId: string, productLine: string, actionData: any ): void {
		if ( !this._dataHookLogFormatter && !this._server ) {
			return;
		}

		let data = this._dataHookLogFormatter.codeMessage(
			{
				clickDateTime: new Date().getTime(),
				buttonId: buttonId,
				productLine: productLine,
				//userAgent: window.navigator.userAgent,
				actionData: actionData
			} );
		this._server.sendDataHookInfo( data );
	}
}

export interface IDataHookData {
	clickDateTime: number;
	buttonId: string;
	productLine: string;
	//userAgent: string;
	actionData: any;
}


