import { Scenario } from 'corengf/src/states/Scenario';
import { IWinningData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { SpinResponseFormatter } from 'slotngf/src/server/ng/formatter/http/in/SpinResponseFormatter';
import { IFeatureInfo, ISpinResult, IGameState } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { AbstractFeatureScenarioFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/AbstractFeatureScenarioFormatter';
import { FeatureTypes, ISymbolCrawlFeatureDetail, ISymbolPosition, ISymbolCrawlPosition } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';


export class SymbolCrawlFeatureFormatter extends SpinResponseFormatter implements AbstractFeatureScenarioFormatter {

	public getFeatureScenarioBeforeFreeSpinStop ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return freeSpinStopScenario;
	}

	public getFeatureScenarioAfterSpinStop ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		const reelStopSymbols = { reelIndexes: this.getReelStopSymbols( message, featureInfo ) };

		if ( freeSpinStopScenario.hasStateName( this.stateNames.freeSpinsStop ) ) {
			this.margeScenarioStateData( freeSpinStopScenario,
				this.stateNames.freeSpinsStop,
				reelStopSymbols
			);
		}

		if ( freeSpinStopScenario.hasStateName( this.stateNames.spinStop ) ) {
			this.margeScenarioStateData( freeSpinStopScenario,
				this.stateNames.spinStop,
				reelStopSymbols
			);
		}
		return freeSpinStopScenario;
	}

	protected getReelStopSymbols ( message: IGameState, featureInfo: IFeatureInfo ): Array<Array<string>> {
		let result: Array<Array<string>> = this.getReelSymbols( message.spin_result.reels );

		if ( featureInfo != undefined ) {
			let symbolCrawlFeatureDetail: ISymbolCrawlFeatureDetail = this.getSymbolCrawlFeatureDetail( featureInfo.feature_state );
			result = this.getReelSymbols( symbolCrawlFeatureDetail.original_reels );
		}
		return result;
	}

	public getFeatureShowWinningData ( winningData: IWinningData, spinResult: ISpinResult, featureInfo: IFeatureInfo ): IWinningData {
		return winningData;
	}

	public getFeatureScenarioAfterWin ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return freeSpinStopScenario;
	}

	public triggeredFeatureIncomingMessage ( message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		let responseScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.symbolCrawl );
		responseScenario.addStateData( this.stateNames.symbolCrawlTriggered, this.getSymbolCrawlFeatureDetail( featureInfo.feature_state ) );

		return responseScenario;
	}

	public getFeatureRestoreScenario ( restoreScenario: Scenario, featureInfo: IFeatureInfo ): Scenario {
		return restoreScenario;
	}

	protected getSymbolCrawlScenario ( responseScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return null;
	}

	protected getSymbolCrawlFeatureDetail ( data: any ): ISymbolCrawlFeatureDetail {
		let addPosition: Array<ISymbolPosition> = [];
		data.added_position.forEach( element => {
			addPosition.push( this.convertArrayToSymbolPostion( element ) );
		} );

		let crawlPosition: Array<ISymbolCrawlPosition> = [];
		data.crawl_position.forEach( element => {
			crawlPosition.push( {
				position: this.convertArrayToSymbolPostion( element.position ),
				next_position: this.convertArrayToSymbolPostion( element.next_position )
			} as ISymbolCrawlPosition );
		} );

		let featureDetail: ISymbolCrawlFeatureDetail = {
			symbol: data.symbol,
			added_position: addPosition,
			crawl_position: crawlPosition,
			original_reels: data.original_reels
		};

		return featureDetail;
	}

	protected convertArrayToSymbolPostion ( data: Array<number> ): ISymbolPosition {
		return {
			reelIndex: data[ 0 ], symbolIndex: data[ 1 ]
		};
	}

	protected restoreFeatureIncomingMessage ( featureDetail: any ): Scenario {
		let responseScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.symbolCrawl );
		responseScenario.addStateData( this.stateNames.symbolCrawlTriggered, this.getSymbolCrawlFeatureDetail( featureDetail ) );

		return responseScenario;
	}
}



