import { Singleton } from 'typescript-ioc/es5';
import { ScenarioNames } from 'corengf/src/states/ScenarioNames';

@Singleton
export class SlotScenarioNames extends ScenarioNames {
	/** base spin */
	public readonly startSpin: string = 'startSpin';
	public readonly baseSpinStop: string = 'baseSpin';
	public readonly baseSpinWithWinning: string = 'baseSpinWithWinning';
	/** base spin before feature */
	public readonly featureWithWinning: string = 'featureWithWinning';
	/** auto spin*/
	public readonly startAutoSpin: string = 'startAutoSpin';
	public readonly winTaken: string = 'winTaken';
	/** free respin */
	public readonly startFreeRespin: string = 'startFreeRespin';
	public readonly freeRespinReels: string = 'freeRespinReels';
	public readonly freeRespinStop: string = 'freeRespinStop';
	public readonly freeRespinWithWinning: string = 'freeRespinWithWinning';
	public readonly freeRespinFinish: string = 'freeRespinFinish';
	/** free spin */
	public readonly freeSpinsIdle: string = 'freeSpinsIdle';
	public readonly transitionToFreeSpins: string = 'transitionToFreeSpins';
	public readonly freeSpinsStop: string = 'freeSpinsStop';
	public readonly freeSpinsWithWinning: string = 'freeSpinsWithWinning';
	public readonly freeSpinContinue: string = 'freeSpinContinue';
	public readonly freeSpinsFinish: string = 'freeSpinsFinish';
	/** multipliers in free spin */
	public readonly freeSpinsWithWildMultipliers: string = 'freeSpinsWithWildMultipliers';
	/** lock wild feature in free spin */
	public readonly wildLockTriggered: string = 'wildLockTriggered';
	/** re-trigger in free spin */
	public readonly freeSpinsRetriggered: string = 'freeSpinsRetriggered';
	/** rotate reel feature in free spin */
	public readonly freeSpinsRotateReelWithWinning: string = 'freeSpinsRotateReelWithWinning';
	public readonly freeSpinsRotateReelNoWinning: string = 'freeSpinsRotateReelNoWinning';
	/** pick feature */
	public readonly runPickFeature: string = 'runPickFeature';
	public readonly pickFeatureNoWinning: string = 'pickFeatureNoWinning';
	public readonly pickFeatureWithWinning: string = 'pickFeatureWithWinning';
	/** wheel fortune */
	public readonly wheelFortune: string = 'wheelFortune';
	/** jackpot feature */
	public readonly jackpotFeatureIdle: string = 'jackpotFeatureIdle';
	public readonly startJackpotFeature: string = 'startJackpotFeature';
	public readonly stopJackpotFeature: string = 'stopJackpotFeature';
	public readonly replacingSymbols: string = 'replacingSymbols';
	public readonly transitionToBonusSpin: string = 'transitionToBonusSpin';
	public readonly bonusSpinsFinish: string = 'bonusSpinsFinish';

	public readonly bonusSpinsIdle: string = 'bonusSpinsIdle';
	public readonly bonusSpinContinue: string = 'bonusSpinContinue';
	public readonly bonusSpinsStop: string = 'bonusSpinsStop';
	public readonly bonusSpinsWithWinning: string = 'bonusSpinsWithWinning';
	public readonly bonusSpinsNoWinning: string = 'bonusSpinsNoWinning';

	public readonly multiplierApplied: string = 'multiplierApplied';

	public readonly multiplierAdded: string = 'multiplierAdded';


	/** walking wild feature */
	public readonly symbolCrawl: string = 'symbolCrawl';
}


