import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { HeartTicker } from 'corengf/src/time/HeartTicker';
import { ITickConsumer } from 'corengf/src/time/ITickConsumer';
import { AudioTrackName } from 'corengf/src/audio/SoundManager';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import { BigWinView } from './BigWinView';
import { BigWinConfig } from './../bigwin/BigWinConfig';
import { SlotSoundConfig } from './../../core/config/SlotSoundConfig';
import { SlotBankService } from './../../core/SlotBankService';
import { SlotCompController } from './../mvc/SlotCompController';
import { BigWinTierType, IBigWinConfig } from './../../core/config/IBigWinConfig';
import { SlotCoinShowerController } from './../coinshower/SlotCoinShowerController';
import { IWinningData, IFreeSpinsStartData } from 'slotngf/src/states/SlotStatesDataInterfaces';


export class BigWinController extends SlotCompController implements ITickConsumer {

	@Inject
	protected timeManager: HeartTicker;

	@Inject
	protected bankService: SlotBankService;

	public static readonly TIER_REACHED_EVENT: string = 'TierReachedEvent';
	public static readonly BIG_WIN_OUT_COMPLETE_EVENT: string = 'BigWinOutCompleteEvent';
	public static readonly BIG_WIN_FINAL_BIGWINTIER_EVENT: string = 'BigWinFinalBigWinTierEvent';
	public static readonly BIG_WIN_COUNT_COMPLETE_EVENT = 'BigWinCountCompleteEvent';
	public static readonly BIG_WIN_CANCEL_COUNT__EVENT = 'BigWinCancelCountEvent';

	/**
	 * Contains settings for each BigWinTier including total dislay time,
	 * when to start and end, and current win tier phase.
	 */
	public static winTierSettings: TsMap<string, WinTierSettings>;
	protected view: BigWinView;

	protected countUpCreditsTo: number;
	protected countUpCreditsFrom: number;

	// Time in ms to play count up animation
	protected countUpCreditsTimeTotal: number;
	protected countUpCreditsTimeElapsed: number;
	protected stayTimeAfterCountCompleted: number;

	protected isCounting: boolean;
	protected currentBigWinTier: string;
	protected finalBigWinTier: string;

	public bigWinStartSignal: MiniSignal;
	public bigWinUpdateSignal: MiniSignal;
	public bigWinCancelSignal: MiniSignal;
	public bigWinCompleteSignal: MiniSignal;
	public bigWinNewTierReachedSignal: MiniSignal;

	protected stayTimer: SimpleTimer;
	protected bigWinData: IWinningData;

	public targetStates: Array<string> = [
		this.stateNames.freeRespinsAllLineWin,
		this.stateNames.freeRespinsShowWinning,
		this.stateNames.pickFeature,
		this.stateNames.allLineWin,
		this.stateNames.skip_allLineWin,
		this.stateNames.showWinningLoop,
		this.stateNames.skip_showWinning,
		this.stateNames.freeSpinsStart,
		this.stateNames.startSpin,
		this.stateNames.freeSpinsAllLineWin,
		this.stateNames.skip_freeSpinsAllLineWin,
		this.stateNames.prefeatureWinning,
		this.stateNames.transitionToFreeSpins
	];

	constructor ( @Inject view: BigWinView ) {
		super( view );
	}

	public init (): void {
		let config = this.gameConfig.getConfig<IBigWinConfig>( BigWinConfig );

		if ( this.view != null ) {
			this.view.initialize( config );
			this.view.screenTapSignal.add( this.onScreenTap, this );
		}

		this.setupWinTierSettings();
		this.setupSignals();

		this.timeManager.register( this );
		this.finishStateAction();
	}

	protected freeRespinsAllLineWin ( data: IWinningData ): void {
		this.allLineWin( data );
	}

	protected onScreenTap (): void {
		if ( this.isCounting ) {
			this.completeCountUp();
		}
	}

	protected allLineWin ( data: IWinningData ): void {
		this.showBigWin( data );
	}

	protected skip_allLineWin (): void {
		this.abort( false );
	}

	protected freeSpinsAllLineWin ( data: IWinningData ): void {
		this.showBigWin( data );
	}

	protected skip_freeSpinsAllLineWin (): void {
		this.abort( false );
	}

	protected showBigWin ( data: IWinningData ): void {
		this.countUpCreditsFrom = data.balance - data.totalWin;
		this.countUpCreditsTo = data.balance;

		this.countUpCreditsTimeElapsed = 0;
		this.currentBigWinTier = BigWinTierType.None;
		this.finalBigWinTier = BigWinController.getBigWinTier( this.countUpCreditsTo - this.countUpCreditsFrom, data.totalBet );
		this.view.finalBigWinTier = this.finalBigWinTier;

		this.soundManager.play( `${ SlotSoundConfig.win }0${ BigWinController.getBigWinTierIndex( this.finalBigWinTier ) + 1 }`, AudioTrackName.EFFECT );

		this.countUpCreditsTimeTotal = this.getCountTime( this.finalBigWinTier );
		this.stayTimeAfterCountCompleted = this.getStayTime( this.finalBigWinTier );

		this.bigWinData = data;

		if ( data.totalWin != 0 && this.finalBigWinTier != BigWinTierType.None ) {
			if ( this.countUpCreditsTimeTotal != 0 ) {
				this.beginCountUp();
				GlobalDispatcher.dispatchIntoExpectedStates( BigWinController.BIG_WIN_FINAL_BIGWINTIER_EVENT, this.targetStates,
					{ bigWinTier: this.finalBigWinTier } );
			} else {
				this.bankService.balance = data.balance;
				this.bankService.win = data.totalWin;
				this.finishStateAction();
			}
		} else {
			this.finishStateAction();
		}
	}

	protected getStayTime ( tier: string ): number {
		if ( BigWinController.winTierSettings.has( tier ) ) {
			return BigWinController.winTierSettings.get( this.finalBigWinTier ).stayTime;
		} else {
			return 0;
		}
	}

	protected skip_showWinning (): void {
		this.abort( false );
	}

	protected prefeatureWinning ( data: IWinningData ): void {
		this.showBigWin( data );
	}

	protected skip_prefeatureWinning (): void {
		this.abort( false );
	}

	protected skip_freeSpinsShowWinning (): void {
		this.abort( false );
	}

	protected startSpin (): void {
		this.abort( true );
	}

	protected freeSpinsStart ( data: IFreeSpinsStartData ): void {
		this.bankService.win = data.currentFeatureWin;
		this.abort( true );
	}

	protected transitionToFreeSpins (): void {
		this.abort( true );
	}

	protected abort ( isState: boolean ): void {
		const forceStop = true;
		GlobalDispatcher.dispatchIntoExpectedStates( SlotCoinShowerController.STOP_COIN_SHOWER_EVENT, this.targetStates );
		if ( this.isCounting ) {
			this.cancelCountUp( forceStop );
		} else if ( this.stayTimer !== undefined ) {
			this.cancelStay( forceStop );
		} else {
			if ( isState ) {
				this.finishStateAction();
			}
		}
	}

	protected setupSignals (): void {
		this.bigWinStartSignal = new MiniSignal();
		this.bigWinUpdateSignal = new MiniSignal();
		this.bigWinCancelSignal = new MiniSignal();
		this.bigWinCompleteSignal = new MiniSignal();
		this.bigWinNewTierReachedSignal = new MiniSignal();
	}

	protected beginCountUp (): void {
		this.isCounting = true;
		this.soundManager.play( SlotSoundConfig.creditLoop, AudioTrackName.EFFECT, true );
		this.bigWinStartSignal.dispatch();
		if ( this.finalBigWinTier != BigWinTierType.Balance ) {
			this.view.beginCountUp();
		}
	}

	protected updateCountUp ( value: number ): void {
		if ( !this.isCounting ) {
			return;
		}

		this.countUpCreditsTimeElapsed += value;

		if ( this.countUpCreditsTimeElapsed > this.countUpCreditsTimeTotal ) {
			this.completeCountUp();
		} else {
			let prevBigWinTier: string = this.currentBigWinTier;
			this.currentBigWinTier = this.getCurrentTimeBasedWinTier( this.countUpCreditsTimeElapsed, this.finalBigWinTier );
			if ( this.currentBigWinTier != prevBigWinTier ) {
				this.newTierReached( this.currentBigWinTier );
			}
			this.bankService.balance = this.countUpCreditsFrom + this.getCurrentWin();
		}
		this.view.updateCountUp( this.getCurrentWin() );
		this.bigWinUpdateSignal.dispatch();
	}

	protected cancelCountUp ( forceStop: boolean = false ): void {
		this.isCounting = false;
		this.soundManager.stop( SlotSoundConfig.creditLoop );
		this.soundManager.play( SlotSoundConfig.creditLoopEnd, AudioTrackName.EFFECT );

		this.countUpCreditsTimeElapsed = this.countUpCreditsTimeTotal;
		this.view.updateCountUp( this.countUpCreditsTo - this.countUpCreditsFrom );
		GlobalDispatcher.dispatchIntoExpectedStates( BigWinController.BIG_WIN_CANCEL_COUNT__EVENT, this.targetStates, {
			stateName: this.currentStateName,
			stateData: this.bigWinData
		} );

		this.bankService.balance = this.countUpCreditsTo;
		this.bankService.win = this.bankService.win + this.countUpCreditsTo - this.countUpCreditsFrom;

		this.view.cancelCountUp( this.countUpCreditsTo - this.countUpCreditsFrom, forceStop ).then( () => {
			this.bigWinCancelSignal.dispatch();
			this.finishStateAction();
		} );
	}

	protected cancelStay ( forceStop: boolean = false ): void {
		this.clearStayTimer();

		this.view.cancelCountUp( this.bankService.win, forceStop ).then( () => {
			this.bigWinCancelSignal.dispatch();
			this.finishStateAction();
		} );
	}

	protected completeCountUp (): void {
		this.isCounting = false;
		this.soundManager.stop( SlotSoundConfig.creditLoop );
		this.soundManager.play( SlotSoundConfig.creditLoopEnd, AudioTrackName.EFFECT );
		GlobalDispatcher.dispatchIntoExpectedStates( SlotCoinShowerController.STOP_COIN_SHOWER_EVENT, this.targetStates );

		this.bankService.balance = this.countUpCreditsTo;
		this.bankService.win = this.bankService.win + this.countUpCreditsTo - this.countUpCreditsFrom;
		this.view.updateCountUp( this.countUpCreditsTo - this.countUpCreditsFrom );
		GlobalDispatcher.dispatchIntoExpectedStates( BigWinController.BIG_WIN_COUNT_COMPLETE_EVENT, this.targetStates, {
			stateName: this.currentStateName,
			stateData: this.bigWinData
		} );

		this.countUpCreditsTimeElapsed = this.countUpCreditsTimeTotal;

		this.stayTimer = new SimpleTimer( this.stayTimeAfterCountCompleted, () => {
			this.view.completeCountUp( this.countUpCreditsTo - this.countUpCreditsFrom ).then( () => {
				this.bigWinCompleteSignal.dispatch();
				this.finishStateAction();
			} );
			this.clearStayTimer();
		} );
	}

	protected clearStayTimer (): void {
		if ( this.stayTimer !== undefined ) {
			this.stayTimer.stop();
			this.stayTimer = undefined;
		}
	}

	public static getBigWinTierIndex ( bigWinTier: string ): number {
		const tier = [
			BigWinTierType.None,
			BigWinTierType.Balance,
			BigWinTierType.Numbers,
			BigWinTierType.Coins,
			BigWinTierType.BigWin,
			BigWinTierType.ExtremeBigWin,
			BigWinTierType.UltraExtremeBigWin
		];
		return tier.indexOf( bigWinTier );
	}

	protected newTierReached ( bigWinTier: string ): void {
		GlobalDispatcher.dispatchIntoExpectedStates( BigWinController.TIER_REACHED_EVENT, this.targetStates,
			{ tier: bigWinTier, finalTier: this.finalBigWinTier } );
		this.view.newTierReached( bigWinTier );
		this.bigWinNewTierReachedSignal.dispatch( bigWinTier );
	}

	protected getCountTime ( tier: string ): number {
		if ( BigWinController.winTierSettings.has( tier ) ) {
			return BigWinController.winTierSettings.get( this.finalBigWinTier ).countTime;
		} else {
			return 0;
		}
	}

	protected getCurrentWin (): number {
		const totalWon: number = this.countUpCreditsTo - this.countUpCreditsFrom;
		const value: number = Math.fround( totalWon * this.getProgressRatio() );
		const size = Math.pow( 10, 2 );
		let win: number = Math.round( value * size ) / size;
		if ( win > totalWon ) {
			win = totalWon;
		}
		return win;
	}

	protected getProgressRatio (): number {
		return Math.min( 1, ( this.countUpCreditsTimeElapsed / this.countUpCreditsTimeTotal ) );
	}

	protected getCurrentTimeBasedWinTier ( timeElapsed: number, baseWinTier: string ): string {
		let bigWinTier = BigWinController.winTierSettings.get( baseWinTier );
		let result: string = BigWinTierType.Balance;

		bigWinTier.timeTable.forEach( ( value, key ) => {
			if ( ( value.start <= timeElapsed ) && ( timeElapsed < value.end ) ) {
				result = key;
			}
		} );

		return result;
	}

	public static getBigWinTier ( totalWin: number, bet: number ): string {
		let tier = totalWin / bet;
		let result: string = BigWinTierType.None;
		BigWinController.winTierSettings.forEach( ( value, key ) => {
			if ( ( value.winSpan.start <= tier ) && ( tier < value.winSpan.end ) ) {
				result = key;
			}
		} );

		return result;
	}

	public isAlive (): boolean {
		return true;
	}

	public tick ( deltaTimeMs: number ): void {
		if ( this.isCounting ) {
			this.updateCountUp( deltaTimeMs );
		}
	}

	protected setupWinTierSettings (): void {
		BigWinController.winTierSettings = new TsMap<string, WinTierSettings>();
		this.gameConfig.getConfig<IBigWinConfig>( BigWinConfig ).winTierSettings.forEach( ( wts ) => {
			let timeMap: TsMap<string, Span> = new TsMap<string, Span>();

			wts.timeTable.forEach( ( element ) => {
				timeMap.set( element.tier, element.timeSpan );
			} );

			BigWinController.winTierSettings.set( wts.tier, {
				countTime: wts.countTime,
				stayTime: wts.stayTime,
				winSpan: wts.winSpan,
				timeTable: timeMap
			} );
		} );
	}
}

export type WinTierSettings = {
	winSpan: Span;
	countTime: number;
	stayTime: number;
	timeTable: TsMap<string, Span>; //Time settings (start and end time) for each BigWinTier
};

export type Span = {
	start: number;
	end: number;
};


