import { ScenarioManager } from 'corengf/src/core/ScenarioManager';
import { GameConfig } from 'corengf/src/core/GameConfig';
import { Scenario } from 'corengf/src/states/Scenario';
import { Inject } from 'typescript-ioc/es5';

/**
 * Encapsulates code/encode rules for messages
 */
export abstract class AbstractFormatter {

	public static readonly INIT: string = 'init';

	public static readonly HISTORY_SUMMARY: string = 'direct_history_summary';
	public static readonly HISTORY_DETAIL: string = 'direct_history_detail';

	@Inject
	protected gameConfig: GameConfig;

	@Inject
	protected scenarioManager: ScenarioManager;


	/**
	 * does formatting message in structure compatible with end-point server
	 * @param data formatter-specific object with set of properties
	 * @returns {any} object, ready to send to end point server
	 */
	public codeMessage ( data?: any ): any {
		return data;
	}

	/**
	 * reveals data from servers message into framework-friendly format
	 * @param message original server's message
	 */
	public processIncomingMessage ( message: any ): Scenario | number {
		return null;
	}

	protected returnScenario ( scenario: Scenario ): Scenario {
		if ( scenario != undefined ) {
			return scenario;
		} else {
			console.error( 'Response scenario is not specified. Please check message from server.' );
		}
	}
}



