
import { Singleton } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { ButtonBehaviour } from 'corengf/src/buttons/ButtonsStateSet';
import { SlotUIButtonId } from 'slotngf/src/components/ui/buttons/SlotUIButtonId';
import { MLUIButtonId } from 'mlui/src/buttons/MLUIButtonId';
import { MLUILocalButtonsStateSet } from 'mlui/src/buttons/MLUILocalButtonsStateSet';

@Singleton
export class DHBLocalButtonsStateSet extends MLUILocalButtonsStateSet {

	protected initStates (): void {
		super.initStates();
		this.buttonStates.set( this.stateNames.autoSpinAllLineWin, this.autoSpinAllLineWin() );
	}

	protected transitionToFreeSpins (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.transitionToFreeSpins();
		buttonState.set( SlotUIButtonId.BUTTON_TURBO_MODE, new ButtonBehaviour( true, true ) );
		return buttonState;
	}

	protected transitionToBaseGame (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.transitionToBaseGame();
		buttonState.set( SlotUIButtonId.BUTTON_TURBO_MODE, new ButtonBehaviour( true, true ) );
		return buttonState;
	}

	protected freeRespinsStop (): TsMap<string, ButtonBehaviour> {
		return this.prefeatureWinning();
	}

	protected autoSpinAllLineWin (): TsMap<string, ButtonBehaviour> {
		let buttonState = this.autoSpinSpinStop();
		return buttonState;
	}

	protected showWinning (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = this.default();
		buttonState.set( SlotUIButtonId.BUTTON_START_SPIN, new ButtonBehaviour( true, true ) );
		buttonState.set( SlotUIButtonId.BUTTON_STOP_SPIN, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotUIButtonId.BUTTON_BET_MAX, new ButtonBehaviour( true, true ) );
		buttonState.set( SlotUIButtonId.BUTTON_SHOW_AUTO_PLAY, new ButtonBehaviour( true, true ) );
		buttonState.set( SlotUIButtonId.BUTTON_STOP_AUTO_PLAY, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotUIButtonId.BUTTON_TURBO_MODE, new ButtonBehaviour( true, true ) );
		buttonState.set( SlotUIButtonId.BUTTON_SHOW_MENU_OPTIONS, new ButtonBehaviour( true, true ) );
		buttonState.set( SlotUIButtonId.BUTTON_SHOW_BET_OPTIONS, new ButtonBehaviour( true, true ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( true, true ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( true, true ) );
		return buttonState;
	}

	protected startPickFeature (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = this.default();
		buttonState.set( SlotUIButtonId.BUTTON_START_SPIN, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotUIButtonId.BUTTON_STOP_SPIN, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotUIButtonId.BUTTON_SHOW_AUTO_PLAY, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotUIButtonId.BUTTON_STOP_AUTO_PLAY, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotUIButtonId.BUTTON_TURBO_MODE, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotUIButtonId.BUTTON_SHOW_BET_OPTIONS, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotUIButtonId.BUTTON_SHOW_MENU_OPTIONS, new ButtonBehaviour( false, false ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_INCREMENT, new ButtonBehaviour( false, false ) );
		buttonState.set( MLUIButtonId.BUTTON_MAIN_VIEW_BET_DECREMENT, new ButtonBehaviour( false, false ) );
		return buttonState;
	}

}



