import { CoreWebElement, ICoreWebElementConfig } from 'corengf/src/elements/webpage/CoreWebElement';
import { IWebImageConfig } from 'corengf/src/elements/webpage/image/WebImageElement';
import { WebElementFactory } from 'corengf/src/factory/WebElementFactory';

/**
 * Line element for web page
 */
export class WebLineElement extends CoreWebElement {

	public static elementType: string = 'WEB_LINE_ELEMENT';

	public createElement ( lineConfig: IWebLineConfig ): IWebLineConfig {

		if ( lineConfig.image ) {
			lineConfig.image = WebElementFactory.createInstance( lineConfig.image );
		}
		return lineConfig;
	}
}

export interface IWebLineConfig extends ICoreWebElementConfig {
	length?: number;
	height?: number;
	lineHeight?: number;
	color?: string;
	image?: IWebImageConfig;
	show: boolean;
}



