import { WinBoxConfig, IWinBoxConfig } from 'slotngf/src/components/winDisplay/winBoxes/WinBoxConfig';


export class DHBWinBoxConfig extends WinBoxConfig implements IDHBWinBoxConfig {

	public animationFiles: Array<string> | string = 'Highlight';

	public animationName: string = 'Symbol_Highlight';

	public freeSpinAnimationFiles: string = 'Wild_Multiplier';

	public freeSpinAnimationName: string = 'Wild_Multiplier';

	public freeSpinMultiplierReelId: number = 2;

	public loop: boolean = false;

	public boxOffset = {
		x: 0,
		y: 0
	};
	public defaultPoolSize: number = 20; // 2 line

}

export interface IDHBWinBoxConfig extends IWinBoxConfig {
	freeSpinMultiplierReelId: number;
	freeSpinAnimationFiles: string;
	freeSpinAnimationName: string;
}


