import { Helper } from 'corengf/src/utils/Helper';
import { ICompViewConfig } from 'corengf/src/core/config/ICompViewConfig';
import { IBitmapTextFieldConfig } from 'corengf/src/elements/canvas/text/BitmapTextField';


export class SlotFreeSpinsConfig implements ISlotFreeSpinsConfig {
	public readonly name = 'slotFreeSpins';
	public readonly pickNamePrefix = Helper.EMPTY;
	public readonly pickName = Helper.EMPTY;

	public readonly textSlotName = Helper.EMPTY;
	public readonly titleSlotName = Helper.EMPTY;

	public readonly pickLabelOffsetX = 0;
	public readonly pickLabelOffsetY = -200;
	public readonly titleLabelOffsetX = 0;
	public readonly titleLabelOffsetY = -405;

	public readonly introAnimation = Helper.EMPTY;
	public readonly outroAnimation = Helper.EMPTY;
	public readonly idleAnimation = Helper.EMPTY;
	public readonly pickAnimation = Helper.EMPTY;

	public readonly titleFont: IBitmapTextFieldConfig = {
		style: {
			font: {
				name: Helper.DEFAULT_FONTS,
				size: 64,
			},
			align: 'center',
		},
		name: 'bonusTitle',
		key: 'bonusTitle',
	};

	public readonly pickFont: IBitmapTextFieldConfig = {
		style: {
			font: {
				name: Helper.DEFAULT_FONTS,
				size: 74,
			},
			align: 'center',
		},
		name: '',
		key: '',
	};

	public readonly introAnimationTrack = 0;
	public readonly idleAnimationTrack = 1;
	public readonly pickAnimationTrack = 2;

	public readonly pickAsset = Helper.EMPTY;
	public readonly backgroundAsset = Helper.EMPTY;

	public readonly backgroundIntroAnimation = Helper.EMPTY;
	public readonly backgroundOutroAnimation = Helper.EMPTY;
}

export interface ISlotFreeSpinsConfig extends ICompViewConfig {
	readonly pickNamePrefix: string;
	readonly pickName: string;

	readonly textSlotName: string;
	readonly titleSlotName: string;

	readonly titleFont: IBitmapTextFieldConfig;
	readonly pickFont: IBitmapTextFieldConfig;

	readonly pickLabelOffsetX: number;
	readonly pickLabelOffsetY: number;
	readonly titleLabelOffsetX: number;
	readonly titleLabelOffsetY: number;

	readonly introAnimation: string;
	readonly outroAnimation: string;
	readonly idleAnimation: string;
	readonly pickAnimation: string;

	readonly pickAsset: string;
	readonly backgroundAsset: string;

	readonly backgroundIntroAnimation: string;
	readonly backgroundOutroAnimation: string;
}


