import { Inject } from 'typescript-ioc/es5';
import { CoreState } from 'corengf/src/states/CoreState';
import { AudioTrackName } from 'corengf/src/audio/SoundManager';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { AnimationViewController } from 'corengf/src/components/animationView/AnimationViewController';
import { ReelController } from 'slotngf/src/components/reelV2/ReelController';
import { IReelsConfig } from 'slotngf/src/core/config/IReelsConfig';
import { ReelConfig } from 'slotngf/src/components/reelV2/ReelConfig';
import { DHBAnimationView } from 'game/src/components/animationView/DHBAnimationView';

export class DHBAnimationViewController extends AnimationViewController {

	protected view: DHBAnimationView;
	protected isAnticipation: boolean = false;
	protected anticipationReelId: number;
	protected isRespinTriggered: boolean = false;


	constructor ( @Inject view: DHBAnimationView ) {
		super( view );
	}

	public init (): void {
		this.view.setReelsConfig( this.gameConfig.getConfig<IReelsConfig>( ReelConfig ) );
		super.init();
	}

	public addEventListeners (): void {
		GlobalDispatcher.addListener( this, ReelController.ANTICIPATE_REEL_NOTIFICATION, this.onAnticipation );
		GlobalDispatcher.addListener( this, ReelController.ANTICIPATE_END_NOTIFICATION, this.onAnticipationEnd );
		this.view.animationCompleteSignal.add( this.animationComplete, this );
	}

	protected gameStartTutorial ( data?: any ): void {
		// wait animationComplete finishStateAction
	}

	protected animationComplete ( e?: any, data?: any ): void {
		switch ( e ) {
			case 'animation':
				// respin trigger animation
				this.finishStateAction();
				break;
			case 'Intro Splash':
				this.finishStateAction();
				break;

			default:
				break;
		}
	}

	private onAnticipation ( event: any ): void {
		if ( !this.isAnticipation ) {
			this.isAnticipation = true;
		}

		if ( this.soundManager.isPlaying( 'reelAnticipationAudio' ) ) {
			this.soundManager.stop( 'reelAnticipationAudio' );
		}

		this.soundManager.play( 'reelAnticipationAudio', AudioTrackName.EFFECT, false );
		this.anticipationReelId = event.data + 1;
		this.view.onAnticipationStart( this.anticipationReelId );
	}

	private onAnticipationEnd ( event?: any ): void {
		this.isAnticipation = false;
		this.view.onAnticipationEnd();
		this.soundManager.stop( 'reelAnticipationAudio' );
	}

	public startSpin (): void {
		this.isRespinTriggered = false;
		this.finishStateAction();
	}

	protected prefeatureWinning ( data: any ): void {
		if ( data.pick != undefined ) {
			//pick game triggered with all line win
		} else if ( data.respin != undefined && !this.isRespinTriggered && data.featureState == undefined ) {
			//startFreeRespin
		} else if ( data.featureState ) {
			let featureTriggerAudio = data.featureState.subfeatures.length >= 2 ? 'featureTriggerLargeAudio' : 'featureTriggerAudio';
			this.soundManager.play( featureTriggerAudio );
		}
		this.finishStateAction();
	}

	protected startFreeRespin ( data: any ): void {
		if ( !this.isRespinTriggered ) {
			this.isRespinTriggered = true;
			this.view.playFeatureTrigger();
		} else {
			this.finishStateAction();
		}
	}

	protected freeRespinReels ( data: any ): void {
		if ( this.isRespinTriggered ) {
			this.view.freeRespinReels();
		}
		this.finishStateAction();
	}

	public skipState ( state: CoreState ): void {
		if ( this.isAnticipation ) {
			this.onAnticipationEnd();
		}
		super.skipState( state );
	}

}


