import { Inject } from 'typescript-ioc/es5';
import { IAnimationViewConfig } from 'corengf/src/components/animationView/AnimationViewConfig';
import { AnimationViewController } from 'corengf/src/components/animationView/AnimationViewController';
import { BackgroundView } from './BackgroundView';
import { BackgroundConfig } from './BackgroundConfig';


export class BackgroundController extends AnimationViewController {

	constructor ( @Inject view: BackgroundView ) {
		super( view );
	}

	public init ( data: any ): void {
		if ( this.view != undefined ) {
			this.view.initialize( this.gameConfig.getConfig<IAnimationViewConfig>( BackgroundConfig ) );
		}

		this.finishStateAction();
	}
}


