import { Scenario } from 'corengf/src/states/Scenario';
import { IWinningData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { SlotNGHttpFormatter } from 'slotngf/src/server/ng/formatter/SlotNGHttpFormatter';
import { IGameState, IFeatureInfo, ISpinResult } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { IFreeSpinFeatureDetail, FeatureTypes } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { AbstractFeatureScenarioFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/AbstractFeatureScenarioFormatter';

/**
 * formatting retriggered feature
 */
export class FreeSpinRetriggeredFeatureFormatter extends SlotNGHttpFormatter implements AbstractFeatureScenarioFormatter {

	/**
	 * formatting run feature api response
	 * @param message
	 */
	public processIncomingMessage ( message: IGameState, featureInfo?: IFeatureInfo ): Scenario {
		console.warn( 'here shouldn\'t be triggered, since free spin retriggered feature don\'t need another transaction to complete feature' );
		return null;
	}

	/**
	 * formatting feature in feature_triggered
	 */
	public triggeredFeatureIncomingMessage ( message: IGameState, featureTriggeredInfo?: IFeatureInfo ): Scenario {
		if ( featureTriggeredInfo == undefined ) {
			return null;
		}

		return this.getFreeSpinsRetriggeredScenario( message );
	}

	public getFeatureScenarioBeforeFreeSpinStop ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return freeSpinStopScenario;
	}

	public getFeatureScenarioAfterSpinStop ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return freeSpinStopScenario;
	}

	public getFeatureShowWinningData ( winningData: IWinningData, spinResult: ISpinResult, featureInfo: IFeatureInfo ): IWinningData {
		return winningData;
	}

	public getFeatureScenarioAfterWin ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return freeSpinStopScenario;
	}

	/**
	 * get free spins retriggered scenario
	 * @param message
	 */
	protected getFreeSpinsRetriggeredScenario ( message: IGameState ): Scenario {
		let responseScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.freeSpinsRetriggered );

		let featureInfo: IFeatureInfo = this.getFeatureInfo( message );
		let freeSpinFeature: IFreeSpinFeatureDetail = this.getFeatureDetail( featureInfo );

		const currentFreeSpinCount = freeSpinFeature.total_free_spin_count - freeSpinFeature.free_spins_left + 1;
		const totalFreeSpinCount = freeSpinFeature.total_free_spin_count;

		if ( responseScenario.hasStateName( this.stateNames.showRetrigger ) ) {
			responseScenario.setStateData( this.stateNames.showRetrigger, {
				currentFreeSpinCount,
				totalFreeSpinCount
			} );
		}
		return responseScenario;
	}

	/**
	 * get feature info from message
	 * @param message
	 */
	protected getFeatureInfo ( message: IGameState ): any {
		return message.features.find( e => e.type === FeatureTypes.FREE_SPIN );
	}

	protected getFeatureDetail ( featureInfo: IFeatureInfo ): any {
		return featureInfo.feature_state;
	}

	/**
	 * formatting feature while restore feature
	 */
	public restoreFeatureIncomingMessage ( retriggeredFeatureDetail: any ): Scenario {
		let retriggeredFeatureScenario: Scenario = null;
		console.warn( 'didn\'t plan for restore free spin retriggered feature, since it won\'t create another transection' );
		return retriggeredFeatureScenario;
	}

	public getFeatureRestoreScenario ( restoreScenario: Scenario, featureInfo: IFeatureInfo ): Scenario {
		return restoreScenario;
	}
}



