import { Inject } from 'typescript-ioc/es5';
import { Sprite } from 'corengf/src/elements/canvas/sprite/Sprite';
import { TextField } from 'corengf/src/elements/canvas/text/TextField';
import { UIPanelView } from 'corengf/src/components/ui/components/UIPanelView';
import { CoreUIElementFactory } from 'corengf/src/factory/CoreUIElementFactory';
import { MoneyFormatter, NumberDisplayMode } from 'corengf/src/utils/MoneyFormatter';
import { UIBankConfig } from 'corengf/src/components/ui/components/bank/UIBankConfig';


export class UIBankPanelView extends UIPanelView {

	@Inject
	protected moneyFormatter: MoneyFormatter;

	protected config: UIBankConfig;

	protected betIcon: Sprite;
	protected totalBetLabel: TextField;

	protected balanceIcon: Sprite;
	protected balanceLabel: TextField;

	public set totalBet ( value: number ) {
		this.setTotalBetLabel( value );
	}

	public set balance ( value: number ) {
		this.setBalanceLabel( value );
	}

	protected createElements ( config?: UIBankConfig ): void {
		if ( config != null ) {
			this.config = config;
			this.createIcons( config );
			this.createLabels( config );
		}
	}

	protected createIcons ( config: UIBankConfig ): void {
		// bet
		this.betIcon = CoreUIElementFactory.createInstance( config.totalBetIcon );
		this.addChild( this.betIcon );

		// wallet
		this.balanceIcon = CoreUIElementFactory.createInstance( config.balanceIcon );
		this.addChild( this.balanceIcon );
	}

	protected createLabels ( config: UIBankConfig ): void {

		this.totalBetLabel = CoreUIElementFactory.createInstance( config.totalBetValue );
		this.addChild( this.totalBetLabel );

		this.balanceLabel = CoreUIElementFactory.createInstance( config.balanceValue );
		this.addChild( this.balanceLabel );
	}

	protected setTotalBetLabel ( value: number ): void {
		if ( this.totalBetLabel != null ) {
			this.totalBetLabel.text = this.moneyFormatter.formatMoney( value, NumberDisplayMode.VALUE_WITH_CURRENCY );
		}
	}

	protected setBalanceLabel ( value: number ): void {
		if ( this.balanceLabel != null ) {
			this.balanceLabel.text = this.moneyFormatter.formatMoney( value, NumberDisplayMode.VALUE_WITH_CURRENCY );

			//align right
			this.balanceLabel.x = this.viewport.width - this.config.spacing.padding - this.balanceLabel.width;
			if ( this.balanceIcon != null ) {
				this.balanceIcon.x = this.balanceLabel.x - this.config.spacing.iconSpacing - this.balanceIcon.width;
			}
		}
	}
}


