import { Inject } from 'typescript-ioc/es5';
import { MoneyFormatter } from 'corengf/src/utils/MoneyFormatter';
import { AbstractApi } from 'corengf/src/server/abstract/AbstractApi';
import { IPackage } from 'corengf/src/server/abstract/transport/HttpTransport';
import { NGHttpTransport } from 'corengf/src/server/ng/transport/NGHttpTransport';
import { SlotBankService } from 'slotngf/src/core/SlotBankService';
import { SlotNPSModel } from 'slotngf/src/server/ng/sdk/bng/model/SlotNPSModel';


export class BNGBaseAPI extends AbstractApi {

	@Inject
	protected slotNGModel: SlotNPSModel;

	@Inject
	protected bankService: SlotBankService;

	@Inject
	protected moneyFormatter: MoneyFormatter;

	protected transport: NGHttpTransport;

	public init ( transport: NGHttpTransport, config?: any ): void {
		this.config = config;
		this.transport = transport;
	}

	protected encapsulation ( data?: any ): IPackage {
		return null;
	}
}


