import { HttpTransport, HTTP_METHODS } from 'corengf/src/server/abstract/transport/HttpTransport';
import { INGServerConfig } from 'corengf/src/server/ng/config/INGServerConfig';
import { ISendData } from 'corengf/src/server/ng/data/NGHttpDataDefinitions';
import { TsMap } from 'corengf/src/utils/TsMap';
import { UrlParameters } from 'corengf/src/utils/UrlParameters';
import { Singleton } from 'typescript-ioc/es5';

/**
 * Handle Http transport to NG server
 */
@Singleton
export class NGHttpTransport extends HttpTransport {

	protected _url: string;

	public get url (): string {
		return this._url;
	}

	protected config: INGServerConfig;

	public setConfig ( config: INGServerConfig ) {
		this.config = config;
		this._url = config.protocol + this.getDynamicGameServerName() + '.' + this.getDynamicDomainName() + config.connectionPort;
	}

	public send ( data: ISendData ): void {
		this.request( HTTP_METHODS.POST, this._url + data.path, data.name, data.headers, data.content );
	}

	public getHeaders (): TsMap<string, string> {
		const headers = new TsMap<string, string>();
		headers.set( 'Content-Type', 'application/json' );

		let partnerToken: string = UrlParameters.partnerToken;
		if ( !( UrlParameters.isRealMode() ) ) {
			if ( UrlParameters.engagements ) {
				partnerToken = this.config.demoPartnerToken;
			} else {
				partnerToken = this.config.playPartnerToken;
			}
		}
		headers.set( 'X-Genesis-PartnerToken', partnerToken );
		return headers;
	}

	public getDynamicGameServerName (): string {
		let gsName: string = UrlParameters.gs;
		if ( gsName == undefined ) {
			gsName = this.config.rgsName;
		}
		return gsName;
	}
}



