import { CoreWebElement, ICoreWebElementConfig } from 'corengf/src/elements/webpage/CoreWebElement';

/**
 * Text element for web page
 */
export class WebTextElement extends CoreWebElement {

	public static elementType: string = 'WEB_TEXT_ELEMENT';

	public CreateElement ( config: IWebTextConfig ): IWebTextConfig {
		return config;
	}
}

export interface IWebTextConfig extends ICoreWebElementConfig {
	languageKey: string;
	maxAmount?: number;
	superscriptLanguageKey?: string;
	textToReplace?: Array<IReplaceTextConfig>;
}

export interface IReplaceTextConfig {
	replaceKey: string;
	replaceValue: string;
}


