import { WinDisplayController } from 'slotngf/src/components/winDisplay/base/WinDisplayController';
import { Inject } from 'typescript-ioc/es5';
import { LineScoreView } from 'slotngf/src/components/winDisplay/lineScore/LineScoreView';
import { LineScoreConfig } from 'slotngf/src/components/winDisplay/lineScore/LineScoreConfig';
import { ReelsConfig } from 'slotngf/src/components/reels/logic/ReelsConfig';
import { IReelsConfig } from 'slotngf/src/core/config/IReelsConfig';
import { SlotStates } from 'slotngf/src/states/SlotStates';
import { CoreState } from 'corengf/src/states/CoreState';

export class LineScoreController extends WinDisplayController {

	@Inject
	protected stateNames: SlotStates;

	protected view: LineScoreView;

	constructor ( @Inject view: LineScoreView ) {
		super( view );
	}

	public init (): void {
		let config = this.gameConfig.getConfig<LineScoreConfig>( LineScoreConfig );
		this.view.setReelsConfig( this.gameConfig.getConfig<IReelsConfig>( ReelsConfig ) );

		this.view.initialize( config );
		this.finishStateAction();
	}

	protected onStateChanged (): void {
		if ( !this.view.initialized ) {
			return;
		}

		const availableStates = [
			this.stateNames.showWinning,
			this.stateNames.showWinningLoop,
			this.stateNames.freeSpinsShowWinning
		];

		if ( availableStates.indexOf( this.currentStateName ) === -1 ) {
			this.view.hide();
		} else {
			this.view.show();
		}
	}

	public setState ( state: CoreState ): boolean {
		this.currentStateName = state.stateName;
		this.onStateChanged();
		if ( typeof this[ state.stateName ] === 'function' ) {
			this.isStateActionCompleted = false;
			this.invokeStateMethod( state );
			return true;
		} else {
			this.finishStateAction();
			return false;
		}
	}
}


