import { Inject } from 'typescript-ioc/es5';
import { Asset } from 'corengf/src/core/AssetLoader';
import { Component } from 'corengf/src/mvc/CompController';
import { SoundConfig } from 'corengf/src/core/config/SoundConfig';
import { DashboardController } from 'corengf/src/components/dashboard/DashboardController';
import { UILocalButtonsController } from 'corengf/src/components/ui/buttons/UILocalButtonsController';

/**
 * Handel all the element behavior for user interface.
 * It has it's own local machine
 */
export class UIController extends DashboardController {

	protected STATE_MACHINE_NAME: string = 'ui_state_machine';

	@Inject
	protected localButtonsController: UILocalButtonsController;

	/**
	 * Add components in component list into ui component manager.
	 * @param components
	 */
	protected createComponentList ( components: Array<Component> ): void {
		//
	}

	public get loaderList (): Array<Asset> {
		return this.getAssetsList();
	}

	protected getAssetsList (): Array<Asset> {
		return [ {
			assetKey: SoundConfig.buttonClick,
			assetURL: `assets/sound/ui/${ SoundConfig.buttonClick }.mp3`
		}, ];
	}

	protected onLocalStateChanged ( stateName: string ): void {
		this.localButtonsController.applyButtonsTemplate( stateName );
	}
}


