import { Inject } from 'typescript-ioc/es5';
import { UrlParameters } from 'corengf/src/utils/UrlParameters';
import { UIPanelController } from 'corengf/src/components/ui/components/UIPanelController';
import { UIInformationHeaderView } from 'corengf/src/components/ui/components/informationHeader/UIInformationHeaderView';
import { IInformationHeaderConfig, UIInformationHeaderConfig } from 'corengf/src/components/ui/components/informationHeader/UIInformationHeaderConfig';

export class UIInformationHeaderController extends UIPanelController {

	protected view: UIInformationHeaderView;

	constructor ( @Inject view: UIInformationHeaderView ) {
		super( view );
	}

	public init ( stateData: any ): void {
		let config: IInformationHeaderConfig = this.gameConfig.getConfig<IInformationHeaderConfig>( UIInformationHeaderConfig );
		config.isRealMode = UrlParameters.isRealMode();
		config.viewConfig = this.getViewConfig( stateData, config );

		if ( Object.keys( config.viewConfig ).length <= 0 || ( config.viewConfig.show_title === false && config.viewConfig.show_time === false ) ) {
			return;
		}

		this.initialize( config );
		this.finishStateAction();
	}

	protected getViewConfig ( stateData: any, config: IInformationHeaderConfig ): any {
		return {};
	}

	protected initialize ( config: IInformationHeaderConfig ): void {
		this.view.initialize( config );
		this.view.clickBackgroundSignal.add( this.onClickBackground.bind( this ) );
	}

	protected onClickBackground (): void {
		//
	}
}


