import { TsMap } from 'corengf/src/utils/TsMap';
import { IErrorDialogTypeMap } from 'corengf/src/server/abstract/ErrorCode';
import { ErrorDialogType } from 'corengf/src/components/errorDialog/ErrorDialogConfig';


export const ErrorCode = {
	INTERNET_DISCONNECTED: '-1',
	FAILED_TO_LOAD_RESOURCE: '-2',
	INVALID_SESSION_TOKEN2: '403',
	INVALID_SESSION_TOKEN: '500',
	INSUFFICIENT_BALANCE: '402',
	BAD_REQUEST_410: '410',
	BAD_REQUEST_411: '411', // TODO: move to g+
	BAD_REQUEST_412: '412', // TODO: move to g+
	PENDING_BONUS: '400',
	BAD_REQUEST_0: '400-0',
	BAD_REQUEST_1: '400-1',
	BAD_REQUEST_2: '400-2',
	BAD_REQUEST_3: '400-3',
	BAD_REQUEST_4: '400-4',
	BAD_REQUEST_5: '400-5',
	BAD_REQUEST_6: '400-6',
	BAD_REQUEST_7: '400-7',
	BAD_REQUEST_8: '400-8',
	BAD_REQUEST_9: '400-9',
	BAD_REQUEST_10: '400-10',
	BAD_REQUEST_11: '400-11',
	BAD_REQUEST_12: '400-12',
	BAD_REQUEST_13: '400-13',
	BAD_REQUEST_14: '400-14',
	BAD_REQUEST_15: '400-15',
	BAD_REQUEST_16: '400-16',
	BAD_REQUEST_17: '400-17',
	BAD_REQUEST_18: '400-18',
	BAD_REQUEST_19: '400-19',
	BAD_REQUEST_20: '400-20',
	BAD_REQUEST_21: '400-21',
	BAD_REQUEST_22: '400-22',
	BAD_REQUEST_23: '400-23',
	BAD_REQUEST_24: '400-24',
	UNAVAILABLE_FOR_LEGAL_REASONS: '451'
};

export class ErrorDialogData implements IErrorDialogTypeMap {
	public errorDialogTypeMap: TsMap<ErrorDialogType, Array<string>> = new TsMap( [
		[ ErrorDialogType.FULL_SCREEN, [
			ErrorCode.INTERNET_DISCONNECTED,
			ErrorCode.FAILED_TO_LOAD_RESOURCE,
			ErrorCode.INVALID_SESSION_TOKEN,
			ErrorCode.INVALID_SESSION_TOKEN2,
			ErrorCode.BAD_REQUEST_411,
			ErrorCode.PENDING_BONUS,
			ErrorCode.BAD_REQUEST_0,
			ErrorCode.BAD_REQUEST_1,
			ErrorCode.BAD_REQUEST_2,
			ErrorCode.BAD_REQUEST_3,
			ErrorCode.BAD_REQUEST_4,
			ErrorCode.BAD_REQUEST_5,
			ErrorCode.BAD_REQUEST_6,
			ErrorCode.BAD_REQUEST_7,
			ErrorCode.BAD_REQUEST_8,
			ErrorCode.BAD_REQUEST_9,
			ErrorCode.BAD_REQUEST_10,
			ErrorCode.BAD_REQUEST_11,
			ErrorCode.BAD_REQUEST_12,
			ErrorCode.BAD_REQUEST_13,
			ErrorCode.BAD_REQUEST_14,
			ErrorCode.BAD_REQUEST_15,
			ErrorCode.BAD_REQUEST_16,
			ErrorCode.BAD_REQUEST_17,
			ErrorCode.BAD_REQUEST_18,
			ErrorCode.BAD_REQUEST_19,
			ErrorCode.BAD_REQUEST_20,
			ErrorCode.BAD_REQUEST_21,
			ErrorCode.BAD_REQUEST_22,
			ErrorCode.BAD_REQUEST_23,
			ErrorCode.BAD_REQUEST_24,
			ErrorCode.UNAVAILABLE_FOR_LEGAL_REASONS
		] ],
		[
			ErrorDialogType.WITH_OPTION, [
				ErrorCode.INSUFFICIENT_BALANCE
			]
		]
	] );
	public ignoredErrorCode = [
		ErrorCode.BAD_REQUEST_410,
		ErrorCode.BAD_REQUEST_412
	];

	public errorDialogKeyMap: TsMap<string, Array<string>> = new TsMap( [
		[ 'error_-1', [
			ErrorCode.INTERNET_DISCONNECTED
		] ],
		[ 'error_-2', [
			ErrorCode.FAILED_TO_LOAD_RESOURCE
		] ],
		[ 'error_403', [
			ErrorCode.INVALID_SESSION_TOKEN,
			ErrorCode.INVALID_SESSION_TOKEN2
		] ],
		[ 'error_402', [
			ErrorCode.INSUFFICIENT_BALANCE
		] ],
		[ 'error_400', [
			ErrorCode.PENDING_BONUS
		] ],
		[ 'error_400-1', [
			ErrorCode.BAD_REQUEST_0,
			ErrorCode.BAD_REQUEST_1,
			ErrorCode.BAD_REQUEST_2,
			ErrorCode.BAD_REQUEST_3,
			ErrorCode.BAD_REQUEST_4,
			ErrorCode.BAD_REQUEST_5,
			ErrorCode.BAD_REQUEST_6,
			ErrorCode.BAD_REQUEST_7,
			ErrorCode.BAD_REQUEST_8,
			ErrorCode.BAD_REQUEST_9,
			ErrorCode.BAD_REQUEST_10,
			ErrorCode.BAD_REQUEST_12,
			ErrorCode.BAD_REQUEST_13,
			ErrorCode.BAD_REQUEST_14,
			ErrorCode.BAD_REQUEST_15,
			ErrorCode.BAD_REQUEST_16,
			ErrorCode.BAD_REQUEST_17,
			ErrorCode.BAD_REQUEST_18,
			ErrorCode.BAD_REQUEST_19,
			ErrorCode.BAD_REQUEST_20,
			ErrorCode.BAD_REQUEST_21,
			ErrorCode.BAD_REQUEST_22,
			ErrorCode.BAD_REQUEST_23,
			ErrorCode.BAD_REQUEST_24
		] ],
		[ 'error_400-11', [
			ErrorCode.BAD_REQUEST_11
		] ],
		[ 'error_411', [
			ErrorCode.BAD_REQUEST_411	// TODO: move to g+
		] ],
		[ 'error_412', [
			ErrorCode.BAD_REQUEST_412	// TODO: move to g+
		] ],
		[ 'error_451', [
			ErrorCode.UNAVAILABLE_FOR_LEGAL_REASONS
		] ]
	] );

	public getErrorDialogType ( errorCode: string ): ErrorDialogType {
		for ( let i = 0; i < this.errorDialogTypeMap.keys().length; i++ ) {
			let key: ErrorDialogType = this.errorDialogTypeMap.keys()[ i ];
			let values: Array<string> = this.errorDialogTypeMap.values()[ i ];
			for ( let j = 0; j < values.length; j++ ) {
				if ( values[ j ] == errorCode ) {
					return key;
				}
			}
		}
		return ErrorDialogType.FULL_SCREEN;
	}

	public getErrorDialogKey ( errorCode: string ): string {
		for ( let i = 0; i < this.errorDialogKeyMap.keys().length; i++ ) {
			let key: string = this.errorDialogKeyMap.keys()[ i ];
			let values: Array<string> = this.errorDialogKeyMap.values()[ i ];
			for ( let j = 0; j < values.length; j++ ) {
				if ( values[ j ] == errorCode ) {
					return key;
				}
			}
		}
		return this.errorDialogKeyMap.keys()[ 0 ];
	}
}


