import { Text, TextStyleOptions } from 'pixi.js';
import { TextField, ITextFieldConfig } from './TextField';


export class WebTextField extends TextField {

	public static elementType: string = 'WebTextField';

	protected label: Text;
	protected autoscale: boolean = false;

	protected initElement ( config: IWebTextFieldConfig ): void {
		const text = ( config.text ) ? config.text : this.getTextByKey( config.key );
		this.label = new Text( text, config.style );

		this.addChild( this.label );
		super.initElement( config );
	}

	public setMaxWidth ( value: number ): void {
		this.label.style.wordWrapWidth = value;
		this.maxWidth = value;
	}

	public setMaxScale ( maxWidth: number, maxHeight: number ): void {
		this.maxWidth = maxWidth;
		this.maxHeight = maxHeight;
	}

	public setAutoscale ( maxWidth: number, maxHeight: number ): void {
		this.maxWidth = maxWidth;
		this.maxHeight = maxHeight;
		this.autoscale = true;
		this.updateTextField();
	}

	protected updateTextField (): void {
		if ( this.autoscale ) {
			this.autoResize();
		}
	}

	set size ( fontSize: number ) {
		this.label.style.fontSize = fontSize;
	}

	get size (): number {
		return this.label.style.fontSize as number;
	}

	get isLargerThanMaxWidth (): boolean {
		return this.label.width > this.maxWidth;
	}

	get fill (): any {
		return this.label.style.fill;
	}

	set fill ( fill: any ) {
		this.label.style.fill = fill;
	}
}

export interface IWebTextFieldConfig extends ITextFieldConfig {
	text?: string;
	style: TextStyleOptions;
}


