import { Container } from 'typescript-ioc/es5';
import { AbstractApi } from 'corengf/src/server/abstract/AbstractApi';
import { NGHttpTransport } from 'corengf/src/server/ng/transport/NGHttpTransport';
import { IPackage, HTTP_METHODS } from 'corengf/src/server/abstract/transport/HttpTransport';
import { BNGGenerateEntropyMsgFormatter } from './../formatter/http/in/BNGGenerateEntropyMsgFormatter';
import { BNGGenerateEntropyRequestMsgFormatter } from './../formatter/http/out/BNGGenerateEntropyRequestMsgFormatter';

/**
 * generate entropy
 */
export class BNGGenerateEntropy extends AbstractApi {

	protected transport: NGHttpTransport;

	public init ( transport: NGHttpTransport, config?: any ): void {
		this.responseFormatter = Container.get( BNGGenerateEntropyMsgFormatter );
		this.requestFormatter = Container.get( BNGGenerateEntropyRequestMsgFormatter );
		this.config = config;
		this.transport = transport;
	}

	protected encapsulation ( messageType: string ): IPackage {
		return {
			url: this.transport.url + this.config.path.generateEntropy,
			name: messageType,
			method: HTTP_METHODS.POST,
			headers: this.transport.getHeaders(),
			content: this.request
		};
	}

	public send ( messageType: string, data?: any ): Promise<any> {
		this.requestFormat( data );
		const apiPackage = this.encapsulation( messageType );
		return this.transport.psend( apiPackage ).then( ( response ) => {
			this._rawData = response.payload;
			this.responseFormat( response );
			return Promise.resolve( response.payload );
		} );
	}
}


