import { MLUIInformationHeaderView } from 'mlui/src/components/informationHeader/MLUIInformationHeaderView';
import { MLUISpinController } from 'mlui/src/components/spin/MLUISpinController';
import { DHBMLUISpinController } from 'game/src/components/ui/components/spin/DHBMLUISpinController';
import { BaseWinLinesV2Controller } from 'slotngf/src/components/lines/reelV2/BaseWinLinesV2Controller';

export class DHBApplication extends SlotApplication {

	protected setupBindings (): void {
		//do basic bindings first!
		super.setupBindings();

		//server
		Container.bind( AbstractServer ).to( SlotNGServer );
		Container.bind( SlotScenarioManager ).to( DHBSlotScenarioManager );

		//MLUI
		Container.bind( UIBankConfig ).to( MLUIBankConfig );
		Container.bind( UIBankPanelView ).to( MLUIBankPanel );
		Container.bind( SlotInformationHeaderView ).to( MLUIInformationHeaderView );
		Container.bind( SlotUILocalButtonsController ).to( MLUILocalButtonsController );
		Container.bind( MLUIPaytableConfig ).to( DHBMLUIPaytableConfig );
		Container.bind( MLUIHistoryConfig ).to( DHBMLUIHistoryConfig );
		Container.bind( MLUIRulesConfig ).to( DHBMLUIRulesConfig );
		Container.bind( SlotUILocalButtonsStateSet ).to( DHBLocalButtonsStateSet );
		Container.bind( MLUISpinController ).to( DHBMLUISpinController );
		Container.bind( MLUISpinSettingsController ).to( DHBMLUISpinSettingsController );
		Container.bind( MLUITurboConfig ).to( DHBMLUITurboConfig );

		//components
		Container.bind( SoundManager ).to( DHBSoundManager );
		Container.bind( ReelConfig ).to( DHBReelConfig );
		Container.bind( MainConfig ).to( DHBConfig );
		Container.bind( SlotBackgroundConfig ).to( DHBBackgroundConfig );
		Container.bind( ForcingSpinConfig ).to( DHBForcingSpinConfig );
		Container.bind( BigWinConfig ).to( DHBBigWinConfig );
		Container.bind( SlotMarqueeConfig ).to( DHBSlotMarqueeConfig );
		Container.bind( SlotFreeSpinsConfig ).to( DHBSlotFreeSpinsConfig );
		Container.bind( WinBoxConfig ).to( DHBWinBoxConfig );
		Container.bind( AnimationViewConfig ).to( DHBAnimationViewConfig );
		Container.bind( LineScoreConfig ).to( DHBLineScoreConfig );
		Container.bind( DialogConfig ).to( DHBDialogConfig );
		Container.bind( SmartPreloaderView ).to( DHBSmartPreloaderView );
		Container.bind( CoinShowerConfig ).to( DHBCoinShowerConfig );
		Container.bind( SlotCoinShowerView ).to( DHBCoinShowerView );

		Container.bind( BaseSymbol ).to( DHBBaseSymbol );

		//formatter
		Container.bind( FreeRespinReelsFormatter ).to( DHBFreeRespinReelsFormatter );
		Container.bind( PickFeatureFormatter ).to( DHBPickFeatureFormatter );
		Container.bind( FreeSpinFeatureFormatter ).to( DHBFreeSpinFeatureFormatter );
		Container.bind( WildLockedFeatureFormatter ).to( DHBWildLockedFeatureFormatter );
		Container.bind( SpinMsgFormatter ).to( DHBSpinMsgFormatter );
		Container.bind( SingleWildMultiplierFeatureFormatter ).to( DHBSingleWildsMultiplierFeatureFormatter );
	}

	protected createComponentList ( components: Array<Component> ): void {
		components.push( { id: 'background', class: DHBBackgroundController } );
		components.push( { id: 'winning', class: BaseWinLinesV2Controller } );
		components.push( { id: 'reel', class: DHBReelController } );
		components.push( { id: 'winBoxes', class: DHBWinBoxV2Controller } );
		components.push( { id: 'marquee', class: DHBSlotMarqueeController } );
		components.push( { id: 'animationArea', class: DHBAnimationViewController } );
		components.push( { id: 'coinShower', class: DHBCoinShowerController } );
		components.push( { id: 'lineScore', class: LineScoreController } );
		components.push( { id: 'mlui', class: MLUIController } );
		components.push( { id: 'dialog', class: DHBSlotDialogController } );
		components.push( { id: 'tooltip', class: TooltipController } );

		if ( !UrlParameters.isRealMode() ) {
			components.push( { id: 'forceSpinOutcome', class: ForcingSpinController } );
		}

		components.push( { id: 'freeSpins', class: DHBSlotFreeSpinsController } );
		components.push( { id: 'bigWin', class: DHBBigWinController } );
	}
}



