import { extras, spine, particles, Texture } from 'pixi.js';
import { Animation } from 'corengf/src/elements/canvas/animation/Animation';
import { ReelStripView } from 'slotngf/src/components/reelV2/ReelStripView';
import { IAnticipationAnimation, IAnticipationParticle, AnticipationAnimationType, IReelsConfig } from 'slotngf/src/core/config/IReelsConfig';

export class Anticipation extends PIXI.Container {

	protected anticipationAnimations: Array<extras.AnimatedSprite | spine.Spine | particles.Emitter>;

	protected reelConfig: IReelsConfig;

	protected defaultSpineTransitionTime: number = 0;

	constructor () { //   animations: Array<IAnticipationAnimation | IAnticipationParticle>) {
		super();
		this.name = 'AnticipationContainer';
		this.visible = false;
		this.anticipationAnimations = new Array<extras.AnimatedSprite | spine.Spine | particles.Emitter>();
	}

	public initialize ( reelsConfig: IReelsConfig ) {
		this.reelConfig = reelsConfig;
		this.reelConfig.anticipationData.animations.forEach( ( animation: IAnticipationAnimation | IAnticipationParticle ) => {
			if ( animation.type == AnticipationAnimationType.SPINE ) {
				this.anticipationAnimations.push( this.createSpineAnimation( animation ) );
			} else if ( animation.type == AnticipationAnimationType.SEQUENCE ) {
				this.anticipationAnimations.push( this.createSequenceAnimation( animation.name ) );
			} else {
				this.anticipationAnimations.push( this.createParticleAnimation( animation as IAnticipationParticle ) );
			}
		} );
	}

	protected createSpineAnimation ( data: IAnticipationAnimation ): spine.Spine {
		let animation = Animation.createSpineAnimation( data.name );

		let totalReels: Array<number> = new Array<number>();
		for ( const reelSetIndex in this.reelConfig.reelSet ) {
			if ( ( this.reelConfig.reelSet as Object ).hasOwnProperty( reelSetIndex ) ) {
				totalReels.push( this.reelConfig.reelSet[ reelSetIndex ].length );
			}
		}
		let largestNumReels: number = Math.max( ...totalReels );
		for ( let i = ( 1 + this.reelConfig.anticipationData.modelData[ 0 ].anticipationSymbolCountTrigger ); i < largestNumReels; i++ ) {
			animation.stateData.setMix(
				animation.name + '_Reel' + i.toString(),
				animation.name + '_Reel' + ( i + 1 ).toString(),
				data.reelTransitionTime ? data.reelTransitionTime : this.defaultSpineTransitionTime
			);
		}
		animation.skeleton.slots.forEach( ( value, i ) => {
			animation.slotContainers[ i ].name = value.data.name;
		} );

		this.addChild( animation );
		return animation;
	}

	protected createSequenceAnimation ( name: string ): extras.AnimatedSprite {
		let animation = Animation.getAnimationByName( name );
		animation.gotoAndStop( 0 );
		animation.loop = true;
		this.addChild( animation );

		return animation;
	}

	protected createParticleAnimation ( data: IAnticipationParticle ): particles.Emitter {
		let particleResource = PIXI.loader.resources[ data.name ].data;
		let particleImages: Array<Texture> = new Array<Texture>();

		let container = new PIXI.particles.ParticleContainer();
		this.addChild( container );
		data.images.forEach( ( image: string ) => {
			particleImages.push( PIXI.Texture.fromImage( image ) );
		} );

		let animation = new particles.Emitter(
			container,
			particleImages,
			particleResource
		);
		animation.emit = false;
		animation.autoUpdate = true;

		return animation;
	}

	public anticipateReel ( reelView: ReelStripView, reelId: number ): void {
		this.visible = true;
		this.anticipationAnimations.forEach( ( animation: extras.AnimatedSprite | spine.Spine | particles.Emitter ) => {
			if ( this.isSpineAnimation( animation ) ) {
				animation.state.setAnimation( 0, animation.name + '_Idle', true );
				animation.state.setAnimation( 1, animation.name + '_Reel' + ( reelId + 1 ), false ); //Need reelID +1 because reelIDs are 0 based and animations are 1 based
			} else if ( this.isSequenceAnimation( animation ) ) {
				animation.position.x = reelView.position.x;
				animation.position.y = reelView.position.y;
				animation.play();
			} else {
				animation.parent.x = reelView.position.x;
				animation.parent.y = reelView.position.y;
				animation.emit = true;
			}
		} );
	}

	public stopAnticipate (): void {
		this.visible = false;
		this.anticipationAnimations.forEach( ( animation: extras.AnimatedSprite | spine.Spine | particles.Emitter ) => {
			if ( this.isSpineAnimation( animation ) ) {
				animation.state.setEmptyAnimation( 0, 0 );
				animation.state.setEmptyAnimation( 1, 0 );
			} else if ( this.isSequenceAnimation( animation ) ) {
				animation.gotoAndStop( 0 );
			} else {
				animation.emit = false;
			}
		} );
	}

	public playAnticipationIntro ( reelView: ReelStripView, reelId: number ): void {
		//
	}

	protected isSpineAnimation ( animation: extras.AnimatedSprite | spine.Spine | particles.Emitter ): animation is spine.Spine {
		return ( <spine.Spine> animation ).spineData !== undefined;
	}

	protected isSequenceAnimation ( animation: extras.AnimatedSprite | spine.Spine | particles.Emitter ): animation is extras.AnimatedSprite {
		return ( <extras.AnimatedSprite> animation ).playing !== undefined;
	}
}


