import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { Container, Sprite, interaction, Point, Rectangle } from 'pixi.js';
import { NMath } from 'corengf/src/utils/NMath';
import { TextField } from 'corengf/src/elements/canvas/text/TextField';
import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { ICoreElementConfig } from 'corengf/src/elements/canvas/CoreElement';
import { IButtonConfig, Button } from 'corengf/src/elements/canvas/button/Button';
import { InteractiveElement } from 'corengf/src/elements/canvas/InteractiveElement';
import { GameContainerManager } from 'corengf/src/core/GameContainerManager';
import { MoneyFormatter, NumberDisplayMode } from 'corengf/src/utils/MoneyFormatter';
import { ICoreUITextFieldElementConfig, CoreUIElementFactory } from 'corengf/src/factory/CoreUIElementFactory';


export enum SliderStyle {
	SINGLE_LINE,
	SLIDER_BELOW
}

export enum SliderMode {
	STEP,
	SMOOTH
}

export interface SliderStepData {
	defaultIndex?: number;
	items: Array<number>;
}

export interface SliderSmoothData {
	/**
	 * Set the digit precision. If range less than 10, default set to 2, otherwise round to int.
	 *
	 * @type {number}
	 * @memberof SliderSmoothData
	 */
	range: [ number, number ];
	precision?: number;
	defaultValue?: number;
}

export enum SliderFormat {
	NUMBER,
	STRING,
	CURRENCY,
	CURRENCY_VALUE_ONLY,
	INFINITE_NUMBER
}

export interface ISliderConfig extends ICoreElementConfig {
	id: string;
	trackAssetName: string; // The track asset name without postfix
	interactiveButtonConfig: IButtonConfig; // The main button
	titleTextField?: ICoreUITextFieldElementConfig;
	minTextField?: ICoreUITextFieldElementConfig;
	maxTextField?: ICoreUITextFieldElementConfig;
	valueTextField: ICoreUITextFieldElementConfig;

	sliderSmoothData?: SliderSmoothData;  // When set slide mode as SMOOTH, dev should set an range: [minValue, maxValue];
	sliderStepData?: SliderStepData; // when set slider mode as STEP, dev must pass an avaliable item array for pick

	/**
	 * The Slider interactive mode
	 * default: STEP
	 * @type {SliderMode}
	 * @memberof ISliderConfig
	 */
	mode?: SliderMode;

	sliderStyle?: SliderStyle;

	/**
	 * format of slider items
	 */
	format?: SliderFormat;

	/**
	 * If assign a value greater than original asset width, it will stretch the  of the track to fit the value.
	 *
	 * @type {number}
	 * @memberof ISliderConfig
	 */
	width?: number;
}


export class Slider extends InteractiveElement {

	@Inject
	protected gameContainerManager: GameContainerManager;

	@Inject
	private moneyFormatter: MoneyFormatter;

	protected config: ISliderConfig;

	protected sliderContainer: Container;

	protected backTrack: Sprite;
	protected activeTrack: Sprite;

	protected minLabel: TextField;
	protected maxLabel: TextField;
	protected titleLabel: TextField;
	protected valueLabel: TextField;

	protected button: Button;

	private _items: Array<number>;
	public get items (): Array<number> {
		return this._items;
	}
	public set items ( value: Array<number> ) {
		this._items = value;
		this.updateValueText();
		if ( this.config.minTextField ) {
			this.minLabel.text = this.formatValue( this.minValue );
		}

		if ( this.config.maxTextField ) {
			this.maxLabel.text = this.formatValue( this.maxValue );
		}
	}

	private range: [ number, number ];

	private defaultIndex: number;

	private defaultValue: number;

	private _currentValue: number;

	private isDragging: boolean = false;

	private id: string;

	protected mode: SliderMode;
	protected format: SliderFormat;

	protected precision: number;

	private baseValue: number;

	public onIndexChanged: MiniSignal = new MiniSignal();
	public onValueChanged: MiniSignal = new MiniSignal();

	public get currentValue (): number {
		if ( this.mode === SliderMode.STEP ) {
			return this.items[ this.currentIndex ];
		}
		return this._currentValue;
	}

	private _currentIndex: number;

	public get currentIndex (): number {
		return this._currentIndex;
	}

	/**
	 * Return the width of the track
	 *
	 * @readonly
	 * @private
	 * @type {number}
	 * @memberof Slider
	 */
	public get trackWidth (): number {
		return this.sliderContainer.width;
	}

	public get buttonHeight (): number {
		return this.button.height;
	}

	public get buttonX (): number {
		return this.button.x - ( this.button.width * this.button.anchor.x );
	}

	public get buttonY (): number {
		return this.button.y - ( this.button.height * this.button.anchor.y );
	}

	protected get percentage (): number {
		if ( this.mode === SliderMode.SMOOTH ) {
			return ( this.currentValue - this.range[ 0 ] ) / ( this.range[ 1 ] - this.range[ 0 ] );
		} else {
			return this.currentIndex / ( this.items.length - 1 );
		}
	}

	protected get minValue (): number {
		if ( this.mode === SliderMode.SMOOTH ) {
			return this.range[ 0 ];
		} else {
			return this.items[ 0 ];
		}
	}

	protected get maxValue (): number {
		if ( this.mode === SliderMode.SMOOTH ) {
			return this.range[ 1 ];
		} else {
			return this.items[ this.items.length - 1 ];
		}
	}

	constructor ( config: ISliderConfig ) {
		super( config );
	}

	protected initElement ( config: ISliderConfig ): void {
		this.config = config;

		this.setup( config );

		this.sliderContainer = new Container();
		this.sliderContainer.interactive = true;
		this.setupSliderTrack( config );

		this.setupButton( config );

		this.addChild( this.sliderContainer );

		this.setupText( config );

		this.setupHitArea();

		this.addListeners();

		this.interactive = true;
	}

	protected addListeners (): void {
		this.sliderContainer.on( 'pointerdown', this.onPointerDown, this );
		this.gameContainerManager.stageContainer.on( 'pointerup', this.onPointerUp, this );
		this.button.on( 'pointerup', this.onPointerUp, this );
		this.button.on( 'pointerupoutside', this.onPointerUp, this );
		this.gameContainerManager.stageContainer.on( 'pointerupoutside', this.onPointerUp, this );
		this.gameContainerManager.stageContainer.on( 'pointermove', this.onDrag, this );
	}

	protected onPointerDown ( e: interaction.InteractionEvent ): void {
		this.isDragging = true;
		this.onDrag( e );
	}

	protected onDrag ( e: interaction.InteractionEvent ): void {
		if ( !this.isDragging ) {
			return;
		}

		const point = e.data.getLocalPosition( this.sliderContainer );
		if ( this.isDragging ) {
			switch ( this.mode ) {
				case SliderMode.STEP:
					this.onDragStep( point );
					break;
				case SliderMode.SMOOTH:
					this.onDragSmooth( point );
					break;
			}
		}
		e.stopPropagation();
	}

	protected onDragStep ( point: Point ): void {
		const index = this.getIndexByPosition( point );
		let dispatchValueChanged = false;
		if ( this.currentIndex !== index ) {
			this.onIndexChanged.dispatch( { id: this.id, value: index } );
			dispatchValueChanged = true;
		}
		this._currentIndex = index;

		if ( dispatchValueChanged ) {
			this.onValueChanged.dispatch( { id: this.id, value: this.currentValue } );
		}

		this.updateTrack();
		this.updateButton();
		this.updateValueText();
	}

	protected onDragSmooth ( point: Point ): void {
		let value = this.range[ 0 ] + point.x / this.sliderContainer.width * ( this.range[ 1 ] - this.range[ 0 ] );
		value = NMath.clamp( value, this.range[ 0 ], this.range[ 1 ] );
		value = Math.round( value * this.baseValue ) / this.baseValue;
		this._currentValue = value;

		this.onValueChanged.dispatch( { id: this.id, value: this.currentValue } );

		this.updateTrack();
		this.updateButton();
		this.updateValueText();
	}

	protected getIndexByPosition ( point: Point ): number {
		const intervalLength = this.getIntervalLength();
		const bound = intervalLength / 2;

		const intervalIndex = Math.floor( point.x / intervalLength );

		const targetPosition = intervalIndex * intervalLength;

		let index: number;
		if ( point.x < bound ) {
			index = 0;
		} else if ( point.x > ( this.sliderContainer.width - bound ) ) {
			index = this.items.length - 1;
		} else if ( point.x < ( targetPosition + bound ) ) {
			index = intervalIndex;
		} else {
			index = intervalIndex + 1;
		}
		if ( index > this.items.length - 1 ) {
			index = this.items.length - 1;
		}
		return index;
	}

	protected getIntervalLength (): number {
		return this.backTrack.width / ( this.items.length - 1 );
	}

	protected onPointerUp ( e: interaction.InteractionEvent ): void {
		this.isDragging = false;
	}

	protected setupHitArea (): void {
		this.sliderContainer.hitArea = new Rectangle(
			- ( this.button.width * this.button.anchor.x ),
			- this.sliderContainer.height / 2,
			this.sliderContainer.width + ( this.button.width * this.button.anchor.x ),
			this.sliderContainer.height );
	}

	protected setup ( config: ISliderConfig ): void {
		this.id = config.id;
		this.mode = ( config.mode !== undefined ) ? config.mode : SliderMode.STEP;
		this.format = ( config.format !== undefined ) ? config.format : SliderFormat.NUMBER;

		switch ( this.mode ) {
			case SliderMode.STEP:
				if ( !config.sliderStepData || !config.sliderStepData.items || config.sliderStepData.items.length < 2 ) {
					throw new Error( 'Set mode as STEP, please assign avaliable item array.' );
				}
				this._items = config.sliderStepData.items;
				this.defaultIndex = ( config.sliderStepData.defaultIndex !== undefined ) ? config.sliderStepData.defaultIndex : 0;
				this._currentValue = this.items[ this.defaultIndex ];
				this._currentIndex = this.defaultIndex;
				break;
			case SliderMode.SMOOTH:
				if ( !config.sliderSmoothData || config.sliderSmoothData.range.length !== 2 ) {
					throw new Error( 'Set mode as SMOOTH, please assign avaliable range array:[minValue, maxValue].' );
				}
				this.range = config.sliderSmoothData.range;

				if ( config.sliderSmoothData.precision === undefined ) {
					this.precision = ( this.range[ 1 ] - this.range[ 0 ] < 10 ) ? 2 : 0;
				} else {
					this.precision = config.sliderSmoothData.precision;
				}
				this.baseValue = Math.pow( 10, this.precision );

				this.defaultValue = ( config.sliderSmoothData.defaultValue !== undefined )
					? NMath.clamp( config.sliderSmoothData.defaultValue, this.range[ 0 ], this.range[ 1 ] )
					: this.range[ 0 ];
				this._currentValue = this.defaultValue;
				break;
		}
	}

	protected setupText ( config: ISliderConfig ): void {

		if ( config.titleTextField ) {
			this.titleLabel = CoreUIElementFactory.createInstance( config.titleTextField );
			this.addChild( this.titleLabel );
		}

		if ( config.minTextField ) {
			this.minLabel = CoreUIElementFactory.createInstance( config.minTextField );
			this.minLabel.text = this.formatValue( this.minValue );
			this.addChild( this.minLabel );
		}

		if ( config.maxTextField ) {
			this.maxLabel = CoreUIElementFactory.createInstance( config.maxTextField );
			this.maxLabel.text = this.formatValue( this.maxValue );
			this.addChild( this.maxLabel );
		}

		if ( config.valueTextField ) {
			this.valueLabel = CoreUIElementFactory.createInstance( config.valueTextField );
			this.updateValueText();
			this.addChild( this.valueLabel );
		}
	}

	protected formatValue ( value: any ): string {
		switch ( this.format ) {
			case SliderFormat.CURRENCY:
			case SliderFormat.CURRENCY_VALUE_ONLY:
				const displayMode: NumberDisplayMode = ( this.format == SliderFormat.CURRENCY ) ? NumberDisplayMode.VALUE_WITH_CURRENCY : NumberDisplayMode.VALUE_ONLY;
				return value == -1 ? '∞' : this.moneyFormatter.formatMoney( value, displayMode );
			case SliderFormat.NUMBER:
				return value == -1 ? '∞' : value.toString();
			case SliderFormat.STRING:
				return value;
		}
	}

	protected setupSliderTrack ( config: ISliderConfig ): void {
		this.backTrack = new Sprite( PIXI.Texture.from( config.trackAssetName + '_Inactive.png' ) );
		this.activeTrack = new Sprite( PIXI.Texture.from( config.trackAssetName + '_Active.png' ) );
		this.activeTrack.position.set( this.activeTrack.x, ( ( this.backTrack.height / 2 ) - ( this.activeTrack.height / 2 ) ) );
		if ( config.width && config.width > this.backTrack.width ) {
			this.backTrack.width = this.backTrack.width * ( config.width / this.backTrack.width );
		}

		this.updateTrack();

		this.sliderContainer.addChild( this.backTrack );

		this.sliderContainer.addChild( this.activeTrack );
	}

	protected updateValueText (): void {
		if ( this.valueLabel ) {
			this.valueLabel.text = this.formatValue( this.currentValue );
		}
	}

	protected setupButton ( config: ISliderConfig ): void {
		this.button = new Button( config.interactiveButtonConfig );
		this.button.anchor.set( 0.5, 0.5 );
		this.updateButton();

		this.sliderContainer.addChild( this.button );
	}

	protected updateTrack (): void {
		this.activeTrack.width = this.percentage * this.backTrack.width;
	}

	protected updateSlider ( width: number ): void {
		this.backTrack.width = this.backTrack.width * ( width / this.backTrack.width );
		this.updateTrack();
		this.updateButton();
		this.setupHitArea();
	}

	protected updateButton (): void {
		this.button.position.set( this.activeTrack.width, this.backTrack.height / 2 );
	}

	public setEnable ( value: boolean ): void {
		this.button.setEnable( value );
		if ( value ) {
			this.backTrack.alpha = 1;
			this.activeTrack.alpha = 1;
			this.sliderContainer.interactive = true;
		} else {
			this.backTrack.alpha = 0.5;
			this.activeTrack.alpha = 0.5;
			this.sliderContainer.interactive = false;
		}
	}

	public triggerUpdate (): void {
		this.onValueChanged.dispatch( { id: this.id, value: this.currentValue } );
		this.onIndexChanged.dispatch( { id: this.id, value: this.currentIndex } );
	}

	public updateAttributes ( config: ISliderConfig ): void {
		if ( !config ) {
			return;
		}

		if ( config.position ) {
			this.setPosition( config.position );
		}

		if ( config.titleTextField ) {
			this.titleLabel.updateAttributes( config.titleTextField.elementConfig );
		}

		if ( config.minTextField && this.minLabel ) {
			this.minLabel.updateAttributes( config.minTextField.elementConfig );
		}

		if ( config.maxTextField && this.maxLabel ) {
			this.maxLabel.updateAttributes( config.maxTextField.elementConfig );
		}

		if ( config.valueTextField ) {
			this.valueLabel.updateAttributes( config.valueTextField.elementConfig );
		}

		if ( config.width ) {
			this.updateSlider( config.width );
		}
	}

	public reset (): void {
		switch ( this.mode ) {
			case SliderMode.STEP:
				this._currentIndex = 0;
				break;
			case SliderMode.SMOOTH:
				this._currentValue = this.range[ 0 ];
				break;
		}
		this.updateTrack();
		this.updateButton();
		this.updateValueText();
	}

	public forcePointerDown ( e: any ): void {
		this.onPointerDown( e );
	}

	public forcePointerUp ( e: any ): void {
		this.onPointerUp( e );
	}

}

export interface SliderData {
	id: string;
	value: number;
}


