import * as MiniSignal from 'mini-signals';
import { NpsVersion } from './types/nps.d';
import { Inject } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Helper } from 'corengf/src/utils/Helper';
import { UrlParameters } from 'corengf/src/utils/UrlParameters';
import { MoneyFormatter } from 'corengf/src/utils/MoneyFormatter';
import { AbstractApi } from 'corengf/src/server/abstract/AbstractApi';
import { AbstractSdkController } from 'corengf/src/server/abstract/sdk/AbstractSdkController';

/**
 * integrate with BNG sdk
 */
export class NGBNGController extends AbstractSdkController {
	public static onIframeResponse: MiniSignal = new MiniSignal();

	@Inject
	protected moneyFormatter: MoneyFormatter;

	protected static _version: NpsVersion = 'v1';
	public static get version (): NpsVersion {
		return NGBNGController._version;
	}

	public getSdkAPI (): TsMap<string, AbstractApi> {
		let apiMapping: TsMap<string, AbstractApi> = new TsMap<string, AbstractApi>();
		return apiMapping;
	}

	public init (): void {
		const urlSdkVersion: string = UrlParameters.sdkVersion;
		if ( urlSdkVersion != Helper.EMPTY ) {
			NGBNGController._version = UrlParameters.sdkVersion as NpsVersion;
		}

		//add listener
		window.addEventListener( 'message', function ( e ) {
			const messageFromPortal = <BNG.IMessageFromPortal> JSON.parse( e.data );
			switch ( messageFromPortal.method ) {
				case 'getPlayerInfo':
				case 'makeBetTx':
				case 'makeSeedTx':
					NGBNGController.onIframeResponse.dispatch( messageFromPortal );
					break;
				case 'roundFinished':
				case 'roundStarted':
					break;
			}
		} );

		this.moneyFormatter.precision = 4;
	}

	public onReceiveError ( data: any ): void {
		let errorData: any = {
			code: data.code,
			message: data.msg
		};

		if ( data.cause ) {
			const cause = JSON.parse( data.cause );
			if ( cause.traceId ) {
				errorData = {
					...errorData,
					traceId: cause.traceId
				};
			}

			if ( cause.error ) {
				if ( cause.error.code ) {
					errorData.code = cause.error.code;
				}
				if ( cause.error.message ) {
					errorData.message = cause.error.message;
				}
			}
		}

		NGBNGController.postMessage( 'notifyError', errorData );
	}

	public static postMessage ( method: BNG.Method, payload?: any ): void {
		const data: BNG.IMessageFromGame = {
			version: this.version,
			uid: Helper.uuid(),
			method
		};

		if ( payload ) {
			data.payload = payload;
		}

		window.parent.postMessage( JSON.stringify( data ), '*' );
	}
}


