import { AudioTrackName } from 'corengf/src/audio/SoundManager';
import { SymbolPosition } from 'slotngf/src/core/config/IReelsConfig';
import { WildLock } from 'slotngf/src/components/reelV2/action/WildLock';
import { IWildLocked } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { DHBBaseSymbol } from 'game/src/components/reels/symbol/DHBBaseSymbol';

export class DHBWildLock extends WildLock {


	public run ( data: IDHBWildLocked ): Promise<any> {

		this.currentData = data;
		this.totalLockCount = 0;
		this.currentLockededCount = 0;

		return new Promise( ( resolve ) => {
			this.resolveForStop = resolve;

			data.wild_config.forEach( ( reel, index ) => {
				reel.forEach( ( symbol ) => {
					if ( symbol != '*' ) {
						let reelId = index;
						let symbolId = +symbol;
						this.totalLockCount++;
						this.showLockAnimationAt( reelId, symbolId, 0 );
					}
				} );
			} );

		} );
	}

	protected showLockAnimationAt ( reelId: number, symbolId: number, layer?: number ): void {
		let symbolPosition: SymbolPosition = {
			reelIndex: reelId,
			symbolIndex: symbolId
		};

		const object = this.view.placeOverlayAnimationAt( 'WILD', layer, symbolPosition );
		object.playAnimation( 'LOCK' );
		this.soundManager.play( 'WildLockAudio', AudioTrackName.EFFECT );
		object.animationCompleteSignal.once( this.onWildLockCompleted.bind( this, symbolPosition.reelIndex, symbolPosition.symbolIndex ), this );

	}

	protected onWildLockCompleted ( reelId: number, symbolId: number ): void {
		super.onWildLockCompleted( reelId, symbolId );

		let symbolPosition: SymbolPosition = {
			reelIndex: reelId,
			symbolIndex: symbolId
		};

		const object = this.view.placeOverlayAnimationAt( 'WILD', 0, symbolPosition ) as DHBBaseSymbol;
		object.playLoopAnimation( 'LOCK' );
	}
}

export interface IDHBWildLocked extends IWildLocked {
	reset?: boolean;
}


