import * as MiniSignal from 'mini-signals';
import { Rectangle, Graphics, Point } from 'pixi.js';
import { SimpleTween } from 'corengf/src/time/SimpleTween';
import { TextField } from 'corengf/src/elements/canvas/text/TextField';
import { Sprite, ISprite } from 'corengf/src/elements/canvas/sprite/Sprite';
import { CoreElement, ICoreElementConfig } from 'corengf/src/elements/canvas/CoreElement';
import { CoreUIElementFactory, ICoreUITextFieldElementConfig } from 'corengf/src/factory/CoreUIElementFactory';


export class ScrollMarquee extends CoreElement {

	public onMarqueeTweenCompleteSignal: MiniSignal;

	protected config: IScrollMarqueeConfig;

	protected tween: SimpleTween;

	protected background: Sprite;

	protected foreground: Sprite;

	protected marqueeMask: Graphics;

	protected marqueeText: TextField;

	protected currentDisplayMode: MarqueeDisplayMode;

	protected nextMessage: string;

	protected initElement ( config: IScrollMarqueeConfig ): void {
		this.config = config;
		this.onMarqueeTweenCompleteSignal = new MiniSignal();
		this.currentDisplayMode = this.config.marqueeDisplayMode;

		this.createBackground();
		this.createMarqueeText();
		this.createForeground();
	}

	protected createBackground (): void {
		if ( this.config.background == undefined ) {
			return;
		}

		this.background = new Sprite( this.config.background );
		// set default position
		this.setDeafultSpriteSetting( this.background, this.config.background );
		this.addChild( this.background );
	}

	protected createMarqueeText (): void {
		this.marqueeMask = new Graphics();
		this.marqueeMask.position.set( this.config.mask.x, this.config.mask.y );
		this.marqueeMask.drawRect(
			0, 0,
			this.config.mask.width, this.config.mask.height );
		this.addChild( this.marqueeMask );

		this.marqueeText = CoreUIElementFactory.createInstance( this.config.marqueeText );

		this.marqueeText.mask = this.marqueeMask;
		this.addChild( this.marqueeText );
	}

	protected createForeground (): void {
		if ( this.config.foreground == undefined ) {
			return;
		}

		this.foreground = new Sprite( this.config.foreground );
		// set default position
		this.setDeafultSpriteSetting( this.foreground, this.config.foreground );
		this.addChild( this.foreground );
	}

	protected setDeafultSpriteSetting ( sprite: Sprite, spriteConfig: ISprite ): void {
		if ( sprite ) {
			if ( !spriteConfig.position ) {
				sprite.position.set( this.marqueeMask.x, this.marqueeMask.y );
			} else {
				sprite.position.set( spriteConfig.position.x, spriteConfig.position.y );
			}
		}
	}

	public updateAttributes ( config: IScrollMarqueeConfig ): void {
		if ( config == undefined ) {
			return;
		}
		super.updateAttributes( config );

		if ( config.background ) {
			this.background.updateAttributes( config.background );
		}

		if ( config.marqueeText ) {
			this.marqueeText.updateAttributes( config.marqueeText.elementConfig );
		}

		if ( config.mask ) {
			this.marqueeMask.position.set( config.mask.x, config.mask.y );
			this.marqueeMask.clear();
			this.marqueeMask.drawRect(
				0, 0,
				config.mask.width, config.mask.height );
		}

		this.resetFixedModeMarqueeText();

		if ( config.foreground ) {
			this.foreground.updateAttributes( config.foreground );
		}
	}

	public setMarqueeText ( message: string, mode?: MarqueeDisplayMode ): void {
		if ( message == undefined ) {
			return;
		}

		this.currentDisplayMode = ( ( mode ) ? mode : this.config.marqueeDisplayMode );

		this.nextMessage = message;
		this.startMarqueeTween( this.marqueeMask.x + ( this.marqueeMask.width ), this.currentDisplayMode );
	}

	protected resetFixedModeMarqueeText (): void {
		if ( this.currentDisplayMode == MarqueeDisplayMode.FIXED ) {
			this.setMarqueeText( this.marqueeText.text, this.currentDisplayMode );
		}
	}

	protected setMessage ( text: string ): void {
		this.marqueeText.text = text;
		this.marqueeText.setAutoscale( this.marqueeMask.width, this.marqueeMask.height );
	}

	protected setModeLabelScale ( startPointX: number, mode: MarqueeDisplayMode ): void {
		switch ( mode ) {
			case MarqueeDisplayMode.FIXED:
				this.marqueeText.setAutoscale( this.marqueeMask.width, this.marqueeMask.height );
				this.marqueeText.position.set( startPointX, this.marqueeText.position.y );
				break;
			case MarqueeDisplayMode.ONCE:
			case MarqueeDisplayMode.LOOP:
				this.marqueeText.setAutoscale( -1, -1 );
				break;
		}
	}

	protected startMarqueeTween ( startX: number, mode: MarqueeDisplayMode ): void {
		let dis: number;
		let position: Point;
		let duration: number;
		// if current tween is not finished yet, tween out current massage and tween in next massage
		if ( this.tween != undefined ) {
			dis = this.getFullDistance();
			// init exit position
			position = new Point( -dis, this.marqueeText.position.y );
			// ( dis of current x to exit x ) / speed
			duration = ( this.marqueeText.position.x + dis ) / this.config.accelerateSpeed;
			this.tween.stop();
			this.tween = new SimpleTween( this.marqueeText );
			this.tween.to( position, duration )
				.onComplete( () => {
					this.tween = undefined;
					this.setModeLabelScale( startX, mode );
					this.setMarqueeText( this.nextMessage, mode );
				} )
				.start();
		} else { // if current tween is finished
			this.setMessage( this.nextMessage );
			this.setModeLabelScale( startX, mode );
			this.marqueeText.position.set( startX, this.marqueeText.position.y );

			this.tween = new SimpleTween( this.marqueeText );

			if ( mode == MarqueeDisplayMode.FIXED ) {
				position = new Point( this.getCenterPointX(), this.marqueeText.position.y );
				dis = this.marqueeText.position.x - position.x;
				duration = dis / this.config.accelerateSpeed;
				this.tween.to( position, duration )
					.start();
			} else {
				dis = this.getFullDistance();
				position = new Point( -dis, this.marqueeText.position.y );
				duration = dis / this.config.messageSpeed;
				this.tween.to( position, duration )
					.onComplete( () => {
						this.tween = undefined;
						if ( mode == MarqueeDisplayMode.ONCE ) {
							this.onMarqueeTweenCompleteSignal.dispatch();
						} else {
							this.startMarqueeTween( startX, mode );
						}
					} )
					.start();
			}
		}
	}

	protected getFullDistance (): number {
		let dis: number;
		if ( this.marqueeText.width <= this.marqueeMask.width ) {
			dis = ( this.marqueeMask.width / 2 + this.marqueeText.width / 2 );
		} else {
			dis = this.marqueeText.width;
		}
		return dis;
	}

	protected getCenterPointX (): number {
		let x: number;
		if ( this.marqueeText.width <= this.marqueeMask.width ) {
			x = this.marqueeMask.x + ( this.marqueeMask.width / 2 ) - ( this.marqueeText.width / 2 );
		} else {
			x = this.marqueeMask.x;
		}
		return x;
	}
}

export interface IScrollMarqueeConfig extends ICoreElementConfig {
	marqueeDisplayMode: MarqueeDisplayMode;
	mask: Rectangle;
	marqueeText: ICoreUITextFieldElementConfig;
	messageSpeed: number;
	/** define the speed when massage speed in and out */
	accelerateSpeed: number;
	background?: ISprite;
	foreground?: ISprite;
}

export enum MarqueeDisplayMode {
	LOOP,
	ONCE,
	FIXED
}


