import { Inject, Singleton } from 'typescript-ioc/es5';
import { ErrorDialogView } from './ErrorDialogView';
import { ErrorDialogConfig } from './ErrorDialogConfig';
import { CoreEvent } from 'corengf/src/core/CoreEvent';
import { GameConfig } from 'corengf/src/core/GameConfig';
import { DataHookService } from 'corengf/src/core/DataHookService';
import { IErrorMsgData } from 'corengf/src/server/abstract/ErrorCode';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { ErrorHandleService } from 'corengf/src/core/ErrorHandleService';


@Singleton
export class ErrorDialogController {
	protected view: ErrorDialogView;

	@Inject
	protected gameConfig: GameConfig;

	@Inject
	protected dataHookService: DataHookService;

	protected errorHandleService: ErrorHandleService;

	constructor ( @Inject view: ErrorDialogView ) {
		this.view = view;
	}

	public init ( errorHandleService: ErrorHandleService ): void {
		const config = this.gameConfig.getConfig<ErrorDialogConfig>( ErrorDialogConfig );

		config.onCloseDialog = this.onCloseDialog.bind( this );

		this.view.initialize( config );

		errorHandleService.onReceiveErrorSignal.add( this.openDialog, this );

		this.errorHandleService = errorHandleService;
	}

	public onCloseDialog (): void {
		GlobalDispatcher.dispatchIntoAllStates( CoreEvent.ON_CLOSE_ERROR_DIALOG );
	}

	public openDialog ( msgData: IErrorMsgData ): void {
		if ( !msgData ) {
			return;
		}

		GlobalDispatcher.dispatchIntoAllStates( CoreEvent.ON_OPEN_ERROR_DIALOG, msgData );

		this.sendOpenDialogDataHook( msgData.code );
		this.view.openDialog( msgData.type, msgData.code, msgData.msg );
	}

	protected sendOpenDialogDataHook ( errorCode: string ): void {
		this.dataHookService.onButtonClicked( 'SHOW_ERROR_DIALOG', 'NGF',
			{
				action: 'actions_SHOW_ERROR_DIALOG',
				errorCode: errorCode
			} );
	}
}



