import { IFeatureDetail, ISymbolsData, ISpinResult } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';

export class FeatureTypes {
	public static readonly PICK: string = 'PICK';
	public static readonly FREE_SPIN: string = 'FREE_SPIN';
	public static readonly SUPERBOOSTER: string = 'SUPERBOOSTER';
	public static readonly RETRIGGER: string = 'RETRIGGER';
	public static readonly JACKPOT: string = 'JACKPOT';
	public static readonly WHEEL_FORTUNE: string = 'WHEEL_FORTUNE';
	public static readonly WILD_MULTIPLIER: string = 'WILD_MULTIPLIER';
	public static readonly SINGLE_WILD_MULTIPLIER: string = 'SINGLE_WILD_MULTIPLIER';
	public static readonly WILD_LOCKED: string = 'WILD_LOCKED';
	public static readonly REPLACING_SYMBOLS: string = 'REPLACING_SYMBOLS';
	public static readonly ROTATE: string = 'ROTATE';
	public static readonly SINGLE_LINE_WILD_MULTIPLIER: string = 'SINGLE_LINE_WILD_MULTIPLIER';
	public static readonly FREE_RESPIN_REELS: string = 'FREE_RESPIN_REELS';
	public static readonly SYMBOL_CRAWL: string = 'SYMBOL_CRAWL';
	public static readonly MULTIPLIER: string = 'MULTIPLIER';
}

/**
 * pick game detail
 */
export interface IPickFeatureDetail extends IFeatureDetail {
	choices: Array<string>;
}

/**
 * wheel fortune detail
 */
export interface IWheelFortuneDetail extends IFeatureDetail {
	candidate: WheelSymbolCandidate[];
	selected: number;
}

export interface WheelSymbolCandidate {
	symbol: string;
	weight: number;
}

/**
 * free spin feature detail
 */
export interface IFreeSpinFeatureDetail extends IFeatureDetail {
	total_free_spin_count: number;
	free_spins_left: number;
	free_spins_added: number;
	total_free_spin_win_amount: number;
	game_feature: IFeatureDetail; //TODO: this is for Rad+, remove this in near future
}

/**
 * jackpot feature detail
 */
export interface IJackpotDetail extends IFeatureDetail {
	symbol: string;
	position: number;
	prize: number;
	reels: Array<Array<string>>;
}

/*
* wild multiplier detail on whole reel
*/
export interface IWildMultiplier extends IFeatureDetail {
	wild_config: Array<Array<IWildConfigData>>;
	wild_multiplier: number;
}

export interface IWildConfigData {
	index: string;
	value: number;
}

/**
 * multiplier on whole reel
 */
export interface ISingleWildMultiplier extends IFeatureDetail {
	wild_multiplier: number;
}

export interface ISingleLineWildMultiplier extends IFeatureDetail {
	single_line_wild_multiplier_details: Array<ISingleLineWildMultiplierDetail>;
}

export interface ISingleLineWildMultiplierDetail extends IFeatureDetail {
	line_number: number;
	wild_multiplier: number;
	wild_config: Array<Array<string>>;
}

/**
 * lock wild
 */
export interface IWildLocked extends IFeatureDetail {
	wild_config: Array<Array<string>>;
}

export interface ILockReelSymbols extends IFeatureDetail {
	locked_symbols: Array<Array<string>>;
	new_lock_symbols: Array<Array<string>>;
}

/**
 * replacing symbols detail
 */
export interface IReplacingSymbolsDetail extends IFeatureDetail {
	original_reels: Array<ISymbolsData>;
	swapped_symbols: Array<ISymbolsData>;
	count?: number;
}

/**
 * rotate reel feature
 */
export interface IRotateFeatureDetail extends IFeatureDetail {
	total_rotate_wins: number;
	rotates: Array<ISpinResult>;
}

/**
 * free respin feature
 */
export interface IFreeRespinReelsDetail extends IFeatureDetail {
	reels_to_spin: Array<number>;
	free_spins_left?: number;
	total_free_spin_count?: number;
	total_free_spin_win_amount?: number;
}
export interface IMultiplierFeatureDetail extends IFeatureDetail {
	multiplier: number;
	applied_multiplier: number;
	added_multiplier: number;
}

export interface ISymbolCrawlFeatureDetail extends IFeatureDetail {
	symbol: string;
	added_position: Array<ISymbolPosition>;
	crawl_position: Array<ISymbolCrawlPosition>;
	original_reels: Array<ISymbolsData>;
}

export interface ISymbolCrawlPosition {
	position: ISymbolPosition;
	next_position: ISymbolPosition;
}

export interface ISymbolPosition {
	reelIndex: number;
	symbolIndex: number;
}



