/**
 * Base class for all web element
 */
export class CoreWebElement {

	public static elementType: string = 'CoreWebElement';

	public createElement ( config: ICoreWebElementConfig ): ICoreWebElementConfig {
		return config;
	}
}

export interface ICoreWebElementConfig {
	type: string;
	blockStyle?: string;
	config?: any;
}


