import { Helper } from 'corengf/src/utils/Helper';
import { UrlParameters } from 'corengf/src/utils/UrlParameters';
import { IGetPlayerInfoResult } from 'corengf/src/server/ng/sdk/bng/types/nps';
import { SlotNGHttpFormatter } from 'slotngf/src/server/ng/formatter/SlotNGHttpFormatter';

/**
 * Format login request
 */
export class BNGGameInitRequestMsgFormatter extends SlotNGHttpFormatter {

	/**
	 * Builds setting mode message
	 * @param data - ignored
	 * @returns {any} message object
	 */
	public codeMessage ( data?: IGetPlayerInfoResult ): any {
		const requestPayload: any = {};
		requestPayload.game_code = this.config.gameCode;
		requestPayload.player_id = data.playerId;
		requestPayload.player_token = UrlParameters.sessionToken;
		requestPayload.device = Helper.getDeviceInfo().toLocaleUpperCase();
		return requestPayload;
	}
}


