import { Container } from 'typescript-ioc/es5';
import { BNGEntropyAPI } from './BNGEntropyAPI';
import { UrlParameters } from 'corengf/src/utils/UrlParameters';
import { IPackage, HTTP_METHODS } from 'corengf/src/server/abstract/transport/HttpTransport';
import { SlotNGHttpTransport } from 'slotngf/src/server/ng/transport/SlotNGHttpTransport';
import { ForceSpinMsgFormatter } from 'slotngf/src/server/ng/formatter/http/out/ForceSpinMsgFormatter';
import { BNGSpinRequestMsgFormatter } from 'slotngf/src/server/ng/sdk/bng/formatter/http/out/BNGSpinRequestMsgFormatter';
import { SpinMsgFormatter } from 'slotngf/src/server/ng/formatter/http/in/SpinMsgFormatter';
import { NGBNGController } from 'corengf/src/server/ng/sdk/bng/NGBNGController';


export class BNGSpin extends BNGEntropyAPI {

	public init ( transport: SlotNGHttpTransport, config?: any ): void {
		super.init( transport, config );

		this.requestFormatter = ( UrlParameters.isRealMode() ) ? Container.get( BNGSpinRequestMsgFormatter ) : Container.get( ForceSpinMsgFormatter );
		this.responseFormatter = Container.get( SpinMsgFormatter );
	}

	protected encapsulation ( messageType: string ): IPackage {
		return {
			url: this.transport.url + ( UrlParameters.isRealMode() ? this.config.path.spin : this.config.devPath.spin ),
			name: messageType,
			method: HTTP_METHODS.POST,
			headers: this.transport.getHeaders(),
			content: this.request
		};
	}

	/**
	 *
	 * @param messageType
	 * @param data
	 */
	public send ( messageType: string, data?: any ): Promise<any> {
		return this.generateEntropy()
			.then( ( entropyData ) => this.getBetSeedData( entropyData ) )
			.then( ( makeBetResponse: string ) => {
				this.slotNGModel.makeBetInfo = JSON.parse( makeBetResponse );
				this.requestFormat( data );
				const apiPackage = this.encapsulation( messageType );
				return this.transport.psend( apiPackage );
			} )
			.then( ( response ) => {
				console.log( '[TAKE_TURN_RESP]', response );
				this.responseFormat( response );
				return this.onResponse();
			} )
			.catch( ( error ) => {
				if ( typeof error === 'object' ) {
					throw new Error( `[ERROR] ${ JSON.stringify( error ) }` );
				} else {
					throw new Error( error );
				}
			} );
	}

	protected getBetSeedData ( entropyData: any ): Promise<any> {
		if ( NGBNGController.version == 'v1' ) {
			return this.makeBetTx( entropyData );
		}
		return this.makeSeedTx( entropyData );
	}
}


