import { extras, Point, Texture } from 'pixi.js';
import { Inject } from 'typescript-ioc/es5';
import { NMath } from 'corengf/src/utils/NMath';
import { Viewport } from 'corengf/src/display/Viewport';
import { IEmitter } from './../../core/config/ICoinShowerConfig';


export class SlotCoinParticle extends extras.AnimatedSprite {

	protected scaleSpeed: number;
	protected velocity: Point;
	protected angularVelocity: number;
	protected _alive: boolean;
	protected emitterModel: IEmitter;

	@Inject
	protected viewport: Viewport;

	constructor ( frames: string[], model: IEmitter ) {
		let textures: Array<Texture> = new Array<Texture>();
		for ( let frame of frames ) {
			let texture = Texture.fromFrame( frame );
			textures.push( texture );
		}
		super( textures, true );

		this.emitterModel = model;
		this.loop = true;
		this.anchor.set( 0.5, 0.5 );
		this.velocity = new Point( 0, 0 );
		this._alive = false;
		this.reset();
	}

	public update ( deltaTime: number ): void {
		if ( !this._alive ) {
			return;
		}

		super.update( deltaTime );

		let model = this.emitterModel;
		let scale = this.scale.x;
		let timeStep = model.speedMultiplier * deltaTime / 1000;

		if ( scale < model.maximumScale ) {
			scale += this.scaleSpeed * timeStep;
			scale = NMath.clamp( scale, 0, model.maximumScale );
			this.scale.set( scale, scale );
		}

		this.angularVelocity += model.angularAcceleration * timeStep;
		this.rotation += this.angularVelocity * timeStep * NMath.sign( this.velocity.x );

		this.x += this.velocity.x * timeStep;

		this.velocity.y += model.gravity * timeStep;
		this.y += this.velocity.y * timeStep;

		this.animationSpeed += model.animationAcceleration * timeStep;
		if ( this.velocity.y > 0 && this.y >= this.viewport.height ) {
			this.alive = false;
		}
	}

	protected reset (): void {
		let model = this.emitterModel;
		this.visible = false;

		let xOffsetDirection: number = 1;
		if ( NMath.randomInt( 0, 10 ) % 2 == 0 ) {
			xOffsetDirection = -1;
		}

		let yOffsetDirection: number = 1;
		if ( NMath.randomInt( 0, 10 ) % 2 == 0 ) {
			yOffsetDirection = -1;
		}

		if ( NMath.randomInt( 0, 10 ) % 2 == 0 ) {
			this.animationSpeed = Math.abs( this.animationSpeed );
		} else {
			this.animationSpeed = -Math.abs( this.animationSpeed );
		}

		this.x = model.emitterX + ( xOffsetDirection * NMath.randomInt( 0, model.xOffsetRange ) );
		this.y = model.emitterY + ( yOffsetDirection * NMath.randomInt( 0, model.yOffsetRange ) );

		this.velocity.x = NMath.randomInt( model.minimumInitialVelocityX, model.maximumInitialVelocityX );
		this.velocity.y = NMath.randomInt( model.minimumInitialVelocityY, model.maximumInitialVelocityY );

		this.angularVelocity = model.angularVelocity;
		this.rotation = NMath.randomInt( 0, 360 );

		this.scale.set( model.minimumScale, model.minimumScale );
		this.scaleSpeed =
			Math.min( model.maximumScaleSpeed * Math.random() + model.minimumScaleSpeed, model.maximumScaleSpeed );

		this.animationSpeed = model.animationSpeed;
	}

	public play (): void {
		this.visible = true;
		super.play();
	}

	get alive (): boolean {
		return this._alive;
	}

	set alive ( value: boolean ) {
		this._alive = value;
		if ( value ) {
			this.play();
		} else {
			this.reset();
		}
	}
}


