import { IDataHookData } from 'corengf/src/core/DataHookService';
import { IWebSocketRequestData } from 'corengf/src/server/abstract/transport/WebSocketTransport';
import { NGWebSocketFormatter } from 'corengf/src/server/ng/formatter/websocket/NGWebSocketFormatter';

/**
 * formatting data hook log data
 */
export class NGDataHookLogFormatter extends NGWebSocketFormatter {

	/**
	 * sends button press emulation package
	 * @param data
	 */
	public codeMessage ( data: IDataHookData ): IWebSocketRequestData {
		return {
			address: this.config.requestAddressFormat.replace( '{feature}', 'data_hook' ),
			messageData: this.getData( data )
		};
	}

	protected getData ( data: IDataHookData ): INGDataHookData {
		return {
			...data,
			partnerCode: this.getPartnerCode(),
			gameCode: this.config.gameCode
		};
	}

	protected getPartnerCode (): string {
		return '';
	}
}

export interface INGDataHookData extends IDataHookData {
	partnerCode: string;
	gameCode: string;
}



