import { Inject } from 'typescript-ioc/es5';
import { AudioTrackName } from 'corengf/src/audio/SoundManager';
import { BackgroundController } from 'corengf/src/components/background/BackgroundController';
import { SlotSoundConfig } from 'slotngf/src/core/config/SlotSoundConfig';
import { SlotBackgroundView } from 'slotngf/src/components/background/SlotBackgroundView';
import { IInitData, IStartPickFeatureData, ITransitionToBaseGameData, ITransitionToFreeSpinsData } from 'slotngf/src/states/SlotStatesDataInterfaces';


export class SlotBackgroundController extends BackgroundController {
	protected view: SlotBackgroundView;

	constructor ( @Inject view: SlotBackgroundView ) {
		super( view );
	}

	public init ( _data: IInitData ): void {
		this.soundManager.play( SlotSoundConfig.backgroundLoop, AudioTrackName.MUSIC, true );
		super.init( _data );
	}

	protected startPickFeature ( _data: IStartPickFeatureData ): void {
		this.soundManager.play( SlotSoundConfig.bonusIntro, AudioTrackName.MUSIC, false, () => {
			this.soundManager.play( SlotSoundConfig.bonusLoop, AudioTrackName.MUSIC, true );
		} );
		this.finishStateAction();
	}

	protected transitionToBaseGame ( _data: ITransitionToBaseGameData ): void {
		this.soundManager.play( SlotSoundConfig.backgroundLoop, AudioTrackName.MUSIC, true );
		this.finishStateAction();
	}

	protected transitionToFreeSpins ( _data: ITransitionToFreeSpinsData ): void {
		if ( this.soundManager.isPlaying( SlotSoundConfig.bonusLoop ) || this.soundManager.isPlaying( SlotSoundConfig.bonusIntro ) ) {
			this.finishStateAction();
			return;
		}

		this.soundManager.play( SlotSoundConfig.bonusIntro, AudioTrackName.MUSIC, false, () => {
			this.soundManager.play( SlotSoundConfig.bonusLoop, AudioTrackName.MUSIC, true );
		} );
		this.finishStateAction();
	}
}


