import * as MiniSignal from 'mini-signals';
import { Singleton } from 'typescript-ioc/es5';
import { BankService } from 'corengf/src/core/BankService';

@Singleton
export class SlotBankService extends BankService {
	private _lines: number = 0;
	public maxLines: number = 0;

	public onLinesUpdatedSignal: MiniSignal;

	get lines (): number {
		return this._lines;
	}

	get totalBet (): number {
		return this.bet * this._lines;
	}

	//TODO: Implement min lines from server
	get availableLines (): Array<number> {
		let availableLines: Array<number> = new Array<number>();
		for ( let i = 1; i <= this.maxLines; i++ ) {
			availableLines.push( i );
		}
		return availableLines;
	}

	//TODO: Implement minimum lines from server
	set lines ( newLines: number ) {
		if ( newLines > 0 && newLines <= this.maxLines ) {
			this._lines = newLines;
			this.onLinesUpdatedSignal.dispatch( this.lines );
		} else {
			console.warn( 'Bank service can not set lines ' + newLines + ' because that line value is not legal' );
		}
	}

	constructor () {
		super();
		this.onLinesUpdatedSignal = new MiniSignal();
	}

	public increaseLine (): void {
		if ( this._lines < this.maxLines ) {
			++this._lines;
		} else {
			console.warn( 'You can\'t increaseLine because line == maxLines' );
		}
	}

	public decreaseLine (): void {
		if ( this._lines > 1 ) {
			--this._lines;
		} else {
			console.warn( 'You can\'t decreaseLine because line == 1' );
		}
	}
}


