import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { SoundManager } from 'corengf/src/audio/SoundManager';
import { ReelModel } from 'slotngf/src/components/reelV2/ReelModel';
import { ReelControllerView } from 'slotngf/src/components/reelV2/ReelControllerView';


export abstract class ReelActionModel {

	public key: string;

	protected reelModel: ReelModel;

	protected view: ReelControllerView;

	@Inject
	protected soundManager: SoundManager;

	/**
	 * True once we get stop spin notification
	 */
	protected stopRequested: boolean;

	public initialize ( reelModel: ReelModel, view: ReelControllerView, onStateChanged: MiniSignal, config?: IReelActionModelConfig ): void {
		this.reelModel = reelModel;
		this.view = view;
		this.setup( config );
		onStateChanged.add( this.onStateChanged.bind( this ) );
	}

	protected setup ( config?: IReelActionModelConfig ): void {
		//
	}

	protected onStateChanged ( stateName: string ): void {
		//
	}

	public abstract run ( data: any ): Promise<any>;
}

export interface IReelActionModelConfig {

}


