import { Inject } from 'typescript-ioc/es5';
import { SlotButtonId } from './SlotButtonId';
import { TsMap } from 'corengf/src/utils/TsMap';
import { SlotStates } from './../../states/SlotStates';
import { ButtonBehaviour, ButtonsStateSet } from 'corengf/src/buttons/ButtonsStateSet';

/**
 * Buttons State Class
 * Set state for the buttons
 */
export class SlotButtonsStateSet extends ButtonsStateSet {

	@Inject
	protected stateNames: SlotStates;
	/**
	 * set states for the buttons
	 */
	protected initStates (): void {
		super.initStates();
		this.buttonStates.set( this.stateNames.spinStop, this.spinStop() );
		this.buttonStates.set( this.stateNames.skip_spinStop, this.skip_spinStop() );
		this.buttonStates.set( this.stateNames.freeSpinsStop, this.freeSpinsStop() );
		this.buttonStates.set( this.stateNames.skip_freeSpinsStop, this.skip_freeSpinsStop() );
		this.buttonStates.set( this.stateNames.freeRespinsStop, this.freeSpinsStop() );
		this.buttonStates.set( this.stateNames.skip_freeRepinsStop, this.skip_freeSpinsStop() );
		this.buttonStates.set( this.stateNames.transitionToJackpot, this.transitionToJackpot() );
	}

	protected default (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.default();
		buttonState.set( SlotButtonId.PAYOUT, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotButtonId.SKIP_STATE, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotButtonId.SKIP_MESSAGE, new ButtonBehaviour( false, false ) );
		buttonState.set( SlotButtonId.START_JACKPOT, new ButtonBehaviour( false, false ) );
		return buttonState;
	}

	protected baseGameIdle (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = super.baseGameIdle();
		buttonState.set( SlotButtonId.PAYOUT, new ButtonBehaviour() );
		buttonState.set( SlotButtonId.RESERVE, new ButtonBehaviour() );
		return buttonState;
	}

	protected transitionToJackpot (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = new TsMap<string, ButtonBehaviour>();
		buttonState.set( SlotButtonId.START_JACKPOT, new ButtonBehaviour( true, true ) );
		return buttonState;
	}

	protected jackpotStart (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = new TsMap<string, ButtonBehaviour>();
		buttonState.set( SlotButtonId.START_JACKPOT, new ButtonBehaviour( false, false ) );
		return buttonState;
	}

	protected spinStop (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = new TsMap<string, ButtonBehaviour>();
		buttonState.set( SlotButtonId.SKIP_STATE, new ButtonBehaviour() );
		return buttonState;
	}

	protected skip_spinStop (): TsMap<string, ButtonBehaviour> {
		let buttonState: TsMap<string, ButtonBehaviour> = new TsMap<string, ButtonBehaviour>();
		buttonState.set( SlotButtonId.SKIP_STATE, new ButtonBehaviour( false, true ) );
		return buttonState;
	}

	protected freeSpinsStop (): TsMap<string, ButtonBehaviour> {
		return this.spinStop();
	}

	protected skip_freeSpinsStop (): TsMap<string, ButtonBehaviour> {
		return this.skip_spinStop();
	}

	protected freeRespinsStop (): TsMap<string, ButtonBehaviour> {
		return this.freeSpinsStop();
	}

	protected skip_freeRepinsStop (): TsMap<string, ButtonBehaviour> {
		return this.skip_freeSpinsStop();
	}

	protected skip_showWinning (): TsMap<string, ButtonBehaviour> {
		return this.skip_spinStop();
	}
}



