import { Inject } from 'typescript-ioc/es5';
import { SlotEvent } from 'slotngf/src/core/SlotEvent';
import { SlotStates } from 'slotngf/src/states/SlotStates';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { UIPanelController } from 'corengf/src/components/ui/components/UIPanelController';
import { MLUIFullScreenPanel } from 'mlui/src/core/MLUIFullScreenPanel';


export class MLUIFullScreenController extends UIPanelController {

	@Inject
	protected stateNames: SlotStates;

	protected view: MLUIFullScreenPanel;

	constructor ( @Inject view: MLUIFullScreenPanel ) {
		super( view );
	}

	public addEventListeners (): void {
		super.addEventListeners();
		this.view.showPopupSignal.add( this.onShowPopup.bind( this ) );
		this.view.hidePopupSignal.add( this.onHidePopup.bind( this ) );
	}

	public onShowPopup (): void {
		GlobalDispatcher.dispatchIntoAllStates( SlotEvent.SHOW_FULL_SCREEN_UI );
	}

	public onHidePopup (): void {
		GlobalDispatcher.dispatchIntoAllStates( SlotEvent.HIDE_FULL_SCREEN_UI );
	}
}


