import { Asset } from 'corengf/src/core/AssetLoader';
import { SlotSoundConfig } from 'slotngf/src/core/config/SlotSoundConfig';
import { BackgroundView } from 'corengf/src/components/background/BackgroundView';


export class SlotBackgroundView extends BackgroundView {

	public getAssetsList (): Array<Asset> {
		return [
			{
				assetKey: SlotSoundConfig.backgroundLoop,
				assetURL: 'assets/sound/game/Background_Loop.mp3'
			},
			{
				assetKey: SlotSoundConfig.bonusIntro,
				assetURL: 'assets/sound/game/Bonus_Intro.mp3'
			},
			{
				assetKey: SlotSoundConfig.bonusLoop,
				assetURL: 'assets/sound/game/Bonus_Loop.mp3'
			}
		];
	}
}


