import { Inject } from 'typescript-ioc/es5';
import { AnimationView } from './AnimationView';
import { CoreState } from 'corengf/src/states/CoreState';
import { StateMachine } from 'corengf/src/core/StateMachine';
import { LazyCompController } from 'corengf/src/mvc/LazyCompController';
import { AnimationViewConfig, IAnimationViewConfig } from './AnimationViewConfig';


export class AnimationViewController extends LazyCompController {

	protected view: AnimationView;

	protected context: any;

	constructor ( @Inject view: AnimationView, lazyGroupIndex?: number ) {
		super( view, lazyGroupIndex );
		this.context = this;
	}

	public init ( data?: any ): void {
		if ( this.view != undefined ) {
			this.view.initialize( this.gameConfig.getConfig<IAnimationViewConfig>( AnimationViewConfig ) );
		}

		this.finishStateAction();
	}

	public setState ( state: CoreState ): boolean {
		this.view.playAnimation( state.stateName, this.context );
		return super.setState( state );
	}

	public skipState ( state: CoreState ): void {
		this.view.playAnimation( StateMachine.PREFIX_SKIP + state.stateName, this.context );
		super.skipState( state );
	}
}


