import { Graphics, Point } from 'pixi.js';
import * as MiniSignal from 'mini-signals';
import { Actions } from 'corengf/src/utils/Actions';
import { CompView } from 'corengf/src/mvc/CompView';
import { ViewportAlign } from 'corengf/src/display/Viewport';
import { Button, IButtonConfig } from 'corengf/src/elements/canvas/button/Button';
import { ITextButtonConfig, TextButton } from 'corengf/src/elements/canvas/button/TextButton';
import { WebTextField, IWebTextFieldConfig } from 'corengf/src/elements/canvas/text/WebTextField';
import { DemoActions } from './ForcingSpinController';
import { IDemoConfig } from './../../core/config/IDemoConfig';


export class ForcingSpinView extends CompView {

	protected config: IDemoConfig;
	protected background: Graphics;
	protected title: WebTextField;
	protected closeButton: Button;
	public clickSignal: MiniSignal = new MiniSignal();
	public hidePopupSignal: MiniSignal = new MiniSignal();

	protected forcingSpinButtonAction = '';

	public showDemoPanelstate = true;


	protected createElements ( config: IDemoConfig ): void {
		this.config = config;

		this.forcingSpinButtonAction = Actions.CLICK_ACTION_PREFIX + DemoActions.SHOW_DEMO_PANEL;

		this.background = this.getFillBg();
		this.background.visible = false;
		this.mainContainer.addChildAt( this.background, 0 );

		this.createSelectPanel( config );
		this.addCloseButton( config.closeButton );
		this.addTitle( config.title );
		this.setDemoPanelVisible( false );
	}

	protected setDemoPanelVisible ( show: boolean ): void {
		this.mainContainer.visible = true;
		this.mainContainer.children.forEach( element => {
			element.visible = show;
		} );
	}

	protected addTitle ( data: IWebTextFieldConfig ): void {
		this.title = new WebTextField( data );
		this.mainContainer.addChild( this.title );
	}

	protected addCloseButton ( data: IButtonConfig ): void {
		this.closeButton = new Button( data );
		this.closeButton.anchor = new Point( 0.5, 0.5 );
		this.closeButton.elementSignal.add( this.hidePopup.bind( this ) );
		this.addChild( this.closeButton );
	}

	protected createSelectPanel ( config: IDemoConfig ): void {
		let selectorButtonConfig = config.selectorButtonConfig;
		let defaultButtonConfig: ITextButtonConfig = config.selectorButtonConfig.selectorButton;
		let selectorKeys: Array<string> = config.selectorData.keys();

		let xNum = 0;
		let yNum = 0;
		for ( let index = 0; index < selectorKeys.length; index++ ) {
			const name = selectorKeys[ index ];
			let buttonConfig: ITextButtonConfig = defaultButtonConfig;
			buttonConfig.action = name;
			buttonConfig.text.name = name;
			buttonConfig.text.key = name;
			buttonConfig.scale = new Point( selectorButtonConfig.scaleX, selectorButtonConfig.scaleY );
			let posX = selectorButtonConfig.startX + selectorButtonConfig.widthX * xNum;
			let posY = selectorButtonConfig.startY + selectorButtonConfig.heightY * yNum;
			buttonConfig.position.x = posX;
			buttonConfig.position.y = posY;
			this.addDemeButton( buttonConfig );

			xNum++;
			if ( xNum % selectorButtonConfig.buttonOffset == 0 ) {
				yNum++;
				xNum = 0;
			}
		}
	}

	protected addDemeButton ( data: ITextButtonConfig ): void {
		let button = new TextButton( data );
		button.anchor = new Point( 0.5, 0.5 );
		button.interactive = !PRODUCTION;
		button.elementSignal.add( this.onClickHandler.bind( this ) );
		this.mainContainer.addChild( button );
	}

	protected resetSelectorButtonsPosition (): void {
		let xNum = 0;
		let yNum = 0;
		const width = this.config.selectorButtonConfig.widthX;
		const height = this.config.selectorButtonConfig.heightY;
		const offset = this.config.selectorButtonConfig.buttonOffset;
		const startX = ( this.viewport.width / 2 ) - ( offset / 2 ) * width + width / 2;
		const startY = ( this.viewport.height / 2 ) - ( this.config.selectorData.size / offset / 2 * height );

		this.mainContainer.children.forEach( element => {
			if ( element.name == this.config.selectorButtonConfig.selectorButton.name ) {
				element.position.x = startX + width * xNum;
				element.position.y = startY + height * yNum;

				xNum++;
				if ( xNum % offset == 0 ) {
					yNum++;
					xNum = 0;
				}
			}
		} );
	}

	protected onClickHandler ( buttonData: any, data?: any ): void {
		this.clickSignal.dispatch( buttonData.action, data );
	}

	protected getFillBg (): Graphics {
		let leftTopPoint: Point = this.viewport.getAlignedPoint( ViewportAlign.LEFT_TOP );
		let g: Graphics = new Graphics();
		g.beginFill( 0, 0.8 );
		g.drawRect( leftTopPoint.x, leftTopPoint.y, this.viewport.width, this.viewport.height );
		g.endFill();
		return g.clone();
	}

	protected layoutElements (): void {
		super.layoutElements();
		this.resetSelectorButtonsPosition();
		this.background.width = this.viewport.width;
		this.background.height = this.viewport.height;
	}

	public showDemoPanel (): void {
		this.showDemoPanelstate = false;
		this.background.visible = true;
		this.setDemoPanelVisible( true );
		this.forcingSpinButtonAction = Actions.CLICK_ACTION_PREFIX + DemoActions.HIDE_DEMO_PANEL;
	}

	public hideDemoPanel (): void {
		this.showDemoPanelstate = true;
		this.background.visible = false;
		this.setDemoPanelVisible( false );
		this.forcingSpinButtonAction = Actions.CLICK_ACTION_PREFIX + DemoActions.SHOW_DEMO_PANEL;
	}

	public hidePopup (): void {
		this.hideDemoPanel();
		this.hidePopupSignal.dispatch();
	}

	public setInteractive ( value: boolean, inValidState: boolean ): void {
		if ( !PRODUCTION && value && inValidState ) {
			this.mainContainer.interactive = true;
		} else {
			this.mainContainer.interactive = false;
		}
	}
}


