import { CoreWebElement, ICoreWebElementConfig } from 'corengf/src/elements/webpage/CoreWebElement';

/**
 * image slideshow block
 * switch several pictures with slideshow effect
 */
export class WebImageSlideshowElement extends CoreWebElement {

	public static elementType: string = 'WEB_IMAGE_SLIDESHOW';

	public createElement<T extends ICoreWebElementConfig> ( config: IimageSlideshowConfig<T> ): IimageSlideshowConfig<T> {
		return config;
	}
}

/**
 * @param pages config page layout use webElementBlock
 * @param dwellTime image dwell time, default 4 sec
 * @param transformTime image tansform time, default 1.5 sec
 */
export interface IimageSlideshowConfig<T extends ICoreWebElementConfig> extends ICoreWebElementConfig {
	pages: Array<T>;
	TransitionEffectType: TransitionEffectType;
	dwellTime?: number;
	transformTime?: number;
}

export enum TransitionEffectType {
	FADE_IN = 'FADE_IN',
	RIGHT_TO_LEFT = 'RIGHT_TO_LEFT'
}


