import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { UIPanelController } from 'corengf/src/components/ui/components/UIPanelController';
import { MLUISlotPanelView } from 'mlui/src/components/slotPanel/MLUISlotPanelView';
import { MLUISlotPanelConfig, SlotButton } from 'mlui/src/components/slotPanel/MLUISlotPanelConfig';


export class MLUISlotPanelController extends UIPanelController {

	protected view: MLUISlotPanelView;
	public showBetPanelEventSignal: MiniSignal = new MiniSignal();
	public showSpinSettingsPanelEventSignal: MiniSignal = new MiniSignal();
	public showMenuPanelEventSignal: MiniSignal = new MiniSignal();

	public betIncraseSignal: MiniSignal = new MiniSignal();
	public betDecraseSignal: MiniSignal = new MiniSignal();

	constructor ( @Inject view: MLUISlotPanelView ) {
		super( view );
	}

	public init (): void {
		let config = this.gameConfig.getConfig<MLUISlotPanelConfig>( MLUISlotPanelConfig );

		this.view.initialize( config );

		for ( const member in SlotButton ) {
			if ( SlotButton[ member ] ) {
				const buttonConfig = config[ SlotButton[ member ] ];
				this.setButtonActionEventListeners( buttonConfig );
			}
		}
		this.finishStateAction();
	}

	protected action_showBetOptions (): void {
		this.showBetPanelEventSignal.dispatch();
	}

	protected action_showAutoPlayPanel (): void {
		this.showSpinSettingsPanelEventSignal.dispatch();
	}

	protected action_showMenuOptions (): void {
		this.showMenuPanelEventSignal.dispatch();
	}

	protected action_betIncrease (): void {
		this.view.onBetIncrease();
		this.betIncraseSignal.dispatch();
	}

	protected action_betDecrease (): void {
		this.view.onBetDecrease();
		this.betDecraseSignal.dispatch();
	}

	protected baseGameIdle (): void {
		this.view.setBetButtonEnable();
		this.finishStateAction();
	}

	protected showWinning (): void {
		this.view.setBetButtonEnable();
		this.finishStateAction();
	}
}


