import * as MiniSignal from 'mini-signals';
import { TsMap } from 'corengf/src/utils/TsMap';
import { ReelSet } from 'slotngf/src/components/reels/logic/ReelsConfig';
import { IFeatureDetail } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { ReelStripModel, SymbolData } from 'slotngf/src/components/reelV2/ReelStripModel';
import { ISymbolDefinition, ISymbolConfig, SymbolPosition, SpinDirection } from 'slotngf/src/core/config/IReelsConfig';

export class ReelModel {

	public countTopExtraSymbol: number = 1;
	public countBottomExtraSymbol: number = 1;

	protected symbolDefinition: ISymbolDefinition;

	public get generalSymbolConfig (): ISymbolConfig {
		return this.symbolDefinition.generalSymbol;
	}

	private _reelStripModelsMap: TsMap<number, Array<ReelStripModel>> = new TsMap<number, Array<ReelStripModel>>();
	public get reelStripModelsMap (): TsMap<number, Array<ReelStripModel>> {
		return this._reelStripModelsMap;
	}

	public get currentReelStrips (): Array<ReelStripModel> {
		return this.reelStripModelsMap.get( this.currentReelSetIndex );
	}

	private _currentReelSetIndex: number;
	public get currentReelSetIndex (): number {
		return this._currentReelSetIndex;
	}
	public set currentReelSetIndex ( value: number ) {
		this._currentReelSetIndex = value;
	}

	private _currentReelIndex: number;
	public get currentReelIndex (): number {
		return this._currentReelIndex;
	}

	public set currentReelIndex ( value: number ) {
		this._currentReelIndex = value;
	}

	private _isLegalResponse: boolean = true;
	public get isLegalResponse (): boolean {
		return this._isLegalResponse;
	}

	public set isLegalResponse ( value: boolean ) {
		this._isLegalResponse = value;
	}

	private _isFastStopPending: boolean = false;
	public get isFastStopPending (): boolean {
		return this._isFastStopPending;
	}

	public set isFastStopPending ( value: boolean ) {
		if ( value && this.onFastStopReels ) {
			this.onFastStopReels.dispatch();
		}
		this._isFastStopPending = value;
	}

	private _featureData: IFeatureDetail;

	public get featureData (): IFeatureDetail {
		return this._featureData;
	}

	public set featureData ( value: IFeatureDetail ) {
		this._featureData = value;
	}

	private _lastSymbolsOutcome: Array<Array<string>>;

	public get lastSymbolsOutcome (): Array<Array<string>> {
		return this._lastSymbolsOutcome;
	}

	public set lastSymbolsOutcome ( value: Array<Array<string>> ) {
		this._lastSymbolsOutcome = value;
	}

	private _currentSymbolsOutcome: Array<Array<string>>;

	public get currentSymbolsOutcome (): Array<Array<string>> {
		return this._currentSymbolsOutcome;
	}

	public set currentSymbolsOutcome ( value: Array<Array<string>> ) {
		this._currentSymbolsOutcome = value;
	}

	public scattersDropped: Array<SymbolPosition> = [];

	public isShaking: boolean = false;

	public onTweenSymbolComplete: MiniSignal = new MiniSignal();

	public onAllReelStopped: MiniSignal = new MiniSignal();

	public onFastStopReels: MiniSignal = new MiniSignal();

	constructor () {
		this.currentReelSetIndex = ReelSet.BASE;
		this.currentReelIndex = 0;
	}

	public initialize ( config: ReelModelData ): void {
		this.countTopExtraSymbol = ( config.countTopExtraSymbol !== undefined ) ? config.countTopExtraSymbol : 1;
		this.countBottomExtraSymbol = ( config.countBottomExtraSymbol !== undefined ) ? config.countBottomExtraSymbol : 1;
		this.symbolDefinition = config.symbolDefinition;
	}

	public setReelStripModel ( reelSetIndex: number, reelStripModels: Array<ReelStripModel> ): void {
		this.reelStripModelsMap.set( reelSetIndex, reelStripModels );
	}


	private nextSymbol ( reelId: number ): string {
		return this.currentReelStrips[ reelId ].nextSymbol();
	}

	public getSymbolHeightBySymbolName ( name: string ): number {
		return this.currentReelStrips[ 0 ].getSymbolHeightByName( name );
	}

	public shouldMoveLastSymbolToTop ( reelId: number, spinDirection: SpinDirection ): boolean {
		const reelSymbols = this.currentReelStrips[ reelId ].reelSymbols;

		const symbol = reelSymbols[ ( spinDirection == SpinDirection.SpinDown ) ? reelSymbols.length - 1 : 0 ];

		if ( spinDirection == SpinDirection.SpinDown && symbol.posY >= this.currentReelStrips[ reelId ].lowerBound ) {
			return true;
		} else if ( spinDirection == SpinDirection.SpinUp && symbol.posY <= this.currentReelStrips[ reelId ].upperBoundOfTopExtraY ) {
			return true;
		} else {
			return false;
		}
	}

	public createNextSymbol ( reelId: number, spinDirection: SpinDirection, symbolName?: string ): SymbolData {
		const nextSymbolName = symbolName || this.nextSymbol( reelId );
		let data: SymbolData;
		if ( spinDirection == SpinDirection.SpinDown ) {
			data = new SymbolData( nextSymbolName, this.currentReelStrips[ reelId ].reelSymbols[ 0 ].posY - this.getSymbolHeightBySymbolName( nextSymbolName ) );
			this.currentReelStrips[ reelId ].reelSymbols.pop();
			this.currentReelStrips[ reelId ].reelSymbols.unshift( data );
		} else {
			let posY = this.currentReelStrips[ reelId ].reelSymbols[ this.currentReelStrips[ reelId ].reelSymbols.length - 1 ].posY;
			data = new SymbolData( nextSymbolName, posY + this.getSymbolHeightBySymbolName( nextSymbolName ) );
			this.currentReelStrips[ reelId ].reelSymbols.shift();
			this.currentReelStrips[ reelId ].reelSymbols.push( data );
		}
		return data;
	}

	public getReelSymbols ( reelId: number ): number {
		return this.currentReelStrips[ reelId ].reelSymbols.length;
	}

	public getSymbolDataByIndex ( reelId: number, symbolIndex: number ): SymbolData {
		return this.currentReelStrips[ reelId ].reelSymbols[ symbolIndex ];
	}

	public reset (): void {
		this.isFastStopPending = false;
		this.currentReelStrips.forEach( ( reelStripModel, index ) => {
			reelStripModel.reset();
		} );
		this.scattersDropped = [];
	}

	public setSymbolOutcome ( symbolOutcome: Array<Array<string>> ): void {
		this.currentReelStrips.forEach( ( reelStripModel, index ) => {
			reelStripModel.symbolsOutcome = symbolOutcome[ index ];
		} );
	}

	public setScatterDropped ( symbolOutcome: Array<Array<string>> ): void {
		symbolOutcome.forEach( ( reel, reelIndex ) => {
			reel.forEach( ( symbol, symbolIndex ) => {
				if ( symbol === 'SCATTER' ) {
					const position: SymbolPosition = {
						reelIndex,
						symbolIndex
					};
					this.scattersDropped.push( position );
				}
			} );
		} );
	}
}

export interface ReelModelData {
	countBottomExtraSymbol: number;
	countTopExtraSymbol: number;
	symbolDefinition: ISymbolDefinition;
}


