import { PointLike, Point } from 'pixi.js';
import { ToggleButton } from 'corengf/src/elements/canvas/toggle/ToggleButton';
import { WebTextField } from 'corengf/src/elements/canvas/text/WebTextField';
import { ITextButtonConfig } from 'corengf/src/elements/canvas/button/TextButton';
import { LocalButtonsController } from 'corengf/src/buttons/LocalButtonsController';


export class TextToggleButton extends ToggleButton {
	protected textField: WebTextField;

	constructor ( data: ITextButtonConfig, localButtonsController?: LocalButtonsController ) {
		super( data, localButtonsController );
		if ( data.text != undefined ) {
			let textData = data.text;
			this.textField = new WebTextField( textData );
			let inverseScaleX = 1 / this.scale.x;
			let inverseScaleY = 1 / this.scale.y;
			this.textField.scale.set( inverseScaleX, inverseScaleY );
			if ( textData.position == undefined ) {
				this.textField.anchor = new Point( 0.5, 0.5 );
				this.textField.position.set( ( this.activeElement.width ) / 2, ( this.activeElement.height ) / 2 - 2 );
			}
			this.addChild( this.textField );
		}
		if ( data.selected ) {
			this.selected = true;
		}

		this.setAction( data.action );
	}

	public setText ( text: string ) {
		this.textField.text = text;
	}

	public setLabelVisible ( visible: boolean ) {
		this.textField.visible = visible;
	}

	set anchor ( anchorPoint: PointLike ) {
		this.activeElement.anchor.set( anchorPoint.x, anchorPoint.y );
		this.textField.anchor = new Point( anchorPoint.x, anchorPoint.y );
		this.updateHitArea( anchorPoint );
	}

	public updateAttributes ( config: ITextButtonConfig ): void {
		super.updateAttributes( config );

		if ( this.textField && config.text ) {
			if ( config.text.position ) {
				this.textField.position.x = config.text.position.x;
				this.textField.position.y = config.text.position.y;
			}
			if ( config.text.scale ) {
				this.textField.scale.x = config.text.scale.x;
				this.textField.scale.y = config.text.scale.y;
			}
		}
	}
}



