export class WinLine {

	/**
	 * A 2D array that presents the winning symbol index.
	 *
	 * @type {Array<Array<number>>}
	 * @memberof WinLine
	 */
	public reelPositions: Array<Array<number>>;

	/**
	 * A 2D array that presents the winning symbol information.
	 *
	 *
	 * @type {Array<Array<WinningCombination>>}
	 * @memberof WinLine
	 */
	public winningCombinations: Array<Array<WinningCombination>>;
	public line: number;
	public payout: number;
	public wildMultiplier?: Array<WildMultiplier>;
	public symbol: string;
	public rightToLeft: boolean;
}

export class WinLineDisplayData extends WinLine {
	public winTable: Array<Array<boolean>>;
	public currentState: string;
}

export class WildMultiplier {
	public reel_index: number;
	public symbol_index: number;
	public multiplier: number;
}

export class WinningCombination {

	/**
	 * Presents the nth symbol in the reel
	 *
	 * @type {number}
	 * @memberof WinningCombination
	 */
	public index: number;

	public symbol: string;
}


