import * as MiniSignal from 'mini-signals';
import { Container } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Asset } from 'corengf/src/core/AssetLoader';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import { WinLineDisplayData } from 'slotngf/src/components/lines/WinLine';
import { SlotSoundConfig } from 'slotngf/src/core/config/SlotSoundConfig';
import { DHBReelStripView } from 'game/src/components/reels/DHBReelStripView';
import { SymbolAnimationPostfix } from 'slotngf/src/components/reels/ReelsEnums';
import { ReelControllerView } from 'slotngf/src/components/reelV2/ReelControllerView';
import { SymbolObject, OverlayType } from 'slotngf/src/components/reelV2/ReelStripOverlay';

export class DHBReelControllerView extends ReelControllerView {

	protected debugAnimationTimer: SimpleTimer;
	public tutorialOutroCompleteSignal: MiniSignal = new MiniSignal();

	protected isAnticipation: boolean = false;
	protected anticipationReelId: number = 0;

	protected get currentReelView (): Array<DHBReelStripView> {
		return this.reelViewStripMap.get( this.currentReelSetIndex );
	}

	protected reelViewStripMap: TsMap<number, Array<DHBReelStripView>> = new TsMap<number, Array<DHBReelStripView>>();

	protected createReelStripView (): DHBReelStripView {
		return Container.get( DHBReelStripView );
	}

	protected appendBasicOverlay (): void {
		this.appendOverlay( {
			name: 'WILD',
			overlayType: OverlayType.SYMBOL,
			symbolConfig: this.reelConfig.symbolDefinition,
			size: 12,
		} );
		this.appendOverlay( {
			overlayType: OverlayType.SYMBOL,
			rangeSymbols: this.reelConfig.reelDefinition.rangeSymbols,
			symbolConfig: this.reelConfig.symbolDefinition
		} );
		this.baseOverlayIndex = this.overlaySize - 1;
	}


	public showWinLine ( winItemsData: WinLineDisplayData ): void {
		this.deleteAllAnimations();

		let isWildMultiplier: boolean = false;

		winItemsData.winTable.forEach( ( reel, reelIndex ) => {
			if ( reel === null || reel.length === 0 ) {
				return;
			}
			reel.forEach( ( value, symbolIndex ) => {
				if ( !value ) { // skip lose symbol
					return;
				}

				// if the symbol type is wild and wild_multiplier feature, we need change wild animation.
				isWildMultiplier = false;
				const symbolId: number = symbolIndex + this.reelModel.countTopExtraSymbol;
				if ( winItemsData.wildMultiplier != undefined && winItemsData.wildMultiplier.length > 0 ) {
					winItemsData.wildMultiplier.forEach( ( feature ) => {
						let wildId: number = feature.symbol_index + this.reelModel.countTopExtraSymbol;

						if ( feature.reel_index == reelIndex && wildId == symbolId ) { // is wild Symbol
							isWildMultiplier = true;
						}
					} );
				}

				if ( !isWildMultiplier ) {
					const displaylayers = this.getWinDisplayLayers();

					const objectArray: Array<{ layer: number, object: SymbolObject }> = [];

					displaylayers.forEach( ( layer ) => {
						const object = this.getSymbolObject( layer, { reelIndex, symbolIndex } );

						if ( object ) {
							objectArray.push( { layer, object } );
						}
					} );

					if ( objectArray.length > 0 ) {
						objectArray.forEach( ( item ) => {
							const position = this.getActualPosition( item.layer, { reelIndex, symbolIndex } );
							this.playOverlayAnimation( SymbolAnimationPostfix.WIN, item.layer, position );
						} );
					} else {
						const position = this.getActualPosition( this.baseOverlayIndex, { reelIndex, symbolIndex } );
						this.playOverlayAnimation( SymbolAnimationPostfix.WIN, this.baseOverlayIndex, position );
					}


					// this.completedAnimationCounter++;
				}
			} );
		} );

		if ( this.completedAnimationCounter == 0 ) { // when we do not symbols animation
			this.debugAnimationTimer = new SimpleTimer( 1500, this.dispatchAllSymbolsAnimationComplete.bind( this ) );
		}
	}

	protected playWildMultiplierAnimation ( reelIndex: number, symbolIndex: number, multiplier: number ): void {
		let wildType: SymbolAnimationPostfix;
		switch ( multiplier ) {
			case 2: wildType = SymbolAnimationPostfix.WILD_X2; break;
			case 3: wildType = SymbolAnimationPostfix.WILD_X3; break;
			case 4: wildType = SymbolAnimationPostfix.WILD_X4; break;
		}

		if ( wildType != undefined ) {
			this.currentReelView[ reelIndex ].hideSymbol( symbolIndex );
			this.playOverlayAnimation( wildType, this.baseOverlayIndex, {
				reelIndex,
				symbolIndex
			} );
			// this.completedAnimationCounter++;
		}
	}

	public hideOverlay ( layer: number ): void {
		this.reelOverlayMap.get( layer ).visible = false;
	}
	public showOverlay ( layer: number ): void {
		this.reelOverlayMap.get( layer ).visible = true;
	}

	public allSymoblHide (): void {
		this.currentReelSet.forEach( ( reel, reelIndex ) => {
			for ( let symbolIndex = 0; symbolIndex < reel.symbolsOnReel; symbolIndex++ ) {
				this.hideSymbolFromCurrentView( reelIndex, symbolIndex );
			}
		} );
	}

	public tutorialOutroSymbolShow (): void {

		let count = 0;
		let ms = 0;
		for ( let i = 0; i < 4; i++ ) {
			let pos = 4;
			for ( let j = 0; j < 5; j++ ) {
				if ( i % 2 == 0 ) {
					new SimpleTimer( ms, () => {
						this.showSymbolFromCurrentView( j, i );
					} );
				} else {
					let index = pos - j;
					new SimpleTimer( ms, () => {
						this.showSymbolFromCurrentView( index, i );
					} );
				}
				count++;
				ms = 140 * count;
			}
		}
		new SimpleTimer( ms, () => {
			this.tutorialOutroCompleteSignal.dispatch();
		} );


	}

	public setSymbolsScaleFromCurrentView ( reelIndex: number, isAnticipation: boolean ): void {


		if ( !isAnticipation ) {
			this.currentReelView[ reelIndex ].setAllSymbolsScale( { x: 1, y: 1 } );
			this.isAnticipation = false;
		} else {
			this.isAnticipation = true;
			this.anticipationReelId = reelIndex;
		}
	}

	public tweenSymbolsUpdate ( reelIndex: number, symbolIndex: number, y: number ): void {
		this.currentReelView[ reelIndex ].tweenSymbolsUpdate( symbolIndex, y );

		if ( this.isAnticipation && this.anticipationReelId == reelIndex ) {
			let size = { x: 1, y: 1 };
			switch ( symbolIndex ) {
				case 1:
					size = { x: 1.1, y: 1.1 };
					break;
				case 2:
					size = { x: 1.4, y: 1.4 };
					break;
				case 3:
					size = { x: 1.5, y: 1.5 };
					break;
				case 4:
					size = { x: 1.1, y: 1.1 };
					break;
				default:
					break;
			}

			this.currentReelView[ reelIndex ].setSymbolsScale( symbolIndex, size );
		}
	}

	public getAssetsList (): Array<Asset> {

		let assetsList: Array<Asset> = new Array<Asset>();

		for ( let i = 1; i <= 5; i++ ) {
			assetsList.push( {
				assetKey: 'Scatter0' + i,
				assetURL: 'assets/sound/game/Scatter0' + i + '.mp3'
			} );
			assetsList.push( {
				assetKey: 'ReelStop0' + i,
				assetURL: 'assets/sound/game/ReelStop0' + i + '.mp3'
			} );
		}

		assetsList.push( ...[
			{
				assetKey: 'IntroAmbiance',
				assetURL: 'assets/sound/game/IntroAmbiance.mp3'
			},
			{
				assetKey: 'WildLockAudio',
				assetURL: 'assets/sound/game/WildLock.mp3'
			},
			{
				assetKey: 'tutorialOutroSymbolShow',
				assetURL: 'assets/sound/game/tutorialOutroSymbolShow.mp3'
			},
			{
				assetKey: SlotSoundConfig.reelSpin,
				assetURL: 'assets/sound/game/' + SlotSoundConfig.reelSpin + '.mp3'
			},
			{
				assetKey: 'symbols',
				assetURL: 'assets/symbols/symbols.json'
			},
			{
				assetKey: 'symbolsBlur',
				assetURL: 'assets/symbols/symbolsBlur.json'
			},
			{
				assetKey: 'symbolsLose',
				assetURL: 'assets/symbols/symbolsLose.json'
			},
			{
				assetKey: 'symbolAnimationSCA_Drop',
				assetURL: 'assets/symbols/symbolAnimationSCADrop.json'
			},
			{
				assetKey: 'symbolAnimationSCA',
				assetURL: 'assets/symbols/symbolAnimationSCA.json'
			},
			{
				assetKey: 'symbolAnimationW',
				assetURL: 'assets/symbols/symbolAnimationW.json'
			}
		] );

		return assetsList;
	}
}


