import * as MiniSignal from 'mini-signals';
import { Helper } from 'corengf/src/utils/Helper';
import { Graphics, Sprite, Texture, BLEND_MODES } from 'pixi.js';
import { Animation } from 'corengf/src/elements/canvas/animation/Animation';
import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { CoreElement, ICoreElementConfig } from 'corengf/src/elements/canvas/CoreElement';
import { ILoadingBar } from './ILoadingBar';


export class LoadingBar extends CoreElement implements ILoadingBar {

	public finishedLoadingSignal: MiniSignal;

	protected emptyBar: Sprite;
	protected fullBar: Sprite;
	protected loadAnimation: PIXI.extras.AnimatedSprite;
	protected completeAnimation: PIXI.extras.AnimatedSprite;
	protected fullMask: Graphics;

	protected currentPercentage: number = 0;

	protected initElement ( config: ILoadingBarConfig ): void {
		this.finishedLoadingSignal = new MiniSignal();

		this.createEmptyBar( config.emptyAssetName );
		this.createFullBar( config.fullAssetName );
		this.createLoadAnimation( config.animationAssets );
		this.createCompleteAnimation( config.completeAnimationAssets );

		if ( !this.loadAnimation ) {
			this.fullBar.mask = this.fullMask;
		}
	}

	protected createEmptyBar ( emptyAsset: string ): void {
		if ( emptyAsset == undefined || emptyAsset == Helper.EMPTY ) {
			return;
		}
		this.emptyBar = new Sprite( Texture.fromFrame( emptyAsset ) );
		this.addChild( this.emptyBar );
	}

	protected createFullBar ( fullAsset: string ): void {
		if ( fullAsset == undefined || fullAsset == Helper.EMPTY ) {
			return;
		}
		this.fullBar = new Sprite( Texture.fromFrame( fullAsset ) );
		this.addChild( this.fullBar );

		this.fullMask = new Graphics();
		this.fullMask.beginFill( 0 );
		this.fullMask.drawRect( this.fullBar.x, this.fullBar.y, this.fullBar.width, this.fullBar.height );
		this.fullMask.endFill();
		let newX = this.fullBar.x - this.fullMask.x;
		let newY = this.fullBar.y - this.fullMask.y;
		this.fullMask.position.set( newX, newY );
		this.addChild( this.fullMask );
	}

	protected createLoadAnimation ( animationAssets: Array<string> ): void {
		if ( animationAssets == undefined ) {
			return;
		}
		this.loadAnimation = Animation.createFrameAnimation( animationAssets );
		this.loadAnimation.loop = true;
		this.loadAnimation.visible = false;
		this.loadAnimation.animationSpeed = 0.25;
		this.addChildAt( this.loadAnimation, 1 );
		this.loadAnimation.mask = this.fullMask;
		this.loadAnimation.play();
	}

	protected createCompleteAnimation ( completeAnimationAssets: Array<string> ): void {
		if ( completeAnimationAssets == undefined ) {
			return;
		}
		this.completeAnimation = Animation.createFrameAnimation( completeAnimationAssets );
		this.completeAnimation.loop = false;
		this.completeAnimation.visible = false;
		this.completeAnimation.animationSpeed = 0.25;
		this.completeAnimation.blendMode = BLEND_MODES.ADD;
		this.completeAnimation.onComplete = () => {
			this.completeAnimation.visible = false;
			this.finishedLoadingSignal.dispatch();
		};
		this.addChild( this.completeAnimation );
	}

	public updateAttributes ( config: ILoadingBarConfig ): void {
		if ( config == undefined ) {
			return;
		}
		super.updateAttributes( config );

		if ( this.fullBar && config.fullAssetName ) {
			this.fullBar.texture = Texture.fromFrame( config.fullAssetName );
			this.fullMask.width = this.fullBar.width;
			this.fullMask.height = this.fullBar.height;
			this.update( this.currentPercentage );
		}

		if ( config.anchor ) {
			this.setAnchor( config.anchor );
		}
	}

	public update ( percentage: number ): void {
		this.currentPercentage = percentage;
		if ( this.loadAnimation != undefined ) {
			this.loadAnimation.visible = percentage != undefined && percentage != 0;
		} else {
			this.fullBar.visible = percentage != 0;
		}
		let newX = this.fullBar.x - ( this.fullBar.width + ( this.fullBar.width * this.fullBar.anchor.x ) ) + ( percentage * this.fullMask.width );
		let newY = this.fullBar.y - ( this.fullBar.height * this.fullBar.anchor.y );
		this.fullMask.position.set( newX, newY );
	}

	public playCompleteAnimation (): void {
		if ( this.completeAnimation == undefined ) {
			this.finishedLoadingSignal.dispatch();
		} else {
			this.completeAnimation.visible = true;
			this.completeAnimation.play();
		}
	}

	public dispose (): void {
		this.emptyBar.destroy();
		this.fullBar.destroy();

		this.emptyBar = undefined;
		this.fullBar = undefined;

		this.fullMask.destroy();
		this.fullMask = undefined;

		if ( this.completeAnimation != undefined ) {
			this.completeAnimation.destroy();
			this.completeAnimation = undefined;
		}

		this.finishedLoadingSignal.detachAll();
		this.finishedLoadingSignal = undefined;
	}

	protected setAnchor ( anchor: IPoint ): void {
		this.children.forEach( ( child: Sprite ) => {
			if ( this.isSprite( child ) ) {
				child.anchor.set( anchor.x, anchor.y );
			}
		} );
	}

	protected isSprite ( displayObject: Sprite | Graphics ): displayObject is Sprite {
		return ( <Sprite> displayObject ).anchor !== undefined;
	}
}

export interface ILoadingBarConfig extends ICoreElementConfig {
	alpha?: number;
	fullAssetName: string;
	emptyAssetName?: string;
	animationAssets?: Array<string>;
	completeAnimationAssets?: Array<string>;
}


