import { particles, Texture } from 'pixi.js';
import { SlotCoinShowerView } from 'slotngf/src/components/coinshower/SlotCoinShowerView';

export class DHBCoinShowerView extends SlotCoinShowerView {

	protected envelopeShower: particles.Emitter;

	protected createElements ( config: any ): void {
		this.createEnvelopeShower();
		super.createElements( config );
	}

	protected createEnvelopeShower (): void {
		this.envelopeShower = this.createParticleAnimation();
		this.envelopeShower.frequency = 0.1;
		this.envelopeShower.spawnPos.x = 0;
		this.envelopeShower.spawnPos.y = -50;
	}

	protected createParticleAnimation (): particles.Emitter {
		let imagesName: Array<string> = [];
		imagesName.push( 'redEnvelopeSprite.png' );

		let particleResource = PIXI.loader.resources[ 'redEnvelopeParticles' ].data;
		let particleImages: Array<Texture> = new Array<Texture>();

		let container = new PIXI.particles.ParticleContainer();
		this.addChild( container );
		imagesName.forEach( ( image: string ) => {
			particleImages.push( Texture.fromFrame( image ) );
		} );

		let animation = new particles.Emitter(
			container,
			particleImages,
			particleResource
		);
		animation.emit = false;
		animation.autoUpdate = true;

		return animation;
	}

	public startEnvelopeShower (): void {
		this.envelopeShower.emit = true;
	}

	public endEnvelopeShower (): void {
		this.envelopeShower.emit = false;
		this.envelopeShower.cleanup();
	}

	public portraitLayout (): void {
		super.portraitLayout();
		this.envelopeShower.spawnPos.x = 0;
	}

	public landscapeLayout (): void {
		super.landscapeLayout();
		this.envelopeShower.spawnPos.x = 250;
	}
}



