import { Orientation } from 'corengf/src/display/Viewport';
import { NumberDisplayMode } from 'corengf/src/utils/MoneyFormatter';
import { UIBankPanelView } from 'corengf/src/components/ui/components/bank/UIBankPanelView';
import { MLUIBankConfig } from './MLUIBankConfig';
import { ISprite, Sprite } from 'corengf/src/elements/canvas/sprite/Sprite';


export class MLUIBankPanel extends UIBankPanelView {

	protected config: MLUIBankConfig;

	protected currentPadding: number;
	protected currentBalance: number;
	protected currentBet: number;
	protected bankBackgroundBar: Sprite;

	public set totalBet ( value: number ) {
		this.currentBet = value;
		this.setTotalBetLabel( value );
	}

	public set balance ( value: number ) {
		this.currentBalance = value;
		this.setBalanceLabel( value );
	}

	protected createElements ( config: MLUIBankConfig ): void {
		this.createBankBackgroundBar( this.config.bankBackgroundBar );
		super.createElements( config );
	}

	protected createBankBackgroundBar ( config: ISprite ): void {
		this.bankBackgroundBar = new Sprite( config );
		this.addChild( this.bankBackgroundBar );
	}

	protected coreElementLayout ( orientation: Orientation ): void {
		super.coreElementLayout( orientation );

		this.currentPadding = ( orientation == Orientation.LANDSCAPE ) ? this.config.spacing.landscapePadding : this.config.spacing.padding;
		if ( this.currentBalance != undefined ) { this.setBalanceLabel( this.currentBalance ); }
		if ( this.currentBet != undefined ) { this.setTotalBetLabel( this.currentBet ); }
	}

	protected setBalanceLabel ( value: number ): void {
		if ( this.balanceLabel != undefined ) {
			this.balanceLabel.text = this.moneyFormatter.formatMoney( value, NumberDisplayMode.VALUE_WITH_CURRENCY );
			//align right
			this.balanceLabel.x = this.currentPadding - this.balanceLabel.width;
			if ( this.balanceIcon != undefined ) {
				this.balanceIcon.x = this.balanceLabel.x - this.config.spacing.iconSpacing - this.balanceIcon.width;
			}
		}
	}

	protected setTotalBetLabel ( value: number ): void {
		if ( this.totalBetLabel != undefined ) {
			this.totalBetLabel.text = this.moneyFormatter.formatMoney( value, NumberDisplayMode.VALUE_WITH_CURRENCY, false, 2 );
			this.totalBetLabel.x = this.betIcon.x + this.config.spacing.iconSpacing + this.betIcon.width;
		}
	}
}


