import * as MiniSignal from 'mini-signals';
import { AbstractSymbol } from './AbstractSymbol';
import { Animation } from 'corengf/src/elements/canvas/animation/Animation';
import { SymbolAnimationPostfix } from 'slotngf/src/components/reels/ReelsEnums';


export class SpriteSymbol extends AbstractSymbol {

	public static SYMBOL_ANIMATION_FRAME_COUNT: number = 60;

	protected _symbolContainer: PIXI.extras.AnimatedSprite;

	public get visible (): boolean {
		return this._symbolContainer.visible;
	}

	public set visible ( value: boolean ) {
		this._symbolContainer.visible = value;
	}

	public get symbolContainer (): PIXI.extras.AnimatedSprite {
		return this._symbolContainer;
	}

	constructor ( name: string ) {
		super();
		this.symbolName = name;
		this._symbolContainer = Animation.getAnimationFromPool( name );
		this._symbolContainer.animationSpeed = this._symbolContainer.totalFrames / SpriteSymbol.SYMBOL_ANIMATION_FRAME_COUNT;

		this.animationCompleteSignal = new MiniSignal();
	}

	public playAnimation ( type: string | SymbolAnimationPostfix ): boolean {
		return false;
	}
}


