import { Point } from 'pixi.js';
import * as MiniSignal from 'mini-signals';
import { Container } from 'typescript-ioc/es5';
import { NMath } from 'corengf/src/utils/NMath';
import { BaseSymbol } from 'slotngf/src/components/reelV2/symbol/BaseSymbol';
import { ReelStripModel, SymbolData } from 'slotngf/src/components/reelV2/ReelStripModel';
import { IReelStripConfig, ISymbolDefinition, SpinDirection } from 'slotngf/src/core/config/IReelsConfig';
import { SymbolAnimationPostfix, SymbolState } from 'slotngf/src/components/reels/ReelsEnums';

export class ReelStripView {
	public symbolAnimationCompleteSignal: MiniSignal;
	protected mainContainer: PIXI.Container;

	protected _id: number;

	public get id (): number {
		return this._id;
	}
	public set id ( value: number ) {
		this._id = value;
	}

	protected symbols: Array<BaseSymbol>;
	protected totalSymbolsCount: number;
	protected _config: IReelStripConfig;
	public get config (): IReelStripConfig {
		return this._config;
	}
	public set config ( value: IReelStripConfig ) {
		this._config = value;
	}

	public set visible ( visible: boolean ) {
		this.mainContainer.visible = visible;
	}

	public get visible (): boolean {
		return this.mainContainer.visible;
	}

	public set scale ( value: Point | PIXI.ObservablePoint ) {
		this.mainContainer.scale = value;
	}

	public get scale (): Point | PIXI.ObservablePoint {
		return this.mainContainer.scale;
	}

	public set position ( value: Point | PIXI.ObservablePoint ) {
		this.mainContainer.position = value;
	}

	public get position (): Point | PIXI.ObservablePoint {
		return this.mainContainer.position;
	}

	public get alpha (): number {
		return this.mainContainer.alpha;
	}

	public set alpha ( value: number ) {
		this.mainContainer.alpha = value;
	}

	private _originalPosition: PIXI.Point;

	public get originalPosition (): PIXI.Point {
		return this._originalPosition;
	}

	private _isZoomed: boolean = false;

	public get isZoomed (): boolean {
		return this._isZoomed;
	}

	public get container (): PIXI.Container {
		return this.mainContainer;
	}

	protected symbolConfig: ISymbolDefinition;
	protected reelStripModel: ReelStripModel;
	protected layerContainers: Array<PIXI.Container> = [];

	constructor () {
		this.symbolAnimationCompleteSignal = new MiniSignal();
		this.mainContainer = new PIXI.Container();
		this.mainContainer.name = 'reelStrip';
	}

	public reelInit ( reelStripModel: ReelStripModel ): void {
		this.reelStripModel = reelStripModel;
		this.config = reelStripModel.config;
		this.symbolConfig = reelStripModel.symbolConfig;

		let array = null;

		array = this.createSymbolArray( reelStripModel.countTopExtraSymbol )
			.concat( reelStripModel.symbolsOutcome )
			.concat( this.createSymbolArray( reelStripModel.countBottomExtraSymbol ) );
		this.totalSymbolsCount = array.length;

		this.createSymbols( reelStripModel.reelSymbols );
	}

	private createSymbolArray ( size: number ): Array<string> {
		const result = new Array<string>();

		for ( let i = 0; i < size; i++ ) {
			result.push( this.config.rangeSymbols[ NMath.randomInt( 0, this.config.rangeSymbols.length - 1 ) ] );
		}
		return result;
	}

	protected createSymbols ( symbolDataArr: Array<SymbolData> ): void {
		if ( this.config.winAnimationOverride ) {
			BaseSymbol.addWinAnimationOverrides( this.config.winAnimationOverride.symbolOverrides, this.config.winAnimationOverride.tweenData );
		}
		this.symbols = new Array<BaseSymbol>();

		const stripContainer = new PIXI.Container();
		stripContainer.name = 'stripContainer';

		this.layerContainers.push( stripContainer );

		for ( let i = 0; i < symbolDataArr.length; i++ ) {
			const symbol: BaseSymbol = this.createSymbol();
			symbol.setId( i );
			symbol.setConfig( this.symbolConfig );
			symbol.animationCompleteSignal.add( this.symbolAnimationComplete, this );
			symbol.setSymbolNameWithState( symbolDataArr[ i ].symbolName, SymbolState.STATIC );
			symbol.symbolContainer.position.y = symbolDataArr[ i ].posY;
			stripContainer.addChild( symbol.symbolContainer );
			this.symbols.push( symbol );
		}

		this.mainContainer.addChildAt( stripContainer, 0 );

	}

	protected createSymbol (): BaseSymbol {
		return Container.get( BaseSymbol );
	}

	protected symbolAnimationComplete ( symbolId: number, type: SymbolAnimationPostfix ): void {
		this.symbolAnimationCompleteSignal.dispatch( this.id, symbolId, type );
	}

	public tweenSymbolsUpdate ( symbolId: number, y: number ): void {
		this.symbols[ symbolId ].symbolContainer.y = y;
	}

	public moveLastSymbolToTop ( symbolData: SymbolData, state: SymbolState, spinDirection: SpinDirection ): void {
		var symbol: BaseSymbol = ( spinDirection == SpinDirection.SpinDown ) ? this.symbols.pop() : this.symbols.shift();
		symbol.symbolContainer.position.y = symbolData.posY;
		symbol.setSymbolNameWithState( symbolData.symbolName, state );
		if ( spinDirection == SpinDirection.SpinDown ) {
			this.symbols.unshift( symbol );
			symbol.symbolContainer.parent.setChildIndex( symbol.symbolContainer, 0 );
		} else {
			this.symbols.push( symbol );
			symbol.symbolContainer.parent.setChildIndex( symbol.symbolContainer, this.symbols.length - 1 );
		}
		this.symbols.forEach( ( element, i ) => {
			element.setId( i );
		} );
	}

	public hideExtraSymbols (): void {
		let index;
		for ( index = 0; index < this.reelStripModel.countTopExtraSymbol; index++ ) {
			this.hideSymbol( index );
		}
		for ( index += this.config.symbolsOnReel; index < this.totalSymbolsCount; index++ ) {
			this.hideSymbol( index );
		}
	}

	public setSymbolNameWithState ( symbolId: number, symbolName: string, state: SymbolState, ignoreExtra: boolean = true ): void {
		const offset = ( ignoreExtra ) ? this.reelStripModel.countTopExtraSymbol : 0;
		this.symbols[ offset + symbolId ].setSymbolNameWithState( symbolName, state );
	}

	public setSymbolState ( symbolId: number, state: SymbolState, ignoreExtra: boolean = true ): void {
		const offset = ( ignoreExtra ) ? this.reelStripModel.countTopExtraSymbol : 0;
		this.symbols[ offset + symbolId ].setSymbolState( state );
	}

	public playSymbolAnimation ( symbolId: number, typeAnim: SymbolAnimationPostfix ): boolean {
		return this.symbols[ symbolId ].playAnimation( typeAnim );
	}

	public playSymbolShaking ( symbolId: number ): void {
		return this.symbols[ this.reelStripModel.countTopExtraSymbol + symbolId ].playShaking();
	}

	public stopSymbolShaking ( symbolId: number, restorePosX: number, restorePosY: number ): void {
		return this.symbols[ symbolId ].stopShaking( restorePosX, restorePosY );
	}

	public hideSymbol ( symbolId: number ): boolean {
		return this.symbols[ this.reelStripModel.countTopExtraSymbol + symbolId ].symbolContainer.visible = false;
	}

	public hideAllSymbol (): void {
		this.symbols.forEach( ( item ) => {
			item.symbolContainer.visible = false;
		} );
	}

	public showSymbol ( symbolId: number ): boolean {
		return this.symbols[ this.reelStripModel.countTopExtraSymbol + symbolId ].symbolContainer.visible = true;
	}

	public getSymbolContainer ( symbolId: number ): Container {
		return this.symbols[ symbolId ].symbolContainer;
	}

	public playItemsAnimation ( symbolId: number, typeAnim: SymbolAnimationPostfix ): boolean {
		return this.symbols[ this.reelStripModel.countTopExtraSymbol + symbolId ].playAnimation( typeAnim );
	}

	public deleteSymbolsAnimation (): void {
		for ( var i = 0; i < this.symbols.length; i++ ) {
			this.symbols[ i ].deleteAnimation();
		}
	}

	public zoom ( scale: number, position: PIXI.Point ): void {
		if ( !this.isZoomed ) {
			this._originalPosition = new PIXI.Point( this.position.x, this.position.y );
		}
		this._isZoomed = true;
		this.scale.x = scale;
		this.scale.y = scale;
		this.position = position;
	}

	public restoreFromZoomOut (): void {
		this.scale.x = 1;
		this.scale.y = 1;
		this.position = this._originalPosition;
		this._isZoomed = false;
	}

	public setMask ( mask: PIXI.Graphics ): void {
		this.mainContainer.mask = mask;
		this.mainContainer.addChild( mask );
	}
}


