import { SlotNGHttpFormatter } from './../../SlotNGHttpFormatter';

/**
 * Format history summary request
 */
export class HistoryRequestMsgFormatter extends SlotNGHttpFormatter {

	/**
	 * sends button press emulation package
	 * @param data - ignored
	 * @returns {any} message object
	 */
	public codeMessage ( data?: any ): any {
		return {
			game_code: this.config.gameCode,
			page_number: data.pageNum,
			page_size: data.pageSize,
			period_type: data.queryType,
			start_time: data.startTime,
			end_time: data.endTime
		};
	}
}



