import { TextStyleOptions } from 'pixi.js';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Helper } from 'corengf/src/utils/Helper';
import { Orientation } from 'corengf/src/display/Viewport';
import { IButtonConfig } from 'corengf/src/elements/canvas/button/Button';
import { ICompViewConfig } from 'corengf/src/core/config/ICompViewConfig';
import { ITextButtonConfig } from 'corengf/src/elements/canvas/button/TextButton';
import { ICoreElementLayoutConfig } from 'corengf/src/elements/canvas/CoreElement';
import { IWebTextFieldConfig } from 'corengf/src/elements/canvas/text/WebTextField';
import { SlotActions } from 'slotngf/src/utils/SlotActions';
import { MLUIConfig } from 'mlui/src/config/IMLUIConfig';
import { MLUIButtonId } from 'mlui/src/buttons/MLUIButtonId';


export class MLUISettingsConfig extends MLUIConfig implements IMLUISettingsConfig {
	public name = 'settingsPanel';

	public titleStyle = {
		fontFamily: Helper.DEFAULT_FONTS,
		fontSize: 48,
		fontWeight: 'bold',
		fill: 'white',
		align: 'left',
		padding: 50
	};

	public title = {
		name: 'settingsTitle',
		key: 'settings',
		position: {
			x: 81,
			y: 100
		},
		style: this.titleStyle
	};

	public closeButton = {
		isOrientationSupported: true,
		name: 'closeButton',
		position: {
			x: 600,
			y: 132
		},
		action: SlotActions.HIDE_SETTINGS,
		id: MLUIButtonId.BUTTON_HIDE_SETTINGS,
		productLine: this.productLine,
		assetName: 'close',
		isLocal: true
	};

	public soundToggleData: ITextButtonConfig = {
		isOrientationSupported: true,
		name: 'soundToggle',
		position: {
			x: 545,
			y: 220
		},
		id: SlotActions.SOUND_TOGGLE,
		productLine: this.productLine,
		action: MLUIButtonId.TOGGLE_SOUND,
		assetName: 'toggle_switch',
		text: {
			name: 'sound',
			key: 'sound',
			style: {
				fontFamily: Helper.DEFAULT_FONTS,
				fontWeight: 'bold',
				fontSize: 36,
				align: 'left',
				fill: 'white'
			},
			position: { x: -460, y: 15 }
		},
		selected: true
	};

	public musicToggleData = {
		isOrientationSupported: true,
		name: 'musicToggle',
		position: {
			x: 545,
			y: 300
		},
		id: SlotActions.MUSIC_TOGGLE,
		productLine: this.productLine,
		action: MLUIButtonId.TOGGLE_MUSIC,
		assetName: 'toggle_switch',
		text: {
			name: 'music',
			key: 'music',
			style: {
				fontFamily: Helper.DEFAULT_FONTS,
				fontWeight: 'bold',
				fontSize: 36,
				align: 'left',
				fill: 'white'
			},
			position: { x: -460, y: 15 }
		},
		selected: true
	};

	public coreElementOrientationConfig: TsMap<Orientation, TsMap<string, ICoreElementLayoutConfig>> = new TsMap( [
		[
			Orientation.PORTRAIT, new TsMap( [
				[
					this.closeButton.name, {
						position: this.closeButton.position
					}
				],
				[
					this.musicToggleData.name, {
						position: this.musicToggleData.position
					}
				],
				[
					this.soundToggleData.name, {
						position: this.soundToggleData.position
					}
				]
			] )
		],
		[
			Orientation.LANDSCAPE, new TsMap( [
				[
					this.closeButton.name, {
						position: { x: 1120, y: 132 }
					}
				],
				[
					this.musicToggleData.name, {
						position: { x: 820, y: 300 }
					}
				],
				[
					this.soundToggleData.name, {
						position: { x: 820, y: 220 }
					}
				]
			] )
		]
	] );
}

export interface IMLUISettingsConfig extends ICompViewConfig {
	closeButton: IButtonConfig;
	titleStyle: TextStyleOptions;
	title: IWebTextFieldConfig;
	soundToggleData: ITextButtonConfig;
	musicToggleData: ITextButtonConfig;
}


