import { Container } from 'pixi.js';
import { Asset } from 'corengf/src/core/AssetLoader';
import { IBitmapTextFieldTweenConfig, TweenBitmapTextField } from 'corengf/src/elements/canvas/text/TweenBitmapTextField';
import { BigWinView } from 'slotngf/src/components/bigwin/BigWinView';
import { BigWinTierType, IBigWinConfig } from 'slotngf/src/core/config/IBigWinConfig';
import { IDHBBigWinConfig } from 'game/src/components/bigwin/DHBBigWinConfig';

export class DHBBigWinView extends BigWinView {

	protected config: IDHBBigWinConfig;

	protected valueTweenConfig: IBitmapTextFieldTweenConfig;

	protected animateBigWinIn (): void {
		this.bigWinValueLabel.alpha = 1;
		this.bigWinValueLabel.startTween( this.valueTweenConfig );
	}

	protected setupBigWinLabel ( config: IBigWinConfig ): void {
		this.bigWinValueLabel = new TweenBitmapTextField( config.bigWinValueLabel );
		this.bigWinValueLabel.text = '0.00';
		this.bigWinValueLabel.alpha = 0;

		let slotIndex = this.bigWinAnimation.displayObject.skeleton.findSlotIndex( 'text' );
		let slotContainer: Container = this.bigWinAnimation.displayObject.slotContainers[ slotIndex ];
		slotContainer.addChild( this.bigWinValueLabel );

	}

	protected playBigWinAnimation ( finalBigWinTier: string ): void {
		if ( this.hasUniqueAnimation( finalBigWinTier ) ) {
			this.bigWinAnimation.play( 0, finalBigWinTier, false );
		} else {
			this.bigWinAnimation.play( 0, 'Number', false );
		}
	}

	public portraitLayout (): void {
		// this.coreElementLayout( Orientation.PORTRAIT );
		super.portraitLayout();
		this.valueTweenConfig = this.config.portraitValueLabelTweenInConfig;
	}

	/**
	 * Method where we set position, scale, visibility, textures ext. for landscape mode
	 */
	public landscapeLayout (): void {
		// this.coreElementLayout( Orientation.LANDSCAPE );
		super.landscapeLayout();
		this.valueTweenConfig = this.config.landscapeValueLabelTweenInConfig;
	}


	public getAssetsList (): Array<Asset> {
		let assetsList: Array<Asset> = super.getAssetsList();

		assetsList.push( ...[
			{
				assetKey: 'Win05',
				assetURL: 'assets/sound/game/Win05.mp3'
			},
			{
				assetKey: 'Win06',
				assetURL: 'assets/sound/game/Win06.mp3'
			},
			{
				assetKey: 'Win07',
				assetURL: 'assets/sound/game/Win07.mp3'
			},
			{
				assetKey: 'Win05End',
				assetURL: 'assets/sound/game/Win05End.mp3'
			},
			{
				assetKey: 'Win06End',
				assetURL: 'assets/sound/game/Win06End.mp3'
			},
			{
				assetKey: 'Win07End',
				assetURL: 'assets/sound/game/Win07End.mp3'
			},
			{
				assetKey: 'RollupBeep',
				assetURL: 'assets/sound/game/RollupBeep.mp3'
			},
			{
				assetKey: 'WildWinAudio',
				assetURL: 'assets/sound/game/WildWin.mp3'
			},
			{
				assetKey: 'WildWinLaugh',
				assetURL: 'assets/sound/game/WildWinLaugh.mp3'
			},
			{
				assetKey: 'redEnvelope',
				assetURL: 'assets/coinShower/redEnvelope.json'
			},
			{
				assetKey: 'redEnvelopeParticles',
				assetURL: 'assets/coinShower/redEnvelopeParticles.json'
			},
		] );

		return assetsList;
	}

}


