import { TsMap } from 'corengf/src/utils/TsMap';
import { Actions } from 'corengf/src/utils/Actions';
import { Inject, Container } from 'typescript-ioc/es5';
import { CompController } from 'corengf/src/mvc/CompController';
import { GameController } from 'corengf/src/core/GameController';
import { Button } from 'corengf/src/elements/canvas/button/Button';
import { LocalButtonBehaviour, LocalButtonsStateSet } from 'corengf/src/buttons/LocalButtonsStateSet';
import { BaseButtonsController } from 'corengf/src/buttons/BaseButtonsController';

export class LocalButtonsController extends BaseButtonsController {

	@Inject
	protected gameController: GameController;

	protected buttons: TsMap<string, Button>;
	protected buttonStates: LocalButtonsStateSet;

	/**
	 * if action triggered, notify CompController.
	 * key: action
	 * value: CompControllers to be notified
	 */
	protected _actionNotifyList: TsMap<string, Array<CompController>> = new TsMap<string, Array<CompController>>();

	protected init (): void {
		this.buttons = new TsMap<string, Button>();
		this.buttonStates = Container.get( LocalButtonsStateSet );
	}

	/**
	 * set action maping to CompControllers that need to be notified.
	 * @param action
	 * @param component
	 */
	public setAction ( action: string, component: CompController ): void {
		if ( component == undefined || action == undefined ) {
			return;
		}

		action = Actions.CLICK_ACTION_PREFIX + action;

		// if action already been registered
		if ( this._actionNotifyList.has( action ) ) {
			// if component not been registered for that action, push component
			let actionListeners: Array<CompController> = this._actionNotifyList.get( action );
			if ( !actionListeners.includes( component ) ) {
				this._actionNotifyList.get( action ).push( component );
			}
		} else {
			this._actionNotifyList.set( action, [ component ] );
		}
	}

	/**
	 * and call action function for registered components.
	 * @param action
	 * @param data
	 */
	public handleButtonAction ( action: string, data?: any ): void {
		if ( this._actionNotifyList.has( action ) ) {
			let components = this._actionNotifyList.get( action );
			components.forEach( component => {
				if ( typeof component[ action ] == 'function' ) {
					component[ action ]( data );
				}
			} );
		}
		this.gameController.handleButtonAction( action, data );
	}

	protected updateButton ( button: Button, behavior: LocalButtonBehaviour ): void {
		super.updateButton( button, behavior );
		if ( behavior.enabled ) {
			button.setInteractive( behavior.interactive );
		} else {
			button.setInteractive( false );
		}
	}
}



