import { Inject } from 'typescript-ioc/es5';
import { AudioTrackName } from 'corengf/src/audio/SoundManager';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { MoneyFormatter, NumberDisplayMode } from 'corengf/src/utils/MoneyFormatter';
import { SlotBankService } from 'slotngf/src/core/SlotBankService';
import { IStartPickFeatureData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { SlotFreeSpinsController } from 'slotngf/src/components/freeSpins/SlotFreeSpinsController';
import { DHBSlotFreeSpinsView } from 'game/src/components/freeSpins/DHBSlotFreeSpinsView';

export class DHBSlotFreeSpinsController extends SlotFreeSpinsController {

	protected view: DHBSlotFreeSpinsView;
	public static readonly PICKGAME_OUTRO_COMPLETE: string = 'PickgameOutroComplete';
	@Inject
	protected moneyFormatter: MoneyFormatter;
	@Inject
	protected bankService: SlotBankService;

	constructor ( @Inject view: DHBSlotFreeSpinsView ) {
		super( view );
	}

	public addEventListeners (): void {
		this.view.outroStartSignal.add( this.onOutroAnimationStart, this );
		super.addEventListeners();
	}

	public startPickFeature ( data: IDHBStartPickFeatureData ): void {
		this.view.setPicks( data.playerChoices );

		this.view.playIntro();
		// console.error( data.restoreChoices );
		if ( data.restoreChoices ) {
			this.view.restorePicks( data.restoreChoices );
		}
	}

	public beforePickSelection (): void {
		// finish startPickFeature state
		this.finishStateAction();
	}

	public pickFeature ( data?: IDHBStartPickFeatureData ): void {
		if ( data.selected_index ) {
			let index = data.selected_index - 1;
			let choice = data.playerChoices[ index ];
			this.view.setPickResult( index, choice );
			this.soundManager.play( 'pickgameSelect', AudioTrackName.EFFECT, false );
		}

		this.view.enableSelection( true );
	}

	public endPickFeature (): void {
		this.finishStateAction();
	}

	public pickFeatureWin ( data: IDHBStartPickFeatureData ): void {
		if ( data.selected_index ) {
			let index = data.selected_index - 1;
			let choice = data.playerChoices[ index ];
			const winAmount = this.moneyFormatter.formatMoney( data.win_amount, NumberDisplayMode.VALUE_ONLY );
			this.view.setPickResult( index, choice, true, winAmount );

			this.soundManager.play( 'pickgameHitTriple', AudioTrackName.EFFECT, false );
			this.bankService.balance = data.balance;
			this.bankService.win = data.win_amount;
		}

		this.view.enableSelection( false );
	}

	protected onOutroAnimationStart ( choice: string = '' ): void {
		this.soundManager.play( 'pickgameWin' + choice, AudioTrackName.EFFECT, false );
	}

	protected onOutroAnimationComplete (): void {
		GlobalDispatcher.dispatchIntoAllStates( DHBSlotFreeSpinsController.PICKGAME_OUTRO_COMPLETE, 'PickgameOutroComplete' );
		this.finishStateAction();
	}

}


export interface IDHBStartPickFeatureData extends IStartPickFeatureData {
	balance?: number;
	selected_index?: number;
	win_amount?: number;
	restoreChoices?: Array<string>;
}


