import { TsMap } from 'corengf/src/utils/TsMap';
import { BaseButtonBehaviour, BaseButtonsStateSet } from 'corengf/src/buttons/BaseButtonStateSet';

/**
 * Local Buttons State Class
 * Set state for the local buttons
 */
export class LocalButtonsStateSet extends BaseButtonsStateSet {

	protected buttonStates: TsMap<string, TsMap<string, LocalButtonBehaviour>>;

	public has ( stateName: string ): boolean {
		return this.buttonStates.has( stateName );
	}

	public get ( stateName: string ): TsMap<string, LocalButtonBehaviour> {
		return this.buttonStates.get( stateName );
	}

	protected initStates (): void {
		this.buttonStates.set( LocalButtonsStateSet.DEFAULT_TEMPLATE, this.default() );
	}

	protected default (): TsMap<string, LocalButtonBehaviour> {
		return new TsMap<string, LocalButtonBehaviour>();
	}

	protected init (): TsMap<string, LocalButtonBehaviour> {
		return new TsMap<string, LocalButtonBehaviour>();
	}
}

export class LocalButtonBehaviour extends BaseButtonBehaviour {
}


