export interface IWebSocketIncommingPackage {
	address: string;
	eventType: string;
	body: any;
}

export class NGWebSocketData {
	public feature: string;
	public eventType: string;
	public body: any;

	constructor ( feature: string, eventType: string, body: any ) {
		this.feature = feature;
		this.eventType = eventType;
		this.body = body;
	}
}


