import { Sprite, Graphics } from 'pixi.js';
import * as MiniSignal from 'mini-signals';
import { Container } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Orientation } from 'corengf/src/display/Viewport';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import { Animation } from 'corengf/src/elements/canvas/animation/Animation';
import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { ReelModel } from 'slotngf/src/components/reelV2/ReelModel';
import { SlotCompView } from 'slotngf/src/components/mvc/SlotCompView';
import { WinLineDisplayData } from 'slotngf/src/components/lines/WinLine';
import { Anticipation } from 'slotngf/src/components/reelV2/Anticipation';
import { ReelStripView } from 'slotngf/src/components/reelV2/ReelStripView';
import { ReelSet } from 'slotngf/src/components/reelV2/ReelConfig';
import { ReelStripModel, SymbolData } from 'slotngf/src/components/reelV2/ReelStripModel';
import { SymbolAnimationPostfix, SymbolState } from 'slotngf/src/components/reels/ReelsEnums';
import { ReelStripOverlay, OverlayType, IReelStripOverlayConfig, SymbolObject } from 'slotngf/src/components/reelV2/ReelStripOverlay';
import { IReelsConfig, IReelSet, IReelLayoutData, IReelStripConfig, IMask, SymbolPosition, SpinDirection } from 'slotngf/src/core/config/IReelsConfig';

export class ReelControllerView extends SlotCompView {

	public static readonly ALL_SYMBOLS_ANIMATION_COMPLETE: string = 'allSymbolsAnimationComplete';


	//#region GAME_OBJECTS

	/**
	 * this container only for reelsView
	 */
	protected reelsViewContainer: PIXI.Container;

	/**
	 * this container only for reelsOverlay
	 */
	protected reelsOverlayContainer: PIXI.Container = new PIXI.Container();

	protected maskContainer: PIXI.Container = new PIXI.Container();
	protected reelsContainer: PIXI.Container = new PIXI.Container();

	/**
	 * Reel background sprite
	 */
	protected reelBackground: Sprite;

	/**
	 * Reel frame sprites
	 */
	protected reelFrames: Array<Sprite>;

	public anticipation: Anticipation;

	//#endregion GAME_OBJECTS

	protected reelSet: IReelSet;

	protected reelModel: ReelModel;

	public idleAnimationsCompleteSignal: MiniSignal;


	/**
	 * Describe how many animations are completed right now on strips.
	 */
	protected completedAnimationCounter: number = 0;

	protected idleTimerArray: Array<SimpleTimer>;

	protected get currentReelSetIndex (): number {
		if ( !this.reelModel ) {
			return ReelSet.BASE;
		}
		return this.reelModel.currentReelSetIndex;
	}

	protected get currentReelSet (): Array<IReelStripConfig> {
		return this.reelSet[ this.currentReelSetIndex ];
	}

	protected get currentReelView (): Array<ReelStripView> {
		return this.reelViewStripMap.get( this.currentReelSetIndex );
	}

	protected reelConfig: IReelsConfig;

	/**
	 * array of items that only with static picture
	 * and have reel mask
	 */
	protected reelViewStripMap: TsMap<number, Array<ReelStripView>> = new TsMap<number, Array<ReelStripView>>();

	protected reelViewMap: TsMap<number, Container> = new TsMap<number, Container>();

	protected reelOverlayMap: TsMap<number, ReelStripOverlay> = new TsMap<number, ReelStripOverlay>(); // layer index | ReelStripOverlay, index start from zero.

	/**
	 * The number of layers
	 *
	 * @private
	 * @type {number}
	 * @memberof ReelControllerView
	 */
	protected overlaySize: number = 0;

	/**
	 * The presentation layer index that used for win display.
	 *
	 * @protected
	 * @type {number}
	 * @memberof ReelControllerView
	 */
	public baseOverlayIndex: number = 0;

	protected get maskConfig (): string | IMask {
		let config = ( this.reelConfig.orientationMaskData )
			? this.reelConfig.orientationMaskData.get( this.currentReelSetIndex ).get( this.viewport.properOrientation )
			: null;

		const maskConfig = config || this.reelConfig.defaultReelsMask.get( this.currentReelSetIndex );

		if ( !maskConfig ) {
			throw new Error( 'can\'t find mask layout config' );
		}

		return maskConfig;
	}

	protected get layoutData (): IReelLayoutData {
		let orientationLayout: IReelLayoutData;
		if ( this.reelConfig.orientationLayoutData ) {
			orientationLayout = this.reelConfig.orientationLayoutData.get( this.viewport.properOrientation );
		}

		const layoutData = orientationLayout || this.reelConfig.defaultLayoutData;

		if ( !layoutData ) {
			throw new Error( 'can\'t layout config.' );
		}
		return layoutData;
	}

	constructor () {
		super();
		this.idleAnimationsCompleteSignal = new MiniSignal();
		this.idleTimerArray = new Array<SimpleTimer>();
	}

	//#region INIT

	public bindModel ( model: ReelModel ): void {
		this.reelModel = model;

		this.reelOverlayMap.forEach( ( overlay ) => {
			overlay.bindModel( this.reelModel );
		} );
	}

	public createElements ( reelConfig: IReelsConfig ): void {
		this.reelSet = reelConfig.reelSet;
		this.reelConfig = reelConfig;
		this.maskContainer.name = 'maskContainer';
		this.reelsContainer.name = 'reelsContainer';

		this.mainContainer.addChild( this.maskContainer );
		this.maskContainer.addChild( this.reelsContainer );

		this.initReelsBackground();

		this.initReelsView();

		this.initReelsOverlay();

		this.initReelsFrame();
		this.createAnticipations();
		this.createSymbolAnimationPool();
	}

	/**
	 * Add reel background to stage according to setting in config
	 */
	protected initReelsBackground (): void {
		if ( this.reelConfig.reelBackground != null ) {
			this.reelBackground = Sprite.fromFrame( this.reelConfig.reelBackground.assetName );
			this.reelBackground.position.set( this.reelConfig.reelBackground.x, this.reelConfig.reelBackground.y );
			this.addChild( this.reelBackground );
		}
	}

	/**
	 * initialize array with static picture and with reel mask
	 */
	protected initReelsView (): void {
		this.reelsViewContainer = new PIXI.Container();
		this.reelsViewContainer.name = 'reelsView';

		for ( const reelSetIndex of Object.keys( this.reelSet ) ) {
			this.reelViewStripMap.set( +reelSetIndex, [] );

			let reelSetContainer: PIXI.Container = new PIXI.Container();
			reelSetContainer.name = `${ reelSetIndex }`;
			this.reelViewMap.set( +reelSetIndex, reelSetContainer );

			this.reelSet[ reelSetIndex ].forEach( ( reelConfig, reelIndex ) => {
				const layoutData = this.getSingleReelLayoutData( +reelSetIndex, reelIndex );
				let baseReelView: ReelStripView = this.createReelStripView();

				baseReelView.position = new PIXI.Point( layoutData.position.x, layoutData.position.y );

				// default visible = true, if config has visible setting, it will follow this.
				if ( reelConfig.visible !== undefined ) {
					baseReelView.visible = reelConfig.visible;
				}

				if ( this.reelConfig.individualReelsMask ) {
					let reelMask: Graphics = this.getReelMask(
						0,
						0,
						reelConfig.width,
						reelConfig.symbol.height * reelConfig.symbolsOnReel
					);
					baseReelView.setMask( reelMask );
				}

				reelSetContainer.addChild( baseReelView.container );
				baseReelView.id = reelIndex;

				this.reelViewStripMap.get( +reelSetIndex ).push( baseReelView );
			} );

			this.reelsViewContainer.addChild( reelSetContainer );
		}

		this.reelsContainer.addChild( this.reelsViewContainer );
	}

	/**
	 * initialize array of items that only may the size of symbol can be larger than usual
	 */
	protected initReelsOverlay (): void {
		this.reelsOverlayContainer.name = 'overlay-container';

		this.appendBasicOverlay();
		this.reelsContainer.addChild( this.reelsOverlayContainer );
	}

	/**
	 * Append basic overlay used for symbol win display
	 *
	 * @private
	 * @memberof ReelControllerView
	 */
	protected appendBasicOverlay (): void {
		this.appendOverlay( {
			overlayType: OverlayType.SYMBOL,
			rangeSymbols: this.reelConfig.reelDefinition.rangeSymbols,
			symbolConfig: this.reelConfig.symbolDefinition
		} );
		this.baseOverlayIndex = this.overlaySize - 1;
	}

	//#endregion INIT

	/**
	 * Append additional overlay to view for further control. reference: WildLock, Replacing
	 *
	 * @protected
	 * @param {IReelStripOverlayConfig} config
	 * @memberof ReelControllerView
	 */
	protected appendOverlay ( config: IReelStripOverlayConfig ): void {
		const overlay: ReelStripOverlay = Container.get( ReelStripOverlay );
		overlay.init( config );
		overlay.createPool( config.name, config.size );
		overlay.symbolAnimationCompleteSignal.add( this.onSymbolAnimationComplete, this );

		this.reelOverlayMap.set( this.overlaySize, overlay );

		this.reelsOverlayContainer.addChild( overlay.mainContainer );
		this.overlaySize++;
	}

	public resetOverlay ( layer: number ): void {
		this.reelOverlayMap.get( layer ).reset();
	}

	public cleanAllOverlays (): void {
		this.reelOverlayMap.forEach( ( overlay ) => {
			if ( overlay.lock === false ) {
				overlay.clean();
			}
		} );
	}

	public cleanOverlays ( layers: Array<number> ): void {
		layers.forEach( ( layer ) => {
			if ( this.reelOverlayMap.get( layer ).lock === false ) {
				this.reelOverlayMap.get( layer ).clean();
			}
		} );
	}

	public hideAllOverlays (): void {
		this.reelOverlayMap.forEach( ( overlay ) => {
			if ( overlay.lock === false ) {
				overlay.visible = false;
			}
		} );
	}

	/**
	 * Return overlay objects by given layer and reel index.
	 *
	 * @param {number} layer
	 * @param {number} reelIndex
	 * @returns {Array<SymbolObject>}
	 * @memberof ReelControllerView
	 */
	public getObjectsAtOverlay ( layer: number, reelIndex: number ): Array<SymbolObject> {
		return this.reelOverlayMap.get( layer ).getObjectsByReel( reelIndex );
	}

	/**
	 * lock the layer will prevent the layer hide/clean by cleanAllOverlays()/hideAllOverlays().
	 *
	 * @param {number} layer
	 * @param {boolean} [value=true]
	 * @memberof ReelControllerView
	 */
	public lockOverlay ( layer: number, value: boolean = true ): void {
		this.reelOverlayMap.get( layer ).lock = value;
	}

	public lockOverlayPosition ( layer: number, position: SymbolPosition ): void {
		const overlay = this.reelOverlayMap.get( layer );
		overlay.lockPosition( position );
	}

	protected updateOverlayLayout ( positions: Array<IPoint> ): void {
		this.reelOverlayMap.forEach( ( overlay ) => {
			overlay.updateLayout( positions );
		} );
	}

	public updateOverlayPosition ( layer: number, position: IPoint ): void {
		this.reelOverlayMap.get( layer ).updateLayerPosition( position );
	}

	/**
	 * Get object's position if given position in its triggerPostions.
	 *
	 * @param {number} layer
	 * @param {SymbolPosition} symbolPosition
	 * @returns {SymbolPosition}
	 * @memberof ReelControllerView
	 */
	public getActualPosition ( layer: number, symbolPosition: SymbolPosition ): SymbolPosition {
		let object: SymbolObject = this.getSymbolObject( layer, symbolPosition );
		if ( object && object.isContain( symbolPosition ) ) {
			return object.position;
		} else {
			return symbolPosition;
		}
	}

	/**
	 * Get overlay's symbol object
	 *
	 * @param {number} layer
	 * @param {SymbolPosition} symbolPosition
	 * @returns {SymbolObject}
	 * @memberof ReelControllerView
	 */
	protected getSymbolObject ( layer: number, symbolPosition: SymbolPosition ): SymbolObject {
		const overlay = this.reelOverlayMap.get( layer );
		return overlay.getObjectByPosition( symbolPosition );
	}

	/**
	 * Play animation on specific layer
	 *
	 * @param {(string | SymbolAnimationPostfix)} animationName
	 * @param {number} layer
	 * @param {SymbolPosition} position
	 * @param {Array<SymbolPosition>} [triggerPositions] - assign multiple positions that can be trigger animation. Defaults to param:position.
	 * @param {boolean} [hideBaseLayer=true]
	 * @returns {boolean}
	 * @memberof ReelControllerView
	 */
	public playOverlayAnimation ( animationName: string | SymbolAnimationPostfix,
		layer: number, position: SymbolPosition, triggerPositions?: Array<SymbolPosition>, hideBaseLayer: boolean = true ): boolean {

		if ( !this.reelOverlayMap.has( layer ) ) {
			console.error( 'Out of range.' );
			return;
		}
		const overlay = this.reelOverlayMap.get( layer );

		let object: SymbolObject = overlay.getObjectByPosition( position );

		let result: boolean;
		if ( !object ) {
			object = overlay.getObjectAt( animationName, position );
			if ( !object.triggerPositions ) {
				object.triggerPositions = ( triggerPositions ) ? triggerPositions : [ position ];
			}

			overlay.setSymbolMapAt( object, position );
			const result = overlay.playAnimationAt( animationName, position );
			if ( result && hideBaseLayer ) {
				this.currentReelView[ position.reelIndex ].hideSymbol( position.symbolIndex );
			}
		} else {
			result = overlay.playAnimationAt( animationName, object.position );
			if ( result && hideBaseLayer ) {
				this.currentReelView[ object.position.reelIndex ].hideSymbol( object.position.symbolIndex );
			}
		}
		return result;
	}

	public placeOverlayAnimationAt ( animationName: string | SymbolAnimationPostfix, layer: number, position: SymbolPosition ): SymbolObject {
		if ( !this.reelOverlayMap.has( layer ) ) {
			console.error( 'Out of range.' );
			return;
		}
		const overlay = this.reelOverlayMap.get( layer );
		const object = overlay.placeAnimationAt( animationName, position );
		return object;
	}


	public hideOverlaySymbol ( layer: number, position: SymbolPosition ): void {
		const overlay = this.reelOverlayMap.get( layer );
		overlay.hideSymbol( position );
	}

	protected getSingleReelLayoutData ( reelSetIndex: number, reelIndex: number ): IReelLayoutData {
		let orientationLayout: IReelLayoutData;
		if ( this.viewport.properOrientation === Orientation.LANDSCAPE ) {
			orientationLayout = this.reelConfig.reelSet[ reelSetIndex ][ reelIndex ].landscapePosition;
		} else if ( this.viewport.properOrientation === Orientation.PORTRAIT ) {
			orientationLayout = this.reelConfig.reelSet[ reelSetIndex ][ reelIndex ].portraitPosition;
		}
		const layoutData: IReelLayoutData = orientationLayout || this.reelConfig.defaultReelPosition[ reelIndex ];
		return layoutData;
	}

	protected getReelMask ( x: number, y: number, width: number, height: number ): Graphics {
		var reelMask: Graphics = new Graphics();
		reelMask.beginFill( 0 );
		reelMask.drawRect( 0, 0, width, height );
		reelMask.endFill();
		reelMask.position.x = x;
		reelMask.position.y = y;
		return reelMask;
	}

	protected createReelStripView (): ReelStripView {
		return Container.get( ReelStripView );
	}

	/**
	 * Add reel frame to stage according to setting in config
	 */
	protected initReelsFrame (): void {
		if ( this.reelConfig.reelsFrames != null ) {
			if ( this.reelFrames == undefined ) {
				this.reelFrames = new Array<Sprite>();
			}
			for ( let i = 0; i < this.reelConfig.reelsFrames.length; i++ ) {
				const reelsFrame = this.reelConfig.reelsFrames[ i ];

				this.reelFrames.push( Sprite.fromImage( reelsFrame.assetName ) );
				this.reelFrames[ i ].position.set( reelsFrame.x, reelsFrame.y );
				if ( reelsFrame.scaleX != undefined && reelsFrame.scaleY != undefined ) {
					this.reelFrames[ i ].scale.set( reelsFrame.scaleX, reelsFrame.scaleY );
				}
				this.addChild( this.reelFrames[ i ] );
			}
		}
	}

	protected createAnticipations (): void {
		if ( this.reelConfig.anticipationData.animations == undefined || this.reelConfig.anticipationData.animations.length == 0 ) {
			return;
		}
		this.anticipation = Container.get( Anticipation );
		this.anticipation.initialize( this.reelConfig );

		this.addChild( this.anticipation );
	}

	protected createSymbolAnimationPool (): void {
		let symbols: Array<string> = this.reelConfig.reelDefinition.rangeSymbols;
		let numColumns: Array<number> = new Array<number>();
		for ( const reelSetIndex in this.reelSet ) {
			if ( ( this.reelSet as Object ).hasOwnProperty( reelSetIndex ) ) {
				numColumns.push( this.reelSet[ reelSetIndex ].length );
			}
		}
		let maxAnimNeeded: number = Math.max( ...numColumns );

		symbols.forEach( ( symbolName: string ) => {
			for ( let type in SymbolAnimationPostfix ) {
				if ( typeof type === 'string' ) {
					let name = 'symbol_' + symbolName + SymbolAnimationPostfix[ type ];
					Animation.createAnimationPool( name, maxAnimNeeded );
				}
			}
		} );
	}

	public setSymbolsStateForAll ( state: SymbolState ): void {
		this.currentReelSet.forEach( ( reel, reelIndex ) => {
			for ( let symbolIndex = 0; symbolIndex < this.currentReelSet[ reelIndex ].symbolsOnReel; symbolIndex++ ) {
				this.currentReelView[ reelIndex ].showSymbol( symbolIndex );
				this.currentReelView[ reelIndex ].setSymbolState( symbolIndex, state );
			}
		} );
	}

	public setSymbolNameWithState ( position: SymbolPosition, symbolName: string, state: SymbolState, ignoreExtra: boolean = true ): void {
		this.currentReelView[ position.reelIndex ].setSymbolNameWithState( position.symbolIndex, symbolName, state, ignoreExtra );
	}

	public reelInitView ( reelIndex: number, reelStripModel: ReelStripModel ): void {
		this.currentReelView[ reelIndex ].reelInit( reelStripModel );
	}

	protected onSymbolAnimationComplete ( position: SymbolPosition, type: string ): void {

		this.reelOverlayMap.get( this.baseOverlayIndex ).hideSymbol( position );
		this.currentReelView[ position.reelIndex ].showSymbol( position.symbolIndex );

		if ( type != SymbolAnimationPostfix.DROPPED && type != SymbolAnimationPostfix.ANTICIPATION ) {
			this.completedAnimationCounter--;
			if ( this.completedAnimationCounter == 0 ) {
				if ( type == SymbolAnimationPostfix.WIN || this.checkWildMultiplierAnimationType( type ) ) {
					this.dispatchAllSymbolsAnimationComplete();
				} else if ( type == SymbolAnimationPostfix.IDLE ) {
					this.idleAnimationsCompleteSignal.dispatch();
				}
			}
		}
	}

	protected dispatchAllSymbolsAnimationComplete (): void {
		GlobalDispatcher.dispatchIntoExpectedStates(
			ReelControllerView.ALL_SYMBOLS_ANIMATION_COMPLETE, [
			this.stateNames.allLineWin,
			this.stateNames.skip_allLineWin,
			this.stateNames.showWinning,
			this.stateNames.showWinningLoop,
			this.stateNames.prefeatureWinning,
			this.stateNames.freeSpinsAllLineWin,
			this.stateNames.skip_freeSpinsAllLineWin,
			this.stateNames.freeSpinsShowWinning
		] );
	}
	protected checkWildMultiplierAnimationType ( type: string ): boolean {
		if ( type == SymbolAnimationPostfix.WILD_X2 ||
			type == SymbolAnimationPostfix.WILD_X3 ||
			type == SymbolAnimationPostfix.WILD_X4 ) {
			return true;
		}
		return false;
	}

	protected updateContainerPosition (): void {
		this.reelsViewContainer.position.x = this.layoutData.position.x;
		this.reelsViewContainer.position.y = this.layoutData.position.y;

		const scale = this.layoutData.scale || 1;

		this.reelsViewContainer.scale.set( scale, scale );

		this.reelsOverlayContainer.position.x = this.reelsViewContainer.position.x;
		this.reelsOverlayContainer.position.y = this.reelsViewContainer.position.y;
		this.reelsOverlayContainer.scale.set( this.reelsViewContainer.scale.x, this.reelsViewContainer.scale.y );
	}

	protected updateReelPosition ( orientation: Orientation ): void {
		const reelSet = this.reelSet[ this.currentReelSetIndex ];

		const viewContainer = this.reelViewMap.get( this.currentReelSetIndex );

		if ( viewContainer.children.length === 0 ) {
			return;
		}

		const reelPositions = new Array<IPoint>();

		for ( let index = 0; index < reelSet.length; index++ ) {
			let positionData = ( orientation === Orientation.PORTRAIT ) ? reelSet[ index ].portraitPosition : reelSet[ index ].landscapePosition;
			positionData = positionData || this.reelConfig.defaultReelPosition[ index ];

			viewContainer.children[ index ].position.x = positionData.position.x;
			viewContainer.children[ index ].position.y = positionData.position.y;

			reelPositions.push( positionData.position );
		}

		this.updateOverlayLayout( reelPositions );
	}

	/**
	 * when we try show win line animation, we hide static symbol and show overlay symbol
	 * @param {WinLine} winItemsData for show win symbols
	 * @param {Array< Array <number> >} loseSymbolsPosition the position of symbols that we should transform
	 * to LOSE state
	 */
	public showWinLine ( winItemsData: WinLineDisplayData ): void {
		this.deleteAllAnimations();

		let isWildMultiplier: boolean = false;

		winItemsData.winTable.forEach( ( reel, reelIndex ) => {
			if ( reel === null || reel.length === 0 ) {
				return;
			}
			reel.forEach( ( value, symbolIndex ) => {
				if ( !value ) { // skip lose symbol
					return;
				}

				// if the symbol type is wild and wild_multiplier feature, we need change wild animation.
				isWildMultiplier = false;
				const symbolId: number = symbolIndex + this.reelModel.countTopExtraSymbol;
				if ( winItemsData.wildMultiplier != undefined && winItemsData.wildMultiplier.length > 0 ) {
					winItemsData.wildMultiplier.forEach( ( feature ) => {
						let wildId: number = feature.symbol_index + this.reelModel.countTopExtraSymbol;

						if ( feature.reel_index == reelIndex && wildId == symbolId ) { // is wild Symbol
							isWildMultiplier = true;
							this.playWildMultiplierAnimation( reelIndex, symbolIndex, feature.multiplier );
						}
					} );
				}

				if ( !isWildMultiplier ) {
					const displaylayers = this.getWinDisplayLayers();

					const objectArray: Array<{ layer: number, object: SymbolObject }> = [];

					displaylayers.forEach( ( layer ) => {
						const object = this.getSymbolObject( layer, { reelIndex, symbolIndex } );

						if ( object ) {
							objectArray.push( { layer, object } );
						}
					} );

					if ( objectArray.length > 0 ) {
						objectArray.forEach( ( item ) => {
							const position = this.getActualPosition( item.layer, { reelIndex, symbolIndex } );
							this.playOverlayAnimation( SymbolAnimationPostfix.WIN, item.layer, position );
						} );
					} else {
						const position = this.getActualPosition( this.baseOverlayIndex, { reelIndex, symbolIndex } );
						this.playOverlayAnimation( SymbolAnimationPostfix.WIN, this.baseOverlayIndex, position );
					}


					this.completedAnimationCounter++;
				}
			} );
		} );

		// if ( this.completedAnimationCounter == 0 ) { // when we do not symbols animation
		// 	this.debugAnimationTimer = new SimpleTimer( 3000, this.dispatchAllSymbolsAnimationComplete.bind( this ) );
		// }
	}

	/**
	 * The list of avaliable layers that will be looped when show win line
	 *
	 * @protected
	 * @returns {Array<number>}
	 * @memberof ReelControllerView
	 */
	protected getWinDisplayLayers (): Array<number> {
		return [ this.baseOverlayIndex ];
	}

	public updateVisibleByActiveReelSet (): void {
		this.reelsViewContainer.children.forEach( ( reelSetView, index ) => {
			reelSetView.visible = ( index === this.currentReelSetIndex );
		} );
		this.updateMainReelMask();
	}

	protected playWildMultiplierAnimation ( reelIndex: number, symbolIndex: number, multiplier: number ): void {
		let wildType: SymbolAnimationPostfix;
		switch ( multiplier ) {
			case 2: wildType = SymbolAnimationPostfix.WILD_X2; break;
			case 3: wildType = SymbolAnimationPostfix.WILD_X3; break;
			case 4: wildType = SymbolAnimationPostfix.WILD_X4; break;
		}

		if ( wildType != undefined ) {
			this.currentReelView[ reelIndex ].hideSymbol( symbolIndex );
			this.playOverlayAnimation( wildType, this.baseOverlayIndex, {
				reelIndex,
				symbolIndex
			} );
			this.completedAnimationCounter++;
		}
	}

	public hideSymbolFromCurrentView ( reelIndex: number, symbolIndex: number ): void {
		this.currentReelView[ reelIndex ].hideSymbol( symbolIndex );
	}
	public showSymbolFromCurrentView ( reelIndex: number, symbolIndex: number ): void {
		this.currentReelView[ reelIndex ].showSymbol( symbolIndex );
	}

	public moveLastSymbolToTop ( reelIndex: number, symbolData: SymbolData, state: SymbolState, spinDirection: SpinDirection ): void {
		this.currentReelView[ reelIndex ].moveLastSymbolToTop( symbolData, state, spinDirection );
	}

	public tweenSymbolsUpdate ( reelIndex: number, symbolIndex: number, y: number ): void {
		this.currentReelView[ reelIndex ].tweenSymbolsUpdate( symbolIndex, y );
	}

	public setSymbolStateFromPositions ( winTable: Array<Array<boolean>>, winState: SymbolState, loseState: SymbolState ): void {
		winTable.forEach( ( reelPosition, reelIndex ) => {
			reelPosition.forEach( ( value, symbolIndex ) => {
				const symbolState = ( value ) ? winState : loseState;
				this.currentReelView[ reelIndex ].setSymbolState( symbolIndex, symbolState );
			} );
		} );
	}

	public deleteAllAnimations (): void {
		let numTimers = this.idleTimerArray.length;
		for ( let i = 0; i < numTimers; i++ ) {
			let timer = this.idleTimerArray.pop();
			timer.stop();
			timer.destroy();
		}

		this.completedAnimationCounter = 0;
	}

	/**
	 * when we try show drop animation, we hide static symbol and show overlay symbol
	 */
	public symbolDropAnimation ( reelIndex: number, symbolIndex: number ): void {
		this.playOverlayAnimation( SymbolAnimationPostfix.DROPPED, this.baseOverlayIndex, {
			reelIndex,
			symbolIndex
		} );
	}

	//#region IDLE_SYMBOL_HANDLE
	/**
	 * when we try show idle animation, we hide static symbol and show overlay symbol
	 */
	public playIdleAnimationsLoop (): void {
		this.deleteAllAnimations();
		this.currentReelSet.forEach( ( reel, reelIndex ) => {
			for ( let symbolIndex = 0; symbolIndex < reel.symbolsOnReel; symbolIndex++ ) {
				this.playOverlayAnimation( SymbolAnimationPostfix.IDLE, this.baseOverlayIndex, {
					reelIndex,
					symbolIndex
				} );
			}
		} );
	}

	public playRandomIdleAnimations (): void {
		this.deleteAllAnimations();
		this.currentReelSet.forEach( ( reel, reelIndex ) => {
			for ( let symbolIndex = 0; symbolIndex < reel.symbolsOnReel; symbolIndex++ ) {
				let randomData = this.reelConfig.idleAnimationRandomChance;
				if ( Math.random() < randomData.percent ) {
					let time = 1000 * ( ( Math.random() * randomData.startDelayMax ) + randomData.startDelayMin );
					this.idleTimerArray.push( new SimpleTimer( time, () => {
						this.playOverlayAnimation( SymbolAnimationPostfix.IDLE, this.baseOverlayIndex, {
							reelIndex,
							symbolIndex
						} );
					} ) );
				}
			}
		} );
	}


	//#endregion IDLE_SYMBOL_HANDLE

	//#region SHAKING_EFFECT
	public playShaking ( reelIndex: number ): boolean {
		if ( !this.reelConfig.symbolDefinition.shakeConfig ) {
			return false;
		}

		for ( let symbolIndex = 0; symbolIndex < this.currentReelSet[ reelIndex ].symbolsOnReel; symbolIndex++ ) {
			this.currentReelView[ reelIndex ].hideSymbol( symbolIndex );
			this.reelOverlayMap.get( this.baseOverlayIndex ).playSymbolShaking( {
				reelIndex,
				symbolIndex
			} );
		}
		return true;
	}

	public stopShaking (): void {
		this.currentReelSet.forEach( ( reel, reelIndex ) => {
			for ( let symbolIndex = 0; symbolIndex < reel.symbolsOnReel; symbolIndex++ ) {
				const restorePosition = {
					x: this.currentReelView[ reelIndex ].getSymbolContainer( symbolIndex ).x,
					y: this.currentReelView[ reelIndex ].getSymbolContainer( symbolIndex ).y,
				};

				this.currentReelView[ reelIndex ].showSymbol( symbolIndex );
				this.reelOverlayMap.get( this.baseOverlayIndex ).stopSymbolShaking( {
					reelIndex,
					symbolIndex
				}, restorePosition );
			}
		} );

		this.cleanAllOverlays();
	}
	//#endregion SHAKING_EFFECT

	//#region ANTICIPATE_HANDLE
	public anticipateReel ( reelId: number ): void {
		if ( !this.anticipation ) {
			return;
		}
		this.anticipation.anticipateReel( this.currentReelView[ reelId ], reelId );
	}

	public playAnticipationIntro ( reelId: number ): void {
		if ( !this.anticipation ) {
			return;
		}
		this.anticipation.playAnticipationIntro( this.currentReelView[ reelId ], reelId );
	}

	public anticipateScatters ( currentReelIndex: number, dropped: Array<SymbolPosition> ): void {
		dropped.forEach( ( position: SymbolPosition, index: number ) => {
			if ( position.reelIndex === currentReelIndex ) {
				this.playOverlayAnimation( SymbolAnimationPostfix.ANTICIPATION, this.baseOverlayIndex, position );
			}
		} );
	}

	public stopAnticipate (): void {
		if ( !this.anticipation ) {
			return;
		}
		this.anticipation.stopAnticipate();
	}
	//#endregion ANTICIPATE_HANDLE

	public zoom ( scale: number, position: PIXI.Point ): void {
		this.reelsContainer.scale = new PIXI.Point( scale, scale );
		this.reelsContainer.position = position;
	}

	public getReelStripPosition ( index: number ): PIXI.Point {
		return this.currentReelView[ index ].position;
	}

	public setReelStripPosition ( index: number, position: PIXI.Point ): void {
		this.currentReelView[ index ].position = position;
	}

	public getStripOriginalPosition ( index: number ): PIXI.Point {
		return this.currentReelView[ index ].originalPosition;
	}

	//#region LAYOUT_HANDLE

	public positionUpdateOnTransition (): void {
		this.updateReelPosition( this.viewport.orientation );
	}

	public portraitLayout () {
		super.portraitLayout();
		this.updateContainerPosition();
		this.updateReelPosition( Orientation.PORTRAIT );
		this.updateMainReelMask();
	}

	public landscapeLayout () {
		super.landscapeLayout();
		this.updateContainerPosition();
		this.updateReelPosition( Orientation.LANDSCAPE );
		this.updateMainReelMask();
	}
	//#endregion LAYOUT_HANDLE

	//#region MASK_CONTROL
	protected updateMainReelMask (): void {
		if ( this.maskContainer.mask ) {
			let oldMask = this.maskContainer.mask;
			this.maskContainer.mask = undefined;
			this.maskContainer.removeChild( oldMask );
		}
		const mask = this.createReelMask();
		this.maskContainer.mask = mask;
		this.maskContainer.addChild( mask );
	}

	protected createReelMask (): Graphics | Sprite {
		if ( this.reelConfig.orientationMaskData || this.reelConfig.defaultReelsMask ) {
			const mask = this.maskConfig;
			if ( this.isMaskData( mask ) ) {
				return this.getReelMask( mask.x, mask.y, mask.width, mask.height );
			} else {
				return Sprite.fromFrame( mask );
			}
		} else {
			return this.getDefaultGameMask();
		}
	}

	protected getDefaultGameMask (): Graphics {
		const firstConfig = this.reelSet[ this.currentReelSetIndex ][ 0 ];
		const firstConfigPosition = this.getSingleReelLayoutData( this.currentReelSetIndex, 0 ).position;

		const lastConfig = this.reelSet[ this.currentReelSetIndex ][ this.reelSet[ this.currentReelSetIndex ].length - 1 ];
		const lastConfigPosition = this.getSingleReelLayoutData( this.currentReelSetIndex, this.reelSet[ this.currentReelSetIndex ].length - 1 ).position;
		return this.getReelMask(
			firstConfigPosition.x,
			firstConfigPosition.y,
			lastConfigPosition.x + lastConfig.width - firstConfigPosition.x,
			firstConfig.symbol.height * firstConfig.symbolsOnReel
		);
	}

	protected isMaskData ( mask: string | IMask ): mask is IMask {
		return ( <IMask> mask ).x !== undefined;
	}

	//#endregion MASK_CONTROL
}


