import { mesh, utils } from 'pixi.js';
import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { CoreElement, ICoreElementConfig } from 'corengf/src/elements/canvas/CoreElement';

/**
 * The Window Allows you to stretch the sprite.
 * Reference: http://pixijs.download/dev/docs/PIXI.NineSlicePlane.html
 *
 * @export
 * @class Window
 * @extends {Sprite}
 */
export class Window extends CoreElement {

	protected plane: mesh.NineSlicePlane;

	protected initElement ( config: IWindowConfig ): void {
		config.assetName = config.assetName ? config.assetName : 'window.png';
		this.plane = this.getCustomWindow( config );
		this.addChild( this.plane );
	}

	protected getCustomWindow ( config: IWindowConfig ): mesh.NineSlicePlane {
		return new mesh.NineSlicePlane( utils.TextureCache[ config.assetName ], config.leftWidth, config.topHeight, config.rightWidth, config.bottomHeight );
	}

	public updateAttributes ( config: IWindowConfig ): void {
		if ( config == undefined ) {
			return;
		}
		super.updateAttributes( config );

		if ( config.windowSize ) {
			this.plane.width = config.windowSize.x;
			this.plane.height = config.windowSize.y;
		}
	}
}

export interface IWindowConfig extends ICoreElementConfig {
	assetName?: string;
	leftWidth?: number;
	topHeight?: number;
	rightWidth?: number;
	bottomHeight?: number;
	windowSize: IPoint;
}


