import { Inject } from 'typescript-ioc/es5';
import { GameConfig } from 'corengf/src/core/GameConfig';
import { OrientationHintView } from './OrientationHintView';
import { MainConfig } from 'corengf/src/core/config/MainConfig';
import { IMainConfig } from 'corengf/src/core/config/IMainConfig';


export class OrientationHintController {
	@Inject
	protected gameConfig: GameConfig;

	protected view: OrientationHintView;

	constructor () {
		const mainConfig: IMainConfig = this.gameConfig.getConfig<IMainConfig>( MainConfig );
		this.view = new OrientationHintView();
		this.view.initialize( mainConfig.orientation );
	}
}


