import { Inject } from 'typescript-ioc/es5';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import { EventParams } from 'corengf/src/events/GlobalDispatcher';
import { MarqueeDisplayMode } from 'corengf/src/elements/canvas/scroll/ScrollMarquee';
import { SlotMarqueeController } from 'slotngf/src/components/marquee/SlotMarqueeController';
import { DHBSlotMarqueeView } from 'game/src/components/marquee/DHBSlotMarqueeView';

export class DHBSlotMarqueeController extends SlotMarqueeController {

	protected view: DHBSlotMarqueeView;

	constructor ( @Inject view: DHBSlotMarqueeView ) {
		super( view );
	}

	protected onBigWinCountComplete ( e: EventParams ) {
		// set marquee on allLineWin / freeSpinsAllLineWin
	}

	protected allLineWin ( data: any ): void {
		this.setMarquee( this.getTotalWinInfo( data.totalWin, this.config.marqueeLabelTotalWinKey ), MarqueeDisplayMode.FIXED );
		this.finishStateAction();
	}

	protected freeSpinsAllLineWin ( data: any ): void {
		this.setMarquee( this.getTotalWinInfo( data.totalWin, this.config.marqueeLabelTotalWinKey ), MarqueeDisplayMode.FIXED );
		this.finishStateAction();
	}

	protected freeSpinsStart ( data: any ): void {
		//total freeSpinsWinAmount include pick game's winnings.
		this.setMarquee( this.getTotalFeatureWinInfo( data.currentFeatureWin, this.config.marqueeLabelFeatureTotalWinKey ), MarqueeDisplayMode.FIXED );
		this.finishStateAction();
	}

	protected showRetrigger ( stateData: any ): void {
		new SimpleTimer( 1500, () => {
			this.finishStateAction();
		} );
	}

}


