import { Actions } from 'corengf/src/utils/Actions';
import { Button, IButtonConfig } from 'corengf/src/elements/canvas/button/Button';
import { LocalButtonsController } from 'corengf/src/buttons/LocalButtonsController';

/**
 * Toggle Button that supports few extra states:
 * - Enabled ( unselected )
 * - Disabled ( unselected )
 * - Over ( unselected )
 * - Down ( unselected )
 * - Enabled ( selected )
 * - Down ( selected )
 * - Over ( selected )
 */
export class ToggleButton extends Button {
	protected _selected: boolean;

	protected readonly enabledSelectedFrame = '_Selected_Enabled.png';
	protected readonly overSelectedFrame = '_Selected_Over.png';
	protected readonly downSelectedFrame = '_Selected_Down.png';

	public enabledSelectedElement: PIXI.Texture;
	public overSelectedElement: PIXI.Texture;
	public downSelectedElement: PIXI.Texture;

	constructor ( config: IButtonConfig, localButtonsController?: LocalButtonsController ) {
		super( config, localButtonsController );

		// TODO: Add selected as param or function
		this._selected = false;

		this.enabledSelectedElement = this.getTexture( this.enabledSelectedFrame );
		this.overSelectedElement = this.getTexture( this.overSelectedFrame );
		this.downSelectedElement = this.getTexture( this.downSelectedFrame );
	}

	public set selected ( selected: boolean ) {
		this._selected = selected;
		if ( selected ) {
			this.onPointerUp();
		} else {
			this.onPointerOut();
		}
	}

	public get selected (): boolean {
		return this._selected;
	}

	public onClick ( data: any = {} ) {
		this.selected = !this.selected;
		this.playSoundEffect();
		this.onPointerOver();

		data.selected = this.selected;
		if ( this.selected ) {
			data.action = Actions.TOGGLE_SELECTED_MARKER + this.action;
			this.elementSignal.dispatch( data );
		} else {
			data.action = Actions.TOGGLE_UNSELECTED_MARKER + this.action;
			this.elementSignal.dispatch( data );
		}
	}

	protected showEnabledElement () {
		if ( this.selected ) {
			this.activeElement.texture = this.enabledSelectedElement;
		} else {
			this.activeElement.texture = this.enabledElement;
		}
	}

	protected onPointerDown () {
		if ( this.selected ) {
			this.activeElement.texture = this.downSelectedElement;
		} else {
			this.activeElement.texture = this.downElement;
		}
	}

	protected onMouseUpOutside (): void {
		if ( this.selected ) {
			this.activeElement.texture = this.overSelectedElement;
		} else {
			this.showEnabledElement();
		}
	}

	protected onPointerUp () {
		this.onPointerOver();
	}

	protected onPointerOver () {
		if ( this.selected ) {
			this.activeElement.texture = this.overSelectedElement;
		} else {
			this.activeElement.texture = this.overElement;
		}
	}
}


