import { WebElementFactory } from 'corengf/src/factory/WebElementFactory';
import { CoreWebElement, ICoreWebElementConfig } from 'corengf/src/elements/webpage/CoreWebElement';

/**
 * block for web elements
 */
export class WebElementBlock extends CoreWebElement {

	public static elementType: string = 'WEB_ELEMENT_BLOCK';

	public createElement<T extends ICoreWebElementConfig> ( blockConfig: IWebElementBlockConfig<T> ): IWebElementBlockConfig<T> {
		for ( var i = 0; i < blockConfig.config.length; i++ ) {
			blockConfig.config[ i ] = WebElementFactory.createInstance( blockConfig.config[ i ] );
		}
		return blockConfig;
	}
}

export interface IWebElementBlockConfig<T extends ICoreWebElementConfig> extends ICoreWebElementConfig {
	config: Array<T>;
}



