import * as MiniSignal from 'mini-signals';
import { AbstractSymbol } from './AbstractSymbol';
import { SpineAnimation, ISpineAnimationConfig } from 'corengf/src/elements/canvas/animation/spine/SpineAnimation';
import { SymbolAnimationPostfix } from 'slotngf/src/components/reels/ReelsEnums';


export class SpineSymbol extends AbstractSymbol {
	protected _symbolContainer: SpineAnimation;

	public get symbolContainer (): SpineAnimation {
		return this._symbolContainer;
	}

	constructor ( config: ISpineAnimationConfig ) {
		super();
		this.symbolName = config.animationName;
		this._symbolContainer = new SpineAnimation( config );
		this.animationCompleteSignal = new MiniSignal();
	}

	public playAnimation ( type: string | SymbolAnimationPostfix ): boolean {
		const animationName = this.getAnimationName( type );
		this._symbolContainer.play( 0, animationName, false );
		this._symbolContainer.completeSignal.add( () => {
			this.animationCompleteSignal.dispatch( this.position, type );
		} );

		return true;
	}

	protected getAnimationName ( type: string | SymbolAnimationPostfix ): string {
		return `this.symbolName${ type }`;
	}
}


