import { Inject } from 'typescript-ioc/es5';
import { Helper } from 'corengf/src/utils/Helper';
import { Scenario } from 'corengf/src/states/Scenario';
import { IListener } from 'corengf/src/events/GlobalDispatcher';
import { UrlParameters } from 'corengf/src/utils/UrlParameters';
import { SlotStates } from 'slotngf/src/states/SlotStates';
import { SlotBankService } from 'slotngf/src/core/SlotBankService';
import { SlotNGModel } from 'slotngf/src/server/ng/model/SlotNGModel';
import { SlotScenarioNames } from 'slotngf/src/states/SlotScenarioNames';
import { SlotScenarioManager } from 'slotngf/src/core/SlotScenarioManager';
import { IFeatureInfo } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { SlotNGServerConfig } from 'slotngf/src/server/ng/config/SlotNGServerConfig';
import { SlotHttpFormatter } from 'slotngf/src/server/abstract/formatter/SlotHttpFormatter';
import { ISlotNGServerConfig, ISlotPathNGServerConfig } from 'slotngf/src/server/ng/config/ISlotNGServerConfig';


export class SlotNGHttpFormatter extends SlotHttpFormatter implements IListener {

	public static readonly PLAY_MODE_API_PATH: string = 'devPath';

	public static readonly REAL_MODE_API_PATH: string = 'path';

	@Inject
	protected slotNGModel: SlotNGModel;

	@Inject
	protected bankService: SlotBankService;

	@Inject
	protected scenarioManager: SlotScenarioManager;

	@Inject
	protected scenarioNames: SlotScenarioNames;

	@Inject
	protected stateNames: SlotStates;

	protected config: ISlotNGServerConfig;

	protected path: ISlotPathNGServerConfig;

	constructor () {
		super();

		this.config = this.gameConfig.getConfig<ISlotNGServerConfig>( SlotNGServerConfig );
		this.path = ( this.config ) ? this.config[ this.getApiPathByMode() ] : null;
		this.addEventListeners();
	}

	public addEventListeners (): void {
		//
	}

	protected getApiPathByMode (): string {
		return !UrlParameters.isRealMode() ? SlotNGHttpFormatter.PLAY_MODE_API_PATH : SlotNGHttpFormatter.REAL_MODE_API_PATH;
	}

	/**
	 * reveals data from servers message into framework-friendly format
	 * @param message original server's message
	 */
	public processIncomingMessage ( message: any, featureInfo?: IFeatureInfo ): Scenario {
		return null;
	}

	protected setRoundCompletedFlag ( scenario: Scenario, states: Array<string>, roundCompleted: boolean = false ): void {

		let skipSetBaseGame = scenario.hasStateName( this.stateNames.allLineWin );

		states.forEach( ( stateName ) => {
			if ( skipSetBaseGame && stateName === this.stateNames.baseGameIdle ) {
				return;
			}

			if ( scenario.hasStateName( stateName ) ) {
				scenario.addStateData( stateName, {
					roundCompleted: roundCompleted
				} );
			}
		} );
	}

	protected isAllFeatureCompleted ( features: Array<IFeatureInfo> ): boolean {
		if ( !features ) {
			throw new Error( 'features is undefined.' );
		}

		return features.filter( feature => feature.complete ).length === features.length;
	}

	protected margeScenarioStateData ( scenario: Scenario, stateName: string, extraStateData: object ): Scenario {
		let tmpScenario: Scenario = scenario;
		if ( extraStateData && tmpScenario.hasStateName( stateName ) ) {
			if ( tmpScenario.getStateByName( stateName ).stateData ) {
				tmpScenario.setStateData( stateName,
					Helper.mergeTwoObjects( tmpScenario.getStateByName( stateName ).stateData, extraStateData ) );
			} else {
				if ( tmpScenario.getStateByName( stateName ).stateData ) {
					tmpScenario.setStateData( stateName, extraStateData );
				}
			}
			return tmpScenario;
		}
	}
}



