import { Scenario } from 'corengf/src/states/Scenario';
import { IWinningData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { IGameState, IFeatureInfo, ISpinResult } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { IReplacingSymbolsDetail } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { SpinResponseFormatter } from 'slotngf/src/server/ng/formatter/http/in/SpinResponseFormatter';
import { AbstractFeatureScenarioFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/AbstractFeatureScenarioFormatter';

/**
 * formatting wild multiplier feature
 */
export class ReplacingSymbolsFeatureFormatter extends SpinResponseFormatter implements AbstractFeatureScenarioFormatter {

	/**
	 * formatting run feature api response
	 * @param message
	 */
	public processIncomingMessage ( message: IGameState, featureInfo?: IFeatureInfo ): Scenario {
		console.warn( 'here shouldn\'t be triggered, since replacing symbols feature don\'t need another transaction to complete feature' );
		return null;
	}

	public getFeatureScenarioBeforeFreeSpinStop ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return freeSpinStopScenario;
	}

	public getFeatureScenarioAfterSpinStop ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		const reelStopSymbols = { reelIndexes: this.getReelStopSymbols( message, featureInfo ) };

		if ( freeSpinStopScenario.hasStateName( this.stateNames.freeSpinsStop ) ) {
			this.margeScenarioStateData( freeSpinStopScenario,
				this.stateNames.freeSpinsStop,
				reelStopSymbols
			);
		}

		if ( freeSpinStopScenario.hasStateName( this.stateNames.spinStop ) ) {
			this.margeScenarioStateData( freeSpinStopScenario,
				this.stateNames.spinStop,
				reelStopSymbols
			);
		}
		return freeSpinStopScenario;
	}

	protected getReelStopSymbols ( message: IGameState, featureInfo: IFeatureInfo ): Array<Array<string>> {
		let result: Array<Array<string>> = this.getReelSymbols( message.spin_result.reels );

		if ( featureInfo != undefined ) {
			let replacingFeatureDetail: IReplacingSymbolsDetail = this.getReplacingSymbolsDetail( featureInfo );
			result = this.getReelSymbols( replacingFeatureDetail.original_reels );
		}
		return result;
	}

	/**
	 * formatting feature in feature_triggered
	 */
	public triggeredFeatureIncomingMessage ( message: IGameState, featureInfo?: IFeatureInfo ): Scenario {
		if ( featureInfo == undefined ) {
			return null;
		}
		return this.getReplacingSymbolsScenario( message, featureInfo );
	}

	protected getReplacingSymbolsScenario ( message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		let responseScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.replacingSymbols );
		responseScenario.setStateData( this.stateNames.replacingSymbols, featureInfo.feature_state );
		let count: number = 0;
		let replacingData: IReplacingSymbolsDetail = this.getReplacingSymbolsDetail( featureInfo );
		for ( let i: number = 0; i < replacingData.swapped_symbols.length; i++ ) {
			for ( let j: number = 0; j < replacingData.swapped_symbols[ i ].symbols.length; j++ ) {
				let name: string = replacingData.swapped_symbols[ i ].symbols[ j ].symbol;
				if ( name != '*' ) {
					count++;
				}
			}
		}
		responseScenario.addStateData( this.stateNames.replacingSymbols, { count: count } );
		return responseScenario;
	}

	protected getReplacingSymbolsDetail ( featureInfo: IFeatureInfo ): any {
		return featureInfo.feature_state;
	}

	public getFeatureShowWinningData ( winningData: IWinningData, spinResult: ISpinResult, featureInfo: IFeatureInfo ): IWinningData {
		return winningData;
	}

	public getFeatureScenarioAfterWin ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return freeSpinStopScenario;
	}

	/**
	 * formatting feature while restore feature
	 */
	public restoreFeatureIncomingMessage ( replacingSymbolsDetail: IReplacingSymbolsDetail ): Scenario {
		let replacingSymbolFeatureScenario: Scenario = null;
		console.warn( 'didn\'t plan for restore replacing symbol feature, since it won\'t create another transection' );
		return replacingSymbolFeatureScenario;
	}

	public getFeatureRestoreScenario ( restoreScenario: Scenario, featureInfo: IFeatureInfo ): Scenario {
		return restoreScenario;
	}
}



