import { TsMap } from 'corengf/src/utils/TsMap';
import { Orientation } from 'corengf/src/display/Viewport';
import { Sprite, ISprite } from 'corengf/src/elements/canvas/sprite/Sprite';
import { ICoreElementLayoutConfig } from 'corengf/src/elements/canvas/CoreElement';
import { UIBankConfig, IUIBankConfig, IUIBankSpacingConfig } from 'corengf/src/components/ui/components/bank/UIBankConfig';
import { BitmapTextField } from 'corengf/src/elements/canvas/text/BitmapTextField';
import { ICoreUISpriteElementConfig, ICoreUITextFieldElementConfig } from 'corengf/src/factory/CoreUIElementFactory';


export class MLUIBankConfig extends UIBankConfig implements IMLUIBankConfig {

	public name: string = 'bankPanel';

	public totalBetIcon: ICoreUISpriteElementConfig = {
		elementType: Sprite.elementType,
		elementConfig: {
			name: 'totalBetIcon',
			isOrientationSupported: true,
			position: { x: 25, y: 1231 },
			assetName: 'bet.png'
		}
	};

	public bankBackgroundBar: ISprite = {
		name: 'bankBackgroundBar',
		assetName: 'bankBackground.png',
		isOrientationSupported: true,
		position: { x: 0, y: 1220 },
		scale: { x: 3, y: 1 }
	};

	public totalBetValue: ICoreUITextFieldElementConfig = {
		elementType: BitmapTextField.elementType,
		elementConfig: {
			name: 'totalBetValue',
			key: 'totalBetValue',
			text: '',
			isOrientationSupported: true,
			position: { x: 70, y: 1235 },
			autoscale: { x: 238, y: 28, },
			style: {
				align: 'left',
				font: {
					name: 'BankFont',
					size: 24
				}
			}
		}
	};

	public balanceIcon: ICoreUISpriteElementConfig = {
		elementType: Sprite.elementType,
		elementConfig: {
			name: 'balanceIcon',
			isOrientationSupported: true,
			position: { x: 25, y: 1231 },
			assetName: 'balance.png'
		}
	};

	public balanceValue: ICoreUITextFieldElementConfig = {
		elementType: BitmapTextField.elementType,
		elementConfig: {
			name: 'balanceValue',
			key: 'balanceValue',
			isOrientationSupported: true,
			text: '',
			position: { x: 665, y: 1235 },
			autoscale: { x: 238, y: 28, },
			style: {
				align: 'left',
				font: {
					name: 'BankFont',
					size: 24
				}
			}
		}
	};

	public spacing: IMLUIBankSpacingConfig = {
		iconSpacing: 10,
		padding: 720 - 25,
		landscapePadding: 1280 - 235,
	};

	public coreElementOrientationConfig: TsMap<Orientation, TsMap<string, ICoreElementLayoutConfig>> = new TsMap( [
		[
			Orientation.PORTRAIT, new TsMap( [
				[
					this.totalBetValue.elementConfig.name, {
						position: { x: 70, y: 1235 }
					}
				],
				[
					this.totalBetIcon.elementConfig.name, {
						position: { x: 25, y: 1231 }
					}
				],
				[
					this.balanceValue.elementConfig.name, {
						position: { x: 430, y: 1235 }
					}
				],
				[
					this.balanceIcon.elementConfig.name, {
						position: { x: 385, y: 1231 }
					}
				],
				[
					this.bankBackgroundBar.name, {
						position: this.bankBackgroundBar.position,
						scale: { x: 36, y: 1 }
					}
				],
			] )
		],
		[
			Orientation.LANDSCAPE, new TsMap( [
				[
					this.totalBetValue.elementConfig.name, {
						position: { x: 258, y: 674 }
					}
				],
				[
					this.totalBetIcon.elementConfig.name, {
						position: { x: 240, y: 674 }
					}
				],
				[
					this.balanceValue.elementConfig.name, {
						position: { x: 978, y: 674 }
					}
				],
				[
					this.balanceIcon.elementConfig.name, {
						position: { x: 960, y: 674 }
					}
				],
				[
					this.bankBackgroundBar.name, {
						position: { x: 0, y: 660 },
						scale: { x: 7, y: 1 }
					}
				],
			] )
		]
	] );
}

export interface IMLUIBankConfig extends IUIBankConfig {
	spacing: IMLUIBankSpacingConfig;
	bankBackgroundBar: ISprite;
}

export interface IMLUIBankSpacingConfig extends IUIBankSpacingConfig {
	landscapePadding: number;
}



