import { Asset } from 'corengf/src/core/AssetLoader';
import { SmartPreloaderView } from 'corengf/src/components/preloader/SmartPreloaderView';

export class DHBSmartPreloaderView extends SmartPreloaderView {

	public getAssetsList (): Array<Asset> {
		return [
			{
				assetKey: 'preloaderBackground',
				assetURL: 'assets/preloader/{partner}/{locale}/preloaderBackground.json'
			},
			{
				assetKey: 'preloaderUI',
				assetURL: 'assets/preloader/{locale}/preloaderUI.json'
			}
		];
	}
}


