import { Helper } from 'corengf/src/utils/Helper';

export class UrlParameters {
	protected static LANGUAGE: string = 'language';
	protected static SESSION: string = 'session';
	protected static PARTNER: string = 'partner';
	protected static PARTNER_CODE: string = 'partnerCode';
	protected static GS: string = 'gs';
	protected static MODE: string = 'mode';
	protected static MESSENGER: string = 'messenger';
	protected static RETURN_URL: string = 'returnurl';
	protected static HISTORY: string = 'history';
	protected static ENGAGEMENTS: string = 'engagements';
	protected static SDK: string = 'sdk';
	protected static SDK_VERSION: string = 'sdkVersion';

	public static TRUE = 'true';
	public static FALSE = 'false';

	/**
	 * get partner token from url
	 */
	public static get partnerToken (): string {
		let partnerToken: string = this.getURLParameter( UrlParameters.PARTNER );
		return partnerToken;
	}

	/**
	 * get partner code from url
	 */
	public static get partnerCode (): string {
		let partnerCode: string = this.getURLParameter( UrlParameters.PARTNER_CODE );
		return partnerCode == undefined ? Helper.EMPTY : partnerCode;
	}

	/**
 	 * Returns param value
	 * example for http://example.com/path/to/page?name=ferret&color=purple getURLParameter( 'color' ) will be 'purple'
	 * @param name searching param
	 * @returns searching value
	 */
	public static getURLParameter ( name: string ): string {
		return decodeURIComponent( ( new RegExp( '[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)' ).exec( location.search )
			|| [ null, '' ] )[ 1 ].replace( /\+/g, '%20' ) ) || null;
	}

	/**
	 * get session token from url
	 * remove hard code once rgs fixed
	 */
	public static get sessionToken (): string {
		let session: string = this.getURLParameter( UrlParameters.SESSION );
		if ( !UrlParameters.isRealMode() ) {
			let gs: string = this.getURLParameter( UrlParameters.GS );
			if ( gs == 'nurgs-rmx1' ) {
				session = 'a40a269e-f0aa-d16f-63e6-00fd6117afc8-GNS_HouseDemoCNY';
			} else if ( gs == 'demo-nurgs-krug' ) {
				session = '760e878c-482a-4e95-92fa-8920a463e525-GNS_Krug1HouseDemoCNY';
			} else { // session without currency
				session = '0f7efb4f47e1dd49cba3bac1bc3d6455';
			}
		}
		return session;
	}

	/**
	 * get language from url, make sure it's in lower case
	 */
	public static get language (): string {
		let lan = this.getURLParameter( this.LANGUAGE );
		if ( lan ) {
			lan = lan.toLowerCase();
		}
		return lan;
	}

	/**
	 * get gs info from url
	 */
	public static get gs (): string {
		return this.getURLParameter( this.GS );
	}

	/**
 	 * get return url from url
 	 */
	public static get returnUrl (): string {
		return this.getURLParameter( UrlParameters.RETURN_URL );
	}

	/**
	 * get engagements data from url if it's demo mode
	 */
	public static get engagements (): Array<string> {
		if ( !this.isRealMode() ) {
			let engagements: string = this.getURLParameter( UrlParameters.ENGAGEMENTS );
			return engagements ? engagements.split( ',' ) : null;
		} else {
			return null;
		}
	}

	/**
	 * get sdk info from url
	 */
	public static get sdk (): string {
		return this.getURLParameter( UrlParameters.SDK );
	}

	/**
	 * get sdk version info from url,
	 * if not in url, will use defult setting in sdk controller
	 */
	public static get sdkVersion (): string {
		return ( this.getURLParameter( UrlParameters.SDK_VERSION ) ) ? this.getURLParameter( UrlParameters.SDK_VERSION ) : Helper.EMPTY;
	}

	/**
	 * Check if it's using play mode.
	 */
	public static isRealMode (): boolean {
		let mode = this.getURLParameter( UrlParameters.MODE );
		if ( mode == undefined ) {
			mode = Mode.REAL;
		}
		return mode.toLowerCase() !== Mode.PLAY;
	}

	/**
	 * if mode = play && !isRealMode(), return true
	 */
	public static isDemoMode (): boolean {
		if ( !this.isRealMode() && this.engagements ) {
			return true;
		} else {
			return false;
		}
	}

	/**
	 * Check if it's hide/show history.
	 * if UrlParameters.HISTORY == flase || is demo mode
	 * isHistoryVisible() return false
	 */
	public static isHistoryVisible (): boolean {
		let history: string = this.getURLParameter( UrlParameters.HISTORY );
		if ( !this.isRealMode() ) {
			return false;
		}
		return ( ( history != undefined ) ? ( history.toLocaleLowerCase() == this.TRUE ) : true );
	}

	/**
	 * Check if showing messenger.
	 * if it's play mode, disable messenger.
	 */
	public static isShowMessage (): boolean {
		if ( !this.isRealMode() ) {
			return false;
		} else {
			let isMessenger = this.getURLParameter( UrlParameters.MESSENGER );
			if ( isMessenger == undefined ) {
				isMessenger = this.TRUE;
			}
			return isMessenger.toLowerCase() === this.TRUE;
		}
	}
}

export enum Mode {
	PLAY = 'play',
	REAL = 'real'
}


