import { Inject } from 'typescript-ioc/es5';
import { Helper } from 'corengf/src/utils/Helper';
import { Translator } from 'corengf/src/utils/Translator';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { ExternalModuleController } from 'corengf/src/components/externalModule/ExternalModuleController';
import { MLUIExternalModuleManager } from './MLUIExternalModuleManager';
import { IGeneralWebPageConfig } from './../config/GeneralWebPageConfig';


export class MLUIExternalModuleController extends ExternalModuleController {
	public static readonly CLOSE_EXTERNAL_MODULE: string = 'closeExternalModule';
	public static readonly OPEN_EXTERNAL_MODULE: string = 'openExternalModule';

	@Inject
	protected translator: Translator;

	@Inject
	protected externalModule: MLUIExternalModuleManager;

	protected config: IGeneralWebPageConfig;

	/**
	 * marge localisation map by given language keys
	 * @param loader
	 */
	protected getLocalisationMap ( loader: PIXI.loaders.Loader ): object {
		const languageData = loader.resources[ this.translator.language ].data;

		let localisationMap = languageData[ this.config.localisationKeys[ 0 ] ] || {};

		for ( var i = 1; i < this.config.localisationKeys.length; i++ ) {
			if ( languageData[ this.config.localisationKeys[ i ] ] ) {
				localisationMap = Helper.mergeTwoObjects( localisationMap, languageData[ this.config.localisationKeys[ i ] ] );
			}
		}

		return localisationMap;
	}

	protected getExternalModuleConfig ( data?: any ): IGeneralWebPageConfig {
		return this.config;
	}

	protected getPortraitContainerConfig (): any {
		return this.config.portraitLayoutConfig.containerConfig;
	}

	protected getLandscapeContainerConfig (): any {
		return this.config.landscapeLayoutConfig.containerConfig;
	}


	protected onOpen (): any {
		super.onOpen();
		GlobalDispatcher.dispatchIntoAllStates( MLUIExternalModuleController.OPEN_EXTERNAL_MODULE );
		return null;
	}

	protected onClose (): any {
		super.onClose();
		GlobalDispatcher.dispatchIntoAllStates( MLUIExternalModuleController.CLOSE_EXTERNAL_MODULE );
		return null;
	}
}


