import { Texture } from 'pixi.js';
import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { ICoreElementConfig } from 'corengf/src/elements/canvas/CoreElement';


export class Sprite extends PIXI.Sprite {

	public static elementType: string = 'Sprite';

	public isOrientationSupported: boolean = false;

	constructor ( config: ISprite ) {
		super();
		this.name = config.name;
		this.isOrientationSupported = config.isOrientationSupported;

		this.updateAttributes( config );
	}

	public updateAttributes ( config: ISprite ): void {
		if ( config == undefined ) {
			return;
		}

		// set texture from config
		this.setTexture( config.assetName );

		// set visible from config
		this.setVisible( config.visible );

		// set position from config
		this.setPosition( config.position );

		// set scale from config
		this.setScale( config.scale );

		// set anchor from config
		this.setAnchor( config.anchor );

		// set blend mode from config
		this.setBlendMode( config.blendMode );
	}

	/**
	 * set is sprite texture based on value in config.
	 */
	protected setTexture ( assetName: string ): void {
		if ( assetName != undefined ) {
			this._texture = Texture.from( assetName );
		}
	}

	/**
	 * set is visible based on value in config.
	 */
	protected setVisible ( visible: boolean ): void {
		if ( visible != undefined ) {
			this.visible = visible;
		}
	}

	/**
	 * set position based on value in config.
	 */
	protected setPosition ( position: IPoint ): void {
		if ( position != undefined ) {
			this.position.set( position.x, position.y );
		}
	}

	/**
	 * set scale based on value in config.
	 */
	protected setScale ( scale: IPoint ): void {
		if ( scale != undefined ) {
			this.scale.set( scale.x, scale.y );
		}
	}

	/**
	 * set anchor based on value in config.
	 */
	protected setAnchor ( anchor: IPoint ): void {
		if ( anchor != undefined ) {
			this.anchor.set( anchor.x, anchor.y );
		}
	}

	/**
	 * set the blend mode based on value in config
	 */
	protected setBlendMode ( blendMode: number ): void {
		if ( blendMode != undefined ) {
			this.blendMode = blendMode;
		}
	}
}

export interface ISprite extends ICoreElementConfig {
	alpha?: number;
	visible?: boolean;
	assetName: string;
	blendMode?: number;
}



