import { Container } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { CoreState } from 'corengf/src/states/CoreState';
import { Scenario } from 'corengf/src/states/Scenario';
import { IGameState, IFeatureInfo, IInitResponse } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { IFreeRespinReelsDetail, FeatureTypes } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { SpinResponseFormatter } from 'slotngf/src/server/ng/formatter/http/in/SpinResponseFormatter';
import { SlotNGHttpFormatter } from 'slotngf/src/server/ng/formatter/SlotNGHttpFormatter';
import { FreeSpinFeatureFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/pending/FreeSpinFeatureFormatter';



export class FreeRespinReelsFormatter extends SpinResponseFormatter {


	private _featureFormatters: TsMap<string, SlotNGHttpFormatter>;

	protected get featureFormatters (): TsMap<string, SlotNGHttpFormatter> {
		if ( this._featureFormatters == undefined ) {
			this._featureFormatters = this.setupFeatureFormatters();
		}
		return this._featureFormatters;
	}

	/**
	 * Formatters that receive messages from server
	 */
	protected setupFeatureFormatters (): TsMap<string, SlotNGHttpFormatter> {
		let map = new TsMap<string, SlotNGHttpFormatter>();
		map.set( FeatureTypes.FREE_SPIN, Container.get( FreeSpinFeatureFormatter ) );
		map.set( FeatureTypes.FREE_RESPIN_REELS, Container.get( FreeRespinReelsFormatter ) );
		return map;
	}

	public processIncomingMessage ( message: IGameState, pendingFeatureInfo?: IFeatureInfo ): Scenario {

		if ( pendingFeatureInfo != undefined && pendingFeatureInfo.complete ) {
			return null;
		}

		let respinFeatureInfo: IFeatureInfo = message.features.find( e => e.type === FeatureTypes.FREE_RESPIN_REELS );

		let respinDetail: IFreeRespinReelsDetail = this.getFeatureDetail( respinFeatureInfo );

		// if it's the beginning of the feature
		if ( respinDetail.total_free_spin_count ) {
			if ( respinDetail.total_free_spin_count == respinDetail.free_spins_left ) {
				return null;
			} else {
				return this.respinResponseScenario( respinDetail, respinFeatureInfo, message );
			}
		} else {
			if ( respinFeatureInfo.complete ) {
				return this.respinResponseScenario( respinDetail, respinFeatureInfo, message );
			} else {
				return this.triggeredFeatureIncomingMessage( message, respinFeatureInfo );
			}
		}
	}

	protected respinResponseScenario ( respinDetail: IFreeRespinReelsDetail, featureTriggeredInfo?: IFeatureInfo, message?: IGameState ): Scenario {
		let responseScenario: Scenario = null;

		responseScenario = this.getFreeRespinStopScenario( respinDetail, featureTriggeredInfo, message );

		const winScenario: Scenario = this.getFreeRespinWinScenario( respinDetail, featureTriggeredInfo, message );
		if ( winScenario ) {
			responseScenario.addScenario( winScenario );
		}

		if ( ( respinDetail.free_spins_left != null && respinDetail.free_spins_left === 0 ) || featureTriggeredInfo.complete ) {

			responseScenario.addScenario( this.getFinishScenario( respinDetail, featureTriggeredInfo, message ) );

			let isPendingfeatures: boolean = false;
			if ( message.spin_result.features_triggered != undefined && message.spin_result.features_triggered.length > 0 ) {
				// format feature
				responseScenario = this.getFormatFeatureTriggeredsScenario( message, responseScenario );
				isPendingfeatures = this.getPendingFeatureTrigger( message.spin_result.features_triggered );
				responseScenario = this.setWinningData( responseScenario, message );
			}


			if ( !isPendingfeatures ) {
				const freeSpinFeature = message.features.find( feature => feature.type === FeatureTypes.FREE_SPIN );
				if ( freeSpinFeature ) {
					if ( freeSpinFeature.complete ) {
						let endFreeSpinsScenario = this.scenarioManager.getScenario( this.scenarioNames.freeSpinsFinish );
						endFreeSpinsScenario.addStateData( this.stateNames.transitionToBaseGame, { featureWin: message.feature_win } );
						endFreeSpinsScenario.addStateData( this.stateNames.freeSpinsOutroPopup, { featureWin: message.feature_win } );
						responseScenario.addScenario( endFreeSpinsScenario );
					} else {
						responseScenario.addScenario( this.scenarioManager.getScenario( this.scenarioNames.freeSpinsIdle ) );
						responseScenario.addScenario( this.scenarioManager.getScenario( this.scenarioNames.freeSpinContinue ) );
					}
				} else {
					responseScenario.addScenario( this.scenarioManager.getScenario( this.scenarioNames.gameIdle ) );
				}
			}


		} else {
			responseScenario.addScenario( this.getRespinningScenario( respinDetail, featureTriggeredInfo, message ) );
		}

		this.slotNGModel.stateTag = message.state_tag;
		return responseScenario;
	}

	protected getPendingFeatureTrigger ( featureTriggered: Array<IFeatureInfo> ): boolean {
		let isPending: boolean = false;
		for ( let index = 0; index < featureTriggered.length; index++ ) {
			const feature = featureTriggered[ index ];
			if ( !feature.complete ) {
				isPending = true;
				break;
			}
		}
		return isPending;
	}

	protected getFeatureFormatter ( type: string ): any {
		return ( this.featureFormatters.get( type ) );
	}

	protected getFreeRespinStopScenario ( respinDetail: IFreeRespinReelsDetail, featureTriggeredInfo?: IFeatureInfo, message?: IGameState ): Scenario {
		let respinStopScenario: Scenario = null;

		respinStopScenario = this.scenarioManager.getScenario( this.scenarioNames.freeRespinStop );

		if ( respinStopScenario.hasStateName( this.stateNames.freeRespinsStop ) ) {
			respinStopScenario.setStateData( this.stateNames.freeRespinsStop, {
				balance: message.balance,
				bet: this.bankService.bet,
				totalWin: message.spin_result.win_amount,
				reelIndexes: this.getReelSymbols( message.spin_result.reels ),
				lines: this.bankService.lines
			} );
		}

		return respinStopScenario;
	}

	protected getFreeRespinWinScenario ( respinDetail: IFreeRespinReelsDetail, featureTriggeredInfo?: IFeatureInfo, message?: IGameState ): Scenario {
		let respinWinScenario: Scenario = null;
		if ( respinDetail.total_free_spin_win_amount && respinDetail.total_free_spin_win_amount > 0 ) {
			respinWinScenario = this.scenarioManager.getScenario( this.scenarioNames.freeRespinWithWinning );
			respinWinScenario = this.setWinningData( respinWinScenario, message );
		}
		return respinWinScenario;
	}

	public triggeredFeatureIncomingMessage ( message: IGameState, featureTriggeredInfo: IFeatureInfo ): Scenario {

		let responseScenario: Scenario = new Scenario( '', [] );

		let respinFeatureDetail = featureTriggeredInfo.feature_state as IFreeRespinReelsDetail;

		responseScenario = this.getTransitionToFreeRepinScenario( responseScenario, respinFeatureDetail, featureTriggeredInfo, message );

		if ( respinFeatureDetail.free_spins_left ) {
			if ( respinFeatureDetail.free_spins_left == respinFeatureDetail.total_free_spin_count ) {
				responseScenario.addScenario( this.getStartScenario( respinFeatureDetail, featureTriggeredInfo, message ) );
			} else if ( respinFeatureDetail.free_spins_left > 0 ) {
				responseScenario.addScenario( this.getRespinningScenario( respinFeatureDetail, featureTriggeredInfo, message ) );
			} else {
				responseScenario.addScenario( this.getFinishScenario( respinFeatureDetail, featureTriggeredInfo, message ) );
			}
		} else {
			responseScenario.addScenario( this.getRespinningScenario( respinFeatureDetail, featureTriggeredInfo, message ) );
		}

		this.slotNGModel.stateTag = message.state_tag;
		return responseScenario;
	}

	protected getTransitionToFreeRepinScenario ( responseScenario: Scenario, respinDetail: IFreeRespinReelsDetail,
		featureTriggeredInfo?: IFeatureInfo, message?: IGameState ): Scenario {
		let freeSpinFeatrue = message.features.find( e => e.type === FeatureTypes.FREE_SPIN );

		if ( freeSpinFeatrue == undefined ) {
			responseScenario.addState( new CoreState( this.stateNames.prefeatureWinning ) );
			if ( message.spin_result.win_amount > 0 ) {
				responseScenario.addState( new CoreState( this.stateNames.showWinning ) );
			}
		}
		return responseScenario;
	}


	protected getStartScenario ( respinDetail: IFreeRespinReelsDetail, featureTriggeredInfo?: IFeatureInfo, message?: IGameState ): Scenario {
		let startScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.startFreeRespin );
		startScenario.setStateData( this.stateNames.startFreeRespin, respinDetail );
		startScenario.setStateData( this.stateNames.freeRespinReels, respinDetail );

		return startScenario;
	}

	protected getRespinningScenario ( respinDetail: IFreeRespinReelsDetail, featureTriggeredInfo?: IFeatureInfo, message?: IGameState ): Scenario {
		let respinScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.freeRespinReels );
		respinScenario.setStateData( this.stateNames.freeRespinReels, respinDetail );

		return respinScenario;
	}

	protected getFinishScenario ( respinDetail: IFreeRespinReelsDetail, featureTriggeredInfo?: IFeatureInfo, message?: IGameState ): Scenario {
		let finishScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.freeRespinFinish );

		if ( respinDetail.total_free_spin_win_amount ) {
			finishScenario.setStateData( this.stateNames.freeRespinFinish, respinDetail.total_free_spin_win_amount );
		}

		return finishScenario;
	}

	public restoreFeatureIncomingMessage ( respinDetail: IFreeRespinReelsDetail, features: Array<IFeatureInfo>, type?: string, message?: IInitResponse ): Scenario {
		let responseScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.startFreeRespin );

		responseScenario.setStateData( this.stateNames.startFreeRespin, respinDetail );
		responseScenario.setStateData( this.stateNames.freeRespinReels, respinDetail );
		return responseScenario;
	}

	protected getFeatureDetail ( featureInfo: IFeatureInfo ): any {
		return featureInfo.feature_state;
	}
}


