import { Inject } from 'typescript-ioc/es5';
import { Helper } from 'corengf/src/utils/Helper';
import { SlotUIStates } from './states/SlotUIStates';
import { SlotEvent } from 'slotngf/src/core/SlotEvent';
import { CoreState } from 'corengf/src/states/CoreState';
import { SlotBankService } from 'slotngf/src/core/SlotBankService';
import { UIController } from 'corengf/src/components/ui/UIController';
import { GlobalDispatcher, IListener } from 'corengf/src/events/GlobalDispatcher';
import { SlotUILocalButtonsController } from './buttons/SlotUILocalButtonsController';
import {
	IfreeSpinsShowWinningData,
	ISpinStopData,
	IFreeSpinsStartData,
	IPrefeatureWinningData,
	ITransitionToFreeSpinsData,
	ITransitionToBaseGameData,
	IWinningData,
	IStartPickFeatureData,
	IFreeSpinsStopData
} from 'slotngf/src/states/SlotStatesDataInterfaces';

/**
 * Handel all the element behavior for user interface.
 * It has it's own local machine
 */
export class SlotUIController extends UIController implements IListener {

	/**
	 * local states for ui
	 */
	@Inject
	protected states: SlotUIStates;

	@Inject
	protected bankService: SlotBankService;

	@Inject
	protected localButtonsController: SlotUILocalButtonsController;

	protected _isAutoSpin: boolean = false;
	protected get isAutoSpin (): boolean {
		return this._isAutoSpin;
	}

	protected set isAutoSpin ( value: boolean ) {
		this._isAutoSpin = value;
	}

	protected _isFreeSpin: boolean = false;
	protected get isFreeSpin (): boolean {
		return this._isFreeSpin;
	}

	protected set isFreeSpin ( value: boolean ) {
		this._isFreeSpin = value;
	}

	protected _showFullScreenUI: boolean = false;
	protected get isShowFullScreenUI (): boolean {
		return this._showFullScreenUI;
	}

	protected set isShowFullScreenUI ( value: boolean ) {
		this._showFullScreenUI = value;
	}

	protected isGenplusSpin = false;

	public addEventListeners () {
		GlobalDispatcher.addListener( this, SlotEvent.GAME_READY_FOR_STOP, this.onGameReadyForStopSpin );
		GlobalDispatcher.addListener( this, SlotEvent.BOOSTER_AUTOPLAY_START, this.boosterAutoplayStart );
		GlobalDispatcher.addListener( this, SlotEvent.BOOSTER_AUTOPLAY_STOP, this.boosterAutoplayStop );
	}

	protected boosterAutoplayStart (): void {
		this.isGenplusSpin = true;
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.genplusSpinning, null ) );
	}

	protected boosterAutoplayStop (): void {
		this.isGenplusSpin = false;
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.baseGameIdle, null ) );
	}

	protected onAutoPlayStart (): void {
		this.isAutoSpin = true;
	}

	protected onAutoSpinEvent (): void {
		//
	}

	protected onAutoPlayStop (): void {
		this.isAutoSpin = false;
		const winStates = [
			this.states.allLineWin,
			this.states.showWinning,
			this.states.showWinningLoop
		];
		if ( winStates.indexOf( this.currentStateName ) !== -1 ) {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.baseGameIdle, {} ) );
		}
		GlobalDispatcher.dispatchIntoAllStates( SlotEvent.NORMAL_AUTOPLAY_STOP );
	}

	protected baseGameIdle ( stateData: any ): void {
		if ( this.checkGenplusSpinning() ) { return; }

		if ( this.isAutoSpin ) {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.autoSpinDashboardIdle, Helper.mergeTwoObjects( {}, stateData ) ) );
		} else if ( this.isShowFullScreenUI ) {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.dashboardDisable, Helper.mergeTwoObjects( {}, stateData ) ) );
		} else {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.baseGameIdle, Helper.mergeTwoObjects( {}, stateData ) ) );
		}
		this.finishStateAction();
	}

	protected startPickFeature ( stateData: IStartPickFeatureData ) {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.startPickFeature ) );
		this.finishStateAction();
	}

	protected startSpin ( stateData: any ): void {
		if ( this.checkGenplusSpinning() ) { return; }

		if ( this.isAutoSpin ) {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.autoSpinStartSpin, Helper.mergeTwoObjects( {}, stateData ) ) );
		} else {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.startSpin, Helper.mergeTwoObjects( {}, stateData ) ) );

		}
		this.finishStateAction();
	}

	protected onGameReadyForStopSpin (): void {
		if ( this.checkGenplusSpinning() ) { return; }

		if ( !this.isFreeSpin ) {
			if ( this.isAutoSpin ) {
				this.dashboardLocalStateMachine.setState( new CoreState( this.states.autoSpinSpinStopReady, null ) );
			} else {
				this.dashboardLocalStateMachine.setState( new CoreState( this.states.spinStopReady, null ) );
			}
		} else {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.freeSpinStopReady, null ) );
		}
	}

	protected spinStop ( stateData: ISpinStopData ): void {
		if ( this.checkGenplusSpinning() ) { return; }

		if ( this.isAutoSpin ) {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.autoSpinSpinStop, Helper.mergeTwoObjects( {}, stateData ) ) );
		} else {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.spinStop, Helper.mergeTwoObjects( {}, stateData ) ) );
		}
		this.finishStateAction();
	}

	protected spinning ( stateData: any ): void {
		if ( this.checkGenplusSpinning() ) { return; }

		if ( this.isAutoSpin ) {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.autoSpinSpinning, Helper.mergeTwoObjects( {}, stateData ) ) );
		} else {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.spinning, Helper.mergeTwoObjects( {}, stateData ) ) );
		}
		this.finishStateAction();
	}

	protected freeSpinsIdle ( stateData: any ): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.freeSpinsIdle, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected freeSpinsStart ( stateData: IFreeSpinsStartData ): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.freeSpinsStart, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected freeSpinning ( stateData: any ): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.freeSpinning, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected freeSpinsStop ( stateData: IFreeSpinsStopData ): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.freeSpinsStop, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected freeRespinsStop ( stateData: IFreeSpinsStopData ): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.freeRespinsStop, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected showRetrigger ( stateData: any ): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.showRetrigger, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected freeSpinsAllLineWin ( stateData: IfreeSpinsShowWinningData ): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.freeSpinsAllLineWin, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected freeSpinsShowWinning ( stateData: IfreeSpinsShowWinningData ): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.freeSpinsShowWinning, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected freeSpinsWildMultipliers ( stateData: any ): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.freeSpinsWildMultipliers, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected prefeatureWinning ( stateData: IPrefeatureWinningData ): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.prefeatureWinning, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected skip_spinning ( stateData: any ): void {
		if ( this.isAutoSpin ) {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.skip_autoSpinSpinning, Helper.mergeTwoObjects( {}, stateData ) ) );
		} else {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.skip_spinning, Helper.mergeTwoObjects( {}, stateData ) ) );
		}
		this.finishStateAction();
	}

	protected skip_freeSpinning ( stateData: any ): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.skip_freeSpinning, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected showWinningLoop ( stateData: any ): void {
		if ( this.checkGenplusSpinning() ) { return; }

		if ( this.isAutoSpin ) {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.autoSpinDashboardIdle, Helper.mergeTwoObjects( {}, stateData ) ) );
		} else {
			if ( this.dashboardLocalStateMachine.currentState.stateName != this.states.dashboardDisable ) {
				this.dashboardLocalStateMachine.setState( new CoreState( this.states.dashboardIdle, Helper.mergeTwoObjects( {}, stateData ) ) );
			}
		}
		this.finishStateAction();
	}

	protected transitionToAutoSpin ( stateData: any ): void {
		this.isAutoSpin = true;
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.transitionToAutoSpin, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected transitionToFreeSpins ( stateData: ITransitionToFreeSpinsData ): void {
		this.isFreeSpin = true;
		this.isAutoSpin = false;
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.transitionToFreeSpins, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected transitionToBaseGame ( stateData: ITransitionToBaseGameData ): void {
		this.isFreeSpin = false;
		this.isAutoSpin = false;
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.transitionToBaseGame, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected transitionToBonusSpins ( stateData: any ): void {
		this.isFreeSpin = true;
		this.isAutoSpin = false;
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.transitionToBonusSpins, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected transitionBonusToBase ( stateData: any ): void {
		this.isFreeSpin = false;
		this.isAutoSpin = false;
		this.dashboardLocalStateMachine.setState( new CoreState( this.states.transitionBonusToBase, Helper.mergeTwoObjects( {}, stateData ) ) );
		this.finishStateAction();
	}

	protected allLineWin ( stateData: IWinningData ): void {
		if ( this.checkGenplusSpinning() ) { return; }

		if ( this.isAutoSpin || this.isGenplusSpin ) {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.autoSpinAllLineWin, Helper.mergeTwoObjects( {}, stateData ) ) );
		} else {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.allLineWin, Helper.mergeTwoObjects( {}, stateData ) ) );
		}
		this.finishStateAction();
	}

	protected showWinning ( stateData: IWinningData ): void {
		if ( this.checkGenplusSpinning() ) { return; }

		if ( this.isAutoSpin || this.isGenplusSpin ) {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.autoSpinShowWinning, Helper.mergeTwoObjects( {}, stateData ) ) );
		} else {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.showWinning, Helper.mergeTwoObjects( {}, stateData ) ) );
		}
		this.finishStateAction();
	}

	protected allReelStopped ( stateData: any ): void {
		if ( this.checkGenplusSpinning() ) { return; }
		if ( this.isAutoSpin || this.isGenplusSpin ) {
			this.dashboardLocalStateMachine.setState( new CoreState( this.states.allReelStopped, Helper.mergeTwoObjects( {}, stateData ) ) );
		}
		this.finishStateAction();
	}

	/**
	 * on skip show winning
	 * @param data
	 */
	protected skip_showWinning ( data: any ): void {
		//
	}

	protected onLocalStateChanged ( stateName: string ): void {
		this.localButtonsController.applyButtonsTemplate( stateName );
	}

	protected onFullScreenPanel ( e: any ): void {
		console.log( e );
	}

	protected checkGenplusSpinning (): boolean {
		if ( this.isGenplusSpin ) {
			this.finishStateAction();
			return true;
		}
		return false;
	}
}


