import { TsMap } from 'corengf/src/utils/TsMap';
import { Orientation } from 'corengf/src/display/Viewport';
import { ICoreElementLayoutConfig } from 'corengf/src/elements/canvas/CoreElement';
import { IWinDisplayConfig } from 'slotngf/src/core/config/IWinDisplayConfig';
import { ITweenBitmapTextField } from 'corengf/src/elements/canvas/text/TweenBitmapTextField';
import { WinDisplayConfig } from 'slotngf/src/components/winDisplay/base/WinDisplayConfig';


export class LineScoreConfig extends WinDisplayConfig implements ILineScoreConfig {

	public lineScoreLabel: ITweenBitmapTextField = {
		name: 'lineScoreLabel',
		isOrientationSupported: true,
		key: '',
		style: {
			font: {
				name: 'BigWinFont',
				size: 80,
			},
			align: 'center',
		},
		alpha: 0,
		anchor: { x: 0.5, y: 0.5 },
		position: { x: 0, y: 595 },
		tweenConfig: {
			duration: 200,
			tweenStarting: { x: 0, alpha: 0 },
			tweenDestination: { x: 360, alpha: 1 }
		}
	};

	public coreElementOrientationConfig: TsMap<Orientation, TsMap<string, ICoreElementLayoutConfig>> = new TsMap( [
		[
			Orientation.PORTRAIT, new TsMap( [
				[
					this.lineScoreLabel.name, {
						position: this.lineScoreLabel.position
					}
				]
			] )
		],
		[
			Orientation.LANDSCAPE, new TsMap( [
				[
					this.lineScoreLabel.name, {
						position: { x: 380, y: 395 }
					}
				]
			] )
		]
	] );
}

export interface ILineScoreConfig extends IWinDisplayConfig {
	lineScoreLabel: ITweenBitmapTextField;
}


