import * as MiniSignal from 'mini-signals';
import { DisplayObject, Graphics, Point, Container, Texture } from 'pixi.js';
import { Helper } from 'corengf/src/utils/Helper';
import { Asset } from 'corengf/src/core/AssetLoader';
import { SimpleTween } from 'corengf/src/time/SimpleTween';
import { SimpleTimer } from 'corengf/src/time/SimpleTimer';
import { ViewportAlign } from 'corengf/src/display/Viewport';
import { Sprite } from 'corengf/src/elements/canvas/sprite/Sprite';
import { BitmapTextField } from 'corengf/src/elements/canvas/text/BitmapTextField';
import { SpineAnimation } from 'corengf/src/elements/canvas/animation/spine/SpineAnimation';
import { CoreUIElementFactory, ICoreUISpriteElementConfig } from 'corengf/src/factory/CoreUIElementFactory';
import { SlotFreeSpinsView } from 'slotngf/src/components/freeSpins/SlotFreeSpinsView';
import { IDHBSlotFreeSpinsConfig } from 'game/src/components/freeSpins/DHBSlotFreeSpinsConfig';

export class DHBSlotFreeSpinsView extends SlotFreeSpinsView {

	public outroStartSignal: MiniSignal;

	protected pickCoinAnimations: Array<SpineAnimation>;
	protected pickCoins: Array<Sprite>;
	protected config: IDHBSlotFreeSpinsConfig;
	protected background: Graphics;
	protected pickContainer: Container;
	protected pickBackground: Graphics;
	protected prize: SpineAnimation;
	protected triggered: SpineAnimation;
	protected winBonus: SpineAnimation;
	protected winValueLabel: BitmapTextField;

	constructor () {
		super();
		this.outroStartSignal = new MiniSignal();
	}

	protected createElements ( config?: IDHBSlotFreeSpinsConfig ): void {

		this.visible = false;

		this.background = this.getFillBg();
		this.mainContainer.addChildAt( this.background, 0 );

		this.prize = CoreUIElementFactory.createInstance( this.config.prize );
		this.mainContainer.addChild( this.prize );

		this.pickContainer = new Container();
		this.pickContainer.name = 'pickContainer';
		this.pickBackground = new Graphics();
		this.pickBackground.beginFill( 0, 0.8 );
		this.pickBackground.drawRect( -125, -100, 755, 545 );
		this.pickBackground.endFill();
		this.pickBackground.clone();
		this.pickContainer.addChild( this.pickBackground );

		this.mainContainer.addChild( this.pickContainer );
		this.config = config;

		if ( this.config.pickAsset != Helper.EMPTY ) {

			this.pickCoinAnimations = new Array<SpineAnimation>();
			this.pickCoins = new Array<Sprite>();

			let xNum = 0;
			let yNum = 0;
			for ( let index = 0; index < 12; index++ ) {

				let coinName = ( index + 1 ).toString();

				let spriteConfig: ICoreUISpriteElementConfig = {
					elementType: Sprite.elementType,
					elementConfig: {
						name: coinName,
						assetName: 'pickCoin'
					}

				};

				let sprite: Sprite = CoreUIElementFactory.createInstance( spriteConfig );
				let animation: SpineAnimation = CoreUIElementFactory.createInstance( this.config.pickCoinAnimation );
				sprite.on( 'pointertap', this.onPickSelect, this );
				sprite.interactive = true;

				let posX = 170 * xNum;
				let posY = 170 * yNum;
				sprite.position.set( posX - sprite.width / 2 - 1, posY - sprite.height / 2 - 5 );
				animation.position.set( posX, posY );
				animation.displayObject.name = coinName;
				this.pickContainer.addChild( animation );
				this.pickContainer.addChild( sprite );
				this.pickCoinAnimations.push( animation );
				this.pickCoins.push( sprite );
				xNum++;
				if ( xNum % 4 == 0 ) {
					yNum++;
					xNum = 0;
				}
			}

			this.pickContainer.alpha = 0;
			this.prize.alpha = 0;

			this.triggered = CoreUIElementFactory.createInstance( this.config.triggered );
			this.triggered.setEmptyAnimation( 0 );
			this.triggered.alpha = 0;
			this.addChild( this.triggered );

			this.winBonus = CoreUIElementFactory.createInstance( this.config.winBonus );
			this.winBonus.setEmptyAnimation( 0 );
			this.winBonus.alpha = 0;
			this.addChild( this.winBonus );

		}

		this.winValueLabel = new BitmapTextField( this.config.winValueLabel );
		this.winValueLabel.text = '0.00';
		this.winValueLabel.visible = false;
		this.addChild( this.winValueLabel );

		this.enableSelection( false );

	}

	public playIntro (): void {
		this.visible = true;
		this.pickCoinAnimations.forEach( animation => {
			animation.setEmptyAnimation( 0 );
			animation.setEmptyAnimation( 1 );
		} );

		this.pickCoins.forEach( coin => {
			coin.visible = true;
			coin.interactive = true;
			coin.texture = Texture.from( 'pickCoin' );
		} );

		this.pickContainer.alpha = 0;
		this.prize.alpha = 0;

		new SimpleTimer( 300, () => {
			this.triggered.alpha = 1;
			this.triggered.play( 0, 'animation', false );
			this.triggered.appendEmptyAnimation( 0 );

			this.triggered.displayObject.state.tracks[ 0 ].listener = {
				complete: () => {
					new SimpleTween( this.pickContainer )
						.to( { alpha: 1 }, 1000 )
						.start();

					new SimpleTween( this.prize )
						.to( { alpha: 1 }, 1000 )
						.start();
					new SimpleTween( this.triggered )
						.to( { alpha: 0 }, 1000 )
						.onComplete( () => {
							this.beforePickSignal.dispatch();
						} )
						.start();
				}
			};
		} );



	}

	public playOutro ( choice?: string, winAmount?: string ): void {
		this.outroStartSignal.dispatch( choice );
		this.winBonus.alpha = 1;
		this.winBonus.play( 0, choice, false );
		this.winValueLabel.visible = true;
		this.winValueLabel.text = winAmount;
		this.winBonus.displayObject.state.tracks[ 0 ].listener = {
			complete: () => {
				new SimpleTimer( 2000, () => {
					new SimpleTween( this.winBonus )
						.to( { alpha: 0 }, 1000 )
						.onComplete( () => {
							this.winBonus.setEmptyAnimation( 0 );
							this.winValueLabel.visible = false;
							this.onOutroComplete();
						} )
						.start();
				} );

			}
		};
	}

	protected onOutroComplete (): void {
		this.visible = false;

		this.outroSignal.dispatch();
	}

	public restorePicks ( choices: Array<string> ): void {

		for ( let index = 0; index < choices.length; index++ ) {
			const element = choices[ index ];
			if ( element != '*' ) {
				this.pickCoins[ index ].texture = Texture.from( element );
				this.pickCoins[ index ].interactive = false;
			}
		}
	}

	public setPickResult ( index: number, choice: string, isComplete: boolean = false, winAmount?: string ): void {

		this.picks[ index ] = choice;
		this.pickCoins[ index ].visible = false;
		this.pickCoinAnimations[ index ].play( 1, 'Feature_Coin_' + choice, false );
		this.pickCoinAnimations[ index ].displayObject.state.tracks[ 1 ].listener = {
			complete: () => {
				this.pickCoins[ index ].texture = Texture.from( choice );
				this.pickCoins[ index ].visible = true;
				if ( isComplete ) {

					for ( let i = 0; i < this.picks.length; i++ ) {
						const element = this.picks[ i ];
						if ( element == choice ) {
							this.pickCoinAnimations[ i ].play( 1, 'Symbol_Highlight', false );
							this.pickCoinAnimations[ i ].appendEmptyAnimation( 1 );
						}
					}

					this.prize.play( 1, choice, false );
					this.prize.appendEmptyAnimation( 1 );

					new SimpleTimer( 3000, () => {
						this.playOutro( choice, winAmount );
					} );
				}
			}
		};

	}

	protected onPickSelect ( e: PIXI.interaction.InteractionEvent ): void {
		this.enableSelection( false );
		let target: DisplayObject = e.target;

		let animation = this.pickCoinAnimations.find( e => e.displayObject.name == target.name );
		if ( animation ) {
			animation.play( 0, 'Background_Highlight', true );
			this.pickSignal.dispatch( target.name );
			let index = +target.name - 1;
			this.pickCoins[ index ].interactive = false;
		} else {
			this.enableSelection( true );
		}
	}

	protected getFillBg (): Graphics {
		let leftTopPoint: Point = this.viewport.getAlignedPoint( ViewportAlign.LEFT_TOP );
		let g: Graphics = new Graphics();
		g.beginFill( 0, 0.2 );
		g.drawRect( leftTopPoint.x, leftTopPoint.y, this.viewport.width, this.viewport.height );
		g.endFill();
		return g.clone();
	}

	protected layoutElements (): void {
		super.layoutElements();
		this.background.width = this.viewport.width;
		this.background.height = this.viewport.height;
	}

	public portraitLayout (): void {
		super.portraitLayout();
		this.prize.play( 0, 'Portrait_Idle_0' );
		this.pickContainer.position.set( 130, 450 );
		this.pickContainer.scale.set( 0.94, 0.94 );
	}

	public landscapeLayout (): void {
		super.landscapeLayout();
		this.prize.play( 0, 'Landscape_Idle_0' );
		this.pickContainer.position.set( 390, 125 );
		this.pickContainer.scale.set( 1, 1 );
	}

	public getAssetsList (): Array<Asset> {
		return [
			{
				assetKey: 'featureCoin',
				assetURL: 'assets/freeSpins/Feature_Coin.json'
			},
			{
				assetKey: 'featureTriggered',
				assetURL: 'assets/freeSpins/{locale}/Feature_Triggered.json'
			},
			{
				assetKey: 'featureWinBonus',
				assetURL: 'assets/freeSpins/{locale}/Feature_Win_bonus.json'
			},
			{
				assetKey: 'pickCoin',
				assetURL: 'assets/freeSpins/background/Pick_Coin.png'
			},
			{
				assetKey: 'pickFeaturePrize',
				assetURL: 'assets/freeSpins/background/DHBG_Feature_Prize.json'
			},
			{
				assetKey: 'GRAND',
				assetURL: 'assets/freeSpins/background/GRAND.png'
			},
			{
				assetKey: 'MAJOR',
				assetURL: 'assets/freeSpins/background/MAJOR.png'
			},
			{
				assetKey: 'MINOR',
				assetURL: 'assets/freeSpins/background/MINOR.png'
			},
			{
				assetKey: 'MINI',
				assetURL: 'assets/freeSpins/background/MINI.png'
			},
			{
				assetKey: 'pickgameSelect',
				assetURL: 'assets/sound/game/pick/Pick_Select.mp3'
			},
			{
				assetKey: 'pickgameHitTriple',
				assetURL: 'assets/sound/game/pick/Pick_Hit_Triple.mp3'
			},
			{
				assetKey: 'pickgameWinGRAND',
				assetURL: 'assets/sound/game/pick/Pick_Win_Grand.mp3'
			},
			{
				assetKey: 'pickgameWinMAJOR',
				assetURL: 'assets/sound/game/pick/Pick_Win_Major.mp3'
			},
			{
				assetKey: 'pickgameWinMINI',
				assetURL: 'assets/sound/game/pick/Pick_Win_Mini.mp3'
			},
			{
				assetKey: 'pickgameWinMINOR',
				assetURL: 'assets/sound/game/pick/Pick_Win_Minor.mp3'
			},
		];
	}
}


