import { Inject } from 'typescript-ioc/es5';
import { MLUISettingsPanel } from './MLUISettingsPanel';
import { IMLUISettingsConfig, MLUISettingsConfig } from './MLUISettingsConfig';
import { MLUIFullScreenController } from 'mlui//src/core/MLUIFullScreenController';


export class MLUISettingsController extends MLUIFullScreenController {
	protected view: MLUISettingsPanel;

	constructor ( @Inject view: MLUISettingsPanel ) {
		super( view );
	}

	public init (): void {
		let config = this.gameConfig.getConfig<IMLUISettingsConfig>( MLUISettingsConfig );

		this.view.initialize( config );
		this.finishStateAction();
	}

	public showPopup (): void {
		this.view.showPopup();
	}

	public onHidePopup (): any {
		//
	}
}



