import { TsMap } from 'corengf/src/utils/TsMap';
import { Singleton, Container } from 'typescript-ioc/es5';
import { FeatureTypes } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { SlotNGHttpFormatter } from 'slotngf/src/server/ng/formatter/SlotNGHttpFormatter';
import { PickFeatureFormatter } from './pending/PickFeatureFormatter';
import { JackpotFeatureFormatter } from './pending/JackpotFeatureFormatter';
import { FreeSpinFeatureFormatter } from './pending/FreeSpinFeatureFormatter';
import { FreeRespinReelsFormatter } from './pending/FreeRespinReelsFormatter';

/**
 * Features formatter list for run feature, and restore feature.
 * should only be pending type feature in the list here.
 */
@Singleton
export class FeaturesFormatterList {

	/**
	 * Map of incoming formats with message type as a key
	 */
	private _inFeatureFormatters: TsMap<string, SlotNGHttpFormatter>;

	public get inFeatureFormatters (): TsMap<string, SlotNGHttpFormatter> {
		if ( this._inFeatureFormatters == undefined ) {
			this._inFeatureFormatters = this.setupFeatureFormatters();
		}
		return this._inFeatureFormatters;
	}

	/**
	 * Formatters that receive messages from server
	 */
	protected setupFeatureFormatters (): TsMap<string, SlotNGHttpFormatter> {
		let map = new TsMap<string, SlotNGHttpFormatter>();
		map.set( FeatureTypes.PICK, Container.get( PickFeatureFormatter ) );
		map.set( FeatureTypes.FREE_SPIN, Container.get( FreeSpinFeatureFormatter ) );
		map.set( FeatureTypes.JACKPOT, Container.get( JackpotFeatureFormatter ) );
		map.set( FeatureTypes.FREE_RESPIN_REELS, Container.get( FreeRespinReelsFormatter ) );
		return map;
	}
}



