import * as MiniSignal from 'mini-signals';
import { Graphics, Sprite } from 'pixi.js';
import { Inject } from 'typescript-ioc/es5';
import { IDialogConfig } from './DialogConfig';
import { SimpleTween } from 'corengf/src/time/SimpleTween';
import { TextField } from 'corengf/src/elements/canvas/text/TextField';
import { CoreUIElementFactory } from 'corengf/src/factory/CoreUIElementFactory';
import { Translator } from './../../utils/Translator';
import { SimpleTimer } from './../../time/SimpleTimer';
import { TransitionSceneView } from './../transitionScene/TransitionSceneView';


export class DialogView extends TransitionSceneView {

	@Inject
	private translator: Translator;

	protected background: Graphics;
	protected window: Sprite;
	protected title: TextField;
	protected text: TextField;
	protected config: IDialogConfig;
	public dialogClosedSignal: MiniSignal;
	protected clickToHide: boolean = true;
	protected isClosed: boolean = true;

	protected resolveForCloseDialog: Function;


	constructor () {
		super();
		this.dialogClosedSignal = new MiniSignal();
	}

	public registerEvents (): void {
		super.registerEvents();
		this.mainContainer.on( 'pointertap', this.onClick, this );
	}

	protected createElements ( config?: any ): void {
		this.config = config;
		this.background = new Graphics();
		this.background.beginFill( 0, 0.8 );
		this.background.drawRect( 0, 0, this.viewport.width, this.viewport.height );

		this.window = Sprite.fromFrame( this.config.windowAsset );
		this.window.anchor.set( 0.5, 0.5 );
		this.window.position.set( this.config.position.x, this.config.position.y );

		this.title = CoreUIElementFactory.createInstance( this.config.title );
		this.text = CoreUIElementFactory.createInstance( this.config.text );

		this.addChild( this.background );
		this.addChild( this.window );
		this.window.addChild( this.title );
		this.window.addChild( this.text );

		this.visible = false;
	}

	public showDialogFromKey ( titleKey: string, textKey: string, time: number,
		delay: number = 0, clickToHide: boolean = true, interactive: boolean = true ): Promise<any> {
		return this.showDialogFromString( this.translator.t( titleKey ), this.translator.t( textKey ), time, delay, clickToHide, interactive );
	}

	protected setTitleLayout (): void {
		this.title.position.set( 0 - this.title.width / 2, 0 - ( this.window.height / 2 ) + this.title.height );
	}

	public showDialogFromString ( title: string, text: string, time: number,
		delay: number = 0, clickToHide: boolean = true, interactive: boolean = true ): Promise<any> {
		this.title.text = title;

		this.setTitleLayout();

		this.text.text = text;
		this.text.position.set( 0 - this.text.width / 2, 0 );
		this.clickToHide = clickToHide;
		this.visible = true;
		this.mainContainer.alpha = 0;
		this.setInteractive( interactive );
		return this.openDialog( delay, time );
	}

	protected openDialog ( delay: number, time: number = 3000, fadeInDuration: number = 100 ): Promise<any> {
		return new Promise( ( resolve ) => {
			this.resolveForCloseDialog = resolve;

			new SimpleTween( this.mainContainer )
				.to( { alpha: 1 }, fadeInDuration )
				.delay( delay )
				.onComplete( () => {
					this.isClosed = false;
					new SimpleTimer( time, this.onTimerEnd.bind( this ), false );
				} )
				.start();
		} );
	}

	public closeDialog (): void {
		this.isClosed = true;
		new SimpleTween( this.mainContainer )
			.to( { alpha: 0 }, 100 )
			.onComplete( () => {
				this.title.text = '';
				this.text.text = '';
				this.visible = false;
				this.setInteractive( false );
				this.dialogClosedSignal.dispatch();
			} )
			.start();
	}

	protected onClick (): void {
		if ( this.clickToHide && !this.isClosed ) {
			this.closeDialog();
		}
	}

	protected onTimerEnd (): void {
		if ( this.resolveForCloseDialog ) {
			this.resolveForCloseDialog();
			this.resolveForCloseDialog = null;
		}
		if ( !this.isClosed && this.mainContainer.interactive ) {
			this.closeDialog();
		}
	}

	public setInteractive ( value: boolean ): void {
		this.mainContainer.interactive = value;
	}
}



