import * as MiniSignal from 'mini-signals';
import * as EventBus from 'vertx3-eventbus-client';
import { Singleton } from 'typescript-ioc/es5';
import { BlankTransport } from 'corengf/src/server/abstract/transport/BlankTransport';
import { IWebSocketConfig, IServerConfig } from 'corengf/src/server/abstract/config/IServerConfig';

/**
 * Handle web socket transport.
 */
@Singleton
export class WebSocketTransport extends BlankTransport {
	public onNotificationSignal: MiniSignal = new MiniSignal();

	protected config: IWebSocketConfig;
	protected eventBus: EventBus;

	protected isConnect: boolean = false;

	public setConfig ( config: IServerConfig ): void {
		this.config = config as IWebSocketConfig;
	}

	public connect (): void {
		if ( this.config.useWebSocket ) {
			let URL = this.getConnectionURL();

			var options = {
				vertxbus_reconnect_attempts_max: 10, // Max reconnect attempts
				vertxbus_reconnect_delay_min: 1000, // Initial delay (in ms) before first reconnect attempt
				vertxbus_reconnect_delay_max: 5000, // Max delay (in ms) between reconnect attempts
				vertxbus_reconnect_exponent: 2, // Exponential backoff factor
				vertxbus_randomization_factor: 0.5 // Randomization factor between 0 and 1
			};

			this.eventBus = new EventBus( URL, options );
			this.eventBus.enableReconnect( true );

			this.eventBus.onopen = this.onConnectionEstablished.bind( this );
			this.eventBus.onclose = this.onDisconnect.bind( this );
		}
	}

	/**
	 * Get web socket connection url
	 */
	protected getConnectionURL (): string {
		let returnURL: string = '';
		return returnURL;
	}

	/**
	 * Register notification channel by address.
	 * @param address
	 */
	public onSetRegisterHandler ( address: string ): void {
		var self = this;
		this.eventBus.registerHandler( address, null, function ( error, message ) {
			//handle data pushed by server
			self.onNotification( error, message );
		} );
	}

	/**
	 * Calling the function when connection established.
	 */
	protected onConnectionEstablished (): void {
		this.isConnect = true;
		if ( this.onConnect ) {
			this.onConnect();
		}
	}

	/**
	 * Send daata to server
	 * @param data
	 */
	public send ( data: IWebSocketRequestData ): void {
		if ( !this.isConnect ) {
			return null;
		}

		var self = this;
		this.eventBus.send( data.address, data.messageData, function ( error, message ) {
			// receiving message from server
			self.onResponse( error, message );
		} );
	}

	/**
	 * Receive response from server
	 * @param error
	 * @param message
	 */
	protected onResponse ( error, message ): void {
		if ( error != null ) {
			this.onNetworkError( error.failureCode, error.failureType );
		} else if ( this.onReceive ) {
			this.onReceive( message );
		}
	}

	/**
	 * Receive notification from server
	 * @param error
	 * @param message
	 */
	protected onNotification ( error, message ): void {
		if ( error != null ) {
			this.onNetworkError( error.failureCode, error.failureType );
		} else {
			this.onNotificationSignal.dispatch( message );
		}
	}

	/**
	 * Receive error from server
	 * @param errorCode
	 * @param reason
	 */
	protected onNetworkError ( errorCode: string, reason: string ): void {
		var errorData = { 'code': errorCode, 'reason': reason };
		if ( this.onError ) {
			this.onError( errorData );
		}
	}

	/**
	 * Receive disconnect from server
	 */
	protected onDisconnect (): void {
		this.isConnect = false;
		if ( this.eventBus.reconnectEnabled && this.eventBus.reconnectAttempts == this.eventBus.maxReconnectAttempts ) {
			this.eventBus.enableReconnect( false );
			this.onNetworkError( '-1', 'on websocket disconnect' );
		}
	}

	/**
	 * Calling for force disconnect from server
	 */
	public disconnect (): void {
		this.eventBus.close();
	}
}

export class IWebSocketRequestData {
	public address: string;
	public messageData: any;

	constructor ( address: string, data?: any ) {
		this.address = address;
		this.messageData = data;
	}
}



