import { INGServerConfig } from './INGServerConfig';


export class NGServerConfig implements INGServerConfig {
	public game = 'GameName';
	public connectionURL = '3655oule.com';

	public historyApiDomain = '{partner_token}-rgs-history.{domain}';

	public useWebSocket = true;

	// tslint:disable-next-line:max-line-length
	public notificationPath = 'player-notification-{gs}.{domain}{port}/notifications/?partnerToken={partner}&tokenId={session}&gameCode={game_code}&channel={device}';
	public directAddressFormat = 'direct:feature:{feature}:partner:{partnerCode}:player:{playerId}';
	public channelAddressFormat = 'channel:feature:{feature}:partner:{partnerCode}:locale:{locale}';
	public requestAddressFormat = 'request:feature:{feature}';
	public directFeatures = [];
	//public channelFeatures= [];
	public requestFeatures = [
		'data_hook'
	];
	public protocol = 'https://';
	public rgsName = 'nurgs';
	public connectionPort = ':443';
	public gameCode = GAME_CODE; /* please setup your gameCode in game level package.json*/
	public partnerToken = '8c31b93c-24bd-4dfa-aa16-db96c0296b3a';
	public playPartnerToken = 'a40a269e-f0aa-d16f-63e6-00fd6117afc8';
	public demoPartnerToken = '760e878c-482a-4e95-92fa-8920a463e525';
	public devPath = null; /* please override by game */
	public path = null; /* please override by game */
	public header = {
		contentType: 'Content-Type',
		userId: 'X-Genesis-UserId',
		partnerToken: 'X-Genesis-PartnerToken'
	};
}



