import { Container, Inject } from 'typescript-ioc/es5';
import { ErrorHandleService } from 'corengf/src/core/ErrorHandleService';
import { NGBNGController } from 'corengf/src/server/ng/sdk/bng/NGBNGController';
import { GameInit } from 'slotngf/src/server/ng/api/GameInit';
import { SlotNPSModel } from 'slotngf/src/server/ng/sdk/bng/model/SlotNPSModel';
import { ISlotNGServerConfig } from 'slotngf/src/server/ng/config/ISlotNGServerConfig';
import { SlotNGHttpTransport } from 'slotngf/src/server/ng/transport/SlotNGHttpTransport';
import { GameInitMsgFormatter } from 'slotngf/src/server/ng/formatter/http/in/GameInitMsgFormatter';
import { BNGGameInitRequestMsgFormatter } from 'slotngf/src/server/ng/sdk/bng/formatter/http/out/BNGGameInitRequestMsgFormatter';
import { ErrorCode } from 'slotngf/src/server/ng/ErrorCode';


export class BNGGameInit extends GameInit {

	@Inject
	protected errorHandleService: ErrorHandleService;

	@Inject
	protected slotNGModel: SlotNPSModel;

	private resolve: Function;
	private reject: Function;

	public init ( transport: SlotNGHttpTransport, config: ISlotNGServerConfig ): void {
		super.init( transport, config );
		this.responseFormatter = Container.get( GameInitMsgFormatter );
		this.requestFormatter = Container.get( BNGGameInitRequestMsgFormatter );

		NGBNGController.onIframeResponse.add( this.onIframeResponse.bind( this ) );
	}

	protected onIframeResponse ( message: BNG.IMessageFromPortal ): void {
		if ( message.method !== 'getPlayerInfo' ) {
			return;
		}
		const payload = message.payload;

		if ( !payload && this.reject ) {
			this.reject( { code: ErrorCode.INTERNET_DISCONNECTED, stack: new Error().stack } );
			this.reject = null;
			return;
		}

		if ( payload.error && this.reject ) {
			this.reject( payload.error );
			this.reject = null;
		} else if ( this.resolve ) {
			this.resolve( payload.result );
			this.resolve = null;
		}
	}

	public send ( messageType: string, data?: any ): Promise<any> {
		console.log( 'CALL INIT', this.request );
		return this.getPlayerInfo()
			.then( ( msg: BNG.IGetPlayerInfoResult ) => {
				this.slotNGModel.playerInfo = msg;

				this.requestFormat( msg );
				const apiPackage = this.encapsulation( messageType );
				console.log( 'getPlayerInfo success', this.request );
				return this.transport.psend( apiPackage );
			} )
			.then( ( sessionLoginResponse ) => {
				this._rawData = sessionLoginResponse.payload;
				this.responseFormat( sessionLoginResponse );
				this.slotNGModel.fraction = sessionLoginResponse.payload.state.fraction;
				return this.onResponse();
			} )
			.catch( ( msg ) => {
				return Promise.reject( msg );
			} );
	}

	public getPlayerInfo (): Promise<any> {
		return new Promise( ( resolve, reject ) => {
			this.resolve = resolve;
			this.reject = reject;
			NGBNGController.postMessage( 'getPlayerInfo' );
		} );
	}
}


