import { NAudio } from './NAudio';


export class NAudioTrack {
	/** The name associated with the track */
	protected _name: string;

	/** If is single channel only 1 audio will play, calling play wil stop the old sound and play the new one */
	protected _isSingleChannel: boolean;

	/** If enabled the audio will play otherwise is will not */
	protected _enabled: boolean;

	/** If true will keep the audio references to play when sound is re enabled */
	protected keepAudioOnEnable: boolean;

	/** list of audios currently playing on this track*/
	protected audios: Array<NAudio>;

	constructor ( data: IAudioTrackData ) {
		this._name = data.name;
		this._isSingleChannel = data.isSingleChannel;
		this.keepAudioOnEnable = data.keepAudioOnEnable;
		this._enabled = true;
		this.audios = new Array<NAudio>();
	}

	public get name (): string {
		return this._name;
	}

	public get isSingleChannel (): boolean {
		return this._isSingleChannel;
	}

	/**
	 * Plays thje specified audio object and adds it to the list of playimng sounds for this track
	 * @param audio Audio Object to play
	 * @param loop Whether to loop the audio or not
	 */
	public play ( audio: NAudio, loop: boolean ): void {
		//Clear Audio if only singleCHannel
		if ( this.isSingleChannel ) {
			this.clearAllAudio();
		}

		//Check if enabled if not then return but first checks if audioshould be kept for when audio is re enabled
		if ( !this._enabled ) {
			if ( this.keepAudioOnEnable ) {
				this.audios.push( audio );
			}
			return;
		}

		this.audios.push( audio );

		//If not loop then add onend event to remove from array of playing sounds;
		if ( !loop ) {
			audio.once( 'end', () => {
				this.audios.splice( this.audios.indexOf( audio ), 1 );
			} );
		}

		audio.loop( loop ).play();
	}

	/**
	 * Sets the track enabled or disabled. Sounds will not play if disabled.
	 */
	public set enabled ( enabled: boolean ) {
		this._enabled = enabled;
		this.keepAudioOnEnable ? this.pauseAllAudio() : this.clearAllAudio();
	}

	/**
	 * returns whether the track is enabled or nort
	 */
	public get enabled (): boolean {
		return this._enabled;
	}

	/**
	 * Clears all the current playing audio
	 */
	protected clearAllAudio (): void {
		let length = this.audios.length;
		for ( let i = 0; i < length; i++ ) {
			let audio = this.audios.pop();
			audio.stop();
		}
	}

	/**
	 * pauses the current playing audio
	 */
	protected pauseAllAudio (): void {
		this.audios.forEach( ( audio: NAudio ) => {
			this._enabled ? audio.play() : audio.pause();
		} );
	}

	public destroy (): void {
		let length = this.audios.length;
		for ( let i = 0; i < length; i++ ) {
			this.audios.pop();
		}
		this.audios = undefined;
	}
}

export interface IAudioTrackData {
	name: string;
	keepAudioOnEnable: boolean;
	isSingleChannel: boolean;
}


