import { IPoint } from 'corengf/src/elements/config/IElementsConfig';
import { CoreWebElement, ICoreWebElementConfig } from 'corengf/src/elements/webpage/CoreWebElement';
import { IWebImageConfig } from 'corengf/src/elements/webpage/image/WebImageElement';

/**
 * Line pattern element for web page
 */
export class WebLinePatternElement extends CoreWebElement {

	public static elementType: string = 'WEB_LINE_PATTERN_ELEMENT';

	public createElement ( linePatternConfig: IWebLinePatternConfig ): IWebLinePatternConfig {
		return linePatternConfig;
	}
}

/**
 * LinePattern can be set pattern's size and lineweight.
 * LinePattern selects line number of the display part.
 * @param {IWebPatternStyleConfig} patternStyleConfig :one pattern style
 * @param {IPoint} patternPosition :one pattern element position
 * @param {number} startLineNumber :beginning line number, default first number
 * @param {number} endLineNumber :end line number, default end number
 */
export interface IWebLinePatternConfig extends ICoreWebElementConfig {
	patternStyleConfig: IWebPatternStyleConfig;
	patternPosition?: IPoint;
	startLineNumber?: number;
	endLineNumber?: number;
}

/**
 * create one line pattern layout
 * @param {number} blockStyle :set pattern style
 * @param {Array<IWebImageConfig | ICoreWebElementConfig>} backgroundConfig :set pattern background
 * @param {Array<Array<IPoint>>} patternElementPosition:set pattern element position
 * @param {string} elementStyle :set pattern element style
 * @param {string} originalElmentColor :set pattern original background color
 * @param {string} brightElmentColor :set pattern win background color
 * @param {string} noWinElmentColor :set pattern no win background color
 */
export interface IWebPatternStyleConfig {
	blockStyle?: string;
	backgroundConfig: Array<IWebImageConfig | ICoreWebElementConfig>;
	patternElementPosition: Array<Array<IPoint>>;
	elementStyle?: String;
	originalElmentColor?: string;
	brightElmentColor?: string;
	noWinElmentColor?: string;
}


