import { DialogConfig, IDialogConfig } from 'corengf/src/components/dialog/DialogConfig';
import { ICoreUISpineAnimationElementConfig } from 'corengf/src/factory/CoreUIElementFactory';
import { SpineAnimation } from 'corengf/src/elements/canvas/animation/spine/SpineAnimation';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Orientation } from 'corengf/src/display/Viewport';
import { ICoreElementLayoutConfig } from 'corengf/src/elements/canvas/CoreElement';
import { IBitmapTextFieldConfig } from 'corengf/src/elements/canvas/text/BitmapTextField';

export class DHBDialogConfig extends DialogConfig implements IDHBDialogConfig {


	public introAnimation: ICoreUISpineAnimationElementConfig = {
		elementType: SpineAnimation.elementType,
		elementConfig: {
			name: 'introDialog',
			isOrientationSupported: true,
			animationName: 'spinsFeature',
		}
	};

	public outroAnimation: ICoreUISpineAnimationElementConfig = {
		elementType: SpineAnimation.elementType,
		elementConfig: {
			name: 'outroDialog',
			isOrientationSupported: true,
			animationName: 'featureComplete',
			position: { x: null, y: 290 },
		}
	};

	public winValueLabel: IBitmapTextFieldConfig = {
		name: 'freeWinValueLabel',
		key: '',
		isOrientationSupported: true,
		position: { x: 360, y: 620 },
		anchor: { x: 0.5, y: 0.5 },
		style: {
			font: {
				name: 'BigWinFont',
				size: 99,
			},
			align: 'center',
		},
	};

	public coreElementOrientationConfig: TsMap<Orientation, TsMap<string, ICoreElementLayoutConfig>> = new TsMap( [
		[
			Orientation.PORTRAIT, new TsMap( [
				[
					'introDialog', {
						position: { x: 365, y: 685 },
					}
				],
				[
					'outroDialog', {
						position: { x: 351, y: 685 }
					}
				],
				[
					'freeWinValueLabel', {
						position: { x: 360, y: 690 }
					}
				]
			] )
		],
		[
			Orientation.LANDSCAPE, new TsMap( [
				[
					'introDialog', {
						position: { x: 630, y: 360 },
					}
				],
				[
					'outroDialog', {
						position: { x: 630, y: 370 }
					}
				],
				[
					'freeWinValueLabel', {
						position: { x: 630, y: 380 }
					}
				]
			] )
		]
	] );

}

export interface IDHBDialogConfig extends IDialogConfig {
	introAnimation: ICoreUISpineAnimationElementConfig;
	outroAnimation: ICoreUISpineAnimationElementConfig;
	winValueLabel: IBitmapTextFieldConfig;
}


