import { Inject } from 'typescript-ioc/es5';
import { MarqueeView } from './MarqueeView';
import { CompController } from './../../mvc/CompController';
import { MarqueeConfig, IMarqueeConfig } from './MarqueeConfig';


export class MarqueeController extends CompController {

	constructor ( @Inject view: MarqueeView ) {
		super( view );
	}

	public init (): void {
		if ( this.view != undefined ) {
			this.view.initialize( this.gameConfig.getConfig<IMarqueeConfig>( MarqueeConfig ) );
		}
		this.finishStateAction();
	}
}



