import * as PIXI from 'pixi.js';
import { Container, Inject } from 'typescript-ioc/es5';
import { GameConfig } from 'corengf/src/core/GameConfig';
import { Browser } from 'corengf/src/utils/Browser';
import { MainConfig } from 'corengf/src/core/config/MainConfig';
import { IMainConfig } from 'corengf/src/core/config/IMainConfig';
import { GameController } from 'corengf/src/core/GameController';
import { Translator } from 'corengf/src/utils/Translator';
import { Component, CompController } from 'corengf/src/mvc/CompController';
import { HeartTicker } from 'corengf/src/time/HeartTicker';
import { PixiTickConsumer } from 'corengf/src/time/PixiTickConsumer';
import { Viewport, Orientation } from 'corengf/src/display/Viewport';
import { GameContainerManager } from 'corengf/src/core/GameContainerManager';
import { KeyboardManager } from 'corengf/src/keyboard/KeyboardManager';
import { ComponentManager } from 'corengf/src/components/ComponentManager';
import { TweenJSTickConsumer } from 'corengf/src/time/TweenJSTickConsumer';
import { AbstractServer } from 'corengf/src/server/abstract/AbstractServer';
import { ExternalModuleManager } from 'corengf/src/components/externalModule/ExternalModuleManager';
import { ErrorDialogController } from 'corengf/src/components/errorDialog/ErrorDialogController';
import { OrientationHintController } from 'corengf/src/components/orientationHint/OrientationHintController';
import { SmartPreloaderController } from 'corengf/src/components/preloader/SmartPreloaderController';
import { AssetLoader, AssetLoadErrorFormatter } from 'corengf/src/core/AssetLoader';
import { GlobalDispatcher, IListener, EventParams } from 'corengf/src/events/GlobalDispatcher';
import { AbstractApi } from 'corengf/src/server/abstract/AbstractApi';
import { SoundManager } from 'corengf/src/audio/SoundManager';
import { PartnerHelper } from 'corengf/src/utils/PartnerHelper';
import { DOMContent } from 'corengf/src/core/DOMContent';

/**
 * Basic Core class of game.
 */
export class Application implements IListener {

	public static readonly BIND_API = 'BIND_API';

	/**
	 * Viewport that handles all resize manipulations
	 */
	@Inject
	protected viewport: Viewport;

	/**
	 * Ticker for main update loop of game
	 */
	@Inject
	protected timeManager: HeartTicker;

	/**
	 * Main game controller that contains all components and handle all states.
	 */
	@Inject
	protected gameController: GameController;

	@Inject
	protected translator: Translator;

	@Inject
	protected partnerHelper: PartnerHelper;

	@Inject
	protected gameConfig: GameConfig;

	@Inject
	protected externalModuleManager: ExternalModuleManager;

	@Inject
	protected keyboardManager: KeyboardManager;

	@Inject
	protected gameContainerManager: GameContainerManager;

	@Inject
	protected errorDialog: ErrorDialogController;

	@Inject
	protected assetLoader: AssetLoader;

	@Inject
	protected soundManager: SoundManager;

	protected componentManager: ComponentManager;

	protected preloader: SmartPreloaderController;

	protected orientationHint: OrientationHintController;

	protected server: AbstractServer;

	protected mainConfig: IMainConfig;

	public pixi: PIXI.Application;

	constructor () {
		Browser.init();
		this.pixi = new PIXI.Application( this.getPIXIConfiguration() );
		this.pixi.renderer.view.id = 'gameCanvas';
		this.gameContainerManager.init( this.pixi );

		this.bindAPI();

		this.setupBindings();
		this.mainConfig = this.gameConfig.getConfig<IMainConfig>( MainConfig );
		this.setup();
		this.componentManager = new ComponentManager();
		this.gameController.createStateMachine( this.componentManager );
		this.externalModuleManager.init();
		this.addComponents();
		this.createPreloader();

		this.preloader.run();

		if ( Browser.isMobile() && this.mainConfig.orientation !== Orientation.BOTH ) {
			this.orientationHint = new OrientationHintController();
		}
		this.gameContainerManager.enableGameInteraction( false );
	}

	protected getPIXIConfiguration (): PIXI.ApplicationOptions {
		const result: PIXI.ApplicationOptions = {};

		if ( window.devicePixelRatio && window.devicePixelRatio >= 2 ) { // limit max resolution to 2.
			result.resolution = 2;
		}
		return result;
	}

	public addEventListeners (): void {
		GlobalDispatcher.addListener( this, Application.BIND_API, this.addAPI );
	}

	protected addAPI ( message: EventParams ): void {
		const apiMessage: ApiMessage = message.data;
		this.server.addAPI( apiMessage.messageType, apiMessage.api );
	}

	protected bindAPI (): void {
		//
	}

	/**
	 * Adds all relations for injection in project ( matters for abstract classes only
	 * cause there are no direct link to implementation )
	 */
	protected setupBindings (): void {
		//
	}

	/**
	 * Methods that invokes before we start adding objects to stage.
	 * We setup basic settings here.
	 */
	protected setup (): void {
		this.translator.init();
		this.partnerHelper.init();

		this.soundManager.init( this.assetLoader );
		//fill update listeners with some vital components
		this.timeManager.init( this.mainConfig );
		this.timeManager.register( new PixiTickConsumer() );
		this.timeManager.register( new TweenJSTickConsumer() );

		this.viewport.init( this.pixi, this.mainConfig.orientation, this.mainConfig.resolution.height, this.mainConfig.resolution.width );
		this.setupAssetLoader();

		// if isDesktop
		if ( !Browser.isMobile() ) {
			this.keyboardManager.init();
		}
	}

	/**
	 * Methods that init asset loader
	 * And setup error handle service for loader to catch error when failed to load asset
	 */
	protected setupAssetLoader (): void {
		this.assetLoader.init();
		this.assetLoader.errorHandleService.init( Container.get( AssetLoadErrorFormatter ) );
		this.errorDialog.init( this.assetLoader.errorHandleService );
	}

	protected addComponents (): void {
		let components: Array<Component> = new Array<Component>();
		this.createComponentList( components );
		components.forEach( ( component: Component ) => {
			let comp: CompController = Container.get( component.class );
			comp.componentID = component.id;
			this.componentManager.addComponent( comp );
		} );
	}


	protected createComponentList ( components: Array<Component> ): void {
		//Example:
		// components.push( { id : 'background', class: BackgroundController }  );
		// components.push( { id : 'reel', class: ReelsController }  );
	}

	/**
	 * Insert the component after previousComponentID in components array
	 * @param components array with all components of game
	 * @param componentToAdd the component that we are going to insert
	 * @param previousComponentID id of the component that will be before componentToAdd
	 */
	protected insertComponentAfter ( components: Array<Component>, componentToAdd: Component,
		previousComponentID: string ): void {
		let indexOfPreviousComponent: number = components.length - 1;

		for ( var index = 0; index < components.length; index++ ) {
			var element = components[ index ];
			if ( element.id == previousComponentID ) {
				indexOfPreviousComponent = index;
				break;
			}
		}

		components.splice( indexOfPreviousComponent, 0, componentToAdd );
	}

	/**
	 * Instantiates preloader and fill it with list of required assets for loading
	 */
	protected createPreloader (): void {
		this.preloader = Container.get( SmartPreloaderController );
		this.preloader.loadingFinishedSignal.add( this.onPreloadingDone, this );
		this.preloader.setAssetsForLoading( this.componentManager.listOfFilesToLoad );
	}

	protected onShowGame (): void {
		this.preloader.destroy();
		this.preloader = null;
		this.gameContainerManager.enableGameInteraction( true );
	}

	protected onPreloadingDone ( data?: any ): void {
		//Parse the JSON assets for localisation
		this.translator.onAssetsLoaded();

		this.createServer();
		this.errorDialog.init( this.server.errorHandleService );

		this.server.connect();
		this.addEventListeners();
	}

	/**
	 * Creates server
	 */
	protected createServer (): void {
		this.server = Container.get( AbstractServer );
		this.gameController.setServer( this.server );
		this.gameController.gameReadySignal.once( this.onGameReady, this );
	}

	protected onGameReady (): void {
		this.setPageTitle();
		this.preloader.destroy();
	}

	protected setPageTitle (): void {
		DOMContent.setPageTitle( this.translator.t( 'gameName' ) );
	}

}

interface ApiMessage {
	messageType: string;
	api: AbstractApi;
}



