import { Container } from 'pixi.js';
import { Inject, Singleton } from 'typescript-ioc/es5';
import { GameController } from 'corengf/src/core/GameController';
import { InteractiveElement } from 'corengf/src/elements/canvas/InteractiveElement';


@Singleton
export class KeyboardManager {

	@Inject
	protected gameController: GameController;

	private buttonMap: { [ key: string ]: InteractiveElement | Container } = {};

	private isInited: boolean = false;

	public init () {
		if ( !this.isInited ) {
			onkeypress = this.handle.bind( this );
			this.isInited = true;
		}
	}

	/**
	 * Handles keyboard event, called by the browser. Do not invoke directly
	 * @param e DOM Keyboard event
	 */
	private handle ( e: KeyboardEvent ): void {
		const target = this.buttonMap[ e.keyCode ];
		if ( target && target.interactive ) {
			if ( !( target as InteractiveElement ).actionKeycodeMap ) { // assume it is button
				target.emit( 'pointertap' );
			} else {
				( target as InteractiveElement ).actionKeycodeMap[ e.keyCode ]();
			}
		}
	}

	public register ( key: Keycode, target: InteractiveElement | Container ): void {
		if ( !this.buttonMap[ key ] ) {
			this.buttonMap[ key ] = target;
		} else {
			console.warn( `Keycode[${ Keycode[ key ] }] is already bind.` );
		}
	}
}

//Additional keys: https://www.cambiaresearch.com/articles/15/javascript-char-codes-key-codes
//KeyboardEvent.keyCode is the only reliable cross-browser indicator
export enum Keycode {
	backspace = 8,
	tab = 9,
	enter = 13,
	shift = 16,
	ctrl = 17,
	alt = 18,
	pause_break = 19,
	caps_lock = 20,
	escape = 27,
	space = 32,
	page_up = 33,
	page_down = 34,
	end = 35,
	home = 36,
	left_arrow = 37,
	up_arrow = 38,
	right_arrow = 39,
	down_arrow = 40,
	insert = 45,
	delete = 46,
	number_0 = 48,
	number_1 = 49,
	number_2 = 50,
	number_3 = 51,
	number_4 = 52,
	number_5 = 53,
	number_6 = 54,
	number_7 = 55,
	number_8 = 56,
	number_9 = 57,
	a = 65,
	b = 66,
	c = 67,
	d = 68,
	e = 69,
	f = 70,
	g = 71,
	h = 72,
	i = 73,
	j = 74,
	k = 75,
	l = 76,
	m = 77,
	n = 78,
	o = 79,
	p = 80,
	q = 81,
	r = 82,
	s = 83,
	t = 84,
	u = 85,
	v = 86,
	w = 87,
	x = 88,
	y = 89,
	z = 90,
}


