import { extras, Container, spine, Point } from 'pixi.js';
import { Asset } from 'corengf/src/core/AssetLoader';
import { Animation } from 'corengf/src/elements/canvas/animation/Animation';
import { SymbolSet } from 'slotngf/src/components/reels/logic/ReelsConfig';
import { WinDisplayView } from 'slotngf/src/components/winDisplay/base/WinDisplayView';
import { IWinBoxConfig } from 'slotngf/src/components/winDisplay/winBoxes/WinBoxConfig';
import { IShowWinLineData } from 'slotngf/src/components/winDisplay/base/WinDisplayController';


export class WinBoxView extends WinDisplayView {

	protected animations: Array<extras.AnimatedSprite | spine.Spine>;
	protected animationContainer: Container;
	protected config: IWinBoxConfig;


	public getAssetsList (): Array<Asset> {
		return [
			{
				assetKey: 'winBoxes',
				assetURL: 'assets/winDisplay/Win_Boxes.json'
			}
		];
	}

	protected createElements ( config?: IWinBoxConfig ): void {
		this.config = config;
		this.animations = new Array<extras.AnimatedSprite>();
		this.animationContainer = new Container();
		this.addChild( this.animationContainer );
		this.createBoxes( config.defaultPoolSize ? config.defaultPoolSize : 6 ); // default win 2 line, so the animation amount is 2 x 3.
	}

	protected createBoxes ( number: number ): void {

		for ( let i = 0; i < number; i++ ) {
			let animation: extras.AnimatedSprite | spine.Spine;
			if ( Array.isArray( this.config.animationFiles ) ) {
				animation = Animation.createFrameAnimation( this.config.animationFiles );
				animation.anchor.set( 0.5, 0.5 );
				animation.loop = this.config.loop;
			} else {
				animation = Animation.createSpineAnimation( this.config.animationFiles );
			}
			animation.visible = false;
			this.animations.push( animation );
			this.animationContainer.addChild( animation );
		}
	}

	public hide (): void {
		this.visible = false;
		this.stopAnimating();
	}

	public showPayline ( data: IShowWinLineData ): void {
		this.visible = true;
		this.stopAnimating();

		let reelDefinition = this.reelsConfig.reelDefinition;
		let width = reelDefinition.symbol.width;
		let height = reelDefinition.symbol.height;

		let winBoxIndex = 0;
		data.winData.winTable.forEach( ( reel, i ) => {
			const orientationData = this.getSingleReelLayoutData( 0, i );

			reel.forEach( ( value, j ) => {
				if ( !value || ( value && this.isSpecialSymbol( data.winData.symbol ) ) ) {
					return;
				}

				let xStart = orientationData.position.x + ( width / 2 ) + this.config.boxOffset.x;
				let yStart = orientationData.position.y + ( height / 2 ) + ( height * j ) + this.config.boxOffset.y;
				let position = new Point( xStart, yStart );

				let animation = this.animations[ winBoxIndex ];

				if ( !animation ) { // create new animaion and get it again.
					this.createBoxes( 1 );
					animation = this.animations[ winBoxIndex ];
				}

				animation.position = position;
				animation.visible = true;
				animation.updateTransform();
				if ( this.isSpineAnimation( animation ) ) {
					animation.state.setAnimation( 0, this.config.animationName, this.config.loop );
				} else {
					animation.play();
				}
				winBoxIndex++;
			} );
		} );
	}

	protected isSpecialSymbol ( symbol: string ): boolean {
		return symbol === SymbolSet.SCATTER;
	}

	public portraitLayout (): void {
		super.portraitLayout();
		this.animationContainer.position.x = this.layoutData.position.x;
		this.animationContainer.position.y = this.layoutData.position.y;
		this.animationContainer.scale.set( this.layoutData.scale, this.layoutData.scale );
	}

	public landscapeLayout (): void {
		super.landscapeLayout();
		this.animationContainer.position.x = this.layoutData.position.x;
		this.animationContainer.position.y = this.layoutData.position.y;
		this.animationContainer.scale.set( this.layoutData.scale, this.layoutData.scale );
	}

	protected stopAnimating (): void {
		this.animations.forEach( ( animation: extras.AnimatedSprite | spine.Spine ) => {
			if ( this.isSpineAnimation( animation ) ) {
				animation.state.setEmptyAnimation( 0, 0 );
			} else {
				animation.gotoAndStop( 0 );
			}
			animation.visible = false;
		} );
	}

	protected isSpineAnimation ( animation: extras.AnimatedSprite | spine.Spine ): animation is spine.Spine {
		return ( <spine.Spine> animation ).spineData !== undefined;
	}
}



