import { WebTextElement, IWebTextConfig } from 'corengf/src/elements/webpage/text/WebTextElement';

/**
 * Text content element for web page, which contains multiple lines
 */
export class WebTextContentElement extends WebTextElement {

	public static elementType: string = 'WEB_TEXT_CONTENT_ELEMENT';

	public CreateElement ( config: IWebTextContentConfig ): IWebTextContentConfig {
		return config;
	}
}

export interface IWebTextContentConfig extends IWebTextConfig {
	contentStyle?: string;
	subContent?: IWebTextContentConfig;
}



