import { SlotBackgroundConfig } from 'slotngf/src/components/background/SlotBackgroundConfig';
import { ICoreUISpriteElementConfig, ICoreUISpineAnimationElementConfig } from 'corengf/src/factory/CoreUIElementFactory';
import { Sprite } from 'corengf/src/elements/canvas/sprite/Sprite';
import { SpineAnimation } from 'corengf/src/elements/canvas/animation/spine/SpineAnimation';
import * as BackgroundMapConfig from 'game/src/components/background/MapConfig';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Orientation } from 'corengf/src/display/Viewport';
import { ICoreElementLayoutConfig } from 'corengf/src/elements/canvas/CoreElement';


export class DHBBackgroundConfig extends SlotBackgroundConfig {


	public animations: Array<ICoreUISpineAnimationElementConfig> = [
		{
			elementType: SpineAnimation.elementType,
			elementConfig: {
				name: 'baseBackground',
				animationName: 'baseBackground',
				stateAnimationMap: BackgroundMapConfig.map,
			}
		},
		{
			elementType: SpineAnimation.elementType,
			elementConfig: {
				name: 'backgroundPetals',
				animationName: 'backgroundPetals',
				stateAnimationMap: BackgroundMapConfig.petalsMap,
			}
		},
		{
			elementType: SpineAnimation.elementType,
			elementConfig: {
				name: 'backgroundReels',
				animationName: 'backgroundReels',
				stateAnimationMap: BackgroundMapConfig.reelsMap,
			}
		},
		{
			elementType: SpineAnimation.elementType,
			elementConfig: {
				name: 'backgroundMarquee',
				animationName: 'backgroundMarquee',
				stateAnimationMap: BackgroundMapConfig.marqueeMap,

			}
		}
	];


	public staticImages: Array<ICoreUISpriteElementConfig> = [

		{
			elementType: Sprite.elementType,
			elementConfig: {
				name: 'logo',
				assetName: 'logo',
				isOrientationSupported: true

			}
		}
	];

	public coreElementOrientationConfig: TsMap<Orientation, TsMap<string, ICoreElementLayoutConfig>> = new TsMap( [
		[
			Orientation.PORTRAIT, new TsMap( [
				[
					'logo', {
						position: { x: 0, y: 70 },
						scale: { x: 1.45, y: 1.45 }
					}
				]
			] )
		],
		[
			Orientation.LANDSCAPE, new TsMap( [
				[
					'logo', {
						position: { x: -10, y: 30 },
						scale: { x: 0.5, y: 0.5 }
					}
				],
			] )
		]
	] );
}


