import { startOfDay, endOfDay, subDays } from 'date-fns';


/**
 * A helper class for handle dateTime
 *
 * @export
 * @class DateTime
 */
export class DateTime {

	public static utcTimezoneOffset = new Date().getTimezoneOffset() % 60;

	/**
	 * Get the begin and end of today.
	 *
	 * @static
	 * @param {boolean} [inSeconds=false] true = present in seconds, false = present in milliseconds
	 * @returns {DateTimeRange}
	 * @memberof DateTime
	 */
	public static UTCToday ( inSeconds: boolean = false ): DateTimeRange {
		const today = new Date().getTime();

		const startTime = startOfDay( today ).getTime();
		const endTime = endOfDay( today ).getTime();
		return {
			startTime: ( inSeconds ) ? startTime / 1000 : startTime,
			endTime: ( inSeconds ) ? Math.floor( endTime / 1000 ) : endTime
		};
	}

	/**
	 * Get the begin and end of given n days.
	 *
	 * @static
	 * @param {number} nDays - The delta value
	 * @param {boolean} [inSeconds=false] true = present in seconds, false = present in milliseconds
	 * @returns {DateTimeRange}
	 * @memberof DateTime
	 */
	public static UTCPastNDays ( nDays: number, inSeconds: boolean = false ): DateTimeRange {
		const today = new Date().getTime();

		const startTime = startOfDay( subDays( today, nDays - 1 ) ).getTime();
		const endTime = endOfDay( today ).getTime();

		return {
			startTime: ( inSeconds ) ? startTime / 1000 : startTime,
			endTime: ( inSeconds ) ? Math.floor( endTime / 1000 ) : endTime
		};
	}
}

export interface DateTimeRange {
	startTime: number;
	endTime: number;
}


