import { MLUIPanel } from 'mlui/src/core/MLUIPanel';
import { IMLUITurboConfig } from './MLUITurboConfig';
import { TurboButton } from 'mlui/src/elements/button/TurboButton';

export class MLUITurboPanel extends MLUIPanel {

	protected turboButton: TurboButton;
	protected config: IMLUITurboConfig;

	protected createElements ( config: IMLUITurboConfig ): void {
		this.turboButton = new TurboButton( config.turboButton, this.localButtonsController );
		this.addChild( this.turboButton );
	}

	public setTurboButtonMode ( speed: number ): void {
		this.turboButton.changeTexture( speed );
	}

}


