import { IWinLinesConfig } from './../../core/config/IWinLinesConfig';

export class WinLinesConfig implements IWinLinesConfig {
	public name = 'winLines';
	public winLineAnimationDelay = 1000;
	public winLineLoopAnimationDelay = 1000;

	public freeSpinLastspinDelay = 1000;

	/**
	 * the delay time after show specitial winline like SCATTER.
	 *
	 * @memberof WinLinesConfig
	 */
	public specialSymbolDelay = 0;

	/**
	 * showAllLineWin=true. Display the reels win symbol animation at allLineWin/freeAllLineWin state.
	 *
	 * @memberof WinLinesConfig
	 */
	public showAllLineWin = true;
}


