import { NAudio } from './NAudio';
import { Singleton } from 'typescript-ioc/es5';
import { NAudioTrack, IAudioTrackData } from './AudioTrack';
import { AssetLoader } from 'corengf/src/core/AssetLoader';
import { TsMap } from './../utils/TsMap';
import { Browser } from 'corengf/src/utils/Browser';

/**
 * Class contains all available sound and is responsible for
 * loading and playing them.
 * It uses Howler.js to play audio. We have modified Howler sound file.
 * Please look at NAudio for details.
 */
@Singleton
export class SoundManager {
	protected assetLoader: AssetLoader;

	/**
	 * Play sound once we loaded file
	 * TODO: move it to GameConfig
	 */
	public playSoundsOnLoad: boolean = false;

	/**
	 * Disable loading of howler files on initialisation
	 */
	public static ignoreHowlerPreload: boolean = true;

	/**
	 * Cache of all sounds
	 */
	protected audios = new TsMap<string, NAudio>();

	/**
	 * Holds data for AudioTracks mute a track to mute all sound from that track
	 */
	protected tracks = new TsMap<string, NAudioTrack>();

	get allSoundsKeys (): any {
		return this.audios.keys();
	}

	private isInited: boolean = false;

	/**
	 * Holds AudioTracks mute status
	 */
	private muteMap: { [ key: string ]: boolean } = {};

	/**
	 * Override this to add new tracks
	 */
	public init ( assetLoader: AssetLoader ): void {
		if ( this.isInited ) {
			return;
		}
		this.assetLoader = assetLoader;
		this.addTracks( [
			{
				name: AudioTrackName.MUSIC,
				keepAudioOnEnable: true,
				isSingleChannel: true,
			},
			{
				name: AudioTrackName.EFFECT,
				keepAudioOnEnable: false,
				isSingleChannel: false,
			}
		] );

		this.setMuteMap();
		this.isInited = true;
		document.addEventListener( Browser.VISIBILITY_CHANGE, this.onVisibilityChanged.bind( this ), false );
	}

	private setMuteMap (): void {
		this.muteMap[ AudioTrackName.MUSIC ] = false;
		this.muteMap[ AudioTrackName.EFFECT ] = false;
	}

	private onVisibilityChanged (): void {
		if ( document[ Browser.HIDDEN ] ) {
			this.muteAll();
		} else {
			this.unmuteAll();
		}
	}

	/**
	 * Return Howler.js audio object from cache
	 * @param key - the unique id of sound
	 * @return NAudio object
	 */
	public audio ( key: string ): NAudio {
		return this.audios.get( key );
	}

	/**
	 *
	 * @param key The audio key associated with the audio to stop playing
	 * @param trackName The track to play the audio on
	 * @param loop whether the audio should loop or not
	 */
	public play ( key: string, trackName: string = AudioTrackName.EFFECT, loop: boolean = false, onComplete?: Function ): void {
		if ( key === '' ) {
			return;
		}
		if ( !this.assetLoader.resources[ key ] ) {
			console.warn( `Make sure audio:${ key } is loaded.` );
			return;
		}

		let track: NAudioTrack = this.tracks.get( trackName );
		const audio = this.assetLoader.resources[ key ].data as NAudio;

		if ( !this.audios.get( key ) ) {
			this.audios.set( key, audio );
		}

		if ( onComplete ) {
			audio.once( 'end', () => {
				onComplete();
			} );
		}

		if ( trackName === AudioTrackName.MUSIC ) {
			window.setTimeout( () => {
				track.play( audio, loop );
			}, 0 );
		} else {
			track.play( audio, loop );
		}

	}

	/**
	 * Pauses the specified audio
	 * @param key @param key The audio key associated with the audio to stop playing
	 */
	public pause ( key: string ): void {
		if ( !this.audios.get( key ) ) {
			return;
		}

		window.setTimeout( () => {
			this.audios.get( key ).pause();
		}, 0 );
	}

	/**
	 * stops the specified audio from playing
	 * @param key The audio key associated with the audio to stop playing
	 */
	public stop ( key: string ): void {
		if ( !this.audios.get( key ) ) {
			return;
		}

		window.setTimeout( () => {
			this.audios.get( key ).stop();
		}, 0 );
	}

	/**
	 * Returns whether the sound associated with the sound key is playing or not
	 * @param key The audio key associated with the audio to stop playing
	 * @return is the sound playing true for yes false for no
	 */
	public isPlaying ( key: string ): boolean {
		if ( !this.audios.get( key ) ) {
			return false;
		}
		return this.audios.get( key ).playing();
	}
	protected addTrack ( trackData: IAudioTrackData ): void {
		let track = new NAudioTrack( trackData );
		this.tracks.set( trackData.name, track );
	}

	protected addTracks ( data: Array<IAudioTrackData> ): void {
		data.forEach( ( trackData: IAudioTrackData ) => {
			this.addTrack( trackData );
		} );
	}

	public muteTrack ( name: string ): void {
		this.tracks.get( name ).enabled = false;
		this.muteMap[ name ] = true;
	}

	public unmuteTrack ( name: string ): void {
		this.tracks.get( name ).enabled = true;
		this.muteMap[ name ] = false;
	}

	public muteAll (): void {
		this.tracks.forEach( ( track ) => {
			track.enabled = false;
		} );
	}

	public unmuteAll (): void {
		this.tracks.forEach( ( track ) => {
			if ( !this.muteMap[ track.name ] ) {
				track.enabled = true;
			}
		} );
	}

	public isTrackEnabled ( name: string ): boolean {
		return this.tracks.get( name ).enabled;
	}

	public destroy (): void {
		this.audios.clear();
		this.tracks.forEach( ( track: NAudioTrack ) => {
			track.destroy();
		} );
		this.tracks.clear();
	}
}

export enum AudioTrackName {
	MUSIC = 'music',
	EFFECT = 'effect'
}


