import { Container } from 'typescript-ioc/es5';
import { ICoreWebElementConfig } from 'corengf/src/elements/webpage/CoreWebElement';
import { WebTextElement, IWebTextConfig } from 'corengf/src/elements/webpage/text/WebTextElement';
import { WebLineElement, IWebLineConfig } from 'corengf/src/elements/webpage/line/WebLineElement';
import { WebImageElement, IWebImageConfig } from 'corengf/src/elements/webpage/image/WebImageElement';
import { WebPayoutElement, IWebPayoutConfig } from 'corengf/src/elements/webpage/payout/WebPayoutElement';
import { WebElementBlock, IWebElementBlockConfig } from 'corengf/src/elements/webpage/block/WebElementBlock';
import { WebTextContentElement, IWebTextContentConfig } from 'corengf/src/elements/webpage/text/WebTextContentElement';
import { WebLinePatternElement, IWebLinePatternConfig } from 'corengf/src/elements/webpage/linePattern/WebLinePatternElement';
import { WebGeneralPagingElement, IWebGeneralPagingConfig } from 'corengf/src/elements/webpage/paging/WebGeneralPagingElement';
import { IimageSlideshowConfig, WebImageSlideshowElement } from 'corengf/src/elements/webpage/imageSlideshow/WebImageSlideshowElement';

/**
 * elements that can be produce by core ui element factory.
 */
const WebElementList: any = {
	'WEB_TEXT_ELEMENT': Container.get( WebTextElement ),
	'WEB_IMAGE_ELEMENT': Container.get( WebImageElement ),
	'WEB_ELEMENT_BLOCK': Container.get( WebElementBlock ),
	'WEB_TEXT_CONTENT_ELEMENT': Container.get( WebTextContentElement ),
	'WEB_LINE_ELEMENT': Container.get( WebLineElement ),
	'WEB_PAYOUT_ELEMENT': Container.get( WebPayoutElement ),
	'WEB_LINE_PATTERN_ELEMENT': Container.get( WebLinePatternElement ),
	'WEB_GENERAL_PAGING_ELEMENT': Container.get( WebGeneralPagingElement ),
	'WEB_IMAGE_SLIDESHOW': Container.get( WebImageSlideshowElement )
};

/**
 * list for web elements that can preduce by web wlement factory.
 */
export class WebFactoryElementType {
	public static readonly WEB_TEXT_ELEMENT = WebTextElement.elementType;
	public static readonly WEB_IMAGE_ELEMENT = WebImageElement.elementType;
	public static readonly WEB_ELEMENT_BLOCK = WebElementBlock.elementType;
	public static readonly WEB_TEXT_CONTENT_ELEMENT = WebTextContentElement.elementType;
	public static readonly WEB_LINE_ELEMENT = WebLineElement.elementType;
	public static readonly WEB_PAYOUT_ELEMENT = WebPayoutElement.elementType;
	public static readonly WEB_LINE_PATTERN_ELEMENT = WebLinePatternElement.elementType;
	public static readonly WEB_GENERAL_PAGING_ELEMENT = WebGeneralPagingElement.elementType;
	public static readonly WEB_IMAGE_SLIDESHOW = WebImageSlideshowElement.elementType;
}

/**
 * a factory to create all web element.
 */
export class WebElementFactory {

	public static createInstance<T extends ICoreWebElementConfig> ( webElementConfig: ICoreWebElementConfig, ...args: Array<any> ): T {
		if ( WebElementList[ webElementConfig.type ] != undefined ) {
			return WebElementList[ webElementConfig.type ].createElement( webElementConfig, ...args );
		}
		throw new Error( `Class type \'${ webElementConfig.type }\' is not in element list` );
	}

	public static createWebTextElement ( webTextConfig: IWebTextConfig ): IWebTextConfig {
		return this.createInstance( webTextConfig );
	}

	public static createWebImageElement ( webImageConfig: IWebImageConfig ): IWebImageConfig {
		return this.createInstance( webImageConfig );
	}

	public static createWebTextContentElement ( webTextContentConfig: IWebTextContentConfig ): IWebTextContentConfig {
		return this.createInstance( webTextContentConfig );
	}

	public static createWebElementBlock<T extends ICoreWebElementConfig> ( webElementBlockConfig: IWebElementBlockConfig<T> ): IWebElementBlockConfig<T> {
		return this.createInstance( webElementBlockConfig );
	}

	public static createWebLineElement ( webLineConfig: IWebLineConfig ): IWebLineConfig {
		return this.createInstance( webLineConfig );
	}

	public static createWebPayoutElement ( webPayoutConfig: IWebPayoutConfig ): IWebPayoutConfig {
		return this.createInstance( webPayoutConfig );
	}

	public static createLinePatternElement ( webLinePatternConfig: IWebLinePatternConfig ): IWebLinePatternConfig {
		return this.createInstance( webLinePatternConfig );
	}

	public static createGeneralPagingElement<T extends ICoreWebElementConfig> ( webPagingConfig: IWebGeneralPagingConfig<T> ): IWebGeneralPagingConfig<T> {
		return this.createInstance( webPagingConfig );
	}

	public static createImageSlideshowElement<T extends ICoreWebElementConfig> ( webImageSlideshowElement: IimageSlideshowConfig<T> ): IimageSlideshowConfig<T> {
		return this.createInstance( webImageSlideshowElement );
	}
}



