import { Inject } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Actions } from 'corengf/src/utils/Actions';
import { ForcingSpinView } from './ForcingSpinView';
import { ForcingSpinConfig } from './ForcingSpinConfig';
import { SlotCompController } from './../mvc/SlotCompController';
import { IDemoConfig } from 'slotngf/src/core/config/IDemoConfig';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { SlotEvent } from 'slotngf/src/core/SlotEvent';

export class DemoActions {
	public static SHOW_DEMO_PANEL: string = 'showDemoPanel';
	public static HIDE_DEMO_PANEL: string = 'hideDemoPanel';
}

export class ForcingSpinController extends SlotCompController {
	public static readonly FORCE_SPIN_RESULTS: string = 'forceSpinResults';

	protected view: ForcingSpinView;
	protected demoPanelConfig: IDemoConfig;
	protected demoSymbols: TsMap<string, Array<Array<string>>>;

	constructor ( @Inject view: ForcingSpinView ) {
		super( view );
	}

	private onFullScreenUIShow (): void {
		this.view.setInteractive( false, false );
	}

	private onFullScreenUIHide (): void {
		this.view.setInteractive( true, true );
	}

	public init ( stateData: any ): void {
		this.demoPanelConfig = this.gameConfig.getConfig<IDemoConfig>( ForcingSpinConfig );
		this.view.initialize( this.demoPanelConfig );
		this.demoSymbols = this.demoPanelConfig.selectorData;
		this.view.clickSignal.add( this.onClickHandler, this );
		GlobalDispatcher.addListener( this, SlotEvent.SHOW_FULL_SCREEN_UI, this.onFullScreenUIShow.bind( this ) );
		GlobalDispatcher.addListener( this, SlotEvent.HIDE_FULL_SCREEN_UI, this.onFullScreenUIHide.bind( this ) );
		GlobalDispatcher.addListener( this, SlotEvent.SHOW_DEMO_SCREEN_UI, this.updateDemoPanelState.bind( this ) );
		this.finishStateAction();
	}

	protected startSpin (): void {
		this.view.setInteractive( false, false );
		this.view.hideDemoPanel();
		this.finishStateAction();
	}

	protected baseGameIdle (): void {
		this.view.setInteractive( true, true );
		this.finishStateAction();
	}

	protected allLineWin (): void {
		this.view.setInteractive( true, true );
		this.finishStateAction();
	}

	protected onClickHandler ( action: string, data: any ): void {
		let clickAction: string = action.substr( Actions.CLICK_ACTION_PREFIX.length );
		switch ( clickAction ) {
			case DemoActions.SHOW_DEMO_PANEL:
				this.view.showDemoPanel();
				break;

			case DemoActions.HIDE_DEMO_PANEL:
				this.view.hideDemoPanel();
				break;

			default:
				if ( this.demoSymbols.has( clickAction ) ) {
					let forceData = this.demoSymbols.get( clickAction );
					this.onForceClick( forceData );
				}
				break;
		}
	}

	protected onForceClick ( data: Array<Array<string>> ): void {
		this.view.hideDemoPanel();
		GlobalDispatcher.dispatchIntoExpectedStates( ForcingSpinController.FORCE_SPIN_RESULTS,
			[ this.stateNames.baseGameIdle, this.stateNames.showWinning, this.stateNames.showWinningLoop ], { symbols: data } );
	}

	protected updateDemoPanelState (): void {
		if ( this.view.showDemoPanelstate ) {
			this.view.showDemoPanel();
		} else {
			this.view.hideDemoPanel();
		}
	}
}



