import { DialogView } from 'corengf/src/components/dialog/DialogView';
import { Asset } from 'corengf/src/core/AssetLoader';

export class SlotDialogView extends DialogView {
	public getAssetsList (): Array<Asset> {
		const asset = [
			{
				assetKey: 'dialog',
				assetURL: 'assets/dialog/dialog.json'
			}
		];

		return asset;
	}
}


