import { Scenario } from 'corengf/src/states/Scenario';
import { IWinningData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { SpinResponseFormatter } from 'slotngf/src/server/ng/formatter/http/in/SpinResponseFormatter';
import { IFeatureInfo, ISpinResult, IGameState } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { AbstractFeatureScenarioFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/AbstractFeatureScenarioFormatter';
import { IMultiplierFeatureDetail, FeatureTypes } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';

export class MultiplierFeatureFormatter extends SpinResponseFormatter implements AbstractFeatureScenarioFormatter {

	public processIncomingMessage ( message: IGameState, featureInfo?: IFeatureInfo ): Scenario {
		console.warn( 'here shouldn\'t be triggered, since replacing symbols feature don\'t need another transaction to complete feature' );
		return null;
	}

	public getFeatureScenarioBeforeFreeSpinStop ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return freeSpinStopScenario;
	}

	public getFeatureScenarioAfterSpinStop ( responseScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		if ( !responseScenario.hasStateName( this.scenarioNames.multiplierApplied ) && this.getFeatureData( message.features ).applied_multiplier != 0 ) {
			responseScenario.addScenario( this.scenarioManager.getScenario( this.scenarioNames.multiplierApplied ) );
			responseScenario.setStateData( this.stateNames.multiplierApplied, this.getFeatureData( message.features ) );
		}
		return responseScenario;
	}

	public getFeatureShowWinningData ( winningData: IWinningData, spinResult: ISpinResult, featureInfo: IFeatureInfo ): IWinningData {
		return winningData;
	}

	public getFeatureScenarioAfterWin ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return freeSpinStopScenario;
	}

	protected getFeatureDetail ( featureInfo: any ): IMultiplierFeatureDetail {
		return featureInfo.feature_state;
	}

	protected getFeatureData ( message: any ): IMultiplierFeatureDetail {
		let multiplier = 0;
		let appliedMultiplier = 0;
		let addedMultiplier = 0;

		if ( message ) {
			message.forEach( element => {
				if ( element.type === FeatureTypes.MULTIPLIER ) {
					multiplier = element.feature_state.multiplier;
					appliedMultiplier = element.feature_state.applied_multiplier;
					addedMultiplier = element.feature_state.added_multiplier;
				}
			} );
		}

		return {
			multiplier: multiplier,
			applied_multiplier: appliedMultiplier,
			added_multiplier: addedMultiplier
		} as IMultiplierFeatureDetail;
	}

	public triggeredFeatureIncomingMessage ( message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		let responseScenrio = this.scenarioManager.getScenario( this.scenarioNames.multiplierAdded );
		responseScenrio.setStateData( this.stateNames.multiplierAdded,
			this.getFeatureDetail( featureInfo )
		);

		return responseScenrio;
	}

	public getFeatureRestoreScenario ( restoreScenario: Scenario, featureInfo: IFeatureInfo ): Scenario {
		return restoreScenario;
	}

	protected restoreFeatureIncomingMessage ( featureDetail: IMultiplierFeatureDetail ): Scenario {
		// TODO: not finish yet. we need to RGS's data.
		let responseScenrio = this.scenarioManager.getScenario( this.scenarioNames.multiplierAdded );
		responseScenrio.setStateData( this.stateNames.multiplierAdded, {
			multiplier: featureDetail.multiplier,
			applied_multiplier: featureDetail.applied_multiplier,
			added_multiplier: featureDetail.added_multiplier
		} );
		return responseScenrio;
	}
}


