/**
 * The postfix for names of symbol's assets
 */
export enum SymbolStatePostfix {
	STATIC = '',
	BLUR = '_Blur',
	LOSE = '_Lose',
	WIN = '_Win'
}

export enum SymbolState {
	STATIC = 0,
	BLUR = 1,
	LOSE = 2,
	WIN = 3
}

export enum SymbolAnimationPostfix {
	IDLE = '_Idle',
	WIN = '_Win',
	LOCKWIN = '_Lock_Win',
	DROPPED = '_Drop',
	ANTICIPATION = '_Anticipation',
	WIN_INTRO = '_Win_Intro',
	WILD_X2 = '_x2',
	WILD_X3 = '_x3',
	WILD_X4 = '_x4',
}



