import { Inject } from 'typescript-ioc/es5';
import { MLUISpinSettingsController } from 'mlui/src/components/spinSettings/MLUISpinSettingsController';
import { MLUISpinSettingsPanel } from 'mlui/src/components/spinSettings/MLUISpinSettingsPanel';

export class DHBMLUISpinSettingsController extends MLUISpinSettingsController {

	protected view: MLUISpinSettingsPanel;

	constructor ( @Inject view: MLUISpinSettingsPanel ) {
		super( view );
	}

	protected startPickFeature ( stateData: any ): void {
		if ( !this.freeSpinsMode ) {
			this.onStopAutoSpins();
		}
		this.finishStateAction();
	}

	protected freeSpinsShowWinning (): void {
		// this.triggerNextTurn();
		this.finishStateAction();
	}

}


