import * as MiniSignal from 'mini-signals';
import { Inject } from 'typescript-ioc/es5';
import { Translator } from 'corengf/src/utils/Translator';
import { SimpleTween } from 'corengf/src/time/SimpleTween';
import { Orientation } from 'corengf/src/display/Viewport';
import { UIPanelView } from 'corengf/src/components/ui/components/UIPanelView';
import { IInformationHeaderConfig, IInformationHeaderRatioConfig } from 'corengf/src/components/ui/components/informationHeader/UIInformationHeaderConfig';
import { GameConfig } from 'corengf/src/core/GameConfig';

/**
 * display ui information header always shows in game view,
 * including logo, title, and background
 */
export class UIInformationHeaderView extends UIPanelView {
	protected readonly DIV_INFORMATION_HEADER_ID = 'information_header';
	protected readonly DIV_INFORMATION_HEADER_CLOCK_ID: string = 'information_header_clock';
	protected readonly IMG_INFORMATION_HEADER_BACKGROUND_ID: string = 'information_header_background';
	protected readonly IMG_INFORMATION_HEADER_TITLE_LOGO_ID: string = 'information_header_logo';
	protected readonly DIV_INFORMATION_HEADER_TITLE_TEXT_ID: string = 'information_header_text';

	@Inject
	protected translator: Translator;

	@Inject
	protected gameConfig: GameConfig;

	protected config: IInformationHeaderConfig;

	private container: HTMLDivElement = null;
	private useWidthRatio: number;
	private useHeightRatio: number;
	protected readonly pxUnit = 'px';
	protected backgroundHeight;
	public clickBackgroundSignal: MiniSignal = new MiniSignal();

	constructor () {
		super();
		this.viewport.onResizeSignal.add( this.onResize.bind( this ) );
	}

	public setInteractive ( value: boolean ): void {
		if ( this.mainContainer && this.mainContainer.interactive != value ) {
			this.mainContainer.interactive = value;
		}
	}

	protected createElements ( config?: IInformationHeaderConfig ): void {
		super.createElements( config );
		this.createInformationHeader( config );
	}

	protected createInformationHeader ( config: IInformationHeaderConfig ): void {

		const body = document.body;

		var position: InsertPosition = 'afterbegin';

		this.container = document.createElement( 'div' );
		this.container.setAttribute( 'id', this.DIV_INFORMATION_HEADER_ID );

		this.addBackground( config );
		this.addClockCompoment();
		this.addTitleCompoment();

		body.appendChild( this.container );
		body.insertAdjacentElement( position, this.container );

		this.container.ontouchmove = function ( event: any ) {
			event.preventDefault();
		};

		this.onResize();
	}

	protected addBackground ( config: IInformationHeaderConfig ): void {
		if ( this.config.isRealMode || PRODUCTION ) {
			this.container.appendChild( this.createBackground( config ) );
		} else {
			this.container.appendChild( this.createBackground( config, this.onClickBackground.bind( this ) ) );
			this.mainContainer.interactive = true;
		}
		this.container.appendChild( this.createLogoElement( this.config.titleConfig.logo.originalAssetName ) );
	}

	protected onClickBackground (): void {
		this.clickBackgroundSignal.dispatch();
	}

	protected createBackground ( config: IInformationHeaderConfig, callbackFunction?: any ): Node {
		var objectName = this.IMG_INFORMATION_HEADER_BACKGROUND_ID;
		let background = document.createElement( 'div' );
		background.setAttribute( 'id', objectName );
		background.style.backgroundColor = config.background.backgroundColor;
		background.style.opacity = config.background.opacity;
		background.onclick = callbackFunction;
		return background;
	}

	protected getImageElement ( assetName: string, objectName: string ): HTMLImageElement {
		const img = document.createElement( 'img' );

		img.setAttribute( 'src', this.getImageSource( assetName ) );
		img.setAttribute( 'id', objectName );

		return img;
	}

	protected getImageSource ( assetName: string ): string {
		let url = this.config.assetPath + assetName;
		return url;
	}

	protected addTitleCompoment (): void {
		this.container.appendChild( this.createTextElement( this.config.titleConfig.gameName, this.config.isRealMode ) );
	}

	protected createTextElement ( title: string, isRealMode: boolean ): Node {
		const text = document.createElement( 'div' );
		text.setAttribute( 'id', this.DIV_INFORMATION_HEADER_TITLE_TEXT_ID );
		text.innerText = this.translator.t( title );

		if ( !isRealMode ) {
			text.innerText = text.innerText + ' - DEMO';
		}

		return text;
	}

	protected createLogoElement ( assetName: string ): Node {
		var objectName = this.IMG_INFORMATION_HEADER_TITLE_LOGO_ID;
		return this.getImageElement( assetName, objectName );
	}

	protected addClockCompoment (): void {
		this.container.appendChild( this.createClock() );
	}

	/**
	 * Create div in dom as clock
	 */
	protected createClock (): Node {
		var currentDiv = document.createElement( 'div' );
		var position: InsertPosition = 'afterbegin';
		currentDiv.setAttribute( 'id', this.DIV_INFORMATION_HEADER_CLOCK_ID );

		document.body.insertAdjacentElement( position, currentDiv );

		this.ticking();

		return currentDiv;
	}

	protected ticking (): void {
		const dom = document.getElementById( this.DIV_INFORMATION_HEADER_CLOCK_ID );

		if ( dom ) {
			let date = new Date();
			let h = date.getHours(); // 0 - 23
			let m = date.getMinutes(); // 0 - 59

			let strh = ( h < 10 ) ? '0' + h : h;
			let strm = ( m < 10 ) ? '0' + m : m;
			let time = strh + ':' + strm;

			if ( dom.innerText != time ) {
				dom.innerText = time;
			}

			if ( dom.textContent != time ) {
				dom.textContent = time;
			}

			new SimpleTween()
				.delay( 500 )
				.onComplete( () => {
					this.ticking();
				} )
				.start();
		}
	}

	protected onResize (): void {
		const dom = document.getElementById( this.DIV_INFORMATION_HEADER_ID );

		if ( dom ) {
			let marginLeft = this.viewport.canvas.marginLeft;
			let width = this.viewport.canvas.width;
			let height = this.viewport.canvas.height;

			dom.style.marginLeft = marginLeft.toString() + this.pxUnit;
			dom.style.marginTop = ( this.viewport.canvas.marginTop ).toString() + this.pxUnit;
			dom.style.width = width.toString() + this.pxUnit;

			if ( this.viewport.properOrientation === Orientation.PORTRAIT ) {
				this.useHeightRatio = Number.parseFloat( ( height / this.gameConfig.mainConfig.resolution.width ).toFixed( 2 ) );
				this.useWidthRatio = Number.parseFloat( ( width / this.gameConfig.mainConfig.resolution.height ).toFixed( 2 ) );
			} else if ( this.viewport.properOrientation === Orientation.LANDSCAPE ) {
				this.useHeightRatio = Number.parseFloat( ( height / this.gameConfig.mainConfig.resolution.height ).toFixed( 2 ) );
				this.useWidthRatio = Number.parseFloat( ( width / this.gameConfig.mainConfig.resolution.width ).toFixed( 2 ) );
			}
		}

		this.onResizeBackground();
		this.onResizeClock();
		this.onResizeTitle();
	}

	protected onResizeClock (): void {
		const dom = document.getElementById( this.DIV_INFORMATION_HEADER_CLOCK_ID );

		if ( dom ) {
			let width = this.viewport.canvas.width;

			let baseFontSize = this.config.clockConfig.text.baseFontSize;

			if ( this.viewport.properOrientation === Orientation.PORTRAIT ) {
				dom.style.left = this.getTextLeft( width, this.config.clockConfig.text.portraitRatio ) + this.pxUnit;
				dom.style.top = this.getTextTop() + this.pxUnit;
			} else if ( this.viewport.properOrientation === Orientation.LANDSCAPE ) {
				dom.style.left = this.getTextLeft( width, this.config.clockConfig.text.landscapeRatio ) + this.pxUnit;
				dom.style.top = this.getTextTop() + this.pxUnit;
			}

			dom.style.fontSize = ( ( baseFontSize * this.useHeightRatio + baseFontSize * this.useWidthRatio ) / 2 ) + this.pxUnit;
		}
	}

	protected getTextLeft ( width: number, config: IInformationHeaderRatioConfig ): number {
		let value = width * 0.9;

		if ( config ) {
			if ( config.left ) {
				value = width * config.left;
			}
		}

		return value;
	}

	protected getTextTop (): number {
		return this.backgroundHeight / 4;
	}

	protected onResizeTitle (): void {
		this.resizeTitleText();
		this.resizeTitleLogo();
	}

	protected resizeTitleText (): void {
		const dom = document.getElementById( this.DIV_INFORMATION_HEADER_TITLE_TEXT_ID );
		if ( dom ) {
			let baseFontSize = this.config.titleConfig.text.baseFontSize;

			if ( this.viewport.properOrientation === Orientation.PORTRAIT ) {
				dom.style.top = this.getTextTop() + this.pxUnit;
			} else if ( this.viewport.properOrientation === Orientation.LANDSCAPE ) {
				dom.style.top = this.getTextTop() + this.pxUnit;
			}

			dom.style.fontSize = ( ( baseFontSize * this.useHeightRatio + baseFontSize * this.useWidthRatio ) / 2 ) + this.pxUnit;
		}
	}

	protected resizeTitleLogo (): void {
		const dom = document.getElementById( this.IMG_INFORMATION_HEADER_TITLE_LOGO_ID );
		if ( dom ) {
			let width = this.viewport.canvas.width;
			let height = this.viewport.canvas.height;

			if ( this.viewport.properOrientation === Orientation.PORTRAIT ) {
				dom.style.width = ( width * this.config.titleConfig.logo.portraitWidth ).toFixed( 2 ) + this.pxUnit;
				dom.style.height = 'auto';

				dom.style.left = this.getImageLeft( width, this.config.titleConfig.logo.portraitRatio ) * -1 + this.pxUnit;
				dom.style.top = this.getImageTop( height, this.config.titleConfig.logo.portraitRatio ) * -1 + this.pxUnit;
			} else if ( this.viewport.properOrientation === Orientation.LANDSCAPE ) {
				dom.style.height = ( height * this.config.titleConfig.logo.landscapeHeight ).toFixed( 2 ) + this.pxUnit;
				dom.style.width = 'auto';

				dom.style.left = this.getImageLeft( width, this.config.titleConfig.logo.landscapeRatio ) * -1 + this.pxUnit;
				dom.style.top = this.getImageTop( height, this.config.titleConfig.logo.landscapeRatio ) * -1 + this.pxUnit;
			}
		}
	}

	protected getImageLeft ( width: number, config: IInformationHeaderRatioConfig ): number {
		const leftRatio = ( config.left ) ? config.left : 0.005;
		return ( width * leftRatio ) * this.useWidthRatio;
	}

	protected getImageTop ( height: number, config: IInformationHeaderRatioConfig ): number {
		const topRatio = ( config.top ) ? config.top : 0.002;
		return height * topRatio;
	}

	protected onResizeBackground (): void {
		const dom = document.getElementById( this.IMG_INFORMATION_HEADER_BACKGROUND_ID );

		if ( dom ) {
			dom.style.width = this.viewport.canvas.width + this.pxUnit;
			let height = this.viewport.canvas.height;
			if ( this.viewport.properOrientation === Orientation.PORTRAIT ) {
				this.setBlockHeight( height, this.config.background.portraitRatio );
			} else if ( this.viewport.properOrientation === Orientation.LANDSCAPE ) {
				this.setBlockHeight( height, this.config.background.landscapeRatio );
			}
			dom.style.height = this.backgroundHeight + this.pxUnit;
		}
	}

	protected setBlockHeight ( height: number, config: IInformationHeaderRatioConfig ) {
		const heightRatio = ( config.height ) ? config.height : 0.025;
		this.backgroundHeight = ( height * heightRatio ).toFixed( 2 );
	}
}


