import { Inject } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { NGHttpTransport } from 'corengf/src/server/ng/transport/NGHttpTransport';
import { SlotNGModel } from 'slotngf/src/server/ng/model/SlotNGModel';
import { IInitResponse } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';

/**
 * Handle Http transport to NG server for slot game
 */
export class SlotNGHttpTransport extends NGHttpTransport {

	@Inject
	protected slotNGModel: SlotNGModel;

	public init ( data: IInitResponse ): void {
		this.onConnect();
	}

	public getSessionId (): string {
		return this.slotNGModel.sessionId;
	}

	public getPartnerCode (): string {
		return this.slotNGModel.partnerCode;
	}

	public getStateTag (): string {
		return this.slotNGModel.stateTag;
	}

	public setStateTag ( stateTag: string ) {
		this.slotNGModel.stateTag = stateTag;
	}

	public getPlayerId (): string {
		return this.slotNGModel.playerId;
	}

	public getHeaders (): TsMap<string, string> {
		let headers = super.getHeaders();
		headers.set( 'X-Genesis-Session', this.getSessionId() );
		return headers;
	}
}



