import { CoreWebElement, ICoreWebElementConfig } from 'corengf/src/elements/webpage/CoreWebElement';
import { IArrowButton, IDropDownBox } from 'corengf/src/elements/webpage/WebpageElement';

/**
 * general paging element for web page
 */
export class WebGeneralPagingElement extends CoreWebElement {

	public static elementType: string = 'WEB_GENERAL_PAGING_ELEMENT';

	public createElement<T extends ICoreWebElementConfig> ( pagingConfig: IWebGeneralPagingConfig<T> ): IWebGeneralPagingConfig<T> {
		return pagingConfig;
	}
}

export interface IWebPagingButtonConfig {
	arrowButton?: IArrowButton;
	dropDownBox?: IDropDownBox;
}

/**
 * @param pages :This information needs to be formatted
 */
export interface IWebGeneralPagingConfig<T extends ICoreWebElementConfig> extends ICoreWebElementConfig {
	pagingConfig: IWebPagingButtonConfig;
	pages: Array<T>;
}


