import { Point } from 'pixi.js';
import { Inject } from 'typescript-ioc/es5';
import { Button } from 'corengf/src/elements/canvas/button/Button';
import { MLUIPanel } from 'mlui/src/core/MLUIPanel';
import { MLUISlotPanelConfig, SlotButton } from 'mlui/src/components/slotPanel/MLUISlotPanelConfig';
import { SlotBankService } from 'slotngf/src/core/SlotBankService';


export class MLUISlotPanelView extends MLUIPanel {

	@Inject
	protected bankService: SlotBankService;

	protected config: MLUISlotPanelConfig;

	protected buttons: Array<Button> = new Array<Button>();

	protected buttonIncrement: Button;
	protected buttonDecrement: Button;
	protected currentBet: number;

	protected createElements ( config: MLUISlotPanelConfig ): void {
		super.createElements( config );
		for ( const member in SlotButton ) {

			if ( SlotButton[ member ] ) {
				this.renderButton( SlotButton[ member ] );
			}
		}

		this.buttonIncrement = this.buttons.find( ( button ) => { return button.name == this.config.betIncrementButton.name; } );
		this.buttonDecrement = this.buttons.find( ( button ) => { return button.name == this.config.betDecrementButton.name; } );
		this.setConfirmClickData();
	}

	protected renderButton ( buttonName: string ): void {
		const button = new Button( this.config[ buttonName ], this.localButtonsController );
		button.anchor = new Point( 0.5, 0.5 );
		this.buttons.push( button );
		this.addChild( button );
	}

	public onBetIncrease (): void {
		this.bankService.increaseBet();
		this.updateClickData( this.buttonIncrement );
		this.setBetButtonEnable();
	}

	public onBetDecrease (): void {
		this.bankService.decreaseBet();
		this.updateClickData( this.buttonDecrement );
		this.setBetButtonEnable();
	}

	public setBetButtonEnable (): void {
		if ( !this.buttonDecrement.visible || !this.buttonIncrement.visible ) {
			this.buttonIncrement.visible = true;
			this.buttonDecrement.visible = true;
		}

		this.currentBet = this.bankService.currentBetIndex;

		if ( this.currentBet == 0 ) {
			this.buttonDecrement.setEnable( false );
			this.buttonIncrement.setEnable( true );
		} else if ( this.currentBet == this.bankService.betOptions.length - 1 ) {
			this.buttonIncrement.setEnable( false );
			this.buttonDecrement.setEnable( true );
		} else {
			this.buttonDecrement.setEnable( true );
			this.buttonIncrement.setEnable( true );
		}
	}

	protected setConfirmClickData (): void {
		this.buttonDecrement.clickData = {
			data: {}
		};

		this.buttonIncrement.clickData = {
			data: {}
		};
	}

	protected updateClickData ( button: Button ): void {
		const index = this.bankService.betOptions.indexOf( this.bankService.bet );
		const totalBet = this.bankService.betOptions[ index ] * this.bankService.lines;

		let clickData = button.clickData;
		clickData.data.totalBet = totalBet;
		button.clickData = clickData;
	}
}


