import { Scenario } from 'slotngf/corengf/src/states/Scenario';
import { IGameState, IFeatureInfo } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { WildLockedFeatureFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/complete/WildLockedFeatureFormatter';

export class DHBWildLockedFeatureFormatter extends WildLockedFeatureFormatter {

	public getFeatureScenarioBeforeFreeSpinStop ( freeSpinStopScenario: Scenario, message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return this.getWildLockedScenario( message, featureInfo );
	}

	public triggeredFeatureIncomingMessage ( message: IGameState, featureInfo: IFeatureInfo ): Scenario {
		return null;
	}
}



