import { Scenario } from 'corengf/src/states/Scenario';
import { IGameState, IFeatureInfo } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { IPickFeatureDetail } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { SpinResponseFormatter } from 'slotngf/src/server/ng/formatter/http/in/SpinResponseFormatter';

/**
 *  formatting pick feature
 */
export class PickFeatureFormatter extends SpinResponseFormatter {

	/**
	 * formatting run feature api response
	 * @param message
	 */
	public processIncomingMessage ( message: IGameState, pickFeatureInfo?: IFeatureInfo ): Scenario {

		if ( pickFeatureInfo == undefined || pickFeatureInfo.complete ) {
			return null;
		}

		let responseScenario: Scenario = null;

		if ( message.spin_result.features_triggered != undefined ) {
			this.getFormatFeatureTriggeredsScenario( message, responseScenario );
		}

		return responseScenario;
	}

	/**
	 * formatting feature in feature_triggered
	 */
	public triggeredFeatureIncomingMessage ( message: IGameState, pickFeatureInfo?: IFeatureInfo ): Scenario {

		if ( pickFeatureInfo == undefined ) {
			return null;
		}

		let responseScenario: Scenario = null;

		if ( message.spin_result.win_amount > 0 || message.spin_result.scatter_win != undefined ) {
			responseScenario = this.scenarioManager.getScenario( this.scenarioNames.featureWithWinning );
		}

		if ( responseScenario ) {
			responseScenario.addScenario( this.scenarioManager.getScenario( this.scenarioNames.runPickFeature ) );
		} else {
			responseScenario = this.scenarioManager.getScenario( this.scenarioNames.runPickFeature );
		}

		let pickFeatureDetail: IPickFeatureDetail = this.getFeatureDetail( pickFeatureInfo );

		responseScenario.setStateData( this.stateNames.startPickFeature, {
			playerChoices: this.getPickFeatureDetailChoices( pickFeatureDetail )
		} );

		return responseScenario;
	}

	protected getFeatureDetail ( featureInfo: IFeatureInfo ): any {
		return featureInfo.feature_state;
	}

	/**
	 * formatting feature while restore feature
	 */
	public restoreFeatureIncomingMessage ( pickFeatureDetail: IPickFeatureDetail ): Scenario {
		let pickFeatureScenario: Scenario = this.scenarioManager.getScenario( this.scenarioNames.runPickFeature );
		// TODO: pass reel set to view
		pickFeatureScenario.setStateData( this.stateNames.startPickFeature, {
			playerChoices: this.getPickFeatureDetailChoices( pickFeatureDetail )
		} );
		return pickFeatureScenario;
	}

	protected getPickFeatureDetailChoices ( pickFeatureDetail: IPickFeatureDetail ): Array<string> {
		return pickFeatureDetail.choices;
	}
}



