import { SimpleTween } from 'corengf/src/time/SimpleTween';
import { BitmapTextField, IBitmapTextFieldConfig } from 'corengf/src/elements/canvas/text/BitmapTextField';


export class TweenBitmapTextField extends BitmapTextField {

	public static elementType: string = 'TweenBitmapTextField';

	protected labelTween: SimpleTween;

	protected config: ITweenBitmapTextField;


	public startTween ( tweenConfig?: IBitmapTextFieldTweenConfig, callback?: ( object?: any ) => void ): void {
		this.stopTween();
		this.labelTween = null;

		if ( !tweenConfig ) {
			tweenConfig = this.config.tweenConfig;
		}

		// init to starting point
		for ( var property in tweenConfig.tweenStarting ) {
			if ( this.label[ property ] === undefined ) {
				continue;
			}
			this.label[ property ] = tweenConfig.tweenStarting[ property ];
		}

		// setup destination
		this.labelTween = new SimpleTween( this.label )
			.to( tweenConfig.tweenDestination,
				tweenConfig.duration )
			.onComplete( () => {
				this.labelTween = null;
				if ( callback ) {
					callback();
				}
			} )
			.delay( ( tweenConfig.startDelay ? tweenConfig.startDelay : 0 ) )
			.start();
	}

	public stopTween (): void {
		if ( this.labelTween != undefined ) {
			this.labelTween.stop();
		}
	}
}

export interface ITweenBitmapTextField extends IBitmapTextFieldConfig {
	tweenConfig: IBitmapTextFieldTweenConfig;
}

export interface IBitmapTextFieldTweenConfig {
	duration: number;
	startDelay?: number;
	tweenStarting: object;
	tweenDestination: object;
}



