import { ReelStripView } from 'slotngf/src/components/reelV2/ReelStripView';
import { DHBBaseSymbol } from 'game/src/components/reels/symbol/DHBBaseSymbol';
import { IPoint } from 'corengf/src/elements/config/IElementsConfig';

export class DHBReelStripView extends ReelStripView {

	protected symbols: Array<DHBBaseSymbol>;


	public setSymbolsScale ( symbolId: number, size: IPoint ): void {
		this.symbols[ symbolId ].setSymbolScale( size );
	}

	public setAllSymbolsScale ( size: IPoint ): void {
		this.symbols.forEach( ( symbol ) => {
			symbol.setSymbolScale( size );
		} );
	}

}


