import 'pixi-spine';
import { extras, loader, spine, Texture } from 'pixi.js';
import { Container } from 'typescript-ioc/es5';
import { Asset } from 'corengf/src/core/AssetLoader';
import { Helper } from 'corengf/src/utils/Helper';
import { HeartTicker } from 'corengf/src/time/HeartTicker';
import { TsMap } from 'corengf/src/utils/TsMap';

/**
 * This is utility/wrapper class, for easiest using of PIXI and SPINE animations.
 * List of main SPINE functions.
 * animationName.state.addAnimation
 * animationName.state.setAnimation
 * animationName.state.addListeners
 */
export class Animation {
	public static TIME_SCALE: number = 1;
	public static readonly animationsByName: TsMap<string, extras.AnimatedSprite>
		= new TsMap<string, extras.AnimatedSprite>();
	public static readonly animationPool: TsMap<string, Array<extras.AnimatedSprite>> = new TsMap<string, Array<extras.AnimatedSprite>>();

	/**
	 * Creates SPINE animation.
	 * @param name Name of SPINE animation, from assets list.
	 */
	public static createSpineAnimation ( name: string ): spine.Spine {
		let animation: spine.Spine = new spine.Spine( loader.resources[ name ].spineData );
		animation.name = name;
		animation.state.timeScale = Animation.TIME_SCALE;

		//If we turn on turbo mode we change the timeScale. The animation will automatically update his timescale.
		Container.get( HeartTicker ).onTimeScaleChanged.add( () => { animation.state.timeScale = Animation.TIME_SCALE; } );
		return animation;
	}

	/**
	 * Creates PIXI frame-by-frame animation.
	 * @param frames List of frames. For its creation, call generateFrameNames function
	 */
	public static createFrameAnimation ( frames: Array<string> ): extras.AnimatedSprite {
		let textureArray: Array<Texture> = new Array<Texture>();
		for ( let frame of frames ) {
			let texture = Texture.fromFrame( frame );
			textureArray.push( texture );
		}
		return new extras.AnimatedSprite( textureArray );
	}

	/**
	 * Creates a pool of animations you can use later from the name
	 * @param name name of the animation to save in pool
	 * @param numObjectsToCreate number of animations to save in pool, this will be the number of total animation of this playing at a time
	 */
	public static createAnimationPool ( name: string, numObjectsToCreate: number ): void {
		let poolArray: Array<extras.AnimatedSprite> = new Array<extras.AnimatedSprite>();
		for ( let i = 0; i < numObjectsToCreate; i++ ) {
			let animation = Animation.getAnimationByName( name );
			if ( animation ) {
				poolArray.push( animation );
			} else {
				return; //Animation Does not exist will not pool
			}
		}
		this.animationPool.set( name, poolArray );
	}

	/**
	 * Gets an animation from the pool of saved animations. If it does not exist
	 * or all pooled animations are being used it returns a newly created animation
	 * @param name name of animation to get
	 */
	public static getAnimationFromPool ( name: string ): extras.AnimatedSprite {
		let returnAnimation: extras.AnimatedSprite;
		if ( this.animationPool.has( name ) ) {
			this.animationPool.get( name ).forEach( ( animation: extras.AnimatedSprite ) => {
				if ( !animation.parent ) {
					returnAnimation = animation;
					return;
				}
			} );
		}

		//Gets new animation if doesnt exist or all animation are being used
		if ( !returnAnimation ) {
			returnAnimation = this.getAnimationByName( name );
		}

		return returnAnimation;
	}

	/**
	 * Creates list of assets, if they are standalone files
	 * @param path Path to folder
	 * @param prefix "Main" name of file
	 * @param from The number to start sequentially counting from.
	 * @param to The number to count to.
	 * @param suffix The end of the filename. No need extension.
	 * @param zeroPad The number of zeros to pad the min and max values with.
	 */
	public static generateAssetsList ( path: string, prefix: string, from: number, to: number,
		suffix: string = '', zeroPad: number = 0 ): Array<Asset> {

		let result = new Array<Asset>();
		let frames = Animation.generateFrameNames( prefix, from, to, suffix, zeroPad );
		for ( let i = 0; i < frames.length; i++ ) {
			result.push( { assetKey: frames[ i ], assetURL: path + frames[ i ] + Helper.PNG_TYPE } );
		}
		return result;
	}

	/**
	* Really handy function for when you are creating arrays of animation data but it's using frame names and not numbers.
	*
	* @static
	* @param {string} prefix - The start of the filename.
	* @param {number} from - The number to start sequentially counting from.
	* @param {number} to - The number to count to.
	* @param {string} [suffix=''] - The end of the filename.
	* @param {number} [zeroPad=0] - The number of zeros to pad the min and max values with.
	* @return {Array< string >} An array of framenames.
	*/
	public static generateFrameNames ( prefix: string, from: number, to: number,
		suffix: string = '', zeroPad: number = 0 ): Array<string> {
		var result = [];
		var frame = '';
		for ( var i = from; i <= to; i++ ) {
			frame = i.toString();

			while ( frame.toString().length < zeroPad ) {
				frame = '0' + frame;
			}

			frame = prefix + frame + suffix;
			result.push( frame );
		}
		return result;
	}


	public static getAnimationByName ( name: string, keepInMemory: boolean = false, zeroPad: number = 2, )
		: extras.AnimatedSprite {
		if ( this.animationsByName.has( name ) ) {
			return this.animationsByName.get( name );
		} else {
			let textures: Array<Texture> = this.getAnimationFrameTexturesByName( name, zeroPad );
			if ( textures.length > 0 ) {
				let animation: extras.AnimatedSprite = new extras.AnimatedSprite( textures );
				if ( keepInMemory ) {
					this.animationsByName.set( name, animation );
				}
				return animation;
			} else {
				return null;
			}
		}
	}

	private static getAnimationFrameTexturesByName ( prefix: string, zeroPad: number = 0 ): Array<Texture> {
		let textures: Array<Texture> = new Array<Texture>();
		let frame: string = '';
		let indexFrame: number = 0;

		while ( true ) {
			try {
				frame = indexFrame.toString();
				while ( frame.toString().length < zeroPad ) {
					frame = '0' + frame;
				}
				let texture: Texture = Texture.fromFrame( prefix + '_' + frame + '.png' );
				textures.push( texture );
				indexFrame++;
			} catch ( e ) {
				break;
			}
		}
		return textures;
	}
}



