import { Point, PointLike } from 'pixi.js';
import { LocalButtonsController } from 'corengf/src/buttons/LocalButtonsController';
import { Button, IButtonConfig } from 'corengf/src/elements/canvas/button/Button';
import { WebTextField, IWebTextFieldConfig } from 'corengf/src/elements/canvas/text/WebTextField';


export class TextButton extends Button {
	protected textField: WebTextField;

	constructor ( data: ITextButtonConfig, localButtonController?: LocalButtonsController ) {
		super( data, localButtonController );
		if ( data.text != undefined ) {
			let textData = this.getTextData( data );
			this.textField = new WebTextField( textData );
			let inverseScaleX = 1 / this.scale.x;
			let inverseScaleY = 1 / this.scale.y;
			this.textField.scale.set( inverseScaleX, inverseScaleY );
			if ( textData.position != undefined ) {
				this.textField.position.set( textData.position.x, textData.position.y );
			} else {
				this.textField.position.set( 1 * this.scale.x, -1 * this.scale.y );
			}
			this.addChild( this.textField );
		}
		this.setAction( data.action );
	}

	protected getTextData ( buttonConfig: ITextButtonConfig ): IWebTextFieldConfig {
		let data: IWebTextFieldConfig = buttonConfig.text;
		if ( data.autoscale == undefined ) {
			data.autoscale = { x: this.width - 2, y: this.height - 2 };
		}
		return data;
	}

	public updateAttributes ( buttonConfig: ITextButtonConfig ): void {
		super.updateAttributes( buttonConfig );
		if ( buttonConfig.text && this.textField ) {
			this.textField.updateAttributes( buttonConfig.text );
		}
	}

	public setText ( text: string ) {
		this.textField.text = text;
	}

	public setLabelVisible ( visible: boolean ) {
		this.textField.visible = visible;
	}

	set anchor ( anchorPoint: PointLike ) {
		this.activeElement.anchor.set( anchorPoint.x, anchorPoint.y );
		this.textField.anchor = new Point( anchorPoint.x, anchorPoint.y );
		this.updateHitArea( anchorPoint );
	}
}

export interface ITextButtonConfig extends IButtonConfig {
	selected?: boolean;
	text?: IWebTextFieldConfig;
}


