import { IPoint } from 'corengf/src/elements/config/IElementsConfig';

export class Momentum {
	private readonly stopThresholdDefault: number = 0.3;

	private config: MomentumConfig;

	private pointerLastX: number;
	private pointerLastY: number;
	private pointerCurrentX: number;
	private pointerCurrentY: number;

	private boundXmin: number = 0;
	private boundXmax: number = 0;
	private boundYmin: number = 0;
	private boundYmax: number = 0;

	private targetX: number = 0;
	private targetY: number = 0;

	private multiplier: number = 1;

	private friction: number = 1;
	private stopThreshold: number;

	private decVelX: number;
	private decVelY: number;

	private pointerActive: boolean = false;

	private paused: boolean = false;
	private decelerating: boolean = false;

	private onMoving: boolean = false;
	private _ticking: boolean = false;

	private get ticking (): boolean {
		return this._ticking;
	}

	private set ticking ( value: boolean ) {
		this._ticking = value;
	}

	private trackingPoints: Array<PointInfo> = [];

	constructor ( config: MomentumConfig = {} ) {
		this.config = config;

		this.stopThreshold = this.stopThresholdDefault * this.multiplier;
		this.friction = this.config.friction || 0.97;


		if ( config.boundX ) {
			this.boundXmin = config.boundX[ 0 ];
			this.boundXmax = config.boundX[ 1 ];
		}
		if ( config.boundY ) {
			this.boundYmin = config.boundY[ 0 ];
			this.boundYmax = config.boundY[ 1 ];
		}
	}

	public setBoundX ( bound: [ number, number ] ): void {
		this.boundXmin = bound[ 0 ];
		this.boundXmax = bound[ 1 ];
	}

	public setBoundY ( bound: [ number, number ] ): void {
		this.boundYmin = bound[ 0 ];
		this.boundYmax = bound[ 1 ];
	}

	public start ( point: IPoint ): void {
		if ( this.pointerActive || this.paused ) {
			return;
		}
		this.pointerActive = true;
		this.decelerating = false;

		this.pointerLastX = this.pointerCurrentX = point.x;
		this.pointerLastY = this.pointerCurrentY = point.y;
		this.trackingPoints = [];
		this.addTrackingPoint( this.pointerLastX, this.pointerLastY );
	}

	public move ( point: IPoint ): void {
		if ( !this.pointerActive ) {
			return;
		}
		this.onMoving = true;

		this.pointerCurrentX = point.x;
		this.pointerCurrentY = point.y;
		this.addTrackingPoint( this.pointerLastX, this.pointerLastY );
		this.requestTick();
	}

	protected requestTick (): void {
		if ( !this.onMoving ) {
			return;
		}
		if ( !this.ticking ) {
			requestAnimationFrame( this.updateAndRender.bind( this ) );
			//this.updateAndRender();
		}
	}

	public end (): void {
		if ( this.pointerActive ) {
			this.stopTracking();
		}
	}

	protected stopTracking (): void {
		this.pointerActive = false;
		this.onMoving = false;
		this.addTrackingPoint( this.pointerLastX, this.pointerLastY );
		this.startDecelAnim();
	}

	protected startDecelAnim (): void {
		const firstPoint = this.trackingPoints[ 0 ];
		const lastPoint = this.trackingPoints[ this.trackingPoints.length - 1 ];

		const xOffset = lastPoint.x - firstPoint.x;
		const yOffset = lastPoint.y - firstPoint.y;
		const timeOffset = lastPoint.time - firstPoint.time;

		const D = ( timeOffset / 15 ) / this.multiplier;

		this.decVelX = ( xOffset / D ) || 0; // prevent NaN
		this.decVelY = ( yOffset / D ) || 0;

		const diff = this.checkBounds();

		if ( ( Math.abs( this.decVelX ) > 1 || Math.abs( this.decVelY ) > 1 ) || !diff.inBounds ) {
			this.decelerating = true;
			requestAnimationFrame( this.stepDecelAnim.bind( this ) );
		}
	}

	protected stepDecelAnim (): void {
		if ( !this.decelerating ) {
			return;
		}

		this.decVelX *= this.friction;
		this.decVelY *= this.friction;

		this.targetX += this.decVelX;
		this.targetY += this.decVelY;

		const diff = this.checkBounds();

		if ( ( Math.abs( this.decVelX ) > this.stopThreshold || Math.abs( this.decVelY ) > this.stopThreshold ) || !diff.inBounds ) {

			if ( diff.x !== 0 ) {
				if ( diff.x > 0 ) {
					this.targetX = this.boundXmin;
				} else {
					this.targetX = this.boundXmax;
				}
				this.decVelX = 0;
			}
			if ( diff.y !== 0 ) {
				if ( diff.y > 0 ) {
					this.targetY = this.boundYmin;
				} else {
					this.targetY = this.boundYmax;
				}
				this.decVelY = 0;
			}
			this.config.updateCallback( {
				x: this.targetX,
				y: this.targetY,
			} );
			requestAnimationFrame( this.stepDecelAnim.bind( this ) );
		} else {
			this.decelerating = false;
			if ( this.config.onStop ) {
				this.config.onStop();
			}
		}
	}

	protected updateAndRender (): void {
		const pointerChangeX = this.pointerCurrentX - this.pointerLastX;
		const pointerChangeY = this.pointerCurrentY - this.pointerLastY;

		this.targetX += pointerChangeX * this.multiplier;
		this.targetY += pointerChangeY * this.multiplier;

		this.checkBounds( true );

		this.config.updateCallback( {
			x: this.targetX,
			y: this.targetY,
		} );

		this.pointerLastX = this.pointerCurrentX;
		this.pointerLastY = this.pointerCurrentY;
		this.ticking = false;
	}

	private checkBounds ( shouldRestrictToBound: boolean = false ): BoundInfo {
		var xDiff = 0;
		var yDiff = 0;

		if ( this.boundXmin !== undefined && this.targetX < this.boundXmin ) {
			xDiff = this.boundXmin - this.targetX;
		} else if ( this.boundXmax !== undefined && this.targetX > this.boundXmax ) {
			xDiff = this.boundXmax - this.targetX;
		}

		if ( this.boundYmin !== undefined && this.targetY < this.boundYmin ) {
			yDiff = this.boundYmin - this.targetY;
		} else if ( this.boundYmax !== undefined && this.targetY > this.boundYmax ) {
			yDiff = this.boundYmax - this.targetY;
		}

		if ( shouldRestrictToBound ) {
			if ( xDiff !== 0 ) {
				this.targetX = ( xDiff > 0 ) ? this.boundXmin : this.boundXmax;
			}
			if ( yDiff !== 0 ) {
				this.targetY = ( yDiff > 0 ) ? this.boundYmin : this.boundYmax;
			}
		}

		return {
			x: xDiff,
			y: yDiff,
			inBounds: ( xDiff === 0 && yDiff === 0 )
		};
	}

	private addTrackingPoint ( x: number, y: number ): void {
		var time = Date.now();
		while ( this.trackingPoints.length > 0 ) {
			if ( time - this.trackingPoints[ 0 ].time <= 100 ) {
				break;
			}
			this.trackingPoints.shift();
		}

		this.trackingPoints.push( { x, y, time } );
	}

	public resetTargetPostion ( x: number, y: number ): void {
		this.targetX = x;
		this.targetY = y;
	}
}

export interface MomentumConfig {
	updateCallback?: UpdateCallback;
	onStop?: Function;
	multiplier?: number;
	friction?: number;
	boundX?: [ number, number ];
	boundY?: [ number, number ];
}

interface UpdateCallback {
	( point: IPoint ): void;
}

interface PointInfo {
	x: number;
	y: number;
	time: number;
}

interface BoundInfo {
	x: number;
	y: number;
	inBounds: boolean;
}


