import { Inject } from 'typescript-ioc/es5';
import { TsMap } from 'corengf/src/utils/TsMap';
import { Translator } from 'corengf/src/utils/Translator';
import { SoundConfig } from 'corengf/src/core/config/SoundConfig';
import { IExternalComponentConfig } from 'corengf/src/components/externalModule/IExternalComponentConfig';
import { MLUIConfig } from './IMLUIConfig';
import { MLUIButtonId } from './../buttons/MLUIButtonId';
import { IGeneralWebPageLayoutConfig, IContainerConfig, IHeaderConfig, WebElementType } from 'corengf/src/elements/webpage/WebpageElement';
import { ImageSourceType } from 'corengf/src/elements/webpage/image/WebImageElement';
import { IWebTextConfig } from 'corengf/src/elements/webpage/text/WebTextElement';


export interface IGeneralWebPageConfig extends IExternalComponentConfig {
	localisationKeys: Array<string>;
	portraitLayoutConfig: IGeneralWebPageLayoutConfig;
	landscapeLayoutConfig: IGeneralWebPageLayoutConfig;
	buttonIds: TsMap<string, MLUIButtonId>;
	lineConfig?: Array<Array<Array<number>>>;
}

/**
 * common default config in webpage (rule, paytable, history)
 */
export class GeneralWebPageConfig extends MLUIConfig implements IGeneralWebPageConfig {
	public name = '';
	public externalKey = '';
	public domID = 'external-module';
	public moduleName = '';
	public assetNames = [ 'symbols', 'mlui_menu' ];
	public localisationKeys = [ '' ];
	public debug = false;
	public soundKey = SoundConfig;

	@Inject
	protected translator: Translator;

	public buttonIds: TsMap<string, MLUIButtonId> = this.getButtonIds();

	/**
	 * variable for portrait layout
	 */
	public portraitLayoutConfig: IGeneralWebPageLayoutConfig = this.getPortraitLayoutConfig();

	/**
	 * variable for landscape layout config
	 */
	public landscapeLayoutConfig: IGeneralWebPageLayoutConfig = this.getLandscapeLayoutConfig();


	protected getButtonIds (): TsMap<string, MLUIButtonId> {
		return new TsMap( [] );
	}

	/**
	 * layout for portrait screen
	 */
	protected getPortraitLayoutConfig (): IGeneralWebPageLayoutConfig {
		return {
			width: 504,
			height: 896,
			blockStyle: `
				display: flex;
				flex-direction: column;
			`,
			headerConfig: this.getPortraitHeaderConfig(),
			containerConfig: this.getPortraitContainerConfig(),
		};
	}

	/**
	 * portrait header config
	 */
	protected getPortraitHeaderConfig (): IHeaderConfig {
		return {
			blockStyle: `
				padding: 0 56px;
				width: 504px;
			`,
			//header title
			titleConfig: {
				type: WebElementType.WEB_TEXT_ELEMENT,
				blockStyle: `
					font-size:34px;
					white-space: pre;
					font-weight: bold;
					line-height: 145px;
				`,
				languageKey: 'mainTitle',
				maxAmount: 10,
			},
			//header close button
			closeButton: {
				dataHookTrackerConfig: {
					buttonID: this.getButtonIds().get( 'closeButton' )
				},
				soundKey: SoundConfig.buttonClick,
				buttonImage: {
					type: WebElementType.WEB_IMAGE_ELEMENT,
					blockStyle: `
						position: absolute;
						top: 45px;
						right: 55px;
						width: 75px;
						height: 75px;
						border-radius: 50%;
						cursor: pointer;
						user-select: none;
					`,
					imageSource: ImageSourceType.SPRITE,
					imageData: {
						assetName: 'mlui_menu',
						imageName: 'close_Enabled.png',
						ratio: 0.7
					}
				}
			}
		};
	}

	/**
	 * portrait container config
	 */
	protected getPortraitContainerConfig (): IContainerConfig {
		return {
			innerBlockStyle: 'padding: 0 56px 56px;',
			blockStyle: `
				flex-grow: 1;
				flex-shrink: 1;
			`,
		};
	}

	/**
	 * layout for landscape screen
	 */
	protected getLandscapeLayoutConfig (): IGeneralWebPageLayoutConfig {
		return {
			width: 896,
			height: 504,
			blockStyle: `
				display: flex;
				flex-direction: column;
			`,
			headerConfig: this.getLandscapeHeaderConfig(),
			containerConfig: this.getLandscapeContainerConfig(),
		};
	}

	/**
	 * Landscape header config
	 */
	protected getLandscapeHeaderConfig (): IHeaderConfig {

		return {
			blockStyle: `
				width:896px;
				height:105px;
				padding:0 56px;
			`,
			titleConfig: this.getLandscapeHeaderMainTitleConfig(),
			closeButton: {
				dataHookTrackerConfig: {
					buttonID: this.getButtonIds().get( 'closeButton' )
				},
				soundKey: SoundConfig.buttonClick,
				buttonImage: {
					type: WebElementType.WEB_IMAGE_ELEMENT,
					blockStyle: `
						position: absolute;
						top: 45px;
						right: 55px;
						border-radius: 50%;
						cursor: pointer;
						user-select: none;
					`,
					imageSource: ImageSourceType.SPRITE,
					imageData: {
						assetName: 'mlui_menu',
						imageName: 'close_Enabled.png',
						ratio: 0.7
					}
				}
			}
		};
	}

	/**
	 * Landscape header title config
	 */
	protected getLandscapeHeaderMainTitleConfig (): IWebTextConfig {
		return {
			type: WebElementType.WEB_TEXT_ELEMENT,
			blockStyle: `
				font-size:33px;
				white-space: pre;
				font-weight: bold;
				line-height: 140px;
			`,
			languageKey: 'mainTitle',
			maxAmount: 10,
		};
	}

	/**
	 * Landscape container config
	 */
	protected getLandscapeContainerConfig (): IContainerConfig {
		return {
			innerBlockStyle: 'padding: 0 140px',
			blockStyle: `
				flex-grow: 1;
				flex-shrink: 1;
			`,
		};
	}

	/**
	 * set up main title style
	 */
	protected getLandscapeMainTitleStyle (): string {
		return `
			font-size: 33px;
			position: absolute;
			top: -45.5px;
			left:-14px;
			z-index: 11;
			white-space: pre;
			font-weight: bold;
		`;
	}
}




