import { Singleton } from 'typescript-ioc/es5';

@Singleton
export class SlotNGModel {
	private _gameType: string;

	public get gameType (): string {
		return this._gameType;
	}
	public set gameType ( value: string ) {
		this._gameType = value;
	}

	private _playerId: string;

	public set playerId ( playerId: string ) {
		this._playerId = playerId;
	}

	public get playerId (): string {
		return this._playerId;
	}

	private _sessionId: string;

	public set sessionId ( sessionId: string ) {
		this._sessionId = sessionId;
	}

	public get sessionId (): string {
		return this._sessionId;
	}

	private _partnerCode: string;

	public set partnerCode ( partnerCode: string ) {
		this._partnerCode = partnerCode;
	}

	public get partnerCode (): string {
		return this._partnerCode;
	}

	private _stateTag: string;

	public set stateTag ( stateTag: string ) {
		this._stateTag = stateTag;
	}

	public get stateTag (): string {
		return this._stateTag;
	}
}



