import { Inject } from 'typescript-ioc/es5';
import { SlotNPSModel } from 'slotngf/src/server/ng/sdk/bng/model/SlotNPSModel';
import { SpinRequestMsgFormatter } from 'slotngf/src/server/ng/formatter/http/out/SpinRequestMsgFormatter';

/**
 * Format spin request
 */
export class BNGSpinRequestMsgFormatter extends SpinRequestMsgFormatter {

	@Inject
	protected slotNPSModel: SlotNPSModel;

	/**
	 * sends button press emulation package
	 * @param data - ignored
	 * @returns {any} message object
	 */
	public codeMessage ( data?: any ): INPSSpinRequestMsgConfig {
		const msg = super.codeMessage( data ) as INPSSpinRequestMsgConfig;
		msg.custom_props = this.slotNPSModel.makeBetInfo;
		console.log( '[BNG_Spin REQUEST data]', msg );
		return msg;
	}
}

export interface INPSSpinRequestMsgConfig {
	player_id: string;
	session_token: string;
	partner_code: string;
	game_code: string;
	action: string;
	state_tag: string;
	bet_denom_index: number;
	name: string;
	line?: number;
	custom_props: any;
}



