import { States } from 'corengf/src/states/States';
import { Scenario } from 'corengf/src/states/Scenario';
import { CoreState } from 'corengf/src/states/CoreState';
import { Singleton, Inject } from 'typescript-ioc/es5';
import { ScenarioNames } from 'corengf/src/states/ScenarioNames';
import { TsMap } from 'corengf/src/utils/TsMap';


@Singleton
export class ScenarioManager {
	/**
	 * set of scenarios for different server responses
	 */
	protected scenarioFlows: TsMap<string, Scenario> = new TsMap();

	@Inject
	protected stateName: States;

	@Inject
	protected scenarioName: ScenarioNames;

	constructor () {
		this.createScenarios();
	}

	protected createScenarios (): void {
		this.scenarioFlows.set(
			this.scenarioName.connect, this.getScenarioByStateNames( this.scenarioName.connect,
				[
					this.stateName.connect
				]
			)
		);

		this.scenarioFlows.set(
			this.scenarioName.gameInit, this.getScenarioByStateNames( this.scenarioName.gameInit,
				[
					this.stateName.preInit,
					this.stateName.watingEnterGame,
					this.stateName.init,
					this.stateName.gameStartTutorial,
					this.stateName.tutorialOutro,
					this.stateName.baseGameIdle
				]
			)
		);
	}

	/**
	 * returns scenario by name
	 * @param name name of scenario in config file
	 */
	public getScenario ( name: string, clone: boolean = true ) {
		return clone ? this.scenarioFlows.get( name ).clone() : this.scenarioFlows.get( name );
	}

	public getScenarioByStateNames ( scenarioName: string, stateNames: Array<string> ): Scenario {
		var states: Array<CoreState> = new Array<CoreState>();
		stateNames.forEach( ( stateName: string ) => {
			states.push( new CoreState( stateName ) );
		} );
		return new Scenario( scenarioName, states );
	}
}


