import { Inject, Container } from 'typescript-ioc/es5';
import { Helper } from './../../utils/Helper';
import { States } from './../../states/States';
import { CoreState } from './../../states/CoreState';
import { GameConfig } from './../../core/GameConfig';
import { ComponentManager } from './../ComponentManager';
import { LocalStateMachine } from './../../core/LocalStateMachine';
import { CompController, Component } from './../../mvc/CompController';


export class DashboardController extends CompController {

	protected STATE_MACHINE_NAME: string = 'dashboard_state_machine';
	protected dashboardComponentManager: ComponentManager;
	protected dashboardLocalStateMachine: LocalStateMachine;

	@Inject
	protected gameConfig: GameConfig;

	@Inject
	protected stateNames: States;

	/**
	 * override for init controller.
	 * @param stateData
	 */
	public init ( stateData: any ): void {
		this.dashboardComponentManager = new ComponentManager();
		this.createLocalStateMachine( this.dashboardComponentManager );
		this.addComponents();
		this.onStateInit( stateData );
		this.finishStateAction();
	}

	/**
	 * Create local state machine.
	 * @param componentManager
	 */
	protected createLocalStateMachine ( componentManager: ComponentManager ): void {
		if ( this.dashboardLocalStateMachine == undefined ) {
			this.dashboardComponentManager = componentManager;
			this.dashboardLocalStateMachine = Container.get( LocalStateMachine );
			this.dashboardLocalStateMachine.initialize( componentManager );
			this.dashboardLocalStateMachine.stateMachineName = this.STATE_MACHINE_NAME;
			this.dashboardLocalStateMachine.stateChangedSignal.add( this.onLocalStateChanged, this );
		} else {
			throw 'dashboard stateMachine is already created';
		}
	}

	/**
	 * Add components in component list into dashboard component manager.
	 */
	protected addComponents (): void {
		let components: Array<Component> = new Array<Component>();
		this.createComponentList( components );
		components.forEach( ( component: Component ) => {
			let comp = Container.get( component.class ) as CompController;
			comp.componentID = component.id;
			this.dashboardComponentManager.addComponent( comp );
		} );
	}

	/**
	 * Setup component list that need listen by local state machine
	 * @param components
	 */
	protected createComponentList ( components: Array<Component> ): void {
		//Example:
		// components.push( { id : 'autoPlay', class: SlotAutoPlayController } );
	}

	/**
	 * set dashboard local state machine to init state
	 * @param stateData
	 */
	protected onStateInit ( stateData: any ): void {
		this.dashboardLocalStateMachine.setState( new CoreState( this.stateNames.init, Helper.mergeTwoObjects( {}, stateData ) ) );
	}

	protected onLocalStateChanged ( stateName: string ): void {
		//
	}
}



