import * as MiniSignal from 'mini-signals';
import { Button } from 'corengf/src/elements/canvas/button/Button';
import { Window } from 'corengf/src/elements/canvas/window/Window';
import { WebTextField } from 'corengf/src/elements/canvas/text/WebTextField';
import { Sprite } from 'corengf/src/elements/canvas/sprite/Sprite';
import { ScrollContainer } from 'corengf/src/elements/canvas/scroll/ScrollContainer';
import { ToggleMultiButton } from 'corengf/src/elements/canvas/toggle/ToggleMultiButton';
import { SliderData, Slider, SliderStepData } from 'corengf/src/elements/canvas/slider/Slider';
import { CoreUIElementFactory } from 'corengf/src/factory/CoreUIElementFactory';
import { SlotActions } from 'slotngf/src/utils/SlotActions';
import { MLUIFullScreenPanel } from 'mlui/src/core/MLUIFullScreenPanel';
import { IMLUISpinSettingsConfig } from 'mlui/src/components/spinSettings/MLUISpinSettingsConfig';


export class MLUISpinSettingsPanel extends MLUIFullScreenPanel {

	protected config: IMLUISpinSettingsConfig = null;

	protected scrollContainer: ScrollContainer;

	protected winLimit: Slider;
	protected singleWinLimit: Slider;
	protected lossLimit: Slider;

	protected confirmSprite: Sprite;
	protected closeButton: Button;
	protected startButton: Button;
	protected stopButton: Button;

	protected autoplaySelector: ToggleMultiButton;

	public onChangeAutoplayMode: MiniSignal = new MiniSignal();
	public onHide: MiniSignal = new MiniSignal();

	/**
	 * AutoPlayData is sent to the gameController when spin is pressed for the action_autoSpin
	 */
	protected autoPlayData: AutoPlayData = {
		lossLimit: null,
		winLimit: null,
		singleWinLimit: null
	};

	protected createElements ( config: IMLUISpinSettingsConfig ): void {
		this.config = config;
		super.createElements( config );

		this.createMainTitle();
		this.createScrollContainer();
		this.createAutoplayContent();

		this.createLimitSliderContent();

		this.createFooter();
		this.createButtonsSprites();

		this.updateOrientationElementOrder();

		this.hidePopup();
	}

	protected getDefaultClickData (): SpinSettingsDataHookData {
		return {
			lossLimit: {
				id: 'SLIDER_LOSE',
				value: -1
			},
			singleWinLimit: {
				id: 'SLIDER_SINGLE_WIN',
				value: -1
			},
			winLimit: {
				id: 'SLIDER_WIN',
				value: -1
			},
			spins: null
		};
	}

	protected updateOrientationElementOrder (): void {
		const cloneElements = this.orientationCoreElements.slice( 0 );
		let scrollContainerIndex: number = null; // move scrollContainer to last position.
		this.orientationCoreElements.forEach( ( child, index ) => {
			if ( child.name === this.config.scrollContainer.name ) {
				scrollContainerIndex = index;
			}
		} );
		const tmp = cloneElements[ cloneElements.length - 1 ];
		cloneElements[ cloneElements.length - 1 ] = cloneElements[ scrollContainerIndex ];
		cloneElements[ scrollContainerIndex ] = tmp;
		this.orientationCoreElements = cloneElements;
	}

	protected createBackground (): void {
		const background = new Window( this.config.background );
		background.interactive = true;
		this.addChild( background );
	}

	protected createMainTitle (): void {
		const title = new WebTextField( this.config.mainTitle );
		this.addChild( title );
	}

	protected createFooter (): void {
		this.closeButton = CoreUIElementFactory.createInstance( this.config.closeButton, this.localButtonsController );
		this.closeButton.clickData = this.getDefaultClickData();

		this.startButton = CoreUIElementFactory.createInstance( this.config.confirmButton, this.localButtonsController );
		this.startButton.clickData = this.getDefaultClickData();

		this.stopButton = CoreUIElementFactory.createInstance( this.config.stopButton, this.localButtonsController );
		this.closedContainer.addChild( this.stopButton );
		this.orientationCoreElements.push( this.stopButton );

		this.addChild( this.closeButton );
		this.addChild( this.startButton );
	}

	protected createButtonsSprites (): void {
		this.confirmSprite = CoreUIElementFactory.createInstance( this.config.confirmSprite ); // new CoreSprite( this.config.confirmSprite );
		this.addChild( this.confirmSprite );
	}

	protected createAutoplayContent (): void {

		this.autoplaySelector = new ToggleMultiButton( this.config.autoplayConfig );
		this.autoplaySelector.selectedSignal.add( this.onSelectAutoplayButton.bind( this ) );

		this.scrollContainer.addContent( this.autoplaySelector );

		this.orientationCoreElements.push( this.autoplaySelector );
	}

	protected onSelectAutoplayButton ( value: string ): void {
		this.onChangeAutoplayMode.dispatch( value );
		this.autoPlayData.spins = value;
		this.startButton.clickData.spins = value;
		this.closeButton.clickData = this.startButton.clickData;

		this.startButton.setEnable( true );
	}

	protected createLimitSliderContent (): void {
		this.lossLimit = new Slider( this.config.lostLimitConfig );
		this.lossLimit.onValueChanged.add( this.onLossLimitChange.bind( this ) );
		this.scrollContainer.addContent( this.lossLimit );

		this.winLimit = new Slider( this.config.winLimitConfig );
		this.winLimit.onValueChanged.add( this.onWinLimitChange.bind( this ) );
		this.scrollContainer.addContent( this.winLimit );

		this.singleWinLimit = new Slider( this.config.singleWinLimitConfig );
		this.singleWinLimit.onValueChanged.add( this.onSingleWinLimitChange.bind( this ) );
		this.scrollContainer.addContent( this.singleWinLimit );

		this.orientationCoreElements.push( this.lossLimit );
		this.orientationCoreElements.push( this.winLimit );
		this.orientationCoreElements.push( this.singleWinLimit );
	}

	public totalBetUpdate ( totalBet: number ): void {
		this.updateSliderStepItems( totalBet, this.lossLimit, this.config.lostLimitConfig.sliderStepData );
		this.updateSliderStepItems( totalBet, this.winLimit, this.config.winLimitConfig.sliderStepData );
		this.updateSliderStepItems( totalBet, this.singleWinLimit, this.config.singleWinLimitConfig.sliderStepData );
	}

	public updateSliderStepItems ( totalBet: number, slider: Slider, sliderStepData: SliderStepData ): void {
		let stepItems: Array<number> = new Array<number>();
		const stepItemConfig: Array<number> = sliderStepData.items;

		for ( let i = 0; i < stepItemConfig.length; i++ ) {
			if ( stepItemConfig[ i ] > 0 ) {
				stepItems.push( totalBet * stepItemConfig[ i ] );
			} else {
				stepItems.push( stepItemConfig[ i ] );
			}
		}
		slider.items = stepItems;
		slider.triggerUpdate();
	}

	protected onLossLimitChange ( data: SliderData ): void {
		this.autoPlayData.lossLimit = data;
		this.startButton.clickData.lossLimit = data;
	}

	protected onWinLimitChange ( data: SliderData ): void {
		this.autoPlayData.winLimit = data;
		this.startButton.clickData.winLimit = data;
		this.closeButton.clickData = this.startButton.clickData;
	}

	protected onSingleWinLimitChange ( data: SliderData ): void {
		this.autoPlayData.singleWinLimit = data;
		this.startButton.clickData.singleWinLimit = data;
		this.closeButton.clickData = this.startButton.clickData;
	}

	public setStopActionToFreeSpinStop (): void {
		this.stopButton.setAction( SlotActions.FREE_SPIN_STOP );
	}

	public setStopActionToStopAutoSpin (): void {
		this.stopButton.setAction( SlotActions.STOP_AUTO_PLAY );
	}

	protected reset (): void {
		this.startButton.clickData.spins = null;
		this.startButton.setEnable( false );
		this.closeButton.clickData = this.startButton.clickData;
		this.autoplaySelector.enableAllButtons();
	}

	protected createScrollContainer (): void {
		const scrollContainer = new ScrollContainer( this.config.scrollContainer );
		this.addChild( scrollContainer );
		this.scrollContainer = scrollContainer;
	}

	public showPopup (): void {
		super.showPopup();
		this.reset();
	}

	public hidePopup (): void {
		this.autoplaySelector.disableAllButtons();
		super.hidePopup();
	}
}

export interface AutoPlayData {
	lossLimit?: SliderData | number;
	winLimit?: SliderData | number;
	singleWinLimit?: SliderData | number;
	spins?: string;
}

export interface SpinSettingsDataHookData {
	lossLimit: SliderData;
	winLimit: SliderData;
	singleWinLimit: SliderData;
	spins: string;
}


