import { Inject } from 'typescript-ioc/es5';
import { Helper } from 'corengf/src/utils/Helper';
import { Orientation } from 'corengf/src/display/Viewport';
import { DateTime, DateTimeRange } from 'corengf/src/utils/DateTime';
import { GlobalDispatcher } from 'corengf/src/events/GlobalDispatcher';
import { SlotEvent } from 'slotngf/src/core/SlotEvent';
import { IInitData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { MLUIExternalModuleController } from 'mlui/src/core/MLUIExternalModuleController';
import { MLUIHistoryPanel } from './MLUIHistoryPanel';
import { MLUIHistoryConfig } from './MLUIHistoryConfig';
import { IHistoryConfig, DATE_INTERVAL, IHistorySummaryQuery, IHistoryDetailQuery } from './MLUIHistoryInterfaces';


export class MLUIHistoryController extends MLUIExternalModuleController {

	protected moduleConfig: IHistoryConfig;

	protected isOpened = false;

	constructor ( @Inject view: MLUIHistoryPanel ) {
		super( view );

		this.config = this.gameConfig.getConfig<MLUIHistoryConfig>( MLUIHistoryConfig );
		this.productLine = this.config.productLine;

		GlobalDispatcher.addListener( this, SlotEvent.RECV_HISTORY_SUMMARY, this.updateHistorySummary.bind( this ) );
		GlobalDispatcher.addListener( this, SlotEvent.RECV_HISTORY_DETAIL, this.updateHistoryDetail.bind( this ) );
	}

	public init ( stateData: IInitData ): void {
		const loader = PIXI.loader;
		this.moduleConfig = {
			assets: this.convertAssetNames( loader, this.config.assetNames ),
			handleClose: this.onClose.bind( this ),
			handleOpen: this.onOpen.bind( this ),
			language: this.getLocalisationMap( loader ),
			sendHistoryDetailQuery: this.sendHistoryDetailQuery.bind( this ),
			sendHistorySummaryQuery: this.sendHistorySummaryQuery.bind( this ),
			soundKey: this.config.soundKey,
			dataHook: this.sendDataHook.bind( this ),
			playSound: this.playSound.bind( this ),
			locale: this.translator.language,
			engagements: stateData.engagements,
			style: ( this.config as MLUIHistoryConfig ) as any
		};
		super.init();
		this.view.onOrientationChangeSignal.add( this.onOrientationChanged, this );

		GlobalDispatcher.addListener( this, SlotEvent.RECV_HISTORY_SUMMARY, this.updateHistorySummary.bind( this ) );
		GlobalDispatcher.addListener( this, SlotEvent.RECV_HISTORY_DETAIL, this.updateHistoryDetail.bind( this ) );
	}

	protected onOrientationChanged ( orientation: Orientation ): void {
		if ( this.externalModule.GenesisHistory ) {
			this.externalModule.GenesisHistory.update( orientation );
		}
	}

	protected onClose ( data?: any ): void {
		this.isOpened = false;
		GlobalDispatcher.dispatchIntoAllStates( SlotEvent.CLOSE_HISTORY, data );
	}

	protected onOpen (): void {
		this.isOpened = true;
	}

	/**
	 * send data hook
	 * according to dataHookType send data hook
	 */
	protected sendDataHook ( buttonId: string, action: string = Helper.EMPTY, actionData: any = {}, dataHookType: string = Helper.EMPTY ): void {
		super.sendDataHook( buttonId, action, actionData );
	}

	protected sendHistorySummaryQuery ( query: IHistorySummaryQuery ): void {

		let dateTimeRange: DateTimeRange;

		if ( query.dayAmount == 1 ) {
			dateTimeRange = DateTime.UTCToday( true );
		} else {
			dateTimeRange = DateTime.UTCPastNDays( query.dayAmount, true );
		}

		GlobalDispatcher.dispatchIntoAllStates( query.type, {
			queryType: DATE_INTERVAL.BETWEEN,
			startTime: dateTimeRange.startTime,
			endTime: dateTimeRange.endTime,
			pageNum: query.pageNum,
			pageSize: query.pageSize
		} );
	}

	protected sendHistoryDetailQuery ( query: IHistoryDetailQuery ): void {
		GlobalDispatcher.dispatchIntoAllStates( query.type, {
			roundID: query.roundID
		} );
	}

	//call back
	protected updateHistorySummary ( response: any ): void {
		if ( !this.isOpened ) {
			return;
		}
		this.externalModule.GenesisHistory.updateHistorySummary( response.data );
	}

	protected updateHistoryDetail ( response: any ): void {
		if ( !this.isOpened ) {
			return;
		}
		this.externalModule.GenesisHistory.updateHistoryDetail( response.data );
	}
}


