import * as MiniSignal from 'mini-signals';
import { Container } from 'pixi.js';
import { CompView } from 'corengf/src/mvc/CompView';
import { Asset } from 'corengf/src/core/AssetLoader';
import { Orientation } from 'corengf/src/display/Viewport';
import { SimpleTween } from 'corengf/src/time/SimpleTween';
import { Button } from 'corengf/src/elements/canvas/button/Button';
import { Sprite } from 'corengf/src/elements/canvas/sprite/Sprite';
import { ILoadingBar } from 'corengf/src/elements/canvas/progressiveBar/ILoadingBar';
import { ISmartPreloaderConfig } from 'corengf/src/components/preloader/SmartPreloadConfig';
import { LoadingBar, ILoadingBarConfig } from 'corengf/src/elements/canvas/progressiveBar/LoadingBar';
import { ISpineLoadingBarConfig, SpineLoadingBar } from 'corengf/src/elements/canvas/progressiveBar/SpineLoadingBar';


export class SmartPreloaderView extends CompView {

	protected config: ISmartPreloaderConfig;

	protected preloadBar: ILoadingBar;
	protected startButton: Button;
	protected preloadBackground: Sprite;
	protected buttonTweening: SimpleTween;

	public onClickEnterGameSignal = new MiniSignal();

	protected buttonPositionY: number;

	protected buttonStartYPosition: number;

	public getAssetsList (): Array<Asset> {
		return [
			{
				assetKey: 'preloaderBackground',
				assetURL: 'assets/preloader/{locale}/preloaderBackground.json'
			},
			{
				assetKey: 'preloaderUI',
				assetURL: 'assets/preloader/{locale}/preloaderUI.json'
			}
		];
	}

	public initialize ( config?: ISmartPreloaderConfig ): void {
		this.config = config;
		this.mainContainer = new Container();
		this.gameContainerManager.addToPreloader( this.mainContainer );
		this.setMainContainerName( config );
		this.createElements( config );
		this.registerEvents();
		this.initialized = true;
	}

	/**
	 * Show preloader and preloader bar on stage
	 */
	public showPreloader (): void {
		this.preloadBackground = new Sprite( this.config.background );
		this.addChild( this.preloadBackground );

		if ( this.isSpineLoadingBarConfig( this.config.loadingBar ) ) {
			this.preloadBar = new SpineLoadingBar( this.config.loadingBar );
		} else {
			this.preloadBar = new LoadingBar( this.config.loadingBar );
		}
		this.addChild( this.preloadBar );

		this.startButton = new Button( this.config.preloaderButton );
		this.startButton.setEnable( false );
		if ( this.config.preloaderButton.rectangleHitArea ) {
			this.startButton.setRectangleHitArea( this.config.preloaderButton.rectangleHitArea.width,
				this.config.preloaderButton.rectangleHitArea.height );
		}

		this.addChild( this.startButton );
		this.layoutElements();
	}

	public portraitLayout (): void {
		this.buttonPositionY = this.config.coreElementOrientationConfig.get( Orientation.PORTRAIT ).get( this.config.preloaderButton.name ).position.y;
		this.buttonStartYPosition = this.config.buttonStartYPositionPortrait;
		super.portraitLayout();
	}

	public landscapeLayout (): void {
		this.buttonPositionY = this.config.coreElementOrientationConfig.get( Orientation.LANDSCAPE ).get( this.config.preloaderButton.name ).position.y;
		this.buttonStartYPosition = this.config.buttonStartYPositionLandscape;
		super.landscapeLayout();
	}

	protected layoutElements (): void {
		if ( this.preloadBackground == undefined ) {
			return;
		}
		super.layoutElements();
	}

	public setProgress ( progress: number ): void {
		if ( this.preloadBar != undefined ) {
			this.preloadBar.update( progress * 0.99 );
		}
	}

	public setLoaderBarComplete (): void {
		if ( this.preloadBar != undefined ) {
			let tweenData = { percent: 0.99 };
			let tweenTo = { percent: 1 };
			new SimpleTween( tweenData )
				.to( tweenTo, 5000 )
				.onUpdate( () => {
					this.preloadBar.update( tweenData.percent );
				} )
				.start();
		}
	}

	public enablePreloader ( isEnable: boolean ): void {
		this.gameContainerManager.enablePreloaderInteraction( isEnable );
	}

	public finishLoading (): void {
		this.preloadBar.finishedLoadingSignal.add( this.showStartButton, this );
		this.preloadBar.playCompleteAnimation();
	}

	public showStartButton (): void {
		this.startButton.position.y = this.buttonStartYPosition;
		let buttonTween = new SimpleTween( this.startButton )
			.to( { alpha: 1, scaleY: 1, y: this.buttonPositionY }, 150 )
			.onComplete( () => {
				this.startButton.setEnable( true );
				this.startButton.position.y = this.buttonPositionY;

				this.mainContainer.on( 'pointertap', this.onClickEnterGame, this );
				this.mainContainer.interactive = true;

				this.buttonTweening = new SimpleTween( this.startButton )
					.to( { scaleX: 1.2, scaleY: 1.2 }, 1000 )
					.yoyo( true )
					.repeat( Infinity )
					.start();
			} );

		let loadingBarTween = new SimpleTween( this.preloadBar )
			.to( { alpha: 0 }, 250 )
			.onComplete( () => {
				buttonTween.start();
			} );

		loadingBarTween.start();
	}

	public onClickEnterGame (): void {
		this.startButton.triggerClick();
		this.startButton.destroy();
		this.mainContainer.interactive = false;
		this.onClickEnterGameSignal.dispatch();
	}

	public destroy (): void {
		if ( this.buttonTweening != undefined ) {
			this.buttonTweening.stop();
			this.buttonTweening = undefined;
		}

		new SimpleTween( this.mainContainer )
			.to( { alpha: 0 }, 500 )
			.onComplete( () => {
				this.gameContainerManager.removeFromPreloader( this.mainContainer );
				this.mainContainer.removeChild( this.startButton );

				if ( this.startButton != undefined ) {
					this.startButton.destroy();
					this.startButton = undefined;
				}

				super.destroy();
			} )
			.start();
	}

	protected isSpineLoadingBarConfig ( loadingBarConfig: ILoadingBarConfig | ISpineLoadingBarConfig ): loadingBarConfig is ISpineLoadingBarConfig {
		return ( <ISpineLoadingBarConfig> loadingBarConfig ).animationConfig !== undefined;
	}
}



