import * as MiniSignal from 'mini-signals';
import { Inject, Container } from 'typescript-ioc/es5';
import { SmartPreloaderView } from './SmartPreloaderView';
import { Viewport } from 'corengf/src/display/Viewport';
import { GameConfig } from 'corengf/src/core/GameConfig';
import { Translator } from 'corengf/src/utils/Translator';
import { SoundManager } from 'corengf/src/audio/SoundManager';
import { MainConfig } from 'corengf/src/core/config/MainConfig';
import { IMainConfig } from 'corengf/src/core/config/IMainConfig';
import { Asset, AssetLoader } from 'corengf/src/core/AssetLoader';
import { SeleniumPortal } from 'corengf/src/selenium/SeleniumPortal';
import { ResourceConfig } from 'corengf/src/core/config/ResourceConfig';
import { SmartPreloaderConfig, ISmartPreloaderConfig } from './SmartPreloadConfig';

/**
 * Load all assets and show loading screen with progress bar during loading.
 */
export class SmartPreloaderController {
	/**
	 * Sound Manager instance to load and play audio
	 */
	@Inject
	protected soundManager: SoundManager;

	@Inject
	protected gameConfig: GameConfig;

	@Inject
	protected translator: Translator;

	@Inject
	protected viewport: Viewport;

	@Inject
	protected seleniumPortal: SeleniumPortal;

	@Inject
	protected assetLoader: AssetLoader;

	/**
	 * Array of files for loading
	 */
	protected filesToLoad: Array<Asset>;

	/**
	 * Invokes when all assets have been loaded
	 */
	public loadingFinishedSignal: MiniSignal;

	/**
	 * Amount of loaded assets
	 */
	protected loadCount: number = 0;

	/**
	 * Total Amount of assets that we should load
	 */
	protected totalResourcesNumber: number = 0;

	/**
	 * Progress of the loader
	 */
	protected progress: number = 0;

	protected listenedSignals: { [ key: string ]: MiniSignal.MiniSignalBinding } = {};

	protected preloaderView: SmartPreloaderView;

	public gameStartSignal: MiniSignal;

	public isInPreloader = true;

	constructor () {
		this.gameStartSignal = new MiniSignal();
		this.preloaderView = Container.get( SmartPreloaderView );
		this.loadingFinishedSignal = new MiniSignal();
		this.preloaderView.initialize( this.getConfig() );

		this.assetLoader.onGroupProgress.add( this.onProgress.bind( this ) );
		this.assetLoader.onGroupCompleted.add( this.onResourceReady.bind( this ) );
	}

	protected getConfig (): ISmartPreloaderConfig {
		return this.gameConfig.getConfig<ISmartPreloaderConfig>( SmartPreloaderConfig );
	}

	/**
	 * Show preloader and preloader bar on stage
	 */
	protected showPreloader ( idx: number ): void {
		if ( idx === ResourceConfig.PRELOAD ) {
			this.preloaderView.showPreloader();
		}
	}

	/**
	 * Showing progress of loading resources using progress bar
	 * Firing event once all necessary files are loaded and then start loading
	 * optional sounds
	 */
	protected onProgress ( groupIdx: number, ratio: number ): void {
		if ( groupIdx !== ResourceConfig.MAIN_GAME ) {
			return;
		}
		// prevent progress bar from jumping backward
		this.progress = Math.max( this.progress, ratio );
		this.preloaderView.setProgress( this.progress );
	}

	public setLoaderBarComplete (): void {
		this.preloaderView.setLoaderBarComplete();
		this.seleniumPortal.closePreloaderSignal.once( this.preloaderView.onClickEnterGame, this.preloaderView );
	}

	protected showGame (): void {
		if ( !this.isInPreloader ) {
			return;
		}
		this.isInPreloader = false;

		this.gameStartSignal.dispatch();
	}


	public setAssetsForLoading ( pFilesToLoad: Array<Asset> ): void {
		this.filesToLoad = pFilesToLoad;
	}

	/**
	 * Loading vital assets for preloader - preloader background and preloader bar.
	 */
	public run (): void {
		const preloadAssets = this.preloaderView.getAssetsList().map( ( asset ) => {
			const rtn: Asset = {
				group: ResourceConfig.PRELOAD,
				assetKey: asset.assetKey,
				assetURL: asset.assetURL
			};
			return rtn;
		} );
		const allFiles = preloadAssets.concat( this.translator.getLocalisationAssets() ).concat( this.filesToLoad );

		this.assetLoader.add( allFiles );
		this.assetLoader.start( this.showPreloader.bind( this ) );
	}

	/**
	 * Loading vital configs before all the rest
	 */
	protected loadConfigFiles (): void {
		let mainConfig: IMainConfig = this.gameConfig.getConfig<IMainConfig>( MainConfig );

		if ( mainConfig.configFiles != undefined ) {
			this.assetLoader.add( mainConfig.configFiles );
			this.assetLoader.start( () => {
				this.gameConfig.setConfigStorageObject( this.assetLoader.resources );
				this.loadAssets();
			} );
		} else {
			this.loadAssets();
		}
	}

	/**
	 * Load all assets that we need for game
	 */
	protected loadAssets (): void {
		this.assetLoader.reset( true );
		// Load assets for localisation
		this.assetLoader.add( this.translator.getLocalisationAssets() );

		// Load essential assets
		this.assetLoader.add( this.filesToLoad );

		this.assetLoader.start();
	}

	protected onResourceReady ( groupIdx: number ): void {
		if ( groupIdx !== ResourceConfig.MAIN_GAME ) {
			return;
		}
		this.loadingFinishedSignal.dispatch( { callback: this.preloaderView.finishLoading, thisArg: this.preloaderView } );
		this.setLoaderBarComplete();
		this.preloaderView.onClickEnterGameSignal.add( this.showGame, this );
	}

	public destroy (): void {
		this.gameStartSignal.detachAll();
		this.gameStartSignal = null;
		this.loadingFinishedSignal.detachAll();
		this.loadingFinishedSignal = null;
		this.assetLoader.detachAll();
		this.preloaderView.destroy();
	}
}



