import { Scenario } from 'corengf/src/states/Scenario';
import { IWinningData } from 'slotngf/src/states/SlotStatesDataInterfaces';
import { SlotNGHttpFormatter } from 'slotngf/src/server/ng/formatter/SlotNGHttpFormatter';
import { WinLine, WildMultiplier, WinningCombination } from 'slotngf/src/components/lines/WinLine';
import { IWildMultiplier, FeatureTypes } from 'slotngf/src/server/ng/data/SlotNGFeatureDataDefinitions';
import { IGameState, ISpinResultReelWins, ISpinResult, ISymbolsData, IFeatureInfo, IScatterWin } from 'slotngf/src/server/ng/data/SlotNGDataDefinitions';
import { AbstractFeatureScenarioFormatter } from 'slotngf/src/server/ng/formatter/http/in/features/AbstractFeatureScenarioFormatter';

/**
 * general functions for formatting take turn response
 */
export class SpinResponseFormatter extends SlotNGHttpFormatter {

	/**
	 * get reel symbols in reel set
	 * @param reels
	 */
	protected getReelSymbols ( reels: Array<ISymbolsData> ): Array<Array<string>> {
		let reelSymbols = new Array<Array<string>>();
		for ( let i: number = 0; i < reels.length; i++ ) {
			reelSymbols[ i ] = new Array<string>();
			for ( let j: number = 0; j < reels[ i ].symbols.length; j++ ) {
				reelSymbols[ i ].push( reels[ i ].symbols[ j ].symbol );
			}
		}
		return reelSymbols;
	}

	/**
	 * set winning data for states
	 * @param responseScenario
	 * @param message
	 */
	protected setWinningData ( responseScenario: Scenario, message: IGameState ): Scenario {
		let winData: IWinningData = this.getShowWinningData( message.balance, message.spin_result, message.features, message.bet_value );

		const windataStates = [
			this.stateNames.allReelStopped,
			this.stateNames.showWinningLoop,
			this.stateNames.allLineWin,
			this.stateNames.showWinning,
			this.stateNames.prefeatureWinning,
			this.stateNames.freeSpinsAllLineWin,
			this.stateNames.freeSpinsShowWinning,
			this.stateNames.freeRespinsAllLineWin,
			this.stateNames.freeRespinsShowWinning
		];

		windataStates.forEach( ( stateName ) => {
			if ( responseScenario.hasStateName( stateName ) ) {
				responseScenario.setStateData( stateName, winData );
			}
		} );

		return responseScenario;
	}

	/**
	 * get actual win data in IWinningData format
	 * @param current_balance
	 * @param spinResult
	 */
	protected getShowWinningData ( current_balance: number, spinResult: ISpinResult, featureInfo: Array<IFeatureInfo>, betValue: number ): IWinningData {

		let winLines: Array<WinLine> = new Array<WinLine>();
		let featureState: IFeatureInfo = null;
		const winTable = this.createWinTable( spinResult );

		if ( featureInfo && featureInfo.length > 0 ) {
			featureState = this.getFeatureState( featureInfo );
		}

		if ( spinResult.reel_wins != null && spinResult.reel_wins.length > 0 ) {
			spinResult.reel_wins.forEach( ( winElement: ISpinResultReelWins ) => {
				winLines.push( this.getWinline( winElement, winTable ) );
			} );
		}

		if ( spinResult.scatter_win != null ) { // spinResult.scatter_win is an Object
			winLines.push( this.getBaseWinLine( spinResult.scatter_win, winTable ) );
		}

		return {
			balance: current_balance,
			totalBet: betValue,
			totalWin: spinResult.win_amount,
			winLines: winLines,
			featureState: featureState,
			winTable
		};
	}

	/**
	 * Create an empty winTable accroding to spin result
	 *
	 * @protected
	 * @param {ISpinResult} spinResult
	 * @returns {Array<Array<boolean>>}
	 * @memberof SpinResponseFormatter
	 */
	protected createWinTable ( spinResult: ISpinResult ): Array<Array<boolean>> {
		const wintable = [];
		spinResult.reels.forEach( ( reel ) => {
			const reelData = [];
			reel.symbols.forEach( () => {
				reelData.push( false );
			} );
			wintable.push( reelData );
		} );
		return wintable;
	}

	protected getBaseWinLine ( winElement: IScatterWin | ISpinResultReelWins, winTable: Array<Array<boolean>> ): WinLine {
		let reelPositions: Array<Array<number>> = [];
		let winningCombinations: Array<Array<WinningCombination>> = [];

		winElement.line_config.forEach( ( reel, x ) => {
			const winSymbolIndexOnReel = [];
			const winSymbolOnReel = [];

			if ( reel.length === 0 || ( reel.length === 1 && reel[ 0 ] === '*' ) ) { // no win on this reel, skip it
				reelPositions.push( winSymbolIndexOnReel );
				winningCombinations.push( winSymbolOnReel );
				return;
			}

			reel.forEach( ( winSymbolIndex ) => {
				const y = parseInt( winSymbolIndex );
				winSymbolIndexOnReel.push( y );
				const winningCombination: WinningCombination = {
					index: y,
					symbol: this.getSymbolByPosition( winElement.symbol, { x: x, y: y } )
				};
				winSymbolOnReel.push( winningCombination );
				winTable[ x ][ y ] = true;
			} );

			reelPositions.push( winSymbolIndexOnReel );
			winningCombinations.push( winSymbolOnReel );
		} );


		const winline: WinLine = {
			symbol: winElement.symbol,
			payout: winElement.prize,
			line: ( winElement as ISpinResultReelWins ).line_number != null ? ( winElement as ISpinResultReelWins ).line_number : 0,
			winningCombinations,
			reelPositions,
			rightToLeft: ( winElement as ISpinResultReelWins ) !== undefined ? ( winElement as ISpinResultReelWins ).right_to_left : false
		};

		return winline;
	}

	protected getFeatureState ( featureInfo: Array<IFeatureInfo> ): any {
		return featureInfo.find( e => e.type === FeatureTypes.FREE_SPIN );
	}

	/**
	 * get win data for each line
	 * @param winElement
	 * @param reels
	 */
	protected getWinline ( winElement: ISpinResultReelWins, winTable: Array<Array<boolean>>, wildConfig?: IWildMultiplier ): WinLine {

		const winLine = this.getBaseWinLine( winElement, winTable );

		let multiplier_list: Array<WildMultiplier> = [];

		if ( wildConfig != undefined && wildConfig.wild_config.length > 0 ) {
			multiplier_list = new Array<WildMultiplier>();
			for ( let i: number = 0; i < wildConfig.wild_config.length; i++ ) {
				for ( let j: number = 0; j < wildConfig.wild_config[ i ].length; j++ ) {
					if ( wildConfig.wild_config[ i ][ j ][ 'index' ] != '*' ) {
						let wild_multiplier: WildMultiplier = new WildMultiplier();
						wild_multiplier.reel_index = i;
						wild_multiplier.symbol_index = parseInt( wildConfig.wild_config[ i ][ j ][ 'index' ] );
						wild_multiplier.multiplier = wildConfig.wild_config[ i ][ j ][ 'value' ];
						multiplier_list.push( wild_multiplier );
					}
				}
			}
		}

		// TODO: leave the flexibility for a win more then one symol on one reel
		winLine.wildMultiplier = multiplier_list;
		//winLine.rightToLeft = winElement.right_to_left;

		return winLine;
	}

	protected getSymbolByPosition ( symbolSet: string | Array<Array<any>>, position: { x: number, y: number } ): string {
		let symbol: string = null;

		if ( Array.isArray( symbolSet ) ) {
			symbol = symbolSet[ position.x ][ position.y ].symbol;
		} else {
			symbol = symbolSet;
		}
		return symbol;
	}

	/**
	 * get complete feature scenario for take turn response
	 * @param message
	 * @param responseScenario
	 */
	protected getFormatFeatureTriggeredsScenario ( message: IGameState, responseScenario: Scenario ): Scenario {
		for ( let i = 0; i < message.spin_result.features_triggered.length; i++ ) {
			let feature: IFeatureInfo = message.spin_result.features_triggered[ i ];
			let featureScenario: Scenario = this.formatFeatureTriggeredScenario( feature, message );
			if ( featureScenario ) {
				if ( responseScenario == undefined ) {
					responseScenario = featureScenario;
				} else {
					responseScenario.addScenario( featureScenario );
				}

				// if feature.complete == false, means we need to create another transaction for this feature
				if ( !feature.complete ) {
					break;
				}
			}
		}
		return responseScenario;
	}

	/**
	 * format feature into scenario
	 * @param feature
	 * @param message
	 */
	protected formatFeatureTriggeredScenario ( feature: IFeatureInfo, message: IGameState ): Scenario {
		let responseScenario: Scenario = null;
		let featureFormatter: any = this.getFeatureFormatter( feature.type );

		if ( featureFormatter != undefined ) {
			responseScenario = featureFormatter.triggeredFeatureIncomingMessage( message, feature );
		} else {
			responseScenario = this.getGameFeatureScenario( feature, message );
		}
		return responseScenario;
	}

	/**
	 * get feature format from formatter list
	 * @param type
	 */
	protected getFeatureFormatter ( type: string ): any {
		console.warn( 'please override to defined feature list for your formatter' );
		return null;
	}

	/**
	 * for game to override,
	 * implement game feature
	 * @param feature
	 * @param message
	 */
	protected getGameFeatureScenario ( feature: IFeatureInfo, message: IGameState ): Scenario {
		console.warn( 'please override this feature for game if you want to handle it' + feature.type );
		return null;
	}


/**
 * get feature scenario from feature formatters by function name
 * @param functionName
 * @param message
 * @param spinStopScenario
 */	protected getFeatureScenarioByFunction ( functionName: string, message: IGameState, spinStopScenario: Scenario ): Scenario {
		if ( message.spin_result.features_triggered ) {
			let features: Array<IFeatureInfo> = message.spin_result.features_triggered;
			if ( features ) {
				features.forEach( element => {
					let featureFormatter: AbstractFeatureScenarioFormatter = this.getFeatureFormatter( element.type );
					if ( featureFormatter != undefined && featureFormatter[ functionName ] ) {
						spinStopScenario = featureFormatter[ functionName ]( spinStopScenario, message, element );
					}
				} );
			}
		}
		return spinStopScenario;
	}
}



